<template>
    <div>
        <h-form class="expand-row" :label-width="100" cols="4">
            <h-form-item v-if="row.rival_name" label="对手方">
                <h-input disabled v-model="row.rival_name">
                </h-input>
            </h-form-item>
            <h-form-item v-if="row.issuer_name" label="发行人">
                <h-input disabled v-model="row.issuer_name">
                </h-input>
            </h-form-item>
            <h-form-item v-if="row.trust_bank_name" label="托管人">
                <h-input disabled v-model="row.trust_bank_name">
                </h-input>
            </h-form-item>

            <h-form-item v-if="row.open_date" label="开户日期">
                <h-input disabled v-model="row.open_date">
                </h-input>
            </h-form-item>
            <!--<h-form-item v-if="row.busin_name" label="业务类型">-->
                <!--<h-input disabled v-model="row.busin_name">-->
                <!--</h-input>-->
            <!--</h-form-item>-->

            <h-form-item v-if="row.ta_name" label="TA类型">
                <h-input disabled v-model="row.ta_name">}
                </h-input>
            </h-form-item>

            <h-form-item  v-if="row.bank_linkman"  label="联系人">
                <h-input disabled v-model="row.bank_linkman">
                </h-input>
            </h-form-item>

            <h-form-item v-if="row.bank_phone"   label="联系电话" prop="bank_phone">
                <h-input disabled v-model="row.bank_phone">
                </h-input>
            </h-form-item>
            <h-form-item v-if="row.bank_fax"   label="传真" prop="bank_fax">
                <h-input disabled v-model="row.bank_fax">
                </h-input>
            </h-form-item>
            <h-form-item v-if="row.bank_mail"   label="邮箱" prop="bank_mail">
                <h-input disabled v-model="row.bank_mail">
                </h-input>
            </h-form-item>
        </h-form>
    </div>
</template>

<script>
    export default {
        name: "extendRow",
        props: {
            row: Object
        },
    }
</script>

<style scoped>
    .expand-row{
        margin-bottom: 16px;
    }
</style>



