<template>
    <div id="full-h-wrapper" class="page-systemlog">
        <h-row style="margin-bottom: 0px" id="systemLogForm">
            <el-form style="font-size: 12px;" :inline="true" size="mini" :model="queryForm" class="demo-form-inline">
                <el-form-item label="操作人">
                    <el-select v-model="queryForm.operatorNo" filterable clearable>
                        <el-option v-for="item in operatorList" :key="item.code" :label="item.text" :value="item.code">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="操作日期">
                    <el-date-picker
                            v-model="inputDate"
                            type="daterange"
                            :clearable="false"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="refresh">查询</el-button>
                </el-form-item>
            </el-form>
        </h-row>

        <h-datagrid style="margin-top: 0px"
                    class="mt10"
                    :height="autoHeight"
                    stripe
                    hasPage
                    highlight-row
                    :pageSize="100"
                    :pageSizeOpts="pageSizeOpts"
                    :columns="dataColumns"
                    :bindForm="queryForm"
                    :onCurrentChange="handleCurrentChange"
                    :onCurrentChangeCancel="handleUnselect"
                    url="/reportmgnt/systemLogQuery/page.json"
                    ref="datagrid">
        </h-datagrid>

        <div class="systemlog-detail" v-if="currentSystemLog.modify_desc" id="wrapper-0" v-bind:style="isScrollBar">
            <div class="wrapper">
                <header v-if="currentSystemLog.modify_desc" class="headline">
                    <i class="icon-l"></i>
                    <span class="title">操作内容</span>
                </header>
                <h-row v-if="currentSystemLog.modify_desc">
                    <h-col span="6" class="inner" v-for="(item, index) in currentSystemLog.modify_desc.split(';')"
                           :key="index">
                        <div class="item">
                            <div class="label">{{decodeURIComponent(item.split(":")[0])}}</div>
                            <div class="value" :title="decodeURIComponent(item.split(':')[1])">
                                {{decodeURIComponent(item.split(":")[1])}}
                            </div>
                        </div>
                    </h-col>
                </h-row>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        getDictList,
        showText,
        parseDateStr,
        parseDateString,
        parseStrDate,
        operatorList
    } from '@/api/utils'
    import Cookies from 'js-cookie'
    import HDatagrid from '@/components/HDatagrid'

    export default {
        name: "systemLogQuery",
        data() {
            let dataColumns = [
                {
                    title: '序号',
                    key: 'serial_no',
                    sortable: true,
                    width: 80,
                    align: 'right'
                }, {
                    title: "操作类型",
                    key: "check_type",
                    width: 70,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = showText(rowData.check_type, this.checkTypeList);
                        return h("span", {}, typeName);
                    }
                }, {
                    title: "操作项",
                    key: "remark",
                    ellipsis: true,
                    width: 160,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let remarkSpan = h('span', {}, rowData.remark);
                        return h('el-tooltip', {
                            props: {
                                trigger: 'hover',
                                effect: 'light',
                                placement: 'top-start',
                                openDelay: 400,
                                content: rowData.remark,
                                popperClass: 'tooltipclass'
                            }
                        }, [remarkSpan]);
                    }
                }, {
                    title: "操作人",
                    key: "operator_no",
                    width: 150,
                    sortable: true,
                    align: 'left',
                    ellipsis: true,
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = showText(rowData.operator_no, this.operatorList);
                        return h("span", {}, typeName);
                    }
                }, {
                    title: "操作时间",
                    key: "input_date",
                    sortable: true,
                    width: 135,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = parseDateString(rowData.input_date, rowData.input_time);
                        return h("span", {}, typeName);
                    }
                }, {
                    title: "操作菜单名称",
                    key: "menu_name",
                    width: 120,
                    align: 'left',
                    ellipsis: true
                }, {
                    title: "操作内容",
                    key: "modify_desc",
                    align: 'left',
                    ellipsis: true,
                    render: (h, params) => {
                        if (params.row.modify_desc) {
                            return h("span", {}, decodeURIComponent(params.row.modify_desc));
                        } else {
                            return '';
                        }
                    }
                },
            ];
            return {
                queryForm: {
                    operatorNo: '',
                    beginDate: Cookies.get('CurrSysDate'),
                    endDate: Cookies.get('CurrSysDate')
                },
                checkTypeList: [],
                operatorList: [],
                sysDate: Cookies.get('CurrSysDate'),
                autoHeight: 0,
                pageSizeOpts: [25, 50, 75, 100],
                dataColumns: dataColumns,
                isScrollBar: {
                    marginTop: '10px',
                    overflow: 'auto',
                    maxHeight: '',
                },
                currentSystemLog: {},

            }
        },
        components: {
            HDatagrid
        },
        props: {},
        computed: {
            inputDate: {
                get: function () {
                    let begin, end;
                    if (this.queryForm.beginDate) {
                        begin = parseDateString(this.queryForm.beginDate);
                    } else {
                        begin = this.sysDate;
                    }
                    if (this.queryForm.endDate) {
                        end = parseDateString(this.queryForm.endDate);
                    } else {
                        end = this.sysDate;
                    }
                    return [begin, end];
                },
                set: function (newValue) {
                    this.queryForm.beginDate = parseDateStr(newValue[0]);
                    this.queryForm.endDate = parseDateStr(newValue[1]);
                }
            },
        },
        activated() {
            this.refresh();
        },
        created() {
            getDictList(10054).then(res => this.checkTypeList = res.data);
            operatorList().then(res => {
                if (res.data && res.data.length > 0) {
                    this.operatorList = res.data;
                }
            }).catch(error => {
                console.log(error);
            });
        },
        methods: {
            refresh() {
                if (this.queryForm.beginDate && this.queryForm.endDate) {
                    let endTime = parseStrDate(this.queryForm.endDate);
                    let beginTime = parseStrDate(this.queryForm.beginDate);
                    let days = (endTime - beginTime) / (1000 * 60 * 60 * 24);
                    if (days > 30) {
                        this.$alert('查询日期间隔不能大于30天', '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                        return;
                    }
                }

                this.$refs.datagrid.dataChange(1);
                this.clearData();
            },
            clearData() {
                this.currentSystemLog = {};
                this.$nextTick(this.re_layout);
            },
            re_layout() {
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                let fullWrapperHeight = document.getElementById("full-h-wrapper").offsetHeight;
                if (wrapper0Height > fullWrapperHeight / 2) {
                    this.isScrollBar.maxHeight = fullWrapperHeight / 2 + 'px';
                    wrapper0Height = fullWrapperHeight / 2;
                }
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 70;
            },
            handleCurrentChange() {
                this.currentSystemLog = {};
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length === 1) {
                    this.currentSystemLog = selects[0];
                }
                this.$nextTick(this.re_layout);
            },
            handleUnselect() {
                this.clearData();
            },
        }
    }
</script>

<style lang="scss" src="@/style/common.scss"></style>
<style lang="less" rel="stylesheet/less">
    .page-systemlog {
        .item {
            margin-bottom: 8px;
            padding-right: 10px;
        }
    }
</style>
<style lang="scss">
    @mixin headline {
        .headline {
            padding-bottom: 10px;
            display: flex;
            flex-direction: row;
            align-items: center;

            .icon-l {
                display: block;
                width: 3px;
                height: 12px;
                background-color: #11a0f8;
                margin-right: 5px;
            }

            .title {
                padding: 0;
                margin-right: 10px;
                font-size: 12px;
                color: #666666;
            }

            .icon-operation {
                cursor: pointer;
                color: #11a0f8;
            }
        }
    }

    .page-systemlog .wrapper {
        width: 100%;
        // height: 208px;
        padding: 10px;
        background-color: #f6f7f9;

        @include headline();

        .inner {
            // height: 196px;
            padding: 0 12px;

            &:nth-child(4n + 1) {
                padding-left: 0;
            }

            &:not(:nth-child(4n)) {
                border-right: dashed 1px #666666;
            }
        }

        .inner-t,
        .inner {
            .item {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                padding-right: 0;
                white-space: nowrap;
                line-height: 21px;

                &:last-child {
                    margin-bottom: 0;
                }

                .label {
                    white-space: nowrap;
                    font-size: 12px;
                    color: #999999;
                }

                .value {
                    padding-left: 10px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    font-size: 14px;
                    color: #333333;
                }
            }
        }
    }
</style>
<style>
    .tooltipclass {
        width: 200px;
    }
</style>



