<template>
  <div class="cmp-manage-account-dialog">
    <h-msg-box class="customMsgBox" v-model="visible" width="600" :mask-closable=false>
      <p slot="header">{{title}}</p>
      <h-form :model="newAccountInfo" :label-width="105" ref="accountForm">
        <h-row>
          <h-col span="12">

            <h-form-item required label="账户用途" prop="account_class">
              <el-select v-if="showCustodialAccount" :disabled="isDisabled" v-model="newAccountInfo.account_class" @change="acctClassChanged"
                filterable>
                <el-option v-for="item in accountClassList" :key="item.code" :label="item.text" :value="item.code">
                </el-option>
              </el-select>
              <el-select v-if="!showCustodialAccount" :disabled="isDisabled" v-model="newAccountInfo.account_class" @change="acctClassChanged"
                        filterable>
                <el-option v-for="item in selfAccountClassList" :key="item.code" :label="item.text" :value="item.code">
                </el-option>
              </el-select>
            </h-form-item>
            <h-form-item v-if="newAccountInfo.account_class=='2' && fundInfo.fund_settle_type != '1' && fundInfo.fund_settle_type != '4'"
                         :required="newAccountInfo.account_class=='2' && fundInfo.fund_settle_type != '1' && fundInfo.fund_settle_type != '4'" label="托管人" prop="custodial_no">
              <h-select :disabled="isDisabled" v-model="newAccountInfo.custodial_no" filterable  @on-change="custodialNoChanged">
                <h-option v-for="item in trustBankList" :key="item.code" :label="item.text" :value="parseInt(item.code)">
                </h-option>
              </h-select>
            </h-form-item>
            <h-form-item v-if="newAccountInfo.account_class=='2'" required label="币种" prop="currency_no">
              <h-select :disabled="isDisabled" v-model="newAccountInfo.currency_no" filterable :setDefSelect="true" :clearable="false">
                <h-option v-for="item in currencyNoList" :key="item.code" :label="item.text" :value="item.code">
                </h-option>
              </h-select>
            </h-form-item>
            <h-form-item required label="账户名称" prop="bank_account_name">
              <h-input :maxlength="56" :disabled="isDisabled" v-model="newAccountInfo.bank_account_name">
              </h-input>
            </h-form-item>
            <h-form-item required label="银行账号" prop="bank_account_no" :validRules="numberValidRule">
              <!--<h-typefield v-model="newFundInfo.interbank_id" type="money" integerNum="10" suffixNum="0" placeholder="请输入数字"></h-typefield>-->
              <h-input :disabled="isDisabled" :maxlength="32" v-model="newAccountInfo.bank_account_no">
              </h-input>
            </h-form-item>

            <h-form-item required label="账户状态" prop="bank_account_status">
              <h-select :disabled="isDisabled" v-model="newAccountInfo.bank_account_status" filterable>
                <h-option v-for="item in accountStatusList" :key="item.code" :label="item.text" :value="item.code">
                </h-option>
              </h-select>
            </h-form-item>

            <div v-if="newAccountInfo.account_class=='8'">
              <h-form-item required label="期货公司" prop="broker_id">
                <h-select :disabled="isDisabled" v-model="newAccountInfo.broker_id" filterable>
                  <h-option v-for="item in brokerList" :key="item.code" :label="item.text" :value="parseInt(item.code)">
                  </h-option>
                </h-select>
              </h-form-item>
              <h-form-item required label="银期方式" prop="bank_futures_type">
                <h-select :disabled="isDisabled" v-model="newAccountInfo.bank_futures_type" filterable>
                  <h-option v-for="item in bankFuturesTypeList" :key="item.code" :label="item.text" :value="item.code">
                  </h-option>
                </h-select>
              </h-form-item>
            </div>

            <div v-if="newAccountInfo.account_class=='8'">
              <h-form-item required v-if="newAccountInfo.bank_futures_type=='2' || newAccountInfo.bank_futures_type=='4'"
                           label="期货中间账户" prop="futures_mid_account">
                <h-input :maxlength="56" :disabled="isDisabled" v-model="newAccountInfo.futures_mid_account">
                </h-input>
              </h-form-item>
              <h-form-item required v-if="newAccountInfo.bank_futures_type=='3' || newAccountInfo.bank_futures_type=='4'"
                           label="托管人中间账户" prop="bank_mid_account">
                <h-input :maxlength="56" :disabled="isDisabled" v-model="newAccountInfo.bank_mid_account">
                </h-input>
              </h-form-item>
            </div>

            <div v-if="newAccountInfo.account_class!='8' || (newAccountInfo.account_class=='8' && newAccountInfo.bank_futures_type!='4')">
              <h-form-item :required="newAccountInfo.account_class == '2' && (fundInfo.fund_settle_type == '1' || fundInfo.fund_settle_type == '4')" label="银行总行">
                <el-select size="small" style="width: 100%" :disabled="isDisabled" v-model="newAccountInfo.bic_code" filterable clearable>
                  <el-option v-for="item in bankList" :key="item.code" :label="item.text" :value="item.code">
                  </el-option>
                </el-select>
              </h-form-item>
              <h-form-item :required="newAccountInfo.account_class == '2' && (fundInfo.fund_settle_type == '1' || fundInfo.fund_settle_type == '4')" label="开户行名称" prop="sub_bank_no">
                <h-input :maxlength="56" :disabled="isDisabled" v-model="newAccountInfo.sub_bank_no">
                </h-input>
              </h-form-item>
              <h-form-item :required="newAccountInfo.account_class == '2' && (fundInfo.fund_settle_type == '1' || fundInfo.fund_settle_type == '4')" label="大额支付号" prop="pay_no">
                <h-input :maxlength="32" :disabled="isDisabled" v-model="newAccountInfo.pay_no">
                </h-input>
              </h-form-item>
            </div>

          </h-col>
          <h-col span="12">

            <h-form-item v-if="newAccountInfo.account_class=='5'||newAccountInfo.account_class=='6'" label="持有人账号"
              prop="holder_account">
              <h-input :maxlength="32" v-model="newAccountInfo.holder_account">
              </h-input>
            </h-form-item>

            <div v-if="newAccountInfo.account_class=='8' && newAccountInfo.bank_futures_type=='4'">
              <h-form-item label="银行总行" prop="bic_code">
                <el-select size="small" style="width: 100%" :disabled="isDisabled" v-model="newAccountInfo.bic_code" filterable clearable>
                  <el-option v-for="item in bankList" :key="item.code" :label="item.text" :value="item.code">
                  </el-option>
                </el-select>
              </h-form-item>
              <h-form-item label="开户行名称" prop="sub_bank_no">
                <h-input :maxlength="56" :disabled="isDisabled" v-model="newAccountInfo.sub_bank_no">
                </h-input>
              </h-form-item>
              <h-form-item label="大额支付号" prop="pay_no">
                <h-input :maxlength="32" :disabled="isDisabled" v-model="newAccountInfo.pay_no">
                </h-input>
              </h-form-item>
            </div>

            <h-form-item :required="newAccountInfo.account_class == '2' && (fundInfo.fund_settle_type == '1' || fundInfo.fund_settle_type == '4')" label="开户城市">
              <el-cascader style="width:100%" size="mini" :disabled="isDisabled" v-model="provinceCity" :options="cityTree"
                filterable clearable></el-cascader>
            </h-form-item>

            <h-form-item label="联系人" prop="bank_linkman">
              <h-input :maxlength="56" :disabled="isDisabled" v-model="newAccountInfo.bank_linkman">
              </h-input>
            </h-form-item>
            <h-form-item label="联系电话" prop="bank_phone">
              <h-input :maxlength="32" :disabled="isDisabled" v-model="newAccountInfo.bank_phone">
              </h-input>
            </h-form-item>
            <h-form-item label="传真" prop="bank_fax">
              <h-input :maxlength="32" :disabled="isDisabled" v-model="newAccountInfo.bank_fax">
              </h-input>
            </h-form-item>
            <h-form-item style="margin-top: 5px" label="邮箱" prop="bank_mail" :validRules="multiMailValidRule">
              <!--<el-input placeholder="多个邮箱请用英文逗号隔开" type="textarea" size="small" maxlength="1024" resize="none"
                :disabled="isDisabled" :autosize="{minRows: 2,maxRows:5}" v-model="newAccountInfo.bank_mail">
              </el-input>-->
              <h-input :maxlength="128" v-model="newAccountInfo.bank_mail" placeholder="多个邮箱请用英文逗号隔开">
              </h-input>
            </h-form-item>
            <h-form-item v-if="newAccountInfo.account_class != '8' || newAccountInfo.bank_futures_type!='4'" label="备注" prop="remark" style="margin-top:5px">
              <h-input type="textarea" :disabled="isDisabled" v-model="newAccountInfo.remark">
              </h-input>
            </h-form-item>
          </h-col>
        </h-row>

        <h-form-item v-if="newAccountInfo.account_class == '8' && newAccountInfo.bank_futures_type=='4'" label="备注" prop="remark" style="margin-top:5px">
          <h-input type="textarea" :disabled="isDisabled" v-model="newAccountInfo.remark">
          </h-input>
        </h-form-item>

      </h-form>
      <div slot="footer">
        <h-button type="ghost" style="margin-left: 8px" @click="handleCancel">取消</h-button>
        <h-button type="primary" v-if="actionType!='detail'" @click="handleOk">确认</h-button>
      </div>
    </h-msg-box>
  </div>
</template>

<script type="text/ecmascript-6">
import { mapState } from 'vuex'

import {
  saveAccount,
    getBicCodeByCustodialNo
} from '@/api/fundAccount/fundAccount'
import { deepCopy } from '@/utils/func'

export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    actionType: {
      type: String,
      default: 'detail'
    },
    fundInfo: {
      type: Object,
      default: null
    },
    accountInfo: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      codeRule: [{ test: /^[0-9a-zA-Z]+$/, message: '只能输入字母和数字' }],
      numberValidRule: [{test: /^[A-Za-z0-9()-]+$/, message: '账号格式不正确'}],
      bankPhoneRule: [{ test: /^(\d{3,4}-\d{7,8}$)|(1[0-9]{10})$/, message: '电话格式不正确' }],
      bankFaxRule: [{ test: /^\d{3,4}-\d{7,8}$/, message: '传真格式不正确' }],
      mailValidRule: [{ test: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/, message: '邮箱格式不正确' }],
      multiMailValidRule: [{ test: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?([,][\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?)*/, message: '邮箱格式不正确' }],
      showCustodialAccount: true,
      accountClassList: [{
        code: '1',
        text: '普通账户'
      },
      {
        code: '2',
        text: '托管账户'
      },
      {
        code: '3',
        text: '三方存管账户'
      },
      {
        code: '5',
        text: '上清DVP账户'
      },
      {
        code: '6',
        text: '中债DVP账户'
      },
      {
        code: '8',
        text: '期货资金账户'
      }],
      selfAccountClassList: [
        {
          code: '1',
          text: '普通账户'
        }, {
          code: '2',
          text: '自营账户'
        }, {
          code: '3',
          text: '三方存管账户'
        }, {
          code: '5',
          text: '上清DVP账户'
        }, {
          code: '6',
          text: '中债DVP账户'
        }, {
          code: '8',
          text: '期货资金账户'
        }
      ],
      // msg box 是否展示
      visible: false,
      newAccountInfo: {}
    }
  },
  computed: {
    ...mapState({
      fundTypeList: state => state.dict.dict_610087,
      fundSettleTypes: state => state.dict.dict_90531,
      taSettleTypes: state => state.dict.dict_90530,
      dataSourceList: state => state.dict.dict_90528,
      accountTypeList: state => state.dict.dict_60058,
      accountStatusList: state => state.dict.dict_200008,
      bankFuturesTypeList: state => state.dict.dict_90532,
      currencyNoList: state => state.dict.dict_610406,
      operatorList: state => state.dict.operatorList,
      bankList: state => state.dict.bankList,
      trustBankList: state => state.dict.trustBankList,
      brokerList: state => state.dict.brokerList,
      cityTree: state => state.dict.cityTree
    }),
    provinceCity: {
      get: function () {
        return [this.newAccountInfo.province_code, this.newAccountInfo.city_no]
      },
      set: function (newValue) {
        this.newAccountInfo.province_code = newValue[0];
        this.newAccountInfo.city_no = newValue[1];
      }
    },
    title() {
      let title = ''
      let actionType = this.actionType
      if (actionType === 'add') {
        title = '新增账户'
      }
      if (actionType === 'edit') {
        title = '修改账户'
      }
      if (actionType === 'detail') {
        title = '账户详情'
      }
      return title
    },
    isDisabled() {
      return this.actionType === 'detail'
    }
  },
  watch: {
    value: {
      handler: function (val) {
        this.visible = val
      },
      immediate: true
    },
    visible: {
      handler: function (val) {
        this.$emit('input', val)
        if (val) {
          this.showAccountInfo()
          if(this.fundInfo.fund_settle_type == '1' || this.fundInfo.fund_settle_type == '4'){
            this.showCustodialAccount = false;
          } else {
            this.showCustodialAccount = true;
          }
        }
      }
    },
    accountInfo: {
      handler: function (val) {
        this.showAccountInfo()
      }
    }
  },
  created() {

  },
  mounted() {

  },
  activated() {

  },
  methods: {
    showAccountInfo() {
      //console.log('showAccountInfo', this.actionType)
      if (this.actionType === 'edit' || this.actionType === 'detail') {
        this.newAccountInfo = deepCopy(this.accountInfo);
        if (this.newAccountInfo.city_no) {
          this.provinceCity = [this.newAccountInfo.province_code, this.newAccountInfo.city_no];
        }
      }
      if (this.actionType === 'add') {
        this.resetInputAccountInfo()
        this.$refs.accountForm.resetFields()
      }
    },
    acctClassChanged() {
        if(this.newAccountInfo.account_class =='2'){
            if(this.actionType === 'add'){
                this.newAccountInfo.custodial_no = this.fundInfo.fund_trust_bank;
                this.custodialNoChanged(this.fundInfo.fund_trust_bank);
            }else{
                this.newAccountInfo.custodial_no  = this.accountInfo.custodial_no;
                this.newAccountInfo.bic_code  = this.accountInfo.bic_code;
            }
        }else if(this.actionType == 'add' && this.newAccountInfo.account_class == '5'){
          this.newAccountInfo.custodial_no = "";
          this.newAccountInfo.bic_code = 'SHCLEARH'
        }else if(this.actionType == 'add' && this.newAccountInfo.account_class == '6'){
          this.newAccountInfo.custodial_no = "";
          this.newAccountInfo.bic_code = 'CHINABOND'
        }
    },
      custodialNoChanged(value){
          getBicCodeByCustodialNo(value).then(res => {
              if (res.data.data) {
                  this.newAccountInfo.bic_code = res.data.data.custodialorg.bic_code;
              }
          })
      },
    handleCancel() {
      this.visible = false
    },
    handleOk() {
      let validate = true;
      this.newAccountInfo.fund_id = this.fundInfo.fund_id;
      this.$refs.accountForm.validate((valid) => validate = validate && valid);

      if (this.newAccountInfo.account_class == '2' && (this.fundInfo.fund_settle_type == '1' || this.fundInfo.fund_settle_type == '4')) {
        if (!this.newAccountInfo.bic_code) {
          this.$alert('银行总行不能为空, 请重新检查!', '错误', {
            confirmButtonText: '确定',
            type: 'error'
          });
          return;
        }

        if (this.provinceCity && this.provinceCity.length > 1 && this.provinceCity[0] && this.provinceCity[1]) {
          this.newAccountInfo.province_code = this.provinceCity[0];
          this.newAccountInfo.city_no = this.provinceCity[1];
        } else {
          this.$alert('开户城市不能为空, 请重新检查!', '错误', {
            confirmButtonText: '确定',
            type: 'error'
          });
          return;
        }
      }

      if (!validate) {
        this.$alert('输入项不满足要求，请重新检查!', '错误', {
          confirmButtonText: '确定',
          type: 'error'
        });
        return;
      }
      saveAccount(this.newAccountInfo).then(res => {
        if (res.data && res.data.code === '0') {
          this.$alert(res.data.msg, '成功', {
            confirmButtonText: '确定',
            type: 'success',
            callback: action => {
              this.visible = false
            }
          }).then(() => {
            this.visible = false
          });
          this.$emit('refreshAccount',this.fundInfo.fund_id)
        } else if (res.data && res.data.code === '1120010') {
          // 没有修改项时，直接关闭窗口
          this.visible = false
        } else {
          this.$alert(res.data.msg, '错误', {
            confirmButtonText: '确定',
            type: 'error'
          })
        }
      })
    },
    resetInputAccountInfo() {
      this.newAccountInfo = {
        account_class: '2',
        bank_account_status: '1',
        bank_account_type: '1',
        bank_account_id: null,
        fund_id: '',
        custodial_no: this.fundInfo.fund_trust_bank,
        bic_code: '',
        bank_account_no: '',
        bank_account_name: null,
        pay_no: null,
        open_date: null,
        close_date: null,
        remark: null,
        holder_account: null,
        receive_dvp_id: null,
        sub_bank_no: null,
        province_code: null,
        city_no: null,
        bank_linkman: null,
        bank_phone: null,
        bank_fax: null,
        bank_mail: null,
        broker_id: null,
        bank_futures_type: null,
        futures_mid_account: null,
        bank_mid_account: null
      }
        this.custodialNoChanged(this.fundInfo.fund_trust_bank);
    }
  },
  components: {

  }
}
</script>

<style lang="less" rel="stylesheet/less" scoped>
</style>



