<template>
    <div class="fund-tree">
        <h-row>
            <div class="buttons-ctr2">
                <h-input style="width:70%" placeholder="输入“关键字”搜索" v-model="filterText">
                </h-input>
                <div class="buttons-ctr2">
                <div class="seniorquery-ctr" @click="openQuery"  @command="handleConditionQuery">
                    <i class="iconfont icon-gaojichaxun" ></i>
                </div>
                <div>
                    <el-dropdown size="mini" @click="openQuery" @command="handleConditionQuery">
                        <div  class="el-dropdown-link" >
                            <i style="margin-top: 6px;" class="el-icon-arrow-down el-icon--right"> </i>
                        </div>
                        <el-dropdown-menu slot="dropdown" v-if="conditionList.length >0">
                            <el-dropdown-item v-for="item in conditionList" :command="item.condition_id"
                                              :key="item.condition_id" >
                                {{item.condition_name}}
                            </el-dropdown-item>
                        </el-dropdown-menu>
                        <el-dropdown-menu slot="dropdown" v-else>
                            <el-dropdown-item>
                                暂无数据
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
                </div>
            </div>
        </h-row>
        <div class="auto-height" style="margin-top:10px;">
            <el-scrollbar class="h-scrollbar" style="height: 100%;">
                <el-tree :data="fundTree" :props="treeProps" @node-click="nodeClick" ref="fundTree" node-key="code" :default-expand-all="true" :expand-on-click-node="false" :filter-node-method="filterNode">
                    <span class="custom-tree-node" slot-scope="{ node, data }" >
                        <span>{{ node.label }}</span>
                        <span v-if="(node.label == '全部' || node.label == '暂无数据') && showToDo" style="position: relative; left: 25px;">
                            <el-dropdown size="mini" @click="openQuery" @command="handleConditionQuery" placement = "bottom" >
                                <div  class="el-dropdown-link0">
                                    <SwitchIcon :names="['icon-shaixuan', 'icon-morenshaixuan']"></SwitchIcon>
                                </div>
                                <el-dropdown-menu slot="dropdown" class="fund-filter-dropdown" >
                                    <el-checkbox class = "el-checkbox-str0" v-model="showBusinDone" @change = "showBusinDoneChange">隐藏无待办&nbsp;</el-checkbox>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </span>
                    </span>
                </el-tree>
            </el-scrollbar>

        </div>
        <h-msg-box class="customMsgBox"  v-model="setQueryMsg" width="700" :mask-closable=false>
            <p slot="header">
                <span>高级查询</span>
            </p>

            <h-form  :model="queryConditions" :label-width="110" ref="queryConditions" cols="2">
                <h-row :style="{'margin-top':'5px','margin-bottom': '5px'}">
                    <h-col span="12">
                        <h-form-item label="产品" prop="fund_id_list" >
                            <el-select size="mini" v-model="queryConditions.fund_id_list" filterable multiple clearable collapse-tags>
                                <el-option v-for="item in fundList" :value="item.code" :label="item.text"
                                           :key="item.code">
                                </el-option>
                            </el-select>
                        </h-form-item>

                        <h-form-item  style="margin-top: 10px" label="产品状态" prop="fund_status">
                            <el-select size="mini" v-model="queryConditions.fund_status" multiple clearable collapse-tags>
                                <el-option value="1" key="1" label="正常"></el-option>
                                <el-option value="2" key="2" label="注销"></el-option>
                                <el-option value="3" key="3" label="冻结"></el-option>
                            </el-select>
                        </h-form-item>

                        <h-form-item  style="margin-top: 10px" label="是否划款" prop="transfer_flag">
                            <el-select size="mini" v-model="queryConditions.transfer_flag" clearable>
                                <el-option value="1" key="1" label="是"> </el-option>
                                <el-option value="0" key="0" label="否"> </el-option>
                            </el-select>
                        </h-form-item>
                    </h-col>
                    <h-col span="12">
                        <h-form-item label="产品类型" prop="fund_type">
                            <el-select size="mini" v-model="queryConditions.fund_type" filterable multiple clearable collapse-tags>
                                <el-option v-for="item in fundTypeList" :value="item.code" :label="item.text"
                                           :key="item.code">
                                </el-option>
                            </el-select>
                        </h-form-item>
                        <h-form-item  style="margin-top: 10px" label="托管人" prop="fund_trust_bank">
                            <el-select size="mini" v-model="queryConditions.fund_trust_bank"  filterable clearable>
                                <el-option v-for="item in bankList" :value="item.code" :label="item.text"
                                           :key="item.code">
                                </el-option>
                            </el-select>
                        </h-form-item>
                        <h-form-item style="margin-top: 10px"  label="产品来源" prop="data_source">
                            <el-select size="mini" v-model="queryConditions.data_source" filterable clearable>
                                <el-option v-for="item in dataSourceList"
                                           :key="item.code"
                                           :label="item.text"
                                           :value="item.code">
                                </el-option>
                            </el-select>
                        </h-form-item>

                    </h-col>
                </h-row>
                <!--</h-field-panel>-->
                <el-collapse v-model="activeNames">
                    <el-collapse-item title="保存条件" name="1">
                        <h-row>
                            <h-col  span="12">
                                <h-form-item label="名称" prop="name" required>
                                    <h-input v-model="queryConditions.name">
                                    </h-input>
                                </h-form-item>
                            </h-col>
                            <h-col span="8">
                            </h-col>
                            <h-col span="3">
                                <h-button style="float: right" type="primary" @click="handleSave">保存</h-button>
                            </h-col>
                        </h-row>
                    </el-collapse-item>
                </el-collapse>
            </h-form>

            <div slot="footer">
                <h-button type="error" @click="handleDelete">删除</h-button>
                <h-button type="ghost" style="margin-left: 8px" @click="handleCancel">取消</h-button>
                <h-button type="primary" @click="handleOK">确定</h-button>
            </div>
        </h-msg-box>
    </div>


</template>

<script>
    import {
        deleteQueryCondition,
        getBankList,
        getDictList,
        getFundInfo,
        getQueryCondition,
        listQueryCondition,
        saveQueryCondition
    } from '@/api/utils';

    import HPanel from "./HPanel";
    import HFieldset from "./HFieldset";
    import SwitchIcon from "./SwitchIcon";

    export default {
        name: "FundTree",
        components: {
            HFieldset, HPanel,SwitchIcon},
        data() {
            return {
                showToday:'0',
                oldId:'',
                activeNames:['1'],
                fundTree: [{text:'   加载中              ... ',code:''}],
                fundTreeTmp: [],
                filterText:'',
                showBusinDone: false,
                queryConditions: {
                    name:"",
                    default_flag: "0",
                    condition_id: null,
                    keyword: '',
                    data_source: '',
                    fund_id_list: [],
                    fund_status: ['1'],
                    fund_type: [],
                    fund_trust_bank:"",
                    transfer_flag: "1",
                    isSettle:this.isSettle,
                    show_busin_done:'1',
                    show_today_flag:'0',
                    pageType:this.pageType
                },
                conditionName:'',
                treeProps:{
                    label:"text",
                    children:'children'
                },
                dataQuery:{fundId:''},
                setQueryMsg: false,
                fundTypeList: [],
                dataSourceList: [],
                fundList: [],
                conditionList:[],
                bankList:[]
            }
        },
        computed:{
            showDone: {
                get: function () {
                    return this.queryConditions.show_busin_done=='1'
                },
                set: function (newValue) {
                    this.queryConditions.show_busin_done=newValue?'1':'0'
                }
            },
            isDefault: {
                get: function () {
                    return this.queryConditions.default_flag=='1'
                },
                set: function (newValue) {
                    this.queryConditions.default_flag=newValue?'1':'0'
                }
            },

        },
        props: {
            queryUrl:{
                type: String,
                default: '/common/fundName.json'
            },
            showToDo: {
                type: Boolean,
                default: false
            },
            showAll: {
                type: Boolean,
                default: false
            },
            isSettle: {
                type: Boolean,
                default: false
            },
            extendButton: {
                type: Boolean,
                default: false
            },
            height: [Number, String],
            onFundClick: {
                type: Function,
                default(data) {
                    // console.log('选中产品：'+data.code);
                }
            },
            onFundQuery:{
                type: Function,
                default(data) {
                }
            },
            pageType:{
                //页面类型（1-指令制作，2-指令审批）
                pageType: {
                    type: String,
                    default: ""
                },
            }
        },
        watch: {
            filterText(val) {
                this.$refs.fundTree.filter(val);
            },
            setQueryMsg:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        activated(){
            this.handleOK();
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => {
                if (res.data && res.data.length > 0) {
                    this.fundList = res.data;
                }
            });
            getBankList("1").then(res => {
                if (res.data && res.data.length > 0) {
                    this.bankList = res.data;
                }
            });
        },
        created() {
            this.loadCondition();
            getDictList(610087).then(res => {
                if (res.data && res.data.length > 0) {
                    this.fundTypeList = res.data;
                }
            });
            getDictList(90528).then(res => {
                if (res.data && res.data.length > 0) {
                    this.dataSourceList = res.data;
                }
            });
        }, methods: {
            nodeClick(node){
                this.onFundClick(node);
                this.oldId = node.code
            },
            filterNode(value, data) {
                if (!value) return true;
                return data.text.indexOf(value) !== -1;
            },
            openQuery() {
                this.activeNames=['1']
                this.setQueryMsg = true;
            },
            showBusinDoneChange(showBusinDone){
                if(showBusinDone){
                    this.fundTreeTmp = this.fundTree;
                    let fundTreeChildren = this.fundTree[0].children;
                    let fundTreeChildren0 = [];
                    let custodialNoFundIds = '';
                    for (let i=0; i < fundTreeChildren.length; i++){
                        let fundTreeChildrenTmp = fundTreeChildren[i].children;
                        let fundTreeChildrenCon = {};
                        let fundIds = '';
                        let fundTreeChildren1 = [];
                        for (let j=0; j < fundTreeChildrenTmp.length; j++){
                            let fundTreeChildrenTmp0 = fundTreeChildrenTmp[j];
                            if(fundTreeChildrenTmp0.count != null && fundTreeChildrenTmp0.count != 0 ){
                                if(fundIds === ''){
                                    fundIds = fundIds + fundTreeChildrenTmp0.code;
                                }else{
                                    fundIds = fundIds + ',' + fundTreeChildrenTmp0.code;
                                }
                                fundTreeChildren1.push(fundTreeChildrenTmp0);
                            }
                        }
                        if(custodialNoFundIds === ''){
                            custodialNoFundIds = custodialNoFundIds + fundIds;
                        }else{
                            custodialNoFundIds = custodialNoFundIds + ',' + fundIds;
                        }

                        if(fundIds != null && fundIds != ''){
                            fundTreeChildrenCon.code = fundIds;
                            fundTreeChildrenCon.text = fundTreeChildren[i].text;
                            fundTreeChildrenCon.children = fundTreeChildren1;
                            fundTreeChildren0.push(fundTreeChildrenCon);
                        }
                    }
                    if(fundTreeChildren0 == null || fundTreeChildren0 == ''){
                        this.fundTree= [{text:'暂无数据',code:''}]
                    }else{
                        this.fundTree=[{text:'全部',code:custodialNoFundIds,children:fundTreeChildren0}];
                    }
                }else{
                    this.fundTree = this.fundTreeTmp;
                }
            },
            handleConditionQuery(command){
                if (!command) {
                    return
                }
                //重置代办按钮
                this.showBusinDone = false;
                getQueryCondition(command).then(res =>{
                    if (res.data.code==0){
                        let condition = res.data.data;
                        if (condition) {
                            this.queryConditions.condition_id = condition.condition_id;
                            this.queryConditions.name = condition.condition_name;
                            this.queryConditions.data_source = condition.data_source?condition.data_source.toString():"";
                            this.queryConditions.default_flag = '1';
                            this.queryConditions.transfer_flag = condition.transfer_flag;
                            this.queryConditions.fund_trust_bank = condition.fund_trust_bank?condition.fund_trust_bank.toString():"";
                            this.queryConditions.fund_id_list = (condition.fund_id_list!=null && condition.fund_id_list!="") ?condition.fund_id_list.split(","):[];
                            this.queryConditions.fund_status = (condition.fund_status_list!=null && condition.fund_status_list!="")?condition.fund_status_list.split(","):[];
                            this.queryConditions.fund_type = (condition.fund_type_list!=null && condition.fund_type_list!="")?condition.fund_type_list.split(","):[];
                            this.queryConditions.show_busin_done = '1';
                            this.queryConditions.show_today_flag = this.showToday;
                            this.conditionName = condition.condition_name;
                        }
                        var params = {
                            condition_id:this.queryConditions.condition_id,
                            data_source: this.queryConditions.data_source,
                            fund_id_list: this.queryConditions.fund_id_list.join(","),
                            fund_status_list: this.queryConditions.fund_status.join(","),
                            fund_type_list: this.queryConditions.fund_type.join(","),
                            condition_name: this.queryConditions.name,
                            transfer_flag : this.queryConditions.transfer_flag,
                            default_flag: '1',
                            fund_trust_bank: this.queryConditions.fund_trust_bank,
                            show_busin_done: '1',
                            show_today_flag: this.showToday,
                        };
                        saveQueryCondition(params).then(res =>{});
                        getFundInfo(this.queryUrl,this.queryConditions).then(res => {
                            if (res.data.length>0) {
                                if (this.showAll){
                                    let custodialNoFundIds = '';
                                    let fundList = [];
                                    for (let i = 0;i < res.data.length; i++){
                                        let funds = {};
                                        funds.text = res.data[i].text;
                                        funds.code = res.data[i].code;
                                        let fundIds = '';
                                        let fundArr = [];
                                        if(res.data[i].fundTransferCountList != null) {
                                            for (let j = 0; j < res.data[i].fundTransferCountList.length; j++) {
                                                if (fundIds === '') {
                                                    fundIds = fundIds + res.data[i].fundTransferCountList[j].fund_id;
                                                } else {
                                                    fundIds = fundIds + ',' + res.data[i].fundTransferCountList[j].fund_id;
                                                }
                                                let fund = {};
                                                fund.text = res.data[i].fundTransferCountList[j].fund_name;
                                                fund.code = res.data[i].fundTransferCountList[j].fund_id.toString();
                                                fund.count = res.data[i].fundTransferCountList[j].transfer_count;
                                                fundArr.push(fund)
                                            }
                                            funds.code = fundIds;
                                            funds.children = fundArr;
                                            if(custodialNoFundIds === ''){
                                                custodialNoFundIds = custodialNoFundIds + fundIds;
                                            }else{
                                                custodialNoFundIds = custodialNoFundIds +','+ fundIds;
                                            }

                                        }else{
                                            if (custodialNoFundIds === '') {
                                                custodialNoFundIds = custodialNoFundIds + res.data[i].code;
                                            } else {
                                                custodialNoFundIds = custodialNoFundIds + ',' + res.data[i].code;
                                            }
                                        }
                                        fundList.push(funds);
                                    }

                                    this.fundTree=[{text:'全部',code:custodialNoFundIds,children:fundList}]
                                    this.onFundClick({code:custodialNoFundIds});
                                } else{
                                    this.fundTree =res.data
                                    this.onFundClick(this.fundTree[0]);
                                }
                                this.onFundQuery(this.fundTree)
                                this.filterText = ''
                            }else{
                                this.fundTree= [{text:'暂无数据',code:''}]
                                this.onFundClick({code:''});
                            }
                        }).catch(error => {
                            console.log(error);
                        });
                    }else{
                        this.openMask();
                        this.$alert(res.data.desc,'错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    }

                });
            },
            handleOK() {
                // 需要单独处理当日交收查询条件。
                this.queryConditions.show_today_flag = this.showToday;
                this.queryConditions.show_busin_done = '1';
                //重置代办按钮
                this.showBusinDone = false;
                getFundInfo(this.queryUrl,this.queryConditions).then(res => {
                    if (res.data.length>0) {
                        let custodialNoFundIds = '';
                        if (this.showAll){
                            let fundList = [];
                            for (let i = 0;i < res.data.length; i++){
                                let funds = {};
                                funds.text = res.data[i].text;
                                funds.code = res.data[i].code;
                                let fundIds = '';
                                let fundArr = [];
                                if(res.data[i].fundTransferCountList != null){
                                    for (let j = 0; j < res.data[i].fundTransferCountList.length;j++){
                                        if(fundIds === ''){
                                            fundIds = fundIds + res.data[i].fundTransferCountList[j].fund_id;
                                        }else{
                                            fundIds = fundIds +','+ res.data[i].fundTransferCountList[j].fund_id;
                                        }
                                        let fund = {};
                                        fund.text = res.data[i].fundTransferCountList[j].fund_name;
                                        fund.code = res.data[i].fundTransferCountList[j].fund_id.toString();;
                                        fund.count = res.data[i].fundTransferCountList[j].transfer_count;
                                        fundArr.push(fund)
                                    }
                                    funds.code = fundIds;
                                    if(custodialNoFundIds === ''){
                                        custodialNoFundIds = custodialNoFundIds + fundIds;
                                    }else{
                                        custodialNoFundIds = custodialNoFundIds +','+ fundIds;
                                    }
                                    funds.children = fundArr;
                                }else{
                                    if (custodialNoFundIds === '') {
                                        custodialNoFundIds = custodialNoFundIds + res.data[i].code;
                                    } else {
                                        custodialNoFundIds = custodialNoFundIds + ',' + res.data[i].code;
                                    }
                                }
                                fundList.push(funds);
                            }
                            this.fundTree=[{text:'全部',code:custodialNoFundIds,children:fundList}]
                            if (this.oldId&&custodialNoFundIds.indexOf(this.oldId)>=0&&this.oldId.indexOf(",")<0) {
                                this.onFundClick({code:this.oldId});
                            }else {
                                this.onFundClick({code:custodialNoFundIds});
                            }
                        } else{
                            let fundIds = res.data.reduce(function(x, y){
                                return {code:x.code + ',' + y.code}
                            } ).code;
                            this.fundTree =res.data
                            if (this.oldId&&fundIds.indexOf(this.oldId)>=0&&this.oldId.indexOf(",")<0) {
                                this.onFundClick(res.data.filter(x=>x.code==this.oldId)[0]);
                            }else {
                                this.onFundClick({code:fundIds});
                            }
                        }
                        this.onFundQuery(this.fundTree)
                        this.filterText = ''
                    }else{
                        this.fundTree= [{text:'暂无数据',code:''}]
                        this.onFundClick({code:''});
                    }
                }).catch(error => {
                    console.log(error);
                });
                this.setQueryMsg = false;
            },
            handleSave() {
                let isValidate = true;
                this.$refs.queryConditions.validate((valid) =>isValidate = isValidate && valid)
                if(!isValidate){
                    this.$alert('名称不能为空!','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    })
                    return
                }
                var params = {
                    condition_id:this.queryConditions.condition_id,
                    data_source: this.queryConditions.data_source,
                    fund_id_list: this.queryConditions.fund_id_list.join(","),
                    fund_status_list: this.queryConditions.fund_status.join(","),
                    fund_type_list: this.queryConditions.fund_type.join(","),
                    condition_name: this.queryConditions.name,
                    transfer_flag : this.queryConditions.transfer_flag,
                    default_flag: '0',
                    fund_trust_bank: this.queryConditions.fund_trust_bank,
                    show_busin_done: this.queryConditions.show_busin_done,
                    show_today_flag: '0',
                };
                if (params.condition_id&&this.conditionName!=params.condition_name){
                    this.$confirm('检测到当前条件名称已修改，请选择是否保留原条件', '确认信息', {
                        distinguishCancelAndClose: true,
                        confirmButtonText: '覆盖到原条件',
                        cancelButtonText: '保存为新条件'
                    })
                        .then(() => {
                            this.saveCondition(params);
                        })
                        .catch(action => {
                            params.condition_id = null
                            this.saveCondition(params);
                        });
                }else {
                    this.saveCondition(params);
                }

            },
            saveCondition(params){
                saveQueryCondition(params).then(res =>{
                    if (res.data.success){
                        this.$alert('保存成功','提示',{
                            confirmButtonText: '确定',
                            type: 'success'
                        });
                        this.loadCondition();
                    } else{
                        if (res.data.code =="1120003") {
                            this.$confirm('条件名称不能重复，是否覆盖同名条件？', '确认', {
                                confirmButtonText: '确定',
                                cancelButtonText: '取消',
                                type: 'warning'
                            }).then(() => {
                                params.condition_id=res.data.data;
                                this.saveCondition(params);
                            });
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }

                    }
                });
            },
            handleCancel() {
                this.setQueryMsg = false;
            },
            handleDelete() {
                if (this.queryConditions.condition_id){
                    this.$confirm('确定删除所选条件？', '确认', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        deleteQueryCondition(this.queryConditions.condition_id).then(res =>{
                            this.$alert('删除成功','提示',{
                                confirmButtonText: '确定',
                                type: 'info'
                            });
                            this.resetCondition();
                            this.setQueryMsg = false;
                            this.loadCondition();
                        });
                    });
                } else{
                    this.$alert('未选中查询条件','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    });

                }
            },
            resetCondition(){
                this.queryConditions =  {
                    name:"",
                    default_flag: '0',
                    condition_id: null,
                    keyword: '',
                    data_source: '',
                    fund_id_list: [],
                    fund_status: ['1'],
                    fund_type: [],
                    fund_trust_bank:"",
                    transfer_flag: "1",
                    isSettle:this.isSettle
                },
                this.queryConditions.condition_id = null;
                this.queryConditions.pageType = this.pageType;
            },
            loadCondition() {
                listQueryCondition().then(res =>{
                    if (res.data && res.data.length > 0) {
                        this.conditionList = res.data;
                    }else{
                        let conditionListTmp = {};
                        conditionListTmp.condition_name = '未找到数据';
                        this.conditionList[0] = conditionListTmp;
                    }
                });
            },
            getFundList(){
                return this.fundList;
            },
            getQueryCondition(){
                return this.queryConditions;
            },
            clear(){
                this.oldId = null;
            }

        }

    }
</script>

<style lang="less" rel="stylesheet/less">
    .fund-tree {
        height: 100%;
        /*min-width: 240px;*/
        .auto-height {
            height: calc(~"100% - 38px");
        }
        .search-bar {
            display: flex;
            justify-content: space-between;
            align-items: center;
            & > div:first-child {
                flex-shrink: 2;
                margin-right: 10px;
            }
            & > div:last-child {
                width: 120px;
            }
        }
    }
</style>


<style lang="less" rel="stylesheet/less">
    .fund-tree {
        .el-button-group > .el-button:first-child {
            width: calc(~"100% - 54px");
        }
        .el-button {
            font-size: 12px;
            font-weight: normal;
        }
    }
    .el-scrollbar.h-scrollbar {
        .el-scrollbar__wrap{
            overflow-x: hidden;
        }
    }
    .el-tree>.el-tree-node {
        min-width: 100%;
        display: inline-block;
    }

    .seniorquery-ctr {
        width: 30px;
        border-style: solid;
        border-top-width: 1px;
        border-right-width: 0px;
        border-bottom-width: 1px;
        border-left-width: 1px;
        padding-left: 5px;
        padding-top: 2px;
        color: #C3CBD6;
        margin-left: 5px;
        cursor:pointer;
    }

    .el-dropdown .el-dropdown-link {
        cursor: pointer;
        color: #C3CBD6;
        border-right:1px solid #C3CBD6 ;
        border-top:1px solid #C3CBD6;
        border-bottom:1px solid #C3CBD6 ;
        margin-right: 5px;
        height: 28px;
        width: 25px;
    }

    .el-dropdown .el-dropdown-link0 {
        color: #11a0f8;
        height: 28px;
        width: 25px;
    }

    .el-dropdown .el-dropdown-link:hover{
        color: #11a0f8;
    }

    .seniorquery-ctr .iconfont:hover{
        color: #11a0f8;
    }

    .el-checkbox-str0{
        margin-left: 3px;
        margin-right: 2px;
    }

    .el-tree > .el-tree-node {
        display: block;
    }
    .custom-tree-node {
        flex: 1;
        display: flex;
        align-items: center;
        font-size: 12px;
        padding-right: 8px;
        margin-right: 20px;
    }

    .el-dropdown-menu .el-checkbox{
        color:#11a0f8;
    }
    .fund-filter-dropdown.el-dropdown-menu {
        padding: 5px;
    }
</style>


