<template>
    <div class="full-h" >
        <h-split v-model="split" min="130" max="600" @on-moving="onSplitMoving">
            <div slot="left" span="4" class="full-h">
                <bank-tree :onBankClick="bankClick" :trust-flag="true" :show-all="false" ref="liquidateBankTree"></bank-tree>
            </div>
            <div slot="right" span="4" class="full-h" style="padding-left: 10px" id="full-h-wrapper">
                <h-button-group>
                    <h-button icon="add" @click="ruleAdd">新增</h-button>
                    <h-button icon="editor" @click="ruleModify">修改</h-button>
                    <h-button icon="delete" @click="ruleDelete">删除</h-button>
                    <h-button icon="close" @click="changeRuleFlag">停用</h-button>
                </h-button-group>
                <h-datagrid :height="autoHeight"
                            stripe hasPage highlight-row
                            :auto-load="false" style="overflow: hidden"
                            :pageSize="100" :pageSizeOpts="pageSizeOpts"
                            :columns="ruleColumns" :bindForm="queryForm"
                            :on-row-db-click="ruleConfigDbClick"
                            url="/liquidate/rule/page.json"
                            ref="datagrid">
                </h-datagrid>
            </div>
        </h-split>
        <h-msg-box class="customMsgBox" v-model="showRuleDialog" width="600" :mask-closeable="false">
            <p slot="header">
                <span v-if="actionType=='add'">新增勾销规则</span>
                <span v-else>修改勾销规则</span>
            </p>
            <h-form :model="liquidateRuleInfo" :label-width="80" ref="liquidateRuleForm">
                <h-row>
                    <h-col span="18">
                        <h-form-item required label="规则名称" prop="liquidate_rule_name">
                            <h-input v-model="liquidateRuleInfo.liquidate_rule_name" ></h-input>
                        </h-form-item>
                    </h-col>
                    <h-col span="6">
                        <h-form-item required label="启用标志" prop="flag">
                            <h-switch v-model="liquidateRuleInfo.flag" size="small"></h-switch>
                        </h-form-item>
                    </h-col>
                </h-row>


                <h-form-item required label="操作" prop="liquidate_ins_deal_type">
                    <h-radio-group v-model="liquidateRuleInfo.liquidate_ins_deal_type" type="button" >
                        <h-radio v-for="item in dealTypeList" :key="item.code" :label="item.code">
                            <span>{{item.text}}</span>
                        </h-radio>
                    </h-radio-group>
                </h-form-item>

                <h-form-item label="业务分类" prop="cop_busin_class"
                             :required="liquidateRuleInfo.liquidate_ins_deal_type == '1'||liquidateRuleInfo.liquidate_ins_deal_type == '2'||liquidateRuleInfo.liquidate_ins_deal_type == '3'"
                             v-show="liquidateRuleInfo.liquidate_ins_deal_type != 5" >
                    <h-select v-model="liquidateRuleInfo.cop_busin_class" @on-change="businClassChange">
                        <h-option v-for="item in copBusinClassList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>

                <h-form-item label="业务标志" prop="cop_busin_flag"
                             :required="liquidateRuleInfo.liquidate_ins_deal_type == '3'"
                             v-show="liquidateRuleInfo.liquidate_ins_deal_type != 5"  >
                    <h-select v-model="liquidateRuleInfo.cop_busin_flag" filterable>
                        <h-option v-for="item in businFlagList" :key="item.code" :label="item.text" :value="parseInt(item.code)"></h-option>
                    </h-select>
                </h-form-item>

                <h-form-item required label="资金方向" prop="cash_direction">
                    <h-radio-group v-model="liquidateRuleInfo.cash_direction" type="button" >
                        <h-radio v-for="item in cashDirectionList" :key="item.code" :label="item.code">
                            <span>{{item.text}}</span>
                        </h-radio>
                    </h-radio-group>
                </h-form-item>


                <h-form-item label="允许误差<" prop="cash_diff"  v-show="liquidateRuleInfo.liquidate_ins_deal_type==1||liquidateRuleInfo.liquidate_ins_deal_type==2">
                    <h-typefield type="money" placeholder="" integerNum="15" suffixNum="2" divided
                                 v-model="liquidateRuleInfo.cash_diff">
                        <span slot="append">元</span>
                    </h-typefield>
                </h-form-item>

                <h-row>
                    <h-col>
                        <h-form-item  label="划款摘要" prop="transfer_remark">
                            <h-poptip trigger="hover" placement="right">
                                <h-input style="width: 478px;" v-model="liquidateRuleInfo.transfer_remark"></h-input>
                                <div slot="title">
                                    <p v-show="showMacro">支持以下宏定义或固定内容</p>
                                    <p v-show="!showMacro">支持固定内容</p>
                                </div>
                                <div  slot="content" v-show="showMacro">
                                    <p>{证券代码}</p>
                                    <p>{证券名称}</p>
                                    <p>{产品代码}</p>
                                    <p>{产品简称}</p>
                                    <p>{产品全称}</p>
                                </div>
                            </h-poptip>
                        </h-form-item>

                        <h-form-item label="划款用途" prop="transfer_purpose">
                            <h-poptip trigger="hover" placement="right">
                                <h-input style="width: 478px;" v-model="liquidateRuleInfo.transfer_purpose"></h-input>
                                <div slot="title">
                                    <p v-show="showMacro"> 支持以下宏定义或固定内容</p>
                                    <p v-show="!showMacro">支持固定内容</p>
                                </div>
                                <div slot="content" v-show="showMacro">
                                    <p>{证券代码}</p>
                                    <p>{证券名称}</p>
                                    <p>{产品代码}</p>
                                    <p>{产品简称}</p>
                                    <p>{产品全称}</p>
                                </div>
                            </h-poptip>
                        </h-form-item>

                        <h-form-item label="划款附言" prop="transfer_postscript">
                            <h-poptip trigger="hover" placement="right">
                                <h-input style="width: 478px;" v-model="liquidateRuleInfo.transfer_postscript"></h-input>
                                <div slot="title">
                                    <p v-show="showMacro">支持以下宏定义或固定内容</p>
                                    <p v-show="!showMacro">支持固定内容</p>
                                </div>
                                <div  slot="content" v-show="showMacro">
                                    <p>{证券代码}</p>
                                    <p>{证券名称}</p>
                                    <p>{产品代码}</p>
                                    <p>{产品简称}</p>
                                    <p>{产品全称}</p>
                                </div>
                            </h-poptip>
                        </h-form-item>

                        <h-form-item required label="操作员" prop="operator_no">
                            <h-select v-model="liquidateRuleInfo.operator_no" :clearable=false filterable>
                                <h-option v-for="item in operatorNoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                            </h-select>
                        </h-form-item>

                        <h-form-item label="规则说明" prop="remark">
                            <el-input type="textarea" resize="none" :autosize="{minRows: 2,maxRows: 3}" v-model="liquidateRuleInfo.remark"></el-input>
                        </h-form-item>
                    </h-col>
                </h-row>

            </h-form>
            <div slot="footer">
                <h-button type="ghost" class="margin-left:8px" @click="cancelLiquidateRule">取消</h-button>
                <h-button type="primary" @click="saveLiquidateRule">确认</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>

    import HDatagrid from "../../../components/HDatagrid";

    import {
        showText,
        getDictList,
        operatorList,
        formatMoney,
        getBusinFlagList
    } from "@/api/utils"

    import {
        saveLiquidateRule,
        deleteLiquidateRule,
        changeRuleFlag
    } from "@/api/liquidate/rule"
    import BankTree from "../../../components/BankTree";

    export default {
        name: "ruleConfig",
        components: {BankTree, HDatagrid},
        data(){
            return{
                split: 0.15,
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                showRuleDialog:false,
                actionType:'',
                operatorNoList:[],
                dealTypeList:[],
                copBusinClassList:[],
                copBusinFlagList:[],
                businFlagList:[],
                ruleColumns:[
                    {
                        title: "规则编号",
                        key: "liquidate_rule_id",
                        align: "left",
                        hiddenCol:true,
                        ellipsis: true
                    },
                    {
                        title: "规则名称",
                        key: "liquidate_rule_name",
                        align: "left",
                        width:120,
                        ellipsis: true
                    },
                    {
                        title: "启用标志",
                        key: "use_flag",
                        align: "center",
                        width:80,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let useFlag = rowData.use_flag;
                            if(useFlag == 0){
                                return h("i",{class:'iconfont icon-record',style:{color: '#EA3E30','font-size':'20px'}})
                            }else{
                                return h("i",{class:'iconfont icon-record',style:{color: '#68F904','font-size':'20px'}})
                            }
                        }
                    },
                    {
                        title: "操作类型",
                        key: "liquidate_ins_deal_type",
                        align: "left",
                        width:80,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let dealType = showText(rowData.liquidate_ins_deal_type,this.dealTypeList);
                            return h("span",{},dealType);
                        }
                    },
                    {
                        title: "业务分类",
                        key: "cop_busin_class",
                        align: "left",
                        width:80,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let businClass = showText(rowData.cop_busin_class,this.copBusinClassList)
                            return h("span",{},businClass)
                        }
                    },
                    {
                        title: "业务标志",
                        key: "cop_busin_flag",
                        align: "left",
                        width:100,
                        ellipsis: true,
                        width:150,
                        render: (h,params) =>{
                            const rowData = params.row
                            let businFlag = showText(rowData.cop_busin_flag,this.copBusinFlagList)
                            return h("span",{attrs:{title:businFlag}},businFlag);
                        }
                    },
                    {
                        title: "资金方向",
                        key: "cash_direction",
                        align: "left",
                        width:80,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row
                            let cashDirection = showText(rowData.cash_direction,this.cashDirectionList)
                            return h("span",{attrs:{title:cashDirection}},cashDirection)
                        }
                    },
                    {
                        title: "划款摘要",
                        key: "transfer_remark",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.transfer_remark
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },

                    {
                        title: "划款用途",
                        key: "transfer_purpose",
                        align: "left",
                        width:120,
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.transfer_purpose
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                    {
                        title: "划款附言",
                        key: "transfer_postscript",
                        align: "left",
                        width:120,
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.transfer_postscript
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                    {
                        title: "金额偏差",
                        key: "cash_diff",
                        align: "left",
                        width:100,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let cashDiff = formatMoney(rowData.cash_diff);
                            return h("span",{style: {fontWeight:'bold'}},cashDiff)
                        }
                    },
                    {
                        title: "操作人",
                        key: "operator_no",
                        align: "left",
                        width:100,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let operatorName = showText(rowData.operator_no,this.operatorNoList);
                            return h("span",{},operatorName)
                        }
                    },

                    {
                        title: "规则说明",
                        key: "remark",
                        align: "left",
                        width:120,
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.remark
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },

                ],
                liquidateRuleInfo:{
                    custodial_no:'',
                    liquidate_rule_id:'',
                    liquidate_rule_name:'',
                    cash_diff:'',
                    rival_account_no:'',
                    rival_account_name:'',
                    cash_direction:'',
                    transfer_remark:'',
                    transfer_purpose:'',
                    transfer_postscript:'',
                    liquidate_ins_deal_type:'',
                    cop_busin_class:'',
                    cop_busin_flag:'',
                    operator_no:'',
                    remark:'',
                    use_flag:'',
                    flag:true
                },
                queryForm:{
                    custodialNo:''
                },
                cashDirectionList:[
                    {
                        code:'1',
                        text:'入款'
                    },
                    {
                        code:'2',
                        text:'出款'
                    }
                ],
            }
        },
        watch:{
            showDialog: function (show) {
                if (show) {
                    this.closeMask();
                } else {
                    this.closeMask();
                }
            }
        },
        computed:{
            showMacro(){
                return this.liquidateRuleInfo.liquidate_ins_deal_type==1||this.liquidateRuleInfo.liquidate_ins_deal_type==2
            },
            businClassFlag: {
                get: function () {
                    let businClass = this.liquidateRuleInfo.cop_busin_class
                    if (this.liquidateRuleInfo.cop_busin_class == '4'
                        || this.liquidateRuleInfo.cop_busin_class == '5'
                        || this.liquidateRuleInfo.cop_busin_class == '6') {
                        businClass = '4,5,6'
                    }
                    return [businClass, this.liquidateRuleInfo.cop_busin_flag.toString()]
                },
                set: function (newValue) {
                    this.liquidateRuleInfo.cop_busin_class = newValue[0]
                    this.liquidateRuleInfo.cop_busin_flag = newValue[1]
                }
            }
        },
        mounted() {
            getDictList(91192).then(res => this.dealTypeList = res.data);
            operatorList().then(res => this.operatorNoList = res.data);
            this.re_layout();
            getDictList(90523).then(res => this.copBusinClassList = res.data);
            getDictList('businFlag').then(res => this.copBusinFlagList = res.data);
        },
        methods:{
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 40;
            },
            ruleAdd(){
                if(!this.queryForm.custodialNo){
                    this.openMask();
                    this.$alert('请选择需要添加规则的托管人','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask()
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                this.liquidateRuleInfo ={
                    custodial_no:this.queryForm.custodialNo,
                    liquidate_rule_id:'',
                    liquidate_rule_name:'',
                    cash_diff:'',
                    rival_account_no:'',
                    rival_account_name:'',
                    cash_direction:'1',
                    transfer_remark:'',
                    transfer_purpose:'',
                    transfer_postscript:'',
                    liquidate_ins_deal_type:'1',
                    cop_busin_class:'',
                    cop_busin_flag:'',
                    operator_no:'',
                    remark:'',
                    use_flag:'',
                    flag:true
                }
                this.$refs.liquidateRuleForm.resetFields();
                this.actionType = 'add';
                this.showRuleDialog = true;
            },
            ruleModify(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                this.ruleConfigDbClick(selects[0]);
            },
            ruleConfigDbClick(data){
                this.liquidateRuleInfo = data;
                if(this.liquidateRuleInfo.operator_no){
                    this.liquidateRuleInfo.operator_no = this.liquidateRuleInfo.operator_no.toString();
                }
                if(this.liquidateRuleInfo.use_flag.toString()){
                    if(this.liquidateRuleInfo.use_flag == '0'){
                        this.liquidateRuleInfo.flag = false;
                    }else{
                        this.liquidateRuleInfo.flag = true;
                    }
                }
                this.actionType = 'modify';
                this.showRuleDialog = true;
            },
            ruleDelete(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }

                this.openMask();
                this.$confirm('确认删除该勾销规则吗？', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteLiquidateRule(selects[0].custodial_no,selects[0].liquidate_rule_id).then(res =>{
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            });
                            this.refresh()
                        } else {
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText: '确定',
                                type: 'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            });
                        }
                    })
                }).catch(() =>{
                    this.closeMask();
                });
            },
            changeRuleFlag(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                }
                changeRuleFlag(selects[0].custodial_no,selects[0].liquidate_rule_id,0).then(res =>{
                    if (res.data && res.data.code == '0') {
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                        this.refresh()
                    } else {
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                    }
                })
            },
            cancelLiquidateRule(){
                this.showRuleDialog = false;
            },
            saveLiquidateRule(){
                let isValidate = true;
                this.$refs.liquidateRuleForm.validate(validate => isValidate = isValidate && validate)
                if(!isValidate){
                    this.openMask();
                    this.$alert('输入项不满足要求，请重新检查','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask()
                        }
                    }).then(() => {
                        this.closeMask()
                    })
                    return
                }
                if(this.liquidateRuleInfo.flag == false){
                    this.liquidateRuleInfo.use_flag = 0
                }else{
                    this.liquidateRuleInfo.use_flag = 1
                }
                saveLiquidateRule(this.liquidateRuleInfo).then(res => {
                    if(res.data && res.data.code == '0'){
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText:'确定',
                            type:'success',
                            callback:action => {
                                this.showRuleDialog = false
                            }
                        }).then(() =>[
                            this.showRuleDialog =false
                        ])
                        this.refresh()
                    }else{
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText:'确定',
                            type:'error'
                        })
                    }
                })

            },
            businClassChange: function(currentValue){
                if(currentValue){
                    getBusinFlagList(currentValue).then(res => this.businFlagList = res.data);
                }
            },
            bankClick(data) {
                if(data){
                    this.queryForm.custodialNo = data.code;
                    this.refresh()
                }
            },
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            onSplitMoving() {
                this.$refs.datagrid.handleResize();
            }
        }
    }
</script>

<style>

</style>


