import fetch from "@/api/httpFetch";

export function addFeeRule(feeRuleInfo) {
    return fetch.post("/fee/rule/add.json",feeRuleInfo, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function modifyFeeRule(feeRuleInfo) {
    return fetch.post("/fee/rule/modify.json",feeRuleInfo, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function deleteFeeRule(serial_nos) {
    return fetch.post("/fee/rule/delete.json", {serial_nos:serial_nos,
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function discardFeeRule(serial_nos) {
    return fetch.post("/fee/rule/discard.json", {serial_nos:serial_nos,
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}





