import fetch from "@/api/httpFetch";

export function addBankBusflag(bankBusinFlag) {
    return fetch.post("/systmmgnt/bankBusflag/insertBankBusflag.json",bankBusinFlag, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function modifyBankbusflag(bankBusinFlag) {
    return fetch.post("/systmmgnt/bankBusflag/modifyBankbusflag.json",bankBusinFlag, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function delBankBusflag(serialNoList) {
    return fetch.post('/systmmgnt/bankBusflag/delBankBusflag.json',{serialNoList:serialNoList})
}



