<template>
    <div class="full-h">
        <h-split v-model="split" min="130" max="600">
            <div slot="left" span="4" class="full-h">
                <bank-tree  :onBankClick="bankClick" ref="bankTree" :showAll="true" :query-url="'/common/treeElcBank.json'"></bank-tree>
            </div>
            <div slot="right" span="20" class="full-h" style="padding-left: 10px" id="full-h-wrapper">
                <h-row>
                    <h-col>
                        <el-form style="font-size: 12px;float: left" :inline="true" size="mini" :model="queryForm" class="demo-form-inline">
                            <el-form-item label="日期">
                                <el-date-picker :clearable="false" v-model="queryDate" type="date" placeholder="选择日期"></el-date-picker>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="refresh">查询</el-button>
                                <el-button type="ghost" @click="cashRefresh">余额查询</el-button>
                                <el-button type="ghost" @click="multipleRefresh">批量查询</el-button>
                            </el-form-item>
                        </el-form>
                    </h-col>
                </h-row>
                <h-datagrid :height="autoHeight"
                            stripe hasPage highlight-row
                            :auto-load="false" style="overflow: hidden"
                            :pageSize="100" :pageSizeOpts="pageSizeOpts"
                            :columns="accCashColumns" :bindForm="queryForm"
                            url="/reportmgnt/accCash/accCashQuery.json" ref="datagrid">
                </h-datagrid>
            </div>
        </h-split>
        <!--<h-msg-box class="customMsgBox" v-model="showCashQueryDialog" width="300" :mask-closable="false">
            <p slot="header">
                <span>余额查询</span>
            </p>
            <h-form :model="cashQueryInfo" :label-width="80" ref="cashQueryForm">
                <h-form-item required label="产品名称" prop="fund_id">
                    <h-select v-model="cashQueryInfo.fund_id" filterable @on-change="fundIdChange">
                        <h-option v-for="item in fundIdList" :key="item.code" :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>
                <h-form-item required label="托管人名称" prop="custodial_no">
                    <h-select v-model="cashQueryInfo.custodial_no" @on-change="bankNoChange" filterable>
                        <h-option v-for="item in bankNoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item required label="账户名称" prop="bank_account_name" >
                    <h-select v-model="cashQueryInfo.bank_account_name" @on-change="bankAccountNameChange" filterable>
                        <h-option v-for="item in bankAccountNameList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item required label="银行账号" prop="bank_account_no" >
                    <h-select v-model="cashQueryInfo.bank_account_no" filterable>
                        <h-option v-for="item in bankAccountNoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="cancelCashQuery">取消</h-button>
                <h-button type="primary" :loading="loading" @click="queryCashQuery">确定</h-button>
            </div>
        </h-msg-box>-->
        <h-msg-box class="customMsgBox" v-model="showMultipleQueryDialog" width="800" height="400" :mask-closable="false">
            <p slot="header">
                <span>余额批量查询</span>
            </p>
            <h-row>
                <h-col>
                    <h-datagrid height="355"
                                stripe hasPage highlight-row
                                :pageSize="100" :pageSizeOpts="pageSizeOpts"
                                :bindForm="multipleBindForm"
                                :columns="multipleCashColumns" ref="multiplecashdatagrid"
                                url="/reportmgnt/accBatchQuery.json"></h-datagrid>
                </h-col>
            </h-row>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="multipleCancel">取消</h-button>
                <h-button type="primary" :loading="loading" @click="multipleSave">确定</h-button>
            </div>
        </h-msg-box>

        <accCashFlowQueryMsgBox :msgBoxShow="cashMsgBoxShow"
                                :queryType="cashQueryType"
                                :includeSelfRun=false
                                @onCashFlowClose="onCashClose"
                                @onOutRefresh="onCashRefresh"
                                ref="accCashQueryMsg">
        </accCashFlowQueryMsgBox>
    </div>
</template>

<script>
    import BankTree from "../../../components/BankTree";
    import HDatagrid from "../../../components/HDatagrid";
    import accCashFlowQueryMsgBox from '@/components/accCashFlowQueryMsgBox'

    import {
        getFundInfo,
        parseDateString,
        parseDateStr,
        formatMoney,
        getDictList,
        showText
    }from '@/api/utils'

    import {
        getBankNo,
        getAccName,
        getAccNo,
        submitAccQuery,
        submitAccBatchQuery
    } from '@/api/reportmgnt/accQuery'

    import Cookies from 'js-cookie'

    export default {
        name: "accCashQuery",
        components: {HDatagrid, BankTree, accCashFlowQueryMsgBox},
        data(){
            return{
                loading: false,
                split: 0.15,
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                showCashQueryDialog:false,
                showMultipleQueryDialog:false,
                fundIdList:[],
                bankNoList:[],
                bankAccountNameList:[],
                bankAccountNoList:[],
                queryStatusList:[],
                sysDate:Cookies.get('CurrSysDate'),
                accCashColumns:[
                    {
                        title:'托管人查询序号',
                        key:'query_id',
                        align:'center',
                        hiddenCol:true,
                    },{
                        title:'日期',
                        key:'curr_date',
                        align:'center',
                        sortable:true,
                        ellipsis:true
                    },{
                        title:'托管人编号',
                        key:'custodial_no',
                        align:'center',
                        ellipsis:true,
                        sortable:true
                    },{
                        title:'托管人名称',
                        key:'custodian_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'产品编号',
                        key:'fund_id',
                        align:'center',
                        ellipsis:true,
                        hiddenCol:true
                    },{
                        title:'产品名称',
                        key:'fund_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'银行账号',
                        key:'bank_account_no',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'账户名称',
                        key:'bank_account_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'账户编号',
                        key:'bank_account_id',
                        align:'center',
                        ellipsis:true,
                        hiddenCol:true
                    },{
                        title:'余额',
                        key:'account_cur_balance',
                        align:'center',
                        ellipsis:true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let accountCurBalance = formatMoney(rowData.account_cur_balance)
                            return h("span",{style:{fontWeight:'bold'}},accountCurBalance)
                        }
                    },{
                        title:'查询状态',
                        key:'query_status',
                        align:'center',
                        ellipsis:true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let queryStatus = showText(rowData.query_status,this.queryStatusList)
                            return h("span",{},queryStatus)
                        }
                    },{
                        title:'备注',
                        key:'remark',
                        align:'center',
                        ellipsis:true
                    }
                ],
                multipleCashColumns:[
                    {
                        type: 'selection',
                        width: 45,
                        align: "left"
                    },{
                        title:'托管人查询序号',
                        key:'query_id',
                        align:'center',
                        hiddenCol:true,
                    },{
                        title:'产品编号',
                        key:'fund_id',
                        align:'center',
                        ellipsis:true,
                        hiddenCol:true
                    },{
                        title:'产品代码',
                        key:'fund_code',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'产品名称',
                        key:'fund_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'托管人编号',
                        key:'custodial_no',
                        align:'center',
                        ellipsis:true,
                        sortable:true
                    },{
                        title:'托管人名称',
                        key:'custodian_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'银行账号',
                        key:'bank_account_no',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'账户名称',
                        key:'bank_account_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'账户编号',
                        key:'bank_account_id',
                        align:'center',
                        ellipsis:true,
                        hiddenCol:true
                    },{
                        title:'查询状态',
                        key:'query_status',
                        align:'center',
                        ellipsis:true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let queryStatus = showText(rowData.query_status,this.queryStatusList)
                            if(!queryStatus){
                                queryStatus = '未查询'
                            }
                            return h("span",{},queryStatus)
                        }
                    }
                ],
                queryForm:{
                    queryDate:Cookies.get('CurrSysDate'),
                    custodialNo:''
                },
                multipleBindForm:{
                    queryDateBatch:'',
                    queryTypeBatch:''
                },
                cashQueryInfo:{
                    fund_id:'',
                    custodial_no:'',
                    bank_account_name:'',
                    bank_account_no:'',
                    queryDate:'',
                    queryType:'1'
                },
                cashQueryType: '',
                cashMsgBoxShow: false,
            }
        },
        watch:{
            showCashQueryDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            },
            showMultipleQueryDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
            queryDate:{
                get:function () {
                    if(this.queryForm.queryDate)
                        return parseDateString(this.queryForm.queryDate)
                    else
                        return parseDateString(this.sysDate)
                },
                set:function (newValue) {
                    this.queryForm.queryDate = parseDateStr(newValue)
                }
            }
        },
        mounted(){
            getDictList(90520).then(res => this.queryStatusList = res.data)
            this.re_layout();
        },
        activated(){
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => this.fundIdList = res.data);
        },
        methods:{
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 40;
            },
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            cashRefresh(){
                //现仅支持查询当日余额 时间固定为当天
                /*this.cashQueryInfo = {
                    fund_id:'',
                    custodial_no:'',
                    bank_account_name:'',
                    bank_account_no:'',
                    queryDate:'',
                    queryType:'1'
                }
                this.cashQueryInfo.queryDate = parseDateStr(this.sysDate);
                this.$refs.cashQueryForm.resetFields();
                this.showCashQueryDialog = true;
                this.loading = false;*/

                this.cashMsgBoxShow = true;
                this.cashQueryType = "1";
                this.$refs.accCashQueryMsg.resetCashFlowQueryForm();
            },
            multipleRefresh(){
                //现仅支持查询当日余额 时间固定为当天
                this.multipleBindForm.queryDateBatch = parseDateStr(this.sysDate);
                this.multipleBindForm.queryTypeBatch = '1'
                this.$refs.multiplecashdatagrid.dataChange(1);
                this.showMultipleQueryDialog = true;
                this.loading = false;
            },
            /*fundIdChange:function(current){
                getBankNo(current).then(res => this.bankNoList = res.data)
            },
            bankNoChange:function (current) {
                getAccName(this.cashQueryInfo.fund_id,current).then(res => this.bankAccountNameList = res.data)
            },
            bankAccountNameChange(current){
                getAccNo(this.cashQueryInfo.fund_id,this.cashQueryInfo.custodial_no,current).then(res => this.bankAccountNoList = res.data)
            },
            cancelCashQuery(){
                this.showCashQueryDialog = false;
            },
            queryCashQuery(){
                let isValidate = true;
                this.$refs.cashQueryForm.validate((validate) => isValidate = isValidate && validate)
                if(!isValidate){
                    this.$alert('输入项不满足要求，请重新检查','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }
                this.loading = true;
                submitAccQuery(this.cashQueryInfo.fund_id,this.cashQueryInfo.custodial_no,this.cashQueryInfo.bank_account_no,this.cashQueryInfo.queryDate,'1').then(
                    res =>{
                        if(res && res.status == 200){
                            if(res.data.success == true){
                                this.$alert('操作成功','成功',{
                                    confirmButtonText:'确定',
                                    type:'success',
                                    callback:action => {
                                        this.showCashQueryDialog = false;
                                    }
                                }).then(() => {
                                    this.showCashQueryDialog = false;
                                })
                                this.refresh();
                            }else{
                                this.$alert(res.data.msg,'错误',{
                                    confirmButtonText:'确定',
                                    type:'error'
                                })
                                this.showCashQueryDialog = false;
                            }
                        }else{
                            this.$alert('余额查询失败','错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                            this.showCashQueryDialog = false;
                        }
                    }
                )
            },*/
            multipleCancel(){
                this.showMultipleQueryDialog = false;
            },
            multipleSave(){
                let selects = this.$refs.multiplecashdatagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.$alert('请选择要查询的记录','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }
                let params = '';
                for(let i = 0; i<selects.length; i++){
                    params+=selects[i].fund_id+","+selects[i].custodial_no+','+selects[i].bank_account_no+','+selects[i].query_id+'|'
                }
                params = params.substring(0,params.length-1);
                this.loading = true;
                submitAccBatchQuery(this.multipleBindForm.queryTypeBatch,this.multipleBindForm.queryDateBatch,params).then( res => {
                    if(res && res.status == 200){
                        if(res.data.success == true){
                            this.$alert('操作成功','成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.showMultipleQueryDialog = false;
                                }
                            }).then(() => {
                                this.showMultipleQueryDialog = false;
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                            this.showMultipleQueryDialog = false;
                        }
                    }else{
                        this.$alert('批量查询失败','错误',{
                            confirmButtonText:'确定',
                            type:'error'
                        })
                        this.showMultipleQueryDialog = false;
                    }
                })
            },
            bankClick(data){
                if(data){
                    if(data.code.toString().split(',').length > 1){
                        data.code = '';
                    }
                    this.queryForm.custodialNo = data.code;
                    this.$refs.datagrid.dataChange(1);
                }
            },
            onCashClose() {
                this.cashMsgBoxShow = false;
            },
            onCashRefresh() {
                this.refresh();
            },
        }
    }
</script>

<style scoped>

</style>


