<template>
    <div>
        <el-table :max-height="height1"
                :data="tableData"
                border
               colspan="1"
                show-summary
                size="small"

                :span-method="arraySpanMethod"
                :summary-method="getSummaries"
                style="width: 100%">
            <el-table-column
                    prop="pre"
                    :resizable="false"
                    width="60">
            </el-table-column>
            <el-table-column
                    prop="agency_name"
                    :resizable="false"
                    label="销售商"
                    align="center"
                    width="100">
            </el-table-column>
            <el-table-column
                    prop="net_amount"
                    :resizable="false"
                    align="right"
                    label="申赎轧差净额"
                    header-align="center"
                    :formatter="formatter">
            </el-table-column>
            <el-table-column
                    prop="purchase_amount"
                    :resizable="false"
                    label="申购金额"
                    header-align="center"
                    align="right"
                    :formatter="formatter">
            </el-table-column>
            <el-table-column
                    prop="redemption_amount"
                    :resizable="false"
                    label="赎回金额"
                    header-align="center"
                    align="right"
                    :formatter="formatter">
            </el-table-column>
            <el-table-column
                    prop="profit_amount"
                    :resizable="false"
                    label="分红金额"
                    header-align="center"
                    align="right"
                    :formatter="formatter">
            </el-table-column>
            <el-table-column
                    prop="cash_settle_date"
                    :resizable="false"
                    label="交收日期"
                    header-align="center"
                    align="right"
            ></el-table-column>
            <el-table-column
                    prop="curr_date"
                    :resizable="false"
                    label="确认日期"
                    header-align="center"
                    align="right"
            ></el-table-column>
        </el-table>

        <el-table
                :max-height="height2"
                v-show="tableData1.length>1"
                :data="tableData1"
                border
                size="small"
                show-summary
                :show-header="false"
                :span-method="arraySpanMethod"
                :summary-method="getSummaries"
                style="width: 100%; ">
            <el-table-column
                    prop="pre"
                    width="60">
            </el-table-column>
            <el-table-column
                    prop="agency_name"
                    label="销售商"
                    align="center"
                    width="100">
            </el-table-column>
            <el-table-column
                    prop="net_amount"
                    label="申赎轧差净额"
                    align="right"
                    :formatter="formatter">
            </el-table-column>
            <el-table-column
                    prop="purchase_amount"
                    label="申购金额"
                    align="right"
                    :formatter="formatter">
            </el-table-column>
            <el-table-column
                    prop="redemption_amount"
                    label="赎回金额"
                    align="right"
                    :formatter="formatter">
            </el-table-column>
            <el-table-column
                    prop="profit_amount"
                    label="分红金额"
                    align="right"
                    :formatter="formatter">
            </el-table-column>
            <el-table-column
                    prop="cash_settle_date"
                    :resizable="false"
                    label="交收日期"
                    header-align="center"
                    align="right"
            ></el-table-column>
            <el-table-column
                    prop="curr_date"
                    :resizable="false"
                    label="确认日期"
                    header-align="center"
                    align="right"
            ></el-table-column>
        </el-table>
    </div>
    
</template>

<script>
    import{
        formatMoney,
    } from "@/api/utils"

    export default {
        name: "TADetailTable",
        data() {
            return {
                height1:this.height,
                height2:0,
                tableData:[],
                tableData1: []
            };
        },
        props:{
            height:{
                type:Number
            },
        },
        methods:{
            formatter(row, column, cellValue, index){
                return formatMoney(cellValue)
            },
            getSummaries(param) {
                const { columns, data } = param;
                const sums = [];
                columns.forEach((column, index) => {
                    if (index === 0) {
                        sums[index] = '总计';
                        return;
                    }
                    if (column.property === 'cash_settle_date' || column.property === 'curr_date') {
                        return;
                    }
                    const values = data.map(item => Number(item[column.property]));
                    if (!values.every(value => isNaN(value))) {
                        sums[index] = values.reduce((prev, curr) => {
                            const value = Number(curr);
                            if (!isNaN(value)) {
                                return prev + curr;
                            } else {
                                return prev;
                            }
                        }, 0);
                        sums[index] = formatMoney(sums[index])
                        // sums[index] += ' 元';
                    } else {
                        sums[index] = 'N/A';
                    }
                });

                return sums;
            },
            arraySpanMethod({ row, column, rowIndex, columnIndex }) {
                if (rowIndex  ===0) {
                    if (columnIndex==0||columnIndex==6)
                        return [1, 6];
                    else
                        return [0,0]
                }
            },
            setData(data){

                var shData = [{
                    pre: '中登上海',
                }]
                var szData = [{
                    pre: '中登深圳',
                }]
                var zjData = [{
                    pre: '自建TA',
                }]
                data.forEach(x => {
                    if (x.ta_type == '1') {
                         zjData.push(x)
                    } else if (x.ta_type == '2') {
                         shData.push(x)
                    } else {
                         szData.push(x)
                    }

                });
                if (zjData.length>1){
                    this.tableData = zjData
                    this.tableData1 = []
                }else if(shData.length ==1&&szData.length ==1){
                    this.tableData = []
                    this.tableData1 = []
                }else {
                    // this.tableData = this.shData
                    // this.tableData1 = this.szData
                    this.tableData = shData.length>1?shData:szData
                    this.tableData1 = shData.length>1?szData:[]
                }

                if (this.tableData&&this.tableData1.length>0){
                    this.height1 = this.height / 2
                    this.height2 = this.height / 2
                } else {
                    this.height1 = this.height
                    this.height2 = 0
                }
            }
        }

    }
</script>

<style scoped>

</style>


