<template>
    <div class="page-transfer-template full-h">
        <h-split v-model="split" min="50" max="600" @on-moving="onSplitMove">
            <div slot="left" span="4" class="full-h">
                <h-other-fund-tree :show-all="true" :onFundClick="fundClick"></h-other-fund-tree>
            </div>
            <div slot="right" span="20" class="full-h" style="padding-left: 10px" id="full-h-wrapper">
                <h-button-group>
                    <h-button icon="add" @click="templateAdd">新增</h-button>
                    <h-button icon="editor" @click="templateModify">修改</h-button>
                    <h-button icon="delete" @click="templateDelete">删除</h-button>
                </h-button-group>
                <h-datagrid hasPage stripe
                            :height="autoHeight" highlight-row
                            :pageSize="100" :pageSizeOpts="pageSizeOpts"
                            :bindForm="queryForm"
                            :auto-load="false" :on-row-db-click="transferTemplateDbClick"
                            :columns="templateColumns" url="/systemmgnt/template/templateList.json"
                            ref="datagrid">
                </h-datagrid>
            </div>
        </h-split>
        <h-msg-box class="customMsgBox" v-model="showTemplateDialog" width="500" :mask-closable=false>
            <p slot="header">
                <span v-if="actionType == 'add'">新增划款单模板</span>
                <span v-else>修改划款单模板</span>
            </p>
            <h-form :model="templateInfo" :label-width="90" ref="templateForm">
                <h-form-item required label="托管方式">
                    <h-select v-model="trust_type" filterable :clearable="false" @on-change="trustTypeChange">
                        <h-option v-for="item in trustTypeList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                    </h-select>
                </h-form-item>

                <h-form-item v-if="actionType == 'add'" :showMessage="actionType == 'add'" :required="actionType == 'add'" label="产品" prop="fund_id">
                    <div>
                        <!--<h-select style="width: 315px" v-model="templateInfo.fund_id" hideMult isString multiple filterable showBottom>
                            <h-option v-for="item in trustFundList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                        </h-select>-->
                        <h-simple-select style="width: 315px" v-model="templateInfo.fund_id" autoPlacement checkToHead isString multiple filterable showBottom isSelectFilter>
                            <h-select-block :data="multTrustFundList"></h-select-block>
                        </h-simple-select>
                        <h-button type="primary" size="small" style="float: right;height:28px;margin-top:3px;" @click="choseFundList">选择</h-button>
                    </div>
                </h-form-item>

                <h-form-item v-if="actionType == 'modify'" :showMessage="actionType == 'add'" :required="actionType == 'modify'" label="产品" prop="fund_id">
                    <el-select style="width: 100%" v-model="templateInfo.fund_id" filterable >
                        <el-option v-for="item in trustFundList" :value="item.code" :label="item.text" :key="item.code"></el-option>
                    </el-select>
                </h-form-item>

                <h-form-item required label="业务分类" prop="cop_busin_class_list">
                    <h-select v-model="templateInfo.cop_busin_class_list" @on-change="businClassChange" hideMult multiple isString filterable showBottom>
                        <h-option v-for="item in businClassList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item :required="showBusinFlag" v-show="showBusinFlag" label="业务标志" prop="cop_busin_flag_list">
                    <h-simple-select v-model="copBusinFlag" multiple isString filterable checkToHead showBottom>
                        <h-select-block :data="copBusinFlagList"></h-select-block>
                    </h-simple-select>
                </h-form-item>
                <h-form-item v-show="trust_type == '2'" :required="trust_type == '2'" label="托管人" prop="custodial_no">
                    <!--<el-select style="width: 100%;" v-model="templateInfo.custodial_no" filterable>
                        <el-option v-for="item in bankList" :key="item.code" :label="item.text" :value="item.code"></el-option>
                    </el-select>-->
                    <h-simple-select v-model="templateInfo.custodial_no" filterable>
                        <h-select-block :data="bankList"></h-select-block>
                    </h-simple-select>
                </h-form-item>
                <h-form-item required label="划款单模板" prop="file_name">
                    <h-input disabled v-model="templateInfo.file_name"></h-input>
                </h-form-item>
                <h-form-item >
                    <el-upload ref="uploader"
                               class="drag-in-box" drag
                               :action="uploadUrl" :data="uploadData"
                               with-credentials :on-success="fileUploaded"
                               :on-remove="fileRemoved"
                               accept=".pdf,.hct"
                               :limit="2" :file-list="fileList">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                    </el-upload>
                </h-form-item>
                <h-form-item label="公司签章" prop="stamp_id">
                    <h-select v-model="templateInfo.stamp_id" filterable>
                        <h-option v-for="item in stampList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item label="签章2" prop="stamp_src2">
                    <h-select v-model="templateInfo.stamp_src2" @on-change="firstChange" filterable>
                        <h-option v-for="item in stampInfoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item label="签章3" prop="stamp_src3">
                    <h-select v-model="templateInfo.stamp_src3" @on-change="secondChange" filterable>
                        <h-option v-for="item in stampInfoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item label="签章4" prop="stamp_src4">
                    <h-select v-model="templateInfo.stamp_src4" @on-change="thirdChange" filterable>
                        <h-option v-for="item in stampInfoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item label="签章5" prop="stamp_src5">
                    <h-select v-model="templateInfo.stamp_src5" @on-change="forthChange" filterable>
                        <h-option v-for="item in stampInfoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item label="备注" prop="remark">
                    <h-input v-model="templateInfo.remark"></h-input>
                </h-form-item>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="cancelTemplate">取消</h-button>
                <h-button type="primary" @click="saveTemplate">确认</h-button>
            </div>
        </h-msg-box>
        <h-msg-box class="customMsgBox"  width="500px" v-model="showResult">
            <p slot="header">
                <span>新增结果</span>
            </p>
            <div>一共新增{{errorData.total}}条划款单模板配置，失败了{{errorData.fail}}条，以下是具体的错误原因：</div>
            <h-table stripe border v-show="errorData.length>0" :height="350" :data="errorData" :columns="templateColums">

            </h-table>
        </h-msg-box>
        <fund-choose-msg-box
                @saveFundInfo="saveFundInfo"
                ref="templateMsgBox">
        </fund-choose-msg-box>
    </div>
</template>

<script>
    import HDatagrid from "../../../components/HDatagrid"

    import {
        showText,
        showTextList,
        getDictList,
        getFundInfo,
        getStampList,
        getBankList,
        showLabel,
        trustFundList,
        getBusinFlagList
    } from "@/api/utils";

    import {
        insertFixTemplate,
        modifyFixTemplate,
        deleteTemplate
    } from "@/api/systemmgnt/template"
    import HOtherFundTree from "../../../components/HOtherFundTree";
    import FundChooseMsgBox from "../../../components/FundChooseMsgBox";

    export default {
        name: "transferTemplate",
        components: {FundChooseMsgBox, HOtherFundTree, HDatagrid},
        data(){
            return{
                split:0.18,
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                fundIdList:[],
                businClassList:[],
                bankList:[],
                stampList:[],
                stampInfoList:[],
                copBusinFlagList: [],
                copBusinFlag: [],
                showTemplateDialog:false,
                showResult: false,
                showBusinFlag: false,
                actionType:'',
                trust_type:'',
                uploadUrl: window.LOCAL_CONFIG.API_HOME + '/file/upload.json',
                fileList: [],
                errorData: [],
                trustFundList: [],
                multTrustFundList: [],
                allBusinFlagList: [],
                templateColumns:[
                    {
                        title:'序号',
                        type:'index',
                        sortable:true,
                        width:80,
                        align:'center'
                    },
                    {
                        title: "编号",
                        key: "serial_no",
                        width: 80,
                        hiddenCol:true,
                        ellipsis: true,
                        align: 'right'
                    },{
                        title: "产品",
                        key: "fund_name",
                        align: 'left',
                        ellipsis: true,
                        width: 150,
                    },{
                        title: "业务分类",
                        key: "cop_busin_class_list",
                        align: 'left',
                        width:150,
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let copBusinClass = '';
                            let copBusinClassList = rowData.cop_busin_class_list.split(",");
                            for( let i = 0; i < copBusinClassList.length; i++){
                                copBusinClass += showText(copBusinClassList[i],this.businClassList)+','
                            }
                            copBusinClass = copBusinClass.substring(0,copBusinClass.length-1);
                            return h("span",{},copBusinClass);
                        }
                    },{
                        title: "业务标志",
                        key: "cop_busin_flag_list",
                        align: 'left',
                        width:150,
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let copBusinFlagNameList = showTextList(rowData.cop_busin_flag_list, this.allBusinFlagList);
                            return h("span", {}, copBusinFlagNameList);
                        }
                    },{
                        title: "托管人",
                        key: "custodial_no",
                        align: 'left',
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let bankName;
                            if (rowData.custodial_no == 0) {
                                bankName = '自营';
                            } else {
                                bankName = showLabel(rowData.custodial_no,this.bankList)
                            }
                            return h("span",{},bankName);
                        }
                    },{
                        title: "划款单模板",
                        key: "file_name",
                        align: 'left',
                        ellipsis: true,
                    },{
                        title: "公司签章",
                        key: "stamp_id",
                        align: 'left',
                        width: 100,
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let stampId = showText(rowData.stamp_id,this.stampList);
                            return h("span",{},stampId);
                        }
                    },{
                        title: "签章2",
                        key: "stamp_src2",
                        align: 'left',
                        width: 70,
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let stampSrc = showText(rowData.stamp_src2,this.stampInfoList);
                            return h("span",{},stampSrc);
                        }
                    },{
                        title: "签章3",
                        key: "stamp_src3",
                        align: 'left',
                        width: 70,
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let stampSrc = showText(rowData.stamp_src3,this.stampInfoList);
                            return h("span",{},stampSrc);
                        }
                    },{
                        title: "签章4",
                        key: "stamp_src4",
                        align: 'left',
                        width: 70,
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let stampSrc = showText(rowData.stamp_src4,this.stampInfoList);
                            return h("span",{},stampSrc);
                        }
                    },{
                        title: "签章5",
                        key: "stamp_src5",
                        align: 'left',
                        width: 70,
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let stampSrc = showText(rowData.stamp_src5,this.stampInfoList);
                            return h("span",{},stampSrc);
                        }
                    },{
                        title: "备注",
                        key: "remark",
                        align: 'left',
                        width: 100,
                        ellipsis: true,
                    }, {
                        title: '划款单',
                        align: 'left',
                        width: 130,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            if(rowData.file_name){
                                return h('div', [

                                    h('Button', {
                                        props: {
                                            type: 'primary',
                                            size: 'small'
                                        },
                                        style: {
                                            marginRight: '5px'
                                        },
                                        on: {
                                            click: () => {
                                                this.previewPDF(rowData.serial_no)
                                            }
                                        }
                                    }, '预览'),
                                    h('Button', {
                                        props: {
                                            type: 'primary',
                                            size: 'small'
                                        },
                                        on: {
                                            click: () => {
                                                this.downloadBySerialNo(rowData.serial_no)
                                            }
                                        }
                                    }, '下载')
                                ]);
                            }else{
                                var typeName = '-'
                                return h("span", {}, typeName);
                            }
                        }
                    }
                ],
                templateColums:[
                    {width: 80,type:"index",title:"序号",align:"center"},
                    {key:"errorInfo",title:"错误原因",align:"center"},
                ],
                trustTypeList: [
                    {
                        code: '1',
                        text: '自营'
                    }, {
                        code: '2',
                        text: '托管'
                    }
                ],
                queryForm:{
                    fundId:''
                },
                uploadData:{
                    fileType:'4',
                    fundId:''
                },
                templateInfo:{
                    serial_no:'',
                    fund_id:'',
                    custodial_no:'',
                    file_id:'',
                    cop_busin_class_list:'',
                    cop_busin_flag_list:'-1',
                    file_name:'',
                    stamp_id:'',
                    stamp_src1:'',
                    stamp_src2:'',
                    stamp_src3:'',
                    stamp_src4:'',
                    stamp_src5:'',
                    remark:'',
                }
            }
        },
        activated(){
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => {
                if (res.data && res.data.length > 0) {
                    this.fundIdList = res.data;
                }
            });
            getStampList(3).then(res => {
                if(res.data && res.data.length >0){
                    this.stampList = res.data;
                }
            })
            getBankList('1').then(res => this.bankList = res.data.map(res => {
                let item = {};
                item.value = res.code;
                item.label = res.text;
                return item;
            }))
        },
        watch:{
            showTemplateDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.fileList = [];
                    this.closeMask();
                }
            },
            copBusinFlag(newValue,oldValue){
                if((newValue.indexOf('-1') != -1 && oldValue.indexOf('-1') == -1) || newValue == '-1'){
                    this.templateInfo.cop_busin_flag_list = '-1';
                }else if((newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') != -1)
                    || (newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') == -1)){
                    this.templateInfo.cop_busin_flag_list = newValue;
                }else{
                    let newValueList = newValue.split(',');
                    this.templateInfo.cop_busin_flag_list = this.removeArr(newValueList,'-1');
                }
                this.copBusinFlag = this.templateInfo.cop_busin_flag_list;
            }
        },
        mounted(){
            getDictList(90723).then(res => this.stampInfoList = res.data.filter(value => value.code != '1'));
            getDictList(90523).then(res => this.businClassList = res.data);
            getBusinFlagList('','',true).then(res => {
                this.allBusinFlagList = [{code:'-1', text:'全部'}];
                if (res && res.data) {
                    this.allBusinFlagList = this.allBusinFlagList.concat(res.data);
                }
            });
            this.re_layout();
        },
        methods:{
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 35;
            },
            fundClick(data){
                if(data){
                    this.queryForm.fundId = data.code;
                    this.$refs.datagrid.dataChange(1);
                }
            },
            templateAdd(){
                this.fileList = [];
                this.$refs.templateForm.resetFields();
                this.templateInfo={
                    serial_no:null,
                    fund_id:'',
                    cop_busin_class_list:'',
                    cop_busin_flag_list: '-1',
                    file_name:null,
                    file_id:'',
                    transfer_template_path:'',
                    remark:'',
                    stamp_id:'',
                    stamp_src1:'',
                    stamp_src2:'',
                    stamp_src3:'',
                    stamp_src4:'',
                    stamp_src5:'',
                    custodial_no:'',
                    fund_name:''
                };
                this.copBusinFlag = this.templateInfo.cop_busin_flag_list;
                if(this.queryForm.fundId){
                    this.templateInfo.fund_id = this.queryForm.fundId;
                }else{
                    this.templateInfo.fund_id = this.fundIdList[0].code;
                }
                getFundInfo("/common/getFundInfo.json?fundId=" + this.templateInfo.fund_id).then(res => {
                    if (res.data.fund_settle_type == '1' || res.data.fund_settle_type == '4'){
                        this.trust_type = '1';
                    } else {
                        this.trust_type = '2';
                    }
                    this.trustTypeChange(this.trust_type);
                });
                this.showTemplateDialog = true;
                this.actionType='add';
            },
            templateModify(){
                let select = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(select.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录","错误",{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    });
                    return
                }
                this.transferTemplateDbClick(select[0]);
            },
            transferTemplateDbClick(data){
                this.fileList = [{name:data.file_name}];
                this.templateInfo = data;
                if (data.custodial_no == '0') {
                    this.trust_type = '1';
                    this.trustTypeChange(this.trust_type);
                    this.templateInfo.custodial_no = data.custodial_no.toString();
                } else if (data.custodial_no) {
                    this.trust_type = '2';
                    this.trustTypeChange(this.trust_type);
                    this.templateInfo.custodial_no = data.custodial_no.toString();
                } else {
                    this.templateInfo.custodial_no = '';
                }
                this.copBusinFlag = this.templateInfo.cop_busin_flag_list;
                this.templateInfo.fund_id = data.fund_id.toString();
                if(data.stamp_id){
                    this.templateInfo.stamp_id = data.stamp_id.toString();
                }
                this.showTemplateDialog = true;
                this.actionType = 'modify';
            },
            templateDelete(){
                let select = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(select.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录","错误",{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    });
                    return
                }

                this.openMask();
                this.$confirm('确认要删除这条记录吗？', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteTemplate(select[0].serial_no,select[0].transfer_template_path).then(res =>{
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,"成功",{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            });
                            this.refresh()
                        }else{
                            this.$alert(res.data.msg,"错误",{
                                confirmButtonText:'确定',
                                type:'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            });
                        }
                    })
                }).catch(() => {
                    this.closeMask();
                })
            },
            cancelTemplate(){
                this.showTemplateDialog = false;
            },
            saveTemplate(){
                let isValidata = true;
                this.$refs.templateForm.validate((validate) => isValidata = isValidata && validate);

                if(!this.templateInfo.fund_id){
                    this.$alert("产品没有维护,请重新检查!","错误",{
                        confirmButtonText:'确定',
                        type:'error'
                    });
                    return
                }

                if(!this.templateInfo.file_name){
                    this.$alert("划款单模板没有上传,请重新检查!","错误",{
                        confirmButtonText:'确定',
                        type:'error'
                    });
                    return
                }

                if(!isValidata){
                    this.$alert("输入项不满足要求，请重新检查!","错误",{
                        confirmButtonText:'确定',
                        type:'error'
                    });
                    return
                }

                if (this.trust_type == '1') {
                    this.templateInfo.custodial_no = '0';
                }

                if(this.actionType == 'add'){
                    insertFixTemplate(this.templateInfo).then(res => {
                        if(res.data && res.data.code =='0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.showTemplateDialog = false;
                                }
                            }).then(() => {
                                this.showTemplateDialog = false;
                            })
                            this.refresh();
                        }else if(res.data.code=='1120002'){
                            this.showTemplateDialog = false;
                            this.showResult = true;
                            this.errorData = res.data.data;
                            this.errorData.total = this.templateInfo.fund_id.split(",").length;
                            this.errorData.fail = res.data.data.length;
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }else{
                    modifyFixTemplate(this.templateInfo).then(res => {
                        if(res.data && res.data.code =='0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.showTemplateDialog = false;
                                }
                            }).then(() => {
                                this.showTemplateDialog = false;
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }
            },
            firstChange:function (value) {
                if(value == this.templateInfo.stamp_src3){
                    this.templateInfo.stamp_src2 = value;
                    this.templateInfo.stamp_src3 = '';
                }
                if(value == this.templateInfo.stamp_src4){
                    this.templateInfo.stamp_src2 = value;
                    this.templateInfo.stamp_src4 = '';
                }
                if(value == this.templateInfo.stamp_src5){
                    this.templateInfo.stamp_src2 = value;
                    this.templateInfo.stamp_src5 = '';
                }
            },
            secondChange:function (value) {
                if(value == this.templateInfo.stamp_src2 && this.templateInfo.stamp_src3 && this.templateInfo.stamp_src2){
                    this.$confirm('签章3不能和签章2一样', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.templateInfo.stamp_src3 = '';
                    }).catch(() => {
                        this.templateInfo.stamp_src3 = '';
                    });
                }
                if(value == this.templateInfo.stamp_src4){
                    this.templateInfo.stamp_src3 = value;
                    this.templateInfo.stamp_src4 = '';
                }
                if(value == this.templateInfo.stamp_src5){
                    this.templateInfo.stamp_src3 = value;
                    this.templateInfo.stamp_src5 = '';
                }
            },
            thirdChange:function (value) {
                if(value == this.templateInfo.stamp_src2 && this.templateInfo.stamp_src4 && this.templateInfo.stamp_src2){
                    this.$confirm('签章4不能和签章2一样', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.templateInfo.stamp_src4 = '';
                    }).catch(() => {
                        this.templateInfo.stamp_src4 = '';
                    });
                }
                if(value == this.templateInfo.stamp_src3 && this.templateInfo.stamp_src4 && this.templateInfo.stamp_src3){
                    this.$confirm('签章4不能和签章3一样', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.templateInfo.stamp_src4 = '';
                    }).catch(() => {
                        this.templateInfo.stamp_src4 = '';
                    });
                }
                if(value == this.templateInfo.stamp_src5){
                    this.templateInfo.stamp_src4 = value;
                    this.templateInfo.stamp_src5 = '';
                }
            },
            forthChange:function (value) {
                if(value == this.templateInfo.stamp_src2 && this.templateInfo.stamp_src5 && this.templateInfo.stamp_src2){
                    this.$confirm('签章5不能和签章2一样', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.templateInfo.stamp_src5 = '';
                    }).catch(() => {
                        this.templateInfo.stamp_src5 = '';
                    });
                }
                if(value == this.templateInfo.stamp_src3  && this.templateInfo.stamp_src5 && this.templateInfo.stamp_src3){
                    this.$confirm('签章5不能和签章3一样', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.templateInfo.stamp_src5 = '';
                    }).catch(() => {
                        this.templateInfo.stamp_src5 = '';
                    });
                }
                if(value == this.templateInfo.stamp_src4  && this.templateInfo.stamp_src5 && this.templateInfo.stamp_src4){
                    this.$confirm('签章5不能和签章4一样', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.templateInfo.stamp_src5 = '';
                    }).catch(() => {
                        this.templateInfo.stamp_src5 = '';
                    });
                }
            },
            fileUploaded(response,file,fileList){
                if(response.code == '0'){
                    if(fileList.length == 1){
                        this.templateInfo.file_id = response.data;
                        this.templateInfo.file_name = fileList[0].name;
                    }else if(fileList.length == 2){
                        this.templateInfo.file_id = response.data;
                        this.templateInfo.file_name = fileList[1].name;
                        fileList.splice(0,1);
                    }else{
                        this.templateInfo.file_id = '';
                        this.templateInfo.file_name = '';
                    }
                }else{
                    this.$alert(response.msg, '错误',{
                        confirmButtonText:'确定',
                        type: 'error'
                    });
                    fileList.splice(fileList.length-1,1);
                }

            },
            fileRemoved(file,fileList){
                this.templateInfo.file_name = fileList.reduce(function(x,y) {
                    return x + "," + y.response.data
                }, '').replace(",","")
            },
            choseFundList(){
                let wrapperHeight = document.getElementById("full-h-wrapper").offsetHeight;
                this.$refs.templateMsgBox.showMsgBox(this.templateInfo.fund_id,wrapperHeight,'1',this.trust_type)
            },
            saveFundInfo(data){
                this.templateInfo.fund_id = data;
            },
            previewPDF(serialNo){
                let pdfUrl = "/cop-web/static/plugins/pdfjs/generic/web/viewer.html?file="+encodeURIComponent(window.LOCAL_CONFIG.API_HOME + '/file/downloadFixTemplate.json?serialNo=' +serialNo);
                var myWindow=window.open(pdfUrl,'划款单预览','width=800,height=600');
                myWindow.focus();
            },
            downloadBySerialNo(serialNo){
                window.open(
                    window.LOCAL_CONFIG.API_HOME + '/file/downloadFixTemplate.json?serialNo=' +serialNo)
            },
            onSplitMove(){
                this.$refs.datagrid.handleResize();
            },
            trustTypeChange: function (val) {
                trustFundList(val).then(res => {
                    if (this.actionType == 'add') {
                        this.multTrustFundList = res.data.map(item =>{
                            let json = {};
                            json.value = item.code;
                            json.label = item.text;
                            return json;
                        })
                    } else {
                        this.trustFundList = res.data;
                    }
                    if (!showText(this.templateInfo.fund_id, res.data) && res.data){
                        this.templateInfo.fund_id = res.data[0].code;
                    }
                });
                if (val == '2' && this.templateInfo.custodial_no && this.templateInfo.custodial_no == 0) {
                    if (this.bankList) {
                        this.templateInfo.custodial_no = this.bankList[0].value;
                    } else {
                        this.templateInfo.custodial_no = ''
                    }
                }
            },
            businClassChange: function (businClass) {
                if (businClass.length > 1 || businClass.length == 0) {
                    this.copBusinFlagList = [{value: '-1', label: '全部'}];
                    this.showBusinFlag = false;
                    this.templateInfo.cop_busin_flag_list = '-1';
                } else {
                    getBusinFlagList(businClass.join(','), '', true).then(res => {
                        if (res && res.data) {
                            this.copBusinFlagList = [{value: '-1', label: '全部'}];
                            this.copBusinFlagList = this.copBusinFlagList.concat(res.data.map(res => {
                                let item = {};
                                item.value = res.code;
                                item.label = res.text;
                                return item;
                            }))
                        }
                        if (!showTextList(this.templateInfo.cop_busin_flag_list, this.copBusinFlagList)){
                            this.templateInfo.cop_busin_flag_list = '-1';
                        }
                    });
                    this.showBusinFlag = true;
                }
                this.copBusinFlag = this.templateInfo.cop_busin_flag_list;
            },
            removeArr(arr, target) {
                for (let i = 0; i < arr.length; i++) {
                    if (arr[i] == target) {
                        arr.splice(i, 1);
                    }
                }
                return arr;
            }
        }
    }
</script>

<style lang="scss">
    .page-transfer-template .drag-in-box {

        .el-upload {
            display: block;
            height: 100%;

            .el-upload-dragger {
                margin-top: 10px;
                width: 100%;
                height: 100%;
            }
        }
    }
</style>
<style >

</style>


