<template>
  <div class="page-fundaccount">
    <div slot="right" class="right-content" ref="rightContent">
      <!-- 顶部筛选栏 -->
      <h-row>
        <h-col>
          <h-form ref="searchToolbar" cols="5">
            <h-form-item label="托管人" :label-width="50">
              <h-select class="select" v-model="fundTrustBanks" filterable multiple collapseTags showBottom>
                <h-option v-for="bank in trustBankList" :value="bank.code" :key="bank.code">{{ bank.text }}</h-option>
              </h-select>
            </h-form-item>
            <h-form-item label="产品类型" :label-width="80">
              <h-select class="select" v-model="fundTypes" filterable multiple collapseTags showBottom>
                <h-option v-for="type in fundTypeList" :value="type.code" :key="type.code">{{ type.text }}</h-option>
              </h-select>
            </h-form-item>
            <h-form-item label="产品状态" :label-width="80">
              <h-select class="select" v-model="fundStatuses" multiple collapseTags>
                <h-option v-for="status in fundStatusList" :value="status.code" :key="status.code">{{ status.text }}</h-option>
              </h-select>
            </h-form-item>
            <h-form-item label="产品来源" :label-width="80">
              <h-select class="select" v-model="dataSource" filterable collapseTags>
                <h-option v-for="source in dataSourceList" :value="source.code" :key="source.code">{{ source.text }}</h-option>
              </h-select>
            </h-form-item>
            <h-form-item :label-width="20">
              <h-button type="primary" size="small" @click="onSearchFunds">查询</h-button>
            </h-form-item>
            <h-form-item v-show="false">
              <h-file-export sheetType="table"
                             fileName='产品查询导出'
                             :sheetNames="['产品查询']"
                             :sheetHeaders="fundSheetHeadersColumns"
                             :sheetTableData="fundSheetTableData">
                <h-button icon="undo" ref="fundRecordExportBtn">导出</h-button>
              </h-file-export>
            </h-form-item>
            <h-form-item v-show="false">
              <h-file-export sheetType="table"
                             fileName='账户查询导出'
                             :sheetNames="['账户查询']"
                             :sheetHeaders="accountSheetHeadersColumns"
                             :sheetTableData="accountSheetTableData">
                <h-button icon="undo" ref="accountRecordExportBtn">导出</h-button>
              </h-file-export>
            </h-form-item>
          </h-form>
        </h-col>
      </h-row>

      <h-split v-model="split2" mode="vertical" min="200" max="200" @on-moving="handleSplitMove">
        <!-- 产品列表  -->
        <div class="fund-list" slot="top">
          <!-- 产品操作工具栏  -->
          <div ref="fundToolbar" class="ctrls">
            <h-button-group>
              <h-button icon="add" @click="addFund">新增</h-button>
              <h-button icon="editor" @click="editFund">修改</h-button>
              <h-button icon="delete" @click="delFund">删除</h-button>
              <h-button icon="t-b-locking" @click="disableFund">冻结</h-button>
              <h-button v-if="synTypeByB45" icon="loop" @click="synFund">同步</h-button>
              <h-button icon="undo" @click="downloadFunds">导出</h-button>
            </h-button-group>

            <h-input v-model="keyword" :maxlength="32" placeholder="输入产品代码或名称搜索" style="width: 215px">
              <div slot="append" >
                <a href="javascript:void(0)">
                  <h-icon name="search" @click.native.stop="onSearchFunds"></h-icon>
                </a>
              </div>
            </h-input>
          </div>
          <FundList
            :searchCondition="fundListSearchCondition"
            :tableHeight="fundListTableHeight"
            @fundSelected="handleFundSelected"
            @fundDetail="showFundDetail"
            ref="fundList"></FundList>
        </div>

        <div slot="bottom">
          <!-- 用户操作工具栏 -->
          <div class="account-ctrls" ref="accountToolbar">
            <h-button-group>
              <h-button icon="add" @click="newAccount">新增</h-button>
              <h-button icon="editor" @click="editAccount">修改</h-button>
              <h-button icon="delete" @click="delAccount">删除</h-button>
              <h-button icon="android-upload" @click="uploadExcel">导入</h-button>
              <h-button icon="undo" @click="downloadAccounts">导出</h-button>
            </h-button-group>
          </div>
          <!-- 用户列表 -->
          <div class="account-list">
            <AccountList
              :accounts="curFundAccounts"
              :tableHeight="accountListTableHeight"
              :fundSettleType="fundSettleType"
              @accountDetail="showAccountDetail"
              ref="accountList"></AccountList>
          </div>
        </div>
      </h-split>

    </div>

    <ManageFundDialog
      :actionType="actionType"
      :fundInfo="curFund"
      @fundAdded="onFundAdded"
      @refreshFund="onSearchFunds"
      v-model="manageFundDialogVisible"></ManageFundDialog>
    <ManageAccountDialog
      :actionType="actionType"
      :fundInfo="curFund"
      :accountInfo="curAccount"
      @refreshAccount="refreshAccountList"
      v-model="manageAccountDialogVisible"></ManageAccountDialog>
    <ImportAccountTransfer :on-import-success="onSearchFunds" ref="importAccountTransfer"></ImportAccountTransfer>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import FundTree from '@/components/FundTree'
import {
  operatorList,
  getDictList,
  getBankList,
  getBrokerList,
  getCityTree,
  getKeys,
  getAgencyList,
  getFundInfo,
  pageFund,
  showText,
  showTextList,
  getSysParamValue
} from '@/api/utils'

import {
  synFund,
  disableFund,
  deleteFund,
  deleteAccount,
  getFundAccount,
  getFundAccountList
} from '@/api/fundAccount/fundAccount'

import ImportAccountTransfer from '@/components/ImportAccountTransfer'

import FundList from './comps/FundList/fund'
import AccountList from './comps/AccountList/account'
import ManageFundDialog from './comps/ManageFundDialog/manageFund'
import ManageAccountDialog from './comps/ManageAccountDialog/manageAccount'

export default {
  name: 'fundaccount',
  data() {
    return {
      // 加密密钥
      keys: [],
      // 新增产品对话框是否可见
      manageFundDialogVisible: false,
      // 新增产品对话框是否可见
      manageAccountDialogVisible: false,
      // 用于筛选的托管人
      fundTrustBanks: [],
      // 用于筛选的产品分类
      fundTypes: [],
      // 用于筛选的产品状态
      fundStatuses: [],
      // 用于筛选的产品来源
      dataSource: '',
      // 用于筛选的关键字
      keyword: '',
      // 当前的产品列表
      funds: [],
      // 产品详情
      curFund: null,
      // 选中的产品的账户列表
      curFundAccounts: [],
      // 选中的账户
      curAccount: null,
      // 操作类型: add edit detail
      actionType: 'add',
      // 产品列表查询条件
      fundListSearchCondition: null,
      // 产品表格高度
      fundListTableHeight: 0,
      // 账户表格高度
      accountListTableHeight: 0,
      //账户列表查询条件
      accountListSearchCondition: null,
      //产品编号
      fundId: null,
      // 产品列表
      fundIdList: [],
      // 产品excel文件下载表头
      fundSheetHeadersColumns: [this.getFundSheetHeadersColumns()],
      // 产品excel文件下载数据
      fundSheetTableData: [[]],
      // 账户excel文件下载表头
      accountSheetHeadersColumns: [this.getAccountSheetHeaderColumns()],
      // 账户excel文件下载数据
      accountSheetTableData: [[]],
      fundSettleType: '',
      // 同步模式是否是B45
      synTypeByB45: false,
      split: 0.15,
      split2: 0.5
    }
  },
  computed: {
    ...mapState({
      fundTypeList: state => state.dict.dict_610087,
      fundSettleTypes: state => state.dict.dict_90531,
      taSettleTypes: state => state.dict.dict_90530,
      dataSourceList: state => state.dict.dict_90528,
      accountTypeList: state => state.dict.dict_60058,
      accountStatusList: state => state.dict.dict_200008,
      bankFuturesTypeList: state => state.dict.dict_90532,
      accountClassList: state => state.dict.dict_90525,
      fundStatusList: state => state.dict.dict_40002,
      isFlagList: state => state.dict.dict_610001,
      taMatchTypeList: state => state.dict.dict_91386,
      operatorList: state => state.dict.operatorList,
      bankList: state => state.dict.bankList,
      trustBankList: state => state.dict.trustBankList,
      currencyNoList: state => state.dict.dict_610406,
      agencyList: state => state.dict.agencyList,
      brokerList: state => state.dict.brokerList,
      cityTree: state => state.dict.cityTree
    })
  },
  watch: {

  },
  activated() {
    this.updateTablesHeight()
    getBankList('1').then(res => {
        this.setTrustBankList(res.data)
    });
    getFundInfo('/common/fundNamelist.json?type='+'id').then(res => {
      if (res.data && res.data.length > 0) {
        this.fundIdList = res.data;
      }
    });
    getSysParamValue('10065').then(res => {
      if (res.data) {
        if (res.data.data == '2') {
          this.synTypeByB45 = false
        }else {
          this.synTypeByB45 = true
        }
      }
    })
    SSObservable.subscribe('moduleHeightChange',this.updateTablesHeight)
    this.onSearchFunds();
  },
  deactivated() {
    SSObservable.unsubscribe('moduleHeightChange',this.updateTablesHeight)
  },
  mounted() {
    this.getNecessaryDatas()
    // window.onresize = () => {
    //   this.updateTablesHeight()
    // }
    setTimeout(() => {
      this.updateTablesHeight()
    }, 100);
    this.onSearchFunds();
  },
  methods: {
    ...mapMutations({
      setDict610087: 'DICT_610087',
      setDict_90531: 'DICT_90531',
      setDict_90530: 'DICT_90530',
      setDict_90528: 'DICT_90528',
      setDict_60058: 'DICT_60058',
      setDict_200008: 'DICT_200008',
      setDict_90532: 'DICT_90532',
      setDict_90525: 'DICT_90525',
      setDict_91386: 'DICT_91386',
      setDict_40002: 'DICT_40002',
      setDict_610001: 'DICT_610001',
      setDict_610406: 'DICT_610406',
      setOperatorList: 'OPERATOR_LIST',
      setBankList: 'BANK_LIST',
      setTrustBankList: 'TRUST_BANK_LIST',
      setAgencyList: 'AGENCY_LIST',
      setBrokerList: 'BROKER_LIST',
      setCityTree: 'CITY_TREE'
    }),
    getNecessaryDatas() {
      getDictList(610087).then(res => {
        this.setDict610087(res.data)
      })
      getDictList(610406).then(res => {
        this.setDict_610406(res.data)
      })
      getDictList(90531).then(res => {
        this.setDict_90531(res.data)
      })
      getDictList(90530).then(res => {
        this.setDict_90530(res.data)
      })
      getDictList(90528).then(res => {
        this.setDict_90528(res.data)
      })
      getDictList(60058).then(res => {
        this.setDict_60058(res.data)
      })
      getDictList(200008).then(res => {
        this.setDict_200008(res.data)
      })
      getDictList(90532).then(res => {
        this.setDict_90532(res.data)
      })
      getDictList(90525).then(res => {
        this.setDict_90525(res.data)
      })
      getDictList(91386).then(res => {
        this.setDict_91386(res.data)
      })
      getDictList(91386).then(res => {
        this.setDict_91386(res.data)
      })
      getDictList(40002).then(res => {
        this.setDict_40002(res.data)
      })
      getDictList(610001).then(res => {
        this.setDict_610001(res.data)
      })
      operatorList().then(res => {
        this.setOperatorList(res.data)
      })
      getBankList().then(res => {
        this.setBankList(res.data)
      })
      getAgencyList().then(res => {
          this.setAgencyList(res.data)
      })
      getBrokerList().then(res => {
        this.setBrokerList(res.data)
      })
      getCityTree().then(res => {
        this.setCityTree(res.data)
      })
      // 加密 key 获取
      getKeys().then(res => {
        this.keys = res.data
      })
    },
    onSearchFunds() {
      this.fundListSearchCondition = {
        fundTrustBanks: this.fundTrustBanks.join(','),
        fundTypes: this.fundTypes.join(','),
        fundStatuses: this.fundStatuses.join(','),
        dataSource: this.dataSource,
        keyword: this.keyword
      }

      this.refreshAccountList(null);
    },
    handleFundSelected(fundData) {
      this.curFund = fundData;
      this.fundSettleType = fundData.fund_settle_type;
      this.refreshAccountList(fundData.fund_id);
    },
    refreshAccountList(fundId){
      getFundAccount(fundId).then(res => {
        this.curFundAccounts = res.data.list;
      })
    },
    showFundDetail(fundData) {
      this.actionType = 'edit'
      this.curFund = fundData
      this.curFund.fundIds = this.fundId;
      this.manageFundDialogVisible = true
    },
    addFund() {
      this.actionType = 'add'
      this.manageFundDialogVisible = true
    },
    editFund(){
      let selects = this.$refs.fundList.$refs.fundContent.getSelection();
      if(selects.length == 0){
        this.$alert('请选择一个产品','错误',{
          confirmButtonText: '确定',
          type: 'error'
        });
        return;
      }
      this.showFundDetail(selects[0]);
    },
    delFund(){
      let selects = this.$refs.fundList.$refs.fundContent.getSelection();
      if(selects.length == 0){
        this.$alert('请选择一个产品','错误',{
          confirmButtonText: '确定',
          type: 'error'
        });
        return;
      }

      this.openMask();
      this.$confirm('确认要删除该产品吗？', '确认', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteFund(selects[0].fund_id).then(res=>{
          if (res.data && res.data.code == '0') {
            this.$alert(res.data.msg,'成功',{
              confirmButtonText: '确定',
              type: 'success',
              callback:action => {
                this.closeMask();
              }
            }).then(() => {
              this.closeMask();
            })
            this.onSearchFunds();
          } else {
            this.$alert(res.data.msg,'错误',{
              confirmButtonText: '确定',
              type: 'error',
              callback:action => {
                this.closeMask();
              }
            }).then(() => {
              this.closeMask();
            })
          }
        })
      }).catch(() => {this.closeMask()});
    },
    disableFund(){
      let selects = this.$refs.fundList.$refs.fundContent.getSelection();
      if(selects.length == 0){
        this.$alert('请选择一个产品','错误',{
          confirmButtonText: '确定',
          type: 'error'
        });
        return;
      }

      this.openMask();
      this.$confirm('确认要冻结该产品吗？', '确认', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        disableFund(selects[0].fund_id).then(res=>{
          if (res.data && res.data.code == '0') {
            this.$alert(res.data.msg,'成功',{
              confirmButtonText: '确定',
              type: 'success',
              callback:action => {
                this.closeMask();
              }
            }).then(() => {
              this.closeMask();
            })
            this.onSearchFunds();
          } else {
            this.$alert(res.data.msg,'错误',{
              confirmButtonText: '确定',
              type: 'error',
              callback:action => {
                this.closeMask();
              }
            }).then(() => {
              this.closeMask();
            })
          }
        })
      }).catch(() => {this.closeMask()});
    },
    onFundAdded() {
        this.onSearchFunds();
    },
    synFund() {
      synFund().then(res => {
        if (res.data && res.data.code === '0') {
          this.openMask()
          this.$alert(res.data.msg, '成功', {
            confirmButtonText: '确定',
            type: 'success',
            callback: action => {
              this.closeMask()
            }
          }).then(() => {
            this.closeMask()
          })
          this.onSearchFunds();
        } else {
          this.openMask()
          this.$alert(res.data.msg, '错误', {
            confirmButtonText: '确定',
            type: 'error',
            callback: action => {
              this.closeMask()
            }
          }).then(() => {
            this.closeMask()
          })
        }
      })
    },
    uploadExcel() {
      this.$refs.importAccountTransfer.showDialog('1', null)
    },
    showAccountDetail(accountData) {
      this.actionType = 'edit'
      this.curAccount = accountData
      this.manageAccountDialogVisible = true
    },
    newAccount() {
      if (this.curFund === null) {
        this.alert('请选择一个产品', 'error')
        return
      }
      this.actionType = 'add'
      this.manageAccountDialogVisible = true
    },
    delAccount() {
      let selectedAccounts = this.$refs.accountList.getSelections()
      if (selectedAccounts.length === 0) {
        this.alert('请选择至少一个账户进行删除', 'error')
        return
      }

      let bankAccountIdList = "";
      selectedAccounts.forEach(account => {
        bankAccountIdList = bankAccountIdList + account.bank_account_id + ",";
      });
      bankAccountIdList = bankAccountIdList.substring(0, bankAccountIdList.length - 1);

      this.openMask()
      this.$confirm('确认要删除该账户吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteAccount(bankAccountIdList, this.curFund.fund_id).then(res => {
          if (res.data.success) {
            this.$alert(res.data.msg, '成功', {
              confirmButtonText: '确定',
              type: 'success',
              callback: action => {
                this.closeMask()
              }
            }).then(() => {
              this.closeMask()
            });
            this.refreshAccountList(this.curFund.fund_id);
          } else {
            this.$alert(res.data.msg, '错误', {
              confirmButtonText: '确定',
              type: 'error',
              callback: action => {
                this.closeMask()
              }
            }).then(() => {
              this.closeMask()
            })
          }
        })
      }).catch(() => {
        this.closeMask()
      })
    },
    editAccount() {
      let selectedAccounts = this.$refs.accountList.getSelections()
      if (selectedAccounts.length === 0 || selectedAccounts.length > 1) {
        this.alert('请选择一个账户进行编辑', 'error')
        return
      }
      this.curAccount = selectedAccounts[0];
      if(this.curAccount.bic_code == null){
        this.curAccount.bic_code = '';
      }
      this.actionType = 'edit';
      this.manageAccountDialogVisible = true
    },
    alert(msg, type) {
      this.openMask()
      this.$alert(msg, '错误', {
        confirmButtonText: '确定',
        type: type,
        callback: action => {
          this.closeMask()
        }
      }).then(() => {
        this.closeMask()
      })
    },
    handleSplitMove() {
      this.updateTablesHeight()
    },
    updateTablesHeight() {
        this.$nextTick(() => {
            let totalHeight = this.$refs.rightContent.clientHeight;
            let accountToolbarHeight = this.$refs.accountToolbar.clientHeight;
            let fundToolbarHeight = this.$refs.fundToolbar.clientHeight;
            this.fundListTableHeight = totalHeight * this.split2 - fundToolbarHeight;
            this.accountListTableHeight = totalHeight - this.fundListTableHeight - fundToolbarHeight - accountToolbarHeight - 40;
        });
    },
    downloadFunds(){
      const loading = this.$loading({
        lock: true,
        text: '处理中',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      pageFund(this.fundTrustBanks.join(','), this.fundTypes.join(','), 1, null, this.fundStatuses.join(','), this.dataSource, this.keyword).then(res =>{
        loading.close();
        if(res && res.data && res.data.list.length > 0){
          this.fundSheetTableData = [res.data.list];
          this.$nextTick(() => {
            this.$refs.fundRecordExportBtn.$el.click();
          });
        }else if(res && res.data && res.data.list.length == 0){
          this.$alert('暂无导出数据!', '错误', {
            confirmButtonText: '确定',
            type: 'error'
          });
        }else{
          this.$alert(res.data.msg, '错误', {
            confirmButtonText: '确定',
            type: 'error'
          });
        }
      }).catch(error =>{
        loading.close();
        this.$alert(error, '错误', {
          confirmButtonText: '确定',
          type: 'error'
        });
      })
    },
    downloadAccounts(){
        const loading = this.$loading({
          lock: true,
          text: '处理中',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        getFundAccountList(this.fundTrustBanks.join(','), this.fundTypes.join(','), this.fundStatuses.join(','), this.dataSource, this.keyword).then(res =>{
          loading.close();
          if(res && res.data.data){
            this.accountSheetTableData = [res.data.data];
            this.$nextTick(() => {
              this.$refs.accountRecordExportBtn.$el.click();
            });
          }else{
            this.$alert(res.data.msg, '错误', {
              confirmButtonText: '确定',
              type: 'error'
            });
          }
        }).catch(error =>{
          loading.close();
          this.$alert(error, '错误', {
            confirmButtonText: '确定',
            type: 'error'
          });
        })
    },

    getFundSheetHeadersColumns() {
      return [
        {
          title: '产品名称',
          key: 'fund_name'
        }, {
          title: '产品全称',
          key: 'fund_caption'
        }, {
          title: '产品代码',
          key: 'fund_code'
        }, {
          title: '实际产品代码',
          key: 'actual_code'
        }, {
          title: '产品类型',
          key: 'fund_type',
          exportRender: (value, rowdata) => {
            return showText(value, this.fundTypeList);
          }
        }, {
          title: '产品状态',
          key: 'fund_status',
          exportRender: (value, rowdata) => {
            return showText(value, this.fundStatusList);
          }
        }, {
          title: '托管人',
          key: 'fund_trust_bank',
          exportRender: (value, rowdata) => {
            return showText(value, this.trustBankList);
          }
        }, {
          title: '托管结算模式',
          key: 'fund_settle_type',
          exportRender: (value, rowdata) => {
            return showText(value, this.fundSettleTypes);
          }
        }, {
          title: 'TA金额交收模式',
          key: 'ta_settle_type',
          exportRender: (value, rowdata) => {
            return showText(value, this.taSettleTypes);
          }
        }, {
          title: '银行间ID',
          key: 'interbank_id'
        }, {
          title: '上海股东代码',
          key: 'stockholder_id'
        }, {
          title: '深圳股东代码',
          key: 'stockholder_id_sz'
        }, {
          title: '深圳配售对象编码',
          key: 'object_code'
        }, {
          title: 'TA深圳备付金账号',
          key: 'sz_ta_account'
        }, {
          title: 'TA上海备付金账号',
          key: 'sh_ta_account'
        }, {
          title: '是否划款',
          key: 'transfer_flag',
          exportRender: (value, rowdata) => {
            return showText(value, this.isFlagList);
          }
        }, {
          title: '划款单编号',
          key: 'paper_no'
        }, {
          title: '特约销售机构',
          key: 'out_agency_code_list',
          exportRender: (value, rowdata) => {
            return showTextList(value, this.agencyList);
          }
        }, {
          title: 'TA轧差模式',
          key: 'ta_match_type',
          exportRender: (value, rowdata) => {
            return showText(value, this.taMatchTypeList);
          }
        }, {
          title: '投资经理',
          key: 'fund_mgr_operator',
          exportRender: (value, rowdata) =>{
            return showText(value, this.operatorList)
          }
        }, {
          title: '主办会计',
          key: 'fund_accountant',
          exportRender: (value, rowdata) =>{
            return showText(value, this.operatorList)
          }
        }, {
          title: '产品邮箱',
          key: 'fund_mail'
        }, {
          title: '成立日期',
          key: 'establish_date'
        }, {
          title: '结束日期',
          key: 'expire_date'
        }, {
          title: '子产品代码',
          key: 'sub_fund_code_list'
        }, {
          title: '指令来源',
          key: 'data_source',
          exportRender: (value, rowdata) => {
            return showText(value, this.dataSourceList)
          }
        }
      ]
    },
    getAccountSheetHeaderColumns(){
      return [
        {
          title: '产品名称',
          key: 'fund_id',
          exportRender: (value, rowdata) => {
            return showText(value, this.fundIdList)
          }
        },{
          title: '账户用途',
          key: 'account_class',
          exportRender: (value, rowdata) => {
            return showText(value, this.accountClassList)
          }
        }, {
          title: '账户名称',
          key: 'bank_account_name'
        }, {
          title: '银行账号',
          key: 'bank_account_no'
        }, {
          title: '开户行名称',
          key: 'sub_bank_no'
        }, {
          title: '银行总行',
          key: 'bic_code',
          exportRender: (value, rowdata) => {
            return showText(value, this.bankList)
          }
        }, {
          title: '托管人',
          key: 'custodial_no',
          exportRender: (value, rowdata) => {
            return showText(value, this.trustBankList)
          }
        }, {
          title: '币种',
          key: 'currency_no',
          exportRender: (value, rowdata) => {
            return showText(value, this.currencyNoList)
          }
        },{
          title: '大额支付号',
          key: 'pay_no'
        }, {
          title: '期货公司',
          key: 'broker_id',
          exportRender: (value, rowdata) => {
            return showText(value, this.brokerList)
          }
        }, {
          title: '银期方式',
          key: 'bank_futures_type',
          exportRender: (value, rowdata) => {
            return showText(value, this.bankFuturesTypeList)
          }
        }, {
          title: '持有人账号',
          key: 'holder_account'
        }, {
          title: '托管人传真',
          key: 'bank_fax'
        }, {
          title: '托管人邮箱',
          key: 'bank_mail'
        }, {
          title: '账户状态',
          key: 'bank_account_status',
          exportRender: (value, rowdata) => {
            return showText(value, this.accountStatusList)
          }
        }
      ]
    }
  },
  components: {
    FundTree,
    ImportAccountTransfer,
    ManageFundDialog,
    ManageAccountDialog,
    FundList,
    AccountList
  }
}
</script>

<style lang="less" rel="stylesheet/less">
  @import "./style.less";
</style>



