<template>
  <div class="c-detail-panel">
    <!-- 上部分 -->
    <div v-if="queryForm.fundIds != '' && queryForm.fundIds.indexOf(',') < 0 && from !== 'finished'">
      <table class="table brief" border="1" cellspacing="0" v-if="queryForm.businClass == '1'">
        <tbody>
          <tr class="wrap-content">
            <td v-if="trustAcctList != null && trustAcctList.length > 0">
              <div :title="trustAcctList[0].BANK_ACCOUNT_NAME">{{trustAcctList[0].BANK_ACCOUNT_NAME | removeBrackets}}</div>
              <div class="bold">{{formatMoney(trustAcctList[0].ACCOUNT_CUR_BALANCE)}}<span class="orange">{{trustAcctList[0].BANK_ACCOUNT_NAME | getBrackets}}</span></div>
            </td>
            <td v-if="acctBalanceList != null && acctBalanceList.length > 0">
              <div :title="acctBalanceList[0].BANK_ACCOUNT_NAME">{{acctBalanceList[0].BANK_ACCOUNT_NAME | removeBrackets}}</div>
              <div class="bold">{{formatMoney(acctBalanceList[0].ACCOUNT_CUR_BALANCE)}}<span class="orange">{{acctBalanceList[0].BANK_ACCOUNT_NAME | getBrackets}}</span></div>
            </td>
            <td v-if="businCashList.length > 0">
              <div :title="businCashList[0].CASHTYPE">{{businCashList[0].CASHTYPE}}</div>
              <div>
                <span :style="getColoredStyle(businCashList[0].AMOUNT)">{{formatMoney(businCashList[0].AMOUNT)}}</span>
                <h-button type="primary" size="small" @click="quickSend(5)" v-if="from === 'approve'">划款</h-button></div>
            </td>
            <td v-if="businCashList.length > 2">
              <div :title="businCashList[2].CASHTYPE">{{businCashList[2].CASHTYPE}}</div>
              <div class="bold">{{formatMoney(businCashList[2].AMOUNT)}}</div>
            </td>
            <td v-if="acctBalanceList != null && acctBalanceList.length > 1">
              <div :title="acctBalanceList[1].BANK_ACCOUNT_NAME">{{acctBalanceList[1].BANK_ACCOUNT_NAME}}</div>
              <div class="bold">{{formatMoney(acctBalanceList[1].ACCOUNT_CUR_BALANCE)}}</div>
            </td>
            <td v-if="businCashList.length > 1">
              <div :title="businCashList[1].CASHTYPE">{{businCashList[1].CASHTYPE}}</div>
              <div>
                <span :style="getColoredStyle(businCashList[1].AMOUNT)">{{formatMoney(businCashList[1].AMOUNT)}}</span>
                <h-button type="primary" size="small" @click="quickSend(6)" v-if="from === 'approve'">划款</h-button></div>
            </td>
          </tr>
        </tbody>
      </table>
      <table class="table brief" border="1" cellspacing="0" v-else-if="queryForm.businClass == '4,5,6'">
        <tr>
          <td v-for="(item, index) in trustAcctList" :key="index">
            <div :title="item.BANK_ACCOUNT_NAME">{{item.BANK_ACCOUNT_NAME}}</div>
            <div class="bold">{{formatMoney(item.ACCOUNT_CUR_BALANCE)}}</div>
          </td>
          <td v-if="businCashList.length > 0">
            <div :title="businCashList[0].CASHTYPE">{{businCashList[0].CASHTYPE}}</div>
            <div class="bold">{{formatMoney(businCashList[0].AMOUNT)}}</div>
          </td>
          <td v-if="businCashList.length > 1">
            <div :title="businCashList[1].CASHTYPE">{{businCashList[1].CASHTYPE}}</div>
            <div class="bold">{{formatMoney(businCashList[1].AMOUNT)}}</div>
          </td>
        </tr>
      </table>
      <table class="table brief" border="1" cellspacing="0" v-else>
        <tr>
          <td v-for="(item, index) in trustAcctList" :key="index">
            <div :title="item.BANK_ACCOUNT_NAME">{{item.BANK_ACCOUNT_NAME | removeBrackets}}</div>
            <div class="bold">{{formatMoney(item.ACCOUNT_CUR_BALANCE)}}<span class="orange">{{item.BANK_ACCOUNT_NAME | getBrackets}}</span></div>
          </td>
          <td v-for="(item, index) in acctBalanceList" :key="index" v-if="queryForm.businClass == '1' && (item.ACCOUNT_CLASS == 5 || item.ACCOUNT_CLASS == 6)">
            <div :title="item.BANK_ACCOUNT_NAME">{{item.BANK_ACCOUNT_NAME}}</div>
            <div class="bold">{{formatMoney(item.ACCOUNT_CUR_BALANCE)}}</div>
          </td>
          <td v-for="(item, index) in businCashList" :key="index">
            <div :title="item.CASHTYPE">{{item.CASHTYPE}}</div>
            <div class="bold">{{formatMoney(item.AMOUNT)}}</div>
          </td>
        </tr>
      </table>
    </div>

    <!-- 下部分 -->
    <table class="table" border="1" cellspacing="0" v-if="item.transfer_ins_id">
      <tbody>
        <tr>
          <td class="collapse-label" rowspan="5">基本信息</td>
          <td class="label">业务标志</td>
          <td class="info" :title="showText(item.cop_busin_flag, businFlagList)">{{showText(item.cop_busin_flag, businFlagList)}}</td>
          <td class="collapse-label" rowspan="5">出款账户</td>
          <td class="label">银行总行</td>
          <td class="info" :title="item.self_bank">{{item.self_bank}}</td>
          <td class="collapse-label" rowspan="5">入款账户</td>
          <td class="label">银行总行</td>
          <td class="info" :title="item.rival_bank">{{item.rival_bank}}</td>
        </tr>
        <tr>
          <td class="label">交收金额</td>
          <td class="info"><span class="bold">{{formatMoney(item.settle_balance)}}</span></td>
          <td class="label">账户名称</td>
          <td class="info" :title="item.self_account_name">{{item.self_account_name}}</td>
          <td class="label">账户名称</td>
          <td class="info" :title="item.rival_account_name">{{item.rival_account_name}}</td>
        </tr>
        <tr>
          <td class="label">划款事由</td>
          <td class="info" :title="item.transfer_purpose">{{item.transfer_purpose}}</td>
          <td class="label">银行账号</td>
          <td class="info" :title="item.self_account">{{item.self_account}}</td>
          <td class="label">银行账号</td>
          <td class="info" :title="item.rival_account">{{item.rival_account}}</td>
        </tr>
        <tr>
          <td class="label">划款附言</td>
          <td class="info" :title="item.transfer_postscript">{{item.transfer_postscript}}</td>
          <td class="label">开户行</td>
          <td class="info" :title="item.self_open_bank">{{item.self_open_bank}}</td>
          <td class="label">开户行</td>
          <td class="info" :title="item.rival_open_bank">{{item.rival_open_bank}}</td>
        </tr>
        <tr>
          <td class="label">划款备注</td>
          <td class="info" :title="item.transfer_remark">{{item.transfer_remark}}</td>
          <td class="label">大额支付号</td>
          <td class="info" :title="item.self_pay_no">{{item.self_pay_no}}</td>
          <td class="label">大额支付号</td>
          <td class="info" :title="item.rival_pay_no">{{item.rival_pay_no}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script type="text/ecmascript-6">
import {
  showText,
  formatMoney
} from '@/api/utils'

export default {
  props: {
    from: {
      type: String,
      default: 'approve' // approve settle finished
    },
    businClass: {
      type: String,
      default: '0'
    },
    item: {
      type: Object,
      default: () => {}
    },
    businFlagList: {
      type: Array,
      default: () => []
    },
    trustAcctList: {
      type: Array,
      default: () => []
    },
    acctBalanceList: {
      type: Array,
      default: () => []
    },
    businCashList: {
      type: Array,
      default: () => []
    },
    queryForm: {
      type: Object,
      default: () => {}
    },
  },
  data() {
    return {

    }
  },
  watch: {
    item: {
      handler: function (val) {
      }
    }
  },
  filters: {
    removeBrackets(val) {
      if (!val) {
        return val
      }
      return val.replace(/\(.*\)/g, '')
    },
    getBrackets(val) {
      if (!val) {
        return ''
      }
      let results = val.match(/\((.+?\))/g)
      return (results && results.length > 0) ? results[0] : ''
    }
  },
  mounted() {

  },
  methods: {
    showText: showText,
    formatMoney: formatMoney,
    quickSend(accountClass) {
      this.$emit('quickSend', accountClass)
    },
    getColoredStyle(val) {
      let color = '#333'
      if (store.state.theme.theme === 'black') {
        color = '#fff'
      }
      if (parseFloat(val) > 0) {
        color = 'green'
      }
      if (parseFloat(val) < 0) {
        color = 'red'
      }
      return {
        color: color,
        fontWeight: 'bold'
      }
    }
  },
  components: {

  }
}
</script>

<style lang="less" rel="stylesheet/less">
.c-detail-panel {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: white;
  .table {
    width: 100%;
    border-spacing: 0;
    border-collapse: collapse;
    border: 1px solid #d4d4d4;
    font-size: 13px;
    table-layout: fixed;
  }
  .table td {
    height: 30px;
    padding: 0 6px;
  }
  td[colspan],
  td[rowspan],
  td.label {
    color: #666;
    background-color: #f5f5f5;
  }
  td.collapse-label {
    width: 26px;
    text-align: center;
    color: #666;
    background-color: #f5f5f5;
  }
  td.label {
    max-width: 120px;
  }
  td.label-big {
    width: 240px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  td.info {
    width: 200px;
    color: #333;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .brief {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    border-spacing: 0;
    border-collapse: collapse;
    border: 0;
    border-left: 1px solid #d4d4d4;
    border-top: 1px solid #d4d4d4;
    font-size: 13px;
    background-color: #f5f5f5;
    margin-bottom: -1px;
    table-layout: fixed;
    .wrap-content {
      display: flex;
      flex-wrap: wrap;
    }
    &::after {
      position: absolute;
      content: '';
      left: 0;
      right: 1px;
      bottom: 0;
      height: 1px;
      background-color: #d4d4d4;
    }
    &::before {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      bottom: 0;
      width: 1px;
      background-color: #d4d4d4;
    }
    tr {
      display: flex;
      flex-wrap: wrap;
    }
    td {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: calc(100% / 3);
      min-height: 30px;
      padding: 0;
      box-sizing: border-box;
      border: 0;
      border-right: 1px solid #d4d4d4;
      border-bottom: 1px solid #d4d4d4;
      overflow: hidden;
    }
    td > div:first-child {
      padding-left: 10px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #666;
    }
    td > div:last-child {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 200px;
      height: 100%;
      line-height: 30px;
      padding: 0 10px;
      background-color: white;
      border-left: 1px solid #d4d4d4;
      flex-shrink: 0;
    }
  }
  .orange {
    color: orange;
  }
  .bold {
    font-weight: bold;
  }
  .h-btn.h-btn-small {
    padding: 2px 4px;
    font-size: 12px;
    border-radius: 3px;
    height: 22px;
    line-height: 18px;
    margin-left: 10px;
  }
}
</style>



