<template>
    <div class="full-h" id="full-h-wrapper">
        <label style="display:none;"><span></span><input type="text" name="hidden1" ></label>
        <label style="display:none;"><span></span><input type="password" name="hidden2" ></label>
        <h-row>
            <h-col>
                <el-form style="font-size: 12px;" :inline="true" size="mini" :model="queryForm" class="demo-form-inline">
                    <el-form-item label="开户所在地">
                        <el-select v-model="taType" filterable multiple clearable collapse-tags>
                            <el-option v-for="item in taTypeList" :key="item.code" :label="item.text" :value="item.code">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-input v-model="queryForm.keyword" placeholder="请输入销售机构编码或名称" style="width: 175px"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="refresh">查询</el-button>
                    </el-form-item>
                </el-form>
            </h-col>
        </h-row>
        <h-button-group>
            <h-button icon="add" @click="agencyAdd">新增</h-button>
            <h-button icon="editor" @click="agencyModify">修改</h-button>
            <h-button icon="delete" @click="agencyDelete">删除</h-button>
            <h-button icon="android-upload" @click="agencyImport">导入</h-button>
        </h-button-group>
        <h-datagrid stripe hasPage
                    :height="autoHeight"
                    highlight-row
                    :pageSize="100" :pageSizeOpts="pageSizeOpts"
                    :columns="agencyColumn"
                    :bindForm="queryForm"
                    :on-row-db-click="agencyDbClick"
                    url="/systemmgnt/agencyInfo/agencyInfoList.json"
                    ref="datagrid">
        </h-datagrid>
        <h-msg-box class="customMsgBox" v-model="showAgencyDialog" width="400" :mask-closable=false>
            <p slot="header">
                <span v-if="addAgency">新增销售机构</span>
                <span v-else>修改销售机构</span>
            </p>
            <h-form :model="agencyInfo" :label-width="100" ref="agencyForm">
                <h-row>
                    <h-col span="24">
                        <h-form-item required label="销售机构编码" prop="agency_code">
                            <h-input :disabled='!addAgency' :maxlength="8" v-model="agencyInfo.agency_code"></h-input>
                        </h-form-item>
                        <h-form-item required label="销售机构名称" prop="agency_name">
                            <h-input :maxlength="128" v-model="agencyInfo.agency_name"></h-input>
                        </h-form-item>
                        <h-form-item required label="开户所在地" prop="ta_type">
                            <h-select  v-model="agencyInfo.ta_type" isString filterable>
                                <h-option v-for="item in taTypeList" :value="item.code" :label="item.text" :key="item.code">
                                </h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required label="销售渠道" prop="sale_channel">
                            <h-select  v-model="agencyInfo.sale_channel" isString filterable>
                              <h-option v-for="item in channelList" :value="item.code" :label="item.text" :key="item.code">
                              </h-option>
                            </h-select>
                        </h-form-item>
                    </h-col>
                </h-row>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="agencyCancel">取消</h-button>
                <h-button type="primary" @click="saveAgency">确认</h-button>
            </div>
        </h-msg-box>
        <import-account-transfer :on-import-success="refreshAgency" ref="agencyImportDialog"></import-account-transfer>
    </div>
</template>

<script>
    import HDatagrid from "../../../components/HDatagrid";
    import {
      showText,
      getDictList
    } from '@/api/utils';
    import {
        saveAgencyInfo,
        deleteAgency
    } from '@/api/systemmgnt/agency'
    import ImportAccountTransfer from "../../../components/ImportAccountTransfer";
    export default {
        name: "agency",
        data(){
            return{
                autoHeight:0,
                pageSizeOpts: [25,50,75,100],
                agencyColumn: [
                    {
                        type: 'selection',
                        width: 30,
                        align: 'center'
                    }, {
                        title: "销售机构序号",
                        key: "serial_no",
                        width: 150,
                        hiddenCol:true,
                        align: 'right',
                        ellipsis: true
                    }, {
                        title: "销售机构编码",
                        key: "agency_code",
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: "销售机构名称",
                        key: "agency_name",
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: "销售渠道",
                        key: "sale_channel",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let saleChannel = showText(rowData.sale_channel, this.channelList);
                            return h("span", {}, saleChannel);
                        }
                    }, {
                        title: "开户所在地",
                        key: "ta_type",
                        align: 'left',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let typeName = showText(rowData.ta_type, this.taTypeList);
                            return h("span", {}, typeName);
                        }
                    }
                ],
                taTypeList: [
                    {
                        code:'2',
                        text:'上海'
                    },
                    {
                        code:'3',
                        text:'深圳'
                    }
                ],
                channelList: [],
                queryForm: {
                    taType: '',
                    keyword: ''
                },
                agencyInfo:{
                    agency_code:'',
                    agency_name:'',
                    ta_type:''
                },
                showAgencyDialog:false,
                addAgency:false
            }
        },
        watch:{
            showAgencyDialog: function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
            taType:{
                get: function () {
                    if (this.queryForm.taType)
                        return this.queryForm.taType.split(",")
                    else
                        return []
                },
                set: function (newValue) {
                    this.queryForm.taType = newValue.join(",")
                }
            }
        },
        mounted(){
            getDictList(90779).then(res => this.channelList = res.data)
            this.re_layout();
        },
        methods:{
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 85;
            },
            agencyAdd(){
                this.addAgency = true;
                this.agencyInfo = {
                    serial_no:null,
                    agency_code:null,
                    agency_name:'',
                    ta_type:'2',
                    sale_channel:'2'
                }
                this.$refs.agencyForm.resetFields();
                this.showAgencyDialog = true;
            },
            agencyModify(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请先选中一条记录','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return
                }
                if(selects.length > 1){
                    this.openMask();
                    this.$alert('只能选择一条记录修改','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return
                }
                this.agencyDbClick(selects[0]);
            },
            agencyDbClick(data){
                this.agencyInfo = data;
                this.addAgency = false;
                this.showAgencyDialog = true;
            },
            agencyDelete(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请先选择一条记录','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return
                }

                let serialNoList= '';
                for( let i = 0; i < selects.length; i++){
                    serialNoList += selects[i].serial_no + ",";
                }
                serialNoList = serialNoList.substring(0 ,serialNoList.length - 1)

                this.openMask();
                this.$confirm('确认删除该销售机构吗？', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteAgency(serialNoList).then(res =>{
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            });
                            this.refresh()
                        } else {
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText: '确定',
                                type: 'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            });
                        }
                    })
                }).catch(() =>{
                    this.closeMask();
                });
            },
            agencyCancel(){
                this.showAgencyDialog = false;
            },
            saveAgency(){
                let isValidate = true;
                this.$refs.agencyForm.validate((valid) =>isValidate = isValidate && valid)
                if(!isValidate){
                    this.$alert('输入项不满足要求，请重新检查!','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    })
                    return
                }
                saveAgencyInfo(this.agencyInfo).then(res =>{
                    if(res.data && res.data.code == '0'){
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                                this.showAgencyDialog = false;
                            }
                        }).then(() => {
                            this.showAgencyDialog = false;
                        });
                        this.refresh();
                    } else {
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }
                })
            },
            agencyImport(){
                this.$refs.agencyImportDialog.showDialog(5,null);
            },
            refreshAgency(){
                this.$refs.datagrid.dataChange(1);
            }
        },
        components: {
            ImportAccountTransfer,
            HDatagrid
        }
    }
</script>

<style>

</style>


