<template>
    <div class="full-h" id="full-h-wrapper">
        <h-row class="full-h">
            <h-col class="full-h">
                <h-button-group>
                    <h-button icon="add" @click="dealAdd">新增</h-button>
                    <h-button icon="editor" @click="dealModify">修改</h-button>
                    <h-button icon="delete" @click="dealDelete">删除</h-button>
                    <h-button icon="refresh" @click="refresh">刷新</h-button>
                </h-button-group>
                <h-datagrid
                            hasPage stripe :height="autoHeight"
                            highlight-row :columns="columns" :on-row-db-click="dealStampDbClick"
                            :page-size="100" :page-size-opts="pageSizeOpts"
                            url="systemmgnt/dealStamp/page.json" ref="datagrid">
                </h-datagrid>
                <h-msg-box class="customMsgBox" v-model="showDealStampDialog" width="400" :mask-closable=false>
                    <p slot="header">
                        <span v-if="actionType == 'add'">新增配置</span>
                        <span v-else>修改配置</span>
                    </p>
                    <h-form :model="dealInfo" :label-width="80" ref="dealForm">
                        <h-row>
                            <h-col>

                                <h-form-item required v-if="actionType == 'add'" label="产品名称" prop="fund_id_list">
                                    <div>
                                        <h-select style="width: 215px" v-model="dealInfo.fund_id_list" hideMult isString multiple filterable showBottom>
                                            <h-option v-for="item in fundInfoList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                                        </h-select>
                                        <h-button type="primary" style="float: right;margin-top:4px;height:28px;line-height:16px" @click="choseFundList">选择</h-button>
                                    </div>
                                </h-form-item>

                                <h-form-item v-if="actionType == 'modify'" label="产品名称" prop="fund_id">
                                    <el-select size="mini" style="width: 100%;" disabled v-model="dealInfo.fund_id">
                                        <el-option v-for="item in fundInfoList" :key="item.code" :label="item.text" :value="item.code"></el-option>
                                    </el-select>
                                </h-form-item>

                                <h-form-item required label="公司签章" prop="stamp_id">
                                    <h-select v-model="dealInfo.stamp_id" filterable>
                                        <h-option v-for="item in stampInfoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                                    </h-select>
                                </h-form-item>

                            </h-col>
                        </h-row>
                    </h-form>
                    <div slot="footer">
                        <h-button type="ghost" style="margin-left: 8px" @click="cancelDeal">取消</h-button>
                        <h-button type="primary" @click="saveDeal">确定</h-button>
                    </div>
                </h-msg-box>
                <h-msg-box class="customMsgBox"  width="500px" v-model="showResult">
                    <p slot="header">
                        <span>新增结果</span>
                    </p>
                    <div>一共新增{{errorData.total}}条成交单配置，失败了{{errorData.fail}}条，以下是具体的错误原因：</div>
                    <h-table stripe border v-show="errorData.length>0" :height="350" :data="errorData" :columns="dealStampColums">

                    </h-table>
                </h-msg-box>
                <fund-choose-msg-box
                        @saveFundInfo="saveFundInfo"
                        ref="dealStampMsgBox">
                </fund-choose-msg-box>
            </h-col>
        </h-row>
    </div>
</template>

<script>
    import HDatagrid from "../../../../components/HDatagrid";

    import{
        showText,
        getFundInfo,
    } from "@/api/utils"

    import {
        queryStampByType,
        addDealStamp,
        modifyDealStamp,
        deleteDealStamp
    } from "@/api/systemmgnt/stamp"
    import FundChooseMsgBox from "../../../../components/FundChooseMsgBox";

    export default {
        name: "dealStamp",
        components: {FundChooseMsgBox, HDatagrid},
        data(){
            return{
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                actionType:'',
                fullHeight: '',
                fundInfoList:[],
                errorData: [],
                showDealStampDialog:false,
                showResult: false,
                columns:[
                    {
                        type: 'selection',
                        width: 30,
                        align: 'center'
                    }, {
                        title:'序号',
                        type:'index',
                        sortable:true,
                        width:80,
                        align:'center'
                    },
                    {
                        key:'fund_id',
                        title:'产品名称',
                        sortable:true,
                        ellipsis:true,
                        align:"left",
                        render:(h,params) =>{
                            const rowData = params.row;
                            let fundName = showText(rowData.fund_id,this.fundInfoList);
                            return h("span",{},fundName);
                        }
                    },
                    {
                        key:'stamp_id',
                        title:'公司签章',
                        ellipsis:true,
                        align:"left",
                        render:(h,params) =>{
                            const rowData = params.row;
                            let stampName = showText(rowData.stamp_id,this.stampInfoList);
                            return h("span",{},stampName);
                        }
                    }
                ],
                dealStampColums:[
                    {width: 80,type:"index",title:"序号",align:"center"},
                    {key:"errorInfo",title:"错误原因",align:"center"},
                ],
                dealInfo:{
                    fund_id:'',
                    fund_id_list:'',
                    stamp_id:''
                }
            }
        },
        watch:{
            showDealStampDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        props:{
            stampInfoList:{
                type:Array,
                default:[]
            },
        },
        activated() {
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => {
                if (res.data && res.data.length > 0) {
                    this.fundInfoList = res.data;
                }
            })
        },
        methods:{
            resizeTable(height, fullHeight){
                this.autoHeight = height;
                this.fullHeight = fullHeight;
            },
            dealAdd(){
                this.dealInfo ={
                    fund_id:'',
                    fund_id_list:'',
                    stamp_id:''
                };

                this.actionType = 'add';
                this.$refs.dealForm.resetFields();
                this.showDealStampDialog = true;
            },
            dealModify(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }

                if(selects.length > 1){
                    this.openMask();
                    this.$alert('只能选择一条记录修改','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                this.dealStampDbClick(selects[0]);
            },
            dealStampDbClick(data){
                this.dealInfo.fund_id = data.fund_id.toString();
                this.dealInfo.stamp_id = data.stamp_id.toString();
                this.actionType = 'modify';
                this.showDealStampDialog = true;
            },
            dealDelete(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }

                let fundIdList= '';
                for( let i = 0; i < selects.length; i++){
                    fundIdList += selects[i].fund_id + ",";
                }
                fundIdList = fundIdList.substring(0 ,fundIdList.length - 1)

                this.openMask();
                this.$confirm('确认删除这条签章信息?','确认',{
                    confirmButtonText:'确认',
                    cancelButtonText:'取消',
                    type:'warning'
                }).then( () =>{
                    deleteDealStamp(fundIdList).then( res => {
                        if(res.data && res.data.code =='0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                        }
                    })
                })

            },
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            cancelDeal(){
                this.showDealStampDialog = false;
            },
            saveDeal(){
                let isValidate = true;
                this.$refs.dealForm.validate( (validate) => isValidate = isValidate && validate )
                if(!isValidate){
                    this.$alert('输入项不满足要求，请重新检查','错误',{
                        confirmButtonText:'确认',
                        type:'error'
                    })
                    return
                }

                if(this.actionType == 'add'){
                    addDealStamp(this.dealInfo.fund_id_list,this.dealInfo.stamp_id).then(res => {
                        if(res.data && res.data.code =='0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.showDealStampDialog = false;
                                }
                            }).then(() => {
                                this.showDealStampDialog = false;
                            })
                            this.refresh();
                        }else if(res.data.code=='1120002'){
                            this.showDealStampDialog = false;
                            this.showResult = true;
                            this.errorData = res.data.data;
                            this.errorData.total = this.dealInfo.fund_id_list.split(",").length;
                            this.errorData.fail = res.data.data.length;
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }else{
                    modifyDealStamp(this.dealInfo.fund_id,this.dealInfo.stamp_id).then(res => {
                        if(res.data && res.data.code =='0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.showDealStampDialog = false;
                                }
                            }).then(() => {
                                this.showDealStampDialog = false;
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }
            },
            choseFundList(){
                this.$refs.dealStampMsgBox.showMsgBox(this.dealInfo.fund_id_list, this.fullHeight, '1');
            },
            saveFundInfo(data){
                this.dealInfo.fund_id_list = data;
            }
        }
    }
</script>

<style scoped>

</style>


