<template>
    <div class="page-traderival full-h" id="full-h-wrapper">
        <label style="display:none;"><span></span><input type="text" name="hidden1" ></label>
        <label style="display:none;"><span></span><input type="password" name="hidden2" ></label>
        <h-row>
            <h-col>
                <el-form style="font-size: 12px;" :inline="true" size="mini" class="demo-form-inline" :model="queryForm">
                    <el-form-item label="交易对手类型">
                        <el-select v-model="rivalType" filterable multiple clearable collapse-tags>
                            <el-option v-for="item in rivalTypeList" :key="item.code" :value="item.code" :label="item.text">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="市场类型">
                        <el-select v-model="queryForm.rivalMarketType" filterable clearable>
                            <el-option v-for="item in rivalMarketTypeList" :key="item.code" :value="item.code" :label="item.text"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="refreshRival">查询</el-button>
                    </el-form-item>
                </el-form>
            </h-col>
        </h-row>
        <div slot="right" class="full-h">
            <div class="ctrls">
                <h-button-group>
                    <h-button icon="add" @click="addRival">新增</h-button>
                    <h-button icon="edit" @click="modifyRival">修改</h-button>
                    <h-button icon="delete" @click="deleteRival">删除</h-button>
                    <h-button icon="android-upload" @click="uploadRival">导入</h-button>
                </h-button-group>

                <h-input v-model="queryForm.keyword" :maxlength="32" placeholder="请输入交易对手编号或名称" style="width: 215px">
                    <div slot="append" >
                        <a href="javascript:void(0)">
                            <h-icon name="search"  @click.native.stop="refreshRival"></h-icon>
                        </a>
                    </div>
                </h-input>
            </div>
            <h-datagrid :height="autoHeight"
                        stripe hasPage highlight-row
                        :on-row-db-click="rivalDbclick" :pageSize="100"
                        :page-size-opts="pageSizeOpts" :columns="rivalColumn"
                        :bind-form="queryForm" url="/systemmgnt/rival/page.json"
                        ref="datagrid">
            </h-datagrid>
        </div>

        <h-msg-box class="customMsgBox" v-model="showTradeRivalDialog" width="500" :mask-closable="false">
            <p slot="header">
                <span v-if="actionType == 'add'">新增交易对手</span>
                <span v-if="actionType == 'modify'">修改交易对手</span>
            </p>
            <h-row>
                <h-col>
                    <h-form :model="rivalInfo" :label-width="100" ref="rivalForm">
                        <h-form-item required label="交易对手编号" prop="rival_code">
                            <h-input v-model="rivalInfo.rival_code" lengthByByte :maxlength="32"></h-input>
                        </h-form-item>

                        <h-form-item required label="交易对手名称" prop="traderival_name">
                            <h-input v-model="rivalInfo.traderival_name" lengthByByte :maxlength="128"></h-input>
                        </h-form-item>

                        <h-form-item label="交易对手类型" prop="rival_type">
                            <h-select v-model="rivalInfo.rival_type" clearable filterable>
                                <h-option v-for="item in rivalTypeList" :value="item.code" :key="item.code"
                                          :label="item.text"></h-option>
                            </h-select>
                        </h-form-item>

                        <h-form-item label="市场类型" prop="rival_market_types">
                            <h-select v-model="rivalInfo.rival_market_types" multiple isString filterable hideMult
                                      showBottom>
                                <h-option v-for="item in rivalMarketTypeList" :value="item.code" :key="item.code"
                                          :label="item.text"></h-option>
                            </h-select>
                        </h-form-item>

                        <h-form-item label="联系人" prop="linkman">
                            <h-input v-model="rivalInfo.linkman" lengthByByte :maxlength="128"></h-input>
                        </h-form-item>

                        <h-form-item label="联系地址" prop="address">
                            <h-input v-model="rivalInfo.address" lengthByByte :maxlength="128"></h-input>
                        </h-form-item>

                        <h-form-item label="联系电话" prop="phone_no">
                            <h-input v-model="rivalInfo.phone_no" lengthByByte :maxlength="32"></h-input>
                        </h-form-item>

                        <h-form-item label="传真号码" prop="fax_no">
                            <h-input v-model="rivalInfo.fax_no" lengthByByte :maxlength="128"></h-input>
                        </h-form-item>
                    </h-form>
                </h-col>
            </h-row>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="cancelRival">取消</h-button>
                <h-button type="primary" @click="submitRival">确认</h-button>
            </div>
        </h-msg-box>
        <import-account-transfer :on-import-success="refreshRival" ref="rivalUploadDialog"></import-account-transfer>
    </div>
</template>

<script>
    import {
        showText,
        getDictList,
        showTextList
    } from '@/api/utils';
    import {
        insertRival,
        updateRival,
        deleteRival
    } from '@/api/systemmgnt/tradeRival';
    import HDatagrid from "../../../components/HDatagrid";
    import ImportAccountTransfer from "../../../components/ImportAccountTransfer";

    export default {
        name: "tradeRival",
        data() {
            return {
                autoHeight: 0,
                pageSizeOpts: [25, 50, 75, 100],
                actionType: '',
                showTradeRivalDialog: false,
                rivalTypeList: [],
                rivalMarketTypeList: [],
                rivalColumn: [
                    {
                        title: '交易对手序号',
                        key: 'rival_id',
                        hiddenCol: true,
                        align: '',
                        ellipsis: true
                    }, {
                        title: '交易对手编号',
                        key: 'rival_code',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '交易对手名称',
                        key: 'traderival_name',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '交易对手类型',
                        key: 'rival_type',
                        align: 'left',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let rivalType = showText(rowData.rival_type, this.rivalTypeList);
                            return h("span", {}, rivalType);
                        }
                    }, {
                        title: '市场类型',
                        key: 'rival_market_types',
                        align: 'left',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let marketType = showTextList(rowData.rival_market_types, this.rivalMarketTypeList);
                            return h("span", {}, marketType)
                        }
                    }, {
                        title: '联系人',
                        key: 'linkman',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '联系地址',
                        key: 'address',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '联系电话',
                        key: 'phone_no',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '传真号码',
                        key: 'fax_no',
                        align: 'left',
                        ellipsis: true
                    }
                ],
                queryForm: {
                    rivalTypes: '',
                    rivalMarketType: '',
                    keyword: ''
                },
                rivalInfo: {
                    rival_id: '',
                    rival_code: '',
                    traderival_name: '',
                    rival_type: '',
                    rival_market_types: '',
                    linkman: '',
                    address: '',
                    phone_no: '',
                    fax_no: ''
                }
            }
        },
        computed: {
            rivalType: {
                get: function () {
                    if(this.queryForm.rivalTypes) {
                        return this.queryForm.rivalTypes.split(",")
                    }
                    return [];
                },
                set: function (newValue) {
                    this.queryForm.rivalTypes = newValue.join(",");
                }
            }
        },
        mounted() {
            getDictList(40388).then(res => this.rivalTypeList = res.data);
            getDictList(600301).then(res => this.rivalMarketTypeList = res.data);
            this.re_layout();
        },
        methods: {
            re_layout() {
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 90;
            },
            addRival() {
                this.rivalInfo = {
                    rival_id: '',
                    rival_code: '',
                    traderival_name: '',
                    rival_type: '',
                    rival_market_types: '',
                    linkman: '',
                    address: '',
                    phone_no: '',
                    fax_no: ''
                };
                this.$refs.rivalForm.resetFields();
                this.actionType = 'add';
                this.showTradeRivalDialog = true;
            },
            modifyRival() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.$alert('请选择一条交易对手记录!', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }
                this.rivalDbclick(selects[0]);
            },
            rivalDbclick(data) {
                this.rivalInfo = data;
                this.actionType = 'modify';
                this.showTradeRivalDialog = true;
            },
            deleteRival() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.$alert('请选择一条交易对手记录!', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }

                this.$confirm('确定删除该条记录吗', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteRival(selects[0].rival_id).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '确定', {
                                confirmButtonText: '确定',
                                type: 'success'
                            });
                            this.refreshRival()
                        } else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    })
                }).catch(() => {

                })
            },
            uploadRival() {
                this.$refs.rivalUploadDialog.showDialog('7', null);
            },
            cancelRival() {
                this.showTradeRivalDialog = false;
            },
            submitRival() {
                let isValidate = true;
                this.$refs.rivalForm.validate((validate) => isValidate = validate && isValidate);
                if (!isValidate) {
                    this.$alert('输入项不满足要求,请重新检查', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }
                if (this.actionType == 'add') {
                    insertRival(this.rivalInfo).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '确定', {
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                    this.showTradeRivalDialog = false;
                                }
                            }).then(() => {
                                this.showTradeRivalDialog = false;
                            });
                            this.refreshRival();
                        } else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    })
                } else if (this.actionType == 'modify'){
                    updateRival(this.rivalInfo).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '确定', {
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                    this.showTradeRivalDialog = false;
                                }
                            }).then(() => {
                                this.showTradeRivalDialog = false;
                            });
                            this.refreshRival();
                        } else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    })
                }
            },
            refreshRival() {
                this.$refs.datagrid.dataChange(1);
            }
        },
        components: {ImportAccountTransfer, HDatagrid}
    }
</script>

<style lang="less" rel="stylesheet/less">
    .page-traderival {
        .ctrls {
            display: flex;
            justify-content: space-between;
            height: 34px;
            .h-input-type {
                height: 28px;
            }
        }
    }
</style>


