import fetch from "@/api/httpFetch";

export function matchInsAndFlow(insId,flowNo) {
    return fetch.post("/liquidate/instruction/match.json",{insId:insId,flowNo:flowNo})
}


export function pushFlow(params) {
    return fetch.post("/liquidate/flow/push.json",params,{
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function cancelLiquidate(data) {
    return fetch.post("/liquidate/cancel.json",data,{
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function cancelFilter(flowNo) {
    return fetch.post("/liquidate/filter/cancel.json",{flowNo:flowNo})
}
export function getRuleDetail(ruleId) {
    return fetch.post("/liquidate/rule/get.json",{ruleId:ruleId})
}





