<template>
  <div class="page page-file-sendrecord">
    <h-split v-model="split" min="200" max="200" @on-moving="onSplitMove">
      <div slot="left" class="c-left-menu">
        <h-other-fund-tree :showAll="true" :onFundClick="onFundClick" @getFundTree="getFundTree"></h-other-fund-tree>
      </div>
      <div slot="right" class="c-right-content" ref="rightContent">
        <!-- top toolbar -->
        <h-tabs :animated="false">
          <h-tab-pane label="开基业务划款单">
            <div class="c-content-top-toolbar" ref="topToolbar">
              <el-form :inline="true" size="mini"  class="demo-form-inline" style="font-size: 12px;">
                <el-form-item label="发送日期">
                  <h-date-picker v-model="form.date" type="date" :clearable="false" placeholder="选择日期" format="yyyy-MM-dd" @on-change="onDateChange"></h-date-picker>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="onSearch">查询</el-button>
                </el-form-item>
              </el-form>
            </div>
            <!-- content table -->
            <div class="c-content">
              <h-datagrid
                      hasPage
                      stripe
                      :columns="columns"
                      :pageSize="pageSize"
                      :page-size-opts="pageSizeOpts"
                      :height="contentTableHeight"
                      :bindForm="filters"
                      url="/file/sendRecord/page.json"
                      ref="table"></h-datagrid>
            </div>
          </h-tab-pane>
        </h-tabs>
      </div>
    </h-split>
  </div>
</template>

<script type="text/ecmascript-6">
import Cookies from 'js-cookie'
import { mapState, mapMutations } from 'vuex'
import HOtherFundTree from '@/components/HOtherFundTree'
import HDatagrid from '@/components/HDatagrid';
import { getDictList, showText } from '@/api/utils'
import service from './service'

export default {
  data() {
    return {
      split: 0.2,
      columns: [],
      // 右侧列表table组件的高度
      contentTableHeight: 0,
      // 分页之每页数量
      pageSize: 50,
      // 分页组件配置
      pageSizeOpts: [50, 100, 150, 200],
      // 当前选择的产品ID
      fundId: null,
      // 顶部工具栏的表单数据
      form: {
        date: Cookies.get('CurrSysDate')
      },
      // 过滤筛选项数据
      filters: {
        fundId: null,
        sendDate: Cookies.get('CurrSysDate')
      },
      // 所有产品列表
      allFundList: []
    }
  },
  computed: {
    ...mapState({
      sendfileTypeList: state => state.dict.dict_90524,
      fileSendStatus: state => state.dict.dict_90426,
      transferFundStatus: state => state.dict.dict_90571,
      transferInsStatus: state => state.dict.dict_90537
    })
  },
  mounted() {
    this.initDatas()
  },
  activated() {
    SSObservable.subscribe('onresize', this.handleResize)
    this.updateLayout()
  },
  deactivated() {
    SSObservable.unsubscribe('onresize', this.handleResize)
  },
  methods: {
    ...mapMutations({
      setDict90524: 'DICT_90524',
      setDict90426: 'DICT_90426',
      setDict90571: 'DICT_90571',
      setDict90537: 'DICT_90537'
    }),
    initDatas() {
      getDictList(90523).then(res => {
          this.setDict90523(res.data)
      })
      getDictList(90524).then(res => {
        this.setDict90524(res.data)
      })
      getDictList(90426).then(res => {
        this.setDict90426(res.data)
      })
      getDictList(90571).then(res => {
        this.setDict90571(res.data)
      })
      getDictList(90537).then(res => {
        this.setDict90537(res.data)
      })
      this.columns = service.getColumns(this)

      this.contentTableHeight = this.$refs.rightContent.clientHeight - this.$refs.topToolbar.clientHeight
    },
    getDatas() {
      this.$refs.table.dataChange(1)
    },
    onFundClick(data) {
      // h-other-fund-tree 数据加载完成后会触发该方法，以下防止重复请求
      if (this.filters.fundId !== null || data.code.length > 0) {
        this.filters.fundId = data.code
        this.getDatas()
      } else {
        this.filters.fundId = ''
      }
    },
    handleResize() {
      this.updateLayout()
    },
    updateLayout() {
      setTimeout(() => {
        this.contentTableHeight = this.$refs.rightContent.clientHeight - this.$refs.topToolbar.clientHeight
        this.$refs.table.handleResize()
      }, 50)
    },
    onSearch() {
      this.getDatas()
    },
    onDateChange(date) {
      this.filters.sendDate = date.split('-').join('')
    },
    getFundTree(fundTree) {
      if (fundTree && fundTree.length > 0) {
        this.allFundList = fundTree[0].children
      }
    },
    onSplitMove() {
      this.$refs.table.handleResize()
    },
    downloadFile(fileId) {
      window.open(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=3&fileId=' + fileId)
    }
  },
  components: {
    HOtherFundTree,
    HDatagrid
  }
}
</script>

<style lang="less" rel="stylesheet/less">
  @import "./style.less";
</style>



