        watch: {
            'transferInfo.data_source'(val) {
                let payDate = this.transferInfo.require_pay_date;
                if (val=='2'){
                    this.pickerOptions= {
                        disabledDate (date) {
                            return parseDateStr(date) != payDate;
                        }
                    }
                } else {
                    this.pickerOptions= {
                        disabledDate (date) {
                            return false
                        }

                    }
                }
            },
            'transferInfo.cop_busin_flag'(val) {
                if (this.transferInfo.cop_busin_class=='7'&&this.rivalContactFlags.indexOf(val.toString())>=0) {
                    this.showRivalContact = true;
                }else {
                    this.showRivalContact = false;
                    this.rivalAcct = {
                        bank_fax: '',
                            bank_mail: '',
                            bank_phone: '',
                    }
                }
                switch (this.transferInfo.cop_busin_class) {
                    case '1':
                        if (this.actionType=='add') {
                            if (val == 20020) {
                                this.transferInfo.settle_ment = '1'
                            } else {
                                this.transferInfo.settle_ment = '3'
                            }
                        }
                        this.showRival = false;
                        this.showReportCode = val == 20020;
                        //设置清算机构
                        if(val == 20117 || val == 10071 || val == 30008){
                            this.showClearAgent = false;
                            this.transferInfo.clear_agent_code_cn='3';
                        }else if(val == 10070 || val == 20116 || val == 30110){
                            this.showClearAgent = false;
                            this.transferInfo.clear_agent_code_cn='4';
                        }else if(!this.showReportCode){
                            this.showClearAgent = true;
                        }else if(this.showReportCode){
                            this.showClearAgent = false;
                        }
                        break;
                    case '4,5,6':
                    case '4':
                    case '5':
                    case '6':
                        this.transferInfo.clear_agent_code_cn='';
                        this.showRival = false;
                        this.showReportCode = false;
                        this.showClearAgent = false;
                        break
                    case '9':
                        this.transferInfo.clear_agent_code_cn='';
                        /*this.showRival =
                            this.transferInfo.cop_busin_flag != 20045
                            && this.transferInfo.cop_busin_flag != 20046*/
                        this.showRival = false;
                        this.showReportCode = val == 20017 || val == 20047 || val == 20049 || val == 20126 || val == 20128 || val == 20050 || val == 20051
                            || val == 20139 || val == 20140 || val == 10053 || val == 10054 || val == 10098 || val == 10099;
                        this.showClearAgent = false;
                        break
                    case '7' :
                        this.transferInfo.clear_agent_code_cn='';
                        this.showRival = false;
                        this.showReportCode = false;
                        this.showClearAgent = false;
                        break;
                    case '20':
                        this.transferInfo.clear_agent_code_cn='';
                        this.showRival = false;
                        this.showReportCode = false;
                        this.showClearAgent = false;
                        break;
                    default:
                        this.transferInfo.clear_agent_code_cn='';
                        this.showRival = false;
                        this.showReportCode = false;
                        this.showClearAgent = false;
                        break;

                }
            },
