                                            this.rivalAcct.bank_phone = this.outAcctList[0].bank_phone
                                            this.rivalAcct.bank_fax = this.outAcctList[0].bank_fax
                                            this.rivalAcct.bank_mail = this.outAcctList[0].bank_mail;
