                                    }
                                } else {
                                    /*if (this.actionType != 'modify' && this.actionType != 'detail' && !this.useTemplateFlag) {
                                        this.outAcct = {};
                                    }*/
                                    if (this.getAcct) {
                                        this.outAcct = {};
                                    }
                                }

                                if (this.inAcctList && this.inAcctList.length > 0) {
                                    if (this.inAcctList[0].account_class == 'i' || this.inAcctList[0].account_class == '7') {
                                        this.transferInfo.rival_code = this.inAcctList[0].rival_code;
                                    }
                                    if (this.inAcctList[0].account_class == 'd') {
                                        this.transferInfo.rival_code = this.inAcctList[0].issuer_code;
                                    }
                                    let acct = this.inAcctList[0];
                                    if(acct.bank_account_no==this.outAcct.bank_account_no && this.inAcctList.length>1 && acct.account_class == '2'){
                                        acct = this.inAcctList[1];
                                    }
                                    if (this.actionType == 'modify' || this.actionType == 'detail'  || this.useTemplateFlag) {
                                        //修改业务小类时（从一个小类改到另一个小类）
                                        if (oldValue.fundId && oldValue.businClass && oldValue.businFlag) {
                                            Object.assign(this.inAcct, acct);
                                        } else {

                                        }
                                    } else {
                                        this.inAcct = {
                                            fullName:acct.fullName,
                                            bank_account_no: acct.bank_account_no,
                                            bank_account_name: acct.bank_account_name,
                                            bank_account_id: acct.bank_account_id,
                                            bic_code: acct.bic_code,
                                            sub_bank_no: acct.sub_bank_no,
                                            pay_no: acct.pay_no,
                                            city_no:acct.city_no,
                                            province_code:acct.province_code,
                                            rival_code:acct.rival_code
                                        }
                                        if(this.showRivalContact&&(acct.account_class == 'f'||acct.account_class == 'd'||acct.account_class == 'h')){
                                            this.rivalAcct.bank_phone = acct.bank_phone
                                            this.rivalAcct.bank_fax = acct.bank_fax
                                            this.rivalAcct.bank_mail = acct.bank_mail;
                                        }
                                        /*Object.assign(this.inAcct, this.inAcctList[0]);*/
                                    }
                                } else {
                                    /*if (this.actionType != 'modify' && this.actionType != 'detail' && !this.useTemplateFlag) {
                                        this.inAcct = {};
                                    }*/
                                    if (this.getAcct) {
                                        this.inAcct = {};
                                    }
                                }

                                if (res.data.data.bankCorporateLink == true) {
                                    this.bankCorporateLink = true;
                                } else {
                                    this.bankCorporateLink = false;
                                }
                            } else {
                                // this.$hMessage.error(res.data.msg);
                            }
                        });
                        if(this.transferInfo.cop_busin_class == '1' && this.transferInfo.cop_busin_flag == '20017'){
                            this.showClearAgent = true;
                            this.showReportCode = false;
                            this.transferInfo.settle_ment = '3'
                        }else if(this.transferInfo.cop_busin_class == '9' && this.transferInfo.cop_busin_flag == '20017'){
                            this.showClearAgent = false;
                            this.showReportCode = true;
                        }
                        this.businFlagChange(this.transferInfo.cop_busin_class,this.transferInfo.cop_busin_flag);
                        this.updateCurrency();
                    } else {
                        this.outAcctList = []
                        this.inAcctList = []
                        this.outAcct ={
                            bank_account_no: "",
                            bic_code: '',
                            bank_account_name: '',
