            macroParam(newValue, oldValue) {
                if(this.inAcct.account_class == 'f' || this.outAcct.account_class == 'f'){
                    if (this.getMacro && newValue.fundId && this.custodial_no !== null && this.custodial_no !== '' && newValue.businClass && newValue.businFlag && newValue.inAccId && newValue.outAccId) {
                        //金额从不带小数点到带小数点时，有问题（如从5到5.00，从5.1到5.10等）--Hui的控件问题
                        /*if (parseFloat(newValue.settleBalance) === parseFloat(oldValue.settleBalance)) {
                            return;
                        }*/

                        //其他字段都不变，只有金额变化
                        if (newValue.fundId === oldValue.fundId && newValue.businClass === oldValue.businClass && newValue.businFlag === oldValue.businFlag
                            && newValue.agentCode === oldValue.agentCode && newValue.payTime === oldValue.payTime && newValue.inAccId === oldValue.inAccId
                            && newValue.outAccId === oldValue.outAccId && newValue.custodialNo === oldValue.custodialNo) {
                            //金额从不带小数点到带小数点时，有问题（如从5到5.00，5.到5.00，从5.1到5.10等）--Hui的控件问题
                            /*if (newValue.settleBalance === oldValue.settleBalance + '.00' || newValue.settleBalance === oldValue.settleBalance + '00'
                                || newValue.settleBalance === oldValue.settleBalance + '0') {
                                return;
                            }*/
                            if (newValue.settleBalance === oldValue.settleBalance + '.00') {
                                return;
                            }
                        }

                        this.refreshMacro();
                    }
                }else{
                    if (this.getMacro && newValue.fundId && this.custodial_no !== null && this.custodial_no !== '' && newValue.businClass && newValue.businFlag) {

                        if (newValue.fundId === oldValue.fundId && newValue.businClass === oldValue.businClass && newValue.businFlag === oldValue.businFlag
                            && newValue.agentCode === oldValue.agentCode && newValue.payTime === oldValue.payTime && newValue.custodialNo === oldValue.custodialNo) {
                            if (newValue.settleBalance === oldValue.settleBalance + '.00') {
                                return;
                            }
                        }

                        this.refreshMacro();
                    }
                }
            },
            showDialog: function (show) {
                if (show) {
                    if (this.actionType=='add'){
                        getBusinClassTree().then(res => {
                            this.openMask();
                            this.businClassTree = res.data;
                            if (this.queryForm.businClass != '') {
                                this.copBusinFlagList =
                                    this.businClassTree.filter(
                                        value => value.value == this.queryForm.businClass)[0].children;
                            }
                        });
                    } else{
                        getSameDirectBusinFlag({
                                                   businClass: this.transferInfo.cop_busin_class,
                                                   cashDirection: this.transferInfo.cash_direction,
                                                   businFlag: this.transferInfo.cop_busin_flag
                                                   // dataSource: this.transferInfo.data_source
                                               }).then(res => {
                            this.openMask();
                            this.copBusinFlagList = res.data;
                            let businClass = this.transferInfo.cop_busin_class
                            if (this.transferInfo.cop_busin_class == '4'
                                || this.transferInfo.cop_busin_class == '5'
                                || this.transferInfo.cop_busin_class == '6') {
                                businClass = '4,5,6'
                            }
                            this.businClassTree = this.businClassTreeAll.filter(value => value.value == businClass)
                            this.businClassTree[0].children = res.data
                        }).catch(error => {
                            // this.$hMessage.error('获取相同资金方向业务类型出错');
                            console.log(error)
                        });
                    }
                    this.activeName = [ '2', '3'];
