        computed: {
            showAcctCity(){
                let isShow = (this.transferInfo.cop_busin_flag&&this.outAcct.bic_code=='CMBCCNBS'&&this.transfer_type=='1'&&this.transferInfo.settle_ment!='3'
                    &&this.transferInfo.cop_busin_flag!=20135&&this.transferInfo.cop_busin_flag!=20074
                    &&this.transferInfo.cop_busin_flag!=20116
                    &&this.transferInfo.cop_busin_flag!=10070
                    &&this.transferInfo.cop_busin_flag!=30008
                    &&this.transferInfo.cop_busin_flag!=30110
                    &&this.transferInfo.cop_busin_flag!=20117
                    &&this.transferInfo.cop_busin_flag!=10071) || this.bankCorporateLink;
                return isShow;
            },
            provinceCity: {
                get: function () {
                    // if (this.actionType!='add'){
                    //     return [this.transferInfo.self_province_code, this.transferInfo.self_city_no]
                    // }
                    return [this.inAcct.province_code, this.inAcct.city_no]
                },
                set: function (newValue) {
                    this.inAcct.province_code = newValue[0]
                    this.inAcct.city_no = newValue[1]
                }
            },
            outProvinceCity: {
                get: function () {
                    // if (this.actionType!='add'){
                    //     return [this.transferInfo.self_province_code, this.transferInfo.self_city_no]
                    // }
                    return [this.outAcct.province_code, this.outAcct.city_no]
                },
                set: function (newValue) {
                    this.outAcct.province_code = newValue[0]
                    this.outAcct.city_no = newValue[1]
                }
            },
            upBalance() {
                return smalltoBIG(this.transferInfo.settle_balance);
            },

            acctOptionsParam() {
                return {
                    fundId: this.transferInfo.fund_id,
                    businClass: this.transferInfo.cop_busin_class,
                    businFlag: this.transferInfo.cop_busin_flag,
                    agentCode: this.transferInfo.clear_agent_code_cn,
                    settleMent : this.transferInfo.settle_ment,
                    reportCode : this.transferInfo.report_code,
                    agencyCode: this.transferInfo.agency_code,
                    custodialNo :this.custodial_no,
                }
            },
            macroParam() {
                return {
                    fundId: this.transferInfo.fund_id,
                    businClass: this.transferInfo.cop_busin_class,
                    businFlag: this.transferInfo.cop_busin_flag,
                    agentCode: this.transferInfo.clear_agent_code_cn,
                    settleBalance: this.transferInfo.settle_balance,
                    payTime: this.transferInfo.require_pay_date,
                    inAccId: this.inAcct.original_bank_account_id ?  this.inAcct.original_bank_account_id : this.inAcct.bank_account_id,
                    outAccId: this.outAcct.original_bank_account_id ? this.outAcct.original_bank_account_id : this.outAcct.bank_account_id,
                    custodialNo :this.custodial_no,
                    rivalCode: this.transferInfo.rival_code
                }
            },
            businClassFlag: {
                get: function () {
                    if (this.showTemplateList) {
                        return [this.queryTemp.cop_busin_class, this.queryTemp.cop_busin_flag]
                    }
                    let businClass = this.transferInfo.cop_busin_class
                    if (this.transferInfo.cop_busin_class == '4'
                        || this.transferInfo.cop_busin_class == '5'
                        || this.transferInfo.cop_busin_class == '6') {
                        businClass = '4,5,6'
                    }
                    return [businClass, this.transferInfo.cop_busin_flag]
                },
                set: function (newValue) {
                    if (this.showTemplateList) {
                        this.queryTemp.cop_busin_class = newValue[0]
                        this.queryTemp.cop_busin_flag = newValue[1]
                    } else {
                        this.transferInfo.cop_busin_class = newValue[0]
                        this.transferInfo.cop_busin_flag = newValue[1]
                    }

                }
            },
            beginDate: {
                get: function () {
                    return parseDateString(this.transferInfo.fee_begin_date)
                },
                set: function (newValue) {
                    this.transferInfo.fee_begin_date = parseDateStr(newValue)
                }
            },
            payDateTime: {
                get: function () {
                    if(this.transferInfo.require_pay_date || this.transferInfo.require_pay_time){
                        return parseDateString(this.transferInfo.require_pay_date,this.transferInfo.require_pay_time)
                    }
                    return this.sysDate;
                },
                set: function (newValue) {
                    this.transferInfo.require_pay_date = parseDateStr(newValue)
                    this.transferInfo.require_pay_time = parseDateStr(newValue,'hhmmss')
                }
            },
            endDate: {
                get: function () {
                    return parseDateString(this.transferInfo.fee_end_date)
                },
                set: function (newValue) {
                    this.transferInfo.fee_end_date = parseDateStr(newValue)
                }
            },
            gridName() {
                let gridName = "approveGrid";
                if (this.pageType === "1") {
                    gridName = "transferMakeGrid";//指令制作
                } else if (this.pageType === "2") {
                    gridName = "approveGrid";//指令审批
                }
                return gridName;
            }
        },
        methods: {
            showCity:showCity,
            showText: showText,
            parseDateString:parseDateString,
            formatMoney: formatMoney,
            previewPDF(fileId) {
                let pdfUrl = "/cop-web/static/plugins/pdfjs/generic/web/viewer.html?file="+encodeURIComponent(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=1&fileId=' +fileId);

                var myWindow=window.open(pdfUrl,'划款单预览','width=800,height=600');
                myWindow.focus();
            },
            previewTransfer(transferId,modifyId){
                getTransferFile({transferId:transferId,modifyId:modifyId}).then(res => {
                    if (res.data.success) {
                        this.previewPDF(res.data.data)
                    }else {
                        this.$alert(res.data.msg,'提示',{
                            confirmButtonText: '确定',
                            type: 'error'
                        })
                    }
                })
            },
            showTADetail(fundId,businFlag,cashSettleDate){
                let taType =''
                if ('30066,30067,30068'.indexOf(businFlag)>=0){
                    taType = '1'
                }else if ('30087,30088,40002'.indexOf(businFlag)>=0){
                    taType = '2'
                }else{
                    taType = '3'
                }

                listTAFlow({fundId:fundId,settleDate:cashSettleDate,taType:taType}).then(res => {
                    if (res.data.success) {
                        this.$refs.taDetailTable.setData(res.data.data);
                    }
                })
                this.showTADialog = true
            },
            addSeatFee() {
                if (this.seatList.length <= 19) {
                    this.seatList.push({
                                           seatId: '',
                                           fee: '',
                                       });
                } else {
                    this.$alert('最多只能添加20个席位','提示',{
                        confirmButtonText: '确定',
                        type: 'warning'
                    })
                }
            },
            delSeatFee(index) {
                this.seatList.splice(index, 1);
                this.feeChange();
            },
            feeChange() {
                if (this.seatList && this.seatList.length > 0) {
                    let sumFee = 0;
                    this.seatList.forEach(item => {
                        if (item.fee) {
                            sumFee += parseFloat(item.fee);
                        }
                    });
                    this.transferInfo.settle_balance = sumFee;
                }
            },
            fundChanged(value) {
                if(this.actionType=='detail'){
                    return
                }

                this.$set(this.acctOptionsParam, 'fundId', value)
                if(value&&value!=''&&value.indexOf(",")<0){
                    getFundDetail(value).then(res => {
                        if (res.data.success) {
                            this.macroInfo.fund_code = res.data.data.fund_code;
                            this.macroInfo.fund_name = res.data.data.fund_name;
                            this.macroInfo.sz_ta_account = res.data.data.sz_ta_account;
                            this.macroInfo.sh_ta_account = res.data.data.sh_ta_account;
                            this.macroInfo.stockholder_id = res.data.data.stockholder_id;
                            this.macroInfo.stockholder_id_sz = res.data.data.stockholder_id_sz;
                            var sqAcct = res.data.data.accountList.filter(x=>x.account_class==5)
                            if (sqAcct&&sqAcct.length>0){
                                this.macroInfo.sq_holder_account = sqAcct[0].holder_account
                            }
                            var zzAcct = res.data.data.accountList.filter(x=>x.account_class==6)
                            if (zzAcct&&zzAcct.length>0){
                                this.macroInfo.zz_holder_account = zzAcct[0].holder_account
                            }
                        }
                    });
                    if (this.actionType=='add'){
                        getTrustAcct({fundId: value, accountClass: '2'}).then(res => {
                            if (res.data.success) {
                                if (res.data.data && res.data.data.length > 0) {
                                    this.selfAcct = res.data.data[0]
                                    this.transfer_type = res.data.data[0].transfer_type
                                    this.custodial_no =res.data.data[0].custodial_no;
                                }else {
                                    this.transfer_type = null
                                    this.custodial_no =null
                                }
                            }
                        })
                        this.updateCurrency();
                    }
                }

            },
            updateCurrency(){
                //如果是进入修改页面，则不需要更新币种，维持原有信息
                if (this.actionType=='modify') {
                    return;
                }
                //判断当前业务类型，不是存款的话直接置为人民币
                if (this.transferInfo.cop_busin_class == null || this.transferInfo.cop_busin_class !='8'){
                    this.transferInfo.currency_no = 'CNY';
                } else if (this.selfAcct.currency_no != null && this.selfAcct.currency_no != ''){
                    this.transferInfo.currency_no = this.selfAcct.currency_no;
                } else {
                    this.transferInfo.currency_no = 'CNY';
                }
            },
            outAcctChanged(value) {
                if (value && value.length > 128) {
                    this.outAcct.fullName = '';
                    this.$alert('长度不能超过128!','提示',{
                        confirmButtonText: '确定',
                        type: 'info'
                    });
                    return;
                }
                let acct = this.outAcctList.filter(x => x.fullName == value);
                if (acct.length > 0) {
                    this.outAcct.bank_account_id = acct[0].bank_account_id;
                    this.outAcct.bic_code = acct[0].bic_code
                    this.outAcct.bank_account_name = acct[0].bank_account_name
                    this.outAcct.bank_account_no = acct[0].bank_account_no
                    this.outAcct.pay_no = acct[0].pay_no
                    this.outAcct.sub_bank_no = acct[0].sub_bank_no
                    this.outAcct.account_class = acct[0].account_class;
                    this.outAcct.province_code = acct[0].province_code
                    this.outAcct.city_no = acct[0].city_no;
                    this.outAcct.account_class = acct[0].account_class
                    // 如果是理财/开基/对手方账户，更换时需要同时更换transferinfo中的rival_code
                    if (acct[0].account_class == 'i' || acct[0].account_class == '7') {
                        this.transferInfo.rival_code = acct[0].rival_code;
                    }
                    if (acct[0].account_class == 'd') {
                        this.transferInfo.rival_code = acct[0].issuer_code;
                    }
                    if (acct[0].account_class=='2'){
                        this.custodial_no = acct[0].custodial_no;
                        this.transfer_type = acct[0].transfer_type
                        this.selfAcct.bank_phone = acct[0].bank_phone
                        this.selfAcct.bank_fax = acct[0].bank_fax
                        this.selfAcct.bank_mail = acct[0].bank_mail;
                        this.selfAcct.sh_fdb_account = acct[0].sh_fdb_account;
                        this.selfAcct.sz_fdb_account = acct[0].sz_fdb_account;
                        this.selfAcct.currency_no = acct[0].currency_no;
                    }else if(acct[0].account_class == 'f'){
                        this.macroParam.outAccId = acct[0].bank_account_id;
                        getMacroPattern(this.macroParam).then(res => {
                            if(res.data){
                                let patterns = {
                                    postscript: res.data.postscript,
                                    purpose: res.data.purpose,
                                    remark: res.data.remark,
                                };

                                if(patterns.postscript && patterns.postscript.length > 0){
                                    this.transferInfo.transfer_postscript = resolveMacro(this.transferInfo, this.macroInfo, patterns.postscript,
                                        this.businClassList,this.businFlagList,this.clearAgentList,this.settleMentList,this.depositTimeUnitList,
                                        this.selfAcct);
                                }
                                if(patterns.purpose && patterns.purpose.length > 0){
                                    this.transferInfo.transfer_purpose = resolveMacro(this.transferInfo, this.macroInfo, patterns.purpose,
                                        this.businClassList,this.businFlagList,this.clearAgentList,this.settleMentList,this.depositTimeUnitList,
                                        this.selfAcct);
                                }
                                if(patterns.remark && patterns.remark.length > 0){
                                    this.transferInfo.transfer_remark = resolveMacro(this.transferInfo, this.macroInfo, patterns.remark,
                                        this.businClassList,this.businFlagList,this.clearAgentList,this.settleMentList,this.depositTimeUnitList,
                                        this.selfAcct);
                                }
                            }
                        })
                    }else if(this.showRivalContact&&(acct[0].account_class == 'f'||acct[0].account_class == 'd'||acct[0].account_class == 'h')){
                        this.rivalAcct.bank_phone = acct[0].bank_phone
                        this.rivalAcct.bank_fax = acct[0].bank_fax
                        this.rivalAcct.bank_mail = acct[0].bank_mail;
                    }
                } else {
                    this.outAcct = {
                        bank_account_no: "",
                        bic_code: null,
                        bank_account_name: value,
                        fullName:value,
                        pay_no: ''
                    }
                }
            },
