            refreshMacro() {
                    let businClass;
                    if(this.transferInfo.cop_busin_class == '4,5,6'){
                        getBusinFlagInfo(null,this.transferInfo.cop_busin_flag).then(res => {
                            businClass = res.data.data.cop_busin_class;
                        })
                    }
                    getMacroPattern(this.macroParam).then(res => {
                        if (res.data) {
                            let patterns = {
                                postscript: res.data.postscript,
                                purpose: res.data.purpose,
                                remark: res.data.remark
                            };
                            let detail = {};
                            Object.assign(detail, this.transferInfo)

                            if(detail.cop_busin_class == '4,5,6'){
                                detail.cop_busin_class = businClass;
                            }
                            if(patterns.purpose&&patterns.purpose.length>0){
                                this.transferInfo.transfer_purpose =
