                this.$refs.templateLstData.dataChange();
            },
            saveTemplate() {
                let isValid = true;
                this.$refs.tempForm.validate((valid) => isValid = isValid && valid);
                if (!isValid) {
                    this.$alert('输入项不满足要求，请重新检查!', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }
                let transDTO = {};
                Object.assign(transDTO, this.transferInfo);
                transDTO.template_name = this.transferInfo4Temp.template_name;
                transDTO.remark = this.transferInfo4Temp.remark;
                transDTO.selfAcct = this.outAcct;
                transDTO.rivalAcct = this.inAcct;
                if(transDTO.cash_direction =='1'){
                    transDTO.rivalAcct.bank_fax = this.selfAcct.bank_fax
                    transDTO.rivalAcct.bank_mail = this.selfAcct.bank_mail
                    transDTO.rivalAcct.bank_phone = this.selfAcct.bank_phone
                    transDTO.selfAcct.bank_fax = this.rivalAcct.bank_fax
                    transDTO.selfAcct.bank_mail = this.rivalAcct.bank_mail
                    transDTO.selfAcct.bank_phone = this.rivalAcct.bank_phone
                }else{

                    transDTO.selfAcct.bank_fax = this.selfAcct.bank_fax
                    transDTO.selfAcct.bank_mail = this.selfAcct.bank_mail
                    transDTO.selfAcct.bank_phone = this.selfAcct.bank_phone
                    transDTO.rivalAcct.bank_fax = this.rivalAcct.bank_fax
                    transDTO.rivalAcct.bank_mail = this.rivalAcct.bank_mail
                    transDTO.rivalAcct.bank_phone = this.rivalAcct.bank_phone
                }

                transDTO.custodian_bank_no = this.custodial_no;

                //设置席位相关字段
                if (this.transferInfo.cop_busin_class === '22'
                    && this.transferInfo.cop_busin_flag === '20061' &&
                    this.seatList && this.seatList.length > 0) {
                    let seatIdList = '';
                    let feeList = '';
                    this.seatList.forEach(item => {
                        seatIdList = seatIdList + item.seatId + ',';
                        feeList = feeList + item.fee + ',';
                    });
                    transDTO.seat_id_list = seatIdList.substring(0, seatIdList.length - 1);
                    transDTO.fee_list = feeList.substring(0, feeList.length - 1);
                }

                saveTemplate(transDTO).then(res => {
                    if (res.data.success) {
                        this.showDialog = false
                        this.showTemplate = false
                        this.openMask();
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    } else {
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }
                });
            },
            saveTransfer(isQuick,commit) {
                let isValid = true;
                if (isQuick) {
                    this.transferInfo.settle_balance = this.settleBalanceQuick;
                    this.$refs.quickForm.validate((valid) => isValid = isValid && valid);
                } else {
                    this.$refs.transForm0.validate((valid) => isValid = isValid && valid);
                    this.$refs.transForm.validate((valid) => isValid = isValid && valid);
                    this.$refs.outAcctForm.validate((valid) => isValid = isValid && valid);
                    this.$refs.inAcctForm.validate((valid) => isValid = isValid && valid);
                    if (this.$refs.seatListForm && this.$refs.seatListForm.length > 0) {
                        this.$refs.seatListForm.forEach(seatForm => {
                            seatForm.validate((valid) => isValid = isValid && valid);
                        });
                    }
                    this.$refs.selfAcctForm.validate((valid) => isValid = isValid && valid);
                }
                if(!this.transferInfo.require_pay_date || !this.transferInfo.require_pay_time){
                    this.$alert('付款时间不能为空!','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return
                }

                if (!isValid) {
                    this.$alert('输入项不满足要求，请重新检查!','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                } else if (!this.transferInfo.cop_busin_class || !this.transferInfo.cop_busin_flag
                    || !this.outAcct.fullName || !this.inAcct.fullName) {
                    this.$alert('输入项不满足要求，请重新检查!', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                } else if (this.transferInfo.settle_balance<=0) {
                    this.$alert('划款金额必须大于0!','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                } else if(this.transferInfo.cop_busin_flag == '30006' && this.outAcct.bank_account_id == this.inAcct.bank_account_id){
                    this.$alert('银行账户内转业务出入款账户不能一致！','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                } else {
                    let transDTO = {};
                    Object.assign(transDTO, this.transferInfo);
                    transDTO.selfAcct = this.outAcct;
                    transDTO.rivalAcct = this.inAcct;

                    if(transDTO.cash_direction =='1'){
                        transDTO.rivalAcct.bank_fax = this.selfAcct.bank_fax
                        transDTO.rivalAcct.bank_mail = this.selfAcct.bank_mail
                        transDTO.rivalAcct.bank_phone = this.selfAcct.bank_phone
                        transDTO.selfAcct.bank_fax = this.rivalAcct.bank_fax
                        transDTO.selfAcct.bank_mail = this.rivalAcct.bank_mail
                        transDTO.selfAcct.bank_phone = this.rivalAcct.bank_phone
                    }else{
                        transDTO.selfAcct.bank_fax = this.selfAcct.bank_fax
                        transDTO.selfAcct.bank_mail = this.selfAcct.bank_mail
                        transDTO.selfAcct.bank_phone = this.selfAcct.bank_phone
                        transDTO.rivalAcct.bank_fax = this.rivalAcct.bank_fax
                        transDTO.rivalAcct.bank_mail = this.rivalAcct.bank_mail
                        transDTO.rivalAcct.bank_phone = this.rivalAcct.bank_phone
                    }

                    transDTO.custodian_bank_no = this.custodial_no;

                    //设置席位相关字段
                    if (this.transferInfo.cop_busin_class === '22'
                        && this.transferInfo.cop_busin_flag === '20061' &&
                        this.seatList && this.seatList.length > 0) {
                        let seatIdList = '';
                        let feeList = '';
                        this.seatList.forEach(item => {
                            seatIdList = seatIdList + item.seatId + ',';
                            feeList = feeList + item.fee + ',';
                        });
                        transDTO.seat_id_list = seatIdList.substring(0, seatIdList.length - 1);
                        transDTO.fee_list = feeList.substring(0, feeList.length - 1);
                    }

                    const loading = this.$loading({
                        lock: true,
                        text: '处理中',
                        spinner: 'el-icon-loading',
                        background: 'rgba(0, 0, 0, 0.7)'
                    });
                    //保存并提交
                    transDTO.commitFlag = commit
                    saveTransfer(transDTO).then(res => {
                        if (res.data.success) {
                            loading.close();
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText: '确定',
                                type: 'success',
                                callback:action => {
                                    this.handleCancel();
                                }
                            }).then(() => {
                                this.handleCancel();
                            })
                            this.refreshFundTree()
                        } else {
                            loading.close();
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText: '确定',
                                type: 'error',
                                callback:action => {
                                    this.handleCancel();
                                }
                            }).then(() => {
                                this.handleCancel();
                            })
                        }
                    }).catch(error => {
                        loading.close();
                        this.$alert('请求失败，请检查登录状态','错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                                this.handleCancel();
                            }
                        }).then(() => {
                            this.handleCancel();
                        })
                    });
                }

            },
            quickSend(accountClass) {
                this.selfAcct = {
                    bank_fax: '',
                    bank_mail: '',
                    bank_phone: '',
                    sh_fdb_account: '',
                    sz_fdb_account: '',
                    currency_no:'',
                }
                //重置表单
                this.$refs.quickForm.resetFields();
                this.$refs.transForm0.resetFields();
                this.$refs.transForm.resetFields();
                this.$refs.outAcctForm.resetFields();
                this.$refs.inAcctForm.resetFields();
                // this.$refs.selfAcctForm.resetFields();


                this.miniTrans = true;
                this.actionType='add'
                this.transferInfo.fund_id = this.queryForm.fundIds;
                this.transferInfo.cop_busin_class = '1'
                this.bankBalance = this.trustAcctList[0].ACCOUNT_CUR_BALANCE;
                if (accountClass == 5) {
                    this.dvpBalance = this.acctBalanceList[0].ACCOUNT_CUR_BALANCE;
                    this.dvpSum = this.businCashList[0].AMOUNT;
                    this.transferInfo.clear_agent_code_cn ='4'
                } else {
                    this.dvpBalance = this.acctBalanceList[1].ACCOUNT_CUR_BALANCE;
                    this.dvpSum = this.businCashList[1].AMOUNT;
                    this.transferInfo.clear_agent_code_cn ='3'
                }

                this.businFlagChange(this.transferInfo.cop_busin_class, this.transferInfo.cop_busin_flag);

                var sum = parseFloat(this.dvpBalance) + parseFloat(this.dvpSum);
                if (sum >= 0) {
                    this.transferInfo.settle_balance = sum;
                    this.settleBalanceQuick = sum;
                    if (accountClass =='5'){
                       this.quickFlagList = this.OutFromSHQList
                       this.transferInfo.cop_busin_flag = 10070
                    }else{
                      this.quickFlagList = this.OutFromZZList
                      this.transferInfo.cop_busin_flag = 10071
                    }
                } else {
                    // var debt = sum + parseFloat(this.bankBalance)
                    // if (debt > 0) {
                        this.transferInfo.settle_balance = -sum;
                        this.settleBalanceQuick = -sum;
                        if (accountClass =='5'){
                          this.quickFlagList = this.IntoSHQList
                          this.transferInfo.cop_busin_flag = 20116
                        }else{
                          this.quickFlagList = this.IntoZZList
                          this.transferInfo.cop_busin_flag = 20117
                        }
                    // } else {
                    //     this.transferInfo.settle_balance = this.bankBalance;
                    //     this.settleBalanceQuick = this.bankBalance;
                    // }
                }
            },
            clickDaySettle(e,val){
                let fundInfoData  = {};
                if(val){
                    this.queryForm.showToday = '1';
                    this.$refs.treeFund.showToday ='1'
                }else{
                    this.queryForm.showToday = '0';
                    this.$refs.treeFund.showToday ='0'

                }
                fundInfoData.code = this.queryForm.fundIds;
                this.fundClick(fundInfoData);
                this.refreshFundTree();
            },
            fundClick(data) {
                const orignGroup = [{
                    cop_busin_class: '',
                    dic_name: '全部',
                    count: 0
                }]
                if(!data){
                    this.businGroup = orignGroup
                    this.queryForm.fundIds=''
                    this.refreshTable();
                    return
                }

                this.queryForm.fundIds = data.code;
                if (this.queryForm.businClass == '') {
                    this.$refs.datagrid.changeGrid(this.gridName);
                } else {
                    let gridStr = this.gridName+"-"+this.queryForm.businClass;
                    this.$refs.datagrid.changeGrid(gridStr.replace(/[,]/g,''));
                }
                getBusinClassGroup({
                                       fundId: data.code,
                                       isSettle: false,
                                       showToday:this.queryForm.showToday,
                                       pageType:this.pageType
                                   }).then(res => {
                    if (res.data.success && res.data.data.length > 0) {


                        this.businGroup = res.data.data
                    }
                });
                this.refreshTable();
                if (data.code.indexOf(",")<0){
                    this.queryTemp.fundIds = data.code;
                    findAccAmountByFundId(data.code, Cookies.get('CurrSysDate')).then(res => {
                        this.acctBalanceList = []
                        this.trustAcctList = []
                        res.data.forEach(value => {
                            if (value.ACCOUNT_CLASS =='2'){
                                this.trustAcctList.push({
                                                            ACCOUNT_CLASS: value.ACCOUNT_CLASS,
                                                            BANK_ACCOUNT_NAME: value.BANK_ACCOUNT_NAME + '余额'
                                                                               + parseMinute(
                                                                value.INPUT_TIME),
                                                            ACCOUNT_CUR_BALANCE: value.ACCOUNT_CUR_BALANCE
                                                        });
                            }else {
                                this.acctBalanceList.push({
                                                              ACCOUNT_CLASS: value.ACCOUNT_CLASS,
                                                              BANK_ACCOUNT_NAME: value.BANK_ACCOUNT_NAME + '余额' + parseMinute(value.INPUT_TIME),
                                                              ACCOUNT_CUR_BALANCE: value.ACCOUNT_CUR_BALANCE
                                                          })
                            }
                        });
                    });
                    findFundCashByFundId(data.code, Cookies.get('CurrSysDate')).then(res => {
                        this.fundCashList = res.data;
                        this.getBusinCashList();
                    });
                }

            },
            // parseAcctClass(acctClass) {
            //     switch (acctClass) {
            //         case '2':
            //             return '托管户'
            //         case '5':
            //             return '上清DVP'
            //         case '6':
            //             return '中债DVP'
            //         default:
            //             return ''
            //     }
            //
            // },
            businClassChanged() {
                if(this.queryForm.businClass == 0){
                    this.queryForm.businClass = '';
                }
                this.getBusinCashList();
                if (this.queryForm.businClass == '') {
                    this.$refs.datagrid.changeGrid(this.gridName);
                } else {
                    let gridStr = this.gridName+"-"+this.queryForm.businClass;
                    this.$refs.datagrid.changeGrid(gridStr.replace(/[,]/g,''));
                }
                // ||(&&)
                this.refreshTable();
                this.re_layout("full-h-wrapper-on-transfer-approve", "inner-on-transfer-approve", 108);
            },
            getBusinCashList() {
                switch (this.queryForm.businClass) {
                    case "1":
                        this.businCashList =
                            this.fundCashList.filter(
                                x => x.CASHTYPEID == 4 || x.CASHTYPEID == 5 || x.CASHTYPEID == 9)
                        break;
                    case "4,5,6":
                        this.businCashList = this.fundCashList.filter(x => x.CASHTYPEID == 6 || x.CASHTYPEID == 7);
                        break;
                    case '7':
                        //开基
                        this.businCashList = this.fundCashList.filter(x => x.CASHTYPEID == 2)
                        break;
                    case '8':
                        this.businCashList = this.fundCashList.filter(x => x.CASHTYPEID == 3)
                        //存款
                        break;
                    case '9':
                        //网下申购
                        this.businCashList = this.fundCashList.filter(x => x.CASHTYPEID == 1)
                        break
                    default:
                        this.businCashList = []
                        break
                }
            },
