                this.showDialog = false
                this.showTemplateList = false
                this.miniTrans = false
            },
            handleTemplateCancel(){
                this.showTemplate = false
            },
            handleCommand(command) {
                if (command === "3") {
                    this.handleUnion();
                } else if (command === "1" || command === "2") {
                    this.openImport(command);
                } else if (command === "4"||command === "5") {
                    this.handleUrgentFlag(command);
                } else if(command === "6"){
                    this.handleApprove(7)
                } else if(command === "7"){
                    this.changeDealType()
                }
            },
            changeDealType(){
                let selects = this.$refs.datagrid.getSelection();
                //console.log('handleModify', selects)
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选择指令','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                if (selects[0].transfer_merge_flag === '0' &&
                    selects[0].transfer_ins_batch_no && parseInt(selects[0].transfer_ins_batch_no) > 0) {
                    this.openMask();
                    this.$alert('轧差后的子指令无法修改', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return;
                }

                this.showDealType = true

            },
            saveDealType(){
                let selects = this.$refs.datagrid.getSelection();
                saveDealType({
                    transferList: selects,
                    param: this.dealType,
                }).then(res => {
                    this.openMask();
                    if (res.data.success) {
                        this.$alert(res.data.msg, '成功', {
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                        this.showDealType = false
                        this.refreshFundTree()
                    }else {
