            handleModify() {
                // let selects = this.$refs.datagrid.$refs.gridContent.getSelection();

                this.getMacro = false;//暂停获取托管人宏信息
                this.getAcct = false;
                let selects = this.$refs.datagrid.getSelection();
                if (selects.length != 1) {
                    this.openMask();
                    this.$alert('请选中一条指令','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                if (selects[0].transfer_merge_flag === '0' &&
                    selects[0].transfer_ins_batch_no && parseInt(selects[0].transfer_ins_batch_no) > 0) {
                    this.openMask();
                    this.$alert('轧差后的子指令无法修改', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return;
                }
                if (selects[0].node_name != 'approve') {
                    this.openMask();
