                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }

                this.actionType = 'modify'
                this.transferInfo = selects[0]
                this.transfer_type = selects[0].transfer_type
                this.custodial_no = this.transferInfo.fund_trust_bank
                getTransferDetail({
                    transferInsId:selects[0].transfer_ins_id,
                    transferInsModify:selects[0].transfer_ins_modify
                }).then(res => {
                    if (res.data.success) {
                        if (res.data.data.outAcct) {
                            this.outAcct = res.data.data.outAcct
