            union(data, question, isPrompt) {
                var len = data.length;
                if (len < 2) {
                    this.openMask();
                    this.$alert('至少需要两条指令才能轧差','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return;
                } else {
                    //过滤掉子指令
                    data = filterTransferChildren(data, this);
                    if (data.length <= 0) {
                        return;
                    } else if (data.length < 2) {
                        this.openMask();
                        this.$alert('轧差后的子指令不能操作', '错误', {
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                        return;
                    }

                    //需要提示信息（全部轧差暂时是轧当前页面，需要提示） TODO
                    if (isPrompt === 1) {
                        this.openMask();
                        this.$confirm(question, '提示', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'warning'
                        }).then(() => {
                            unionAll({
                                transferList: data
                            }).then(res => {
                                if (res.data.success) {
                                    this.$alert(res.data.msg, '成功', {
                                        confirmButtonText: '确定',
                                        type: 'success',
                                        callback: action => {
                                            this.closeMask();
                                        }
                                    }).then(() => {
                                        this.closeMask();
                                    })
                                    this.refreshFundTree()
                                } else {
                                    this.$alert(res.data.msg, '错误', {
                                        confirmButtonText: '确定',
                                        type: 'error',
                                        callback: action => {
                                            this.closeMask();
                                        }
                                    }).then(() => {
                                        this.closeMask();
                                    })
                                }
                            }).catch(error => {
                                this.$alert('请求失败，请检查登录状态', '错误', {
                                    confirmButtonText: '确定',
                                    type: 'error',
                                    callback: action => {
                                        this.closeMask();
                                    }
                                }).then(() => {
                                    this.closeMask();
                                })
                            });
                        }).catch(() => {
                            this.closeMask();
                        });
                    } else {
                        unionAll({
                            transferList: data
                        }).then(res => {
                            if (res.data.success) {
                                this.openMask();
                                this.$alert(res.data.msg, '成功', {
                                    confirmButtonText: '确定',
                                    type: 'success',
                                    callback: action => {
                                        this.closeMask();
                                    }
                                }).then(() => {
                                    this.closeMask();
                                })
                                this.refreshFundTree()
                            } else {
                                this.openMask();
                                this.$alert(res.data.msg, '错误', {
                                    confirmButtonText: '确定',
                                    type: 'error',
                                    callback: action => {
                                        this.closeMask();
                                    }
                                }).then(() => {
                                    this.closeMask();
                                })
                            }
                        }).catch(error => {
                            this.openMask();
                            this.$alert('请求失败，请检查登录状态', '错误', {
                                confirmButtonText: '确定',
                                type: 'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                        });
                    }
                }
            },
