                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                        return
                    }
                }

                //过滤掉子指令
                selects = filterTransferChildren(selects, this);
                if (selects.length <= 0) {
                    return;
                }

                this.$refs.screenLoading.open();
                handleTransfer('/cashsettle/approveOpetate.json',
