                                this.$alert(res.data.msg,'成功',{
                                    confirmButtonText: '确定',
                                    type: 'success',
                                    callback:action => {
                                        this.closeMask();
                                        this.handleCancel();
                                    }
                                }).then(() => {
                                    this.closeMask();
                                    this.handleCancel();
                                })
                                this.refreshFundTree()
                            }
                        } else if (res.data.code == "1120002") {
                            this.openMask();
                            const h = this.$createElement;
                            let errInfos =[]
                            res.data.msg.split(";").forEach(x=>{
