                                                     },
                                                   })
                                             ]),
                                             showCancelButton: true,
                                             confirmButtonText: '继续',
                                             cancelButtonText: '取消',
                                             beforeClose: (action, instance, done) => {
                                                 if (action === 'confirm') {
                                                     handleTransfer('/cashsettle/approveOpetate.json', {
                                                         transferList: selects,
                                                         flowFlag: flag,
                                                         ignore: true,
                                                         remark: value,
                                                         param: this.pageType
                                                     }).then(res => {
