            handleApprove(flag,isSingle) {

                //let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                let selects;
                if (isSingle){
                  selects = []
                  selects.push(this.transferInfo)
                }else {
                   selects = this.$refs.datagrid.getSelection();
                }

                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选中一条记录','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }

                //过滤掉子指令
                selects = filterTransferChildren(selects, this);
                if (selects.length <= 0) {
                    return;
                }

                let action = '';
                switch (flag) {
                    case 7:
                        action = "作废";
                        break;
                    case 9:
                        action = "驳回";
                        break;
                    case -8:
                        action = "无需审批直接通过";
                        break;
                    default:
                        break;
                }
                let title = "确定" + action + "所选指令?";

                if (this.pageType === '1' && flag === 9) {
                    //取消轧差时，无需确认
                    this.approve(flag, selects, '');
                } else {
                    this.openMask();
                    this.$prompt(title + '请填入原因并确认：', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                    }).then(({value}) => this.approve(flag, selects, value)).catch(() => {
                        this.closeMask();
                    }).catch(() => {
                        this.closeMask();
                    });
                }
            },
            fileUploaded(response, file, fileList) {
                if(response.code && response.code == '0'){
                    this.transferInfo.bill_name = fileList.reduce(function (x, y) {
                        return x + ',' + y.response.data
                    }, '').replace(',', '');
                }else{
                    if(!response.data){
                        this.$alert(response.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                        fileList.splice(fileList.length-1,1);
                        return
                    }
                    getFundName(this.transferInfo.fund_id).then(res =>{
                        if(res.data && res.data.code == '0'){
                            let fundName = res.data.data;
                            this.$confirm(fundName+"产品未维护公司签章，无法对于成交单进行加盖公司签章，是否继续上传?","提示",{
                                confirmButtonText:'继续',
                                cancelButtonText:'取消',
                                type:'warning'
                            }).then(() =>{
                                this.transferInfo.bill_name = fileList.reduce(function (x, y) {
                                    return x + ',' + y.response.data
                                }, '').replace(',', '');
                            }).catch(() =>{
                                fileList.splice(fileList.length-1,1);
                            })
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    })

                }
            },
            beforeUpload(file){
                if(this.transferInfo.cop_busin_class == '1'){
                    this.uploadData.fundId = this.transferInfo.fund_id;
                }else{
                    this.uploadData.fundId = '';
                }
            },
            fileRemoved(file, fileList) {
                this.transferInfo.bill_name = fileList.reduce(function (x, y) {
                    return x + ',' + y.response.data
                }, '').replace(',', '');
            },
            exceedLimit() {
                this.$alert('最多上传20个附件','错误',{
                    confirmButtonText: '确定',
                    type: 'error'
                });
            },
            downloadFile(file) {
                window.open(
                    window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=2&fileId=' +
                    file.response.data)
            },
            rivalScroll(num) {
                if (num <= 1) {
                    let length = this.rivalList.length;
                    let pageSize = 50;
                    let pagNo = length / 50;
                    getRivalList(pagNo, pageSize).then(res => {
                                                           this.rivalList = this.rivalList.concat(res.data.list)
                                                           if (!res.data.hasNextPage) {
                                                               this.rivalComplete = true
                                                           }
                                                       }
                    )
                }
            },
            rivalQuery(query) {
                this.rivalLoading = true;
                getRivalList(0, 50, query).then(res => {
                    this.rivalList = res.data.list
                    this.rivalLoading = false;
                    if (!res.data.hasNextPage) {
                        this.rivalComplete = true
                    }
                })
            },
            issuerScroll(num) {
                if (num <= 1) {
                    let length = this.issuerList.length;
                    let pageSize = 50;
                    let pagNo = length / 50;
                    getIssuerList(pagNo, pageSize).then(res => {
                                                            this.issuerList = this.issuerList.concat(res.data.list)
                                                            if (!res.data.hasNextPage) {
                                                                this.issuerComplete = true
                                                            }
                                                        }
                    )
                }
            },
            issuerQuery(query) {
                getIssuerList(0, 50, query).then(res => {
                    this.issuerList = res.data.list
                    if (!res.data.hasNextPage) {
                        this.issuerComplete = true
                    }
                })
            },
            rivalChange(rivalCode){
                if(this.transferInfo.cop_busin_class != '7' && rivalCode){
                    this.rivalList.forEach(item => {
                        if(item.code == rivalCode){
                            this.transferInfo.rival_name = item.text
                        }
                    })
                }else if(this.transferInfo.cop_busin_class == '7' && rivalCode){
                    this.issuerList.forEach(item => {
                        if(item.code == rivalCode){
                            this.transferInfo.rival_name = item.text
                        }
                    })
                }
            },
            uploadExcel(){
                this.$refs.importAccountTransfer.showDialog("3",this.queryForm.businClass);
            },
            openLayout(){
                this.$refs.datagrid.openLayout();
            },
            handleUrgentFlag(flag){
                //let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                let selects = this.$refs.datagrid.getSelection();
                if (selects == null || selects.length === 0) {
                    this.openMask();
                    this.$alert('请先选中至少一条记录', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return;
                }

                //过滤掉子指令
                selects = filterTransferChildren(selects, this);
                if (selects.length <= 0) {
                    return;
                }

                handleUrgentFlag({
                    transferList: selects,
                    param: flag
                }).then(res => {
                    if (res.data.success) {
                        this.openMask();
                        this.$alert(res.data.msg, '成功', {
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                        this.refreshFundTree();
                    } else {
                        this.openMask();
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                    }
                }).catch(error => {
                    this.openMask();
                    this.$alert(error, '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                });
            },
            rowClassName(row) {
                //“审批驳回”和“划款失败”标红显示
                if (row.transfer_flow_flag === "9" || row.transfer_fund_status === "5" || (row.transfer_merge_flag != '1' && row.settle_balance == 0)) {
                    return 'alert-table-red-row';
                } else {
                    return '';
                }
            },
            businClassChang(val){
                if(val[0] == '8' || val[0] == '21' || val[0] == '22' || val[0] == '23'){
                    this.transferInfo.invest_type = '';
                }else{
                    this.transferInfo.invest_type = '1';
                }
            },
            onBTAClose(){
                this.showBTADialog = false;
                this.refreshFundTree();
            },
            businFlagChange(businClass,businFlag){
                getBusinFlagInfo(businClass, businFlag).then( res => {
                    if(this.actionType=='add'){
                        if(res.data && res.data.code == '0'){
                            this.payDateTime = parseStrDate(Cookies.get('CurrSysDate') + res.data.data.require_pay_time);
                        }else{
                            this.payDateTime = this.sysDate
                        }
                    }
                    if(this.transferInfo.cop_busin_class == '7'){
                        if(res.data.data.account_out_class_list == 'd' || res.data.data.account_in_class_list == 'd'){
                            this.showRival = false;
                            this.showAgency = false;
                        }else if(res.data.data.account_out_class_list == 'h' || res.data.data.account_in_class_list == 'h'){
                            this.showRival = false;
                            this.showAgency = true;
                        }
                    } else {
                        this.showAgency = false;
                    }
                });
            },
            onSplitMove() {
                this.$refs.datagrid.handleResize();
            },
            onSelectionChange(selections) {
                this.selectionTipVisible = selections.length > 0
                this.selections = selections
                SSObservable.publish('onresize')
                SSObservable.publish('re_layout')
            }
        }
}
</script>

