    .block-in-box {
         .headline {
            padding-bottom: 10px;
            display: flex;
            flex-direction: row;
            align-items: center;

            .icon-l {
                display: block;
                width: 3px;
                height: 12px;
                background-color: #11a0f8;
                margin-right: 5px;
            }

            .title {
                padding: 0;
                margin-right: 10px;
                font-size: 12px;
                color: #666666;
            }

            .icon-operation {
                cursor: pointer;
