    }

    .collapse-title {
      padding: 0;
      margin-right: 10px;
      font-size: 12px;
      color: #666666;
    }

    .wrapper {
        width: 100%;
        // height: 208px;
        padding: 10px;
        border: solid 1px #d3d3d3;
        background-color: #f6f7f9;

         .headline {
            padding-bottom: 10px;
            display: flex;
            flex-direction: row;
            align-items: center;

            .icon-l {
                display: block;
