<template>
  <div class="s-switch-icon">
    <i class="iconfont" :class="iconname" @mouseleave="onMouseleave" @mouseover="onMouseover"></i>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'SwitchIcon',
  props: {
    names: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      hover: false
    }
  },
  computed: {
    iconname() {
      return this.hover ? this.names[0] : this.names[1]
    }
  },
  methods: {
    onMouseover() {
      this.hover = true
    },
    onMouseleave() {
      this.hover = false
    }
  },
  components: {

  }
}
</script>

<style lang="less" rel="stylesheet/less">
.s-switch-icon {
}
</style>



