<template>
    <div class="full-h" id="full-h-wrapper">
        <h-row >
            <h-col>
                <el-form style="font-size: 12px;":inline="true" size="mini" :model="queryForm" class="demo-form-inline">
                    <el-form-item label="产品名称" v-if="rule == 'fund'">
                        <el-select v-model="fundId" multiple clearable collapse-tags filterable>
                            <el-option v-for="item in fundIdList" :key="item.code" :label="item.text" :value="item.code"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="业务名称" v-if="rule == 'business'">
                        <el-select v-model="businessClass" filterable multiple clearable collapse-tags>
                            <el-option v-for="item in copBusinClassList" :key="item.code" :label="item.text" :value="item.code"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="refresh">查询</el-button>
                    </el-form-item>
                </el-form>
            </h-col>
        </h-row>
        <h-button-group>
            <h-button icon="add" @click="addApproveRule">新增</h-button>
            <h-button icon="editor" @click="modifyApproveRule">修改</h-button>
            <h-button icon="delete" @click="deleteApproveRule">删除</h-button>
        </h-button-group>
        <h-datagrid stripe hasPage
                    :height="autoHeight" highlight-row
                    :pageSize="100" :pageSizeOpts="pageSizeOpts"
                    :columns="approveRuleColumns" :bindForm="queryForm"
                    :on-row-db-click="approveRuleDbClick"
                    url="/systemmgnt/approveRule/approveRuleInfo.json" ref="datagrid">
        </h-datagrid>
        <h-msg-box class="customMsgBox" v-model="showApproveRuleDialog" width="400" :mask-closable="false">
            <p slot="header">
                <span v-if="actionType == 'add'">新增审批规则</span>
                <span v-else>修改审批规则</span>
            </p>
            <h-form :model="approveRuleInfo" :label-width="120" ref="approveRuleForm" >
                <h-form-item v-if="rule == 'fund' && actionType == 'add'" required label="产品名称" prop="fund_id">
                    <div>
                        <h-select showTitle style="width: 175px" v-model="approveRuleInfo.fund_id"
                                  hideMult isString multiple filterable showBottom>
                            <h-option v-for="item in fundIdList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                        </h-select>
                        <h-button style="float: right;margin-top:3px;" @click="choseFundList">选择</h-button>
                    </div>
                </h-form-item>

                <h-form-item required v-if="rule == 'fund' && actionType == 'modify'" label="产品名称" prop="fund_id">
                    <el-select size="mini" style="width: 100%;" disabled v-model="approveRuleInfo.fund_id">
                        <el-option v-for="item in fundIdList" :key="item.code" :label="item.text" :value="item.code"></el-option>
                    </el-select>
                </h-form-item>

                <h-form-item v-if="rule == 'business'" required label="业务名称" prop="cop_busin_class">
                    <h-select showTitle :disabled="actionType == 'modify'" v-model="approveRuleInfo.cop_busin_class" filterable hideMult multiple isString showBottom>
                        <h-option v-for="item in copBusinClassList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>

                <h-form-item required label="工作流流程名称" prop="businflow_no">
                    <h-select v-model="approveRuleInfo.businflow_no" filterable>
                        <h-option v-for="item in businFlowNoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>

            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 10px" @click="cancelApproveRule">取消</h-button>
                <h-button type="primary" @click="saveApproveRule">确定</h-button>
            </div>
        </h-msg-box>
        <h-msg-box class="customMsgBox"  width="500px" v-model="showResult">
            <p slot="header">
                <span>导入结果</span>
            </p>
            <h-table stripe  border v-show="errorData.length>0" :height="350" :data="errorData" :columns="errorColums">

            </h-table>
            <div slot="footer">
                <h-button type="primary" @click="cancelErrorInfo">确定</h-button>
            </div>
        </h-msg-box>
        <fund-choose-msg-box
                @saveFundInfo="saveFundInfo"
                ref="approveRuleMsgBox">
        </fund-choose-msg-box>
    </div>
</template>

<script>
    import HDatagrid from "../../../components/HDatagrid";

    import {
        getDictList,
        getFundInfo,
        getProcessKey,
        getSysParamValue,
        showText
    } from '@/api/utils'

    import {
        addApproveRule,
        modifyApproveRule,
        delApproveRule
    } from '@/api/systemmgnt/approveRule'
    import FundChooseMsgBox from "../../../components/FundChooseMsgBox";

    export default {
        name: "approveRule",
        components: {FundChooseMsgBox, HDatagrid},
        data(){
            return{
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                fundIdList:[],
                copBusinClassList:[],
                businFlowNoList:[],
                errorData:[],
                showApproveRuleDialog:false,
                showResult:false,
                approveRuleColumns:[],
                actionType:'',
                rule:'',
                errorColums:[
                    {width: 80,key:"lineNum",title:"行号",align:"center"},
                    {key:"errorInfo",title:"错误原因",align:"center"},
                ],
                fundColumns:[
                    {
                        title: '审批规则序号',
                        type: 'index',
                        align: 'center'
                    }, {
                        title:'产品编号',
                        key:'fund_id',
                        sortable:true,
                        align:'center',
                        ellipsis:true,
                        hiddenCol: true
                    },{
                        title:'产品名称',
                        key:'fund_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'工作流流程名称',
                        key:'businflow_no',
                        align:'center',
                        ellipsis:true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let businflowName = showText(rowData.businflow_no,this.businFlowNoList);
                            return h("span",{},businflowName)
                        }
                    }
                ],
                businessColumns:[
                    {
                        title: '审批规则序号',
                        type: 'index',
                        align: 'center'
                    }, {
                        title:'业务编号',
                        key:'cop_busin_id',
                        align:'center',
                        sortable:true,
                        ellipsis:true,
                        hiddenCol: true
                    },{
                        title:'业务名称',
                        key:'cop_busin_class',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'工作流流程名称',
                        key:'businflow_no',
                        align:'center',
                        ellipsis:true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let businflowName = showText(rowData.businflow_no,this.businFlowNoList);
                            return h("span",{},businflowName)
                        }
                    }
                ],
                queryForm:{
                    fundId:'',
                    businessClass:''
                },
                approveRuleInfo:{
                    fund_id:'',
                    cop_busin_class:'',
                    businflow_no:''
                }
            }
        },
        watch:{
            showApproveRuleDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            },
            showResult:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
            fundId:{
                get:function () {
                    if(this.queryForm.fundId)
                        return this.queryForm.fundId.split(",")
                    else
                        return []
                },
                set:function (newValue) {
                    this.queryForm.fundId = newValue.join(",")
                }
            },
            businessClass:{
                get:function () {
                    if(this.queryForm.businessClass)
                        return this.queryForm.businessClass.split(",")
                    else
                        return []
                },
                set:function (newValue) {
                    this.queryForm.businessClass = newValue.join(",")
                }
            }
        },
        activated(){
            getSysParamValue(10016).then(res => {
                let paramValue = res.data.data;
                if(paramValue == '1'){
                    this.rule = 'business'
                    this.approveRuleColumns = this.businessColumns
                }else{
                    this.rule = 'fund'
                    this.approveRuleColumns = this.fundColumns
                }
            });
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => this.fundIdList = res.data);
            this.refresh()
        },
        mounted(){
            getDictList(90523).then(res => this.copBusinClassList = res.data)
            getProcessKey().then(res => {
                if(res.data && res.data.length > 0){
                    this.businFlowNoList = res.data
                }
            });
            this.re_layout();
        },
        methods:{
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 120;
            },
            addApproveRule(){
                this.approveRuleInfo={
                    fund_id:'',
                    cop_busin_class:'',
                    businflow_no:''
                }
                this.$refs.approveRuleForm.resetFields();
                this.actionType = 'add'
                this.showApproveRuleDialog = true;
            },
            modifyApproveRule(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                this.approveRuleDbClick(selects[0]);
            },
            approveRuleDbClick(data){
                this.approveRuleInfo = data;
                if(data.fund_id){
                    this.approveRuleInfo.fund_id = data.fund_id.toString();
                }
                if(data.cop_busin_id){
                    this.approveRuleInfo.cop_busin_class = data.cop_busin_id.toString();
                }
                this.showApproveRuleDialog = true;
                this.actionType = 'modify';
            },
            deleteApproveRule(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }

                this.openMask();
                this.$confirm('确认删除这条审批配置规则?', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    delApproveRule(selects[0].fund_id,selects[0].cop_busin_id, this.rule).then(res =>{
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'确定',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            })
                        }
                    })
                }).catch(() =>{
                    this.closeMask();
                })
            },
            cancelApproveRule(){
                this.showApproveRuleDialog = false;
            },
            cancelErrorInfo(){
                this.showResult = false;
                this.refresh();
            },
            saveApproveRule(){
                let isValidate = true;
                this.$refs.approveRuleForm.validate((validate) => isValidate = isValidate && validate)
                if(!isValidate){
                    this.$alert('输入项不满足要求，请重新检查!','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }

                if(this.actionType == 'add'){
                    addApproveRule(this.approveRuleInfo).then(res => {
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.showApproveRuleDialog = false;
                                }
                            }).then(() =>{
                                this.showApproveRuleDialog = false;
                            })
                            this.refresh();
                        }else if(res.data && res.data.code == '1120002'){
                            this.showApproveRuleDialog = false;
                            this.showResult = true;
                            this.errorData = res.data.data;
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }else if(this.actionType == 'modify'){
                    modifyApproveRule(this.approveRuleInfo).then(res => {
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.showApproveRuleDialog = false;
                                }
                            }).then(() =>{
                                this.showApproveRuleDialog = false;
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }
            },
            choseFundList(){
                let wrapperHeight = document.getElementById("full-h-wrapper").offsetHeight;
                this.$refs.approveRuleMsgBox.showMsgBox(this.approveRuleInfo.fund_id, wrapperHeight, '1');
            },
            saveFundInfo(data){
                this.approveRuleInfo.fund_id = data;
            }
        }

    }
</script>

<style scoped>

</style>


