<template>
  <div class="page-marketTradeDay full-h">
    <import-account-transfer :on-import-success="refresh" ref="tradeDayImportDialog"></import-account-transfer>
    <h-split v-model="split" min="130" max="800">
      <div slot="left" class="demo-split-pane no-padding" span="4">
        <div style="padding: 5px">
          交易日类型
        </div>
        <div style="overflow: auto; height: 514px;">
          <template>
            <div>
              <h-radio-group v-model="vertical" vertical @on-change="onchange">
                <h-radio label="0">
                  <span>系统交易日</span>
                </h-radio>
                <h-radio label="1">
                  <span>银行间交易日</span>
                </h-radio>
              </h-radio-group>
            </div>
          </template>
        </div>
      </div>
      <div slot="right" class="right-content" style="padding-left: 10px ; height:100% ; overflow: hidden" span="20"
           id="full-h-wrapper-on-marketTradeDay">
        <div class="infomessage"><i class="h-icon iconfont icon-ios-information-outl h-icon-ios-information-outl"
                                    style="color: rgb(17, 160, 248); font-size: 15px;"></i> <span>
            蓝色背景为正常交易日，灰色背景为非交易日，无背景表示该日期未被定义，单击右键可设置该属性</span></div>
        <div style="padding: 10px">
          <h-button-group>
            <h-button icon="android-upload" title="参数JYRFTLX ：0为系统市场交易日 1为银行间市场交易日
参数RQ：YYYYMMDD格式日期；
参数SFJYR ：0为非交易接收日 1为交易接收日" @click="importTradeDay">导入交易日
            </h-button>
          </h-button-group>
        </div>
        <div style="height:100%; padding-bottom:7%;overflow: auto">
          <h-calendar ref="calendar" :dateCellRender="dateRender" :monthViewNum="12" @on-context-menu="handleMenu"
                      @on-preBtn-click="updateyear" @on-nextBtn-click="updateyear">
            <ul class="calendar-menu" slot="menu">
              <li class="calendar-menu-item" @click.stop="setDate(1)">设为交易日</li>
              <li class="calendar-menu-item" @click.stop="setDate(0)">设为非交易日</li>
            </ul>
          </h-calendar>
          <h-spin size="large" fix v-if="spinShow"></h-spin>
        </div>
      </div>
    </h-split>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import Cookies from 'js-cookie'
  import ImportAccountTransfer from '../../../components/ImportAccountTransfer'
  import ImportDialog from '../transfer/ImportDialog'
  import {
    parseDateStr
  } from '@/api/utils'
  import {
    showTradeDay,
    setTradeDay
  } from '@/api/fundAccount/MarketTradeDay'

  export default {
    name: 'marketTradeDay',
    components: { ImportAccountTransfer },
    data() {
      return {
        vertical: '0', // 单选 交易日类型 默认为0
        open: false,
        date: new Date(),
        spinShow: false,
        split: 0.12,
        queryForm: { // 查询参数
          tradeDayType: '0',
          year: new Date().getFullYear()
        },
        setForm: { // 更新参数
          tradeDayType: null,
          setDate: null,
          dateFlag: null
        },
        queryRes: null
      }
    },
    created: function () {
      this.refresh()
    },
    watch:{
      queryRes:function (val , oldval) {
        if (val == null){
          this.spinShow = true;
        } else {
          this.spinShow = false;
        }
      }
    },
    computed: {
      ...mapState({
        theme: state => state.theme.theme
      }),
      month() {
        return this.date.getMonth() + 1
      },
      year() {
        return this.date.getFullYear()
      }
    },
    methods: {
      handleMenu(e, date) {
        this.date = date
      },
      updateyear(year) {
        this.queryForm.year = year
        this.refresh()
      },
      onchange() {
        this.queryForm.tradeDayType = this.vertical
        this.refresh()
      },
      setDate(flag) {
        this.setForm.tradeDayType = this.vertical
        this.setForm.setDate = parseDateStr(this.date.date)
        this.setForm.dateFlag = flag
        // 向后台发送该日期要设置的flag
        if (flag == this.date.dateFlag){
          this.$refs.calendar.hideCtxMenu()
        } else {
          let selecteddate = parseDateStr(this.date.date)
          setTradeDay(this.setForm).then(res => {
            if (res.data.success) {
              let isInsert = true;
              this.queryRes.forEach(function (item, index) { // 改写缓存的查询结果
                if (item.db_date == selecteddate) {
                  item.trade_flag = flag;
                  isInsert = false;
                }
              })
              if (isInsert){  //如果缓存没有，则重新拉取缓存
                this.refresh();
              }
            }
          })
          this.$refs.calendar.hideCtxMenu()
        }
      },
      togglePicker() {
        this.open = !this.open
      },
      handleChange() {
        this.open = false
      },
      importTradeDay() {
        this.$refs.tradeDayImportDialog.showDialog(6, null)
      },
      dateRender(h, date) { // 实时渲染
        let theme = this.theme
        let bgcolor = ''
        if (this.queryRes != null) { // 仅当从数据库获取到数据时
          let datenum
          let foundDateFromDB = false // 该日期是否能在数据库找到
          if (date != null) {
            datenum = parseDateStr(date.date)
            this.queryRes.forEach(function (item, index) { // 遍历将数据库中的flag赋值给前端flag
              if (item.db_date == datenum) {
                foundDateFromDB = true
                date.dateFlag = item.trade_flag
              }
            })
          }
          if (date != null && foundDateFromDB) {
            if (date.dateFlag == 0) { // 根据前端flag渲染背景色 此为非交易日
              if (theme === 'black') {
                bgcolor = '#9AAEBE'
              } else {
                bgcolor = '#DDDDDD'
              }
            } else if (date.dateFlag == 1) { // 交易日背景色
              if (theme === 'black') {
                bgcolor = '#76C5FF'
              } else {
                bgcolor = '#e1f0fe'
              }
            }
          } else if (date != null && !foundDateFromDB) { // 如果数据库没有对应日期，则为白底
            if (theme === 'black') {
              bgcolor = '#3C4655'
            } else {
              bgcolor = 'white'
            }
          }
        } else { // 未从数据库获取到结果
          if (theme === 'black') {
            bgcolor = '#3C4655'
          } else {
            bgcolor = 'white'
          }
        }
        return h('div', { // 根据上述判断生成的参数进行渲染
          'class': {
            [`custom-style`]: true
          },
          style: {
            background: bgcolor,
            color: theme === 'black' ? 'white' : 'black'
          }
        }, [date != null ? '' + date.date.getDate() : ''])
      },
      refresh() { // 刷新数据
        this.queryRes = null
        showTradeDay(this.queryForm).then(res => {
          if (res.data.success) {
            this.queryRes = res.data.data
          }
        })
      }
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  .page-marketTradeDay {
    .demo-split-pane {
      padding: 10px;
    }

    .demo-split-pane.no-padding {
      height: 100%;
      padding: 0;
    }

    .custom-style {
      height: 100%;
      width: 100%;
    }

    .calendar-menu {
      width: 100px;
      padding: 5px;
      border: 1px solid #e7e7e7;
      border-radius: 4px;
      list-style: none;
      background: #fff;
    }

    .calendar-menu-item {
      display: block;
      line-height: 24px;
      text-align: center;
      cursor: default;
    }

    .calendar-menu-item:hover {
      color: #2e94d8;
    }

    .infomessage {
      background-color: #eff9fd;
    }

    .date-input {
      display: inline-block;
      background: #db6520;
      font-size: 16px;
      color: #fff;
      padding: 5px 8px;
      border-radius: 3px;
      cursor: pointer;
    }
  }

</style>



