<template>
    <div class="full-h" id="full-h-wrapper">
        <h-row class="full-h">
            <h-col class="full-h">
                <h-button-group>
                    <h-button icon="add" @click="addInterfaceFile">新增</h-button>
                    <h-button icon="editor" @click="modifyInterfaceFile">修改</h-button>
                    <h-button icon="delete" @click="deleteInterfaceFile">删除</h-button>
                    <h-button icon="android-sync" @click="refresh">刷新</h-button>
                </h-button-group>
                <h-datagrid hasPage stripe
                            :height="autoHeight" highlight-row
                            :pageSize="100" :pageSizeOpts="pageSizeOpts"
                            :columns="interfaceFileColumns" ref="datagrid"
                            :on-row-db-click="interfaceFileDbClick"
                            url="/systemmgnt/interfaceFile/InterfaceFile.json">
                </h-datagrid>
                <h-msg-box class="customMsgBox" v-model="showInterfaceFileDialog" width="400" :mask-closable="false" >
                    <p slot="header">
                        <span v-if="actionType == 'add'">新增接口文件配置</span>
                        <span v-else>修改接口文件配置</span>
                    </p>
                    <h-form :model="interfaceFileInfo" :label-width="100" ref="interfaceFileForm">
                        <h-form-item label="文件类型" required prop="file_type">
                            <h-select :disabled="actionType == 'modify'" v-model="interfaceFileInfo.file_type" filterable :clearable="false">
                                <h-option v-for="item in fileTypeList" :key="item.code" :value="item.code" :label="item.text"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required v-if="interfaceFileInfo.file_type == '6' || interfaceFileInfo.file_type == 'F'||interfaceFileInfo.file_type == 'E' " label="文件名" prop="file_name">
                            <h-select :disabled="actionType == 'modify' || interfaceFileInfo.file_type == 'F'" v-model="interfaceFileInfo.file_name" :filterable="interfaceFileInfo.file_type == 'E'">
                                <h-option v-for="item in fileNameList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required v-if="interfaceFileInfo.file_type == '5'" label="文件名" prop="file_name">
                            <h-input :disabled="actionType == 'modify'" v-model="interfaceFileInfo.file_name"></h-input>
                        </h-form-item>
                        <h-form-item v-if="interfaceFileInfo.file_type == 'E'" required label="上传文件名" prop="ftp_file_name">
                            <h-input disabled v-model="interfaceFileInfo.ftp_file_name"></h-input>
                        </h-form-item>
                        <h-form-item required v-if="interfaceFileInfo.file_type == '5' || interfaceFileInfo.file_type == 'F'" label="文件路径" prop="file_path">
                            <h-input v-model="interfaceFileInfo.file_path"></h-input>
                        </h-form-item>
                        <h-form-item required v-if="interfaceFileInfo.file_type == '6'" label="文件路径" prop="file_path">
                            <h-poptip trigger="hover" placement="right" class="cop-poptip-interfacefile">
                                <h-input v-model="interfaceFileInfo.file_path"></h-input>
                                <div slot="title">
                                    <p>支持的宏定义</p>
                                </div>
                                <div slot="content">
                                    <p>日期宏：YYYYMMDD、YYYYMMDD-、</p>
                                    <p>YYYYMM、YYMMDD、YYYY、YYMM、</p>
                                    <p>MMDD等（前面没有#）</p>
                                </div>
                            </h-poptip>
                        </h-form-item>
                        <h-form-item v-if="interfaceFileInfo.file_type == 'E'">
                            <el-upload ref="uploader"
                                       class="drag-in-box" drag
                                       :action="uploadUrl" :data="uploadData"
                                       with-credentials :on-success="fileUploaded"
                                       :on-remove="fileRemoved"
                                       accept=".xls,.xlsx"
                                       :limit="2" :file-list="fileList">
                                <i class="el-icon-upload"></i>
                                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            </el-upload>
                        </h-form-item>
                        <h-form-item v-if="interfaceFileInfo.file_type == 'E'" required label="文件检查类型" prop="filecheck_type">
                            <h-select v-model="interfaceFileInfo.filecheck_type" filterable>
                                <h-option v-for="item in fileCheckTypeList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item v-if="interfaceFileInfo.file_type == 'E'" label="导入阶段" prop="cop_imp_stage">
                            <h-select v-model="interfaceFileInfo.cop_imp_stage" showBottom hideMult isString multiple filterable>
                                <h-option v-for="item in impStageList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item  label="备注" v-if="interfaceFileInfo.file_type == '5'" prop="remark">
                            <h-input disabled type="textarea" v-model="remark">
                            </h-input>
                        </h-form-item>
                    </h-form>
                    <div slot="footer">
                        <h-button type="ghost" style="margin-left: 8px" @click="cancelInterfaceFile">取消</h-button>
                        <h-button type="primary" @click="saveInterfaceFile">确定</h-button>
                    </div>
                </h-msg-box>
            </h-col>
        </h-row>
    </div>
</template>

<script>
    import HDatagrid from "../../../components/HDatagrid";

    import {
        showText,
        getBankList,
        getDictList,
        getNoneEleBankInfo
    } from "@/api/utils";

    import {
        addInterfaceFile,
        updateInterfaceFile,
        deleteInterfaceFile
    } from "@/api/systemmgnt/interfaceFile"

    export default {
        name: "interfaceFile",
        components: {HDatagrid},
        data(){
            return{
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                showInterfaceFileDialog:false,
                fileNameList:[],
                noneEleBankList:[],
                fileCheckTypeList:[],
                impStageList:[],
                actionType:'',
                fileList:[],
                uploadUrl: window.LOCAL_CONFIG.API_HOME + '/file/upload.json',
                interfaceFileColumns:[
                    {
                        title:'序号',
                        type:'index',
                        sortable:true,
                        width:80,
                        align:'center'
                    },
                    {
                        title: "文件类型",
                        key: "file_type",
                        ellipsis: true,
                        align: 'left',
                        render: (h,params) =>{
                            const rowData = params.row;
                            let fileType = showText(rowData.file_type,this.fileTypeList)
                            return h("span",{},fileType);
                        }
                    },{
                        title: "文件名",
                        key: "file_name",
                        ellipsis: true,
                        align: 'left',
                        render: (h,params) =>{
                            const rowData = params.row;
                            let fileName = showText(rowData.file_name,this.noneEleBankList);
                            if(!fileName){
                                fileName = rowData.file_name;
                            }
                            return h("span",{},fileName);
                        }
                    },{
                        title: "文件路径",
                        key: "file_path",
                        ellipsis: true,
                        align: 'left'
                    },{
                        title: "文件检查类型",
                        key: "filecheck_type",
                        ellipsis: true,
                        align: 'left',
                        render: (h,params) =>{
                            const rowData = params.row;
                            let filecheckType = showText(rowData.filecheck_type,this.fileCheckTypeList);
                            return h("span",{},filecheckType);
                        }
                    },{
                        title: "导入阶段",
                        key: "cop_imp_stage",
                        ellipsis: true,
                        align: 'left',
                        render: (h,params) =>{
                            const rowData = params.row;
                            let stage = '';
                            if(rowData.cop_imp_stage){
                                let stageArr = rowData.cop_imp_stage.split(",")
                                for ( let i = 0; i<stageArr.length; i++){
                                    stage += showText(stageArr[i],this.impStageList)+',';
                                }
                            }
                            return h("span",{},stage.substring(0,stage.length - 1))
                        }
                    }
                ],
                fileTypeList:[
                    {
                        code:'5',
                        text:'估值对账文件'
                    },
                    {
                        code:'6',
                        text:'TA接口文件'
                    },{
                        code:'E',
                        text:'银行余额文件'
                    },{
                        code:'F',
                        text:'网下申购文件'
                    }
                ],
                uploadData:{
                    fileType:'6',
                    fundId:''
                },
                interfaceFileInfo:{
                    file_type:'',
                    file_name:'',
                    file_path:'',
                    ftp_file_name:'',
                    filecheck_type:'',
                    cop_imp_stage:'',
                    file_id:''
                },
                remark:''
            }
        },
        watch:{
            showInterfaceFileDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            },
            'interfaceFileInfo.file_type'(val){
                switch (val) {
                    case 'E':
                        this.fileNameList = this.noneEleBankList;
                        if(!this.interfaceFileInfo.file_name || isNaN(this.interfaceFileInfo.file_name)){
                            this.interfaceFileInfo.file_name = this.noneEleBankList[0].code;
                        }
                        break;
                    case 'F':
                        this.fileNameList = [
                            {
                                code:'网下申购文件',
                                test:'网下申购文件'
                            }
                        ]
                        this.interfaceFileInfo.file_name = '网下申购文件';
                        break;
                    case '6':
                        this.fileNameList = [
                            {
                                code: '自建TA文件',
                                text: '自建TA文件'
                            }, {
                                code: '中登TA文件',
                                text: '中登TA文件'
                            }
                        ]
                        if (this.actionType == 'add') {
                            this.interfaceFileInfo.file_name = '自建TA文件'
                        }
                        break;
                    case '5':
                        this.fileNameList = [];
                        if(this.actionType == 'add'){
                            this.interfaceFileInfo.file_name = 'GZ#FUNDCODE.dbf'
                        }
                        this.remark = '只允许导入产品层的估值对账文件';
                        break;
                    default:
                        this.fileNameList = [];
                        break;
                }
            }
        },
        mounted(){
            getDictList(810029).then(res => this.fileCheckTypeList = res.data)
            getDictList(90754).then(res => this.impStageList = res.data);
            getNoneEleBankInfo().then(res => this.noneEleBankList = res.data)
            this.re_layout();
        },
        methods:{
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 90;
            },
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            addInterfaceFile(){
                this.actionType = 'add';
                this.interfaceFileInfo = {
                    file_type:'5',
                    file_name:'',
                    file_path:'',
                    ftp_file_name:'',
                    filecheck_type:'3',
                    cop_imp_stage:'',
                    file_id:''
                }
                this.$refs.interfaceFileForm.resetFields();
                this.fileList = [];
                this.showInterfaceFileDialog = true;
            },
            modifyInterfaceFile(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录","错误",{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                this.interfaceFileDbClick(selects[0]);
            },
            interfaceFileDbClick(data){
                this.interfaceFileInfo = data;
                if(data.file_path && data.file_type == 'E'){
                    let filePath = data.file_path.split("/");
                    let ftpFileName = filePath[filePath.length-1].split("-")
                    this.interfaceFileInfo.ftp_file_name = ftpFileName[ftpFileName.length-1];
                    this.fileList = [{
                        name : this.interfaceFileInfo.ftp_file_name
                    }]
                }
                this.showInterfaceFileDialog = true;
                this.actionType = 'modify'
            },
            deleteInterfaceFile(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录","错误",{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }

                this.openMask();
                this.$confirm('确认要删除这条记录吗？', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteInterfaceFile(selects[0].file_type,selects[0].file_name).then( res =>{
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            })
                        }
                    })
                }).catch(() => {
                    this.closeMask();
                })
            },
            cancelInterfaceFile(){
                this.showInterfaceFileDialog=false;
            },
            saveInterfaceFile(){
                let isValidate = true;
                this.$refs.interfaceFileForm.validate((validate) => isValidate = isValidate && validate)
                if(this.interfaceFileInfo.file_type == 'E' && !this.interfaceFileInfo.ftp_file_name){
                    isValidate = false;
                }
                if(!isValidate){
                    this.$alert('输入项不满足要求，请重新检查','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }

                if(this.actionType == 'add'){
                    addInterfaceFile(this.interfaceFileInfo).then( res =>{
                        this.openMask();
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.showInterfaceFileDialog = false;
                                }
                            }).then(() =>{
                                this.showInterfaceFileDialog = false;
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }else{
                    updateInterfaceFile(this.interfaceFileInfo).then(res =>{
                        this.openMask();
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.showInterfaceFileDialog = false;
                                }
                            }).then(() =>{
                                this.showInterfaceFileDialog = false;
                            })
                            this.refresh()
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }
            },
            fileUploaded(response,file,fileList){
                if(response.code == '0'){
                    if(fileList.length == 1){
                        this.interfaceFileInfo.file_id = response.data;
                        this.interfaceFileInfo.ftp_file_name = fileList[0].name;
                    }else if(fileList.length == 2){
                        this.interfaceFileInfo.file_id = response.data;
                        this.interfaceFileInfo.ftp_file_name = fileList[1].name;
                        fileList.splice(0,1);
                    }else{
                        this.interfaceFileInfo.file_id = '';
                        this.interfaceFileInfo.ftp_file_name = '';
                    }
                }else{
                    this.$alert(response.msg, '错误',{
                        confirmButtonText:'确定',
                        type: 'error'
                    });
                    fileList.splice(fileList.length-1,1);
                }
            },
            fileRemoved(file,fileList){
                this.interfaceFileInfo.file_id = '';
                this.interfaceFileInfo.ftp_file_name = '';
                this.fileList = [];
            }
        }
    }
</script>

<style lang="scss">
    .drag-in-box {

        .el-upload {
            display: block;
            height: 100%;

            .el-upload-dragger {
                margin-top:0px;
                width: 100%;
                height: 100%;
            }
        }
    }

    .cop-poptip-interfacefile.h-poptip {
        width: 100%;
        .h-poptip-rel {
            display: block;
        }
    }
</style>

<style>

</style>


