import fetch from "@/api/httpFetch";

export function getBankNo(fundId) {
    return fetch.post('/reportmgnt/accQueryGetBankNo.json',{fundId:fundId})
}

export function getAccName(fundId,custodialNo) {
    return fetch.post('/reportmgnt/accQueryGetAccName.json',{fundId:fundId,custodialNo:custodialNo})
}

export function getAccNo(fundId,custodialNo,accName) {
    return fetch.post('/reportmgnt/accQueryGetAccNo.json',{fundId:fundId,custodialNo:custodialNo,accName:accName})
}

export function accQueryGetAccInfo(fundId,includeSelfRun) {
    return fetch.post('/reportmgnt/accQueryGetAccInfo.json', {fundId: fundId, includeSelfRun:includeSelfRun})
}

export function submitAccQuery(fundId,custodialNo,bankAccountNo,queryDate,queryType) {
    return fetch.post('/reportmgnt/submitAccQuery.json',{fundId:fundId,custodialNo:custodialNo,bankAccountNo:bankAccountNo,queryDate:queryDate,queryType:queryType})
}

export function submitAccBatchQuery(queryTypeBatch,queryDateBatch,queryData) {
    return fetch.post('/reportmgnt/submitAccBatchQuery.json',{queryTypeBatch:queryTypeBatch,queryDateBatch:queryDateBatch,queryData:queryData})
}


