<template>
  <div class="cmp-comm-tree-grid">
    <div class="tree-grid">
      <h-asyc-tree-gird
        :columns="solvedColumns"
        :data="sortedDatas"
        :disableEdit="true"
        :border="false"
        :canDrag="true"
        :height="gridHeight"
        :isCheckbox="isCheckbox"
        :checkStrictly="true"
        :highlight-row="true"
        :headSelection="headSelection"
        :row-class-name="rowClassName"
        :loading="loading"
        size="small"
        :no-data-text="onDataText"
        @on-row-click="handleRowClick"
        @on-row-dblclick="handleRowDblClick"
        @on-select-change="onSelectChange"
        @on-select-all="onSelectAll"
        @on-expand="onExpand"
        ref="gridContent"></h-asyc-tree-gird>
    </div>

    <h-page class="page-ctrl" :total="total" show-elevator show-total show-sizer
            :placement="placement"
            :page-size="pageSize"
            :pageSizeOpts="pageSizeOpts"
            @on-page-size-change="pageSizeChange"
            @on-change="changePage" v-if="pageCtrlVisible"></h-page>

    <h-msg-box class = "customMsgBox" v-if="gridName" v-model="showLayout" width = "490px">
        <p slot="header">
            <span>布局</span>
        </p>
        <h-transfer
            :titles="['隐藏列','显示列']"
            :data="columndata"
            :target-keys="showColumns"
            :render-format="renderMethod"
            @on-right-drag="upDownMove"
            @on-change="handleChange">
        </h-transfer>
        <div slot="footer">
            <h-button type="primary" @click="submitLayout">保存</h-button>
            <h-button type="ghost" @click="handleCancel">取消</h-button>
        </div>
    </h-msg-box>

  </div>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue'
import { deepCopy } from '../../../utils/func'
import { addClass } from '../../../utils/dom'
import {
    getShowColumn,
    setShowColumn
} from '@/api/utils'
import Sortheader from './Sortheader'
import service from './service'
Vue.component('Sortheader', Vue.extend(Sortheader))

export default {
  props: {
      headSelection: {
          type: Boolean,
          default: true
      },
      isCheckbox: {
          type: Boolean,
          default: true
      },
    gridName: {
      type: String,
      default: null
    },
    columns: {
      type: Array,
      default: () => []
    },
    // 内容高度
    height: {
      type: Number,
      default: 0
    },
    // 接口地址
    url: {
      type: String,
      default: ''
    },
    //
    fundData: {
      type: Object,
      default: () => {}
    },
    pageSize: Number,
      pageSizeOpts: Array,
      placement: {
          type: String,
          default: 'top'
      },
    onRowClick: {
      type: Function,
      default: null
    },
    onRowDbClick: {
      type: Function,
      default: null
    },
    rowClassName: Function,
    onDataText: String
  },
  data() {
    return {
      datas: [],
      loading: false,
      total: 0,
      pageNo: 1,
      solvedColumns: [],
      showColumns: this.getTargetKeys(),
      showColumnsTmp:'',
      columndata: this.getMockData(),
      selectedRowDatas: [],
      sortedDatas: [],
      sortingField: '',
      gridNameDetail:'',
      showLayout: false,
      pageInfo: {
         pageNo: 1,
         pageSize: 100
      },
    }
  },
  computed: {
    pageCtrlVisible() {
      return this.total > this.pageSize
    },
    gridHeight() {
      if (this.total > this.pageSize) {
        return this.height - 40
      } else {
        return this.height
      }
    }
  },
  watch: {
    columns: {
      handler: function (columns) {
        for (let column of columns) {
          if (column.render) {
            column.hiddenOther = true
          }
        }
        this.solvedColumns = columns
      },
      immediate: true
    },
    fundData: {
      handler: function(val) {
        this.pageNo = 1
        this.getDatas()
      },
      immediate: true
    },
    showColumns: {
        handler: function(shows) {
            let allColumns = this.columns || [];
            let solvedColumnsTmp = allColumns.filter(e => e.fixedLayout == '1');
            for (let i=0; i < shows.length; i++){
                let tempColumn = allColumns.filter(e => e.key == shows[i]);
                if (tempColumn && tempColumn.length > 0) {
                    solvedColumnsTmp.push(tempColumn[0]);
                }
            }

            /** 以下代码用于处理 treeGrid 的bug，当 width 不设置时候，水平滚动条的问题 */
            for (let col of solvedColumnsTmp) {
                if (!col.width) {
                    col.width = 120
                }
                if (col.key === 'transfer_ins_id') {
                    if (solvedColumnsTmp.length < 10) {
                        delete col.width
                    } else {
                        col.width = 120
                    }
                }
            }

            this.solvedColumns = solvedColumnsTmp;
        },
        immediate: true
    },
      pageSize :function(val) {
          this.pageInfo.pageSize = val;
      },
    pageNo: {
      handler: function (no) {
        this.getDatas()
      }
    }
  },
  mounted() {
  },
  methods: {
    getMockData () {
        let mockData = [];
        let tmpData = [];
        /***********列表存在勾选框等其他不需要展示列可以通过该方式剔除（x => x.fixedLayout.indexOf('1')）*************************/
        tmpData = this.columns.filter(x => !x.fixedLayout || x.fixedLayout.indexOf('1') < 0);
        for(let i = 0; i < tmpData.length; i++){
            mockData.push({
                key: tmpData[i].key,
                label: tmpData[i].title
            });
        }
        return mockData;
    },
    getTargetKeys () {
        return this.getMockData().filter(() => Math.random() * 2 > 1).map(item => item.key);
    },
    renderMethod (item) {
        return item.label;
    },
    handleChange (newTargetKeys) {
        this.showColumns = newTargetKeys;
    },
    upDownMove (newTargetKeys){
        let newTmpTargetKeys = [];
        for(let i=0;i<newTargetKeys.length;i++){
            newTmpTargetKeys.push( newTargetKeys[i].key);
        }
        this.showColumns = newTmpTargetKeys;
    },
    getSelection() {
      return deepCopy(this.selectedRowDatas)
    },
    changeGrid(name) {
      service.getShowColumn(name).then(res => {
          let data = res.data.data;
          this.showColumns = data ? data.split(',') : [];
          this.gridNameDetail = name;
      })
    },
    dataChange(i) {
      if (i) {
        this.pageNo = i;
        this.pageInfo.pageNo = i;
      }
      this.selectedRowDatas = [];//清空选中的数据
      this.getDatas()
    },
    getDatas() {
      this.loading = true
      service.getDatas(this.url, this.fundData, this.pageNo, this.pageInfo.pageSize).then(res => {
        this.loading = false;
        if (!res.data.list) {
            res.data.list = [];
        }
        let copyed = deepCopy(res.data.list)
        let treeData = service.makeTreeData(copyed)
        let solved = service.makeDataChecked(treeData)
        this.datas = solved
        this.sortedDatas = deepCopy(this.datas)
        this.total = res.data.total
      })
    },
    handleRowClick(data) {
      /** 获取原数据，并选中 */
      let rowData = data[0]

      // 先全不选中，除了当前项
      service.makeDataCheckedWithoutExcepts(this.sortedDatas, false, [rowData.transfer_ins_id])
      // 切换当前项选中
      let find = service.findDataById(this.sortedDatas, rowData.transfer_ins_id)
      find.checked = !find.checked

      /** 设置选中数据集合和最近选中数据 */
      if (find.checked) {
        this.selectedRowDatas = [find]
        this.onRowClick && this.onRowClick(data)
      } else {
        this.selectedRowDatas = []
        this.onRowClick && this.onRowClick(null)
      }
    },
    handleRowDblClick(data) {
        //console.log(data)
      /** 取消已选中的 */
      if (this.selectedRowDatas.length > 0) {
        for (let rowData of this.selectedRowDatas) {
          rowData.checked = false
        }
      }
      // 找到指定数据的引用
      let find = service.findDataById(this.sortedDatas, data.transfer_ins_id)
      find.checked = true
      this.selectedRowDatas = [find]

      this.onRowClick && this.onRowClick([data, -1])
      this.onRowDbClick && this.onRowDbClick(data)
    },
    onSelectChange(selectedDatas) {
      this.selectedRowDatas = []

      // 首先取消选中所有
      service.makeDataChecked(this.sortedDatas, false)
      /** 从原数据中获取选中的数据集合 */
      for (let data of selectedDatas) {
        let id = data.transfer_ins_id
        let find = service.findDataById(this.sortedDatas, id)
        find.checked = true
        this.selectedRowDatas.push(find)
      }
    },
    onExpand(item, status) {
      let id = item.transfer_ins_id
      let find = service.findDataById(this.sortedDatas, id)
      find.expand = status
      service.makeDataExpand(find.children, false)

      // 对 + - 处理
      let index = service.getFlatIndexById(this.sortedDatas, id)
      this.solveIcons(index, status)
    },
    onSelectAll(checked) {
      // 所有数据设置 checked = true
      service.makeDataChecked(this.sortedDatas, checked)
      // 赋值当前选中行数据
      if (checked) {
        this.selectedRowDatas = service.flatTreeDatas(this.sortedDatas)
      } else {
        this.selectedRowDatas = []
      }
    },
     makeDataCheckedBySelections() {
      for (let data of this.selectedRowDatas) {
        let find = service.findDataById(this.sortedDatas, data.transfer_ins_id)
        find.checked = true
      }
    },
    changePage(pageNo) {
      this.pageNo = pageNo
    },
      //页码改变
      pageSizeChange (i) {
          this.pageInfo.pageSize = i;
          this.pageInfo.pageNo = 1;
          this.getDatas();
      },
    openLayout() {
      this.showLayout = true
    },
    submitLayout() {
        this.showColumnsTmp = this.showColumns.join(",");
        setShowColumn({
            datagridName: this.gridNameDetail ? this.gridNameDetail : this.gridName,
            showColumnStr: this.showColumnsTmp
      }).then(res => {
        if (res.data && res.data.code == '0') {
          this.showLayout = false
        } else {
          this.$alert('调整布局失败', '错误', {
            confirmButtonText: '确定',
            type: 'error'
          })
        }
      })
    },
    handleCancel() {
      this.showLayout = false
    },
    /** 对 + - 处理 */
    solveIcons(index, status) {
      let rowElems = this.$refs.gridContent.$el.querySelector('.h-editgird-tbody').children
      let rowElem = rowElems[index]
      let targetElem = rowElem.querySelector('.h-icon-play_fill')
      if (targetElem) {
        this.$nextTick(() => {
          if (status) {
            addClass(targetElem, 'my-minus')
          } else {
            addClass(targetElem, 'my-add')
          }
        })
      }
    },
    sortAtField(status, field, isNum = false) {
      service.makeDataExpand(this.sortedDatas, false)
      if (status === 'none') {
        this.sortedDatas = deepCopy(this.datas)
        this.makeDataCheckedBySelections()
      } else {
        this.sortedDatas = service.sort(this.sortedDatas, field, status, isNum)
      }
      this.sortingField = field
    },
    handleResize() {
      this.$refs.gridContent.handleResize()
    }
  },
  components: {
  }
}
</script>

<style lang="less" rel="stylesheet/less">
  @import "./style.less";
</style>



