<template>
    <div class="full-h" >
        <h-split v-model="split" min="130" max="600" @on-moving="onSplitMove">
            <div slot="left" span="4" class="full-h">
                <busin-tree :show-all="true" :on-busin-click="businClick" ref="businTree"></busin-tree>
            </div>
            <div slot="right" span="20" class="full-h" style="padding-left: 10px" id="full-h-wrapper">
                <h-row>
                    <h-col>
                        <h-button-group>
                            <h-button v-if="queryForm.businClass != '1,20,21,22,23,4,5,6,7,8,9' && queryForm.businClass != '8' && queryForm.businClass != '21'" @click="businFlagAdd">新增</h-button>
                            <h-button v-if="queryForm.businClass != '1,20,21,22,23,4,5,6,7,8,9' && queryForm.businClass != '8' && queryForm.businClass != '21'" @click="businFlagModify">修改</h-button>
                            <h-button v-if="queryForm.businClass != '1,20,21,22,23,4,5,6,7,8,9' && queryForm.businClass != '1' && queryForm.businClass != '4' && queryForm.businClass != '5' && queryForm.businClass != '6'"
                                      @click="operate('1')">允许</h-button>
                            <h-button v-if="queryForm.businClass != '1,20,21,22,23,4,5,6,7,8,9' && queryForm.businClass != '1' && queryForm.businClass != '4' && queryForm.businClass != '5' && queryForm.businClass != '6'"
                                      @click="operate('0')">不允许</h-button>
                            <h-button @click="businSets">设置</h-button>
                        </h-button-group>

                        <h-datagrid :height="autoHeight"
                                    stripe has-page highlight-row
                                    :auto-load="false" :page-size="100"
                                    :page-size-opts="pageSizeOpts"
                                    :columns="businFlagColumns" :bind-form="queryForm"
                                    :on-row-db-click="businFlagDbClick"
                                    url="/systemmgnt/businFlag/page.json" ref="datagrid">
                        </h-datagrid>

                        <h-msg-box class="customMsgBox" v-model="showBusinFlagDialog" width="300" :mask-closable=false>
                            <p slot="header">
                                <span>业务信息配置</span>
                            </p>
                            <h-row>
                                <h-col span="24">
                                    <h-form :model="businFlag" :label-width="85" ref="businFlagForm" >
                                        <h-form-item label="最迟到账时间" prop="require_pay_time">
                                            <h-time-picker style="width: 168px" v-model="businFlag.require_pay_time" type="time" placeholder="选择时间" ></h-time-picker>
                                        </h-form-item>
                                    </h-form>
                                </h-col>
                            </h-row>
                            <div slot="footer">
                                <h-button type="ghost" style="margin-left: 8px" @click="cancelBusinFlag">取消</h-button>
                                <h-button type="primary" @click="saveBusinFlag">确定</h-button>
                            </div>
                        </h-msg-box>
                        <h-msg-box class="customMsgBox" v-model="showDialog" width="310" :mask-closable="false">
                            <p slot="header">
                                <span v-if="actionType == 'add'">新增业务标志</span>
                                <span v-else>修改业务标志</span>
                            </p>
                            <h-row>
                                <h-col>
                                    <h-form :model="otherBusinFlag" :label-width="100" ref="otherBusinFlag">
                                        <h-form-item required label="业务标志" prop="cop_busin_caption">
                                            <h-input v-model="otherBusinFlag.cop_busin_caption"></h-input>
                                        </h-form-item>
                                        <h-form-item required label="资金方向" prop="cash_direction">
                                            <h-select v-model="otherBusinFlag.cash_direction ">
                                                <h-option v-for="item in cashDirectionList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                                            </h-select>
                                        </h-form-item>
                                        <h-form-item :required="otherBusinFlag.cash_direction == '1' || otherBusinFlag.cash_direction == '2'" label="出款账户" prop="account_out_class_list">
                                            <h-select v-model="otherBusinFlag.account_out_class_list " >
                                                <h-option v-for="item in limitAccountClassList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                                            </h-select>
                                        </h-form-item>
                                        <h-form-item :required="otherBusinFlag.cash_direction == '1' || otherBusinFlag.cash_direction == '2'" label="入款账户" prop="account_in_class_list">
                                            <h-select v-model="otherBusinFlag.account_in_class_list" >
                                                <h-option v-for="item in limitAccountClassList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                                            </h-select>
                                        </h-form-item>
                                        <h-form-item required label="最迟到账时间" prop="require_pay_time">
                                            <h-time-picker style="width: 100%" v-model="otherBusinFlag.require_pay_time" type="time" placeholder="选择时间" ></h-time-picker>
                                        </h-form-item>
                                    </h-form>
                                </h-col>
                            </h-row>
                            <div slot="footer">
                                <h-button type="ghost" style="margin-left: 8px;" @click="cancelOtherBusinFlag">取消</h-button>
                                <h-button type="primary" @click="saveOtherBusinFlag">确定</h-button>
                            </div>
                        </h-msg-box>
                    </h-col>
                </h-row>
            </div>
        </h-split>
    </div>
</template>
<script>
    import HDatagrid from "../../../components/HDatagrid";
    import BusinTree from "../../../components/BusinTree";
    import {
        getDictList,
        showText,
        parseTime,
        parseStrDate,
        showTextList,
    } from "@/api/utils";
    import {
        update,
        otherInsert,
        otherUpdate
    } from "@/api/systemmgnt/businFlag";

    export default {
        components: {BusinTree, HDatagrid},
        data() {
            return{
                split: 0.15,
                autoHeight: 0,
                pageSizeOpts: [25,50,75,100],
                showDialog: false,
                showBusinFlagDialog: false,
                actionType:'',
                useFlagList:[],
                businClassList: [],
                businFlagList: [],
                updateBusinFlagList: [],
                accountClassList: [],
                limitAccountClassList: [],
                businFlag: {
                    require_pay_time:''
                },
                businFlagColumns:[],
                columns: [
                    {
                        type: 'selection',
                        width: 30,
                        align: 'center'
                    }, {
                        title: '序号',
                        type: 'index',
                        align: 'center',
                        width: 60
                    }, {
                        title: '业务分类',
                        key: 'cop_busin_class',
                        align: "left",
                        width: 120,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let businClass = showText(rowData.cop_busin_class, this.businClassList);
                            return h("span", {}, businClass);
                        }
                    }, {
                        title: '业务标志',
                        key: 'cop_busin_caption',
                        align: 'left',
                        ellipsis: true,
                    }, {
                        title: '资金方向',
                        key: 'cash_direction',
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let direction = showText(rowData.cash_direction, this.cashDirectionList);
                            return h("span", {}, direction);
                        }
                    }, {
                        title: '出款账户',
                        key: 'account_out_class_list',
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let accountOutClass = showTextList(rowData.account_out_class_list, this.accountClassList);
                            return h("span", {}, accountOutClass);
                        }
                    }, {
                        title: '入款账户',
                        key: 'account_in_class_list',
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let accountInClass = showTextList(rowData.account_in_class_list, this.accountClassList);
                            return h("span", {}, accountInClass);
                        }
                    }, {
                        businClass: '4,5,6,7,8,9,20,21,22,23',
                        title: '允许手工新增',
                        key: 'use_flag',
                        align: 'left',
                        width: 120,
                        ellipsis: true,
                        sortable:true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let flag = showText(rowData.use_flag, this.useFlagList);
                            return h("span", {}, flag);
                        }
                    }, {
                        title: '最迟到账时间',
                        key: 'require_pay_time',
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let payTime = parseTime(rowData.require_pay_time);
                            return h("span", {}, payTime);
                        }
                    }
                ],
                queryForm: {
                    businClass:''
                },
                otherBusinFlag:{
                    cop_busin_class: '',
                    cop_busin_flag: '',
                    cash_direction: '',
                    account_out_class_list: '',
                    account_in_class_list: '',
                    require_pay_time: ''
                },
                cashDirectionList: [
                    {
                        code: '0',
                        text: '不变化'
                    }, {
                        code: '1',
                        text: '入款'
                    }, {
                        code: '2',
                        text: '出款'
                    },
                ]
            }
        },
        watch: {
            showBusinFlagDialog: function (show) {
                if(!show){
                    this.updateBusinFlagList = [];
                }
            },
            'otherBusinFlag.cop_busin_class'(val) {
                if(val){
                    if(val === '1'){
                        this.limitAccountClassList = this.accountClassList.filter( x => "2,5,6,7,f".indexOf(x.code) !== -1);
                    } else if (val === '4') {
                        this.limitAccountClassList = this.accountClassList.filter( x => "2,7,b".indexOf(x.code) !== -1);
                    } else if (val === '5') {
                        this.limitAccountClassList = this.accountClassList.filter( x => "2,7,c".indexOf(x.code) !== -1);
                    } else if (val === '6') {
                        this.limitAccountClassList = this.accountClassList.filter( x => "2,b".indexOf(x.code) !== -1);
                    } else if (val === '7') {
                        this.limitAccountClassList = this.accountClassList.filter( x => "2,d,h".indexOf(x.code) !== -1);
                    } else if (val === '9') {
                        this.limitAccountClassList = this.accountClassList.filter( x => "2,7,a,f".indexOf(x.code) !== -1);
                    } else if (val === '20') {
                        this.limitAccountClassList = this.accountClassList.filter( x => "2,7,i".indexOf(x.code) !== -1);
                    } else if (val === '22') {
                        this.limitAccountClassList = this.accountClassList.filter( x => "2,e".indexOf(x.code) !== -1);
                    } else if (val === '23') {
                        this.limitAccountClassList = this.accountClassList.filter( x => "2,3,8,g,7,1".indexOf(x.code) !== -1);
                    }
                }
            }
        },
        created() {
            this.businFlagColumns = this.columns.filter( x => !x.businClass);
        },
        mounted() {
            getDictList(90523).then(res => this.businClassList = res.data);
            getDictList(610001).then(res => this.useFlagList = res.data);
            this.re_layout();
            getDictList(90525).then(res => this.accountClassList = res.data.map(item => {
                if(item.code == '2'){
                    let json = {};
                    json.code = '2';
                    json.text = '托管账户/自营账户';
                    return json;
                }
                return item;
            }));
        },
        methods:{
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 40;
            },
            businClick(data){
                this.queryForm.businClass = data.code;
                this.businFlagColumns = this.columns.filter(x => !x.businClass || (","+x.businClass+",").indexOf((","+this.queryForm.businClass+",")) >= 0);
                this.refresh();
            },
            operate(flag){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录","错误",{
                        confirmButtonText: "确定",
                        type: "error",
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return
                }
                this.openMask();
                update({
                    businFlagList: selects,
                    useFlag: flag,
                    requirePayTime: null
                }).then(res =>{
                    if(res.data && res.data.success){
                        this.$alert(res.data.msg,"成功",{
                            confirmButtonText: "确定",
                            type: "success",
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                        this.refresh();
                    }else{
                        this.$alert(res.data.msg, "错误", {
                            confirmButtonText: "确定",
                            type: "error",
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                    }
                })
            },
            businSets(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录","错误",{
                        confirmButtonText: "确定",
                        type: "error",
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return
                }
                this.businFlag.require_pay_time = "150000";
                Object.assign(this.updateBusinFlagList,selects);
                this.showBusinFlagDialog = true;
            },
            cancelBusinFlag(){
                this.showBusinFlagDialog = false;
            },
            saveBusinFlag(){
                if(this.businFlag.require_pay_time){
                    update({
                        businFlagList: this.updateBusinFlagList,
                        useFlag: null,
                        requirePayTime: parseInt(this.businFlag.require_pay_time.replace(":","").replace(":",""))
                    }).then(res =>{
                        if(res.data && res.data.success){
                            this.$alert(res.data.msg,"成功",{
                                confirmButtonText: "确定",
                                type: "success"
                            })
                            this.showBusinFlagDialog = false;
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg, "错误", {
                                confirmButtonText: "确定",
                                type: "error"
                            })
                        }
                    })
                }else{
                    this.showBusinFlagDialog = false;
                    this.refresh();
                }
            },
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            businFlagAdd(){
                this.otherBusinFlag = {
                    cop_busin_class: this.queryForm.businClass,
                    cop_busin_flag: '',
                    cash_direction: '1',
                    account_out_class_list: '',
                    account_in_class_list: '',
                    use_flag: '1',
                    require_pay_time: '150000'
                }
                this.$refs.otherBusinFlag.resetFields();
                this.showDialog = true;
                this.actionType = 'add';
            },
            businFlagModify(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录","错误",{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return;
                }
                this.businFlagDbClick(selects[0]);
            },
            businFlagDbClick(data){
                if(data.cop_busin_class !== '8' && data.cop_busin_class !== '21') {
                    if(data.cop_busin_flag < 100000){
                        this.$alert('COP基础业务标志不支持修改!', '提醒',{
                            confirmButtonText: '确定',
                            type: 'warning'
                        });
                        return;
                    }
                    this.otherBusinFlag = data;
                    this.otherBusinFlag.require_pay_time = data.require_pay_time.toString();
                    this.showDialog = true;
                    this.actionType = 'modify';
                }
            },
            cancelOtherBusinFlag(){
                this.showDialog = false;
            },
            saveOtherBusinFlag(){
                let isValidate = true;
                this.$refs.otherBusinFlag.validate((valid) => isValidate = isValidate && valid)
                if(!isValidate){
                    this.openMask();
                    this.$alert("输入项不满足要求，请重新检查!","错误",{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return;
                }
                if(this.otherBusinFlag.require_pay_time){
                    this.otherBusinFlag.require_pay_time = this.otherBusinFlag.require_pay_time.replace(":","").replace(":","")
                }
                if(this.actionType == 'add'){
                    otherInsert(this.otherBusinFlag).then(res => {
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg, '成功', {
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                    this.showDialog = false;
                                }
                            }).then(() => {
                                this.showDialog = false;
                            });
                            this.refresh()
                        }else{
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    })
                }else if(this.actionType == 'modify'){
                    otherUpdate(this.otherBusinFlag).then(res => {
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg, '成功', {
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                    this.showDialog = false;
                                }
                            }).then(() => {
                                this.showDialog = false;
                            });
                            this.refresh()
                        }else{
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    })
                }
            },
            onSplitMove(){
                this.$refs.datagrid.handleResize();
            }
        }
    }
</script>


