import fetch from "@/api/httpFetch";

export function addBankInfo(params) {
    return fetch.post("/systemmgnt/bankInfo/bankInfoAdd.json",params, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function modifyBankInfo(params) {
    return fetch.post("/systemmgnt/bankInfo/bankInfoModify.json",params, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function deleteBankInfo(custodialNo) {
    return fetch.post("/systemmgnt/bankInfo/bankInfoDel.json",{custodialNo:custodialNo})
}


