<template>
    <div style="height:100%;">
        <label style="display:none;"><span></span><input type="text" name="hidden1" ></label>
        <label style="display:none;"><span></span><input type="password" name="hidden2" ></label>

        <h-row>

            <h-col style="padding-left: 6px;padding-right: 6px">
                <el-input  size="mini" placeholder="关键字" v-model="filterText" class="input-with-select">
                </el-input>
            </h-col>
        </h-row>

        <div class="auto-height" style="margin-top:10px;">
            <!--<h-tree :data="fundTree"  @on-select-change="onFundClick" :show-checkbox="showCheckBox" ref="fundTree"></h-tree>-->
            <el-scrollbar style="height: 100%;" class="h-scrollbar">
                <el-tree :data="fundTree" :props="treeProps" @node-click="onFundClick" ref="fundTree" node-key="code" :default-expand-all="true" :expand-on-click-node="false" :filter-node-method="filterNode">
                    <span  class="custom-tree-node" slot-scope="{ node, data }" style="width: 100%">
                        <span >{{ node.label }}</span>
                    </span>
                </el-tree>
            </el-scrollbar>
        </div>
        <h-msg-box class="customMsgBox"  v-model="setQueryMsg" width="650" :mask-closable=false>
            <p slot="header">
                <span>高级查询</span>
            </p>

            <h-form  :model="queryConditions" :label-width="130" ref="queryConditions" cols="2">


                <h-row :style="{'margin-top':'5px','margin-bottom': '5px'}">
                    <h-col span="12">
                        <h-form-item label="产品" prop="fund_id_list">
                            <el-select size="mini" v-model="queryConditions.fund_id_list" filterable multiple clearable>
                                <el-option v-for="item in fundList" :value="item.code" :label="item.text"
                                           :key="item.code">
                                </el-option>
                            </el-select>
                        </h-form-item>

                        <h-form-item  style="margin-top: 10px" label="产品状态" prop="fund_status">
                            <el-select size="mini" v-model="queryConditions.fund_status" multiple clearable>
                                <el-option value="1" key="1" label="正常"></el-option>
                                <el-option value="2" key="2" label="注销"></el-option>
                                <el-option value="3" key="3" label="冻结"></el-option>
                            </el-select>
                        </h-form-item>

                        <h-form-item  style="margin-top: 10px" label="是否划款" prop="transfer_flag">
                            <el-select size="mini" v-model="queryConditions.transfer_flag" clearable>
                                <el-option value="1" key="1" label="是"> </el-option>
                                <el-option value="0" key="0" label="否"> </el-option>
                            </el-select>
                        </h-form-item>
                        <h-form-item label="显示无待办的产品">
                            <h-switch  v-model="showDone">
                                <span slot="open">是</span>
                                <span slot="close">否</span>
                            </h-switch>
                        </h-form-item>
                    </h-col>
                    <h-col span="12">
                        <h-form-item label="产品类型" prop="fund_type">
                            <el-select size="mini" v-model="queryConditions.fund_type" filterable multiple clearable>
                                <el-option v-for="item in fundTypeList" :value="item.code" :label="item.text"
                                           :key="item.code">
                                </el-option>
                            </el-select>
                        </h-form-item>
                        <h-form-item  style="margin-top: 10px" label="托管人" prop="fund_trust_bank">
                            <el-select size="mini" v-model="queryConditions.fund_trust_bank"  filterable clearable>
                                <el-option v-for="item in bankList" :value="item.code" :label="item.text"
                                           :key="item.code">
                                </el-option>
                            </el-select>
                        </h-form-item>
                        <h-form-item style="margin-top: 10px"  label="产品来源" prop="data_source">
                            <el-select size="mini" v-model="queryConditions.data_source" filterable clearable>
                                <el-option v-for="item in dataSourceList"
                                           :key="item.code"
                                           :label="item.text"
                                           :value="item.code">
                                </el-option>
                            </el-select>
                        </h-form-item>

                    </h-col>
                </h-row>
                <!--</h-field-panel>-->
                <el-collapse   v-model="activeNames">
                    <el-collapse-item title="     保存条件" name="2">
                        <h-row>
                            <h-col  span="12">
                                <h-form-item label="名称" prop="name" required>
                                    <h-input v-model="queryConditions.name">
                                    </h-input>
                                </h-form-item>
                            </h-col>
                            <h-col span="8">
                                <h-form-item label="设为默认">
                                    <h-switch v-model="isDefault">
                                        <span slot="open">是</span>
                                        <span slot="close">否</span>
                                    </h-switch>
                                </h-form-item>
                            </h-col>
                            <h-col span="3">
                                <h-button style="float: right" type="primary" @click="handleSave">保存</h-button>
                            </h-col>
                        </h-row>
                    </el-collapse-item>
                </el-collapse>


            </h-form>


            <div slot="footer">
                <h-button type="error" @click="handleDelete">删除</h-button>
                <h-button type="ghost" style="margin-left: 8px" @click="handleCancel">取消</h-button>
                <h-button type="primary" @click="handleOK">确定</h-button>
            </div>
        </h-msg-box>
    </div>


</template>

<script>
    import {getFundInfo,getBankList,getDictList,getQueryCondition,deleteQueryCondition,saveQueryCondition,listQueryCondition} from '@/api/utils' ;
    import {
        disableFund,
        deleteFund
    } from '@/api/fundAccount/fundAccount';

    import HPanel from "./HPanel";
    import HFieldset from "./HFieldset";



    export default {
        name: "HOtherFundTree",
        components: {HFieldset, HPanel},
        data() {
            return {
                activeNames:['1'],
                fundTree: [{text:'   加载中              ... ',code:''}],
                filterText:'',
                queryConditions: {
                    name:"",
                    default_flag: "0",
                    condition_id: null,
                    keyword: '',
                    data_source: '',
                    fund_id_list: [],
                    fund_status: ['1'],
                    fund_type: [],
                    fund_trust_bank:"",
                    transfer_flag: "1",
                    isSettle:this.isSettle,
                    show_busin_done:'1'
                },
                treeProps:{
                    label:"text",
                    children:'children'
                },
                dataQuery:{fundId:''},
                setQueryMsg: false,
                fundTypeList: [],
                dataSourceList: [],
                fundList: [],
                conditionList:[],
                bankList:[]
            }
        },
        computed:{
            showDone: {
                get: function () {
                    return this.queryConditions.show_busin_done=='1'
                },
                set: function (newValue) {
                    this.queryConditions.show_busin_done=newValue?'1':'0'
                }
            },

            isDefault: {
                get: function () {
                    return this.queryConditions.default_flag=='1'
                },
                set: function (newValue) {
                    this.queryConditions.default_flag=newValue?'1':'0'
                }
            },

        },
        props: {
            queryUrl:{
                type: String,
                default: '/common/fundName.json'
            },
            showAll: {
                type: Boolean,
                default: false
            },
            isSettle: {
                type: Boolean,
                default: false
            },
            extendButton: {
                type: Boolean,
                default: false
            },
            height: [Number, String],
            onFundClick: {
                type: Function,
                default(data) {
                    // console.log('选中产品：'+data.code);
                }
            },
            onFundQuery:{
                type: Function,
                default(data) {

                }
            }
        },
        watch: {
            filterText(val) {
                this.$refs.fundTree.filter(val);
            },
            fundTree(val) {
                this.$emit('getFundTree', val)
            }
        },
        activated(){
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => {
                if (res.data && res.data.length > 0) {
                    this.fundList = res.data;
                }
            });
            getBankList("1").then(res => {
                if (res.data && res.data.length > 0) {
                    this.bankList = res.data;
                }
            });
        },
        created() {

            // this.loadCondition();
            this.handleConditionQuery();
            getDictList(610087).then(res => {
                if (res.data && res.data.length > 0) {
                    this.fundTypeList = res.data;
                }
            });
            getDictList(90528).then(res => {
                if (res.data && res.data.length > 0) {
                    this.dataSourceList = res.data;
                }
            });
        }, methods: {
            filterNode(value, data) {
                if (!value) return true;
                return data.text.indexOf(value) !== -1;
            },
            openQuery() {
                this.activeNames=['1']
                this.setQueryMsg = true;
            },
            handleConditionQuery(command){
                // if(command==0){
                //     this.resetCondition();
                //     this.handleOK();
                // }else{
                // getQueryCondition(command).then(res =>{
                //     if (res.data.code==0){
                //     let condition = res.data.data;
                //     if (condition) {
                //         this.queryConditions.condition_id = condition.condition_id;
                //         this.queryConditions.name = condition.condition_name;
                //         this.queryConditions.data_source = condition.data_source?condition.data_source.toString():"";
                //         this.queryConditions.default_flag = condition.default_flag;
                //         this.queryConditions.transfer_flag = condition.transfer_flag;
                //         this.queryConditions.fund_trust_bank = condition.fund_trust_bank?condition.fund_trust_bank.toString():"";
                //         this.queryConditions.fund_id_list = condition.fund_id_list!=null?condition.fund_id_list.split(","):[];
                //         this.queryConditions.fund_status = condition.fund_status_list!=null?condition.fund_status_list.split(","):[];
                //         this.queryConditions.fund_type = condition.fund_type_list!=null?condition.fund_type_list.split(","):[];
                //         this.queryConditions.show_busin_done = condition.show_busin_done;
                //     }
                    getFundInfo(this.queryUrl,this.queryConditions).then(res => {
                        if (this.showAll){
                        this.fundTree=[{text:'全部',code:'',children:res.data}]
                    } else{
                        this.fundTree =res.data
                    }

                    this.onFundClick(this.fundTree[0]);

                    this.onFundQuery(this.fundTree)
                    this.filterText = ''
                    this.selectNodeByCode(this.fundTree[0].code);
            });
                // }

            },
            handleOK() {
                getFundInfo(this.queryUrl,this.queryConditions).then(res => {
                    if (this.showAll){
                    this.fundTree=[{text:'全部',code:'',children:res.data}]
                } else{
                    this.fundTree =res.data
                }
                this.onFundClick(this.fundTree[0]);

                this.onFundQuery(this.fundTree)
                this.filterText = ''
                this.selectNodeByCode(this.fundTree[0].code);
            }).catch(error => {
                    // this.$hMessage.error('加载产品失败');
            });
                this.setQueryMsg = false;
            },
            handleSave() {
                this.$refs.queryConditions.validate((valid) => {
                    if (!valid) {
                        this.$alert('名称不能为空!','错误',{
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    return;
                }
            });
                var params = {
                    condition_id:this.queryConditions.condition_id,
                    data_source: this.queryConditions.data_source,
                    fund_id_list: this.queryConditions.fund_id_list.join(","),
                    fund_status_list: this.queryConditions.fund_status.join(","),
                    fund_type_list: this.queryConditions.fund_type.join(","),
                    condition_name: this.queryConditions.name,
                    transfer_flag : this.queryConditions.transfer_flag,
                    default_flag: this.queryConditions.default_flag,
                    fund_trust_bank: this.queryConditions.fund_trust_bank,
                    show_busin_done: this.queryConditions.show_busin_done,
                };
                saveQueryCondition(params).then(res =>{
                    if (res.data.success){
                        this.$alert('保存成功','提示',{
                            confirmButtonText: '确定',
                            type: 'info'
                        });
                    this.loadCondition();
                } else{
                    if (res.data.code =="1120003") {
                        /*this.$hMsgBox.confirm({
                            title: '确认',
                            content: '条件名称不能重复，是否覆盖同名条件？',
                            onOk: () => {
                            this.queryConditions.condition_id=res.data.data;
                        this.handleSave();
                    }
                    })*/
                        this.$confirm('条件名称不能重复，是否覆盖同名条件？', '确认', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'warning'
                        }).then(() => {
                            this.queryConditions.condition_id=res.data.data;
                            this.handleSave();
                        });
                    }else{
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }

                }
            });
            },
            handleCancel() {
                this.setQueryMsg = false;
            },
            handleDelete() {
                if (this.queryConditions.condition_id){
                    /*this.$hMsgBox.confirm({
                        title: '确认',
                        content: '确定删除所选条件？',
                        onOk: () => {

                        deleteQueryCondition(this.queryConditions.condition_id).then(res =>{
                        this.$alert('删除成功','提示',{
                            confirmButtonText: '确定',
                            type: 'info'
                        });
                    this.resetCondition();
                    this.setQueryMsg = false;
                    this.loadCondition();
                });
                }
                })*/
                    this.$confirm('确认删除所选条件？', '确认', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        deleteQueryCondition(this.queryConditions.condition_id).then(res =>{
                            this.$alert('删除成功','提示',{
                                confirmButtonText: '确定',
                                type: 'info'
                            });
                            this.resetCondition();
                            this.setQueryMsg = false;
                            this.loadCondition();
                        });
                    });
                } else{
                    this.$alert('未选中查询条件','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                }
            },
            resetCondition(){
                this.queryConditions =  {
                    name:"",
                    default_flag: '0',
                    condition_id: null,
                    keyword: '',
                    data_source: '',
                    fund_id_list: [],
                    fund_status: ['1'],
                    fund_type: [],
                    fund_trust_bank:"",
                    transfer_flag: "1",
                    isSettle:this.isSettle
                },
                    this.queryConditions.condition_id = null;
                // this.queryConditions.transfer_flag = '1';
            },
            loadCondition() {
                listQueryCondition().then(res =>{
                    if (res.data && res.data.length > 0) {
                    this.conditionList = res.data;
                }
            });
            },
            getFundList(){
                return this.fundList;
            },
            selectNodeByCode(code) {
                this.$nextTick(() => {
                    this.$refs.fundTree.setCurrentKey(code)
                })
            }

        }

    }
</script>

<style lang="scss" scoped>
    .auto-height {
        height: calc(100% - 38px);
    }
</style>

<style lang="less" rel="stylesheet/less">
    .el-scrollbar.h-scrollbar {
        .el-scrollbar__wrap{
            overflow-x: hidden;
        }
    }
    .el-tree>.el-tree-node {
        min-width: 100%;
        display: inline-block;
    }
</style>



