export default {
    data() {
        return {
            autoHeight: 0
        }
    },
    methods: {
        /**
         *
         * @param {自适应容器元素Id} outer
         * @param {自适应子元素Id} inner
         * @param {固定高度} fixed
         */
        re_layout(outer, inner, fixed) {
            this.$nextTick(() => {
                const outer_height = document.getElementById(outer) ? document.getElementById(outer).offsetHeight : 0;
                const inner_height = document.getElementById(inner) ? document.getElementById(inner).offsetHeight : 0;
                const fixed_height = typeof fixed == "number" ? fixed : 0;

                if (inner_height==0)
                    this.autoHeight = outer_height - 65;
                else
                    this.autoHeight = outer_height - inner_height - fixed_height;

            })
        }
    }
}


