<template>
    <div id="full-h-wrapper" class="systemmgnt-stamp">
        <el-tabs tab-position="top" @tab-click="tabChange">
            <el-tab-pane label="人员签章">
                <stamp-info ref="operatorStamp" :stamp-type="'1'" :stamp-columns="operatorStampColumns" :fund-id-list="fundIdList" :operator-no-list="operatorNoList"></stamp-info>
            </el-tab-pane>
            <el-tab-pane label="产品签章">
                <stamp-info ref="fundStamp" :stamp-type="'2'" :stamp-columns="fundStampColumns" :fund-id-list="fundIdList" :operator-no-list="operatorNoList"></stamp-info>
            </el-tab-pane>
            <el-tab-pane label="公司签章">
                <stamp-info ref="companyStamp" :stamp-type="'3'" :stamp-columns="companyStampColumns" :fund-id-list="fundIdList" :operator-no-list="compOperatorNoList"></stamp-info>
            </el-tab-pane>
            <!--<el-tab-pane label="成交单签章">
                <deal-stamp ref="dealStamp" :stamp-info-list="stampInfoList"></deal-stamp>
            </el-tab-pane>-->
        </el-tabs>
    </div>
</template>

<script>

    import {
        showText,
        showTextList,
        parseDateString,
        getFundInfo,
        operatorList
    } from '@/api/utils'

    import {
        queryStampByType
    } from '@/api/systemmgnt/stamp'

    import StampInfo from "./stampInfo";
    import DealStamp from "./dealStamp";
    export default {
        name: "stamp",
        components: {DealStamp, StampInfo},
        data(){
            return{
                tab: '',
                fundIdList:[],
                operatorNoList:[],
                compOperatorNoList: [],
                stampInfoList:[],
                operatorStampColumns: [
                    {
                        type: 'selection',
                        width: 30,
                        align: 'center'
                    }, {
                        title:'序号',
                        type:'index',
                        sortable:true,
                        width:80,
                        align:'center'
                    },
                    {
                        title: "签章序号",
                        key: "stamp_id",
                        width: 80,
                        hiddenCol:true,
                        ellipsis: true,
                        align: 'right'
                    },{
                        title: "操作员",
                        key: "operator_no",
                        align: 'left',
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let operatorName = showText(rowData.operator_no,this.operatorNoList);
                            return h("span",{},operatorName);
                        }
                    },{
                        title: "签章名称",
                        key: "stamp_name",
                        align: 'left',
                        ellipsis: true
                    },{
                        title: "修改时间",
                        key: "input_date",
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let inputDate = parseDateString(rowData.input_date,null)
                            return h("span",{},inputDate);
                        }
                    },{
                        title: "备注",
                        key: "remark",
                        align: 'left',
                        ellipsis: true
                    },{
                        key: "file_id",
                        align: 'left',
                        ellipsis: true,
                        hiddenCol:true
                    }
                ],
                fundStampColumns: [
                    {
                        type: 'selection',
                        width: 30,
                        align: 'center'
                    }, {
                        title:'序号',
                        type:'index',
                        sortable:true,
                        width:80,
                        align:'center'
                    },
                    {
                        title: "签章序号",
                        key: "stamp_id",
                        width: 80,
                        hiddenCol:true,
                        align: 'right',
                        ellipsis: true
                    },{
                        title: "产品",
                        key: "fund_id",
                        align: 'left',
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let fundName = showText(rowData.fund_id,this.fundIdList);
                            return h("span",{},fundName);
                        }
                    },{
                        title: "签章名称",
                        key: "stamp_name",
                        align: 'left',
                        ellipsis: true
                    },{
                        title: "修改时间",
                        key: "input_date",
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let inputDate = parseDateString(rowData.input_date,null)
                            return h("span",{},inputDate);
                        }
                    },{
                        title: "备注",
                        key: "remark",
                        align: 'left',
                        ellipsis: true
                    },{
                        key: "file_id",
                        align: 'left',
                        ellipsis: true,
                        hiddenCol:true
                    }
                ],
                companyStampColumns: [
                    {
                        type: 'selection',
                        width: 30,
                        align: 'center'
                    }, {
                        title:'序号',
                        type:'index',
                        sortable:true,
                        width:80,
                        align:'center'
                    },
                    {
                        title: "签章序号",
                        key: "stamp_id",
                        width: 80,
                        hiddenCol:true,
                        align: 'right',
                        ellipsis: true
                    },{
                        title: "签章名称",
                        key: "stamp_name",
                        align: 'left',
                        ellipsis: true
                    },{
                        title: "可使用操作员",
                        key: "operator_list",
                        align: 'left',
                        ellipsis: true,
                        render: (h, param) => {
                            const rowData = param.row;
                            let operatorNameList = showTextList(rowData.operator_list, this.compOperatorNoList);
                            return h("span", {}, operatorNameList);
                        }
                    },{
                        title: "修改时间",
                        key: "input_date",
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let inputDate = parseDateString(rowData.input_date,null)
                            return h("span",{},inputDate);
                        }
                    },{
                        title: "备注",
                        key: "remark",
                        align: 'left',
                        ellipsis: true
                    },{
                        key: "file_id",
                        align: 'left',
                        ellipsis: true,
                        hiddenCol:true
                    }
                ],
            }
        },
        activated(){
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => {
                if (res.data && res.data.length > 0) {
                    this.fundIdList = res.data;
                }
            });
            if (this.tab) {
                if (this.tab == '1') {
                    this.$refs.operatorStamp.refresh();
                } else if (this.tab == '2') {
                    this.$refs.fundStamp.refresh();
                } else if (this.tab == '3') {
                    this.$refs.companyStamp.refresh();
                }
            } else {
                this.$refs.operatorStamp.refresh();
            }
        },
        mounted(){
            operatorList().then(res => {
                this.operatorNoList = res.data;
                this.compOperatorNoList = [{code:'-1', text: '全部'}];
                this.compOperatorNoList = this.compOperatorNoList.concat(res.data);
            });
            this.$refs.operatorStamp.refresh();
            this.re_layout();
        },
        methods:{
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                let height = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 110;
                this.$refs.operatorStamp.resizeTable(height);
                // this.$refs.operatorStamp.refresh();
            },
            tabChange(tab,event){
                if(tab.label == '人员签章'){
                    this.tab = '1';
                    let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                    let operatorHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 110;
                    this.$refs.operatorStamp.resizeTable(operatorHeight);
                    this.$refs.operatorStamp.refresh();
                }else if(tab.label == '产品签章'){
                    this.tab = '2';
                    let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                    let fullWrapperHeight = document.getElementById("full-h-wrapper").offsetHeight;
                    let fundHeight = fullWrapperHeight - wrapper0Height - 110;
                    this.$refs.fundStamp.resizeTable(fundHeight, fullWrapperHeight);
                    this.$refs.fundStamp.refresh();
                }else if(tab.label == '公司签章'){
                    this.tab = '3';
                    let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                    let companyHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 110;
                    this.$refs.companyStamp.resizeTable(companyHeight);
                    this.$refs.companyStamp.refresh();
                }
            }
        }
    }
</script>
<style lang="less" rel="stylesheet/less">
    .systemmgnt-stamp {

    }
</style>


