<template>
    <div>
        <h-msg-box class="customMsgBox" v-model="show">
            <p slot="header">
                <span v-if="inputType==1">产品账户导入</span>
                <span v-else-if="inputType==2">公共账户导入</span>
                <span v-else-if="inputType==3">划款指令导入</span>
                <span v-else-if="inputType==4">划款规则配置导入</span>
                <span v-else-if="inputType==6">市场交易日导入</span>
                <span v-else-if="inputType==7">交易对手导入</span>
                <span v-else-if="inputType==8">发行人导入</span>
                <span v-else>销售机构导入</span>
            </p>
            <h-row >
                <h-col span="5">
                    <label>模板下载:</label>
                </h-col>
                <h-col span="19" style="margin-bottom: 15px">
                    <a id="selfAccountExcel" v-if="inputType==1"  @click="downLoadSelfExcel">产品账户导入模板.xlsx</a>

                    <a id="pubAccountExcel"  v-if="inputType==2"  @click="downLoadPubExcel">公共账户导入模板.xlsx</a>
                    <div v-if="inputType==3" >
                        <!--<div>-->
                            <!--<a id="transferExcel"   @click="downLoadTransferExcel(1)">通用指令模板.xlsx</a>-->
                        <!--</div>-->
                        <div style="margin: 0 0 8px 0" >
                            <a  @click="downLoadTransferExcel(2)" >债券分销模板.xlsx</a>
                        </div>
                        <div>
                            <a   @click="downLoadTransferExcel(3)">费用支付模板.xlsx</a>
                        </div>
                    </div>

                    <a v-if="inputType==4"  @click="downLoadBankBusinExcel">划款规则配置导入模板.xlsx</a>

                    <a v-if="inputType==5"  @click="downLoadAgencyExcel">销售机构导入模板.xlsx</a>
                    <a v-if="inputType==6"  @click="downLoadTradeDayExcel">市场交易日导入模板.xlsx</a>
                    <a v-if="inputType==7"  @click="downLoadTradeRival">交易对手导入模板.xlsx</a>
                    <a v-if="inputType==8"  @click="downLoadIssuer">发行人导入模板.xlsx</a>
                </h-col>
            </h-row>

            <h-row v-if="inputType == 7 || inputType == 8" class-name="full-h" style="margin-bottom: 5px">
                <h-col span="5"  class-name="full-h">
                    <span>模板参数说明:</span>
                </h-col>
                <h-col v-if="inputType == 7" span="19" class-name="full-h">
                    <span>市场类型: 1-银行间一级市场，2-银行间二级市场，3-交易所市场，4-境外OTC市场，5-场外市场，6-非标市场，填写数字，中间不需要逗号隔开</span>
                </h-col>
                <h-col v-if="inputType == 8" span="19" class-name="full-h">
                    <span>发行人分类: 1-企业，2-银行，3-基金管理公司，4-其它，5-政府，6-国际金融机构，7-证券公司，8-保险公司，9-中央企业，a-地方国有企业，b-民营企业，c-信托公司，填写数字或字母，中间用逗号隔开</span>
                </h-col>
            </h-row>

            <h-row :gutter="10" class-name="full-h">
                <h-col span="5"  class-name="full-h">
                    <span v-if="inputType==1 || inputType==2">账户导入:</span>
                    <span v-else-if="inputType==3">指令导入:</span>
                    <span v-else-if="inputType==4">划款规则配置导入:</span>
                    <span v-else-if="inputType==6">市场交易日导入:</span>
                    <span v-else-if="inputType==7">交易对手导入:</span>
                    <span v-else-if="inputType==8">发行人导入:</span>
                    <span v-else>销售机构导入:</span>
                </h-col>
                <h-col span="19" class-name="full-h">
                    <el-upload
                            style="margin-bottom: 10px"
                            ref="upload" class="drag-in-box" drag
                            :action="uploadUrl" with-credentials
                            :on-success="uploadSuccess" :on-change="changeUpload"
                            :data="uploadData" :auto-upload="false"
                            accept=".xlsx"
                            :limit="limit" :file-list="fileList">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">将文件拖到此处，或<em :title="fileTips">点击选择</em></div>
                    </el-upload>
                </h-col>
            </h-row>
            <div slot="footer">
                <h-button type="primary" @click="submitUpload">导入</h-button>
                <h-button type="ghost" style="margin-left: 8px" @click="handleCancel">取消</h-button>
            </div>
        </h-msg-box>

        <h-msg-box class="customMsgBox"  width="500px" v-model="showResult">
            <p slot="header">
                <span>导入结果</span>
            </p>
            <h-table stripe  border v-show="errorData.length>0" :height="350" :data="errorData" :columns="accountColums">

            </h-table>
            <div slot="footer">
                <h-button type="primary" @click="confirm">确认</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>

    export default {
        name: "ImportAccountTransfer",
        props: {
            onImportSuccess: {
                type: Function,
                default() {
                }
            },
        },
        data() {
            return {
                uploadData:{dealFlag:0, subclass:'1',ignore:false},
                fileList:[],
                errorData:[],
                accountColums:[
                    {width: 80,key:"lineNum",title:"行号",align:"center"},
                    {key:"errorInfo",title:"错误原因",align:"center"},
                ],
                errorColumns:[
                    {width: 80,key:"lineNum",title:"行号",align:"center"},
                    {width: 310,key:"errorInfo",title:"错误原因",align:"center"},
                ],
                show:false,
                showResult:false,
                fileTips: '',
                uploadUrl:'',
                inputType:1,
                businessClass:'',
            }
        },
        watch:{
            show:function (show) {
                if(show){
                    this.uploadData.ignore = false;
                    this.openMask();
                }else{
                    this.$refs.upload.clearFiles();
                    this.closeMask();
                }
            },
            showResult:function (value) {
                if (!value) {
                    this.$refs.upload.clearFiles();
                    this.closeMask();
                } else {
                    this.openMask();
                }
            }
        },
        computed:{
            limit(){
                return 2
            }
        },
        methods: {
            changeUpload(file,fileList){
                if(fileList.length == 2){
                    fileList.splice(0,1);
                }
            },
            uploadSuccess(response,file,fileList){
                if (response.success){
                    if(response.msg == "操作成功"){
                        this.$alert('导入成功','成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                                this.show = false;
                            }
                        }).then(() => {
                            this.show = false;
                        });
                    }else{
                        this.$alert(response.msg,'成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                                this.show = false;
                            }
                        }).then(() => {
                            this.show = false;
                        });
                    }
                    this.onImportSuccess();
                    this.$refs.upload.clearFiles();
                } else if(response.code=='1120002' && this.inputType == '3'){
                    const h = this.$createElement;
                    let errInfos = [];
                    response.data.forEach(x => {
                        errInfos.push({lineNum: x.lineNum, errorInfo: x.errorInfo});
                    });
                    this.$msgbox({
                        title: '注意',
                        message: h('p', null, [
                            h('span', null, '存在导入异常的指令，是否剔除并继续？'),
                            h('h-table', {
                                style: 'width: 100%', props: {
                                    columns: this.errorColumns,
                                    data: errInfos,
                                    height: 300
                                },
                            })
                        ]),
                        closeOnClickModal: false,
                        showCancelButton: true,
                        confirmButtonText: '继续',
                        cancelButtonText: '取消',
                        beforeClose: (action, instance, done) => {
                            if (action === 'confirm') {
                                this.uploadData.ignore = true;
                                file.status = 'ready';
                                done();
                            } else {
                                done();
                            }
                        }
                    }).then(action => {
                        this.$refs.upload.submit();
                    }).catch(() => {
                        this.show = false;
                        this.$refs.upload.clearFiles();
                    });
                }else if(response.code=='1120002' && this.type != '3') {
                    this.show = false;
                    this.showResult = true
                    this.errorData = response.data;
                }else{
                    this.$alert(response.msg,'错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.show = false;
                        }
                    }).then(() => {
                        this.show = false;
                    });
                    this.$refs.upload.clearFiles();
                }
            },
            confirm(){
                this.showResult = false;
                this.onImportSuccess();
            },
            submitUpload() {
                this.$refs.upload.submit();
            },
            handleCancel(){
                this.show = false
            },
            showDialog(inputType,businessClass){
                if(inputType == '1'){
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/uploadAccountExcel.json'
                }else if(inputType == '2'){
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/uploadPubAccountExcel.json'
                }else if(inputType == '3') {
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/uploadTransferExcelTemplate.json'
                    this.businessClass = businessClass
                }else if(inputType == '4'){
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/uploadBankBusinFlagExcelTemplate.json'
                }else if(inputType == '5'){
                    this.uploadUrl = window.LOCAL_CONFIG.API_HOME+'/uploadAgencyExcel.json';
                }else if(inputType == '6'){
                    this.uploadUrl = window.LOCAL_CONFIG.API_HOME+'/uploadMarketTradeDayExcel.json';
                }else if(inputType == '7'){
                    this.uploadUrl = window.LOCAL_CONFIG.API_HOME+"/uploadTradeRivalExcel.json";
                }else if(inputType == '8'){
                    this.uploadUrl = window.LOCAL_CONFIG.API_HOME+"/uploadIssuerExcel.json";
                }
                this.inputType = inputType;
                this.show = true;
            },
            downLoadSelfExcel(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getFundAccountExcelTemplate.json';
            },
            downLoadPubExcel(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getPubAccountExcelTemplate.json';
            },
            downLoadTransferExcel(value){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getTransferExcelTemplate.json?type='+value +'&businessClass='+this.businessClass;
            },
            downLoadBankBusinExcel(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getBankBusinExcelTemplate.json';
            },
            downLoadAgencyExcel(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getAgencyTemplate.json';
            },
            downLoadTradeDayExcel(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getMarketTradeDayTemplate.json';
            },
            downLoadTradeRival(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getTradeRivalTemplate.json';
            },
            downLoadIssuer(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getIssuerTemplate.json';
            }
        }
    }
</script>

<style lang="scss" src="@/style/common.scss"></style>
<style lang="scss">
    .drag-in-box {

        .el-upload {
            display: block;
            height: 100%;

            .el-upload-dragger {
                width: 100%;
                height: 100%;
            }
        }
    }
</style>


