<template>
  <div class="cmp-account-list">
    <h-table
      stripe
      size="small"
      :columns="columns"
      :data="accounts_"
      :height="tableHeight"
      :highlight-row="true"
      @on-row-click="onRowClick"
      @on-selection-change="onSelectChange"
      @on-row-dblclick="onRowDbClick"
      :row-class-name="rowClassName"
      ref="table"
      border></h-table>
  </div>
</template>

<script type="text/ecmascript-6">
import { deepCopy } from '@/utils/func'
import { mapState, mapMutations } from 'vuex'
import {
  showText
} from '@/api/utils'

export default {
  props: {
    accounts: {
      type: Array,
      default: () => []
    },
    tableHeight: {
      type: Number,
      default: 0
    },
    fundSettleType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      columns: this.getColumns(),
      accounts_: [],
      selectedAccounts: []
    }
  },
  watch: {
    accounts: {
      handler: function (val) {
        let accounts = deepCopy(val)
        this.accounts_ = accounts
        this.selectedAccounts = []
        this.$nextTick(() => {
          this.$refs.table.selectAll(false)
        })
      }
    }
  },
  created() {

  },
  mounted() {

  },
  activated() {

  },
  computed: {
    ...mapState({
      accountClassList: state => state.dict.dict_90525,
      bankList: state => state.dict.bankList,
      trustBankList: state => state.dict.trustBankList,
      accountTypeList: state => state.dict.dict_60058,
      accountStatusList: state => state.dict.dict_200008,
    })
  },
  methods: {
    onRowClick(data) {
      let account = data[0]
    },
    onSelectChange(selection) {
      this.selectedAccounts = selection
    },
    onRowDbClick(data) {
      this.$emit('accountDetail', data)
    },
    deleteAccount() {
      if (this.selectedAccounts.length === 0) {
        this.$hMessage.info('请选择一个或多个账号进行操作')
        return
      }
      this.$emit('deleteAccount', this.selectedAccounts)
    },
    getSelections() {
      return this.selectedAccounts
    },
    getColumns() {
      return [
        {
          type: 'selection',
          width: 40,
          align: 'center',
          ellipsis: true,
        },
        {
          title: '账户用途',
          type: 'text',
          width: 100,
          key: 'account_class',
          ellipsis: true,
          render: (h, params) => {
            const rowData = params.row;
            let typeName;
            if((this.fundSettleType == '1' || this.fundSettleType == '4') && rowData.account_class == '2'){
              typeName = '自营账户';
            } else {
              typeName = showText(rowData.account_class, this.accountClassList)
            }
            return h('span', {}, typeName);
          }
        },
        {
          title: '账户名称',
          type: 'text',
          width: 160,
          key: 'bank_account_name',
          ellipsis: true,
        },
        {
          title: '银行账号',
          type: 'text',
          width: 160,
          key: 'bank_account_no',
          ellipsis: true,
        },
        {
          title: '银行总行',
          type: 'text',
          width: 160,
          key: 'bic_code',
          ellipsis: true,
          render: (h, params) => {
            const rowData = params.row;
            var typeName = showText(rowData.bic_code, this.bankList);
            return h('span', {}, typeName);
          }
        },
        // {
        //   title: '托管人',
        //   type: 'text',
        //   width: 160,
        //   key: 'custodial_no',
        //   ellipsis: true,
        //   render: (h, params) => {
        //     const rowData = params.row;
        //     var typeName = showText(rowData.custodial_no, this.trustBankList);
        //     return h('span', {}, typeName);
        //   }
        // },
        {
          title: '开户行名称',
          type: 'text',
          width: 160,
          key: 'sub_bank_no',
          ellipsis: true,
        },
        {
          title: '大额支付号',
          type: 'text',
          width: 160,
          key: 'pay_no',
          ellipsis: true,
        },
        {
          title: '账户类型',
          type: 'text',
          width: 160,
          key: 'bank_account_type',
          ellipsis: true,
          render: (h, params) => {
            const rowData = params.row;
            var typeName = showText(rowData.bank_account_type, this.accountTypeList);
            return h('span', {}, typeName);
          }
        },
        {
          title: '账户状态',
          type: 'text',
          width: 160,
          key: 'bank_account_status',
          ellipsis: true,
          render: (h, params) => {
            const rowData = params.row;
            var typeName = showText(rowData.bank_account_status, this.accountStatusList);
            return h('span', {}, typeName);
          }
        },
        {
          title: '联系人',
          type: 'text',
          width: 160,
          key: 'bank_linkman',
          ellipsis: true,
        },
        {
          title: '联系电话',
          type: 'text',
          width: 160,
          key: 'bank_phone',
          ellipsis: true,
        },
        {
          title: '传真',
          type: 'text',
          width: 160,
          key: 'bank_fax',
          ellipsis: true,
        },
        {
          title: '邮箱',
          type: 'text',
          width: 160,
          key: 'bank_mail',
          ellipsis: true,
        },
        {
          title: '持有人账号',
          type: 'text',
          width: 160,
          key: 'holder_account',
          ellipsis: true,
        },{
          title: '备注',
          type: 'text',
          width: 160,
          key: 'remark',
          ellipsis: true,
        },
        {
          title: '期货公司',
          type: 'text',
          width: 160,
          hiddenCol:true,
          key: 'broker_id',
          ellipsis: true,
        },
        {
          title: '银期方式',
          type: 'text',
          width: 160,
          hiddenCol:true,
          key: 'bank_futures_type',
          ellipsis: true,
        },
        {
          title: '期货中间账户',
          type: 'text',
          width: 160,
          hiddenCol:true,
          key: 'futures_mid_account',
          ellipsis: true,
        }
      ]
    },
    rowClassName(row){
      if(row.bank_account_status != '1'){
        return 'alert-table-red-row';
      }else{
        return '';
      }
    }
  },
  components: {

  }
}
</script>

<style lang="less" rel="stylesheet/less" scoped>

  .alert-table-red-row{
    color: red;
  }

</style>



