<template>

    <div class="full-h">
      <h-split v-model="split" min="130" max="600">
        <div slot="left" class="full-h" span="4">
          <h-other-fund-tree :showAll="false" :onFundClick="fundClick"></h-other-fund-tree>
        </div>
        <div slot="right" class="full-h" style="padding-left: 10px" span="20" id="full-h-wrapper-on-fundcashreport">
          <el-form :inline="true" size="mini"  class="demo-form-inline" style="font-size: 12px;">
            <el-form-item label="交收日期">
              <el-date-picker  type="date" align="center" v-model="settleDate" :clearable="false" @change="settleDateChange" >
              </el-date-picker>
            </el-form-item>
            <el-form-item label="确认日期">
              <el-date-picker type="date" align="center" v-model="currDate" :clearable="false" @change="currDateChange"></el-date-picker>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="refresh">查询</el-button>
            </el-form-item>
          </el-form>
          <TADetailTable ref="detailTable" :height="tableHeight" >

          </TADetailTable>
        </div>
      </h-split>
    <!--<h-row :gutter="10" class-name="full-h" >-->
      <!--<h-col span="4"  class-name="full-h">-->
        <!--<h-other-fund-tree style="box-shadow:0 2px 4px 0 rgba(200,200,200,0.50);border-radius:2px;"-->
                           <!--:showAll="false" :onFundClick="fundClick">-->
        <!--</h-other-fund-tree>-->
      <!--</h-col>-->
      <!--<h-col span="20" class-name="pb10 full-h">-->
        <!---->
      <!--</h-col>-->
    <!--</h-row>-->
  </div>
</template>

<script>
    import Cookies from 'js-cookie'
    import{
      formatMoney,
      parseDateString,
      parseDateStr
    } from "@/api/utils"
    import{
      listTAFlow,
    } from "@/api/reportmgnt/taRecord"
    import HOtherFundTree from "../../../components/HOtherFundTree";
    import TADetailTable from "../../../components/TADetailTable";

    export default {
        name: "taRecord",
      components: {TADetailTable, HOtherFundTree},
      data() {
        return {
          tableHeight:0,
          split: 0.15,
          queryForm:{
            settleDate: Cookies.get('CurrSysDate'),
            currDate: '',
            fundCode:''
          },
          businClass:'1',
          businClassList:[{code:'1',text:'2009年01月09日'}],
          shData:[],
          szData:[],
          zjData:[],
          tableData:[],
          tableData1: []
        };
      },
      computed:{
          settleDate:{
              get:function () {
                return parseDateString(this.queryForm.settleDate)
              },
              set:function (newValue) {
                  this.queryForm.settleDate = parseDateStr(newValue);
              }
          },
          currDate: {
              get:function () {
                return parseDateString(this.queryForm.currDate)
              },
              set:function (newValue) {
                this.queryForm.currDate = parseDateStr(newValue);
              }
          }
      },
        mounted(){
          this.$nextTick(() => {
            this.tableHeight = document.getElementById("full-h-wrapper-on-fundcashreport").offsetHeight  - 30 ;
          })
          /*setTimeout(() =>{
            this.tableHeight = document.getElementById("full-h-wrapper-on-fundcashreport").offsetHeight  - 30 ;
          }, 10)*/
        },
      methods: {
        fundClick(data) {
          if (data) {
            if (data.text.indexOf(" ") >= 0)
              this.queryForm.fundCode = data.text.split(" ")[0];
            else
                this.queryForm.fundCode = null
            this.refresh()
          }
        },
        refresh(){
          listTAFlow(this.queryForm).then(res => {
            if (res.data.success) {
              // this. shData = [{
              //   pre: '中登上海',
              // }]
              // this. szData = [{
              //   pre: '中登深圳',
              // }]
              // this. zjData = [{
              //   pre: '自建TA',
              // }]
              // res.data.data.forEach(x => {
              //   if (x.ta_type == '1') {
              //     this. zjData.push(x)
              //   } else if (x.ta_type == '2') {
              //     this. shData.push(x)
              //   } else {
              //     this. szData.push(x)
              //   }
              //
              // });
              // if (this.zjData.length>1){
              //   this.tableData = this.zjData
              //   this.tableData1 = []
              // }else if(this.shData.length ==1&&this.szData.length ==1){
              //   this.tableData = []
              //   this.tableData1 = []
              // }else {
              //   // this.tableData = this.shData
              //   // this.tableData1 = this.szData
              //     this.tableData = this.shData.length>1?this.shData:this.szData
              //     this.tableData1 = this.shData.length>1?this.szData:[]
              // }
              this.$refs.detailTable.setData(res.data.data);
            }
          })
        },
        settleDateChange() {
          if (this.queryForm.currDate) {
            this.queryForm.currDate = '';
          }
        },
        currDateChange() {
          if (this.queryForm.settleDate) {
            this.queryForm.settleDate = '';
          }
        }
      }
    }
</script>

<style scoped>

</style>



