<template>
  <div class="cmp-sortheader">
    <div class="title" @click="switchSort">{{title}}</div>
    <div class="ctrls">
      <i class="up iconfont icon-android-arrow-dropup" :class="{active: status === 'asc' && field === sortingField}" @click="sortBy('asc')"></i>
      <i class="down iconfont icon-android-arrow-dropdo" :class="{active: status === 'desc' && field === sortingField}" @click="sortBy('desc')"></i>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'Sortctrl',
  props: {
    title: {
      type: String,
      default: ''
    },
    field: {
      type: String,
      default: ''
    },
    sortingField: {
      type: String,
      default: 'none'
    }
  },
  data() {
    return {
      status: 'none'
    }
  },
  methods: {
    sortBy(status) {
      if (status === this.status) {
        this.status = 'none'
      } else {
        this.status = status
      }
      this.$emit('sort', this.status)
    },
    switchSort() {
      let statuses = ['none', 'asc', 'desc']
      let idx = statuses.indexOf(this.status)
      let nextIdx = idx + 1
      nextIdx = nextIdx > statuses.length - 1 ? 0 : nextIdx
      this.$emit('sort', statuses[nextIdx])
      this.status = statuses[nextIdx]
    }
  },
  components: {

  }
}
</script>

<style lang="less" rel="stylesheet/less">
@import "~@/style/less/mixins.less";

.cmp-sortheader {
  .flex(@alignItems: center);
  height: 30px;
  margin-left: 6px;
  .title {
    margin-right: 6px;
    cursor: pointer;
  }
  .ctrls {
    .flex(column, space-between, center);
    .size(16px, 16px);
    overflow: hidden;
    .up, .down {
      .size(16px, 10px);
      font-size: 18px;
      line-height: 8px;
      color: #c3cbd6;
      cursor: pointer;
    }
    .up:hover, .down:hover {
      color: gray;
    }
    .active {
      color: #298DFF;
    }
  }
}
</style>



