<template>
    <div class="full-h" >
        <h-split v-model="split" min="130" max="600" @on-moving="onSplitMove">
            <div slot="left" span="4" class="full-h">
                <bank-tree :onBankClick="bankClick" :trustFlag = "true" ref="treeBank"></bank-tree>
            </div>
            <div slot="right" span="20" class="full-h" style="padding-left: 10px" id="full-h-wrapper">
                <h-row>
                    <h-col>
                        <el-form style="font-size: 12px;float: left" :inline="true" size="mini" :model="queryForm"
                                  class="demo-form-inline">
                            <el-form-item label="业务分类">
                                <el-select v-model="queryForm.businClass" clearable filterable @change="queryBusinClassChange">
                                    <el-option v-for="item in businessClassList" :value="item.code" :label="item.text" :key="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="业务标志">
                                <el-select v-model="queryForm.businFlag" clearable filterable >
                                    <el-option v-for="item in businFlagList" :value="item.code" :label="item.text" :key="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="指令处理方式">
                                <el-select v-model="queryForm.transferFlag" clearable filterable>
                                    <el-option v-for="item in transferFlagList" :value="item.code" :label="item.text" :key="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="refreshBankBusinFlag">查询</el-button>
                            </el-form-item>
                        </el-form>
                    </h-col>
                </h-row>
                <h-button-group>
                    <h-button icon="add" @click="bankBusinAdd">新增</h-button>
                    <h-button icon="editor" @click="bankBusinModify">修改</h-button>
                    <h-button icon="editor" @click="bankBusinCopy">复制</h-button>
                    <h-button icon="delete" @click="bankBusinDelete">删除</h-button>
                    <h-button icon="android-upload" @click="uploadExcel">导入</h-button>
                </h-button-group>
                <h-datagrid :height="autoHeight"
                            stripe hasPage highlight-row
                            :auto-load="false" :on-row-db-click="bankBusinFlagDbClick"
                            :pageSize="100" :pageSizeOpts="pageSizeOpts"
                            :columns="bankcolumns" :bindForm="queryForm"
                            url="/systmmgnt/bankBusflag/bankbusflag.json"
                            ref="datagrid">
                </h-datagrid>
            </div>
        </h-split>
        <h-msg-box class="customMsgBox" v-model="showBankBusinessDialog" width="700" :mask-closable=false>
            <p slot="header">
                <span v-if="actionType=='add'">新增划款规则信息</span>
                <span v-else-if="actionType=='modify'">修改划款规则信息</span>
                <span v-else>复制划款规则信息</span>
            </p>
            <h-row>
                <h-col span="12" style="margin-right: 5px">
                    <h-form :model="bankBusinInfo" :label-width="110" ref="bankBusinFlagForm" :onlyBlurRequir=true>

                        <h-form-item required label="托管方式">
                            <h-select  v-model="custodial_type" filterable :clearable="false">
                                <h-option v-for="item in custodialTypeList" :key="item.code"  :label="item.text" :value="item.code"></h-option>
                            </h-select>
                        </h-form-item>

                        <h-form-item required v-if="custodial_type == '1'" label="托管人名称" prop="custodial_no">
                            <h-select v-if="updateComponent" v-model="bankBusinInfo.custodial_no" filterable
                                      :multiple ="actionType == 'add'" :hideMult="actionType == 'add'" :isString="actionType == 'add'" :showBottom="actionType == 'add'">
                                <h-option v-for="item in bankListWithoutSelf" :key="item.code" :label="item.text" :value="item.code.toString()"></h-option>
                            </h-select>
                        </h-form-item>

                        <h-form-item required label="业务分类" prop="cop_busin_class">
                            <h-select v-model="bankBusinInfo.cop_busin_class" @on-change="businClassChange" filterable>
                                <h-option v-for="item in businessClassList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                            </h-select>
                        </h-form-item>

                        <h-form-item v-if="actionType == 'add'" required label="业务标志" prop="cop_busin_flag">
                            <h-select v-model="bankBusinInfo.cop_busin_flag" filterable
                                      :multiple ="actionType == 'add'" :hideMult="actionType == 'add'" :isString="actionType == 'add'" :showBottom="actionType == 'add'">
                                <h-option v-for="item in businessFlagList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                            </h-select>
                        </h-form-item>

                        <h-form-item v-if="actionType == 'modify' || actionType == 'copy'" required label="业务标志" prop="cop_busin_flag">
                            <el-select v-model="bankBusinInfo.cop_busin_flag" filterable size="mini" style="width: 100%">
                                <el-option v-for="item in businessFlagList" :key="item.code" :label="item.text" :value="item.code"></el-option>
                            </el-select>
                        </h-form-item>

                        <h-form-item v-if="bankBusinInfo.cop_busin_class=='1' && bankBusinInfo.cop_busin_flag != 20020" required label="清算机构" prop="clear_agent_code_list">
                            <h-select v-model="bankBusinInfo.clear_agent_code_list" hideMult multiple isString filterable showBottom>
                                <h-option v-for="item in agenctList" :key="item.code" :label="item.text"
                                          :value="item.code">
                                </h-option>
                            </h-select>
                        </h-form-item>

                        <h-form-item  v-if="custodial_type == '1'" label="托管人业务代码" prop="bank_busin_no">
                            <h-input type="text" v-model="bankBusinInfo.bank_busin_no" :maxlength="128"></h-input>
                        </h-form-item>

                        <h-form-item v-if="custodial_type == '1'" label="托管人业务名称" prop="bank_busin_name">
                            <h-input type="text" v-model="bankBusinInfo.bank_busin_name" :maxlength="128"></h-input>
                        </h-form-item>

                        <h-form-item label="指令处理方式" prop="transfer_flag">
                            <h-select v-model="bankBusinInfo.transfer_flag" filterable>
                                <h-option v-for="item in transferFlagList" :key="item.code" :label="item.text"
                                          :value="item.code">
                                </h-option>
                            </h-select>
                        </h-form-item>

                        <h-form-item label="划款事由" prop="transfer_purpose">
                            <h-input type="text" v-model="bankBusinInfo.transfer_purpose" @on-focus="purposeFocus" :maxlength="1024"></h-input>
                        </h-form-item>

                        <h-form-item label="划款附言" prop="transfer_postscript">
                            <h-input type="text" v-model="bankBusinInfo.transfer_postscript" @on-focus="postscriptFocus" :maxlength="1024"></h-input>
                        </h-form-item>

                        <h-form-item label="划款备注" prop="transfer_remark">
                            <h-input type="text" v-model="bankBusinInfo.transfer_remark" @on-focus="remarkFocus" :maxlength="1024"></h-input>
                        </h-form-item>

                        <h-form-item v-if="bankBusinInfo.cop_busin_class=='1' || bankBusinInfo.cop_busin_class=='4'
                        || bankBusinInfo.cop_busin_class=='5'|| bankBusinInfo.cop_busin_class=='6'|| bankBusinInfo.cop_busin_class=='21'"
                                     label="附加信息合并" prop="additional_info_merge_flag">
                            <h-select v-model="bankBusinInfo.additional_info_merge_flag" filterable>
                                <h-option v-for="item in mergeFlagList" :key="item.code" :label="item.text"
                                          :value="item.code">
                                </h-option>
                            </h-select>
                        </h-form-item>
                    </h-form>
                </h-col>
                <h-col span="11">
                    <h-datagrid :height="autoNoteHeight"
                                stripe hasPage
                                :pageSize="100" :pageSizeOpts="pageSizeOpts"
                                :columns="bankNoteMacroColumn"
                                :onRowDbClick="setNoteMacro"
                                url="/systmmgnt/bankBusflag/getNoteMacro.json" ref="noteMacroDatagrid">
                    </h-datagrid>
                </h-col>
            </h-row>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="bankBusinCancel">取消</h-button>
                <h-button type="primary" @click="saveBankBusin">确认</h-button>
            </div>
        </h-msg-box>
        <import-account-transfer :on-import-success="refreshBankTree" ref="importAccountTransfer"></import-account-transfer>
    </div>
</template>

<script>
    import HDatagrid from "../../../components/HDatagrid";

    import{
        addBankBusflag,
        modifyBankbusflag,
        delBankBusflag
    } from "@/api/systemmgnt/bankBusinFlag"

    import{
        showText,
        getDictList,
        getBankList,
        getBankListIncludeSelf,
        getBusinFlagList
    } from "@/api/utils";

    import ImportAccountTransfer from '../../../components/ImportAccountTransfer'
    import BankTree from "../../../components/BankTree";

    export default {
        name: "bankBusiness",
        data(){
            return{
                split: 0.15,
                autoHeight:0,
                autoNoteHeight:0,
                pageSizeOpts:[25,50,75,100],
                businessClassList:[],
                bankList:[],
                bankListWithoutSelf:[],
                businessFlagList:[],
                copBusinFlagList: [],
                businFlagList: [],
                showBankBusinessDialog:false,
                actionType:'',
                noteMacro:'',
                focusType:'',
                updateComponent: true,
                custodial_type:'',
                custodialTypeList:[
                    {
                        code:'1',
                        text:'托管'
                    },
                    {
                        code:'2',
                        text:'自营'
                    }
                ],
                bankcolumns:[],
                //  hiddenInCustodialNo属性用于指定该列在在托管人编号为指定值时隐藏
                bankBusinessColumns:[
                    {
                        type: 'selection',
                        width: 30,
                        align: "left"
                    },{
                        type:'index',
                        title:'序号',
                        width: 60,
                        align:'center'
                    },{
                        title: "托管人业务序号",
                        key: "serial_no",
                        hiddenCol:true,
                        align: "left",
                        ellipsis: true
                    },{
                        title: "托管人序号",
                        key: "custodial_no",
                        align: "left",
                        hiddenCol:true,
                        ellipsis: true
                    },{
                        title: "托管人名称",
                        key: "custodian_name",
                        align: "left",
                        ellipsis: true
                    },{
                        title: "业务分类",
                        key: "cop_busin_class",
                        align: "left",
                        ellipsis: true,
                        render:(h,params) => {
                            const rowData = params.row;
                            var copBusinClass = showText(rowData.cop_busin_class,this.businessClassList);
                            return h("span",{},copBusinClass);
                        }
                    },{
                        title: "清算机构",
                        key: "clear_agent_code_list",
                        align: "left",
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row;
                            let clearAgentCode = '';
                            if( rowData.clear_agent_code_list){
                                let clearAgentCodeList = rowData.clear_agent_code_list.split(",");
                                for( let i = 0; i < clearAgentCodeList.length; i++){
                                    clearAgentCode += showText(clearAgentCodeList[i],this.agenctList)+','
                                }
                                clearAgentCode = clearAgentCode.substring(0,clearAgentCode.length-1);
                            }
                            return h("span",{},clearAgentCode);

                        }
                    },{
                        title: "业务标志",
                        key: "cop_busin_caption",
                        align: "left",
                        width:130,
                        ellipsis: true
                    },{
                        title: "托管人业务代码",
                        hiddenInCustodialNo:'0',
                        key: "bank_busin_no",
                        align: "left",
                        width:120,
                        ellipsis: true
                    },{
                        title: "托管人业务名称",
                        hiddenInCustodialNo:'0',
                        key: "bank_busin_name",
                        align: "left",
                        width:120,
                        ellipsis: true
                    },{
                        title: "划款事由",
                        key: "transfer_purpose",
                        align: "left",
                        ellipsis: true
                    },{
                        title: "划款附言",
                        key: "transfer_postscript",
                        align: "left",
                        ellipsis: true
                    },{
                        title: "划款备注",
                        key: "transfer_remark",
                        align: "left",
                        ellipsis: true
                    },{
                        title: "清算机构",
                        key: "clear_agent_cn_list",
                        align: "left",
                        ellipsis: true,
                        hiddenCol:true
                    },{
                        title: "指令处理方式",
                        key: "transfer_flag",
                        align: "left",
                        ellipsis: true,
                        render:(h,params) => {
                            const rowData = params.row;
                            var transferFlag = showText(rowData.transfer_flag,this.transferFlagList);
                            return h("span",{},transferFlag);
                        }
                    }
                ],
                bankNoteMacroColumn:[
                    {
                        title: "宏命令",
                        key: "code",
                        align: "left",
                        width: 80,
                        ellipsis: true
                    },
                    {
                        title: "宏定义",
                        key: "text",
                        align: "left",
                        ellipsis: true
                    }
                ],
                agenctList:[
                    {
                        code:'3',
                        text:'中债登'
                    },{
                        code:'4',
                        text:'上清所'
                    }
                ],
                transferFlagList:[
                ],
                mergeFlagList:[
                    {
                        code:'0',
                        text:'否'
                    },{
                        code:'1',
                        text:'是'
                    }
                ],
                errorColumns: [
                    {
                        width: 100,
                        key: "bank",
                        title: "托管人名称",
                        align: "center"
                    },{
                        width: 80,
                        key: "class",
                        title: "业务标志",
                        align: "center"
                    }, {
                        width: 307,
                        key: "err",
                        title: "错误信息",
                        align: "left"
                    }
                ],
                queryForm:{
                    businClass:'',
                    businFlag:'',
                    custodialNo:'',
                    transferFlag:''
                },
                bankBusinInfo:{
                    serial_no:'',
                    custodial_no:'',
                    cop_busin_class:'',
                    cop_busin_flag:'',
                    clear_agent_code_list:'',
                    bank_busin_no:'',
                    bank_busin_name:'',
                    transfer_flag:'',
                    transfer_remark:'',
                    transfer_postscript:'',
                    transfer_purpose:'',
                    additional_info_merge_flag:'',
                    ignore:false
                }
            }
        },
        watch:{
            showBankBusinessDialog:function (show) {
                if(show){
                    this.updateComponent = true;
                    this.openMask();
                }else{
                    this.updateComponent = false;
                    this.closeMask();
                }
            }
        },
        created() {
            this.bankcolumns = this.bankBusinessColumns;
        },
        activated(){
            getBankListIncludeSelf('1').then(res => {
                this.bankList = res.data
                this.bankListWithoutSelf = res.data.filter(x => {
                    if (x.code != 0){
                        return true;
                    }
                })
            });
        },
        mounted() {
            getDictList(90523).then(res => this.businessClassList = res.data);
            getDictList(91216).then(res => this.transferFlagList = res.data);
            this.re_layout();
        },
        methods:{
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 85;
            },
            show_layout(){
                /*暂时写死*/
                this.autoNoteHeight = 335;
                setTimeout(() => {
                    this.autoNoteHeight = this.$refs.bankBusinFlagForm.$el.clientHeight;
                }, 300);
            },
            businClassChange:function(currentValue){
                if(currentValue){
                    getBusinFlagList(currentValue).then(res => this.businessFlagList = res.data);
                    if(this.showBankBusinessDialog && this.actionType == 'modify'){
                        this.bankBusinInfo.cop_busin_flag = '';
                    }
                    if(currentValue == '1'){
                        this.autoNoteHeight = 410;
                    }else if(currentValue == '4' || currentValue == '5' || currentValue == '6' || currentValue == '21'){
                        this.autoNoteHeight = 370;
                        this.bankBusinInfo.clear_agent_code_list = '';
                    }else{
                        this.autoNoteHeight = 335;
                        this.bankBusinInfo.clear_agent_code_list = '';
                    }
                }
            },
            queryBusinClassChange: function(currentValue) {
                if (currentValue) {
                    getBusinFlagList(currentValue).then(res => this.businFlagList = res.data);
                    this.queryForm.businFlag = '';
                } else {
                    this.queryForm.businFlag = '';
                    this.queryForm.businClass = '';
                    this.businFlagList = [];
                }
            },
            bankBusinAdd(){
                this.bankBusinInfo = {
                    serial_no: null,
                    custodial_no: '',
                    cop_busin_class: '',
                    cop_busin_flag: '',
                    clear_agent_code_list: '',
                    bank_busin_no: '',
                    bank_busin_name: '',
                    transfer_flag: '1',
                    transfer_remark: '',
                    transfer_postscript: '',
                    transfer_purpose: '',
                    additional_info_merge_flag:'0',
                    ignore: false
                };
                if(this.queryForm.custodialNo){
                    this.bankBusinInfo.custodial_no = this.queryForm.custodialNo
                }else{
                    this.bankBusinInfo.custodial_no = '';
                }
                this.$refs.bankBusinFlagForm.resetFields();
                this.businessFlagList = [];
                this.noteMacro = '';
                this.showBankBusinessDialog = true;
                this.actionType="add";
                this.show_layout();
            },
            bankBusinModify(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                if(selects.length > 1){
                    this.openMask();
                    this.$alert('只能选择一条记录修改','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                this.bankBusinFlagDbClick(selects[0]);
            },
            bankBusinFlagDbClick(data){
                this.noteMacro = '';
                this.actionType = 'modify';
                this.bankBusinInfo = data;
                if (this.bankBusinInfo.custodial_no != 0){
                    this.custodial_type = '1'
                } else {
                    this.custodial_type = '2';
                }
                if(this.bankBusinInfo.cop_busin_class == '1'){
                    this.autoNoteHeight = 410;
                }else if(this.bankBusinInfo.cop_busin_class == '4' || this.bankBusinInfo.cop_busin_class == '5' || this.bankBusinInfo.cop_busin_class == '6' || this.bankBusinInfo.cop_busin_class == '21'){
                    this.autoNoteHeight = 370;
                }else{
                    this.autoNoteHeight = 335;
                }
                this.$nextTick(() =>{
                    this.showBankBusinessDialog = true;
                })
                this.show_layout();
            },
            bankBusinCopy(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录",'错误',{
                        confirmButtonText:"确定",
                        type:"error",
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                this.bankBusinInfo=selects[0]
                this.bankBusinInfo.serial_no = null
                if(this.bankBusinInfo.cop_busin_class == '1'){
                    this.autoNoteHeight = 410;
                }else if(this.bankBusinInfo.cop_busin_class == '4' || this.bankBusinInfo.cop_busin_class == '5' || this.bankBusinInfo.cop_busin_class == '6' || this.bankBusinInfo.cop_busin_class == '21'){
                    this.autoNoteHeight = 370;
                }else{
                    this.autoNoteHeight = 335;
                }
                this.noteMacro = '';
                this.actionType = "copy";
                this.show_layout();
                this.showBankBusinessDialog = true;
            },
            bankBusinDelete(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }

                let serialNoList= '';
                for( let i = 0; i < selects.length; i++){
                    serialNoList += selects[i].serial_no + ",";
                }
                serialNoList = serialNoList.substring(0 ,serialNoList.length - 1)

                this.openMask();
                this.$confirm('确认删除该记录吗？', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    delBankBusflag(serialNoList).then(res =>{
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            })
                            this.refreshBankBusinFlag();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            })
                        }
                    })
                }).catch(() =>{
                    this.closeMask();
                })
            },
            bankBusinCancel(){
                this.showBankBusinessDialog = false;
            },
            refreshBankTree(){
                this.$refs.treeBank.handleConditionQuery();
            },
            saveBankBusin(){
                let isValid = true;
                this.$refs.bankBusinFlagForm.validate((validate) => isValid = isValid && validate)
                if(!this.bankBusinInfo.cop_busin_flag || !this.bankBusinInfo.cop_busin_class){
                    isValid = false;
                }
                if(this.custodial_type == 2){
                    this.bankBusinInfo.custodial_no = 0;
                }
                if(!isValid){
                    this.$alert('输入项不满足要求，请重新检查','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }
                if(this.bankBusinInfo.cop_busin_class == '1' && (this.bankBusinInfo.cop_busin_flag.indexOf(",20020") != -1 || this.bankBusinInfo.cop_busin_flag.indexOf("20020,") != -1)){
                    this.$alert('同业存单为非DVP业务,不能和其他银行间业务一起维护,请单独维护','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    });
                    return
                }

                if(this.bankBusinInfo.cop_busin_class != '1' && this.bankBusinInfo.cop_busin_class != '4'
                    && this.bankBusinInfo.cop_busin_class != '5' && this.bankBusinInfo.cop_busin_class != '6'
                    && this.bankBusinInfo.cop_busin_class != '21'){
                    this.bankBusinInfo.additional_info_merge_flag = '';
                }

                if(this.actionType == 'add' || this.actionType == 'copy'){
                    this.bankBusinInfo.ignore = false;
                    addBankBusflag(this.bankBusinInfo).then( res =>{
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:"success",
                                callback: action => {
                                    this.showBankBusinessDialog = false;
                                }
                            }).then(() =>{
                                this.showBankBusinessDialog = false;
                            })
                            this.refreshBankBusinFlag();

                        }else if(res.data && res.data.code == '1120002'){
                            this.bankBusinInfo.ignore = true;
                            const h = this.$createElement;
                            let errInfos =[]
                            res.data.data.errorInfo.split(";").forEach(x=>{
                                if (x&&x.length>1)
                                    errInfos.push({bank:x.split(':')[0],class:x.split(':')[1],err:x.split(':')[2]})
                            })

                            this.showBankBusinessDialog = false;

                            let self = this;

                            setTimeout( function () {
                                self.openMask();
                                self.$msgbox({
                                    title: '注意',
                                    message: h('p', null, [
                                        h('span', null, '存在'+res.data.data.count+'条重复维护的托管人信息，是否剔除并继续？'),
                                        h('h-table', { style:'width: 100%',props: {
                                                columns: self.errorColumns,
                                                data:errInfos,
                                                height:300
                                            },
                                        })
                                    ]),
                                    showCancelButton: true,
                                    confirmButtonText: '继续',
                                    cancelButtonText: '取消',
                                    beforeClose: (action, instance, done) => {
                                        if (action === 'confirm') {
                                            addBankBusflag(self.bankBusinInfo).then(res => {
                                                if (res.data.success) {
                                                    self.openMask();
                                                    self.$alert(res.data.msg,'成功',{
                                                        confirmButtonText: '确定',
                                                        type: 'success',
                                                        callback: action => {
                                                            self.closeMask()
                                                        }
                                                    }).then(() =>{
                                                        self.closeMask()
                                                    })
                                                    self.refreshBankBusinFlag();
                                                } else {
                                                    self.openMask();
                                                    self.$alert(res.data.msg,'错误',{
                                                        confirmButtonText: '确定',
                                                        type: 'error',
                                                        callback: action => {
                                                            self.closeMask()
                                                        }
                                                    }).then(() =>{
                                                        self.closeMask()
                                                    })
                                                }
                                            }).catch(error => {
                                            });
                                            done();
                                        } else {
                                            done();
                                        }
                                    }
                                }).then(action => {
                                    self.closeMask()
                                }).catch(() => {
                                    self.closeMask()
                                });
                            },1)
                        }else {
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:"error"
                            })
                        }
                    })
                }else if(this.actionType == 'modify'){
                    this.bankBusinInfo.ignore = true;
                    modifyBankbusflag(this.bankBusinInfo).then(res =>{
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.showBankBusinessDialog = false;
                                }
                            }).then(() =>{
                                this.showBankBusinessDialog = false;
                            })
                            this.refreshBankBusinFlag();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }
            },
            bankClick(data){
                if(data){
                    this.queryForm.custodialNo=data.code;
                    this.bankcolumns = this.bankBusinessColumns.filter(x => !x.hiddenInCustodialNo || !(x.hiddenInCustodialNo.split(',').indexOf(this.queryForm.custodialNo.toString())>-1) );
                    this.$refs.datagrid.dataChange(1);
                }
            },
            uploadExcel(){
                this.$refs.importAccountTransfer.showDialog("4",null);
            },
            refreshBankBusinFlag(){
                this.$refs.datagrid.dataChange(1);
            },
            remarkFocus(){
                this.focusType = "1";
                if(this.bankBusinInfo.transfer_remark == null){
                    this.bankBusinInfo.transfer_remark = '';
                }
                this.noteMacro = '';
            },
            postscriptFocus(){
                this.focusType = "2";
                if(this.bankBusinInfo.transfer_postscript == null){
                    this.bankBusinInfo.transfer_postscript = '';
                }
                this.noteMacro = '';
            },
            purposeFocus(){
                this.focusType = "3";
                if(this.bankBusinInfo.transfer_purpose == null){
                    this.bankBusinInfo.transfer_purpose = '';
                }
                this.noteMacro = '';
            },
            setNoteMacro(value){
                if(this.focusType && this.focusType == '1'){
                    if(!this.bankBusinInfo.transfer_remark){
                        this.bankBusinInfo.transfer_remark = ''
                    }
                    this.bankBusinInfo.transfer_remark += '#'+value.code;
                }else if(this.focusType && this.focusType == '2'){
                    if(!this.bankBusinInfo.transfer_postscript){
                        this.bankBusinInfo.transfer_postscript = ''
                    }
                    this.bankBusinInfo.transfer_postscript += '#'+value.code;
                }else if(this.focusType && this.focusType == '3'){
                    if(!this.bankBusinInfo.transfer_purpose){
                        this.bankBusinInfo.transfer_purpose = ''
                    }
                    this.bankBusinInfo.transfer_purpose += '#'+value.code;
                }
            },
            onSplitMove(){
                this.$refs.datagrid.handleResize();
            }
        },
        components: {
            ImportAccountTransfer,
            HDatagrid,
            BankTree
        }
    }
</script>

<style scoped>

</style>


