import fetch from "@/api/httpFetch";

export function saveLiquidateRule(params) {
    return fetch.post("/liquidate/rule/save.json",params,{
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function deleteLiquidateRule(custodialNo,liquidateRuleId) {
    return fetch.post("/liquidate/rule/delete.json",{custodialNo:custodialNo,liquidateRuleId:liquidateRuleId})
}

export function changeRuleFlag(custodialNo,liquidateRuleId,useFlag) {
    return fetch.post("/liquidate/rule/flag/modify.json",{custodialNo:custodialNo,liquidateRuleId:liquidateRuleId,useFlag:useFlag})
}


