                        const rowData = params.row;
                        var name = formatMoney(rowData.settle_amount);
                        return h("span", {style: {fontWeight: 'bold'}}, name);
                    }
                }, {
                    title: "托管人",
                    key: "fund_trust_bank",
                    ellipsis: true,
                    sortable: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = showText(rowData.fund_trust_bank, this.bankList);
                        return h("span", {}, typeName);
                    }
                }, {
                    title: "交收日期",
                    key: "cash_settle_date",
                    ellipsis: true,
                    sortable: true,
                    align: 'left',
                    width: 100,
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = parseDateString(rowData.cash_settle_date);
                        return h("span", {}, typeName);
                    }
                }, {
                    title: "深圳股东代码",
                    key: "stockholder_id",
                    ellipsis: true,
                    align: 'left',
                    width: 120,
                }, {
                    title: "备付金账号",
                    key: "ta_account",
                    ellipsis: true,
                    align: 'left',
                    width: 120,
                }, {
                    title: "证券代码",
                    key: "report_code",
                    ellipsis: true,
                    align: 'left',
                    width: 80,
                }];

            return {
                split: 0.15,
                mainQueryForm: {
                    fundIds: '',
                    queryType: '',
                    keyword: ''
                },
                mainHeight: 0,
                queryTypeList: [],
                pageSizeOpts: [25, 50, 75, 100],
                tab: '',
                detailColumns4SQS: detailColumns4SQS,
                detailColumns4ZZD: detailColumns4ZZD,
                detailColumns4ZDSH: detailColumns4ZDSH,
                detailColumns4ZDSZ: detailColumns4ZDSZ,
                shclearingInsStatus: [],
                chinabondInsStatus: [],
                zdshInsStatus: [],
                transferFundStatusList: [],
                clearAgentList: [],
                bankList: [],
                clearTypeList: [{code: '1', text: '全额结算'}, {code: '2', text: '净额结算'}],
                settleTypeList: [],
                zdshBusinTypeList: [{code: '601', text: '现券交易'}, {code: '605', text: '现券交易'}, {
                    code: '611',
                    text: '现券交易'
                },
                    {code: '619', text: '协议回购首期'}, {code: '613', text: '协议回购到期'},
                    {code: '614', text: '协议回购到期续做（前期合约了结）'}, {code: '615', text: '协议回购到期续做（合约新开）'}],
                zdszBusinTypeList: [{code: 'JY01', text: '现券交易'},
                    {code: 'XYCS', text: '协议回购首期'}, {code: 'XYDQ', text: '协议回购到期'},
                    {code: 'XYXZ', text: '协议回购续做'}],
                sqsBusinTypeList: [{code: '4', text: '现券交易'},
                    {code: '5', text: '债券远期'}, {code: '8', text: '债券借贷'},
                    {code: '9', text: '质押式回购'}, {code: '10', text: '买断式回购'}],
                zzdBusinTypeList: [{code: 'BT00', text: '债券分销'}, {code: 'BT01', text: '现券交易'},
                    {code: 'BT02', text: '质押式回购'}, {code: 'BT03', text: '买断式回购'},
                    {code: 'BT04', text: '债券远期'}, {code: 'BT05', text: '债券借贷'},
                    {code: 'BT23', text: '质押式回购到期'}, {code: 'BT24', text: '买断式回购到期'}, {code: 'BT25', text: '债券借贷到期'},
                    {code: '320', text: '现券交易'}, {code: '341', text: '质押式回购'}, {code: '331', text: '买断式回购'}],
            }
        },

        mounted() {
            getDictList(91140).then(res => this.shclearingInsStatus = res.data);
            getDictList(91137).then(res => this.chinabondInsStatus = res.data);
            getDictList(91228).then(res => this.zdshInsStatus = res.data);
            getDictList(90571).then(res => this.transferFundStatusList = res.data);
            getDictList(610309).then(res => this.clearAgentList = res.data);
            getDictList(40334).then(res => this.settleTypeList = res.data);
            getBankList("1").then(res => this.bankList = res.data);

            this.reLayout();
        },

        activated() {
            getBankList("1").then(res => this.bankList = res.data);
            this.refreshTable();
            SSObservable.subscribe('moduleHeightChange', this.reLayout);
        },

        deactivated() {
            SSObservable.unsubscribe('moduleHeightChange', this.reLayout);
        },

        methods: {
            onSplitMoving(){
                this.resizeTable();
            },
            reLayout() {
                let fullHeight = document.getElementById("full-h-wrapper-on-bondsettlemgnt") ? document.getElementById("full-h-wrapper-on-bondsettlemgnt").offsetHeight : 0;
                this.mainHeight = fullHeight - 190;
            },
            fundClick(data) {
                if (!data) {
                    this.mainQueryForm.fundIds = '';
                    this.refreshTable();
                    return;
                }

                this.mainQueryForm.fundIds = data.code;
                this.refreshTable();
            },

            refreshTable() {
                this.reLayout();
                if (this.tab) {
                    if (this.tab === '1') {
                        this.mainQueryForm.queryType = '1';
                        this.$refs.bondSettleMgnt4SQS.refresh();
                    } else if (this.tab === '2') {
                        this.mainQueryForm.queryType = '2';
                        this.$refs.bondSettleMgnt4ZZD.refresh();
                    } else if (this.tab === '3') {
                        this.mainQueryForm.queryType = '3';
                        this.$refs.bondSettleMgnt4ZDSH.refresh();
                    } else if (this.tab === '4') {
                        this.mainQueryForm.queryType = '4';
                        this.$refs.bondSettleMgnt4ZDSZ.refresh();
                    }
                } else {
                    this.mainQueryForm.queryType = '1';
                    this.$refs.bondSettleMgnt4SQS.refresh();
                }
            },

            resizeTable() {
                if (this.tab) {
                    if (this.tab === '1') {
                        this.$refs.bondSettleMgnt4SQS.handleResize();
                    } else if (this.tab === '2') {
                        this.$refs.bondSettleMgnt4ZZD.handleResize();
                    } else if (this.tab === '3') {
                        this.$refs.bondSettleMgnt4ZDSH.handleResize();
                    } else if (this.tab === '4') {
                        this.$refs.bondSettleMgnt4ZDSZ.handleResize();
                    }
                } else {
                    this.$refs.bondSettleMgnt4SQS.handleResize();
                }
            },

            tabChange(tab, event) {
                if (tab.label === '上清所') {
                    this.tab = '1';
                } else if (tab.label === '中债登') {
                    this.tab = '2';
                } else if (tab.label === 'PROP') {
                    this.tab = '3';
                } else if (tab.label === 'DCOM') {
                    this.tab = '4';
                }
                this.refreshTable();
            }
        },

    }
</script>



