<template>
    <div id="full-h-wrapper" class="page-fund-reserve-review full-h">
        <review-table :on-selection-change="rowClick" :clear-data="clearData" :query-check="queryCheck" ref="reviewTable"></review-table>
        <div v-if="showDetail" id="wrapper-0" v-bind:style="isScrollBar">
            <div class="detail wrapper">
                <header class="headline">
                    <i class="icon-l"></i>
                    <span class="title">详情</span>
                </header>
                <h-row>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">产品代码</div>
                            <div class="value" :title="fundReserveInfo.fund_code">{{fundReserveInfo.fund_code}}</div>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">上月已缴备付金</div>
                            <div class="value" :title="formatMoney(fundReserveInfo.last_reserve_payment)">{{formatMoney(fundReserveInfo.last_reserve_payment)}}</div>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">本月应缴备付金</div>
                            <div class="value" :title="formatMoney(fundReserveInfo.curr_reserve_payment)">{{formatMoney(fundReserveInfo.curr_reserve_payment)}}</div>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">上日银行余额</div>
                            <div class="value" :title="formatMoney(fundReserveInfo.account_last_balance)">{{formatMoney(fundReserveInfo.account_last_balance)}}</div>
                        </div>
                    </h-col>

                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">产品名称</div>
                            <div class="value" :title="fundReserveInfo.fund_name">{{fundReserveInfo.fund_name}}</div>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">债券交易量调整值</div>
                            <div class="value" :title="formatMoney(fundReserveInfo.bond_trade_adjust_value)">{{formatMoney(fundReserveInfo.bond_trade_adjust_value)}}</div>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">本月应备资金</div>
                            <div class="value" :title="formatMoney(fundReserveInfo.curr_reserve_unpay)">{{formatMoney(fundReserveInfo.curr_reserve_unpay)}}</div>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">上日银行余额-本月应备资金</div>
                            <div class="value" :title="formatMoney(fundReserveInfo.diff_balance)">{{formatMoney(fundReserveInfo.diff_balance)}}</div>
                        </div>
                    </h-col>

                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">日期</div>
                            <div class="value" :title="fundReserveInfo.curr_date">{{fundReserveInfo.curr_date}}</div>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">其他交易量调整值</div>
                            <div class="value" :title="formatMoney(fundReserveInfo.other_trade_adjust_value)">{{formatMoney(fundReserveInfo.other_trade_adjust_value)}}</div>
                        </div>
                    </h-col>

                </h-row>
                <header v-if="fundReserveInfo.modify_desc" class="headline mt10 pt10 border-dashed-top">
                    <i class="icon-l"></i>
                    <span class="title">变更内容</span>
                </header>
                <h-row v-if="fundReserveInfo.modify_desc">
                    <h-col span="6" class="inner" v-for="(item, index) in fundReserveInfo.modify_desc.split(';')" :key="index">
                        <div class="item">
                            <div class="label">{{decodeURIComponent(item.split(":")[0])}}</div>
                            <div class="value" :title="decodeURIComponent(item.split(':')[1])">{{decodeURIComponent(item.split(":")[1])}}</div>
                        </div>
                    </h-col>
                </h-row>
            </div>
        </div>
    </div>
</template>

<script>
    import HDatagrid from '@/components/HDatagrid'
    import {
        formatMoney
    } from '@/api/utils'

    import {
        getCheckDetail,
        submitReview
    } from '@/api/fundAccount/review'
    import ReviewTable from "../../../../components/ReviewTable";

    export default {
        name: "review",
        data() {
            return {
                autoHeight: 0,
                queryCheck: {
                    checkStatus: '1',inputOperator:null,
                    objectType: 3
                },
                fundReserveInfo: {
                    fund_id: '',
                    fund_code:'',
                    fund_name: '',
                    curr_date:0,
                    bond_trade_adjust_value: 0.0,
                    other_trade_adjust_value: 0.0,
                    last_reserve_payment: 0.0,
                    curr_reserve_payment: 0.0,
                    curr_reserve_unpay: 0.0,
                    account_last_balance: 0.0,
                    estimate_curr_reserve_payment:0.0,
                    diff_balance: 0.0,
                    modify_desc:''
                },
                isScrollBar:{
                    // border: 'solid 1px #d3d3d3',
                    marginTop: '10px',
                    overflow: 'auto',
                    maxHeight: ''
                },
                showDetail:false
            }
        },
        created() {

        },
        computed: {

        },
        mounted() {
            this.re_layout();
        },
        components: {
            ReviewTable,
            HDatagrid
        },
        activated() {
            this.$refs.reviewTable.refresh();
        },
        methods: {
            re_layout() {
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                let fullWrapperHeight = document.getElementById("full-h-wrapper").offsetHeight;
                if(wrapper0Height > fullWrapperHeight / 2){
                    this.isScrollBar.maxHeight = fullWrapperHeight / 2 + 'px';
                    wrapper0Height = fullWrapperHeight/2;
                }
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 70 ;
                this.$refs.reviewTable.resizeTable(this.autoHeight );
            },
            formatMoney:formatMoney,
            clearData(){
                this.fundReserveInfo = {};
                this.showDetail = false;
                this.$nextTick(this.re_layout);
            },
            rowClick(arr) {

                getCheckDetail(arr.check_id, arr.object_type).then(res => {
                    this.showDetail = true;
                    if (res.data && res.data.code == '0') {
                    this.fundReserveInfo = res.data.data;
                    /* re layout, 模拟自适应布局 */
                    this.$nextTick(this.re_layout);
                } else {
                    this.openMask();
                    this.$alert(res.data.msg,'错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                        this.closeMask();
                }
                }).then(() => {
                        this.closeMask();
                })
                }

            }).catch(error => {
                });
            }
        }
    }
</script>

<style lang="scss" src="@/style/common.scss"></style>
<style>

</style>
<style lang="less">
    .page-fund-reserve-review {
        .item {
            margin-bottom: 8px;
            padding-right: 10px;
        }

        .wrapper {
            width: 100%;
            // height: 208px;
            padding: 10px;
            background-color: #f6f7f9;
            .headline {
                        padding-bottom: 10px;
                        display: flex;
                        flex-direction: row;
                        align-items: center;

                        .icon-l {
                            display: block;
                            width: 3px;
                            height: 12px;
                            background-color: #11a0f8;
                            margin-right: 5px;
                        }

                        .title {
                            padding: 0;
                            margin-right: 10px;
                            font-size: 12px;
                            color: #666666;
                        }

                        .icon-operation {
                            cursor: pointer;
                            color: #11a0f8;
                        }
                    }

            .inner {
                // height: 196px;
                padding: 0 12px;

                &:nth-child(4n + 1) {
                    padding-left: 0;
                }

                &:not(:nth-child(4n)) {
                    border-right: dashed 1px #666666;
                }
            }

            .inner-t,
            .inner {
                .item {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;
                    padding-right: 0;
                    white-space: nowrap;
                    line-height: 21px;

                    &:last-child {
                        margin-bottom: 0;
                    }

                    .label {
                        white-space: nowrap;
                        font-size: 12px;
                        color: #999999;
                    }

                    .value {
                        padding-left: 10px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        font-size: 14px;
                        color: #333333;
                    }
                }
            }
        }

        .detail {
            border: 1px solid #d3d3d3;
        }
    }

</style>



