<template>
  <div class="cmp-simple-tree-grid">
    <div class="tree-grid customClass" :class="{'fill-scripe': true}">
      <h-simple-tree-gird
          :columns="solvedColumns"
          :data="sortedDatas"
          :height="gridHeight"
          :headSelection="headSelection"
          :checkStrictly="checkStrictly"
          :highlight-row="true"
          :row-class-name="rowClassName"
          :loading="loading"
          :no-data-text="noDataText"
          :stripe="false"
          size="small"
          @on-row-click="handleRowClick"
          @on-row-dblclick="handleRowDblClick"
          @on-expand="handleExpand"
          @on-select-change="handleSelectChange"
          @on-select-all="handleSelectAll"
          :isCheckbox="isCheckbox"
          canDrag
          rowSelect
          ref="table">
      </h-simple-tree-gird>
    </div>

    <h-page
      class="page-ctrl"
      :total="total" show-elevator show-total show-sizer
      :placement="placement"
      :page-size="pageSize"
      :pageSizeOpts="pageSizeOpts"
      @on-page-size-change="pageSizeChange"
      :current="current"
      @on-change="dataChange"
      v-if="pageCtrlVisible"></h-page>

    <h-msg-box class="customMsgBox" v-if="gridName" v-model="showLayout" width = "490px">
        <p slot="header"><span>布局</span></p>
        <h-transfer
          :titles="['隐藏列','显示列']"
          :data="columndata"
          :target-keys="showColumns"
          :render-format="renderMethod"
          @on-right-drag="upDownMove"
          @on-change="handleChange">
        </h-transfer>
        <div slot="footer">
          <h-button type="primary" @click="submitLayout">保存</h-button>
          <h-button type="ghost" @click="handleCancel">取消</h-button>
        </div>
    </h-msg-box>
  </div>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue'
import Sortheader from './sortheader'
import { addClass, removeClass, getStyle, setStyle } from '../../../utils/dom'
import service from './service'
import { deepCopy, uniqueArray } from '../../../utils/func'
import {
  getShowColumn,
  setShowColumn
} from '@/api/utils'
Vue.component('Sortheader', Vue.extend(Sortheader))

export default {
  props: {
    headSelection: {
      type: Boolean,
      default: true
    },
    checkStrictly: {
      type: Boolean,
      default: true
    },
    stripe: {
      type: Boolean,
      default: false
    },
    isCheckbox: {
      type: Boolean,
      default: true
    },
    gridName: {
      type: String,
      default: null
    },
    noDataText: String,
    columns: {
      type: Array,
      default: () => []
    },
    // 内容高度
    height: {
      type: Number,
      default: 0
    },
    // 接口地址
    url: {
      type: String,
      default: ''
    },
    fundData: {
      type: Object,
      default: () => {}
    },
    onRowClick: {
      type: Function,
        default () {
            return ''
        }
    },
    onRowDbClick: {
      type: Function,
        default () {
            return ''
        }
    },
    rowClassName: {
      type: Function,
        default () {
            return ''
        }
    },
    pageSize: Number,
    pageSizeOpts: Array,
    placement: {
      type: String,
      default: 'top'
    }
  },
  data() {
    return {
      datas: [],
      sortedDatas: [],
      sortingField: '',
      solvedColumns: [],
      showColumns: this.getTargetKeys(),
      showColumnsTmp: '',
      columndata: this.getMockData(),
      latestRowData: null,
      total: 0,
      pageNo: 1,
      loading: false,
      gridNameDetail: '',
      showLayout: false,
      gridHeight: 0,
      pageInfo: {
        pageNo: 1,
        pageSize: 100
      },
      current: 1,
      selectedDatas: [],
      stripeHeight: 0,
    }
  },
  computed: {
    pageCtrlVisible() {
      return this.total > this.pageSize
    }
  },
  watch: {
    columns: {
      handler: function (columns) {
        for (let column of columns) {
          if (column.render) {
            column.hiddenOther = true
          }
        }
        this.solvedColumns = columns || []
      },
      immediate: true
    },
    fundData: {
      handler: function(val) {
        this.pageNo = 1
        this.getDatas()
      }
    },
    showColumns: {
      handler: function(shows) {
        let allColumns = this.columns || []
        let solvedColumnsTmp = allColumns.filter(e => e.fixedLayout === '1')
        for (let i = 0; i < shows.length; i++) {
          if (shows[i] === 'transfer_ins_id') {
            continue
          }
          let list = allColumns.filter(e => e.key === shows[i])
          if (list && list.length > 0) {
            solvedColumnsTmp.push(list[0])
          }
        }

        // console.log(solvedColumnsTmp.map(e => e.key + `(${e.width})`).join(','))
        /** 以下代码用于处理 treeGrid 的bug，当 width 不设置时候，点击排序水平滚动条位置错误 & 列少的时候内容宽度填充整宽 */
        for (let col of solvedColumnsTmp) {
          // 没设置宽度的，并不为 urgent_flag，设置一个固定宽度
          if (col && !col.width && col.key !== 'urgent_flag') {
            col.width = 200
          }
          // 为 transfer_ins_id 定固定宽
          if (col.key === 'transfer_ins_id') {
            col.width = 120
          }
          // 为 urgent_flag 定固定宽
          if (col.key === 'urgent_flag') {
            col.width = 40
          }
        }
        this.$nextTick(e => {
          /** 当可见列的宽度和小于元素宽度的时候删除第一个宽度 */
          let sumWidth = solvedColumnsTmp.map(e => parseFloat(e.width)).reduce((a, b) => a + b)
          let domWidth = this.$el.clientWidth
          if (sumWidth < domWidth) {
            delete solvedColumnsTmp[0].width
          }
          this.solvedColumns = solvedColumnsTmp
        })
      },
      immediate: true
    },
    datas: {
      handler: function (val) {
        this.sortedDatas = deepCopy(val)
      }
    },
    sortedDatas: {
      handler: function (val) {
        this.solveRowClassNameBugs()
        this.solveHeaderCheckboxBugs()
      }
    },
    pageSize: {
      handler: function (val) {
        this.pageInfo.pageSize = val;
      }
    },
    pageNo: {
      handler: function (no) {
        this.getDatas()
      }
    },
    selectedDatas: {
      handler: function (val) {
        this.$emit('selectionChange', val)
      }
    }
  },
  mounted() {
    this.updateGridHeight()
    // window.onresize = () => {
    //   this.updateStripe(true)
    // }
  },
  activated() {
    SSObservable.subscribe('moduleHeightChange', this.updateLayoutWhenWindowResize)
  },
  deactivated() {
    SSObservable.unsubscribe('moduleHeightChange',this.updateLayoutWhenWindowResize)
  },
  methods: {
    updateLayoutWhenWindowResize() {
      this.updateStripe(true)
      this.updateGridHeight()
    },
    updateGridHeight() {
      this.$nextTick(() => {
        let totalHeight = this.$el.clientHeight
        if (this.total > this.pageSize) {
          this.gridHeight = totalHeight - 40
        } else {
          this.gridHeight = totalHeight
        }
        // console.log('this.gridHeight', this.gridHeight)
      })
    },
    getMockData () {
      let mockData = []
      let tmpData = []
      /** *********列表存在勾选框等其他不需要展示列可以通过该方式剔除（x => x.fixedLayout.indexOf('1')）*************************/
      tmpData = this.columns.filter(x => !x.fixedLayout || x.fixedLayout.indexOf('1') < 0)
      for (let i = 0; i < tmpData.length; i++) {
        mockData.push({
          key: tmpData[i].key,
          label: tmpData[i].title
        })
      }
      return mockData
    },
    getTargetKeys () {
      return this.getMockData().filter(() => Math.random() * 2 > 1).map(item => item.key)
    },
    upDownMove(newTargetKeys) {
      let newTmpTargetKeys = []
      for (let i = 0; i < newTargetKeys.length; i++) {
        newTmpTargetKeys.push(newTargetKeys[i].key)
      }
      this.showColumns = newTmpTargetKeys
    },
    renderMethod (item) {
      return item.label
    },
    handleChange (newTargetKeys) {
      this.showColumns = newTargetKeys
    },
    submitLayout() {
      this.showColumnsTmp = this.showColumns.join(',')
      setShowColumn({
        datagridName: this.gridNameDetail ? this.gridNameDetail : this.gridName,
        showColumnStr: this.showColumnsTmp
      }).then(res => {
        if (res.data && res.data.code === '0') {
          this.showLayout = false
        } else {
          this.$alert('调整布局失败', '错误', {
            confirmButtonText: '确定',
            type: 'error'
          })
        }
      })
    },
    handleCancel() {
      this.showLayout = false
    },
    // 页码改变
    pageSizeChange (i) {
        this.pageNo = 1
        this.pageInfo.pageSize = i;
        this.pageInfo.pageNo = 1;
        this.current = 1;
        this.getDatas()
    },
    /*changePage(pageNo) {
      this.pageNo = pageNo
    },*/
    clearSelected() {
      let selections = this.selectedDatas
      for (let data of selections) {
        this.$refs.table.selectRow(data.id, false)
      }

      this.$refs.table.clearSelected()
      this.selectedDatas = []
    },
    forceClearAllSelected() {
      let doms = this.$refs.table.$el.querySelectorAll('tr.not-child')
      for (let dom of doms) {
        removeClass(dom, 'h-editgird-row-highlight')
      }
    },
    getDatas() {
      this.loading = true
      service.getDatas(this.url, this.fundData, this.pageNo, this.pageInfo.pageSize).then(res => {
        this.loading = false
        if (!res.data.list) {
          res.data.list = []
        }
        let copyed = deepCopy(res.data.list)
        let treeData = service.makeTreeData(copyed)
        let solved = treeData
        // let solved = service.makeDataChecked(treeData)
        service.makeDataHaveId(solved, 'transfer_ins_id')
        this.datas = solved
        this.sortedDatas = deepCopy(this.datas)
        this.total = res.data.total

        this.updateGridHeight()
        this.makeHeaderChecked(false)
        this.$nextTick(() => {
            this.clearSelected()
        })
        this.updateStripe()
        this.$refs.table.forceUpdate()
      })
    },
    changeGrid(name) {
      service.getShowColumn(name).then(res => {
        let data = res.data.data
        this.showColumns = data ? data.split(',') : []
        this.gridNameDetail = name
      })
    },
    dataChange(i) {
      if (i) {
          this.pageNo = i;
          this.pageInfo.pageNo = i;
          this.current = i;
      }
      this.getDatas()
    },
    openLayout() {
      this.showLayout = true
    },
    handleRowClick(data) {
      /** 是否点击的是同一行 */
      let isSameRow = false
      if (this.latestRowData && this.latestRowData.transfer_ins_id === data.transfer_ins_id) {
        isSameRow = true
      }
      /** 从数据中找到该行，进行 checkbox 切换 */
      let find = service.findDataById(this.sortedDatas, data.transfer_ins_id)
      if (!isSameRow) {
        find.checked = true
      } else {
        find.checked = !find.checked
      }
      this.makeRowChecked(find, find.checked)
      this.makeHeaderChecked(false)
      this.latestRowData = find

      /** 设置选中数据集合和最近选中数据 */
      if (find.checked) {
        this.onRowClick && this.onRowClick([data])
        this.selectedDatas = [find]
      } else {
        this.onRowClick && this.onRowClick(null)
        this.selectedDatas = []
      }

      this.updateGridHeight()
      this.updateSelectionBackground()
    },
    handleRowDblClick(data) {
      this.makeRowChecked(data, true)

      this.onRowClick && this.onRowClick([data])
      this.onRowDbClick && this.onRowDbClick(data)
    },
    handleExpand(item, status) {
      let id = item.transfer_ins_id
      let find = service.findDataById(this.sortedDatas, id)
      find.expand = status

      // 对 + - 处理
      let index = service.getFlatIndexById(this.sortedDatas, id)
      this.solveIcons(id, index, status)

      let trDoms = this.$refs.table.$el.querySelectorAll('tr')
      for (let dom of trDoms) {
        if (getStyle(dom, 'display') === 'block') {
          setStyle(dom, 'display', 'table-row')
        }
      }
      this.updateSelectionBackground()
    },
    handleSelectChange(selections, index) {
       for (let data of this.selectedDatas) {
        this.$refs.table.selectRow(data.id, false)
      }

      this.selectedDatas = selections
      this.updateSelectionBackground()
    },
    handleSelectAll(checked) {
      let selections = this.getSelection()
      if (checked) {
        this.selectedDatas = selections
        this.updateSelectionBackground()
      } else {
        this.clearSelected()
        this.selectedDatas = []
      }
    },
    updateSelectionBackground(selected = true) {
      this.$nextTick(() => {
        let selections = this.getSelection()
        for (let data of selections) {
          this.$refs.table.selectRow(data.id, selected)
        }
      })
    },
    makeHeaderChecked(checked) {
      let wrapperDom = this.$refs.table.$el.querySelector('.h-editgird-header .h-ui-transfer_ins_id .h-checkbox-wrapper')
      let inputDom = this.$refs.table.$el.querySelector('.h-editgird-header .h-ui-transfer_ins_id .h-checkbox')
      if (checked) {
        addClass(wrapperDom, 'h-checkbox-wrapper-checked')
        addClass(inputDom, 'h-checkbox-checked')
      } else {
        removeClass(wrapperDom, 'h-checkbox-wrapper-checked')
        removeClass(inputDom, 'h-checkbox-checked')
      }
    },
    makeRowChecked(rowData, checked) {
      this.$refs.table.checkedRow(rowData.id, checked)
    },
    makeRowExpanded(rowData, expanded) {
      this.$refs.table.expandRow(rowData.id, expanded)
    },
    getSelection() {
      // let selections = deepCopy([...this.$refs.table.getSelection()])
      // let selections = deepCopy(this.solveSelectChangeBug())
      // return selections
      return deepCopy(this.$refs.table.getSelection())
    },
    /** 对 + - 处理 */
    solveIcons(id, index, status) {
      let rowElems = this.$refs.table.$el.querySelector('.h-editgird-tbody').children
      rowElems = [...rowElems].filter(e => e.className.indexOf('not-child') >= 0)
      let rowElem = rowElems[index]
      let targetElem = rowElem.querySelector('.h-ui-transfer_ins_id .h-icon-play_fill')
      if (targetElem) {
        this.$nextTick(() => {
          if (status) {
            removeClass(targetElem, 'my-add')
            addClass(targetElem, 'my-minus')
          } else {
            removeClass(targetElem, 'my-minus')
            addClass(targetElem, 'my-add')
          }
        })
      }

      this.solveRowClassNameBugs()
    },
    sortAtField(status, field, isNum = false) {
      if (status === 'none') {
        this.sortedDatas = deepCopy(this.datas)
      } else {
        this.sortedDatas = service.sort(this.sortedDatas, field, status, isNum)
      }
      this.sortingField = field
      this.latestRowData = null
    },
    updateStripe(force = false) {
      this.$nextTick(() => {
        let bodyElm = this.$el.querySelector('.tree-grid .h-editgird-wrapper .h-editgird-body .h-editgird-tbody')
        if (this.stripeHeight === 0 || force) {
          if (this.datas.length > 0) {
            this.stripeHeight = 60 // bodyElm.clientHeight / this.datas.length * 2
          } else {
            this.stripeHeight = 60
          }
        }
        bodyElm.style.backgroundSize = `100% ${this.stripeHeight}px`
      })
    },
    handleResize() {
      this.$refs.table.handleResize()
    },
    solveRowClassNameBugs() {
      this.$nextTick(() => {
        let doms = this.$refs.table.$el.querySelectorAll('tr.not-child')

        for (let i = 0; i < doms.length; i++) {
          let dom = doms[i]
          let idDom = dom.querySelector('.h-ui-transfer_ins_id').children[1]
          let id = idDom.innerHTML

          let data = service.findDataById(this.sortedDatas, id)
          let className = this.rowClassName(data, i)
          for (let childDom of doms[i].children) {
            addClass(childDom, className)
          }
        }
      })
    },
    solveHeaderCheckboxBugs() {
      let dom = this.$el.querySelector('.h-editgird-header .h-ui-transfer_ins_id .h-checkbox-input')
      dom && (dom.disabled = this.sortedDatas.length === 0)
    }
  },
  components: {

  }
}
</script>

<style lang="less" rel="stylesheet/less">
  @import "./style.less";
</style>



