<template>
    <div class="full-h" >
        <h-row>
            <h-col>
                <el-form style="font-size: 12px;display:flex;" :inline="true" size="mini" :model="queryForm" class="demo-form-inline">
                    <el-form-item label="交收日期" style="flex-shrink:0">
                        <el-date-picker v-model="finalQueryDate" type="date" placeholder="选择日期"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="产品名称" style="flex-shrink:0">
                        <el-select v-model="finalFundIds" filterable multiple clearable collapse-tags>
                            <el-option v-for="item in fundIdList" :key="item.code" :label="item.text" :value="item.code"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="日终对账" style="flex-shrink:0">
                        <el-select v-model="queryForm.finalCkaccStatus" clearable>
                            <el-option v-for="item in checkIsOrNo" :key="item.code" :label="item.text" :value="item.code"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item style="flex-shrink:0">
                        <el-button type="primary" @click="refresh">查询</el-button>
                        <el-button type="ghost" @click="finalPass">对账</el-button>
                        <el-button type="ghost" @click="finalRemark">备注</el-button>
                        <el-button type="primary" @click="cashInit">日初始化</el-button>
                    </el-form-item>
                </el-form>
            </h-col>
        </h-row>
        <h-datagrid
                :height="autoHeight" v-model="finalTemple"
                stripe hasPage highlight-row :auto-load="false"
                :pageSize="100" :pageSizeOpts="pageSizeOpts"
                :columns="allocFinalColumns" :bindForm="queryForm"
                url="/cashcheck/daily/allocFinal.json" ref="datagrid">
        </h-datagrid>
        <div class="wrapper mt10" id="inner-on-transfer-approve">

            <h-row class-name="border-dashed-top pt10 mt10">
                <header class="headline">
                    <h-icon name="pause" ></h-icon>
                    <span class="title">对账进度</span>
                </header>
                <h-col span="8" class="inner">
                    <div class="item">
                        <div class="label" style="font-size: 14px">共{{allocProcess.fundCount}}个产品</div>
                    </div>
                </h-col>
                <h-col span="8" class="inner">
                    <div class="item">
                        <div class="label" style="font-size: 16px;color: #DA0001">{{allocProcess.unfinishedCount}}个未开始</div>
                    </div>
                </h-col>
                <h-col span="8" class="inner">
                    <div class="item">
                        <div class="label" style="font-size: 16px;color: #3BB300">{{allocProcess.completedCount}}个对账完成</div>
                    </div>
                </h-col>
            </h-row>
        </div>
        <h-msg-box class="customMsgBox" v-model="showAllocFinalDialog" width="300" :mask-closable=false>
            <p slot="header">
                <span>备注</span>
            </p>
            <h-form :model="allocFinalInfo" :label-width="60" ref="allocFinalForm" >
                <h-form-item label="产品名称" prop="fund_name">
                    <h-input disabled type="text" v-model="allocFinalInfo.fund_name"></h-input>
                </h-form-item>
                <h-form-item label="差额" prop="final_diff_balance">
                    <h-typefield disabled type="money" suffixNum="2" divided
                                 v-model="allocFinalInfo.final_diff_balance">
                    </h-typefield>
                </h-form-item>
                <h-form-item label="备注" prop="final_check_acc_remark">
                    <h-input type="text" v-model="allocFinalInfo.final_check_acc_remark"></h-input>
                </h-form-item>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="allocFinalCancel">取消</h-button>
                <h-button type="primary" @click="saveRemark">确认</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import HDatagrid from "../../../../components/HDatagrid";

    import {
        showText,
        formatMoney,
        getDictList,
        getFundInfo,
        dailyInit,
        getNextDay,
        parseDateString,
        parseDateStr
    } from "@/api/utils";

    import {
        finalAccConfirm,
        updateFinalAccRemark,
        queryAllocFinal,
        checkData
    } from "@/api/cashcheck/cashCheck"

    import Cookies from 'js-cookie';

    export default {
        name: "balAccFinalTemple",
        components: {HDatagrid},
        data(){
            return{
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                fundIdList:[],
                checkIsOrNo:[],
                transferTypeList:[],
                checkAccResultList:[],
                finalTemple:[],
                sysDate:Cookies.get('CurrSysDate'),
                showAllocFinalDialog:false,
                allocFinalColumns:[
                    {
                        title: "交收日期",
                        key: "curr_date",
                        align: "center",
                        ellipsis: true
                    },{
                        title: "产品编号",
                        key: "fund_id",
                        align: "center",
                        ellipsis: true,
                        hiddenCol:true
                    },{
                        title: "产品代码",
                        key: "fund_code",
                        align: "center",
                        ellipsis: true
                    },{
                        title: "产品名称",
                        key: "fund_name",
                        align: "center",
                        ellipsis: true
                    },{
                        title: "划款模式",
                        key: "transfer_type",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let transferType = showText(rowData.transfer_type,this.transferTypeList)
                            return h("span",{},transferType)
                        }
                    },{
                        title: "日终账套余额",
                        key: "account_final_balance",
                        align: 'center',
                        ellipsis: true,
                        width:120,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let accountFinalBalance = formatMoney(rowData.account_final_balance)
                            return h("span",{style:{fontWeight:'bold'}},accountFinalBalance)
                        }
                    },{
                        title: "日终投资系统可用",
                        key: "o32_final_balance",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let o32FinalBalance = formatMoney(rowData.o32_final_balance)
                            return h("span",{style:{fontWeight:'bold'}},o32FinalBalance)
                        }
                    },{
                        title: "投资系统当前资金冻结金额",
                        key: "o32_final_frozen_balance",
                        align: 'center',
                        ellipsis: true,
                        width:130,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let o32FinalFrozenBalance = formatMoney(rowData.o32_final_frozen_balance)
                            return h("span",{style:{fontWeight:'bold'}},o32FinalFrozenBalance)
                        }
                    },{
                        title: "差额",
                        key: "final_diff_balance",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let enDiffBalance = formatMoney(rowData.final_diff_balance)
                            return h("span",{style:{fontWeight:'bold'}},enDiffBalance)
                        }
                    },{
                        title: "对账结果",
                        key: "final_check_acc_result",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) => {
                            const rowData = params.row;
                            let finalChckAccResult = showText(rowData.final_check_acc_result,this.checkAccResultList)
                            return h("span",{},finalChckAccResult)
                        }
                    },{
                        title: "对账状态",
                        key: "final_check_acc_status",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) => {
                            const rowData = params.row;
                            let finalChckAccStatus = ''
                            if(rowData.final_check_acc_status == '0')
                                finalChckAccStatus = '未对账'
                            else if(rowData.final_check_acc_status == '1')
                                finalChckAccStatus = '已对账'
                            return h("span",{},finalChckAccStatus)
                        }
                    },{
                        title: "未交收笔数",
                        key: "l_unsettle_count",
                        align: 'center',
                        ellipsis: true
                    },{
                        title: "备注",
                        key: "final_check_acc_remark",
                        align: 'center',
                        ellipsis: true
                    }
                ],
                queryForm:{
                    finalQueryDate:Cookies.get('CurrSysDate'),
                    finalCkaccStatus:'',
                    finalFundIds:''
                },
                allocFinalInfo:{
                    fund_id:'',
                    curr_date:'',
                    fund_code:'',
                    fund_name:'',
                    final_diff_balance:'',
                    final_check_acc_remark:''
                },
                allocProcess:{
                    fundCount:0,
                    unfinishedCount:0,
                    completedCount:0
                }
            }
        },
        watch:{
            showAllocFinalDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
            finalQueryDate:{
                get:function () {
                    if(this.queryForm.finalQueryDate)
                        return parseDateString(this.queryForm.finalQueryDate)
                    else
                        return parseDateString(this.sysDate)
                },
                set:function (newValue) {
                    this.queryForm.finalQueryDate = parseDateStr(newValue)
                }
            },
            finalFundIds:{
                get:function () {
                    if(this.queryForm.finalFundIds)
                        return this.queryForm.finalFundIds.split(",")
                    else
                        return []
                },
                set:function (newValue) {
                    this.queryForm.finalFundIds = newValue.join(",")
                }
            }
        },
        activated(){
            getFundInfo('/common/fundNamelist.json?type='+'id',{}).then(res => this.fundIdList = res.data)
        },
        mounted(){
            getDictList(90529).then(res => this.checkAccResultList = res.data)
            getDictList(610001).then(res => this.checkIsOrNo = res.data)
            getDictList(90524).then(res => this.transferTypeList = res.data)
            this.refresh();
        },
        methods:{
            refresh(){
                queryAllocFinal(this.queryForm.finalQueryDate,this.queryForm.finalCkaccStatus,this.queryForm.finalFundIds,this.$refs.datagrid.getPageNo(),this.$refs.datagrid.getPageSize()).then(
                    res =>{
                        this.finalTemple = res.data.list;
                        this.loadSuccess(this.finalTemple);
                        this.$refs.datagrid.dataChange(1);
                    }
                )
            },
            loadSuccess(list){
                let data = list;
                if(data.length > 0){
                    let completeCount=0;
                    for (let i = 0; i < data.length; i++) {
                        if(data[i].final_check_acc_status=='1'){
                            completeCount++;
                        }
                    }
                    let unCompleteCount=data.length-completeCount;
                    this.allocProcess = {
                        fundCount:data.length,
                        unfinishedCount:unCompleteCount,
                        completedCount:completeCount
                    }
                }else{
                    this.allocProcess = {
                        fundCount:0,
                        unfinishedCount:0,
                        completedCount:0
                    }
                }
            },
            resizeTable(height){
                this.autoHeight = height
            },

            cashInit(){
                let nextday;
                getNextDay().then(res => {
                    console.log("here")
                    if (res && res.status == 200) {
                        if (res.data.success) {
                            if (res.data.data == 0){
                                this.openMask();
                                this.$alert('未获取到下一交易日，请先维护交易日历!', '错误', {
                                    confirmButtonText: '确定',
                                    type: 'error',
                                    callback: action => {
                                        this.closeMask();
                                    }
                                }).then(() => {
                                    this.closeMask();
                                })
                            } else {
                                nextday = parseDateString(res.data.data);
                                this.$confirm("下一交易日为"+nextday+"，请确认是否进行日初始化?", '提示', {
                                    confirmButtonText: '确定',
                                    cancelButtonText: '取消',
                                    type: 'warning'
                                }).then(() => {
                                    checkData().then(res => {
                                        debugger
                                        if (!res.data.success) {
                                            this.$confirm(res.data.msg, '提示', {
                                                confirmButtonText: '确定',
                                                cancelButtonText: '取消',
                                                type: 'warning'
                                            }).then(() => {
                                                dailyInit().then(res => {
                                                    if (res && res.status == 200) {
                                                        if (res.data.success) {
                                                            this.openMask();
                                                            this.$alert(res.data.msg, '成功', {
                                                                confirmButtonText: '确定',
                                                                type: 'success',
                                                                callback: action => {
                                                                    Cookies.set('CurrSysDate', res.data.data);
                                                                    location.reload();
                                                                    this.closeMask();
                                                                }
                                                            }).then(() => {
                                                                this.closeMask();
                                                            })
                                                        } else {
                                                            this.openMask();
                                                            this.$alert(res.data.msg, '错误', {
                                                                confirmButtonText: '确定',
                                                                type: 'error',
                                                                callback: action => {
                                                                    this.closeMask();
                                                                }
                                                            }).then(() => {
                                                                this.closeMask();
                                                            })
                                                        }
                                                    } else {
                                                        this.openMask();
                                                        this.$alert('日初始化失败', '错误', {
                                                            confirmButtonText: '确定',
                                                            type: 'error',
                                                            callback: action => {
                                                                this.closeMask();
                                                            }
                                                        }).then(() => {
                                                            this.closeMask();
                                                        })
                                                    }
                                                })
                                            });
                                        }else{
                                            dailyInit().then(res => {
                                                if (res && res.status == 200) {
                                                    if (res.data.success) {
                                                        this.openMask();
                                                        this.$alert(res.data.msg, '成功', {
                                                            confirmButtonText: '确定',
                                                            type: 'success',
                                                            callback: action => {
                                                                Cookies.set('CurrSysDate', res.data.data);
                                                                location.reload();
                                                                this.closeMask();
                                                            }
                                                        }).then(() => {
                                                            this.closeMask();
                                                        })
                                                    } else {
                                                        this.openMask();
                                                        this.$alert(res.data.msg, '错误', {
                                                            confirmButtonText: '确定',
                                                            type: 'error',
                                                            callback: action => {
                                                                this.closeMask();
                                                            }
                                                        }).then(() => {
                                                            this.closeMask();
                                                        })
                                                    }
                                                } else {
                                                    this.openMask();
                                                    this.$alert('日初始化失败', '错误', {
                                                        confirmButtonText: '确定',
                                                        type: 'error',
                                                        callback: action => {
                                                            this.closeMask();
                                                        }
                                                    }).then(() => {
                                                        this.closeMask();
                                                    })
                                                }
                                            })
                                        }

                                    });
                                });
                            }
                        } else {
                            this.openMask();
                            this.$alert('未获取到下一交易日，请先维护交易日历', '错误', {
                                confirmButtonText: '确定',
                                type: 'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                        }
                    } else {
                        this.openMask();
                        this.$alert('未获取到下一交易日，请先维护交易日历', '错误', {
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    }
                })

            },
            finalPass(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('当前不存在日终对账数据，不允许日终余额核对','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                if(this.sysDate != this.queryForm.finalQueryDate){
                    this.openMask();
                    this.$alert('非系统当日，不允许日终余额核对','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                finalAccConfirm().then(res =>{
                    if(res && res.status == 200){
                        if(res.data.errorNo == '0'){
                            this.openMask();
                            this.$alert('操作成功','成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                            this.refresh();
                        }else{
                            this.openMask();
                            this.$alert(res.data.errorInfo,'错误',{
                                confirmButtonText:'确定',
                                type:'error',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                        }
                    }else{
                        this.openMask();
                        this.$alert('日终余额对账操作失败','错误',{
                            confirmButtonText:'确定',
                            type:'error',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    }
                })
            },
            finalRemark(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                if(selects[0].curr_date < parseDateStr(this.sysDate)){
                    this.openMask();
                    this.$alert('只能修改当日数据的备注!','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                this.allocFinalInfo = selects[0];
                this.$refs.allocFinalForm.resetFields();
                this.showAllocFinalDialog= true;
            },
            allocFinalCancel(){
                this.showAllocFinalDialog = false;
            },
            saveRemark(){
                let params = this.allocFinalInfo.fund_id +','+this.allocFinalInfo.curr_date+','+this.allocFinalInfo.final_check_acc_remark;
                updateFinalAccRemark(params).then(res => {
                    if (res.data.success) {
                        this.$alert('操作成功', '成功', {
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                                this.showAllocFinalDialog = false;
                            }
                        }).then(() => {
                            this.showAllocFinalDialog = false;
                        })
                        this.refresh()
                    } else {
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        })
                    }
                })
            }
        }
    }
</script>

<style scoped>

</style>


