<template>
<div class="transfer-settle full-h">
    <full-screen-loading ref="screenLoading"></full-screen-loading>
    <h-split v-model="split" min="100" max="800">
        <div span="4" slot="left" class="full-h">
            <fund-tree :onFundClick="fundClick" :showAll="true" :showToDo="true" :query-url="'/common/fundListWithCount.json'"
                       :is-settle="true" :pageType="pageType" ref="treeFund"></fund-tree>
        </div>
        <div slot="right" span="20" class="full-h" style="padding-left: 10px" id="full-h-wrapper-on-transfer-settle">
            <div>
                <h-tabs size="small" v-model="queryForm.businClass" @on-click="businClassChanged">
                    <h-tab-pane v-for="(item, index) in businGroup" :key="index" :name = "item.cop_busin_class" :label="item.dic_name + '('+ item.count + ')'"></h-tab-pane>
                </h-tabs>
            </div>

            <div class="btn-ctrls">
                <div>
                    <h-button-group v-if="pageType==='3'">
                        <h-button icon="forward" @click="doAction('sendTransfer','')">发送指令</h-button>
                        <el-dropdown @command="handleTransferCommand" >
                            <div class="el-dropdown-link"  >
                                <i class="el-icon-arrow-down el-icon--right"></i>
                            </div>
                            <el-dropdown-menu slot="dropdown" >
                                <el-dropdown-item v-show ="item.code == '3' || item.code == '2'" v-for="item in transferTypeList" :command="item.code"
                                                :key="item.code">
                                    {{item.text}}
                                </el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </h-button-group>
                    <h-button-group v-if="queryForm.businClass!=='21'">
                        <h-button icon="share1" @click="doAction('sendAttachment','')">发送附件</h-button>
                        <el-dropdown @command="handleFileCommand" >
                            <div  class="el-dropdown-link"  >
                                <i class="el-icon-arrow-down el-icon--right"> </i>
                            </div>
                            <el-dropdown-menu slot="dropdown" >
                            <el-dropdown-item v-show ="item.code == '3' || item.code == '2'" v-for="item in transferTypeList" :command="item.code"
                                            :key="item.code">
                                {{item.text}}
                            </el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                    </h-button-group>
                    <h-button-group>
                        <h-button icon="right" @click="transferSuccess">确认划款成功</h-button>
                        <h-button icon="success" @click="settleSuccess(3)" v-if="pageType==='4'">交收成功</h-button>
                        <h-button icon="undo" @click="handleTransfer(9)">驳回</h-button>
                        <h-button icon="trash" @click="handleTransfer(7)">作废</h-button>
                        <h-button @click="delayTransfer" v-if="pageType==='3'">延期交收</h-button>
                        <h-button icon="t-b-download" @click="batchDown" v-if="pageType==='4'">批量下载划款单</h-button>
                        <h-button @click="doAction('queryTransfer','')" v-if="pageType==='4'">查询指令结果</h-button>
                        <h-button icon="t-b-tips" @click="cancelTransfer(13)" v-if="pageType==='4'">撤销指令</h-button>
                        <el-dropdown @command="handleCommand" v-if="pageType==='4'">
                            <el-button type="ghost" size="mini">
                                ...
                            </el-button>
                            <el-dropdown-menu slot="dropdown">
                                <!--<el-dropdown-item command="100">指令结果查询</el-dropdown-item>
                                <el-dropdown-item command="6">延期交收</el-dropdown-item>
                                <el-dropdown-item command="7">作废</el-dropdown-item>-->
                                <el-dropdown-item command="6">延期交收</el-dropdown-item>
                                <el-dropdown-item command="4">交收失败</el-dropdown-item>
                                <el-dropdown-item command="15">强制交收成功</el-dropdown-item>
                                <el-dropdown-item command="16">强制交收失败</el-dropdown-item>
                                <el-dropdown-item command="19">强制撤销指令</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </h-button-group>
                </div>
                <!-- <h-button-group style = "float: right; border-right:solid 0px ;color:transparent;"> -->
                <h-button-group class="btn-layout">
                    <h-button  @click="openLayout"><h-icon class="iconfont icon-ziduanxuanze1"></h-icon> &nbsp;布局</h-button>
                </h-button-group>
            </div>

