                                <div class="value fontWeightBold">{{formatMoney(trustAcctList[0].ACCOUNT_CUR_BALANCE)}}</div>
                            </div>
                        </h-col>
                        <h-col span="8" class="inner-t h-auto" v-if="acctBalanceList!=null&&acctBalanceList.length>0">
                            <div class="item">
