                                <div class="value fontWeightBold" style="color:red" v-if="businCashList[0].AMOUNT<0">{{formatMoney(businCashList[0].AMOUNT)}}</div>
                                <div class="value fontWeightBold" v-else>{{formatMoney(businCashList[0].AMOUNT)}}</div>
                            </div>
                        </h-col>
                        <h-col span="8" class="inner-t h-auto">
