                                <div class="value fontWeightBold" style="color:red" v-if="businCashList[1].AMOUNT<0">{{formatMoney(businCashList[1].AMOUNT)}}</div>
                                <div class="value fontWeightBold" v-else>{{formatMoney(businCashList[1].AMOUNT)}}</div>
