                                <div class="label flex-grow" :title="item.BANK_ACCOUNT_NAME" style="max-width: 30vh; text-overflow: ellipsis; overflow: hidden; white-space: nowrap;">{{item.BANK_ACCOUNT_NAME}}</div>
                                <div class="value fontWeightBold">{{formatMoney(item.ACCOUNT_CUR_BALANCE)}}</div>
