                                <div class="value fontWeightBold">{{formatMoney(item.AMOUNT)}}</div>
                            </div>
                        </h-col>
                    </template>
                </h-row>
                <h-row v-if="false /* item.transfer_ins_id */" class-name="border-dashed-top pt10 mt10">
