                            <div class="label">业务标志</div>
                            <div class="value" :title="showText(item.cop_busin_flag,businFlagList)">{{showText(item.cop_busin_flag,businFlagList)}}</div>
                        </div>
                        <div class="item" v-if="item.settle_balance">
