                            <div class="label">划款事由</div>
                            <div class="value" :title="item.transfer_purpose">{{item.transfer_purpose}}</div>
                        </div>
                        <div class="item" v-if="item.transfer_postscript && item.transfer_postscript.trim()">
                            <div class="label">划款附言</div>
                            <div class="value" :title="item.transfer_postscript">{{item.transfer_postscript}}</div>
                        </div>
                        <div class="item" v-if="item.transfer_remark && item.transfer_remark.trim()">
                            <div class="label">划款备注</div>
                            <div class="value" :title="item.transfer_remark">{{item.transfer_remark}}</div>
                        </div>
