              :footerHide="true" v-model="showDetailBox">
        <h-table stripe border size="small" :columns='childrenDetailColumns' :data='childTrans'></h-table>
    </h-msgBox>-->
    <!-- 指令新增/修改/详情弹出窗 -->
    <h-msg-box class="customMsgBox" v-model="showDialog" width="1040" top="24" :mask-closable="false" :footerHide="true" scrollable>
        <p slot="header">
            <span v-if="actionType=='add'">新增指令</span>
            <span v-if="actionType=='modify'">修改指令</span>
            <span v-if="actionType=='detail'">指令详情</span>
        </p>

        <div class="block-in-box border-dashed-bottom" style="border-color: #d3d3d3;">
            <header class="headline pl5">
                <i class="icon-l"></i>
                <span class="title">业务类型</span>
            </header>
            <h-form :model="transferInfo" :label-width="100" ref="transForm0" cols="3">
                <h-form-item required label="产品名称" prop="fund_id">
                    <h-select :disabled="actionType=='detail'" v-model="transferInfo.fund_id" filterable>
                        <h-option v-for="item in fundList" :key="item.code" :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>

