                    <el-cascader :disabled="actionType=='detail'" style="width: 100%" size="mini" v-model="businClassFlag" :options="businClassTree"></el-cascader>
                </h-form-item>

                <h-form-item required label="业务标志" v-if="queryForm.businClass!=''" prop="cop_busin_flag">
                    <h-select :disabled="actionType=='detail'"
                              v-model="transferInfo.cop_busin_flag" filterable>
                        <h-option v-for="(item,index) in copBusinFlagList" :key="index" :label="item.label" :value="item.value">
                        </h-option>
                    </h-select>
                </h-form-item>

                <h-form-item required v-if="showClearAgent" label="清算机构" prop="clear_agent_code_cn" style="height: 35.77px;">
                    <h-select :disabled="actionType=='detail'" v-model="transferInfo.clear_agent_code_cn">
                        <h-option v-for="item in agentCodeList" :key="item.code" :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>

                <h-form-item label="投资类型" prop="invest_type" style="height: 35.77px;">
                    <h-select v-model="transferInfo.invest_type" :disabled="actionType=='detail'" filterable>
                        <h-option v-for="(item,index) in investTypeList" :key="index"
                                  :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>

                <h-form-item v-if="showAgency" label="销售机构"
