                    <el-select size="small" style="width: 100%" :disabled="actionType=='detail'" v-model="transferInfo.agency_code" >
                        <el-option v-for="(item,index) in agencyList" :key="index"
                                  :label="item.text" :value="item.code">
                        </el-option>
                    </el-select>
                </h-form-item>

                <h-form-item v-if="showRival" label="对手方"
                             prop="rival_code">
                    <h-input  disabled v-if="actionType!='add'" type="text"
                              v-model="transferInfo.rival_name">
                    </h-input>
                </h-form-item>

