                    <h-select disabled v-model="transferInfo.market_no" filterable>
                        <h-option v-for="item in marketList" :key="item.code"
                                  :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>

            </h-form>
            <h-form v-for="(item, index) in seatList" :key="index" :label-width="100" cols="3" :model="item" v-if="transferInfo.cop_busin_class=='22'&&transferInfo.cop_busin_flag=='20061'" ref="seatListForm">
                <h-row>
                    <h-col span="8">
