            }
        });
    },
    mounted() {
        getDictList('businFlag').then(res => this.businFlagList = res.data);
        getDictList(90528).then(res => this.dataSourceList = res.data);
        getDictList(90522).then(res => this.transferFlagList = res.data);
        getDictList(90537).then(res => this.transferStatusList = res.data);
        getDictList(90523).then(res => this.businClassList = res.data);
        getDictList(90524).then(res => this.transferTypeList = res.data);
        getDictList(90571).then(res => this.transferFundStatusList = res.data);
        getDictList(90426).then(res => this.sendStatusList = this.sendStatusList .concat(res.data));
        getCityTree().then(res=>this.cityTree = res.data)
        getDictList(610400).then(res => this.marketList = res.data);
        getDictList(91064).then(res => this.investTypeList = res.data);
        getDictList(610406).then(res => {
            let tempList = res.data;
            tempList.forEach(item => {
                if (item.code == "CNY"){
                    item.text = "元";
                }
            })
            this.currencyList = tempList;
        });
        getBankList().then(res => this.bankList = res.data);
        getBankList("1").then(res => this.bankInfoList = res.data);
        getBusinClassTree(true).then(res => this.businClassTree = res.data);
        operatorList().then(res => this.operatorList = res.data);
        getAgencyList('').then(res => this.agencyList = res.data);

        getSpecialApproveWorkerList().then(res => {
            if (res.data.rows && res.data.rows.length > 0) {
                this.specialApproveWorkerList = res.data.rows;
                this.specialApproveWorkerList.unshift({
                    begin_date: "0",
                    end_date: "0",
                    operator_name: "全部",
                    operator_no: -1,
                    workgroup_id: 0
                });
            } else {
                this.specialApproveWorkerList = [];
            }
        });

        this.re_layout("full-h-wrapper-on-transfer-settle", "inner-on-transfer-settle", 76);
        this.fixElPopoverBug()

        SSObservable.subscribe('ToggleSidebar', (expand) => {
            console.log('settleToggleSidebar')
            this.$refs.datagrid.handleResize()
        })
    },
    watch: {
        'transferInfo.cop_busin_flag'(val) {
            switch (this.transferInfo.cop_busin_class) {
                case '1':
                    this.showRival = false
                    this.showReportCode = val == 20020;
                    if(val == 20117 || val == 10071 || val == 30008){
                        this.showClearAgent = false;
                    }else if(val == 10070 || val == 20116 || val == 30110){
                        this.showClearAgent = false;
                    }else if(!this.showReportCode){
                        this.showClearAgent = true;
                    }else if(this.showReportCode){
                        this.showClearAgent = false;
                    }
                    break;
                case '4,5,6':
                case '4':
                case '5':
                case '6':
                    this.transferInfo.clear_agent_code_cn='';
                    this.showRival = val == 10078 || val == 11010 //解除质押
                    this.showReportCode = false;
                    this.showClearAgent = false;
                    break;
                case '9':
                    this.showRival = val == 10023 || val == 20018 || val == 10074 || val == 10075 || val == 20130 || val == 20019;
                    this.showReportCode = val == 20017 || val == 20047 || val == 20049 || val == 20126 || val == 20128 || val == 20050 || val == 20051
                        || val == 20139 || val == 20140 || val == 10053 || val == 10054 || val == 10098 || val == 10099;
                    this.showClearAgent = false;
                    break;
                case '7' :
                    this.showRival = !this.showAgency;
                    this.showReportCode = false;
                    this.showClearAgent = false;
                    break;
                case '20':
                    this.showRival = true;
                    this.showReportCode = false;
                    this.showClearAgent = false;
                    break;
                default:
                    this.showRival = false;
                    this.showReportCode = false;
                    this.showClearAgent = false;
                    break;

            }
        },
        showDialog: function (show) {
            if (!show) {
                this.closeMask();
                this.showRival = false;
                this.outAcctList = []
                this.inAcctList = []
                this.fileList = [];
                this.transferInfo = {
                    require_pay_date: null,
                    fund_id: this.queryForm.fundIds,
                    sq_holder_account: '',
                    zz_holder_account: '',
                    transfer_purpose: '',
                    transfer_remark: '',
                    transfer_postscript: '',
                    clear_agent_code_cn: null,
                    cop_busin_class: null,
                    settle_balance: '',
                    rival_code:'',
                    invest_type:'',
                };
                //重置表单
                this.$refs.transForm0.resetFields();
                this.$refs.transForm.resetFields();
                this.$refs.outAcctForm.resetFields();
                this.$refs.inAcctForm.resetFields();
                this.$refs.selfAcctForm.resetFields();
            } else {
