        },
        /*showDetailBox:function (show) {
            if(show){
                this.openMask();
            }else{
                this.closeMask();
            }
        }*/
    },
    computed: {
        showAcctCity(){
            let isShow = this.inAcct.province_code;
            if(isShow){
                this.accountStyle.height = '235px';
                this.trusteeStyle.height = '235px';
            }else{
                this.accountStyle.height = '200px';
                this.trusteeStyle.height = '200px';
            }
            return isShow;
        },
        provinceCity: {
            get: function () {
                return [this.inAcct.province_code, this.inAcct.city_no]
            },
            set: function (newValue) {
                this.inAcct.province_code = newValue[0]
                this.inAcct.city_no = newValue[1]
            }
        },
        upBalance() {
            return smalltoBIG(this.transferInfo.settle_balance);
        },
        businClassFlag: {
            get: function () {
                if (this.showTemplateList) {
                    return [this.queryTemp.cop_busin_class, this.queryTemp.cop_busin_flag]
                }
                let businClass = this.transferInfo.cop_busin_class
                if (this.transferInfo.cop_busin_class == '4'
                    || this.transferInfo.cop_busin_class == '5'
                    || this.transferInfo.cop_busin_class == '6') {
                    businClass = '4,5,6'
                }
                return [businClass, this.transferInfo.cop_busin_flag]
            },
            set: function (newValue) {
                if (this.showTemplateList) {
                    this.queryTemp.cop_busin_class = newValue[0]
                    this.queryTemp.cop_busin_flag = newValue[1]
                } else {
                    this.transferInfo.cop_busin_class = newValue[0]
                    this.transferInfo.cop_busin_flag = newValue[1]
                }

            }
        },
        payDate: {
            get: function () {
                if(this.transferInfo.require_pay_date != null){
                    let arr = this.transferInfo.require_pay_date.toString().split(" ");
                    let date = arr[0].replace(new RegExp("-","gm"),"");
                    let time = arr[1];
                    if(typeof(time) == "undefined"){
                        time=this.transferInfo.require_pay_time;
                    }else{
                        time=time.replace(new RegExp(":","gm"),"");
                    }
                    return parseDateString(date,time);
                }
                return parseDateString(this.transferInfo.require_pay_date)
            },
            set: function (newValue) {
                this.transferInfo.require_pay_date = parseDateStr(newValue,"yyyy-MM-dd hh:mm:ss")
            }
        },

        beginDate: {
            get: function () {
                return parseDateString(this.transferInfo.fee_begin_date)
            },
            set: function (newValue) {
                this.transferInfo.fee_begin_date = parseDateStr(newValue)
            }
        },
        endDate: {
            get: function () {
                return parseDateString(this.transferInfo.fee_end_date)
            },
            set: function (newValue) {
                this.transferInfo.fee_end_date = parseDateStr(newValue)
            }
        },
        gridName() {
            let gridName = "settleGrid";
            if (this.pageType === "3") {
                gridName = "settleSendGrid";//指令发送
            } else if (this.pageType === "4") {
                gridName = "settleGrid";//指令交收
            }
            return gridName;
        }
    },

    methods: {
        showText: showText,
        showCity:showCity,
        formatMoney: formatMoney,
        fundClick(data) {
            const orignGroup = [{
                cop_busin_class: '',
                dic_name: '全部',
                count: 0
            }]
            if(!data){
                this.businGroup = orignGroup
                this.queryForm.fundIds=''
                this.refreshTable();
                return
            }
            this.queryForm.fundIds = data.code;
            this.queryForm.showToday = this.$refs.treeFund.getQueryCondition().show_today_flag;
            this.fundList = this.$refs.treeFund.getFundList();
            if (this.queryForm.businClass == '') {
                this.$refs.datagrid.changeGrid(this.gridName);
            } else {
                let settleGridStr = this.gridName+"-"+this.queryForm.businClass;
                this.$refs.datagrid.changeGrid(settleGridStr.replace(/[,]/g,''));
            }
            getBusinClassGroup({
                fundId: data.code,
                showToday:this.$refs.treeFund.getQueryCondition().show_today_flag,
                isSettle: true,
                pageType: this.pageType
            }).then(res => {
                if (res.data.success && res.data.data.length > 0) {
                    this.businGroup = res.data.data
                }
            });
            if (data.code.indexOf(",")<0) {
                findAccAmountByFundId(data.code, Cookies.get('CurrSysDate')).then(res => {
                    this.acctBalanceList = []
                    this.trustAcctList = []
                    res.data.forEach(value => {
                        if (value.ACCOUNT_CLASS =='2'){
                            this.trustAcctList.push({
                                                        ACCOUNT_CLASS: value.ACCOUNT_CLASS,
                                                        BANK_ACCOUNT_NAME: value.BANK_ACCOUNT_NAME + '余额'
                                                                           + parseMinute(
                                                            value.INPUT_TIME),
                                                        ACCOUNT_CUR_BALANCE: value.ACCOUNT_CUR_BALANCE
                                                    });
                        }else {
                            this.acctBalanceList.push({
                                                          ACCOUNT_CLASS: value.ACCOUNT_CLASS,
                                                          BANK_ACCOUNT_NAME: value.BANK_ACCOUNT_NAME + '余额' + parseMinute(value.INPUT_TIME),
                                                          ACCOUNT_CUR_BALANCE: value.ACCOUNT_CUR_BALANCE
                                                      })
                        }
                    });
                });
                findFundCashByFundId(data.code, Cookies.get('CurrSysDate')).then(res => {
                    this.fundCashList = res.data;
                    this.getBusinCashList();
                });
            }

            this.refreshTable();
        },
        parseAcctClass(acctClass) {
            switch (acctClass) {
                case '2':
                    return '托管户'
                case '5':
                    return '上清DVP'
                case '6':
                    return '中债DVP'
                default:
                    return ''
            }

        },
        batchDown(selects){
            if (!selects || !selects.length) {
                //selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                selects = this.$refs.datagrid.getSelection();
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选中一条记录','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
            }
            let fileIds = []
            let transferInsIds = []
            let count = 0
            for (let i = 0 ;i < selects.length ;i++){
                if (selects[i].file_id){
                    fileIds.push(selects[i].file_id)
                } else {
                    count++
                    transferInsIds.push(selects[i].transfer_ins_id+'\r')
                }
            }
            if (fileIds.length > 0){
                for (let i = 0 ;i < fileIds.length ;i++){
                    this.downloadOrder(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=1&fileId=' + fileIds[i])
                }
            }
            if (transferInsIds.length > 0){
                this.openMask();
                this.$alert("有"+count+"条指令无对应划款单，其指令序号为："+transferInsIds, '提示', {
                    confirmButtonText: '确定',
                    type: 'warning',
                    callback: action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                })
            }
        },

        downloadOrder(url) {
            const iframe = document.createElement("iframe");
            iframe.style.display = "none";
            iframe.style.height = '0';
            iframe.src = url;
            document.body.appendChild(iframe);
            // 异步操作，1分钟之后删除
            setTimeout(()=>{
                iframe.remove();
            }, 60 * 1000);
        },

        rowClick(arr) {
            let selects = this.$refs.datagrid.getSelection();
            // let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
            if (selects.length ===0) {
                this.item = {};
                this.re_layout("full-h-wrapper-on-transfer-settle", "inner-on-transfer-settle", 108);
            }else{
                this.item = deepCopy(arr[0]);
                this.re_layout("full-h-wrapper-on-transfer-settle", "inner-on-transfer-settle", 76);
            }

            this.selectionTipVisible = selects.length > 0
            //this.changeChildColumns();
        },
        handleSelectChange(arr) {
            //let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
            let selects = this.$refs.datagrid.getSelection();
            if (selects.length === 1) {
                this.item = deepCopy(selects[0]);
                this.re_layout("full-h-wrapper-on-transfer-settle", "inner-on-transfer-settle", 76);
            } else {
                this.item = {};
                this.re_layout("full-h-wrapper-on-transfer-settle", "inner-on-transfer-settle", 108);
            }

            //this.changeChildColumns();
        },
        loadFiles(bill_name) {
            if (bill_name) {
                getFileList(bill_name,'2').then(res => {
                    if (res.data.success) {
                        res.data.data.forEach(file => this.fileList.push({
                            name: file.file_name,
                            response: {
                                data: file.file_id
                            }
                        }));
                    }
                    if (this.fileList.length>0)
                        this.transferInfo.bill_name = this.fileList.reduce((x, y) => x.response.data + ',' + y.response.data);
                })
            }
        },
        showDetail(value) {
            this.$refs.detailDialog.showTransferDetail(value)
        },
        businClassChanged() {
            if(this.queryForm.businClass == 0){
                this.queryForm.businClass = '';
            }
            this.getBusinCashList();
            if (this.queryForm.businClass == '') {
                this.$refs.datagrid.changeGrid(this.gridName);
            } else {
                let settleGridStr = this.gridName+"-"+this.queryForm.businClass;
                this.$refs.datagrid.changeGrid(settleGridStr.replace(/[,]/g,''));
            }


            // if (this.queryForm.businClass != '') {
            //     this.copBusinFlagList = this.businClassTree.filter(value => value.value == this.queryForm.businClass)[0].children
            // }

            // ||(&&)
            this.refreshTable();
            this.re_layout("full-h-wrapper-on-transfer-settle", "inner-on-transfer-settle", 76);
        },
        getBusinCashList() {
            switch (this.queryForm.businClass) {
                case "1":
                    this.businCashList = this.fundCashList.filter(x => x.CASHTYPEID == 4 || x.CASHTYPEID == 5 || x.CASHTYPEID == 9)
                    break;
                case "4,5,6":
                    this.businCashList = this.fundCashList.filter(x => x.CASHTYPEID == 6 || x.CASHTYPEID == 7);
                    break;
                case '7':
                    //开基
                    this.businCashList = this.fundCashList.filter(x => x.CASHTYPEID == 2)
                    break;
                case '8':
                    this.businCashList = this.fundCashList.filter(x => x.CASHTYPEID == 3)
                    //存款
                    break;
                case '9':
                    //网下申购
                    this.businCashList = this.fundCashList.filter(x => x.CASHTYPEID == 1)
                    break
                default:
                    this.businCashList = [];
                    break;
            }
        },
        refreshFundTree(){
            this.$refs.treeFund.handleOK();
        },
        refreshTable(noInit) {
            if (noInit) {
                this.$refs.datagrid.dataChange();
            } else {
                this.$refs.datagrid.dataChange(1);
            }
            this.item = {};

            this.re_layout("full-h-wrapper-on-transfer-settle", "inner-on-transfer-settle", 108);
        },
        handleCancel() {
            this.showDialog = false
        },
        cancelTransfer(flag){
            //let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
            let selects = this.$refs.datagrid.getSelection();
            if (selects.length != 1) {
                this.openMask();
                this.$alert('请先选中一条指令单独撤销','错误',{
                    confirmButtonText: '确定',
                    type: 'error',
                    callback:action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                })
                return
            }

            //过滤掉子指令
            selects = filterTransferChildren(selects, this);
            if (selects.length <= 0) {
                return;
            }

            let url = '/cashsettle/settle/cancelTransfer.json';
            const loading = this.$loading({
                lock: true,
                text: '处理中',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
            });
            handleTransfer(url, {
                transferList: selects,
                ignore: false,
                flowFlag: flag
            })
                .then(res => {
                    loading.close();
                    if (res.data.success) {
                        this.openMask();
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                        this.refreshFundTree()
                    } else if (res.data.code == "1120002" && flag === 19) {
                        this.openMask();
                        let warnInfo = "";
                        warnInfo = "确定已通过线下沟通撤销指令，并重置划款状态？";

                        this.$confirm(warnInfo, '提示', {
                            confirmButtonText: '继续',
                            cancelButtonText: '取消',
                            type: 'warning'
                        }).then(() => {
                            handleTransfer(url, {
                                transferList: selects,
                                ignore: true,
                                flowFlag: flag
                            }).then(res => {
                                if (res.data.success) {
                                    this.$alert(res.data.msg,'成功',{
                                        confirmButtonText: '确定',
                                        type: 'success',
                                        callback:action => {
                                            this.closeMask();
                                        }
                                    }).then(() => {
                                        this.closeMask();
                                    })
                                    this.refreshTable(true)
                                } else {
                                    this.$alert(res.data.msg,'错误',{
                                        confirmButtonText: '确定',
                                        type: 'error',
                                        callback:action => {
                                            this.closeMask();
                                        }
                                    }).then(() => {
                                        this.closeMask();
                                    })
                                }
                            });
                        }).catch(() => {
                            this.closeMask();
                        });
                    } else if (res.data.code == "1120002" && flag === 13){
                        this.$alert("该指令不支持线上撤销",'错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    }else {
                        this.openMask();
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    }
            });

        },
        transferSuccess() {
            let selects = this.$refs.datagrid.getSelection();
            if (selects.length == 0) {
                this.openMask();
                this.$alert('请先选中一条记录', '错误', {
                    confirmButtonText: '确定',
                    type: 'error',
                    callback: action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                });
                return;
            }

            //过滤掉子指令
            selects = filterTransferChildren(selects, this);
            if (selects.length <= 0) {
                return;
            }

            this.$confirm("若已和托管人确认过划款成功，请继续，否则请取消", '提示', {
                confirmButtonText: '继续',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.doAction('transferSuccess', '');
            }).catch(() => {
                this.closeMask();
            });
        },
        doAction(action,value) {
            //let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
            let selects = this.$refs.datagrid.getSelection();
            if (selects.length == 0) {
                this.openMask();
                this.$alert('请先选中一条记录','错误',{
                    confirmButtonText: '确定',
                    type: 'error',
                    callback:action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                })
                return
            }

            //过滤掉子指令
            if(action!='sendAttachment'){
                selects = filterTransferChildren(selects, this);
                if (selects.length <= 0) {
                    return;
                }
            }

            //遮罩层
            this.$refs.screenLoading.open();

            let url = '/cashsettle/settle/' + action + '.json';
            let actionType = action;
            handleTransfer(url, {
                    transferList: selects,
                    ignore: false,
                    param: value
                })
                .then(res => {
                    this.$refs.screenLoading.close();
                    if (res.data.success) {
                        this.showResult(actionType, res);

                    } else if (res.data.code == "1120002") {
                        this.openMask();
                        const h = this.$createElement;
                        let errInfos =[]
                        res.data.msg.split(";").forEach(x=>{
                            if (x&&x.length>1)
                                errInfos.push({id:x.split(':')[0],err:x.split(':')[1]})
                        })
                        this.$msgbox({
                                         title: '注意',
                                         message: h('p', null, [
                                             h('span', null, '存在异常的指令，是否剔除并继续？'),
                                             h('h-table', { style:'width: 100%',props: {
                                                     columns: this.errorColumns,
                                                     data:errInfos,
                                                     height:300
                                                 },
                                             })
                                         ]),
                                         showCancelButton: true,
                                         confirmButtonText: '继续',
                                         cancelButtonText: '取消',
                                         beforeClose: (action, instance, done) => {
                                             if (action === 'confirm') {
                                                 handleTransfer(url, {
                                                     transferList: selects,
                                                     ignore: true,
                                                     param: value
                                                 }).then(res => {
                                                     if (res.data.success) {
                                                         this.showResult(actionType, res);

                                                     } else {
                                                         this.openMask();
                                                         this.$alert(res.data.msg, '错误', {
                                                             confirmButtonText: '确定',
                                                             type: 'error',
                                                             callback:action => {
                                                                 this.closeMask();
                                                             }
                                                         }).then(() => {
                                                             this.closeMask();
                                                         })

                                                     }
                                                 });
                                                 done();
                                             } else {

                                                 done();
                                             }
                                         }
                                     }).then(action => {
                            this.closeMask();
                        }).catch(() =>{
                            this.closeMask();
                        });
                    } else {
                        this.openMask();
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })

                    }
                }).catch(error => {
                    this.$refs.screenLoading.close();
                    this.openMask();
                    this.$alert('请求失败，请检查登录状态','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                });
        },
        showResult(actionType, res) {
            //发送划款、发送附件（存在失败信息时，提示成功和失败的笔数）
            if (actionType === "sendTransfer" || actionType === "sendAttachment") {
                const h = this.$createElement;
                let errInfos = [];
                let msgInfos = res.data.msg.split(";");
                let titleInfo = "";
                msgInfos.forEach((x, index) => {
                    if (index === 0) {
                        let successCount = x.split(':')[0]
                        let failCount = x.split(':')[1]
                        if (actionType === "sendAttachment") {
                            if (failCount =='0'){
                                titleInfo = "成功 " + successCount + " 笔";
                            } else{
                                titleInfo = "失败 " + failCount + " 笔";
                            }
                        }else{
                            titleInfo = "成功 " + successCount + " 笔，失败 " + failCount + " 笔";
                        }

                    } else {
                        if (x && x.length > 1) {
                            errInfos.push({
                                id: x.split(':')[0].replace(new RegExp("&colon","gm"),":").replace(new RegExp("&semicolon","gm"),";"),
                                err: x.split(':')[1].replace(new RegExp("&colon","gm"),":").replace(new RegExp("&semicolon","gm"),";")
                            });
                        }
                    }
                });
                this.openMask();
                this.$msgbox({
                    title: '提示',
                    message: h('p', null, [
                        h('span', null, titleInfo),
                        h('h-table', {
                            style: 'width: 100%', props: {
                                columns: this.errorColumns,
                                data: errInfos,
                                height: 200
                            },
                        })
                    ]),
                    closeOnClickModal: false,
                    confirmButtonText: '确定',
                    beforeClose: (action, instance, done) => {
                        done();
                    }
                }).then(action => {
                    this.closeMask();
                });
            } else {
                this.openMask();
                this.$alert(res.data.msg, '成功', {
                    confirmButtonText: '确定',
                    type: 'success',
                    callback:action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                })
            }
            this.refreshFundTree();
        },
        delayTransfer() {
            //let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
            let selects = this.$refs.datagrid.getSelection();
            if (selects.length == 0) {
                this.openMask();
                this.$alert('请先选中一条记录','错误',{
                    confirmButtonText: '确定',
                    type: 'error',
                    callback:action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                })
                return
            }

            //过滤掉子指令
            selects = filterTransferChildren(selects, this);
            if (selects.length <= 0) {
                return;
            }

            let url = '/cashsettle/settle/transferDelay.json'
            this.openMask();
            this.$prompt('请输入交收日期，格式为20181231：', '延期交收', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputPattern: /^\d{4}(?:0\d|1[0-2])(?:[0-2]\d|3[01])( (?:[01]\d|2[0-3])\:[0-5]\d\:[0-5]\d)?$/,
                inputErrorMessage: '日期格式不正确'
            }).then(({
                value
            }) => {
                var params = {
                    transferList: selects,
                    ignore: false,
                    param: value
                };
                handleTransfer(url, params).then(res => {
                    if (res.data && res.data.success) {
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                        this.refreshTable(true);
                    } else if (res.data && res.data.code === '1120002') {
                        const h = this.$createElement;
                        let errInfos = [];
                        res.data.msg.split(";").forEach(x => {
                            if (x && x.length > 1) {
                                let returninfo = x.split(':');
                                errInfos.push({id: returninfo[0], err: returninfo[returninfo.length - 1]})
                            }
                        });
                        this.$msgbox({
                            title: '注意',
                            message: h('p', null, [
                                h('span', null, "存在异常的指令，是否剔除并继续？"),
                                h('h-table', {
                                    style: 'width: 100%', props: {
                                        columns: this.errorColumns,
                                        data: errInfos,
                                        height: 300
                                    },
                                })
                            ]),
                            showCancelButton: true,
                            confirmButtonText: '继续',
                            cancelButtonText: '取消',
                            beforeClose: (action, instance, done) => {
                                if (action === 'confirm') {
                                    handleTransfer(url, {
                                        transferList: selects,
                                        ignore: true,
                                        param: value
                                    }).then(res => {
                                        if (res.data.success) {
                                            this.openMask();
                                            this.$alert(res.data.msg, '成功', {
                                                confirmButtonText: '确定',
                                                type: 'success',
                                                callback: action => {
                                                    this.closeMask();
                                                }
                                            }).then(() => {
                                                this.closeMask();
                                            });
                                            this.refreshTable(true);
                                        } else {
                                            this.openMask();
                                            this.$alert(res.data.msg, '错误', {
                                                confirmButtonText: '确定',
                                                type: 'error',
                                                callback: action => {
                                                    this.closeMask();
                                                }
                                            }).then(() => {
                                                this.closeMask();
                                            })
                                        }
                                    });
                                    done();
                                } else {
                                    done();
                                }
                            }
                        }).then(action => {
                            this.closeMask();
                        });
                    } else {
                        this.openMask();
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                    }
                }).catch(error => {
                    this.openMask();
                    this.$alert(error,'错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                });
            }).catch(() =>{
                this.closeMask();
            });
        },
        /*settleSingle(flag) {

            let selects = this.$refs.oldData.$refs.gridContent.getSelection();
            if (selects.length == 0) {
                this.openMask();
                this.$alert('请先选中一条记录','错误',{
                    confirmButtonText: '确定',
                    type: 'error',
                    callback:action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                })
                return
            }
            this.handleTransfer(flag, selects, this.$refs.oldData);
        },*/
        handleCommand(command) {
            switch (parseInt(command)) {
                case 100:
                    this.doAction('queryTransfer','');
                    break
                case 6:
                    this.delayTransfer()
                    break
                case 15:
                    this.settleSuccess(parseInt(command))
                    break
                case 19:
                    this.cancelTransfer(parseInt(command));
                    break;
                default:
                    this.handleTransfer(parseInt(command))
                    break
            }
        },
        handleTransferCommand(command){
            this.doAction('sendTransfer',command);
        },
        handleFileCommand(command){
            this.doAction('sendAttachment',command);
        },
        handleTransfer(flag, selects, dataGird) {
            if (!selects || !selects.length) {
                //selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                selects = this.$refs.datagrid.getSelection();
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选中一条记录','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
            }


            if (flag == 4 ||  flag ==16){
                //同时选中父子指令，则保留子指令（即可对子指令做交收操作）
                selects = filterDuplicateTransfer(selects, this);
                if (selects.length <= 0) {
                    return;
                }

            } else {
                //过滤掉子指令
                selects = filterTransferChildren(selects, this);
                if (selects.length <= 0) {
                    return;
                }
            }

            let action = "";
            let url = '/cashsettle/approveOpetate.json';
            switch (parseInt(flag)) {
                case 4:
                    url = '/cashsettle/settle/settleFail.json'
                    action = "交收失败";
                    break;
                case 6:
                    url = '/cashsettle/settle/transferDelay.json'
                    action = "延期交收";
                    break;
                case 16:
                    url = '/cashsettle/settle/settleFail.json'
                    action = "强制交收失败";
                    break;
                case 7:
                    action = "作废";
                    break;
                case 9:
                    action = "驳回";
                    break;
                case 8:
                    action = "审批通过";
                    break;
                default:
                    break;
            }
            let title = "确定" + action + "所选指令?";

            this.openMask();
            this.$prompt(title + '请填入原因并确认：', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(({
                value
            }) => {
                handleTransfer(url, {
                        transferList: selects,
                        flowFlag: flag,
                        ignore: false,
                        remark: value
                    })
                    .then(res => {
                        if (res.data.success) {
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText: '确定',
                                type: 'success',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                            if (dataGird) {
                                dataGird.dataChange();
                            } else {
                                this.refreshFundTree()
                            }

                        } else{
                            const h = this.$createElement;
                            let errInfos =[]
                            res.data.msg.split(";").forEach(x=>{
                                if (x&&x.length>1){
                                    let returninfo = x.split(':');
                                    errInfos.push({id:returninfo[0],err:returninfo[returninfo.length-1]})
                                }
                            })
                            this.$msgbox({
                                             title: '注意',
                                             message: h('p', null, [
                                                 h('span', null, "存在异常的指令，是否剔除并继续？"),
                                                 h('h-table', { style:'width: 100%',props: {
                                                         columns: this.errorColumns,
                                                         data:errInfos,
                                                         height:300
                                                     },
                                                 })
                                             ]),
                                             showCancelButton: true,
                                             confirmButtonText: '继续',
                                             cancelButtonText: '取消',
                                             beforeClose: (action, instance, done) => {
                                                 if (action === 'confirm') {
                                                     handleTransfer(url, {
                                                         transferList: selects,
                                                         flowFlag: flag,
                                                         ignore: true,
                                                         remark: value
                                                     }).then(res => {
                                                         if (res.data.success) {
                                                             this.openMask();
                                                             this.$alert(res.data.msg,'成功',{
                                                                 confirmButtonText: '确定',
                                                                 type: 'success',
                                                                 callback:action => {
                                                                     this.closeMask();
                                                                 }
                                                             }).then(() => {
                                                                 this.closeMask();
                                                             })
                                                             if (dataGird) {
                                                                 dataGird.dataChange();
                                                             } else {
                                                                 this.refreshFundTree()
                                                             }
                                                         } else {
                                                             this.openMask();
                                                             this.$alert(res.data.msg,'错误',{
                                                                 confirmButtonText: '确定',
                                                                 type: 'error',
                                                                 callback:action => {
                                                                     this.closeMask();
                                                                 }
                                                             }).then(() => {
                                                                 this.closeMask();
                                                             })
                                                         }
                                                     });
                                                     done();
                                                 } else {
                                                     done();
                                                 }
                                             }
                                         }).then(action => {
                                this.closeMask();
                            });

                        }
                    }).catch(error => {
                        this.openMask();
                        this.$alert('请求失败，请检查登录状态','错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    });
            }).catch(() =>{
                this.closeMask();
            });
        },
        settleSuccess(flag, selects, dataGird) {
            if (!selects || !selects.length) {
                //selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                selects = this.$refs.datagrid.getSelection();
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选中一条记录','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
            }

            let selectsWithNoDuplicate = filterDuplicateTransfer(selects, this);

            let url = '/cashsettle/settle/settleSuccess.json';
            const loading = this.$loading({
                lock: true,
                text: '处理中',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
            });
            handleTransfer(url, {
                transferList: selectsWithNoDuplicate,
                flowFlag: flag,
                ignore: false
            }).then(res => {
                    loading.close();
                    if (res.data.success) {
                        this.openMask();
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                        if (dataGird) {
                            dataGird.dataChange();
                        } else {
                            this.refreshFundTree()
                        }

                    } else {
                        const h = this.$createElement;
                        let errInfos =[]
                        res.data.msg.split(";").forEach(x=>{
                            if (x&&x.length>1){
                                let returninfo = x.split(':');
                                errInfos.push({id:returninfo[0],err:returninfo[returninfo.length-1]})
                            }
                        })
                        this.openMask();
                        this.$msgbox({
                            title: '注意',
                            message: h('p', null, [
                                h('span', null, "存在异常的指令，是否继续？"),
                                h('h-table', { style:'width: 100%',props: {
                                    columns: this.errorColumns,
                                        data:errInfos,
                                        height:300
                                    },
                                })
                            ]),
                            showCancelButton: true,
                            confirmButtonText: '继续',
                            cancelButtonText: '取消',
                            beforeClose: (action, instance, done) => {
                                if (action === 'confirm') {
                                    handleTransfer(url, {
                                        transferList: selectsWithNoDuplicate,
                                        flowFlag: flag,
                                        ignore: true
                                    }).then(res => {
                                        if (res.data.success) {
                                            this.openMask();
                                            this.$alert(res.data.msg,'成功',{
                                                confirmButtonText: '确定',
                                                type: 'success',
                                                callback:action => {
                                                    this.closeMask();
                                                }
                                            }).then(() => {
                                                this.closeMask();
                                            })
                                            if (dataGird) {
                                                dataGird.dataChange();
                                            } else {
                                                this.refreshFundTree()
                                            }
                                        } else {
                                            this.openMask();
                                            this.$alert(res.data.msg,'错误',{
                                                confirmButtonText: '确定',
                                                type: 'error',
                                                callback:action => {
                                                    this.closeMask();
                                                }
                                            }).then(() => {
                                                this.closeMask();
                                            })
                                        }
                                    });
                                    done();
                                } else {
                                    done();
                                }
                            }
                        }).then(action => {
                            this.closeMask();
                        }).catch(() =>{
                            this.closeMask();
                        });
                    }
                }).catch(error => {
                this.openMask();
                this.$alert('请求失败，请检查登录状态','错误',{
                    confirmButtonText: '确定',
                    type: 'error',
                    callback:action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                })
            });
        },
        fileUploaded(response, file, fileList) {
            this.transferInfo.bill_name =
                fileList.reduce((x, y) => x.response.data + ',' + y.response.data);
        },
        exceedLimit() {
            this.$alert('最多上传5个附件','错误',{
                confirmButtonText: '确定',
                type: 'error'
            });
        },
        previewPDF(fileId) {
            let pdfUrl = "/cop-web/static/plugins/pdfjs/generic/web/viewer.html?file="+encodeURIComponent(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=1&fileId=' +fileId);
            var myWindow=window.open(pdfUrl,'划款单预览','width=800,height=600');
            myWindow.focus();
        },
        previewTransfer(transferId,modifyId){
            getTransferFile({transferId:transferId,modifyId:modifyId}).then(res => {
                if (res.data.success) {
                    this.previewPDF(res.data.data)
                }else {
                    this.$alert(res.data.msg,'提示',{
                        confirmButtonText: '确定',
                        type: 'error'
                    })
                }
            })
        },
        downloadByFileId(fileId) {
            window.open(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=1&fileId=' +fileId)

        },
        downloadFile(file) {
            window.open(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=2&fileId=' + file.response.data)
        },
        openLayout(){
            this.$refs.datagrid.openLayout();
        },

        rowClassName(row) {
            //“审批驳回”和“划款失败”标红显示
            if (row.transfer_flow_flag === "9" || row.transfer_fund_status === "5" || (row.transfer_merge_flag != '1' && row.settle_balance == 0)) {
                return 'alert-table-red-row';
            } else {
                return '';
            }
        },

        onSelectionChange(selections) {
            this.selectionTipVisible = selections.length > 0
            this.selections = selections
            SSObservable.publish('onresize')
            SSObservable.publish('re_layout')
        }
    }
}
</script>

