    border-bottom:1px solid #1388E6 ;
    height: 28px;
    width: 25px;
}

.transfer-settle .el-icon-arrow-down {
    border-top: 7px solid transparent;
}
.transfer-settle .buttons-ctr2 .el-icon-arrow-down {
    border-top: 0px solid transparent;
}
</style>
<style lang="less" rel="stylesheet/less">
