        .block-in-box {
            .headline {
                padding-bottom: 10px;
                display: flex;
                flex-direction: row;
                align-items: center;

                .icon-l {
                    display: block;
                    width: 3px;
                    height: 12px;
                    background-color: #11a0f8;
