                }

                .title {
                    padding: 0;
                    margin-right: 10px;
                    font-size: 12px;
                    color: #666666;
                }

                .icon-operation {
                    cursor: pointer;
                    color: #11a0f8;
                }
            }
        }

        .collapse-icon-l {
            display: block;
            margin-left: 10px;
            width: 3px;
            height: 12px;
            background-color: #11a0f8;
            margin-right: 5px;
        }

        .collapse-title {
            padding: 0;
            margin-right: 10px;
            font-size: 12px;
            color: #666666;
        }

        .wrapper {
            width: 100%;
            // height: 208px;
            padding: 10px;
            border: solid 1px #d3d3d3;
