<template>
    <div class="systemmgnt-workgroup" id="full-h-wrapper">
        <div class="contentsome">
            <div class="content-left">
                <h-button-group>
                    <h-button icon="add" @click="workGroupAdd">新增</h-button>
                    <h-button icon="editor" @click="workGroupModify">修改</h-button>
                    <h-button icon="delete" @click="workGroupDelete">删除</h-button>
                </h-button-group>
                <h-datagrid title="工作组"
                            hasPage stripe :height="autoHeight"
                            highlight-row :page-size="pageSize"
                            :page-size-opts="pageSizeOpts" :auto-load="true"
                            :columns="groupColumns" :onRowClick="clickWorkGroup" :on-row-db-click="workGroupDbClick"
                            url="/systemmgnt/workGroup/pageWorkGroup.json"
                            ref="groupdatagrid">
                </h-datagrid>
            </div>
            <div class="content-right">
                <div>
                    <h-button-group>
                        <h-button icon="add" @click="opWorkGroupAdd">新增</h-button>
                        <h-button icon="editor" @click="opWorkGroupModify">修改</h-button>
                        <h-button icon="delete" @click="opWorkGroupDelete">删除</h-button>
                    </h-button-group>
                    <h-datagrid :title="operatorTitle"
                                hasPage stripe :height="autoHeight"
                                highlight-row :page-size="pageSize"
                                :page-size-opts="pageSizeOpts" :auto-load="false"
                                :columns="operatorColumns" :bindForm="queryForm"
                                :on-row-db-click="opWorkGroupDbClick"
                                url="/systemmgnt/workGroup/pageOpWorkGroup.json"
                                ref="operatordatagrid">
                    </h-datagrid>
                </div>
            </div>
        </div>

            <h-msg-box class="customMsgBox" v-model="showGroupDialog" width="400" :mask-closable=false>
                <p slot="header">
                    <span v-if="workGroupActionType == 'add'">新增工作组</span>
                    <span v-else>修改工作组</span>
                </p>
                <h-form :model="workGroup" :label-width="90" ref="workGroupForm">
                    <h-row>
                        <h-col span="24">
                            <h-form-item required label="工作组名称" prop="workgroup_name">
                                <h-input v-model="workGroup.workgroup_name"></h-input>
                            </h-form-item>
                            <h-form-item label="工作组状态" prop="use_flag">
                                <h-select v-model="workGroup.use_flag" :clearable=false filterable>
                                    <h-option v-for="item in useFlagList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                                </h-select>
                            </h-form-item>
                        </h-col>
                    </h-row>
                </h-form>
                <div slot="footer">
                    <h-button type="ghost" style="margin-left: 8px" @click="workgroupCancel">取消</h-button>
                    <h-button type="primary" @click="saveWorkGroup">确定</h-button>
                </div>
            </h-msg-box>
            <h-msg-box class="customMsgBox" v-model="showOperatorDialog" width="350" :mask-closable=false>
                <p slot="header">
                    <span v-if="opWorkGroupActionType == 'add'">新增工作组操作员</span>
                    <span v-else>修改工作组操作员</span>
                </p>
                <h-form :model="workOperator" :label-width="80" ref="workGroupOperatorForm">
                    <h-row>
                        <h-col span="24">
                            <h-form-item label="操作员工" required prop="operator_no">
                                <h-select :disabled="opWorkGroupActionType == 'modify'" v-model="workOperator.operator_no" filterable>
                                    <h-option v-for="item in operatorList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                                </h-select>
                            </h-form-item>
                            <h-form-item label="生效日期" prop="begin_date">
                                <el-date-picker size="mini" style="width: 100%" v-model="beginDate" type="date" placeholder="选择日期"></el-date-picker>
                            </h-form-item>
                            <h-form-item label="失效日期" prop="end_date">
                                <el-date-picker size="mini" style="width: 100%" v-model="endDate" type="date" placeholder="选择日期"></el-date-picker>
                            </h-form-item>
                        </h-col>
                    </h-row>
                </h-form>
                <div slot="footer">
                    <h-button type="ghost" style="margin-right: 8px" @click="operatorCancel">取消</h-button>
                    <h-button type="primary" @click="saveOperator">确认</h-button>
                </div>
            </h-msg-box>
    </div>
</template>

<script>
    import HDatagrid from "../../../components/HDatagrid";

    import {
        showText,
        operatorList,
        parseDateStr,
        parseDateString
    } from "@/api/utils"

    import {
        saveWorkGroup,
        deleteWorkGroup,
        saveOpWorkGroup,
        deleteOpWorkGroup
    } from "@/api/systemmgnt/workGroup"

    export default {
        name: "workGroup",
        components: {HDatagrid},
        data(){
            return{
                operatorTitle:'操作员',
                autoHeight:0,
                pageSize:100,
                pageSizeOpts:[25,50,75,100],
                showGroupDialog:false,
                showOperatorDialog:false,
                workGroupActionType:'',
                opWorkGroupActionType:'',
                count:1,
                operatorList:[],
                groupColumns:[
                    {
                        title: "工作组序号",
                        key: "workgroup_id",
                        align: 'center',
                        ellipsis: true
                    },
                    {
                        title: "工作组名字",
                        key: "workgroup_name",
                        ellipsis: true,
                        align: 'center'
                    },
                    {
                        title: "工作组状态",
                        key: "use_flag",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row
                            let useFlag = showText(rowData.use_flag,this.useFlagList)
                            return h("span",{},useFlag)
                        }
                    }
                ],
                operatorColumns:[
                    {
                        title: "工作组序号",
                        key: "workgroup_id",
                        hiddenCol:true,
                        align: 'center',
                        ellipsis: true
                    },
                    {
                        title: "员工号",
                        key: "operator_no",
                        align: 'center',
                        ellipsis: true,
                        sortable: true
                    },
                    {
                        title: "操作员名称",
                        key: "operator_name",
                        align: 'center',
                        ellipsis: true
                    },
                    {
                        title: "生效日期",
                        key: "begin_date",
                        align: 'center',
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row
                            let beginDate = rowData.begin_date;
                            if(beginDate == '0')
                                return h("span",{},"无限制")
                            else
                                return h("span",{},beginDate)
                        }
                    },
                    {
                        title: "失效日期",
                        key: "end_date",
                        align: 'center',
                        ellipsis: true,
                        render:(h,params) =>{
                            const rowData = params.row
                            let endDate = rowData.end_date;
                            if(endDate == '0')
                                return h("span",{},"无限制")
                            else
                                return h("span",{},endDate)
                        }
                    }
                ],
                useFlagList:[
                    {
                        code:'1',
                        text:'启用'
                    },
                    {
                        code:'2',
                        text:'不启用'
                    }
                ],
                workGroup:{
                    workgroup_id:'',
                    workgroup_name:'',
                    use_flag:''
                },
                workOperator:{
                    workgroup_id:'',
                    operator_no:'',
                    operator_name:'',
                    begin_date:'',
                    end_date:'',
                    actionType:''
                },
                queryForm:{
                    workGroupId:''
                }
            }
        },
        computed:{
            beginDate:{
                get:function () {
                    if(this.workOperator.begin_date)
                        return parseDateString(this.workOperator.begin_date)
                    else
                        return ''
                },
                set:function (newValue) {
                    this.workOperator.begin_date = parseDateStr(newValue)
                }
            },
            endDate:{
                get:function () {
                    if(this.workOperator.end_date)
                        return parseDateString(this.workOperator.end_date)
                    else
                        return ''
                },
                set:function (newValue) {
                    this.workOperator.end_date = parseDateStr(newValue)
                }
            }
        },
        watch:{
            showGroupDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            },
            showOperatorDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        mounted() {
            operatorList().then(res => this.operatorList = res.data)
            this.re_layout();
        },
        methods:{
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 90;
                // this.$refs.groupdatagrid.handleResize()
                // this.$refs.operatordatagrid.handleResize()

            },
            workGroupAdd(){
                this.workGroupActionType = 'add';
                this.workGroup={
                    workgroup_id:'',
                    workgroup_name:'',
                    use_flag:'1'
                }
                this.$refs.workGroupForm.resetFields();
                this.showGroupDialog = true;
            },
            workGroupModify(){
                let selects = this.$refs.groupdatagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条工作组记录","错误",{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    });
                    return
                }
                this.workGroupDbClick(selects[0]);
            },
            workGroupDbClick(data){
                this.workGroup = data;
                this.showGroupDialog = true;
                this.workGroupActionType = 'modify';
            },
            workGroupDelete(){
                let selects = this.$refs.groupdatagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条工作组记录","错误",{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    });
                    return
                }

                this.openMask();
                this.$confirm("是否删除该工作组信息","确认",{
                    confirmButtonText:'确定',
                    cancelButtonText:'取消',
                    type:'warning'
                }).then(() =>{
                    deleteWorkGroup(selects[0].workgroup_id).then(res => {
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            });
                            this.$refs.groupdatagrid.dataChange(1);
                            this.$refs.operatordatagrid.dataChange(1);
                            this.operatorTitle = '操作员';
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            });
                        }
                    })
                }).catch(() => {
                    this.closeMask();
                })
            },
            clickWorkGroup(){
                let selects = this.$refs.groupdatagrid.$refs.gridContent.getSelection();
                if(selects.length>0){
                    this.queryForm.workGroupId = selects[0].workgroup_id;
                    this.operatorTitle = selects[0].workgroup_name + ' 操作员';
                }
                this.$refs.operatordatagrid.dataChange(1);
            },
            workgroupCancel(){
                this.showGroupDialog = false;
            },
            saveWorkGroup(){
                let isValidate = true;
                this.$refs.workGroupForm.validate(validate => isValidate = isValidate && validate);
                if(!isValidate){
                    this.$alert('输入项不满足要求，请重新检查','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }

                saveWorkGroup(this.workGroup).then(res => {
                    if(res.data && res.data.code == '0'){
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText:'确定',
                            type:'success',
                            callback: action => {
                                this.showGroupDialog = false
                            }
                        }).then(() =>{
                            this.showGroupDialog = false
                        });
                        this.$refs.groupdatagrid.dataChange(1);
                        if(this.workGroupActionType == 'modify'){
                            this.operatorTitle = this.workGroup.workgroup_name + ' 操作员';
                        }
                    }else{
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText:'确定',
                            type:'error'
                        });
                    }
                })
            },
            opWorkGroupAdd(){
                if(!this.queryForm.workGroupId){
                    this.openMask();
                    this.$alert("请选择一条工作组信息","错误",{
                        confirmButtonText:'确定',
                        type:"error",
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    });
                    return
                }
                this.opWorkGroupActionType = 'add'
                this.workOperator = {
                    workgroup_id:this.queryForm.workGroupId,
                    operator_no:'',
                    operator_name:'',
                    begin_date:'',
                    end_date:'',
                    actionType:'add'
                }

                this.$refs.workGroupOperatorForm.resetFields();
                this.showOperatorDialog = true;
            },
            opWorkGroupModify(){
                let selects = this.$refs.operatordatagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条操作员记录","错误",{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    });
                    return
                }
                this.opWorkGroupDbClick(selects[0]);
            },
            opWorkGroupDbClick(data){
                this.workOperator = data;
                if(this.workOperator.operator_no){
                    this.workOperator.operator_no = this.workOperator.operator_no.toString();
                }
                this.workOperator.actionType = 'modify'
                this.opWorkGroupActionType = 'modify';
                this.showOperatorDialog = true
            },
            opWorkGroupDelete(){
                let selects = this.$refs.operatordatagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条操作员记录","错误",{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    });
                    return
                }

                this.openMask()
                this.$confirm("确定要删除该操作员信息","确认",{
                    confirmButtonText:'确定',
                    cancelButtonText:'取消',
                    type:'warning'
                }).then(() =>{
                    deleteOpWorkGroup(selects[0].operator_no,selects[0].workgroup_id).then(res => {
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            });
                            this.$refs.operatordatagrid.dataChange(1);
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            });
                        }
                    })
                }).catch(() => {
                    this.closeMask();
                })
            },
            operatorCancel(){
                this.showOperatorDialog = false;
            },
            saveOperator(){
                let isValidate = true;
                this.$refs.workGroupOperatorForm.validate(validate => isValidate = validate && isValidate)
                if(!isValidate){
                    this.$alert('输入项不满足要求，请重新检查','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }

                if(this.workOperator.begin_date != 0 && this.workOperator.end_date != 0 && this.workOperator.begin_date > this.workOperator.end_date){
                    this.$alert('生效日期不能大于失效日期','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }

                saveOpWorkGroup(this.workOperator).then(res => {
                    if(res.data && res.data.code == '0'){
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText:'确定',
                            type:'success',
                            callback: action => {
                                this.showOperatorDialog = false;
                            }
                        }).then(() =>{
                            this.showOperatorDialog = false;
                        });
                        this.$refs.operatordatagrid.dataChange(1);
                    }else{
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText:'确定',
                            type:'error'
                        });
                    }
                })
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less">
    .systemmgnt-workgroup {
        .contentsome{
            position: relative;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            padding: 0 10px;
            box-sizing: border-box;
            width: 100%;
            height: 100%;
            & > div:first-child {
                margin-right: 20px;
            }
            & > div {
                position: relative;
                width: 50%;
                height: 100%;
            }
        }
    }
</style>


