import fetch from "@/api/httpFetch";

export function insertIssuer(params) {
    return fetch.post("/systemmgnt/issuer/insert.json", params, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    })
}

export function updateIssuer(params) {
    return fetch.post("/systemmgnt/issuer/update.json", params, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    })
}

export function deleteIssuer(issuerId) {
    return fetch.post("/systemmgnt/issuer/delete.json", {issuerId: issuerId})
}


