<template>
    <div class="full-h">
        <h-split v-model="split" min="130" max="600">
            <div slot="left" span="4" class="full-h">
                <bank-tree :onBankClick="bankClick" :showAll="true" ref="bankTree" :query-url="'/common/treeElcBank.json'"></bank-tree>
            </div>
            <div slot="right" class="full-h" style="padding-left: 10px" id="full-h-wrapper-on-accflowquery">
                <h-row id="inner-on-accflowquery">
                    <h-col>
                        <el-form style="font-size: 12px;float: left" :inline="true" size="mini" :model="queryForm" class="demo-form-inline">
                            <el-form-item label="日期">
                                <el-date-picker :clearable="false" v-model="queryDate" type="date" placeholder="选择日期"></el-date-picker>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="refresh">查询</el-button>
                                <el-button type="ghost" @click="flowRefresh">流水查询</el-button>
                                <el-button type="ghost" @click="multipleRefresh">批量查询</el-button>
                            </el-form-item>
                        </el-form>
                    </h-col>
                </h-row>

                <h-datagrid
                        hasPage highlight-row stripe
                        :height="autoHeight" :auto-load="false"
                        :pageSize="100" :pageSizeOpts="pageSizeOpts"
                        :bindForm="queryForm" :columns="accFlowColumns"
                        :onRowClick="rowClick" style="margin-top: 10px;"
                        url="/reportmgnt/accFlow/accFlowQuery.json"
                        ref="datagrid">
                </h-datagrid>

                <el-tabs tab-position="top" style="margin-top: 5px">
                    <el-tab-pane label="托管户流水">
                        <h-datagrid
                                hasPage :pageSize="100"
                                highlight-row stripe height="150"
                                :bindForm="accFlowQuery"
                                :columns="tradeFlowColumns" :auto-load="false"
                                url="/reportmgnt/accFlow/accTradeFlow.json"
                                ref="flowquerydatagrid">
                        </h-datagrid>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </h-split>

        <!--<h-msg-box class="customMsgBox" v-model="showFlowQueryDialog" width="300" :mask-closable="false">
            <p slot="header">
                <span>流水查询</span>
            </p>
            <h-form :model="flowQueryInfo" :label-width="90" ref="flowQueryForm">
                <h-form-item required label="产品名称" prop="fund_id">
                    <h-select v-model="flowQueryInfo.fund_id" filterable @on-change="fundIdChange">
                        <h-option v-for="item in fundIdList" :key="item.code" :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>
                <h-form-item required label="托管人名称" prop="custodial_no">
                    <h-select v-model="flowQueryInfo.custodial_no" @on-change="bankNoChange" filterable>
                        <h-option v-for="item in bankNoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item required label="账户名称" prop="bank_account_name">
                    <h-select v-model="flowQueryInfo.bank_account_name" @on-change="bankAccountNameChange" filterable>
                        <h-option v-for="item in bankAccountNameList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item required label="银行账号" prop="bank_account_no">
                    <h-select v-model="flowQueryInfo.bank_account_no" filterable>
                        <h-option v-for="item in bankAccountNoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="cancelFlowQuery">取消</h-button>
                <h-button type="primary" :loading="loading" @click="queryFlowQuery">确定</h-button>
            </div>
        </h-msg-box>-->
        <h-msg-box class="customMsgBox" v-model="showMultipleQueryDialog" width="800" height="400" :mask-closable="false">
            <p slot="header">
                <span>流水批量查询</span>
            </p>
            <h-row>
                <h-col>
                    <h-datagrid height="355"
                                stripe hasPage highlight-row
                                :pageSize="100" :pageSizeOpts="pageSizeOpts"
                                :bindForm="multipleBindForm"
                                :columns="multipleFlowColumns" ref="multipleflowdatagrid"
                                url="/reportmgnt/accBatchQuery.json"></h-datagrid>
                </h-col>
            </h-row>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="multipleCancel">取消</h-button>
                <h-button type="primary" :loading="loading" @click="multipleSave">确定</h-button>
            </div>
        </h-msg-box>

        <accCashFlowQueryMsgBox :msgBoxShow="flowMsgBoxShow"
                                :queryType="flowQueryType"
                                :includeSelfRun=false
                                @onCashFlowClose="onFlowClose"
                                @onOutRefresh="onFlowRefresh"
                                ref="accFlowQueryMsg">
        </accCashFlowQueryMsgBox>
    </div>
</template>

<script>

    import{
        showText,
        getDictList,
        getFundInfo,
        formatMoney,
        parseDateString,
        parseDateStr
    } from "@/api/utils"

    import {
        getBankNo,
        getAccName,
        getAccNo,
        submitAccQuery,
        submitAccBatchQuery
    } from '@/api/reportmgnt/accQuery'

    import Cookies from 'js-cookie'

    import BankTree from "../../../components/BankTree";
    import HDatagrid from "../../../components/HDatagrid"
    import accCashFlowQueryMsgBox from '@/components/accCashFlowQueryMsgBox'

    /* mixin dependency */
    import render from "@/mixins/render";

    export default {
        mixins: [render],
        name: "accFlowQuery",
        components:{BankTree,HDatagrid,accCashFlowQueryMsgBox},
        data(){
            return{
                loading: false,
                split: 0.15,
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                showFlowQueryDialog:false,
                showMultipleQueryDialog:false,
                bankNoList:[],
                bankAccountNameList:[],
                bankAccountNoList:[],
                queryStatusList:[],
                fundIdList:[],
                sysDate:Cookies.get('CurrSysDate'),
                accFlowColumns:[
                    {
                        title:'托管人查询序号',
                        key:'query_id',
                        align:'center',
                        hiddenCol:true,
                    },{
                        title:'日期',
                        key:'curr_date',
                        align:'center',
                        sortable:true,
                        ellipsis:true
                    },{
                        title:'托管人编号',
                        key:'custodial_no',
                        align:'center',
                        ellipsis:true,
                        sortable:true
                    },{
                        title:'托管人名称',
                        key:'custodian_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'产品编号',
                        key:'fund_id',
                        align:'center',
                        ellipsis:true,
                        hiddenCol:true
                    },{
                        title:'产品名称',
                        key:'fund_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'银行账号',
                        key:'bank_account_no',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'账户名称',
                        key:'bank_account_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'账户编号',
                        key:'bank_account_id',
                        align:'center',
                        ellipsis:true,
                        hiddenCol:true
                    },{
                        title:'查询状态',
                        key:'query_status',
                        align:'center',
                        ellipsis:true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let queryStatus = showText(rowData.query_status,this.queryStatusList)
                            return h("span",{},queryStatus)
                        }
                    },{
                        title:'备注',
                        key:'remark',
                        align:'center',
                        ellipsis:true
                    }
                ],
                tradeFlowColumns:[
                    {
                        title:'交易日期',
                        key:'curr_date',
                        align:'center',
                        ellipsis:true,
                        sortable:true
                    },{
                        title:'银行账号',
                        key:'bank_account_no',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'账户名称',
                        key:'bank_account_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'出入款方向',
                        key:'cash_direction',
                        align:'center',
                        ellipsis:true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let cashDirection = showText(rowData.cash_direction,this.cashDirectionList)
                            return h("span",{},cashDirection)
                        }
                    },{
                        title:'对方银行账户',
                        key:'rival_account_no',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'对方账户名称',
                        key:'rival_account_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'交易金额',
                        key:'trade_balance',
                        align:'center',
                        ellipsis:true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let tradeBalance = formatMoney(rowData.trade_balance)
                            return h("span",{style:{fontWeight:'bold'}},tradeBalance)
                        }
                    },{
                        title:'账户余额',
                        key:'bank_remain_balance',
                        align:'center',
                        ellipsis:true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let bankRemainBalance = formatMoney(rowData.bank_remain_balance)
                            return h("span",{style:{fontWeight:'bold'}},bankRemainBalance)
                        }
                    },{
                        title:'摘要',
                        key:'transfer_remark',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'款项用途',
                        key:'transfer_purpose',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'附言',
                        key:'transfer_postscript',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'备注',
                        key:'remark',
                        align:'center',
                        ellipsis:true
                    }
                ],
                multipleFlowColumns:[
                    {
                        type: 'selection',
                        width: 45,
                        align: "left"
                    },{
                        title:'托管人查询序号',
                        key:'query_id',
                        align:'center',
                        hiddenCol:true,
                    },{
                        title:'产品编号',
                        key:'fund_id',
                        align:'center',
                        ellipsis:true,
                        hiddenCol:true
                    },{
                        title:'产品代码',
                        key:'fund_code',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'产品名称',
                        key:'fund_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'托管人编号',
                        key:'custodial_no',
                        align:'center',
                        ellipsis:true,
                        sortable:true
                    },{
                        title:'托管人名称',
                        key:'custodian_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'银行账号',
                        key:'bank_account_no',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'账户名称',
                        key:'bank_account_name',
                        align:'center',
                        ellipsis:true
                    },{
                        title:'账户编号',
                        key:'bank_account_id',
                        align:'center',
                        ellipsis:true,
                        hiddenCol:true
                    },{
                        title:'查询状态',
                        key:'query_status',
                        align:'center',
                        ellipsis:true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let queryStatus = showText(rowData.query_status,this.queryStatusList)
                            if(!queryStatus){
                                queryStatus = '未查询'
                            }
                            return h("span",{},queryStatus)
                        }
                    }
                ],
                queryForm:{
                    queryDate:Cookies.get('CurrSysDate'),
                    custodialNo:''
                },
                flowQueryInfo:{
                    fund_id:'',
                    custodial_no:'',
                    bank_account_name:'',
                    bank_account_no:'',
                    queryDate:'',
                    queryType:'2'
                },
                multipleBindForm:{
                    queryDateBatch:'',
                    queryTypeBatch:'2'
                },
                accFlowQuery:{
                    queryDate:'',
                    fundId:'',
                    accountId:''
                },
                cashDirectionList:[
                    {
                        code:'1',
                        text:'入款'
                    },{
                        code:'2',
                        text:'出款'
                    }
                ],
                flowQueryType: '',
                flowMsgBoxShow: false,
            }
        },
        watch:{
            showFlowQueryDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            },
            showMultipleQueryDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
            queryDate:{
                get:function () {
                    if(this.queryForm.queryDate){
                        return parseDateString(this.queryForm.queryDate)
                    }else{
                        return parseDateString(this.sysDate)
                    }
                },
                set:function (newValue) {
                    this.queryForm.queryDate = parseDateStr(newValue);
                }
            }
        },
        mounted(){
            this.reLayout()
            getDictList(90520).then(res => this.queryStatusList = res.data)
        },
        activated(){
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => this.fundIdList = res.data);
        },
        methods:{
            reLayout() {
                this.re_layout("full-h-wrapper-on-accflowquery", "inner-on-accflowquery", 220);
            },
            bankClick(data){
                if(data){
                    if(data.code.split(',').length > 1){
                        data.code = '';
                    }
                    this.queryForm.custodialNo = data.code;
                    this.refresh();
                }
            },
            refresh(){
                this.$refs.datagrid.dataChange(1);
                this.$refs.flowquerydatagrid.clearData();
            },
            flowRefresh(){
                //现仅支持查询当日流水明细 时间固定为当天
                /*this.flowQueryInfo = {
                    fund_id:'',
                    custodial_no:'',
                    bank_account_name:'',
                    bank_account_no:'',
                    queryDate:parseDateStr(this.sysDate),
                    queryType:'2'
                }
                this.$refs.flowQueryForm.resetFields();
                this.showFlowQueryDialog = true;
                this.loading = false;*/

                this.flowMsgBoxShow = true;
                this.flowQueryType = "2";
                this.$refs.accFlowQueryMsg.resetCashFlowQueryForm();
            },
            multipleRefresh(){
                //现仅支持查询当日流水明细 时间固定为当天
                this.multipleBindForm.queryDateBatch = parseDateStr(this.sysDate);
                this.multipleBindForm.queryTypeBatch = '2';
                this.$refs.multipleflowdatagrid.dataChange(1);
                this.showMultipleQueryDialog = true;
                this.loading = false;
            },
            rowClick(arr){
                this.accFlowQuery.queryDate = this.queryForm.queryDate;
                this.accFlowQuery.fundId = null;
                this.accFlowQuery.accountId = arr[0].bank_account_id;
                this.$refs.flowquerydatagrid.dataChange(1);
            },
            /*fundIdChange:function(current){
                getBankNo(current).then(res => this.bankNoList = res.data)
            },
            bankNoChange:function (current) {
                getAccName(this.flowQueryInfo.fund_id,current).then(res => this.bankAccountNameList = res.data)
            },
            bankAccountNameChange(current){
                getAccNo(this.flowQueryInfo.fund_id,this.flowQueryInfo.custodial_no,current).then(res => this.bankAccountNoList = res.data)
            },
            cancelFlowQuery(){
                this.showFlowQueryDialog = false;
            },
            queryFlowQuery(){
                let isValidate = true;
                this.$refs.flowQueryForm.validate((validate) => isValidate = isValidate && validate)
                if(!isValidate){
                    this.$alert('输入项不满足要求，请重新检查!','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }
                this.loading = true;
                submitAccQuery(this.flowQueryInfo.fund_id,this.flowQueryInfo.custodial_no,this.flowQueryInfo.bank_account_no,this.flowQueryInfo.queryDate,this.flowQueryInfo.queryType).then(
                    res =>{
                        if(res && res.status == 200){
                            if(res.data.success == true){
                                this.$alert('操作成功','成功',{
                                    confirmButtonText:'确定',
                                    type:"success",
                                    callback:action => {
                                        this.showFlowQueryDialog = false;
                                    }
                                }).then(() => {
                                    this.showFlowQueryDialog = false;
                                })
                                this.$refs.datagrid.dataChange(1)
                            }else{
                                this.$alert(res.data.msg,'错误',{
                                    confirmButtonText:'确定',
                                    type:"error"
                                })
                                this.showFlowQueryDialog = false;
                            }
                        }else{
                            this.$alert('流水查询失败','错误',{
                                confirmButtonText:'确定',
                                type:"error"
                            })
                            this.showFlowQueryDialog = false;
                        }
                    }
                )
            },*/
            multipleCancel(){
                this.showMultipleQueryDialog = false;
            },
            multipleSave(){
                let selects = this.$refs.multipleflowdatagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.$alert('请选择要查询的记录','错误',{
                        confirmButtonText:'确定',
                        type:"error"
                    })
                    return
                }
                let params = '';
                for(let i = 0; i<selects.length; i++){
                    params+=selects[i].fund_id+","+selects[i].custodial_no+','+selects[i].bank_account_no+','+selects[i].query_id+'|'
                }
                params = params.substring(0,params.length-1);
                this.loading = true;
                submitAccBatchQuery(this.multipleBindForm.queryTypeBatch,this.multipleBindForm.queryDateBatch,params).then( res => {
                    if(res && res.status == 200){
                        if(res.data.success == true){
                            this.$alert('操作成功','成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.showMultipleQueryDialog = false;
                                }
                            }).then(() => {
                                this.showMultipleQueryDialog = false;
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                            this.showMultipleQueryDialog = false;
                        }
                    }else{
                        this.$alert('批量查询失败','错误',{
                            confirmButtonText:'确定',
                            type:'error'
                        })
                        this.showMultipleQueryDialog = false;
                    }
                })
            },
            onFlowClose() {
                this.flowMsgBoxShow = false;
            },
            onFlowRefresh() {
                this.refresh();
            },
        }
    }
</script>

<style scoped>

</style>



