<template>
    <div class="full-h" id="full-h-wrapper">
        <h-row>
            <h-col>
                <el-form style="font-size: 12px;":inline="true" size="mini" :model="queryForm" class="demo-form-inline">
                    <el-form-item label="交收日期">
                        <el-date-picker
                                v-model="settleDate"
                                type="daterange"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                :clearable="false">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="产品名称">
                        <el-select v-model="fundId" filterable multiple clearable collapse-tags>
                            <el-option v-for="item in fundIdList" :key="item.code" :value="item.code" :label="item.text"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="refresh">查询</el-button>
                        <el-button type="ghost" @click="remarkRemark">备注</el-button>
                    </el-form-item>
                </el-form>
            </h-col>
        </h-row>
        <h-datagrid
                stripe hasPage ref="datagrid"
                :height="autoHeight" highlightRow
                :pageSize="100" :pageSizeOpts="pageSizeOpts"
                :columns="accCheckColumns" :bindForm="queryForm"
                url="/cashcheck/query/allocCheckListInfo.json">
        </h-datagrid>
        <h-msg-box class="customMsgBox" v-model="showBalAccCheckDialog" width="400" :mask-closable="false">
            <p slot="header">
                <span>备注</span>
            </p>
            <h-form :model="balAccCheck" :label-width="100" ref="accCheckForm">
                <h-row>
                    <h-col>
                        <h-form-item label="产品名称" prop="fund_name">
                            <h-input disabled v-model="balAccCheck.fund_name"></h-input>
                        </h-form-item>
                        <h-form-item label="日初差额" prop="init_diff_balance">
                            <h-typefield disabled type="money" suffixNum="2" v-model="balAccCheck.init_diff_balance"></h-typefield>
                        </h-form-item>
                        <h-form-item required label="日初备注" prop="init_check_acc_remark">
                            <h-input v-model="balAccCheck.init_check_acc_remark"></h-input>
                        </h-form-item>
                        <h-form-item label="日终差额" prop="final_diff_balance">
                            <h-typefield disabled type="money" suffixNum="2" v-model="balAccCheck.final_diff_balance"></h-typefield>
                        </h-form-item>
                        <h-form-item required label="日终备注" prop="final_check_acc_remark">
                            <h-input v-model="balAccCheck.final_check_acc_remark"></h-input>
                        </h-form-item>
                    </h-col>
                </h-row>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="cancelCheck">取消</h-button>
                <h-button type="primary" @click="saveRemark">确定</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import HDatagrid from "../../../../components/HDatagrid";
    import {
        showText,
        formatMoney,
        getDictList,
        getFundInfo,
        parseDateString,
        parseDateStr
    } from "@/api/utils";

    import {
        updateCheckAccRemark
    } from "@/api/cashcheck/cashCheck"

    import Cookies from 'js-cookie'

    export default {
        name: "balAccCheck",
        components: {HDatagrid},
        data(){
            return{
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                showBalAccCheckDialog:false,
                fundIdList:[],
                transferTypeList:[],
                checkIsOrNo:[],
                checkAccResultList:[],
                sysDate:Cookies.get('CurrSysDate'),
                accCheckColumns:[
                    {
                        title: "日期",
                        key: "curr_date",
                        width: 100,
                        align: 'right',
                        ellipsis: true
                    },{
                        title: "产品编号",
                        key: "fund_id",
                        align: 'right',
                        hiddenCol:true,
                        ellipsis: true
                    },{
                        title: "产品代码",
                        key: "fund_code",
                        align: 'right',
                        sortable:true,
                        ellipsis: true
                    },{
                        title: "产品名称",
                        key: "fund_name",
                        align: 'left',
                        ellipsis: true
                    },{
                        title: "划款模式",
                        key: "transfer_type",
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let transferType = showText(rowData.transfer_type,this.transferTypeList)
                            return h("span",{},transferType)
                        }
                    },{
                        title: "是否需要日初对账",
                        key: "init_check_acc_flag",
                        align: 'left',
                        width: 120,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let initCheckAccFlag = showText(rowData.init_check_acc_flag,this.checkIsOrNo)
                            return h("span",{},initCheckAccFlag)
                        }
                    },{
                        title: "日初对账结果",
                        key: "init_check_acc_result",
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) => {
                            const rowData = params.row;
                            let initChckAccResult = showText(rowData.init_check_acc_result,this.checkAccResultList)
                            return h("span",{},initChckAccResult)
                        }
                    },{
                        title: "日终对账结果",
                        key: "final_check_acc_result",
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) => {
                            const rowData = params.row;
                            let finalChckAccResult = showText(rowData.final_check_acc_result,this.checkAccResultList)
                            return h("span",{},finalChckAccResult)
                        }
                    },{
                        title: "T-1日日终账套余额",
                        key: "account_last_balance",
                        align: 'right',
                        ellipsis: true,
                        width:120,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let accountLastBalance = formatMoney(rowData.account_last_balance)
                            return h("span",{style:{fontWeight:'bold'}},accountLastBalance)
                        }
                    },{
                        title: "日初账套余额",
                        key: "account_init_balance",
                        align: 'right',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let accountInitBalance = formatMoney(rowData.account_init_balance)
                            return h("span",{style:{fontWeight:'bold'}},accountInitBalance)
                        }
                    },{
                        title: "日初证券清算款",
                        key: "stock_clear_balance",
                        align: 'right',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let stockClearBalance = formatMoney(rowData.stock_clear_balance)
                            return h("span",{style:{fontWeight:'bold'}},stockClearBalance)
                        }
                    },{
                        title: "日初投资系统可用",
                        key: "o32_init_balance",
                        align: 'right',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let o32InitBalance = formatMoney(rowData.o32_init_balance)
                            return h("span",{style:{fontWeight:'bold'}},o32InitBalance)
                        }
                    },{
                        title: "投资系统当前资金冻结金额",
                        key: "o32_init_frozen_balance",
                        align: 'right',
                        ellipsis: true,
                        width:130,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let o32InitFrozenBalance = formatMoney(rowData.o32_init_frozen_balance)
                            return h("span",{style:{fontWeight:'bold'}},o32InitFrozenBalance)
                        }
                    },{
                        title: "日初差额",
                        key: "init_diff_balance",
                        align: 'right',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let initDiffBalance = formatMoney(rowData.init_diff_balance)
                            return h("span",{style:{fontWeight:'bold'}},initDiffBalance)
                        }
                    },{
                        title: "日初备注",
                        key: "init_check_acc_remark",
                        align: 'left',
                        ellipsis: true
                    },{
                        title: "日终账套余额",
                        key: "account_final_balance",
                        align: 'right',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let accountFinalBalance = formatMoney(rowData.account_final_balance)
                            return h("span",{style:{fontWeight:'bold'}},accountFinalBalance)
                        }
                    },{
                        title: "日终投资系统可用",
                        key: "o32_final_balance",
                        align: 'right',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let o32FinalBalance = formatMoney(rowData.o32_final_balance)
                            return h("span",{style:{fontWeight:'bold'}},o32FinalBalance)
                        }
                    },{
                        title: "投资系统日终资金冻结金额",
                        key: "o32_final_frozen_balance",
                        align: 'right',
                        ellipsis: true,
                        width:130,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let o32FinalFrozenBalance = formatMoney(rowData.o32_final_frozen_balance)
                            return h("span",{style:{fontWeight:'bold'}},o32FinalFrozenBalance)
                        }
                    },{
                        title: "日终差额",
                        key: "final_diff_balance",
                        align: 'right',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let finalDiffBalance = formatMoney(rowData.final_diff_balance)
                            return h("span",{style:{fontWeight:'bold'}},finalDiffBalance)
                        }
                    },{
                        title: "日终备注",
                        key: "final_check_acc_remark",
                        align: 'left',
                        ellipsis: true
                    }
                ],
                queryForm:{
                    fundIds:'',
                    beginDate:Cookies.get('CurrSysDate'),
                    endDate:Cookies.get('CurrSysDate')
                },
                balAccCheck:{
                    fund_id:'',
                    curr_date:'',
                    fund_code:'',
                    fund_name:'',
                    init_diff_balance:'',
                    init_check_acc_remark:'',
                    final_diff_balance:'',
                    final_check_acc_remark:''
                }
            }
        },
        watch:{
            showBalAccCheckDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
            settleDate:{
                get:function () {
                    let beginDate,endDate;
                    if(this.queryForm.beginDate){
                        beginDate = parseDateString(this.queryForm.beginDate)
                    }else{
                        beginDate = parseDateString(this.sysDate)
                    }
                    if(this.queryForm.endDate){
                        endDate = parseDateString(this.queryForm.endDate)
                    }else{
                        endDate = parseDateString(this.sysDate)
                    }
                    return [beginDate,endDate]
                },
                set:function (newValue) {
                    this.queryForm.beginDate = parseDateStr(newValue[0])
                    this.queryForm.endDate = parseDateStr(newValue[1])
                }
            },
            fundId:{
                get:function () {
                    if(this.queryForm.fundIds){
                        return this.queryForm.fundIds.split(",")
                    }
                    return []
                },
                set:function (newValue) {
                    this.queryForm.fundIds = newValue.join(",")
                }
            }
        },
        activated(){
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => this.fundIdList = res.data)
        },
        mounted(){
            getDictList(90529).then(res => this.checkAccResultList = res.data)
            getDictList(610001).then(res => this.checkIsOrNo = res.data)
            getDictList(90524).then(res => this.transferTypeList = res.data)
            this.re_layout();
        },
        methods:{
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 90;
            },
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            remarkRemark(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录",'错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                this.balAccCheck = selects[0];
                this.$refs.accCheckForm.resetFields();
                this.showBalAccCheckDialog = true;
            },
            cancelCheck(){
                this.showBalAccCheckDialog = false;
            },
            saveRemark(){
                let isValidate = true;
                this.$refs.accCheckForm.validate((validate) => isValidate = isValidate && validate)
                if(!isValidate){
                    this.$alert('输入项不满足要求，请重新检查','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }
                let queryData = this.balAccCheck.fund_id + ',' + this.balAccCheck.curr_date + ',' + this.balAccCheck.init_check_acc_remark + ',' + this.balAccCheck.final_check_acc_remark;
                updateCheckAccRemark(queryData).then(res =>{
                    if(res && res.status == 200){
                        if(res.data.errorNo == '0'){
                            this.$alert('修改对账备注成功','成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.showBalAccCheckDialog = false
                                }
                            }).then(() => {
                                this.showBalAccCheckDialog = false
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    }else{
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText:'确定',
                            type:'error'
                        })
                    }
                })
            }
        }
    }
</script>

<style scoped>

</style>


