<template>
    <div class="page-issuer full-h" id="full-h-wrapper">
        <label style="display:none;"><span></span><input type="text" name="hidden1" ></label>
        <label style="display:none;"><span></span><input type="password" name="hidden2" ></label>
        <h-row>
            <h-col>
                <el-form style="font-size: 12px;" :inline="true" size="mini" class="demo-form-inline" :model="queryForm">
                    <el-form-item label="发行人类型">
                        <el-select v-model="queryForm.issuerType" filterable clearable >
                            <el-option v-for="item in issuerTypeList" :key="item.code" :value="item.code" :label="item.text">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="refreshIssuer">查询</el-button>
                    </el-form-item>
                </el-form>
            </h-col>
        </h-row>

        <div slot="right" class="full-h">
            <div class="ctrls">
                <h-button-group>
                    <h-button icon="add" @click="addIssuer">新增</h-button>
                    <h-button icon="edit" @click="modifyIssuer">修改</h-button>
                    <h-button icon="delete" @click="deleteIssuer">删除</h-button>
                    <h-button icon="android-upload" @click="uploadIssuer">导入</h-button>
                </h-button-group>

                <h-input v-model="queryForm.keyword" :maxlength="32" placeholder="请输入发行人编号或名称" style="width: 215px">
                    <div slot="append" >
                        <a href="javascript:void(0)">
                            <h-icon name="search"  @click.native.stop="refreshIssuer"></h-icon>
                        </a>
                    </div>
                </h-input>
            </div>
            <h-datagrid :height="autoHeight"
                        stripe hasPage highlight-row
                        :on-row-db-click="issuerDbclick" :pageSize="100"
                        :page-size-opts="pageSizeOpts" :columns="rivalColumn"
                        :bind-form="queryForm" url="/systemmgnt/issuer/page.json"
                        ref="datagrid">
            </h-datagrid>
        </div>

        <h-msg-box class="customMsgBox" v-model="showIssuerDialog" width="500" :mask-closable="false">
            <p slot="header">
                <span v-if="actionType == 'add'">新增发行人</span>
                <span v-if="actionType == 'modify'">修改发行人</span>
            </p>
            <h-row>
                <h-col>
                    <h-form :model="issuerInfo" :label-width="100" ref="issuerForm">
                        <h-form-item required label="发行人名称" prop="issuer_name">
                            <h-input v-model="issuerInfo.issuer_name" lengthByByte :maxlength="128"></h-input>
                        </h-form-item>

                        <h-form-item required label="发行人编号" prop="issuer_code">
                            <h-input v-model="issuerInfo.issuer_code" lengthByByte :maxlength="32"></h-input>
                        </h-form-item>

                        <h-form-item label="发行人类型" prop="issuer_type">
                            <h-select v-model="issuerInfo.issuer_type" hideMult isString multiple filterable showBottom>
                                <h-option v-for="item in issuerTypeList" :value="item.code" :key="item.code" :label="item.text"></h-option>
                            </h-select>
                        </h-form-item>

                        <h-form-item label="联系人" prop="linkman">
                            <h-input v-model="issuerInfo.linkman" lengthByByte :maxlength="128"></h-input>
                        </h-form-item>

                        <h-form-item label="联系地址" prop="address">
                            <h-input v-model="issuerInfo.address" lengthByByte :maxlength="128"></h-input>
                        </h-form-item>

                        <h-form-item label="联系电话" prop="phone_no">
                            <h-input v-model="issuerInfo.phone_no" lengthByByte :maxlength="32"></h-input>
                        </h-form-item>

                        <h-form-item label="传真号码" prop="fax_no">
                            <h-input v-model="issuerInfo.fax_no" lengthByByte :maxlength="128"></h-input>
                        </h-form-item>
                    </h-form>
                </h-col>
            </h-row>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="cancelIssuer">取消</h-button>
                <h-button type="primary" @click="submitIssuer">确认</h-button>
            </div>
        </h-msg-box>
        <import-account-transfer :on-import-success="refreshIssuer" ref="issuerUploadDialog"></import-account-transfer>
    </div>
</template>

<script>
    import  {
        showText,
        showTextList,
        getDictList
    } from "@/api/utils";
    import {
        insertIssuer,
        updateIssuer,
        deleteIssuer
    } from "@/api/systemmgnt/issuer";
    import ImportAccountTransfer from "../../../components/ImportAccountTransfer";
    import HDatagrid from "../../../components/HDatagrid";
    export default {
        name: "issuer",
        data() {
            return {
                autoHeight: 0,
                pageSizeOpts: [25,50,75,100],
                actionType: '',
                showIssuerDialog: false,
                issuerTypeList: [],
                rivalColumn: [
                    {
                        title: '发行人序号',
                        key: 'issuer_id',
                        hiddenCol: true,
                        align: '',
                        ellipsis: true
                    }, {
                        title: '发行人编号',
                        key: 'issuer_code',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '发行人名称',
                        key: 'issuer_name',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '发行人类型',
                        key: 'issuer_type',
                        align: 'left',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let issuerType = showTextList(rowData.issuer_type, this.issuerTypeList);
                            return h("span", {}, issuerType);
                        }
                    }, {
                        title: '联系人',
                        key: 'linkman',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '联系地址',
                        key: 'address',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '联系电话',
                        key: 'phone_no',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '传真号码',
                        key: 'fax_no',
                        align: 'left',
                        ellipsis: true
                    }
                ],
                queryForm: {
                    issuerType: '',
                    keyword: ''
                },
                issuerInfo: {
                    issuer_id: '',
                    issuer_code: '',
                    issuer_name: '',
                    issuer_type: '',
                    linkman: '',
                    address: '',
                    phone_no: '',
                    fax_no: ''
                }
            }
        },
        mounted() {
            getDictList("10090").then(res => this.issuerTypeList = res.data);
            this.re_layout();
        },
        methods: {
            re_layout() {
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 90;
            },
            refreshIssuer() {
                this.$refs.datagrid.dataChange(1);
            },
            addIssuer() {
                this.issuerInfo = {
                    issuer_id: '',
                        issuer_code: '',
                        issuer_name: '',
                        issuer_type: '',
                        linkman: '',
                        address: '',
                        phone_no: '',
                        fax_no: ''
                };
                this.$refs.issuerForm.resetFields();
                this.actionType = 'add';
                this.showIssuerDialog = true;
            },
            modifyIssuer() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.$alert('请选择一条发行人信息!', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }

                this.issuerDbclick(selects[0]);
            },
            issuerDbclick(data) {
                this.issuerInfo = data;
                this.actionType = 'modify';
                this.showIssuerDialog = true;
            },
            deleteIssuer() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.$alert('请选择一条发行人信息!', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }

                this.$confirm('确定要删除该发行人?', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteIssuer(selects[0].issuer_id).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '成功', {
                                confirmButtonText: '确定',
                                type: 'success'
                            });
                            this.refreshIssuer();
                        }else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    })
                }).catch(() => {

                })
            },
            uploadIssuer() {
                this.$refs.issuerUploadDialog.showDialog('8', null);
            },
            cancelIssuer() {
                this.showIssuerDialog = false;
            },
            submitIssuer() {
                let isValidate = true;
                this.$refs.issuerForm.validate((validate) => isValidate = isValidate && validate);
                if (!isValidate) {
                    this.$alert('输入项不满足要求,请重新检查', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    })
                    return;
                }

                if (this.actionType == 'add') {
                    insertIssuer(this.issuerInfo).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '确定', {
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                    this.showIssuerDialog = false;
                                }
                            }).then(() => {
                                this.showIssuerDialog = false;
                            });
                            this.refreshIssuer();
                        } else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    })
                } else if(this.actionType == 'modify') {
                    updateIssuer(this.issuerInfo).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '确定', {
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                    this.showIssuerDialog = false;
                                }
                            }).then(() => {
                                this.showIssuerDialog = false;
                            });
                            this.refreshIssuer();
                        } else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    })
                }
            }
        },
        components: {HDatagrid, ImportAccountTransfer}
    }
</script>

<style lang="less" rel="stylesheet/less">
    .page-issuer{
        .ctrls {
            display: flex;
            justify-content: space-between;
            height: 34px;
        .h-input-type {
            height: 28px;
        }
        }
    }
</style>


