//系统对账
import fetch from '@/api/httpFetch.js'

//关联方资金报表  资金调整查询
export function accountCashQuery(fundId,queryDate) {
    return fetch.post("/cashcheck/relatedCash/accountCashQuery.json", {
        fundId: fundId,
        queryDate:queryDate,
        accountClass: '1,2'
    });
}

//关联方资金报表  资金调整更新
export function updateAccountCash(accountCashList) {
    return fetch.post("/cashcheck/relatedCash/updateAccountCash.json", {
        accountCashList: accountCashList
        }, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }});
}

//备付金报表 参数查询
export function getSysParam(paramNo) {
    return fetch.post("/common/getSysParam.json", {
        paramNo: paramNo
    });
}

//备付金报表 参数更新
export function updateSysParam(paramNo,paramValue) {
    return fetch.post("/common/updateSysParam.json", {
        paramNo: paramNo,
        paramValue :paramValue
    });
}

//备付金报表 判断是否为当月最后2个交易日 当月最开始2个交易日
export function checkModifyDay(queryDate) {
    return fetch.post("/cashcheck/fundReserve/checkModifyDay.json", {
        queryDate: queryDate
    });
}

//备付金报表 更新备付金报表
export function updateFundReserve(fundReserve) {
    return fetch.post("/cashcheck/fundReserve/updateAdjustValue.json",
        fundReserve
        , {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }});
}

//对账查询日初、日终备注更新
export function updateCheckAccRemark(queryData){
    return fetch.post('/cashcheck/query/checkAccRemarkUpdate.json',{queryData:queryData})
}

//日初确认对账
export function initAccConfirm() {
    return fetch.post('/cashcheck/daily/initAccConfirm.json')
}

export function checkData() {
    return fetch.post('/cashcheck/daily/checkData.json')
}

//日初对账说明更新
export function updateInitAccRemark(queryData) {
    return fetch.post('/cashcheck/daily/initAccRemarkUpdate.json',{queryData:queryData})
}

export function finalAccConfirm() {
    return fetch.post('/cashcheck/daily/finalAccConfirm.json')
}

export function cashInit() {
    return fetch.post('/cashcheck/daily/cashInit.json')
}

export function updateFinalAccRemark(queryData) {
    return fetch.post('/cashcheck/daily/finalAccRemarkUpdate.json',{queryData:queryData})
}

export function queryAllocInit(initQueryDate,initCkaccStatus,initFundIds,pageNo,pageSize) {
    return fetch.post('/cashcheck/daily/allocInit.json',{initQueryDate:initQueryDate,initCkaccStatus:initCkaccStatus,initFundIds:initFundIds,pageNo:pageNo,pageSize:pageSize})
}

export function queryAllocFinal(finalQueryDate,finalCkaccStatus,finalFundIds,pageNo,pageSize) {
    return fetch.post('/cashcheck/daily/allocFinal.json',{finalQueryDate:finalQueryDate,finalCkaccStatus:finalCkaccStatus,finalFundIds:finalFundIds,pageNo:pageNo,pageSize:pageSize})
}


