<template>
    <div style="height:100%;">
        <label style="display:none;"><span></span><input type="text" name="hidden1" ></label>
        <label style="display:none;"><span></span><input type="password" name="hidden2" ></label>

        <h-row>
            <h-col span="24" style="padding-left: 6px;padding-right: 6px">
                <el-input  size="mini"  placeholder="关键字" v-model="filterText" class="input-with-select">
                </el-input>
            </h-col>
        </h-row>

        <div class="auto-height" style="margin-top:10px;">
            <el-scrollbar style="height: 100%;" class="h-scrollbar">
                <el-tree :data="businTree" :props="treeProps" @node-click="onBusinClick" ref="businTree" node-key="code" :default-expand-all="true" :expand-on-click-node="false" :filter-node-method="filterNode">
                <span  class="custom-tree-node" slot-scope="{ node, data }" style="width: 100%">
                    <span >{{ node.label }}</span>
                </span>
                </el-tree>
            </el-scrollbar>
        </div>
    </div>
</template>

<script>

    import {getDictList} from '@/api/utils' ;

    export default {
        name: "BusinTree",
        data() {
            return {
                businTree: [{text:'   加载中              ... ',code:''}],
                filterText:'',
                setQueryMsg: false,
                treeProps:{
                    label:"text",
                    children:'children'
                },
                businQuery: {
                    trust_flag:''
                }
            }
        },
        props: {
            trustFlag: {
                type: Boolean,
                default: false
            },
            onBusinClick: {
                type: Function,
                default(data) {

                }
            },
            showAll:{
                type: Boolean,
                defalut:false
            },
            onBusinQuery:{
                type: Function,
                default(data) {

                }
            }
        },
        watch: {
            filterText(val) {
                this.$refs.businTree.filter(val);
            },
        },
        mounted() {
            this.querydeal()
        },
        activated(){
            this.handleConditionQuery();
        },
        methods: {
            filterNode(value, data) {
                if (!value) return true;
                return data.text.indexOf(value) !== -1;
            },
            openQuery() {
                this.setQueryMsg = true;
            },
            handleCancel() {
                this.setQueryMsg = false;
            },
            querydeal(){
                if(this.trustFlag){
                    this.businQuery.trust_flag = '1';
                }
            },
            handleConditionQuery(){
                getDictList(90523).then(res => {
                    if (res.data.length>0) {
                        if(this.showAll){
                            let businIds = res.data.reduce(function(x, y){
                                return {code:x.code + ',' + y.code}
                            } ).code;
                            this.businTree=[{text:'全部',code:businIds,children:res.data}]
                            this.onBusinClick({code:businIds});
                        }else{
                            this.businTree =res.data
                            this.onBusinClick(this.businTree[0]);
                        }
                        this.onBusinQuery(this.businTree)
                        this.filterText = ''
                    }else{
                        this.businTree= [{text:'暂无数据',code:''}]
                        this.onBusinClick({code:''});
                    }
                }).catch(error => {
                    console.log(error);
                });
                this.setQueryMsg = false;
            }
        }

    }
</script>

<style scoped>
    .auto-height {
        height: calc(100% - 38px);
    }
</style>

<style lang="less" rel="stylesheet/less">
    .el-scrollbar.h-scrollbar {
        .el-scrollbar__wrap{
            overflow-x: hidden;
        }
    }
    .el-tree>.el-tree-node {
        min-width: 100%;
        display: inline-block;
    }
</style>


