import fetch from "@/api/httpFetch";

export function getCheckDetail(checkId,object_type) {
    return fetch.post('/check/getCheckDetail.json',{checkId:checkId,objectType:object_type});
}
export function submitReview(checkIds,isPass,objectType,remark) {
    return fetch.post('/check/submitReview.json',{checkIds:checkIds,isPass:isPass,objectType:objectType,remark:remark});
}





