import fetch from '../../../api/httpFetch.js'
import { deepCopy } from '../../../utils/func'
import {
  getShowColumn,
  setShowColumn,
  parseDateString
} from '@/api/utils'

let service = {
  getShowColumn(gridName) {
    return getShowColumn(gridName)
  },
  getDatas(url, fundData, pageNo, pageSize) {
    let params = {
      pageNo,
      pageSize
    }
    Object.assign(params, fundData)
    return fetch.post(url, params)
  },
  /**
   * 将一维数组转换成 tree 数组，根据 parentId
   */
  makeTreeData(list) {
    let treeData = []
    for (let data of list) {
      if (treeData.length === 0) {
        treeData.push(data)
      } else {
        let find = this.findDataById(treeData, data.transfer_ins_batch_no)
        if (find) {
          if (!find.children) {
            find.children = []
          }
          find.children.push(data)
        } else {
          treeData.push(data)
        }
      }
    }
    return treeData
  },
  makeDataHaveId(datas, idField) {
    let dosome = (datas) => {
      for (let data of datas) {
        data.id = data[idField]
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
  },
  makeDataChecked(datas, checked = false) {
    let dosome = (datas) => {
      for (let data of datas) {
        data.checked = checked
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
    return datas
  },
  makeDataCheckedWithoutExcepts(datas, checked = false, exceptIds = []) {
    let dosome = (datas) => {
      for (let data of datas) {
        if (exceptIds.indexOf(data.id) >= 0) {
          continue
        }
        data.checked = checked
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
    return datas
  },
  makeDataExpand(datas, expand = false) {
    let dosome = (datas) => {
      for (let data of datas) {
        data.expand = expand
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
    return datas
  },
  getFirstLevelDatas(datas) {
    let copyed = deepCopy(datas)
    let solved = []
    for (let data of copyed) {
      data.children = []
      solved.push(data)
    }
    return solved
  },
  getCheckedDatas(datas) {
    let list = []
    let dosome = (datas) => {
      for (let data of datas) {
        if (data.checked) {
          list.push(data)
        }
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
    return list
  },
  findNode(nodes, id, find) {
    // 如果数组为空，则不进行继续
    if (!nodes || nodes.length === 0) {
      return null
    }
    // 遍历数组中的每一个节点，判断节点 id 是否相同，如果相同则跳出遍历，否则进入该节点的子节点进行递归的判断
    for (let i = 0; i < nodes.length; i++) {
      if (nodes[i].transfer_ins_id === id) {
        find.obj = nodes[i]
        break
      }
      if (nodes[i].children) {
        this.findNode(nodes[i].children, id, find)
      }
    }
  },
  findDataById(datas, id) {
    let find = {}
    this.findNode(datas, id, find)
    if (find.obj) {
      return find.obj
    }
    return null
  },
  flatTreeDatas(datas) {
    let list = []
    let dosome = (datas) => {
      for (let data of datas) {
        list.push(data)
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
    return list
  },
  getFlatIndexById(datas, id) {
    // let solved = this.flatTreeDatas(datas)
    return datas.findIndex(e => e.id === id)
  },
  sort(datas, field, status = 'asc', isNum = false) {
    if (['asc', 'desc'].indexOf(status) < 0) {
      return datas
    }
    let compare = (property, asc = true) => {
      return function(obj1, obj2) {
        var value1 = obj1[property]
        var value2 = obj2[property]
        if (typeof (value1) === 'number' && typeof (value2) === 'number') {
          return asc ? value1 - value2 : value2 - value1
        } else {
          if (isNum) {
            value1 = parseFloat(value1)
            value2 = parseFloat(value2)
            return asc ? value1 - value2 : value2 - value1
          } else {
            value1 += ''
            value2 += ''
            return asc ? value1.localeCompare(value2) : value2.localeCompare(value1)
          }
        }
      }
    }
    return datas.sort(compare(field, status === 'asc'))
  }
}

export default service



