import fetch from "@/api/httpFetch";

export function addInterfaceFile(params) {
    return fetch.post("/systemmgnt/interfaceFile/addInterfaceFile.json",params,{
        headers: {
            'Content-Type':'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    })
}

export function updateInterfaceFile(params) {
    return fetch.post("/systemmgnt/interfaceFile/updateInterfaceFile.json",params,{
        headers:{
            'Content-Type':'application/json;charset=UTF-8'
        },
        transformRequest:function (req) {
            return JSON.stringify(req);
        }
    })
}

export function deleteInterfaceFile(fileType,fileName) {
    return fetch.post("/systemmgnt/interfaceFile/deleteInterfaceFile.json",{fileType:fileType,fileName:fileName})
}


