import fetch from "@/api/httpFetch"

export function modifyNewtranTask(params) {
    return fetch.post('/systemmgnt/newtranTask/newtranTaskModify.json', params, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest(req) {
            return JSON.stringify(req)
        }
    })
}

export function copyNewtranTask(params) {
    return fetch.post('/systemmgnt/newtranTask/newtranTaskCopy.json', params, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest(req) {
            return JSON.stringify(req)
        }
    })
}

export function findTaskConfigList(moduleName, className) {
    return fetch.post('/systemmgnt/newtranTask/findTaskConfigList.json', {moduleName: moduleName, className: className})
}

export function batchUpdateTaskConfig(params) {
    return fetch.post('/systemmgnt/newtranTask/batchUpdateTaskConfig.json', params, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest(req) {
            return JSON.stringify(req)
        }
    })
}



