<template>
    <div id="review-full-h-wrapper" class="page-fundaccount-review">
        <!--<div>-->
       <review-table  :on-selection-change="rowClick" :clear-data="clearData" :query-check="queryCheck" ref="reviewTable"></review-table>

        <div class="account-detail" v-if="showFund||accountList.length>0" id="wrapper-review-detail" v-bind:style="isScrollBar">
            <div class="wrapper">
                <header v-if="showFund" class="headline">
                    <i class="icon-l"></i>
                    <span class="title">产品信息</span>
                </header>
                <h-row v-if="showFund">
                    <h-col span="6" class-name="inner2">
                        <div class="item">
                            <div class="label">产品名称</div>
                            <div class="value" :title="fundInfo.fund_name">{{fundInfo.fund_name}}</div>
                        </div>
                        <div class="item">
                            <div class="label">产品全称</div>
                            <div class="value" :title="fundInfo.fund_caption">{{fundInfo.fund_caption}}</div>
                        </div>
                        <div class="item">
                            <div class="label">产品代码</div>
                            <div class="value" :title="fundInfo.fund_code">{{fundInfo.fund_code}}</div>
                        </div>
                        <div class="item">
                            <div class="label">实际产品代码</div>
                            <div class="value" :title="fundInfo.actual_code">{{fundInfo.actual_code}}</div>
                        </div>
                        <div class="item">
                            <div class="label">产品类型</div>
                            <div class="value" :title="showText(fundInfo.fund_type,fundTypeList)">{{showText(fundInfo.fund_type,fundTypeList)}}</div>
                        </div>
                        <div class="item">
                            <div class="label">产品状态</div>
                            <div class="value" :title="showText(fundInfo.fund_status,fundStatusList)">{{showText(fundInfo.fund_status,fundStatusList)}}
                            </div>
                        </div>
                        <div class="item">
                            <div class="label">托管人</div>
                            <div class="value" :title="showText(fundInfo.fund_trust_bank,custodianList)">{{showText(fundInfo.fund_trust_bank,custodianList)}}</div>
                        </div>

                    </h-col>
                    <h-col span="6" class-name="inner2">
                        <div class="item">
                            <div class="label">托管结算模式</div>
                            <div class="value" :title="showText(fundInfo.fund_settle_type,fundSettleTypes)">
                                {{showText(fundInfo.fund_settle_type,fundSettleTypes)}}
                            </div>
                        </div>
                        <div class="item">
                            <div class="label">TA金额交收模式</div>
                            <div class="value" :title="showText(fundInfo.ta_settle_type,taSettleTypes)">{{showText(fundInfo.ta_settle_type,taSettleTypes)}}
                            </div>
                        </div>
                        <div class="item">
                            <div class="label">深圳配售对象编码</div>
                            <div class="value" :title="fundInfo.object_code">{{fundInfo.object_code}}</div>
                        </div>
                        <div class="item">
                            <div class="label">上海股东代码</div>
                            <div class="value" :title="fundInfo.stockholder_id">{{fundInfo.stockholder_id}}</div>
                        </div>
                        <div class="item">
                            <div class="label">TA深圳备付金账号</div>
                            <div class="value" :title="fundInfo.sz_ta_account">{{fundInfo.sz_ta_account}}</div>
                        </div>

                        <div class="item">
                            <div class="label">TA上海备付金账号</div>
                            <div class="value" :title="fundInfo.sh_ta_account">{{fundInfo.sh_ta_account}}</div>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner2">
                        <div class="item">
                            <div class="label">银行间ID</div>
                            <div class="value" :title="fundInfo.interbank_id">{{fundInfo.interbank_id}}</div>
                        </div>
                        <div class="item">
                            <div class="label">是否划款</div>
                            <div class="value" :title="showText(fundInfo.transfer_flag,transferFlagList)">{{showText(fundInfo.transfer_flag,transferFlagList)}}
                            </div>
                        </div>
                        <div class="item">
                            <div class="label">划款单编号</div>
                            <div class="value" :title="fundInfo.paper_no">{{fundInfo.paper_no}}</div>
                        </div>

                        <div class="item">
                            <div class="label">产品来源</div>
                            <div class="value" :title="showText(fundInfo.data_source,dataSourceList)">{{showText(fundInfo.data_source,dataSourceList)}}
                            </div>
                        </div>
                        <div class="item">
                            <div class="label">产品邮箱</div>
                            <div class="value" :title="fundInfo.fund_mail">{{fundInfo.fund_mail}}</div>
                        </div>
                        <div class="item">
                            <div class="label">文件</div>
                            <div class="value" :title="fundInfo.file_name">{{fundInfo.file_name}}</div>
                            <el-button v-show="fundInfo.file_id" class="ml5" size="mini"
                                       type="ghost"
                                       @click="downloadFile">下载
                            </el-button>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner2">
                        <div class="item">
                            <div class="label">主办会计</div>
                            <div class="value" :title="showText(fundInfo.fund_accountant,operatorList)">{{showText(fundInfo.fund_accountant,operatorList)}}
                            </div>
                        </div>
                        <div class="item">
                            <div class="label">投资经理</div>
                            <div class="value" :title="showText(fundInfo.fund_mgr_operator,operatorList)">{{showText(fundInfo.fund_mgr_operator,operatorList)}}</div>
                        </div>
                        <div class="item">
                            <div class="label">成立日期</div>
                            <div class="value" :title="fundInfo.establish_date">{{fundInfo.establish_date}}</div>
                        </div>
                        <div class="item">
                            <div class="label">结束日期</div>
                            <div class="value" :title="fundInfo.expire_date">{{fundInfo.expire_date}}</div>
                        </div>
                        <div class="item">
                            <div class="label">公司签章</div>
                            <div class="value" :title="fundInfo.stamp_name">{{fundInfo.stamp_name}}</div>
                        </div>

                    </h-col>
                </h-row>

                <h-row :gutter="4">
                    <h-col span="6" class="mt6" v-for="item in accountList"
                           :key="item.bank_account_id">
                        <div>
                            <header class="headline">
                                <i class="icon-l"></i>
                                <span class="title flex-grow">{{item.bank_account_name}}</span>
                            </header>

                            <div class="inner2">
                                <div class="item" v-if="item.account_class">
                                    <div class="label">账户用途</div>
                                    <div v-if="!showSelfSupport" class="value" :title="showAccountClass(item.account_class, item.fund_settle_type)">{{showAccountClass(item.account_class, item.fund_settle_type)}}
                                    </div>
                                </div>
                                <div class="item" v-if="item.bic_code">
                                    <div class="label">银行总行</div>
                                    <div class="value" :title="showText(item.bic_code, bankList)">{{showText(item.bic_code, bankList)}}</div>
                                </div>
                                <div class="item" v-if="item.bank_account_no">
                                    <div class="label">银行账号</div>
                                    <div class="value" :title="item.bank_account_no">{{item.bank_account_no}}</div>
                                </div>
                                <div class="item" v-if="item.sub_bank_no">
                                    <div class="label">开户行名称</div>
                                    <div class="value" :title="item.sub_bank_no">{{item.sub_bank_no}}</div>
                                </div>
                                <div class="item" v-if="item.pay_no">
                                    <div class="label">大额支付号</div>
                                    <div class="value" :title="item.pay_no">{{item.pay_no}}</div>
                                </div>

                                <div v-if="item.bank_account_type" class="item">
                                    <div class="label">账户类型：</div>
                                    <div class="value" :title="showText(item.bank_account_type,accountTypeList)">
                                        {{showText(item.bank_account_type,accountTypeList)}}
                                    </div>
                                </div>
                                <div v-if="item.provinceCity" class="item">
                                    <div class="label">开户城市 ：</div>
                                    <div class="value" :title="showCity(item.provinceCity,cityTree)">{{showCity(item.provinceCity,cityTree)}}
                                    </div>
                                </div>
                                <div v-if="item.custodial_no" class="item">
                                    <div class="label">托管人 ：</div>
                                    <div class="value" :title="showText(item.custodial_no,custodianList)">{{showText(item.custodial_no,custodianList)}}
                                    </div>
                                </div>
                                <div v-if="item.currency_no" class="item">
                                    <div class="label">币种 ：</div>
                                    <div class="value" :title="showText(item.currency_no,currencyNoList)">{{showText(item.currency_no,currencyNoList)}}
                                    </div>
                                </div>
                                <div v-if="item.holder_account" class="item">
                                    <div class="label" v-if="item.account_class=='5'">上清持有人账号：
                                    </div>
                                    <div class="label" v-else>中债持有人账号：</div>
                                    <div class="value" :title="item.holder_account">{{item.holder_account}}</div>
                                </div>
                                <div v-if="item.broker_id" class="item">
                                    <div class="label">期货公司：</div>
                                    <div class="value" :title="showText(item.broker_id.toString(),brokerList)">
                                        {{showText(item.broker_id.toString(),brokerList)}}
                                    </div>
                                </div>
                                <div v-if="item.bank_futures_type" class="item">
                                    <div class="label">银期方式：</div>
                                    <div class="value" :title="showText(item.bank_futures_type,bankFuturesTypeList)">
                                        {{showText(item.bank_futures_type,bankFuturesTypeList)}}
                                    </div>
                                </div>
                                <div v-if="item.futures_mid_account" class="item">
                                    <div class="label">期货中间账户：</div>
                                    <div class="value" :title="item.futures_mid_account">{{item.futures_mid_account}}</div>
                                </div>
                                <div v-if="item.bank_mid_account" class="item">
                                    <div class="label">托管人中间账户：</div>
                                    <div class="value" :title="item.bank_mid_account">{{item.bank_mid_account}}</div>
                                </div>
                            </div>
                        </div>
                    </h-col>
                </h-row>
                <header v-if="modify_desc" class="headline mt10 pt10 border-dashed-top">
                    <i class="icon-l"></i>
                    <span class="title">变更内容</span>
                </header>
                <h-row v-if="modify_desc">
                    <h-col span="6" class="inner2" v-for="(item, index) in modify_desc.split(';')"
                           :key="index">
                        <div class="item">
                            <div class="label">{{decodeURIComponent(item.split(":")[0])}}</div>
                            <div class="value" :title="decodeURIComponent(item.split(':')[1])">{{showReal(decodeURIComponent(item.split(":")[0]),decodeURIComponent(item.split(":")[1]))}}</div>
                        </div>
                    </h-col>
                </h-row>
            </div>

        </div>
    </div>
</template>

<script>
  import { mapState } from 'vuex'
    import HDatagrid from '@/components/HDatagrid'
    import {
        operatorList,
        getDictList,
        getFundDetail,
        getFundInfo,
        getBankList,
        getBrokerList,
        getCityTree,
        getKeys,
        parseDateString,
        parseDateStr,
        encrypt,
        showCity,
        showText
    } from '@/api/utils'

    import {
        getCheckDetail,
        submitReview
    } from '@/api/fundAccount/review'
    import ReviewTable from "../../../components/ReviewTable";

    export default {
        name: "review",
        data() {
            return {
                autoHeight: 0,
                pageSizeOpts: [25, 50, 75, 100],
                queryCheck: {
                    checkStatus: '1',inputOperator:null,
                    objectType: 1
                },
                showSelfSupport: false,
                showFund: false,
                fundInfo: {
                    fund_id: null,
                    fund_name: "",
                    fund_code: '',
                    fund_mgr_operator: "",
                    fund_status: '',
                    fund_trust_bank: '',
                    fund_type: '',
                    establish_date: '',
                    expire_date: '',
                    interbank_id: '',
                    paper_no: '',
                    stockholder_id: '',
                    object_code: '',
                    sh_ta_account: '',
                    sz_ta_account: '',
                    transfer_flag: '',
                    file_name: '',
                    data_source: '',
                    fund_mail: '',
                    fund_accountant: '',
                    ta_settle_type: '',
                    fund_settle_type: '',
                    trust_flag: ''
                },
                isScrollBar: {
                    // border: 'solid 1px #999999',
                    marginTop: '10px',
                    overflow: 'auto',
                    maxHeight: '',
                },
                fundStatusList: [
                    {code: "1", text: "正常"},
                    {code: "2", text: "注销"},
                    {code: "3", text: "冻结"},
                ],
                transferFlagList: [
                    {code: "1", text: "是"},
                    {code: "0", text: "否"},
                ],
                accountList: [],
                bankList: [],
                custodianList: [],
                currencyNoList:[],
                operatorList: [],
                fundTypeList: [],
                dataSourceList: [],
                fundSettleTypes: [],
                taSettleTypes: [],
                accountTypeList: [],
                accountStatusList: [],
                bankFuturesTypeList: [],
                brokerList: [],
                cityTree: [],
                accountClassList: [{code: '1', text: '普通账户'},
                    {code: '2', text: '托管账户'},
                    {code: '3', text: '三方存管账户'},
                    {code: '4', text: '存款账户'},
                    {code: '5', text: '上清DVP账户'},
                    {code: '6', text: '中债DVP账户'},
                    {code: '8', text: '期货资金账户'}],
            }
        },
        activated() {
            this.$refs.reviewTable.refresh();
        },
        computed: {
            ...mapState({
                theme: state => state.theme.theme
            }),
            modify_desc() {
                if (this.showFund) {
                    return this.fundInfo.modify_desc
                } else {
                    if (this.accountList && this.accountList.length > 0) {
                        return this.accountList[0].modify_desc
                    }
                }
            }
        },
        watch: {
        },
        created() {
            getDictList(610087).then(res => this.fundTypeList = res.data);
            getDictList(90531).then(res => this.fundSettleTypes = res.data);
            getDictList(90530).then(res => this.taSettleTypes = res.data);
            getDictList(90528).then(res => this.dataSourceList = res.data);
            getDictList(60058).then(res => this.accountTypeList = res.data);
            getDictList(200008).then(res => this.accountStatusList = res.data);
            getDictList(90532).then(res => this.bankFuturesTypeList = res.data);
            getDictList(610406).then(res => this.currencyNoList = res.data);

            operatorList().then(res => {
                if (res.data && res.data.length > 0) {
                    this.operatorList = res.data;
                }
            }).catch(error => {
                console.log(error);
            });

            getBankList().then(res => {
                if (res.data && res.data.length > 0) {
                    this.bankList = res.data;
                }
            }).catch(error => {
                console.log(error);
            });
            getBankList("1").then(res => {
                if (res.data && res.data.length > 0) {
                    this.custodianList = res.data;
                }
            }).catch(error => {
                console.log(error);
            });


            getBrokerList().then(res => {
                if (res.data && res.data.length > 0) {
                    this.brokerList = res.data;
                }
            }).catch(error => {
                console.log(error);
            });

        },
        mounted() {
            getCityTree().then(res => this.cityTree = res.data);
        },
        components: {
            ReviewTable,
            HDatagrid
        },
        methods: {
            re_layout() {
                let el = this.$el.querySelector(".account-detail .wrapper")
                let wrapper0Height = el ? el.clientHeight : 0;
                let fullWrapperHeight = this.$el.clientHeight;
                if(wrapper0Height > fullWrapperHeight / 2){
                    this.isScrollBar.maxHeight = fullWrapperHeight / 2 + 'px';
                    wrapper0Height = fullWrapperHeight/2;
                }
                this.autoHeight = this.$el.offsetHeight - wrapper0Height - 70 ;

                this.$refs.reviewTable.resizeTable(this.autoHeight );
            },
            showText(value, list) {
                return showText(value, list)
            },
            showReal(label , value) {
                if (label == "币种") {
                    let str = value.replace(/CNY/g, '人民币');
                    let str1 = str.replace(/USD/g, '美元');
                    return str1;
                } else {
                    return value;
                }
            },
            showCity:showCity,
            clearData(){
                this.fundInfo = {}
                this.showFund = false
                this.accountList = []
                this.$nextTick(this.re_layout);
            },
            downloadFile() {
                if (this.fundInfo.file_id) {
                    window.open(
                        window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=fund&fileId=' +
                        this.fundInfo.file_id)
                    // downloadFile(this.fundInfo.file_id);
                } else {
                    this.openMask();
                    this.$alert('没有可下载的文件', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                }
            },
            rowClick(arr) {
                if (arr) {
                    getCheckDetail(arr.check_id, arr.object_type).then(res => {
                        if (res.data && res.data.code == '0') {
                            if (arr.object_type == 1) {
                                this.showFund = true;
                                this.fundInfo = res.data.data;
                                this.fundInfo.establish_date =
                                    parseDateString(res.data.data.establish_date);
                                this.fundInfo.expire_date = parseDateString(res.data.data.expire_date);
                                this.accountList = res.data.data.accountList;
                                this.accountList.forEach(x => {
                                    if (x.city_no) {
                                        x.provinceCity = [x.province_code, x.city_no];
                                    }
                                });
                            } else {
                                this.showFund = false;
                                this.accountList = [res.data.data];
                                this.accountList.forEach(x => {
                                    if (x.city_no) {
                                        x.provinceCity = [x.province_code, x.city_no];
                                    }
                                });
                            }
                            this.$nextTick(this.re_layout);
                        } else {
                            this.openMask();
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error',
                                callback:action => {
                                    this.closeMask();
                                }
                            })/*.then(() => {
                                this.closeMask();
                            }) */
                        }

                    }).catch(error => {
                    });
                }
            },
            showAccountClass(accountClass, fundSettleType) {
                if(!fundSettleType) {
                    fundSettleType = this.fundInfo.fund_settle_type;
                }
                if(accountClass == '2' && (fundSettleType == '1' || fundSettleType == '4')){
                    return '自营账户';
                }else {
                    return this.showText(accountClass, this.accountClassList);
                }
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less">
    .page-fundaccount-review {
        .item {
            margin-bottom: 8px;
            padding-right: 10px;
        }

        // .account-detail {
        //     position: absolute;
        //     left: 0;
        //     right: 0;
        //     bottom: 0;
        // }

        .wrapper {
            width: 100%;
            // height: 208px;
            padding: 10px;
            background-color: #f6f7f9;


            .headline {
                padding-bottom: 10px;
                display: flex;
                flex-direction: row;
                align-items: center;

                .icon-l {
                    display: block;
                    width: 3px;
                    height: 12px;
                    background-color: #11a0f8;
                    margin-right: 5px;
                }

                .title {
                    padding: 0;
                    margin-right: 10px;
                    font-size: 12px;
                    color: #666666;
                }

                .icon-operation {
                    cursor: pointer;
                    color: #11a0f8;
                }
            }

            .inner2 {
                // height: 196px;
                padding: 0 12px;

                &:nth-child(4n + 1) {
                    padding-left: 0;
                }

                &:not(:nth-child(4n)) {
                    border-right: dashed 1px #666666;
                }
            }

            .inner-t,
            .inner2 {
                .item {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;
                    padding-right: 0;
                    white-space: nowrap;
                    line-height: 21px;

                    &:last-child {
                        margin-bottom: 0;
                    }

                    .label {
                        white-space: nowrap;
                        font-size: 12px;
                        color: #999999;
                    }

                    .value {
                        padding-left: 10px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        font-size: 14px;
                        color: #333333;
                    }
                }
            }
        }
    }
</style>


