<template>
<div class="full-h" id="full-h-wrapper">
    <label style="display:none;"><span></span><input type="text" name="hidden1" ></label>
    <label style="display:none;"><span></span><input type="password" name="hidden2" ></label>
    <h-form ref="form" :model="queryForm" cols='5'>

        <h-form-item label="现金流类型" :label-width="80" prop="cashFlowTypes" >
            <h-select v-model="queryForm.cashFlowTypes" filterable isString multiple>
                <h-option v-for="item in cashFlowTypeList" :key="item.code" :label="item.text" :value="item.code">
                </h-option>
            </h-select>
        </h-form-item>

        <h-form-item label="成交编号" :label-width="80" prop="dealNo" >
            <h-input v-model="queryForm.dealNo" type="text"></h-input>
        </h-form-item>

        <h-form-item label="产品名称" :label-width="80" prop="fundCodes" >
            <h-select v-model="queryForm.fundCodes" filterable isString multiple>
                <h-option v-for="item in fundList" :key="item.code" :label="item.text" :value="item.code">
                </h-option>
            </h-select>
        </h-form-item>

        <h-form-item label="现金流交割日期" :label-width="100" prop="settleDate" >
            <h-date-picker type="date" placeholder="选择日期" v-model="cashSettleDate" format="yyyy-MM-dd">
            </h-date-picker>
        </h-form-item>

        <h-form-item :label-width="10" >
            <h-button type="primary" @click="on_form_query">查询</h-button>
            <h-button type="ghost" @click="on_form_refresh">重置</h-button>
        </h-form-item>
    </h-form>

    <div class="pb10">
        <h-button-group>
            <!--<h-button @click="clearSuccess">清算完成</h-button>
            <h-button @click="clearFail">清算失败</h-button>
            <h-button @click="clearBack">回溯</h-button>-->
            <h-button @click="cashModify">修改</h-button>
            <!--<h-button @click="on_upload">上传</h-button>
            <h-button @click="on_export">导出数据</h-button>-->
        </h-button-group>
        <h-datagrid
                hasPage stripe
                highlight-row :height="autoHeight"
                :pageSize="100" :pageSizeOpts="pageSizeOpts"
                ref="Dtgrd_CFR" url="/cashmgnt/cashflow/query.json"
                :columns="cashFlowReportColumn"
                :bindForm="queryForm">
        </h-datagrid>
    </div>
    <h-msg-box class="customMsgBox" v-model="showChangeCashClearStatusDialog" width="300" :mask-closable=false>
        <p slot="header">
            <span >修改清算状态</span>
        </p>
        <h-form :model="cashFlowInfo" :label-width="80" ref="cashFlowForm">
            <h-form-item required label="清算状态" prop="cash_flow_detail_clear_status">
                <h-select v-model="cashFlowInfo.cash_flow_detail_clear_status" filterable>
                    <h-option v-for="item in statusList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                </h-select>
            </h-form-item>
        </h-form>
        <div slot="footer">
            <h-button type="ghost" style="margin-left: 8px" @click="cancel">取消</h-button>
            <h-button type="primary" @click="save">确定</h-button>
        </div>
    </h-msg-box>
</div>
</template>

<script>
import {
    showText,
    getDictList,
    getFundInfo,
    parseDateString,
    formatMoney,
    parseDateStr,
    getRivalList
} from "@/api/utils";
import {
    updateClearStatus
} from "@/api/cashmgnt/cashFlow"
import HDatagrid from "../../../components/HDatagrid";
export default {

    components: {
        HDatagrid
    },
    data() {
        return {
            //表单数据
            queryForm: {
                cashFlowTypes: '',
                dealNo: '',
                fundCodes: '',
                settleDate: ''
            },
            pageSizeOpts:[25,50,75,100],
            cashFlowModifyInfo:[],
            showChangeCashClearStatusDialog: false,
            cashFlowInfo:{
                cash_flow_detail_clear_status:''
            },
            cashFlowReportColumn: [
                {
                    type: 'selection',
                    width: 30,
                    align: "left"
                }, {
                    title: "现金流编号",
                    key: "cashFlowDetailId",
                    width: 100,
                    ellipsis: true,
                    sortable:true,
                    align: 'center'
                }, {
                    title: "产品名称",
                    key: "fundName",
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "指令编号",
                    key: "insId",
                    width: 100,
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "成交编号",
                    key: "dealNo",
                    width: 100,
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "交易对手",
                    key: "traderivalName",
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "现金流类型编号",
                    key: "cashFlowDetailType",
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "现金流类型",
                    key: "itemName",
                    ellipsis: true,
                    align: 'center',
                    width: 120
                }, {
                    title: "现金流交割日期",
                    key: "settleDate",
                    width: 100,
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "收付方向",
                    key: "cashChangeDirection",
                    width: 100,
                    ellipsis: true,
                    align: 'center',
                    render: (h,params) =>{
                        const rowData = params.row
                        let direction = showText(rowData.cashChangeDirection,this.directionList);
                        return h("span",{},direction)
                    }
                }, {
                    title: "金额(元)",
                    key: "settleBalance",
                    width: 100,
                    ellipsis: true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = formatMoney(rowData.settleBalance);
                        return h("span", {
                            style: {
                                fontWeight: 'bold'
                            }
                        }, name);
                    }
                }, {
                    title: "清算状态",
                    key: "cashFlowDetailClearStatus",
                    width: 100,
                    ellipsis: true,
                    align: 'center',
                    render: (h,params) => {
                        const rowData = params.row;
                        let status = showText(rowData.cashFlowDetailClearStatus,this.statusList);
                        return h("span",{},status)
                    }
                },  {
                    title: "委托方向",
                    key: "entrustDirectionName",
                    ellipsis: true,
                    align: 'center'
                }
            ],
            Data_CashFlowReport: {},
            fundList: [],
            cashFlowTypeList: [
                {
                    code:'1',
                    text:'TA申购流入(代销)'
                }, {
                    code:'2',
                    text:'TA申购流入(直销)'
                }, {
                    code:'3',
                    text:'TA赎回流出'
                }, {
                    code:'4',
                    text:'TA现金分红'
                }, {
                    code:'5',
                    text:'TA转换转出'
                }, {
                    code:'6',
                    text:'TA转换转入'
                }, {
                    code:'7',
                    text:'存款存入'
                }, {
                    code:'8',
                    text:'存款到期兑付'
                }, {
                    code:'9',
                    text:'存款付息'
                }, {
                    code:'10',
                    text:'分销买入缴款'
                }, {
                    code:'11',
                    text:'固收大宗债券买入'
                }, {
                    code:'12',
                    text:'固收大宗债券卖出'
                }, {
                    code:'13',
                    text:'交易所债券兑付'
                }, {
                    code:'14',
                    text:'交易所债券付息'
                }, {
                    code:'15',
                    text:'交易所证券清算款'
                }, {
                    code:'16',
                    text:'可转债申购补缴'
                }, {
                    code:'17',
                    text:'可转债申购定金'
                }, {
                    code:'18',
                    text:'可转债申购退款'
                }, {
                    code:'19',
                    text:'买断式回购融券到期'
                }, {
                    code:'20',
                    text:'买断式回购融券首期'
                }, {
                    code:'21',
                    text:'买断式回购融资到期'
                }, {
                    code:'22',
                    text:'买断式回购融资首期'
                }, {
                    code:'23',
                    text:'买断式回购中途收息'
                }, {
                    code:'24',
                    text:'清算备付金补款(上海)'
                }, {
                    code:'25',
                    text:'清算备付金回款(上海)'
                }, {
                    code:'26',
                    text:'同业存单还本付息'
                }, {
                    code:'27',
                    text:'同业存单认购缴款'
                }, {
                    code:'28',
                    text:'银行间债券兑付'
                }, {
                    code:'29',
                    text:'银行间债券付息'
                }, {
                    code:'30',
                    text:'银行间债券卖出'
                }, {
                    code:'31',
                    text:'银行间质押式融券到期'
                }, {
                    code:'32',
                    text:'银行间质押式融券首期'
                }, {
                    code:'33',
                    text:'银行间质押式融资到期'
                }, {
                    code:'34',
                    text:'银行间质押式融资首期'
                }, {
                    code:'35',
                    text:'管理费支付'
                }, {
                    code:'36',
                    text:'佣金支付'
                }, {
                    code:'37',
                    text:'中债费用支付'
                }, {
                    code:'38',
                    text:'上清所费用支付'
                }, {
                    code:'39',
                    text:'托管费支付'
                }, {
                    code:'40',
                    text:'上一日交易所证券清算款'
                }, {
                    code:'41',
                    text:'清算备付金补款(深圳)'
                }, {
                    code:'42',
                    text:'清算备付金回款(深圳)'
                }, {
                    code:'43',
                    text:'银行间债券买入'
                }
            ],
            rivalCodeList:[],
            directionList:[
                {
                    code:'0',
                    text:'不变化'
                }, {
                    code:'1',
                    text:'资金增加'
                }, {
                    code:'2',
                    text:'资金减少'
                }
            ],
            loading_query: false,
            autoHeight:0,
            statusList:[
                {
                    code:'0',
                    text:'未处理'
                }, {
                    code:'1',
                    text:'清算成功'
                }, {
                    code:'2',
                    text:'清算失败'
                }
            ]
        }
    },
    computed:{
        cashSettleDate:{
            get:function () {
                if(this.queryForm.settleDate){
                    return parseDateString(this.queryForm.settleDate);
                }
                return ''
            },
            set:function (newValue) {
                this.queryForm.settleDate = parseDateStr(newValue);
            }
        },
        businClass:{
            get:function () {
                if(this.queryForm.businClass){
                    return this.queryForm.businClass.split(",")
                }
                return []
            },
            set:function (newValue) {
                this.queryForm.businClass = newValue.join(",")
            }
        },
        inputDate:{
            get:function () {
                if(this.queryForm.inputDate){
                    return parseDateString(this.queryForm.inputDate);
                }
                return ''
            },
            set:function (newValue) {
                this.queryForm.inputDate = parseDateStr(newValue);
            }
        }
    },
    activated(){
        getFundInfo('/common/fundNamelist.json?type='+'code').then(res => this.fundList = res.data);
    },
    mounted() {
        this.re_layout();
        getRivalList(0,50).then(res => this.rivalCodeList = res.data)
    },
    methods: {
        on_export() {
            this.$refs['Dtgrd_CFR'].exportCsv({
                filename: '现金流报表',
                original: false
            });
        },
        on_upload() {
            alert('upload be cliced')
        },
        on_form_query() {
            this.$refs.Dtgrd_CFR.dataChange(1);
        },
        on_form_refresh(){
            this.queryForm ={
                cashFlowTypes: '',
                dealNo: '',
                fundCodes: '',
                settleDate: ''
            }
        },
        cashModify(){
            let selects = this.$refs.Dtgrd_CFR.$refs.gridContent.getSelection();
            if(selects.length == 0){
                this.openMask();
                this.$alert("请选择一条记录","错误",{
                    confirmButtonText: "确定",
                    type: "error",
                    callback: action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                });
                return;
            }
            Object.assign(this.cashFlowModifyInfo,selects);
            this.cashFlowInfo.cash_flow_detail_clear_status = '1'
            this.showChangeCashClearStatusDialog = true;
        },
        /*clearSuccess(){
            let selects = this.$refs.Dtgrd_CFR.$refs.gridContent.getSelection();
            if(selects.length == 0){
                this.$alert("请选择一条记录","错误",{
                    confirmButtonText: "确定",
                    type: "error"
                });
                return;
            }
            updateClearStatus({
                cashFlowModifyList: selects,
                clearStatus: "1"
            }).then(res =>{
                if(res.data && res.data.success){
                    this.$alert(res.data.msg,"成功",{
                        confirmButtonText: "确定",
                        type: "success"
                    })
                    this.showChangeCashClearStatusDialog = false;
                    this.on_form_query();
                }else{
                    this.$alert(res.data.msg, "错误", {
                        confirmButtonText: "确定",
                        type: "error"
                    })
                }
            })
        },
        clearFail(){
            let selects = this.$refs.Dtgrd_CFR.$refs.gridContent.getSelection();
            if(selects.length == 0){
                this.$alert("请选择一条记录","错误",{
                    confirmButtonText: "确定",
                    type: "error"
                });
                return;
            }
            updateClearStatus({
                cashFlowModifyList: selects,
                clearStatus: "2"
            }).then(res =>{
                if(res.data && res.data.success){
                    this.$alert(res.data.msg,"成功",{
                        confirmButtonText: "确定",
                        type: "success"
                    })
                    this.showChangeCashClearStatusDialog = false;
                    this.on_form_query();
                }else{
                    this.$alert(res.data.msg, "错误", {
                        confirmButtonText: "确定",
                        type: "error"
                    })
                }
            })
        },
        clearBack(){

        },*/
        cancel(){
            this.showChangeCashClearStatusDialog = false;
        },
        save(){
            if(this.cashFlowInfo.cash_flow_detail_clear_status){
                updateClearStatus({
                    cashFlowModifyList: this.cashFlowModifyInfo,
                    clearStatus: this.cashFlowInfo.cash_flow_detail_clear_status
                }).then(res =>{
                    if(res.data && res.data.success){
                        this.$alert(res.data.msg,"成功",{
                            confirmButtonText: "确定",
                            type: "success"
                        })
                        this.showChangeCashClearStatusDialog = false;
                        this.on_form_query();
                    }else{
                        this.$alert(res.data.msg, "错误", {
                            confirmButtonText: "确定",
                            type: "error"
                        })
                    }
                })
            }
        },
        re_layout(){
            let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
            this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 90;
        }
    }
}
</script>

<style>

    .mr10 {
        margin-right: 10px;
    }

    .h-form .h-form-item-label {
        line-height: 32px;
    }

</style>



