import fetch from "@/api/httpFetch";

export function showTradeDay(queryForm) {
    return fetch.post('/fundaccount/showMarketTradeDay.json',queryForm);
};

export function setTradeDay(setForm) {
    return fetch.post('/fundaccount/setMarketTradeDay.json',setForm);
};





