<template>
  <div class="cmp-fund-list">
    <h-table
      stripe
      :loading="loadingData"
      size="small"
      :columns="columns"
      :data="funds"
      :height="tableRealHeight"
      @on-row-click="onRowClick"
      @on-row-dblclick="onRowDbClick"
      :row-class-name="rowClassName"
      :highlight-row="true" border
      ref="fundContent">
    </h-table>
    <h-page
      class="page-ctrl"
      :total="totalNo"
      :current="current"
      :page-size="pageSize"
      :pageSizeOpts="pageSizeOpts"
      show-elevator
      show-total
      show-sizer
      @on-change="onChangePage"
      @on-page-size-change="onPageSizeChange"
      v-if="pageCtrlVisible"></h-page>
  </div>
</template>

<script type="text/ecmascript-6">
import { mapState } from 'vuex'
import {
  showText,
  pageFund,
  parseDateString
} from '@/api/utils'
//import mockPageFund from './mockFundList'

export default {
  props: {
    // searchKeys
    searchCondition: {
      type: Object,
      default: () => {
        return {
          fundTrustBanks: '', // 以逗号分隔
          fundTypes: '', // 以逗号分隔
          fundStatuses: '', // 以逗号分隔
          dataSource: '',
          keyword: '',
        }
      }
    },
    tableHeight: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      sortingField: '',
      funds: [],
      pageNo: 0,
      pageSize: 50,
      current: 1,
      pageSizeOpts: [25, 50, 75, 100],
      loadingData: false,
      totalNo: 0,
      columns: this.getColumns()
    }
  },
  watch: {
    searchCondition: {
      handler: function () {
        this.pageNo = 1;
        this.totalNo = 0;
        this.getFundListBySearch()
      }
    }
  },
  mounted() {

  },
  computed: {
    ...mapState({
      fundTypeList: state => state.dict.dict_610087,
      fundStatusList: state => state.dict.dict_40002,
      trustBankList: state => state.dict.trustBankList,
      fundSettleTypes: state => state.dict.dict_90531,
      taSettleTypes: state => state.dict.dict_90530,
      dataSourceList: state => state.dict.dict_90528,
      isFlagList: state => state.dict.dict_610001,
      taMatchTypeList: state => state.dict.dict_91386,
      operatorList: state => state.dict.operatorList
    }),
    pageCtrlVisible() {
      if (this.totalNo <= this.pageSize) {
        return false;
      }
      return true;
    },
    tableRealHeight() {
      if (this.pageCtrlVisible) {
        return this.tableHeight - 40
      } else {
        return this.tableHeight
      }
    }
  },
  methods: {
    onRowClick(data) {
      let fundData = data[0]
      let index = data[1]
      //console.log('onRowClick', fundData, index)
      this.$emit('fundSelected', fundData, index)
    },
    onRowDbClick(data) {
      this.$emit('fundDetail', data)
    },
    onChangePage(pageNo) {
      this.pageNo = pageNo;
      this.current = pageNo;
      // this.getFundListBySearch(this.searchCondition.cond.fundTrustBanks, this.searchCondition.cond.fundTypes)
      this.getFundListBySearch()
    },
    onPageSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.pageNo = 1;
      this.current = 1;
      this.getFundListBySearch()
    },
    getFundListBySearch() {
      this.loadingData = true;
      pageFund(this.searchCondition.fundTrustBanks, this.searchCondition.fundTypes, this.pageNo, this.pageSize,
              this.searchCondition.fundStatuses, this.searchCondition.dataSource, this.searchCondition.keyword).then(res => {
        this.funds = res.data.list;
        this.totalNo = res.data.total;
        this.loadingData = false;
      })
    },

    downloadByFileId(fileId) {
      window.open( window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=fund&fileId=' + fileId)
    },

    getColumns() {
      return [{
        title: '序号',
        key: 'fund_id',
        ellipsis: true,
        hiddenCol: true
      }, {
        title: '产品代码',
        key: 'fund_code',
        width: 120,
        align: 'left',
        ellipsis: true,
        sortable: true
      }, {
        title: '产品名称',
        key: 'fund_name',
        width: 140,
        align: 'left',
        ellipsis: true
      }, {
        title: '实际产品代码',
        key: 'actual_code',
        width: 120,
        align: 'left',
        ellipsis: true,
        sortable:true
      }, {
        key: 'fund_type',
        width: 100,
        title: '产品类型',
        align: 'left',
        sortable:true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = showText(rowData.fund_type, this.fundTypeList)
          return h('span', {}, typeName)
        }
      }, {
        title: '产品状态',
        key: 'fund_status',
        align: 'left',
        width: 80,
        sortable:true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = showText(rowData.fund_status, this.fundStatusList)
          return h('span', {}, typeName)
        }
      }, {
        title: '托管人',
        key: 'fund_trust_bank',
        align: 'left',
        width: 140,
        ellipsis: true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = showText(rowData.fund_trust_bank, this.trustBankList)
          return h('span', {}, typeName)
        }
      }, {
        title: '托管结算模式',
        key: 'fund_settle_type',
        ellipsis: true,
        width: 100,
        align: 'left',
        sortable:true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = showText(rowData.fund_settle_type, this.fundSettleTypes)
          return h('span', {}, typeName)
        }
      }, {
        title: 'TA金额交收模式',
        key: 'ta_settle_type',
        ellipsis: true,
        align: 'left',
        width: 140,
        sortable:true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = showText(rowData.ta_settle_type, this.taSettleTypes)
          return h('span', {}, typeName)
        }
      }, {
        title: '银行间ID',
        key: 'interbank_id',
        width: 90,
        ellipsis: true,
        align: 'left',
        sortable:true
      }, {
        title: '上海股东代码',
        key: 'stockholder_id',
        width: 120,
        align: 'left'
      }, {
        title: '深圳股东代码',
        key: 'stockholder_id_sz',
        width: 120,
        ellipsis: true,
        align: 'left'
      }, {
        title: '深圳配售对象编码',
        key: 'object_code',
        width: 120,
        align: 'left',
        ellipsis: true
      }, {
        title: 'TA上海备付金账号',
        key: 'sh_ta_account',
        align: 'left',
        width: 120,
        ellipsis: true
      }, {
        title: 'TA深圳备付金账号',
        key: 'sz_ta_account',
        align: 'left',
        width: 120,
        ellipsis: true
      }, {
        title: '是否划款',
        width: 100,
        key: 'transfer_flag',
        ellipsis: true,
        align: 'left',
        sortable:true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = showText(rowData.transfer_flag, this.isFlagList)
          return h('span', {}, typeName)
        }
      }, {
        title: '投资经理',
        key: 'fund_mgr_operator',
        width: 120,
        align: 'left',
        ellipsis: true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = showText(rowData.fund_mgr_operator, this.operatorList)
          return h('span', {}, typeName)
        }
      }, {
        title: '主办会计',
        key: 'fund_accountant',
        width: 120,
        ellipsis: true,
        align: 'left',
        render: (h, params) => {
          const rowData = params.row
          var typeName = showText(rowData.fund_accountant, this.operatorList)
          return h('span', {}, typeName)
        }
      }, {
        title: '产品邮箱',
        key: 'fund_mail',
        width: 120,
        ellipsis: true,
        align: 'left'
      }, {
        title: '成立日期',
        key: 'establish_date',
        width: 80,
        ellipsis: true,
        align: 'left',
        sortable:true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = parseDateString(rowData.establish_date)
          return h('span', {}, typeName)
        }
      }, {
        title: '结束日期',
        width: 80,
        key: 'expire_date',
        ellipsis: true,
        align: 'left',
        sortable:true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = parseDateString(rowData.expire_date)
          return h('span', {}, typeName)
        }
      }, {
        title: '三方存管模式',
        key: 'trust_flag',
        ellipsis: true,
        align: 'left',
        hiddenCol: true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = showText(rowData.transfer_flag, this.isFlagList)
          return h('span', {}, typeName)
        }
      }, {
        title: '产品来源',
        key: 'data_source',
        width: 80,
        align: 'left',
        ellipsis: true,
        sortable:true,
        render: (h, params) => {
          const rowData = params.row
          let dataSource = showText(rowData.data_source, this.dataSourceList)
          return h('span', {}, dataSource)
        }
      }, {
        title: 'TA轧差模式',
        key: 'ta_match_type',
        width: 130,
        align: 'left',
        ellipsis: true,
        sortable:true,
        render: (h, params) => {
          const rowData = params.row
          let taMatchType = showText(rowData.ta_match_type, this.taMatchTypeList)
          return h('span', {}, taMatchType)
        }
      }, {
        title: '公司签章',
        key: 'stamp_name',
        width: 130,
        align: 'left',
        ellipsis: true,
        sortable:true
      }, {
        title: '文件',
        key: 'file_name',
        width: 80,
        ellipsis: true,
        align: 'left',
        render: (h, params) => {
          const rowData = params.row;
          if (rowData.file_id && rowData.file_name) {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.downloadByFileId(rowData.file_id);
                  }
                }
              }, '下载')
            ]);
          } else {
            return h("span", {}, '');
          }
        }
      }]
    },
    rowClassName(row){
      if(row.fund_status != '1'){
        return 'alert-table-red-row';
      }else{
        return '';
      }
    }
  },
  components: {

  }
}
</script>

<style lang="less" rel="stylesheet/less">
  .cmp-fund-list {
    .page-ctrl {
      height: 40px;
    }
    .h-page-item, .h-page-item-jump-next, .h-page-next, .h-page-prev, .h-page-item-jump-prev {
      min-width: 24px;
      height: 26px;
      line-height: 25px
    }
    .h-select-single .h-select-selection {
      height: 26px;
    }
    .h-page-options-elevator {
      height: 26px;
      line-height: 26px;
      input {
        height: 26px;
      }
    }
  }

  .alert-table-red-row{
    color: red;
  }
</style>



