<template>
    <div class="review-table">
        <h-row style="margin-bottom: 0px">
            <h-col span="10">
                <h-button class="btn-custom" icon="right" @click="handlePass">复核通过</h-button>
                <h-button class="btn-custom" icon="close" @click="handleReject">复核拒绝</h-button>
            </h-col>
            <h-col span="14">
                <el-form style="font-size: 12px;float: right" :inline="true" size="mini" :model="queryCheck"
                          class="demo-form-inline">
                    <el-form-item label="修改人">
                        <el-select v-model="queryCheck.inputOperator" filterable clearable>
                            <el-option v-for="item in operatorList" :key="item.code"
                                       :label="item.text" :value="item.code">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="复核状态">
                        <el-select v-model="queryCheck.checkStatus" clearable filterable>
                            <el-option v-for="item in checkStatusList" :key="item.code"
                                       :label="item.text" :value="item.code">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="refresh">查询</el-button>
                    </el-form-item>
                </el-form>
            </h-col>
        </h-row>
        <h-datagrid style="margin-top: 0px" class="mt10" :height="autoHeight" stripe hasPage highlight-row :pageSize="100" :pageSizeOpts="pageSizeOpts"
                    :columns="reviewColumns" :bindForm="queryCheck"
                    :onCurrentChange="handleCurrentChange" :onCurrentChangeCancel="handleUnselect"
                    url="/check/checkPage.json" ref="datagrid"></h-datagrid>

        <h-msg-box class="customMsgBox"  width="500px" v-model="showResult">
            <p slot="header">
                <span>复核结果</span>
            </p>
            <h-table stripe  border v-show="errorData.length>0" :height="350" :data="errorData" :columns="resultColums">

            </h-table>
            <div slot="footer">
                <h-button type="primary" @click="confirm">确认</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import HDatagrid from '@/components/HDatagrid'
    import {
        operatorList,
        getDictList,
        getFundDetail,
        getFundInfo,
        getBankList,
        getBrokerList,
        getCityTree,
        getKeys,
        parseDateString,
        parseDateStr,
        encrypt,
        showText
    } from '@/api/utils'

    import {
        getCheckDetail,
        submitReview
    } from '@/api/fundAccount/review'
    export default {
        name: "ReviewTable",
        data() {
            let reviewColumns = [
                {
                    type: 'selection',
                    width: 40,
                    align: 'center'
                }, {
                    title:'序号',
                    key:'check_id',
                    sortable:true,
                    width:80,
                    align:'center'
                }, {
                    title: "变更类型",
                    key: "check_type",
                    width: 100,
                    sortable: true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = showText(rowData.check_type, this.checkTypeList);
                        return h("span", {}, typeName);
                    }

                }, {
                    title: "修改人",
                    key: "operator_name",
                    width: 150,
                    sortable: true,
                    align: 'center',
                    ellipsis: true
                }, {
                    title: "变更项",
                    key: "object_name",
                    ellipsis: true,
                    width: 150,
                    align: 'center'
                }, {
                    title: "变更时间",
                    key: "input_date",
                    sortable: true,
                    width: 150,
                    ellipsis: true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = parseDateString(rowData.input_date, rowData.input_time);
                        return h("span", {}, typeName);
                    }
                }, {
                    title: "复核状态",
                    sortable: true,
                    key: "check_status",
                    ellipsis: true,
                    width: 100,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = showText(rowData.check_status, this.checkStatusList);
                        return h("span", {}, typeName);
                    }
                }, {
                    title: "变更内容",
                    key: "modify_desc",
                    align: 'center',
                    ellipsis: true,
                    render: (h, params) => {
                        if (params.row.modify_desc)
                            return h("span", {}, decodeURIComponent(params.row.modify_desc));
                        else
                            return ''
                    }
                }, {
                    title: "复核人",
                    key: "check_operator",
                    width: 150,
                    sortable: true,
                    align: 'center',
                    ellipsis: true,
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = showText(rowData.check_operator, this.operatorList);
                        return h("span", {}, typeName);
                    }
                },{
                    title: "备注",
                    key: "remark",
                    width: 200,
                    align: 'center',
                    ellipsis: true
                },

            ];
            let resultColums = [
                {width: 80,key:"lineNum",title:"复核编号",align:"center"},
                {key:"errorInfo",title:"失败原因",align:"center"}
            ];
            return {
                showResult: false,
                resultColums: resultColums,
                autoHeight: 0,
                errorData:[],
                pageSizeOpts: [25, 50, 75, 100],
                reviewColumns: reviewColumns,
                checkStatusList:[
                    {code:'1',	text:'未复核'},
                    {code:'2',	text:'复核通过'},
                    {code:'3',	text:'复核拒绝'},
               ],
                checkTypeList: [],
                operatorList: [],
            }
        },
        props: {
            queryCheck:{
                type: Object,
                default: ()=>new Object({checkStatus: '1',inputOperator:null})
            },
            onSelectionChange: {
                type: Function,
                default(data) {
                }
            },
            clearData:{
                type: Function,
                default(data) {

                }
            }
        },
        created() {
            // getDictList(610073).then(res => this.checkStatusList = res.data);
            getDictList(10054).then(res => this.checkTypeList = res.data);
            operatorList().then(res => {
                if (res.data && res.data.length > 0) {
                    this.operatorList = res.data;
                }
            }).catch(error => {
                console.log(error);
            });
        },
        components: {
            HDatagrid
        },
        methods: {
            refresh() {
                this.$refs.datagrid.dataChange(1);
                this.clearData();
            },
            resizeTable(height){
                this.autoHeight = height
            },
            handlePass() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选中一条记录', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return
                }
                let allUnCheck = true;
                let checkId = '';
                for (let i = 0; i < selects.length; i++){
                    if (selects[i].check_status!='1'){
                        allUnCheck = false;
                        break;
                    }
                    checkId += selects[i].check_id + ',';
                }
                if(!allUnCheck){
                    this.openMask();
                    this.$alert('存在已经复核的记录,请确认后重新提交！', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return
                }
                checkId = checkId.substring(0, checkId.length - 1);

                let objectType = selects[0].object_type;

                submitReview(checkId, true,objectType).then(res => {
                    if (res.data && res.data.code == '0') {
                        this.openMask()
                        this.$alert(res.data.msg, '成功', {
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                        this.refresh()
                    } else if(res.data.code=='1120002'){
                        this.showResult = true;
                        this.errorData = res.data.data
                    } else {
                        this.openMask();
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                        this.refresh()
                    }
                })
            },
            handleReject() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选中一条记录', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return
                }
                let allUnCheck = true;
                let checkId = '';
                for (let i = 0; i < selects.length; i++){
                    if (selects[i].check_status!='1'){
                        allUnCheck = false;
                        break;
                    }
                    checkId += selects[i].check_id + ',';
                }
                if(!allUnCheck){
                    this.openMask();
                    this.$alert('存在已经复核的记录,请确认后重新提交！', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return
                }
                checkId = checkId.substring(0, checkId.length - 1);
                let objectType = selects[0].object_type;
                this.openMask();
                this.$prompt('确定拒绝该申请？请填写原因：', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(({
                             value
                         }) => {
                    submitReview(checkId, false, objectType,value).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '成功', {
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            });
                            this.refresh()
                        } else if(res.data.code=='1120002'){
                            this.showResult = true;
                            this.errorData = res.data.data
                        } else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            });
                        }

                    }).catch(error => {
                    });
                }).catch(() => {
                    this.closeMask();
                });

            },
            handleCurrentChange() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length === 1) {
                    this.onSelectionChange(selects[0]);
                }
            },
            handleUnselect() {
                this.clearData();
            },
            confirm() {
                this.showResult = false;
                this.refresh();
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less">
    .s-theme-black {
        .review-table .btn-custom {
            color: #0092FF;
        }
    }
</style>


