<template>
    <div>
        <h-msg-box class="customMsgBox" v-model="show" :footerHide="footerHide">
            <p slot="header">
                <span v-if="fileType==1">TA导入</span>
                <span v-if="fileType==2">中签文件导入</span>
            </p>

            <div slot="footer">
                <h-button type="primary" @click="submitUpload">导入</h-button>
                <h-button v-show="fileType==1" type="primary" @click="revokeTA">回溯</h-button>
                <h-button type="ghost" style="margin-left: 8px" @click="handleCancel">取消</h-button>
            </div>

            <!--TA导入-->
            <h-tabs size="small" v-model="tabsActive" @on-click="tabsClick" v-show="fileType == 1">
                <h-tab-pane label="文件导入">
                    <el-upload v-show="fileType == 1"
                            style="margin-bottom: 10px"
                            ref="uploadTA"
                            class="drag-in-box" drag
                            :action="uploadUrl"
                            :multiple="fileType!=1"
                            with-credentials
                            :on-success="uploadSuccess"
                            :data="uploadData"
                            :auto-upload="false"
                            :on-change="uploadChange"
                            :file-list="fileList"
                            :limit="limit">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">将文件拖到此处，或<em :title="fileTips">点击选择</em></div>
                        <!--<el-button :title="fileTips"-->
                        <!--slot="trigger" size="small" type="primary">选取文件</el-button>-->
                    </el-upload>
                    <h-form :label-width="100" cols="1">
                        <h-form-item  v-if="fileType==1" :label-width="80" required label="导入类型">
                            <el-radio v-model="uploadData.subclass" label="1">自建TA</el-radio>
                            <el-radio v-model="uploadData.subclass" label="2">中登TA</el-radio>
                        </h-form-item>
                    </h-form>
                </h-tab-pane>
                <h-tab-pane label="导入结果">
                    <h-datagrid stripe
                                highlight-row
                                :height="importResultHeight"
                                :columns="importResultColumns"
                                :gridData="tData"
                                ref="importResultDatagrid">
                    </h-datagrid>
                </h-tab-pane>
            </h-tabs>

            <!--中签文件导入-->
            <el-upload v-show="fileType == 2"
                    style="margin-bottom: 10px"
                    ref="uploadApply"
                    class="drag-in-box" drag
                    :action="uploadUrl"
                    :multiple="fileType!=1"
                    with-credentials
                    :on-success="uploadSuccess"
                    :data="uploadData"
                    :auto-upload="false"
                    :on-change="uploadChange"
                    :file-list="fileList"
                    accept=".xls,.xlsx"
                    :limit="limit">
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em :title="fileTips">点击选择</em></div>
            </el-upload>
            <div v-if="fileType == 2">
                <span v-themed:text>下一日缴款</span>
                <h-checkbox v-model="uploadData.isNextDay"></h-checkbox>
            </div>

        </h-msg-box>

        <h-msg-box class="customMsgBox"  width="800px" v-model="showResult">
            <p slot="header">
                <span>导入结果</span>
            </p>
            <div v-show="fileType==2">文件导入成功{{successCount}}个，失败{{errorCount}}个</div>
            <div v-show="fileType==2&&showContinueBtn" style="color:red">(存在已导入过中签文件，是否作废并重新导入，若是请点击“继续”按钮)</div>
            <div v-show="fileType==1&&showContinueBtn" style="color:red">(导入文件包含系统中不存在的产品，是否剔除并导入，若是请点击“继续”按钮)</div>

            <h-table stripe  border v-show="errorData.length>0" :height="350" :data="errorData" :columns="columns">

            </h-table>
            <div slot="footer">
                <h-button type="primary" @click="handleErrorCancel">确定</h-button>
                <h-button type="primary" style="margin-left: 8px" @click="handleErrorContinue" v-show="showContinueBtn">继续</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import HDatagrid from '@/components/HDatagrid'
    import {showText, parseDateString, operatorList} from '@/api/utils'
    import {taQuery} from '@/api/transfer/transfer'

    export default {
        name: "ImportDialog",
        components: {
            HDatagrid
        },
        watch:{
            show:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                    this.$refs.uploadTA && this.$refs.uploadTA.clearFiles();
                    this.$refs.uploadApply && this.$refs.uploadApply.clearFiles();
                }
            },
            showResult:function (value) {
                if (!value) {
                    if (!this.uploadData.ignore) {
                        this.$refs.uploadTA && this.$refs.uploadTA.clearFiles();
                        this.$refs.uploadApply && this.$refs.uploadApply.clearFiles();
                    }
                }
                this.closeMask();
                if (this.successCount>0){
                    this.openMask();
                    this.onImportSuccess();
                }
            }
        },
        props: {
            onImportSuccess: {
                type: Function,
                default() {
                }
            },
        },
        data() {
            let importResultColumns = [
                {
                    title: "操作类型",
                    key: "import_flag",
                    width: 80,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let statusName = showText(rowData.import_flag, this.importFlagList, {
                            key: 'code',
                            value: 'text'
                        });
                        return h("span", {}, statusName);
                    }
                }, {
                    title: "操作结果",
                    key: "load_status",
                    width: 80,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let statusName = showText(rowData.load_state, this.statusList, {
                            key: 'code',
                            value: 'text'
                        });
                        return h("span", {}, statusName);
                    }
                }, {
                    title: "TA类型",
                    key: "ta_type",
                    width: 80,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let statusName = showText(rowData.ta_type, this.taTypeList, {
                            key: 'code',
                            value: 'text'
                        });
                        return h("span", {}, statusName);
                    }
                }, {
                    title: "文件名称",
                    key: "file_name",
                    width: 170,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let fileNameSpan = h('span', {}, rowData.file_name);
                        return h('el-tooltip', {
                            props: {
                                trigger: 'hover',
                                effect: 'light',
                                placement: 'top-start',
                                openDelay: 400,
                                content: rowData.file_name,
                                popperClass: 'tooltipclass'
                            }
                        }, [fileNameSpan]);
                    }
                }, {
                    title: "操作员",
                    key: "operator_no",
                    width: 120,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let operatorName = showText(rowData.operator_no, this.operatorNoList);
                        return h("span", {}, operatorName);
                    }
                }, {
                    title: "操作时间",
                    key: "input_time",
                    width: 150,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = parseDateString(rowData.input_date, rowData.input_time);
                        return h("span", {}, typeName);
                    }
                }, {
                    title: "备注",
                    key: "remark",
                    width: 110,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let remarkSpan = h('span', {}, rowData.remark);
                        return h('el-tooltip', {
                            props: {
                                trigger: 'hover',
                                effect: 'light',
                                placement: 'top-start',
                                openDelay: 400,
                                content: rowData.remark,
                                popperClass: 'tooltipclass'
                            }
                        }, [remarkSpan]);
                    }
                }];
            return {
                uploadData:{dealFlag:0, subclass:'1',isNextDay: false,ignore:false},
                fileList:[],
                errorData:[],
                columns:[],
                taColumns:[
                    {width: 100,key:"fund_code",title:"产品代码",align:"center"},
                    {width: 100,key:"ta_biz_type",title:"业务类别编码",align:"center"},
                    {width: 100,key:"agency_code",title:"销售机构编码",align:"center"},
                    {width: 100,key:"cash_settle_date",title:"交收日期",align:"center"},
                    {key:"error_info",title:"错误原因",align:"center"}
                ],
                outApplyColumns:[
                    {width: 200,key:"fileName",title:"文件名",align:"center"},
                    {width: 80,key:"lineNum",title:"行号",align:"center"},
                    {key:"errorInfo",title:"错误原因",align:"center"},
                ],
                show:false,
                showResult:false,
                fileTips: '',
                uploadUrl:'',
                fileType:1,
                resultInfo:'',
                errorCount:0,
                successCount:0,
                footerHide: false,
                tabsActive: 0,
                importResultColumns: importResultColumns,
                importResultHeight: 175,
                tData: {rows: []},
                statusList: [
                    {code: '1', text: '成功'},
                    {code: '2', text: '失败'}
                ],
                importFlagList: [
                    {code: '1', text: '导入'},
                    {code: '0', text: '回溯'}
                ],
                taTypeList: [
                    {code: '1', text: '自建TA'},
                    {code: '2', text: '中登TA'}
                ],
                operatorNoList: [],
                showContinueBtn: false,
            }
        },
        computed:{
            limit(){
                return this.fileType==1? 1: 20
            }
        },
        methods: {
            uploadSuccess(response,file,fileList){

                file.status = "ready";//设置文件可以再次导入

                if (this.fileType==1) {
                    if (response.success){
                        if(response.msg == "操作成功"){
                            let msg = '导入成功'
                            if (this.uploadData.dealFlag == 1)
                                msg = '回溯成功'
                            this.$alert(msg,'成功',{
                                confirmButtonText: '确定',
                                type: 'success',
                                callback:action => {
                                    this.show = false;
                                }
                            }).then(() => {
                                this.show = false;
                            })
                        }else{
                            this.$alert(response.msg,'成功',{
                                confirmButtonText: '确定',
                                type: 'success',
                                callback:action => {
                                    this.show = false;
                                }
                            }).then(() => {
                                this.show = false;
                            })
                        }
                        this.onImportSuccess();
                    } else if(response.code=='2'){
                        this.errorData = response.data;

                        //获取错误为系统不存在的产品
                        let errorData4NoFundCode = this.errorData.filter(e => e.error_no === -1);
                        if (errorData4NoFundCode && errorData4NoFundCode.length === this.errorData.length) {
                            this.showContinueBtn = true;
                        } else {
                            this.showContinueBtn = false;
                        }
                        this.showResult = true;
                    }else {
                        this.$alert(response.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.show = false;
                            }
                        }).then(() => {
                            this.show = false;
                        });
                    }

                    if (!this.showContinueBtn) {
                        this.$refs.uploadTA && this.$refs.uploadTA.clearFiles();
                        this.$refs.uploadApply && this.$refs.uploadApply.clearFiles();
                    }
                    this.uploadData.ignore = false;
                }else{
                    if (response.success){
                        this.successCount++;
                    } else {
                        this.errorCount ++
                        this.errorData = this.errorData.concat(response.data)
                    }

                    if (this.successCount === fileList.length) {
                        //全部成功时，不展示导入结果弹框
                        this.$alert('导入成功','成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback:action => {
                                this.show = false;
                            }
                        }).then(() => {
                            this.show = false;
                        });
                        this.uploadData.ignore = false;
                        this.onImportSuccess();
                    } else if (this.successCount + this.errorCount === fileList.length) {

                        //保留错误的且可以作废并导入的文件列表
                        this.fileList = this.fileList.filter(f => this.errorData.find(e => e.errorNo === -1 && e.fileName === f.name));
                        //遍历错误信息列表，存在错误码为-1的数据时展示继续按钮
                        let findContinueData = this.errorData.find(e => e.errorNo === -1);
                        if (findContinueData) {
                            this.showContinueBtn = true;
                        } else {
                            this.showContinueBtn = false;
                        }

                        this.uploadData.ignore = false;
                        this.showResult = true;
                    }
                }
            },
            submitUpload() {
                if (this.fileType==1){
                    this.uploadData.dealFlag = 0;
                    this.columns = this.taColumns;

                    this.$refs.uploadTA.submit();
                } else {
                    this.errorData = []
                    this.errorCount = 0
                    this.successCount = 0
                    this.columns = this.outApplyColumns;

                    this.$refs.uploadApply.submit();
                }
                this.showContinueBtn = false;
                this.uploadData.ignore = false;
            },
            uploadChange(file, fileList){
                if(!file.response){
                    let existFile = fileList.slice(0, fileList.length - 1).find(f => f.name === file.name);
                    if(existFile){
                        this.$alert('当前文件已经存在!','错误',{
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                        fileList.pop()
                    }
                    this.fileList = fileList;
                }
            },
            revokeTA(){
                this.uploadData.dealFlag = 1;

                if (this.fileType == 1) {
                    this.$refs.uploadTA.submit();
                } else if (this.fileType == 2) {
                    this.$refs.uploadApply.submit();
                }
            },
            handleCancel(){
                this.show = false
            },
            showDialog(type){
                if (type==1) {
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/cashsettle/input/taImport.json'
                    this.fileTips="文件名格式：ASSETCHGYYYYMMDD.txt"
                }else {
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/cashsettle/input/outApplyImport.json'
                    this.fileTips= "文件名格式：#证券代码_#证券名称_#日期.xls\n"
                    //  + "字段格式:(按EXCEL中列的序号匹配字段)\n"
                    // + "  字段名称            XLS     是否必填    说明\n"
                    // + "  ----------------------------------------------------------------------------------\n"
                    // + "  托管人名称           第一列     必填    填写托管人名称（如工商银行：填1）\n"
                    // + "  业务分类           第二列     必填      填业务分类编号（如银行间业务：填1）\n"
                    // + "  业务类别           第三列     必填      填业务类别编号（如银行间质押式正回购：填10002）\n"
                    // + "  银行业务代码       第四列     必填      填银行业务代码\n"
                    // + "  银行业务名称       第五列     必填      填银行业务名称\n"
                    // + "  划款备注           第六列     非必填    填划款备注\n"
                    // + "  划款附言           第七列     非必填    填划款附言\n"
                    // + "  划款事由           第八列     非必填    填划款事由\n"
                    // + "  发送划款指令       第九列     非必填    填发送或者不发送，不填默认为发送\" "
                }
                this.show = true;
                this.fileType = type;

                this.tabsActive = 0;
                this.importResultHeight = 175;
                this.footerHide = false;
            },
            tabsClick(activeValue) {
                if (activeValue === 0) {
                    this.importResultHeight = 175;
                    this.footerHide = false;
                } else if (activeValue === 1) {
                    this.importResultHeight = 227;
                    this.footerHide = true;

                    operatorList().then(res => this.operatorNoList = res.data);

                    this.tData = {rows: []};
                    taQuery().then(res => {
                        if (res && res.status === 200 && res.data && res.data.success === true) {
                            this.tData.rows = res.data.data;
                        } else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    }).catch(error => {
                        this.$alert(error, '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    });
                }
            },
            handleErrorCancel() {
                this.showResult = false;
            },
            handleErrorContinue() {
                this.uploadData.ignore = true;
                this.showResult = false;

                if (this.fileType == 1) {
                    this.$refs.uploadTA.submit();
                } else if (this.fileType == 2) {
                    this.$refs.uploadApply.submit();
                }
            }
        }
    }
</script>

<style lang="scss" src="@/style/common.scss"></style>
<style lang="scss">
    .drag-in-box {

        .el-upload {
            display: block;
            height: 100%;

            .el-upload-dragger {
                width: 100%;
                height: 100%;
            }
        }
    }
</style>


