<template>
    <div class="full-h">
        <h-row class="full-h">
            <h-col class="full-h">
                <h-button-group>
                    <h-button class="custom-button" icon="play" @click="trialClick">试算</h-button>
                </h-button-group>
                <h-button-group style="float: right; border-right:solid 0 ;color:transparent;">
                    <h-button @click="openLayout">
                        <h-icon class="iconfont icon-ziduanxuanze1"></h-icon> &nbsp;布局
                    </h-button>
                </h-button-group>
                <h-datagrid
                        hasPage
                        highlight-row
                        stripe
                        :height="mainHeight"
                        :pageSize="100" :pageSizeOpts="pageSizeOpts"
                        :columns="detailColumns"
                        :bindForm="mainQueryForm"
                        url="/cashmgnt/bondSettleMgnt/queryBondSettleMgntList.json"
                        style="margin-top: 10px;overflow: hidden"
                        :auto-load="false"
                        :grid-name="gridName"
                        ref="maindatagrid">
                </h-datagrid>
                <h-datagrid
                        highlight-row
                        stripe
                        :columns="sumColumns"
                        :gridData="sumData"
                        style="margin-top: 1px; overflow: hidden"
                        ref="sumdatagrid">
                </h-datagrid>
            </h-col>
        </h-row>
    </div>
</template>

<script>
    import HDatagrid from '@/components/HDatagrid'
    import {
        parseDateString,
        parseDateStr,
        showText,
        formatMoney,
        dateFormate,
        getDictList,
        getBankList,
        amountSortMethod
    } from '@/api/utils'
    import {queryAmountCount} from '@/api/cashmgnt/bondSettleMgnt'

    export default {
        name: "bondSettleMgntTab",
        components: {HDatagrid},
        data() {
            let sumColumns = [
                {
                    title: "汇总",
                    key: "sum_text",
                    width: 100,
                    ellipsis: true,
                    align: 'left'
                }, {
                    title: "上清所",
                    key: "sqs_amt_count",
                    ellipsis: true,
                    align: 'right',
                    render: (h, params) => {
                        const rowData = params.row;
                        if (rowData.sum_text === '轧差金额（元）') {
                            var name = formatMoney(rowData.sqs_amt_count);
                            if (parseFloat(name) < 0) {
                                return h("span", {style: {fontWeight: 'bold', color: "red"}}, name);
                            } else {
                                return h("span", {style: {fontWeight: 'bold'}}, name);
                            }
                        } else {
                            return h("span", {}, rowData.sqs_amt_count);
                        }
                    }
                }, {
                    title: "中债登",
                    key: "zzd_amt_count",
                    ellipsis: true,
                    align: 'right',
                    render: (h, params) => {
                        const rowData = params.row;
                        if (rowData.sum_text === '轧差金额（元）') {
                            var name = formatMoney(rowData.zzd_amt_count);
                            if (parseFloat(name) < 0) {
                                return h("span", {style: {fontWeight: 'bold', color: "red"}}, name);
                            } else {
                                return h("span", {style: {fontWeight: 'bold'}}, name);
                            }
                        } else {
                            return h("span", {}, rowData.zzd_amt_count);
                        }
                    }
                }, {
                    title: "PROP",
                    key: "prop_amt_count",
                    ellipsis: true,
                    align: 'right',
                    render: (h, params) => {
                        const rowData = params.row;
                        if (rowData.sum_text === '轧差金额（元）') {
                            var name = formatMoney(rowData.prop_amt_count);
                            if (parseFloat(name) < 0) {
                                return h("span", {style: {fontWeight: 'bold', color: "red"}}, name);
                            } else {
                                return h("span", {style: {fontWeight: 'bold'}}, name);
                            }
                        } else {
                            return h("span", {}, rowData.prop_amt_count);
                        }
                    }
                }, {
                    title: "DCOM",
                    key: "dcom_amt_count",
                    ellipsis: true,
                    align: 'right',
                    render: (h, params) => {
                        const rowData = params.row;
                        if (rowData.sum_text === '轧差金额（元）') {
                            var name = formatMoney(rowData.dcom_amt_count);
                            if (parseFloat(name) < 0) {
                                return h("span", {style: {fontWeight: 'bold', color: "red"}}, name);
                            } else {
                                return h("span", {style: {fontWeight: 'bold'}}, name);
                            }
                        } else {
                            return h("span", {}, rowData.dcom_amt_count);
                        }
                    }
                }];
            return {
                pageSizeOpts: [25, 50, 75, 100],
                sumColumns: sumColumns,
                sumData: {
                    rows: [
                        {
                            sum_text: "轧差金额（元）",
                            sqs_amt_count: "0",
                            zzd_amt_count: "0",
                            prop_amt_count: "0",
                            dcom_amt_count: "0"
                        }, {
                            sum_text: "业务笔数（笔）",
                            sqs_amt_count: "0",
                            zzd_amt_count: "0",
                            prop_amt_count: "0",
                            dcom_amt_count: "0"
                        }]
                },
            }
        },
        props: {
            mainHeight: {
                type: Number,
            },
            queryType: {
                type: String,
                default: ''
            },
            detailColumns: {
                type: Array,
                default: []
            },
            mainQueryForm: {
                fundIds: '',
                queryType: '',
                keyword: ''
            },
        },
        watch: {},
        computed: {
            gridName() {
                let gridName = "bondSettleMgnt-1";
                if (this.queryType === "1") {
                    gridName = "bondSettleMgnt-1";//上清所
                } else if (this.queryType === "2") {
                    gridName = "bondSettleMgnt-2";//中债登
                } else if (this.queryType === "3") {
                    gridName = "bondSettleMgnt-3";//PROP
                } else if (this.queryType === "4") {
                    gridName = "bondSettleMgnt-4";//DCOM
                }
                return gridName;
            }
        },
        methods: {
            refresh() {
                this.$refs.maindatagrid.dataChange(1);
                this.$refs.sumdatagrid.handleResize();

                queryAmountCount({
                    fundIds: this.mainQueryForm.fundIds,
                    queryType: this.queryType
                }).then(res => {
                    if (res.data.success) {
                        this.setSumData(res.data.data);
                    } else {
                        this.openMask();
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    }
                });
            },

            handleResize() {
                this.$refs.maindatagrid.handleResize();
                this.$refs.sumdatagrid.handleResize();
            },

            trialClick() {
                let selects = this.$refs.maindatagrid.$refs.gridContent.getSelection();
                if (selects.length === 0) {
                    this.openMask();
                    this.$alert('请先选中至少一条记录', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return;
                }

                let dataArray = [{
                    sqs_amt_count: 0,
                    zzd_amt_count: 0,
                    prop_amt_count: 0,
                    dcom_amt_count: 0
                }, {
                    sqs_amt_count: 0,
                    zzd_amt_count: 0,
                    prop_amt_count: 0,
                    dcom_amt_count: 0
                }];
                selects.forEach(x => {
                    if (x.cop_busin_class === "1" && x.clear_agent_code_cn === "4") {
                        //上清所
                        dataArray[0].sqs_amt_count += (x.cash_direction === "1" ? 1 : -1) * parseFloat(x.settle_balance);
                        dataArray[1].sqs_amt_count += 1;
                    } else if (x.cop_busin_class === "1" && x.clear_agent_code_cn === "3") {
                        //中债登
                        dataArray[0].zzd_amt_count += (x.cash_direction === "1" ? 1 : -1) * parseFloat(x.settle_balance);
                        dataArray[1].zzd_amt_count += 1;
                    } else if (x.cop_busin_class === "4" || x.cop_busin_class === "6") {
                        //中登上海
                        dataArray[0].prop_amt_count += (x.cash_direction === "1" ? 1 : -1) * parseFloat(x.settle_balance);
                        dataArray[1].prop_amt_count += 1;
                    } else if (x.cop_busin_class === "5") {
                        //中登深圳
                        dataArray[0].dcom_amt_count += (x.cash_direction === "1" ? 1 : -1) * parseFloat(x.settle_balance);
                        dataArray[1].dcom_amt_count += 1;
                    }
                });
                dataArray[0].sqs_amt_count = parseFloat(dataArray[0].sqs_amt_count).toFixed(2);
                dataArray[0].zzd_amt_count = parseFloat(dataArray[0].zzd_amt_count).toFixed(2);
                dataArray[0].prop_amt_count = parseFloat(dataArray[0].prop_amt_count).toFixed(2);
                dataArray[0].dcom_amt_count = parseFloat(dataArray[0].dcom_amt_count).toFixed(2);
                this.setSumData(dataArray);
            },

            setSumData: function (dataArray) {
                this.sumData.rows[0].sqs_amt_count = dataArray[0].sqs_amt_count;
                this.sumData.rows[0].zzd_amt_count = dataArray[0].zzd_amt_count;
                this.sumData.rows[0].prop_amt_count = dataArray[0].prop_amt_count;
                this.sumData.rows[0].dcom_amt_count = dataArray[0].dcom_amt_count;
                this.sumData.rows[1].sqs_amt_count = dataArray[1].sqs_amt_count;
                this.sumData.rows[1].zzd_amt_count = dataArray[1].zzd_amt_count;
                this.sumData.rows[1].prop_amt_count = dataArray[1].prop_amt_count;
                this.sumData.rows[1].dcom_amt_count = dataArray[1].dcom_amt_count;
            },

            openLayout() {
                this.$refs.maindatagrid.openLayout();
            },
        }
    }
</script>

<style scoped>

</style>


