<template>
    <div class="full-h">
        <h-row class="full-h">
            <h-col class="full-h">
                <h-button-group>
                    <h-button icon="add" @click="stampAdd">新增</h-button>
                    <h-button icon="editor" @click="stampModify">修改</h-button>
                    <h-button icon="delete" @click="stampDelete">删除</h-button>
                    <h-button icon="android-sync" @click="refresh">刷新</h-button>
                </h-button-group>
                <h-datagrid hasPage stripe
                            :height="autoHeight" highlight-row
                            :pageSize="100" :pageSizeOpts="pageSizeOpts"
                            :bindForm="queryForm"
                            :auto-load="false" :on-row-db-click="stampDbClick"
                            :columns="stampColumns" url="/systemmgnt/stamp/signInfo.json"
                            ref="datagrid">
                </h-datagrid>
                <h-msg-box class="customMsgBox" v-model="showStampDialog" width="800" :mask-closable=false>
                    <p slot="header">
                        <span v-if="actionType == 'add'">新增签章信息</span>
                        <span v-else>修改签章信息</span>
                    </p>
                    <h-form :model="stampInfo" :label-width="100" ref="stampForm">
                        <h-row>
                            <h-col span="12">
                                <h-form-item v-if="stampType == '1'" required label="操作员名称" prop="operator_no">
                                    <h-select v-model="stampInfo.operator_no" filterable>
                                        <h-option v-for="item in operatorNoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                                    </h-select>
                                </h-form-item>
                                <h-form-item v-if="stampType == '2' && actionType == 'add'" required label="产品名称" prop="fund_id">
                                    <div>
                                        <h-select style="width: 215px" v-model="stampInfo.fund_id" hideMult isString multiple filterable showBottom>
                                            <h-option v-for="item in fundIdList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                                        </h-select>
                                        <h-button type="primary"  style="float: right;margin-top:3px;height:28px;line-height:16px;" @click="choseFundList">选择</h-button>
                                    </div>
                                </h-form-item>
                                <h-form-item v-if="stampType == '3'" required label="可使用操作员" prop="operator_list">
                                    <h-select v-model="operatorList" hideMult isString multiple filterable showBottom>
                                        <h-option v-for="item in operatorNoList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                                    </h-select>
                                </h-form-item>
                                <h-form-item v-if="stampType == '2' && actionType == 'modify'" required label="产品名称" prop="fund_id">
                                    <el-select disabled v-model="stampInfo.fund_id" size="small" style="width: 100%;" filterable>
                                        <el-option
                                                v-for="item in fundIdList"
                                                :key="item.code"
                                                :label="item.text"
                                                :value="item.code">
                                        </el-option>
                                    </el-select>
                                </h-form-item>
                                <h-form-item required label="签章名称" prop="stamp_name">
                                    <h-input disabled v-model="stampInfo.stamp_name"></h-input>
                                </h-form-item>
                                <h-form-item >
                                    <el-upload ref="uploader" :action="uploadUrl"
                                               class="drag-in-box" drag :data="uploadData"
                                               with-credentials :on-success="fileUploaded"
                                               :on-remove="fileRemoved"
                                               accept=".jpg,.png,.hsel"
                                               :limit="2" :file-list="fileList">
                                        <i class="el-icon-upload"></i>
                                        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                                    </el-upload>
                                </h-form-item>
                                <h-form-item label="自动转换为hsel" prop="isTransfer">
                                    <h-checkbox v-model="isTransfer"></h-checkbox>
                                </h-form-item>
                                <h-form-item label="备注" prop="remark">
                                    <h-input v-model="stampInfo.remark"></h-input>
                                </h-form-item>
                            </h-col>
                            <h-col style="margin-left: 10px" span="11">
                                <img v-bind:src="imgInfo.imgUrl" v-bind:height="imgInfo.imgHeight" width="360" oncontextmenu="return false;"/>
                            </h-col>
                        </h-row>
                    </h-form>
                    <div slot="footer">
                        <h-button type="ghost" style="margin-left: 8px" @click="cancelStamp">取消</h-button>
                        <h-button type="primary" @click="saveStamp">确认</h-button>
                    </div>
                </h-msg-box>
            </h-col>
        </h-row>
        <h-msg-box class="customMsgBox"  width="500px" v-model="showResult">
            <p slot="header">
                <span v-if="actionType == 'add'">新增结果</span>
                <span v-else>修改结果</span>
            </p>
            <h-table stripe  border v-show="errorData.length>0" :height="350" :data="errorData" :columns="accountColums">
            </h-table>
        </h-msg-box>
        <fund-choose-msg-box
                @saveFundInfo="saveFundInfo"
                ref="stampInfoMsgBox">
        </fund-choose-msg-box>
    </div>
</template>

<script>

    import HDatagrid from "../../../../components/HDatagrid";

    import {
        getUploadedFile,
        stampHsel2Jpg
    }from "@/api/utils"

    import {
        addStamp,
        modifyStamp,
        deleteStamp
    }from "@/api/systemmgnt/stamp"
    import FundChooseMsgBox from "../../../../components/FundChooseMsgBox";

    export default {
        name: "stampInfo",
        components: {FundChooseMsgBox, HDatagrid},
        data(){
            return{
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                actionType:'',
                fullHeight: '',
                fileList:[],
                errorData:[],
                operatorList: [],
                imgInfo:{
                    imgUrl:'',
                    imgHeight:''
                },
                showResult:false,
                showStampDialog:false,
                isTransfer: false,
                uploadUrl: window.LOCAL_CONFIG.API_HOME + '/file/upload.json',
                stampInfo:{
                    stamp_id:'',
                    stamp_name:'',
                    stamp_type:'',
                    operator_no:'',
                    operator_list: '',
                    fund_id:'',
                    remark:'',
                    file_id:''
                },
                queryForm:{
                    type:this.stampType,
                },
                uploadData:{
                    fileType: '5',
                    fundId:''
                },
                accountColums:[
                    {width: 80,key:"lineNum",title:"行号",align:"center"},
                    {key:"errorInfo",title:"错误原因",align:"center"},
                ],
            }
        },
        watch:{
            showStampDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                    this.fileList = [];
                }
                this.isTransfer = false;
            },
            showResult:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            },
            operatorList(newValue,oldValue) {
                if ((newValue.indexOf('-1') != -1 && oldValue.indexOf('-1') == -1) || newValue == '-1' ) {
                    this.stampInfo.operator_list = '-1';
                } else if ((newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') != -1) || (newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') == -1)){
                    this.stampInfo.operator_list = newValue;
                } else {
                    let newValueList = newValue.split(',');
                    this.stampInfo.operator_list = this.removeArr(newValueList, '-1');
                }
                this.operatorList = this.stampInfo.operator_list;
            }
        },
        props:{
            stampType:{
                type:String,
                default:''
            },
            stampColumns:{
                type:Array,
                default:[]
            },
            fundIdList:{
                type:Array,
                default:[]
            },
            operatorNoList:{
                type:Array,
                default:[]
            }
        },
        methods:{
            resizeTable(height, fullHeight){
                this.autoHeight = height;
                this.fullHeight = fullHeight;
            },
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            stampAdd(){
                this.stampInfo = {
                    stamp_id:null,
                    stamp_name:'',
                    operator_no:'',
                    operator_list: '',
                    stamp_type:this.stampType,
                    fund_id:'',
                    remark:'',
                    file_id:''
                }
                this.fileList = []
                this.actionType = 'add';
                this.imgInfo.imgUrl = '';
                this.operatorList = this.stampInfo.operator_list;
                if(this.stampType == '1' || this.stampType == '2'){
                    this.imgInfo.imgHeight = '300';
                }else{
                    this.imgInfo.imgHeight = '265';
                }
                this.$refs.stampForm.resetFields();
                this.showStampDialog = true;
            },
            stampModify(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }

                if(selects.length > 1){
                    this.openMask();
                    this.$alert('只能选择一条记录修改','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                this.stampDbClick(selects[0]);
            },
            stampDbClick(data){
                if(this.stampType == '1' || this.stampType == '2'){
                    this.imgInfo.imgHeight = '300';
                }else{
                    this.imgInfo.imgHeight = '265';
                }
                this.stampInfo = data;
                this.stampInfo.operator_no = data.operator_no.toString();
                this.stampInfo.fund_id = data.fund_id.toString();
                this.operatorList = this.stampInfo.operator_list;
                this.fileList = [{
                    name:this.stampInfo.stamp_name
                }];
                getUploadedFile(this.stampInfo.file_id).then(res =>{
                    let stampPath = res.data.file_path;
                    if(stampPath.indexOf("jpg") != -1 || stampPath.indexOf("png") != -1 || stampPath.indexOf("PNG") != -1 || stampPath.indexOf("JPG") != -1){
                        this.imgInfo.imgUrl = window.LOCAL_CONFIG.API_HOME + '/systemmgnt/stamp/downloadStamp.json?fileId='+res.data.file_id;
                        if(this.stampType == '1' || this.stampType == '2'){
                            this.imgInfo.imgHeight = '335'
                        }else{
                            this.imgInfo.imgHeight = '305'
                        }
                    }else{
                        stampHsel2Jpg(res.data.file_id).then(res =>{
                            this.imgInfo.imgUrl = res.data.stampInfo;
                        })
                        if(this.stampType == '1' || this.stampType == '2'){
                            this.imgInfo.imgHeight = '335'
                        }else{
                            this.imgInfo.imgHeight = '305'
                        }
                    }
                })
                this.showStampDialog = true;
                this.actionType = 'modify'
            },
            stampDelete(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }

                let stampIdList= '';
                for( let i = 0; i < selects.length; i++){
                    stampIdList += selects[i].stamp_id + ",";
                }
                stampIdList = stampIdList.substring(0 ,stampIdList.length - 1)

                this.openMask();
                this.$confirm('确认删除这条签章信息？', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteStamp(stampIdList).then(res => {
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                            this.refresh()
                        }else{
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText:'确定',
                                type:'error',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                        }
                    })
                }).catch(() =>{
                    this.closeMask();
                })
            },
            cancelStamp(){
                this.showStampDialog = false;
            },
            saveStamp(){
                let isValidate = true;
                this.$refs.stampForm.validate((validate) => isValidate = isValidate && validate)
                if(this.stampInfo.file_id == ''){
                    isValidate = false;
                }
                if(!isValidate){
                    this.$alert('输入项不满足要求，请重新检查','错误',{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }

                if(this.actionType == 'add'){
                    addStamp({
                        stamp: this.stampInfo,
                        isTransfer: this.isTransfer
                    }).then(res => {
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.showStampDialog = false
                                }
                            }).then(() => {
                                this.showStampDialog = false
                            })
                            this.refresh()
                        }else if(res.data.code == '1120002'){
                            this.showResult = true
                            this.errorData = res.data.data
                        }else{
                            this.$alert(res.data.msg,'失败',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }else if(this.actionType == 'modify'){
                    modifyStamp({
                        stamp: this.stampInfo,
                        isTransfer: this.isTransfer
                    }).then(res => {
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.showStampDialog = false
                                }
                            }).then(() => {
                                this.showStampDialog = false
                            })
                            this.refresh()
                        } else if (res.data && res.data.code == '1120010') {
                            this.showStampDialog = false;
                        } else {
                            this.$alert(res.data.msg,'失败',{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }
            },
            fileUploaded(response,file,fileList){
                if(response.code == '0'){
                    if(fileList.length == 1){
                        this.stampInfo.file_id = response.data;
                        this.stampInfo.stamp_name = fileList[0].name;
                    }else if(fileList.length == 2){
                        this.stampInfo.file_id = response.data;
                        this.stampInfo.stamp_name = fileList[1].name;
                        fileList.splice(0,1);
                    }else{
                        this.stampInfo.file_id = '';
                        this.stampInfo.stamp_name = '';
                    }
                }else{
                    this.$alert(response.msg, '错误',{
                        confirmButtonText:'确定',
                        type: 'error'
                    });
                    fileList.splice(fileList.length-1,1);
                    return;
                }

                getUploadedFile(response.data).then(res =>{
                    let stampPath = res.data.file_path;
                    if(stampPath.indexOf("jpg") != -1 || stampPath.indexOf("png") != -1 || stampPath.indexOf("PNG") != -1 || stampPath.indexOf("JPG") != -1){
                        this.imgInfo.imgUrl = window.LOCAL_CONFIG.API_HOME + '/systemmgnt/stamp/downloadStamp.json?fileId='+res.data.file_id;
                        if(this.stampType == '1' || this.stampType == '2'){
                            this.imgInfo.imgHeight = '335'
                        }else{
                            this.imgInfo.imgHeight = '305'
                        }
                    }else{
                        stampHsel2Jpg(res.data.file_id).then(res =>{
                            this.imgInfo.imgUrl = res.data.stampInfo;
                        })
                        if(this.stampType == '1' || this.stampType == '2'){
                            this.imgInfo.imgHeight = '335'
                        }else{
                            this.imgInfo.imgHeight = '305'
                        }
                    }
                })
            },
            fileRemoved(file,fileList){
                this.stampInfo.stamp_name = '';
                this.stampInfo.file_id = '';
                this.fileList=[];
                this.imgInfo.imgUrl = '';
                if(this.stampType == '1' || this.stampType == '2'){
                    this.imgInfo.imgHeight = '300';
                }else{
                    this.imgInfo.imgHeight = '265';
                }
            },
            choseFundList(){
                this.$refs.stampInfoMsgBox.showMsgBox(this.stampInfo.fund_id, this.fullHeight, '1');
            },
            saveFundInfo(data){
                this.stampInfo.fund_id = data;
            },
            removeArr(arr, target) {
                for (let i = 0; i < arr.length; i++) {
                    if (arr[i] == target) {
                        arr.splice(i, 1);
                    }
                }
                return arr;
            }
        }
    }
</script>

<style lang="scss">
    .drag-in-box {

        .el-upload {
            display: block;
            height: 100%;

            .el-upload-dragger {
                margin-top: 10px;
                width: 100%;
                height: 100%;
            }
        }
    }
</style>


