<!--BTA导入对话框-->

<template>
    <div>
        <h-msg-box v-model="msgBoxShowNew" width="600" :mask-closable=false class="customMsgBox">
            <p slot="header">
                <span>TA导入</span>
            </p>

            <h-row>
                <h-datagrid stripe
                            highlight-row
                            :canDrag=false
                            height="330"
                            :columns="importColumns"
                            :gridData="tData"
                            ref="importDatagrid">
                </h-datagrid>
            </h-row>

            <div slot="footer" style="text-align:center;">
                <h-button type="primary" @click="onBTAImport">导入</h-button>
                <h-button type="primary" style="margin-left: 8px" @click="onBTARevokeAndImport">回溯并导入</h-button>
                <h-button type="ghost" style="margin-left: 8px" @click="onBTAResetStatus">重置导入状态</h-button>
            </div>
        </h-msg-box>

        <h-msg-box v-model="msgBoxShowRevoke" width="520" top="200" title="回溯并导入" :mask-closable=false
                   class="customMsgBox">
            <h-form :label-width="40" cols="2" :model="queryFormRevoke" ref="revokeForm">
                <h-form-item label="产品">
                    <h-select v-model="queryFormRevoke.fundId" filterable :clearable=false>
                        <h-option v-for="item in fundList" :key="item.code" :label="item.text"
                                  :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item label="类型">
                    <h-select v-model="queryFormRevoke.businFlag" :clearable=false>
                        <h-option v-for="item in businFlagList" :key="item.code" :label="item.text"
                                  :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
            </h-form>
            <div slot="footer" style="text-align:center;">
                <h-button type="primary" @click="onOKRevoke">确定</h-button>
                <h-button type="ghost" style="margin-left: 8px" @click="onCancelRevoke">取消</h-button>
            </div>
        </h-msg-box>

        <full-screen-loading ref="screenLoading"></full-screen-loading>
    </div>
</template>

<script>
    import HDatagrid from '@/components/HDatagrid'
    import FullScreenLoading from "@/components/FullScreenLoading"
    import {showText} from '@/api/utils'
    import {btaQuery, btaImport, btaRevokeAndImport, btaResetStatus} from '@/api/transfer/transfer'

    export default {
        name: "ImportDialog4BTA",
        data() {
            let importColumns = [
                {
                    title: "文件名称",
                    key: "file_name_chinese",
                    width: 150,
                    ellipsis: true,
                    align: 'left',
                }, {
                    title: "状态",
                    key: "load_state",
                    width: 100,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let statusName = showText(rowData.load_state, this.statusList, {
                            key: 'code',
                            value: 'text'
                        });
                        return h("span", {}, statusName);
                    }
                }, {
                    title: "备注",
                    key: "remark",
                    //width: 305,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let remarkSpan = h('span', {}, rowData.remark);
                        return h('el-tooltip', {
                            props: {
                                trigger: 'hover',
                                effect: 'light',
                                placement: 'top-start',
                                openDelay: 400,
                                content: rowData.remark,
                                popperClass: 'tooltipclass'
                            }
                        }, [remarkSpan]);
                    }
                }];
            return {
                importColumns: importColumns,
                tData: {rows: []},
                statusList: [
                    {code: '0', text: '未导入'},
                    {code: '1', text: '导入成功'},
                    {code: '2', text: '导入失败'}
                ],

                msgBoxShowRevoke: false,
                queryFormRevoke: {
                    fundId: '',
                    businFlag: '-1'
                },
                businFlagList: [
                    {code: '-1', text: '全部'},
                    {code: '30066', text: '自建TA申购'},
                    {code: '30067', text: '自建TA赎回'},
                    {code: '30068', text: '自建TA分红'}
                ],
            }
        },

        components: {
            HDatagrid,
            FullScreenLoading
        },

        props: {
            msgBoxShow: {
                type: Boolean,
                default: false
            },
            fundList: Array
        },

        watch: {
            msgBoxShowNew: function (show) {
                if (show) {
                    this.openMask();
                } else {
                    this.closeMask();
                }
            }
        },

        mounted() {
            this.btaQueryRefresh();
        },

        computed: {
            msgBoxShowNew: {
                get: function () {
                    return this.msgBoxShow;
                },
                set: function (setValue) {
                    //关闭按钮‘*’时，触发设置父组件的msgBoxShow的值
                    if (setValue === false) {
                        this.$emit('onBTAClose');
                    }
                }
            },
        },

        methods: {
            btaQueryRefresh() {
                this.tData = {rows: []};
                btaQuery().then(res => {
                    if (res && res.status === 200 && res.data && res.data.success === true) {
                        this.tData.rows = res.data.data;
                    } else {
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }
                }).catch(error => {
                    this.$alert(error, '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                });
            },

            onBTAImport() {
                this.$refs.screenLoading.open();
                btaImport().then(res => {
                    this.$refs.screenLoading.close();
                    if (res && res.status === 200 && res.data && res.data.success === true) {
                        this.btaQueryRefresh();//成功时，重新查询文件导入状态
                        this.$alert('操作成功', '成功', {
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                            }
                        }).then(() => {
                        });
                    } else {
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }
                }).catch(error => {
                    this.$refs.screenLoading.close();
                    this.$alert(error, '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                });

                //成功时，关闭对话框
                //this.msgBoxShowNew = false;
            },

            onBTARevokeAndImport() {
                this.queryFormRevoke.fundId = '';
                this.queryFormRevoke.businFlag = '-1';
                this.msgBoxShowRevoke = true;
            },

            onBTAResetStatus() {
                let selects = this.$refs.importDatagrid.$refs.gridContent.getSelection();
                if (!selects || selects.length === 0) {
                    this.openMask();
                    this.$alert('请先选中一条记录', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return;
                }

                btaResetStatus({
                    fileName: selects[0].file_name
                }).then(res => {
                    if (res && res.status === 200 && res.data && res.data.success === true) {
                        this.btaQueryRefresh();//成功时，重新查询文件导入状态
                        this.$alert('操作成功', '成功', {
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                            }
                        }).then(() => {
                        });
                    } else {
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }
                }).catch(error => {
                    this.$alert(error, '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                });
            },

            onOKRevoke() {
                if (!this.queryFormRevoke.fundId) {
                    this.$alert('产品不能为空', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }
                if (!this.queryFormRevoke.businFlag) {
                    this.$alert('类型不能为空', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }

                btaRevokeAndImport({
                    fundId: this.queryFormRevoke.fundId,
                    businFlag: this.queryFormRevoke.businFlag
                }).then(res => {
                    if (res && res.status === 200 && res.data && res.data.success === true) {
                        this.msgBoxShowRevoke = false;
                        this.btaQueryRefresh();//成功时，重新查询文件导入状态
                        this.$alert('操作成功', '成功', {
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                            }
                        }).then(() => {
                        });
                    } else {
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }
                }).catch(error => {
                    this.$alert(error, '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                });
            },

            onCancelRevoke() {
                this.msgBoxShowRevoke = false;
            },
        },
    }
</script>

<style>
    .tooltipclass {
        width: 300px;
    }
</style>


