<template>
<div class="full-h">
    <h-split v-model="split" min="130" max="600">
        <div slot="left" span="4" class="full-h">
            <fund-tree :showAll="true"  :onFundClick="fundClick" ref="treeFund"></fund-tree>
        </div>
        <div slot="right" span="20" class="full-h" style="padding-left: 10px" id="full-h-wrapper-on-feerecord">
            <h-row>
                <el-form style="font-size: 12px;":inline="true" size="mini" :model="queryForm"  >
                    <el-form-item label="预计缴款日" >
                        <el-date-picker
                                v-model="queryForm.prePayDate"
                                type="daterange"
                                @change="getPrePayDate()"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="费用类型" >
                        <h-select v-model="queryForm.copBusinFlag" filterable clearable>
                            <h-option v-for="item in feeBusinList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                        </h-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="refreshFeeRecord">查询</el-button>
                    </el-form-item>
                </el-form>
            </h-row>
            <h-row>
                <h-datagrid :height="autoHeight"
                            stripe hasPage highlight-row
                            :auto-load="false"
                            :pageSize="20" :pageSizeOpts="pageSizeOpts"
                            :columns="feeRecordColumns" :bindForm="queryForm"
                            url="/fee/record/page.json" ref="datagrid">
                </h-datagrid>
            </h-row>
        </div>
    </h-split>
</div>
</template>
<script>

import FundTree from "@/components/FundTree";
import HDatagrid from "../../../components/HDatagrid";     //加载HDatagrid列表，需要引入该HDatagrid文件
import {
    getBusinFlagList,
    parseDateStr,
    parseDateString,
    showText,
    getBankList,
    formatMoney
} from "@/api/utils";

export default {
    name: "feerecord",    // @unknow不理解name 得作用
    data(){
        return{
            split: 0.15,
            autoHeight:0,
            pageSizeOpts:[25,50,75,100],
            feeBusinList: [],
            bankList: [],
            queryForm:{
                prePayDate:'',
                fundId:'',
                copBusinFlag:'',
                beginDate:'',
                endDate:''
            },
            feeRecordColumns:[
                {
                    type: 'selection',
                    width: 30,
                    align: "left"
                },{
                    title: "产品代码",
                    key: "fund_code",
                    hiddenCol:false,
                    align: "left",
                    width:95,
                    ellipsis: true
                },{
                    title: "产品名称",
                    key: "fund_name",
                    align: "left",
                    ellipsis: true
                },{
                    title: "托管人",
                    key: "fund_trust_bank",
                    hiddenCol:false,
                    align: "left",
                    width:105,
                    ellipsis: true,
                    render: (h, params) => {
                        const rowData = params.row;
                        var bank_name = showText(rowData.fund_trust_bank, this.bankList);
                        return h("span", {}, bank_name);
                    }
                },{
                    title: "费用类型",
                    key: "cop_busin_flag",
                    align: "left",
                    width:105,
                    ellipsis: true,
                    render: (h, params) => {
                        const rowData = params.row;
                        var copBusinName = showText(rowData.cop_busin_flag, this.feeBusinList);
                        return h("span", {}, copBusinName);
                    }
                },{
                    title: "预计缴款日",
                    key: "cash_settle_date",
                    align: "left",
                    width:105,
                    ellipsis: true,
                    render: (h, params) => {
                        const rowData = params.row;
                        var date = parseDateString(rowData.cash_settle_date, null);
                        return h("span", {}, date);
                    }
                },{
                    title: "实际缴款日",
                    key: "real_cash_settle_date",
                    align: "left",
                    width:105,
                    ellipsis: true,
                    render: (h, params) => {
                        const rowData = params.row;
                        var date = parseDateString(rowData.real_cash_settle_date, null);
                        return h("span", {}, date);
                    }
                },{
                    title: "估值科目",
                    key: "subject_no_list",
                    align: "left",
                    width:140,
                    ellipsis: true
                },{
                    title: "结转日",
                    key: "trade_date",
                    align: "left",
                    width:105,
                    ellipsis: true,
                    render: (h, params) => {
                        const rowData = params.row;
                        var date = parseDateString(rowData.trade_date, null);
                        return h("span", {}, date);
                    }
                },{
                    title: "缴款金额",
                    key: "settle_balance",
                    align: "left",
                    width:100,
                    ellipsis: true,
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = formatMoney(rowData.settle_balance);
                        return h("span", {style: {fontWeight: 'bold'}}, typeName);
                    }
                },{
                    title: "划款指令编号",
                    key: "transfer_ins_id",
                    align: "left",
                    width:90,
                    ellipsis: true
                },{
                    title: "序号",
                    key: "serial_no",
                    hiddenCol:true,
                    align: "left",
                    width:75,
                    ellipsis: true
                }
            ]
        }
    },
    methods:{
        re_layout(){
            let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
            this.autoHeight = document.getElementById("full-h-wrapper-on-feerecord").offsetHeight - wrapper0Height - 50;
        },
        fundClick(data) {
            if (data) {
                this.queryForm.fundId = data.code;
                if (data.code.indexOf(",") < 0) {
                    this.refreshFeeRecord();
                } else {
                    this.queryForm.fundId = "";
                    this.refreshFeeRecord();
                }
            }
        },
        refreshFeeRecord() {
            this.$refs.datagrid.dataChange();
        },
        getPrePayDate(){
            if(this.queryForm.prePayDate){
                this.queryForm.beginDate = parseDateStr(this.queryForm.prePayDate[0]);
                this.queryForm.endDate = parseDateStr(this.queryForm.prePayDate[1]);
            }else{
                this.queryForm.beginDate = '';
                this.queryForm.endDate = '';
            }
        }
    },
    mounted() {
        getBusinFlagList(22).then(res => this.feeBusinList = res.data);
        getBankList("1").then(res => this.bankList = res.data);
        // this.re_layout();
    },

    components: {        //@unknow不理解作用
        FundTree,
        HDatagrid

    }
}



</script>









