<template>
    <div id="full-h-wrapper" class="page-review full-h">
        <review-table :on-selection-change="rowCheck" :clear-data="clearDate" :query-check="queryCheck" ref="reviewTable"></review-table>
        <div class="detail" v-if="showDetail" id="wrapper-0" v-bind:style="isScrollBar">
            <div class="wrapper">
                <header class="headline">
                    <i class="icon-1"></i>
                    <span class="title">详情</span>
                </header>
                <h-row>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">签章类型</div>
                            <div class="value" :title="stamp.stampType">{{stamp.stampType}}</div>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">签章名称</div>
                            <div class="value" :title="stamp.stamp_name">{{stamp.stamp_name}}</div>
                        </div>
                    </h-col>
                    <h-col v-if="stamp.stamp_type == '1'" span="6" class-name="inner">
                        <div class="item">
                            <div class="label">操作员</div>
                            <div class="value" :title="stamp.operator_name">{{stamp.operator_name}}</div>
                        </div>
                    </h-col>
                    <h-col v-if="stamp.stamp_type == '2'" span="6" class-name="inner">
                        <div class="item">
                            <div class="label">产品</div>
                            <div class="value" :title="stamp.fund_name">{{stamp.fund_name}}</div>
                        </div>
                    </h-col>
                    <h-col v-if="stamp.stamp_type == '3'" span="6" class-name="inner">
                        <div class="item">
                            <div class="label">可使用操作员</div>
                            <div class="value" :title="stamp.operatorNameList">{{stamp.operatorNameList}}</div>
                        </div>
                    </h-col>
                    <h-col span="6" class-name="inner">
                        <div class="item">
                            <div class="label">备注</div>
                            <div class="value" :title="stamp.remark">{{stamp.remark}}</div>
                        </div>
                    </h-col>
                </h-row>
                <header class="headline mt10 pt10 border-dashed-top">
                    <i class="icon-l"></i>
                    <span class="title">签章</span>
                </header>
                <h-row>
                    <h-col v-if="stamp.stamp_type == '1' || stamp.stamp_type == '2'" span="5" class-name="image-inner">
                        <div class="image">
                            <img v-if="this.checkType == 3 && showModify" v-bind:src="imgInfo.imgUrl1" height="40" width="80" oncontextmenu="return false;"/>
                            <h-icon v-if="this.checkType == 3 && showModify" name="arrow-right-c" ></h-icon>
                            <img v-bind:src="imgInfo.imgUrl2" height="40" width="80" oncontextmenu="return false;"/>
                        </div>
                    </h-col>
                    <h-col v-if="stamp.stamp_type == '3'" span="7" class-name="image-inner">
                        <div class="image">
                            <img v-if="this.checkType == 3 && showModify" v-bind:src="imgInfo.imgUrl1" height="100" width="120" oncontextmenu="return false;"/>
                            <h-icon v-if="this.checkType == 3 && showModify" name="arrow-right-c" ></h-icon>
                            <img v-bind:src="imgInfo.imgUrl2" height="100" width="120" oncontextmenu="return false;"/>
                        </div>
                    </h-col>
                </h-row>
                <header v-if="stamp.modify_desc" class="headline mt10 pt10 border-dashed-top">
                    <i class="icon-l"></i>
                    <span class="title">变更内容</span>
                </header>
                <h-row v-if="stamp.modify_desc">
                    <h-col span="6" class="inner" v-for="(item, index) in stamp.modify_desc.split(';')" :key="index">
                        <div class="item">
                            <div class="label">{{item.split(":")[0]}}</div>
                            <div class="value" :title="decodeURIComponent(item.split(':')[1])">{{decodeURIComponent(item.split(":")[1])}}</div>
                        </div>
                    </h-col>
                </h-row>
            </div>
        </div>
    </div>
</template>

<script>

    import {
        showText,
        showTextList,
        operatorList,
        getFundInfo,
        getDictList,
        stampHsel2Jpg
    } from '@/api/utils'
    import {
        getCheckDetail
    } from '@/api/fundAccount/review';
    import {
        getStampById
    } from '@/api/systemmgnt/stamp'
    import ReviewTable from "../../../../components/ReviewTable";
    export default {
        name: "review",
        data() {
            return {
                autoHeight: 0,
                showDetail: false,
                showModify: false,
                operatorList: [],
                fundList: [],
                stampTypeList: [],
                stampType: '',
                checkType: '',
                queryCheck: {
                    checkStatus: '1',
                    inputOperator:null,
                    objectType: 4
                },
                stamp: {
                    stamp_id: '',
                    stamp_name: '',
                    stamp_type: '',
                    stampType: '',
                    operator_name: '',
                    operator_list: '',
                    operatorNameList: '',
                    fund_name: '',
                    file_id: '',
                    input_date: '',
                    modify_desc: ''
                },
                isScrollBar:{
                    marginTop: '10px',
                    overflow: 'auto',
                    maxHeight: ''
                },
                imgInfo: {
                    imgUrl1: '',
                    imgUrl2: ''
                }
            }
        },
        activated() {
            this.$refs.reviewTable.refresh();
        },
        mounted() {
            operatorList(false).then(res => {
                if (res.data && res.data.length > 0) {
                    this.operatorList = [{code: '-1', text: '全部'}];
                    this.operatorList = this.operatorList.concat(res.data);
                }
            });
            getDictList(90538).then(res => {
                this.stampTypeList = res.data;
            });
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => {
                if (res.data && res.data.length > 0) {
                    this.fundList = this.fundList.concat(res.data);
                }
            });
            this.re_layout();
        },
        methods: {
            re_layout() {
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                let fullWrapperHeight = document.getElementById("full-h-wrapper").offsetHeight;
                if(wrapper0Height > fullWrapperHeight / 2){
                    this.isScrollBar.maxHeight = fullWrapperHeight / 2 + 'px';
                    wrapper0Height = fullWrapperHeight/2;
                }
                this.autoHeight = fullWrapperHeight - wrapper0Height - 70 ;
                this.$refs.reviewTable.resizeTable(this.autoHeight );
            },
            clearDate(){
                this.stamp = {};
                this.showDetail = false;
                this.$nextTick(this.re_layout);
            },
            rowCheck(item) {
                getCheckDetail(item.check_id, item.object_type).then(res => {
                    if(res.data && res.data.code == '0') {
                        this.showDetail = true;
                        this.stamp = res.data.data;
                        this.stamp.stampType = showText(this.stamp.stamp_type, this.stampTypeList);
                        this.stamp.operatorNameList = showTextList(this.stamp.operator_list, this.operatorList);
                        if (item.object_name.indexOf("jpg") != -1 || item.object_name.indexOf("png") != -1
                            || item.object_name.indexOf("PNG") != -1 || item.object_name.indexOf("JPG") != -1) {
                            this.imgInfo.imgUrl2 = window.LOCAL_CONFIG.API_HOME + '/systemmgnt/stamp/downloadStamp.json?fileId='+this.stamp.file_id;
                        } else {
                            stampHsel2Jpg(this.stamp.file_id).then(res =>{
                                this.imgInfo.imgUrl2 = res.data.stampInfo;
                            })
                        }
                        if (item.check_type == 3) {
                            getStampById(this.stamp.stamp_id).then(res => {
                                if (res.data) {
                                    if (res.data.stamp_name.indexOf("jpg") != -1 || res.data.stamp_name.indexOf("png") != -1
                                        || res.data.stamp_name.indexOf("PNG") != -1 || res.data.stamp_name.indexOf("JPG") != -1) {
                                        this.imgInfo.imgUrl1 = window.LOCAL_CONFIG.API_HOME + '/systemmgnt/stamp/downloadStamp.json?fileId='+res.data.file_id;
                                    } else {
                                        stampHsel2Jpg(res.data.file_id).then(res =>{
                                            this.imgInfo.imgUrl1 = res.data.stampInfo;
                                        })
                                    }
                                }
                                if (res.data.file_id == this.stamp.file_id) {
                                    this.showModify = false
                                } else {
                                    this.showModify = true
                                }
                            })
                        }
                        this.$nextTick(this.re_layout);
                    } else {
                        this.openMask();
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    }
                })
                this.checkType = item.check_type;
            },

        },
        components: {ReviewTable}
    }
</script>

<style lang="scss" src="@/style/common.scss"></style>
<style lang="less" rel="stylesheet/less">
    .page-review {
        .item {
            margin-bottom: 8px;
            padding-right: 10px;
        }
        .detail {
            border: solid 1px #d3d3d3;
        }
        .wrapper {
            width: 100%;
            // height: 208px;
            padding: 10px;
            background-color: #f6f7f9;

            .headline {
                padding-bottom: 10px;
                display: flex;
                flex-direction: row;
                align-items: center;

                .icon-l {
                    display: block;
                    width: 3px;
                    height: 12px;
                    background-color: #7e868b;
                    margin-right: 5px;
                }

                .title {
                    padding: 0;
                    margin-right: 10px;
                    font-size: 12px;
                    color: #666666;
                }

                .icon-operation {
                    cursor: pointer;
                    color: #7e868b;
                }
            }

            .inner {
                // height: 196px;
                padding: 0 12px;

                &:nth-child(4n + 1) {
                    padding-left: 0;
                }

                &:not(:nth-child(4n)) {
                    border-right: dashed 1px #666666;
                }
            }

            .image-inner {
                // height: 196px;
                padding: 0 12px;

                &:nth-child(4n + 1) {
                    padding-left: 0;
                }
            }

            .inner-t,
            .inner,
            .image-inner {
                .item {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;
                    padding-right: 0;
                    white-space: nowrap;
                    line-height: 21px;

                    &:last-child {
                        margin-bottom: 0;
                    }

                    .label {
                        white-space: nowrap;
                        font-size: 12px;
                        color: #999999;
                    }

                    .value {
                        padding-left: 10px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        font-size: 14px;
                        color: #333333;
                    }
                }
                .image {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;
                    padding-right: 0;
                    white-space: nowrap;
                    line-height: 21px;
                }
            }
        }
    }

</style>


