<template>
<div id="full-h-wrapper" class="page-review full-h">
    <review-table  :on-selection-change="rowClick" :clear-data="clearData" :query-check="queryCheck" ref="reviewTable"></review-table>
    <div class="detail" v-if="accountInfo.account_class" id="wrapper-0" v-bind:style="isScrollBar">
        <div class="wrapper">
            <header class="headline">
                <i class="icon-l"></i>
                <span class="title">详情</span>
            </header>
            <h-row>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">账户用途</div>
                        <div class="value" :title="showText(accountInfo.account_class,accountClassList)">{{showText(accountInfo.account_class,accountClassList)}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">账户名称</div>
                        <div class="value" :title="accountInfo.bank_account_name">{{accountInfo.bank_account_name}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">银行账号</div>
                        <div class="value" :title="accountInfo.bank_account_no">{{accountInfo.bank_account_no}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">大额支付号</div>
                        <div class="value" :title="accountInfo.pay_no">{{accountInfo.pay_no}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='a'||accountInfo.account_class=='e'||accountInfo.account_class == 'g'">
                    <div class="item">
                        <div class="label">业务标志</div>
                        <div class="value" :title="showTextList(accountInfo.cop_busin_flag_list,businFlagList)">{{showTextList(accountInfo.cop_busin_flag_list,businFlagList)}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='7'">
                    <div class="item">
                        <div class="label">交易对手</div>
                        <div class="value" :title="rivalName">{{rivalName}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='f'">
                    <div class="item">
                        <div class="label">券商</div>
                        <div class="value" :title="brokerName">{{brokerName}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='f'">
                    <div class="item">
                        <div class="label">证券代码</div>
                        <div class="value" :title="accountInfo.report_code">{{accountInfo.report_code}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='f'">
                    <div class="item">
                        <div class="label">证券名称</div>
                        <div class="value" :title="accountInfo.stock_name">{{accountInfo.stock_name}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='d' || accountInfo.account_class=='i'">
                    <div class="item">
                        <div class="label">发行人</div>
                        <div class="value" :title="issuerName">{{issuerName}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='h'">
                    <div class="item">
                        <div class="label">销售机构</div>
                        <div class="value" :title="showText(accountInfo.agency_code,agencyList)">{{showText(accountInfo.agency_code,agencyList)}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class == 'g'||accountInfo.account_class=='e'||accountInfo.account_class=='4'||accountInfo.account_class=='9'">
                    <div class="item">
                        <div class="label">可使用的产品</div>
                        <div class="value" :title="showTextList(accountInfo.fund_id_list,fundList)">{{showTextList(accountInfo.fund_id_list,fundList)}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='9'">
                    <div class="item">
                        <div class="label">TA类型</div>
                        <div class="value" :title="showText(accountInfo.ta_type,taTypeList)">{{showText(accountInfo.ta_type,taTypeList)}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='9'">
                    <div class="item">
                        <div class="label">销售渠道</div>
                        <div class="value" :title="showText(accountInfo.sale_channel,saleChannelList)">{{showText(accountInfo.sale_channel,saleChannelList)}}</div>
                    </div>
                </h-col>

                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='a'||accountInfo.account_class=='b'||accountInfo.account_class=='c'">
                    <div class="item">
                        <div class="label">托管人</div>
                        <div class="value" :title="showTextList(accountInfo.bank_no_list,bankInfoList)">{{showTextList(accountInfo.bank_no_list,bankInfoList)}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class=='i'">
                    <div class="item">
                        <div class="label">证券代码列表</div>
                        <div class="value" :title="showTextList(accountInfo.stock_code_list)">{{accountInfo.stock_code_list}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">银行总行</div>
                        <div class="value" :title="showText(accountInfo.bic_code,bankList)">{{showText(accountInfo.bic_code,bankList)}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner" v-if="accountInfo.account_class">
                    <div class="item">
                        <div class="label">开户行名称</div>
                        <div class="value" :title="accountInfo.sub_bank_no">{{accountInfo.sub_bank_no}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">账户类型</div>
                        <div class="value" :title="showText(accountInfo.bank_account_type,accountTypeList)">{{showText(accountInfo.bank_account_type,accountTypeList)}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">账户状态</div>
                        <div class="value" :title="showText(accountInfo.bank_account_status,accountStatusList)">{{showText(accountInfo.bank_account_status,accountStatusList)}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">开户城市</div>
                        <div class="value" :title="showCity(provinceCity,cityTree)">{{showCity(provinceCity,cityTree)}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">开户日期</div>
                        <div class="value" :title="accountInfo.open_date">{{accountInfo.open_date}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">联系人</div>
                        <div class="value" :title="accountInfo.bank_linkman">{{accountInfo.bank_linkman}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">联系电话</div>
                        <div class="value" :title="accountInfo.bank_phone">{{accountInfo.bank_phone}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">传真</div>
                        <div class="value" :title="accountInfo.bank_fax">{{accountInfo.bank_fax}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">邮箱</div>
                        <div class="value" :title="accountInfo.bank_mail">{{accountInfo.bank_mail}}</div>
                    </div>
                </h-col>
                <h-col span="6" class-name="inner">
                    <div class="item">
                        <div class="label">备注</div>
                        <div class="value" :title="accountInfo.remark">{{accountInfo.remark}}</div>
                    </div>
                </h-col>
            </h-row>
            <header v-if="accountInfo.modify_desc" class="headline mt10 pt10 border-dashed-top">
                <i class="icon-l"></i>
                <span class="title">变更内容</span>
            </header>
            <h-row v-if="accountInfo.modify_desc">
                <h-col span="6" class="inner" v-for="(item, index) in accountInfo.modify_desc.split(';')" :key="index">
                    <div class="item">
                        <div class="label">{{decodeURIComponent(item.split(":")[0])}}</div>
                        <div class="value" :title="decodeURIComponent(item.split(':')[1])">{{decodeURIComponent(item.split(":")[1])}}</div>
                    </div>
                </h-col>
            </h-row>
            <!-- <div v-if="accountInfo.modify_desc" class="inner-t pr10" style="width: 25%;">
            <div v-for="(item, index) in accountInfo.modify_desc.split(';')" :key="index" class="item">
                <div class="label">{{item.split(":")[0]}}</div>
                <div class="value">{{item.split(":")[1]}}</div>
            </div>
        </div> -->

        </div>
    </div>
</div>
</template>

<script>
import HDatagrid from '@/components/HDatagrid'
import {
    operatorList,
    getDictList,
    getFundInfo,
    getBankList,
    getBrokerList,
    getCityTree,
    getRivalList,
    getIssuerList,
    getBusinFlagList,
    showCity,
    showText,
    getAgencyList,
    pageBrokerList
} from '@/api/utils'

import {
    getCheckDetail,
    submitReview
} from '@/api/fundAccount/review'
import ReviewTable from "../../../components/ReviewTable";

export default {
    name: "review",
    data() {
        return {
            autoHeight: 0,
            queryCheck: {
                checkStatus: '1',inputOperator:null,
                objectType: 2
            },
            showFund: false,
            fundInfo: {
                fund_id: null,
                fund_name: "",
                fund_code: '',
                fund_mgr_operator: "",
                fund_status: '',
                fund_trust_bank: '',
                fund_type: '',
                establish_date: '',
                expire_date: '',
                interbank_id: '',
                paper_no: '',
                stockholder_id: '',
                object_code: '',
                sh_ta_account: '',
                sz_ta_account: '',
                transfer_flag: '',
                file_name: '',
                data_source: '',
                fund_mail: '',
                fund_accountant: '',
                ta_settle_type: '',
                fund_settle_type: '',
                trust_flag: ''
            },
            isScrollBar:{
                marginTop: '10px',
                overflow: 'auto',
                maxHeight: ''
            },
            accountInfo: {},
            bankList: [],
            operatorList: [],
            fundTypeList: [],
            dataSourceList: [],
            fundSettleTypes: [],
            taSettleTypes: [],
            accountTypeList: [],
            accountStatusList: [],
            bankFuturesTypeList: [],
            brokerList: [],
            cityTree: [],
            businFlagList: [],
            fundList: [],
            bankInfoList: [],
            rivalList:[],
            saleChannelList:[{code:'1',text:'直销'},{code:'2',text:'代销'}],
            rivalName: null,
            brokerName: null,
            taTypeList: [{
                    code: '1',
                    text: '自建TA'
                },
                {
                    code: '2',
                    text: '中登上海'
                },
                {
                    code: '3',
                    text: '中登深圳'
                },
            ],
            issuerName: null,
            accountClassList: [],
            agencyList: []
        }
    },
    created() {
        getDictList(610087).then(res => this.fundTypeList = res.data);
        getDictList(90531).then(res => this.fundSettleTypes = res.data);
        getDictList(90530).then(res => this.taSettleTypes = res.data);
        getDictList(90528).then(res => this.dataSourceList = res.data);
        getDictList(60058).then(res => this.accountTypeList = res.data);
        getDictList(200008).then(res => this.accountStatusList = res.data);
        getDictList(90532).then(res => this.bankFuturesTypeList = res.data);
        getDictList(90525).then(res => {
            this.accountClassList = res.data.filter( r => (r.code != 1 && r.code != 2 && r.code != 3 && r.code != 5 && r.code != 6 && r.code != 8));
        });

        operatorList().then(res => {
            if (res.data && res.data.length > 0) {
                this.operatorList = res.data;
            }
        });
        getBrokerList().then(res => {
            if (res.data && res.data.length > 0) {
                this.brokerList = res.data;
            }
        });
    },
    computed: {
        provinceCity: {
            get: function () {

                return [this.accountInfo.province_code, this.accountInfo.city_no]
            },
            set: function (newValue) {

                this.accountInfo.province_code = newValue[0]
                this.accountInfo.city_no = newValue[1]
            }
        }
    },
    mounted() {
        getCityTree().then(res => this.cityTree = res.data);
        this.re_layout();
    },
    components: {
        ReviewTable,
        HDatagrid
    },
    activated() {
        this.$refs.reviewTable.refresh();
        getBusinFlagList().then(res => {
            this.businFlagList = [{code:'-1',text:'全部'}];
            if (res.data && res.data.length > 0) {
                this.businFlagList = this.businFlagList.concat(res.data);
            }
        });
        getFundInfo('/common/fundNamelist.json?type='+'id').then(res => {
            this.fundList = [{code:'-1',text:'全部'}];
            if (res.data && res.data.length > 0) {
                this.fundList = this.fundList.concat(res.data);
            }
        });
        getBankList("1").then(res => {
            this.bankInfoList = [{code:'-1',text:'全部'}];
            if (res.data && res.data.length > 0) {
                this.bankInfoList = this.bankInfoList.concat(res.data);
            }
        });
        getAgencyList('').then(res => this.agencyList = res.data);
        getBankList().then(res => this.bankList = res.data);
    },
    methods: {
        re_layout() {
            let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
            let fullWrapperHeight = document.getElementById("full-h-wrapper").offsetHeight;
            if(wrapper0Height > fullWrapperHeight / 2){
                this.isScrollBar.maxHeight = fullWrapperHeight / 2 + 'px';
                wrapper0Height = fullWrapperHeight/2;
            }
            this.autoHeight = fullWrapperHeight - wrapper0Height - 70 ;
            this.$refs.reviewTable.resizeTable(this.autoHeight );
        },
        showTextList(value, keyValueList) {
            let label = ''
            if (value) {

                value.split(",").forEach(x => label = label + "，" + showText(x, keyValueList));
                label = label.substr(1, label.length)
            }
            return label
        },
        showCity:showCity,
        showText: showText,
        clearData(){
            this.accountInfo = {};
            this.$nextTick(this.re_layout);
        },
        rowClick(arr) {
            getCheckDetail(arr.check_id, arr.object_type).then(res => {
                if (res.data && res.data.code == '0') {
                    this.showFund = false;
                    this.accountInfo = res.data.data;
                    if (this.accountInfo.issuer_code) {
                        getIssuerList(0, 5, this.accountInfo.issuer_code).then(res => this.issuerName = res.data.list[0].text);
                    } else {
                        this.issuerName = '';
                    }
                    if (this.accountInfo.rival_code && this.accountInfo.account_class != 'f') {
                        getRivalList(0, 5, this.accountInfo.rival_code).then(res => this.rivalName = res.data.list[0].text);
                    }
                    if (this.accountInfo.rival_code && this.accountInfo.account_class == 'f') {
                        pageBrokerList(1, 5, this.accountInfo.rival_code).then(res => this.brokerName = res.data.list[0].text);
                    }

                    /* re layout, 模拟自适应布局 */
                    this.$nextTick(this.re_layout);
                } else {
                    this.openMask();
                    this.$alert(res.data.msg,'错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                }

            }).catch(error => {
            });
        },
    }
}
</script>

<style lang="scss" src="@/style/common.scss"></style>
<style lang="less" rel="stylesheet/less">
.page-review {
    .item {
        margin-bottom: 8px;
        padding-right: 10px;
    }
    .detail {
        border: solid 1px #d3d3d3;
    }
    .wrapper {
        width: 100%;
        // height: 208px;
        padding: 10px;
        background-color: #f6f7f9;

        .headline {
            padding-bottom: 10px;
            display: flex;
            flex-direction: row;
            align-items: center;

            .icon-l {
                display: block;
                width: 3px;
                height: 12px;
                background-color: #7e868b;
                margin-right: 5px;
            }

            .title {
                padding: 0;
                margin-right: 10px;
                font-size: 12px;
                color: #666666;
            }

            .icon-operation {
                cursor: pointer;
                color: #7e868b;
            }
        }

        .inner {
            // height: 196px;
            padding: 0 12px;

            &:nth-child(4n + 1) {
                padding-left: 0;
            }

            &:not(:nth-child(4n)) {
                border-right: dashed 1px #666666;
            }
        }

        .inner-t,
        .inner {
            .item {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                padding-right: 0;
                white-space: nowrap;
                line-height: 21px;

                &:last-child {
                    margin-bottom: 0;
                }

                .label {
                    white-space: nowrap;
                    font-size: 12px;
                    color: #999999;
                }

                .value {
                    padding-left: 10px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    font-size: 14px;
                    color: #333333;
                }
            }
        }
    }
}

</style>



