import fetch from "@/api/httpFetch";

export function addStamp(params) {
    return fetch.post('/systemmgnt/stamp/addStamp.json',params,{
        headers:{
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest(req){
            return JSON.stringify(req)
        }
    })
}

export function modifyStamp(params) {
    return fetch.post('/systemmgnt/stamp/updateStamp.json',params,{
        headers:{
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest(req){
            return JSON.stringify(req)
        }
    })
}

export function deleteStamp(stampIds) {
    return fetch.post('/systemmgnt/stamp/deleteStamp.json',{stampIds:stampIds})
}

export function queryStampByType(type) {
    return fetch.post('systemmgnt/stamp/stampByType.json',{type:type})
}

export function addDealStamp(fundIdList,stampId) {
    return fetch.post('systemmgnt/dealStamp/add.json',{fundIdList:fundIdList,stampId:stampId})
}

export function modifyDealStamp(fundId,stampId) {
    return fetch.post('systemmgnt/dealStamp/modify.json',{fundId:fundId,stampId:stampId})
}

export function deleteDealStamp(fundIds) {
    return fetch.post('systemmgnt/dealStamp/delete.json',{fundIds:fundIds})
}

export function getStampById(stampId) {
    return fetch.post('/systemmgnt/stamp/getStampById.json', {stampId: stampId})
}




