<template>
    <div class="full-h" >
        <h-row>
            <h-col>
                <el-form style="font-size: 12px;":inline="true" size="mini" :model="queryForm" class="demo-form-inline">
                    <el-form-item label="交收日期">
                        <el-date-picker v-model="initQueryDate" type="date" placeholder="选择日期"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="产品名称">
                        <el-select v-model="initFundIds" filterable multiple clearable collapse-tags>
                            <el-option v-for="item in fundIdList" :key="item.code" :label="item.text" :value="item.code"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="日初对账">
                        <el-select v-model="queryForm.initCkaccStatus" clearable>
                            <el-option v-for="item in checkIsOrNo" :key="item.code" :label="item.text" :value="item.code"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="refresh">查询</el-button>
                        <el-button type="ghost" @click="initPass">对账</el-button>
                        <el-button type="ghost" @click="initRemark">备注</el-button>
                    </el-form-item>
                </el-form>
            </h-col>
        </h-row>
        <h-datagrid
                    :height="autoHeight" v-model="initTemple"
                    stripe hasPage highlight-row
                    :auto-load="false"
                    url="/cashcheck/daily/allocInit.json"
                    :pageSize="100" :pageSizeOpts="pageSizeOpts"
                    :columns="allocInitColumns" :bindForm="queryForm"
                    ref="datagrid">
        </h-datagrid>
        <div class="wrapper mt10" id="inner-on-transfer-approve">

            <h-row class-name="border-dashed-top pt10 mt10">
                <header class="headline">
                    <h-icon name="pause" ></h-icon>
                    <span class="title" style="font-size: 14px">对账进度</span>
                </header>
                <h-col span="8" class="inner">
                    <div class="item">
                        <div class="label" style="font-size: 16px;">共{{allocProcess.fundCount}}个产品</div>
                    </div>
                </h-col>
                <h-col span="8" class="inner">
                    <div class="item">
                        <div class="label" style="font-size: 16px;color: #DA0001">{{allocProcess.unfinishedCount}}个未开始</div>
                    </div>
                </h-col>
                <h-col span="8" class="inner">
                    <div class="item">
                        <div class="label" style="font-size: 16px;color: #3BB300">{{allocProcess.completedCount}}个对账完成</div>
                    </div>
                </h-col>
            </h-row>
        </div>
        <h-msg-box class="customMsgBox" v-model="showAllocInitDialog" width="300" :mask-closable=false>
            <p slot="header">
                <span>备注</span>
            </p>
            <h-form :model="allocInitInfo" :label-width="60" ref="allocInitForm" >
                <h-form-item label="产品名称" prop="fund_name">
                    <h-input disabled type="text" v-model="allocInitInfo.fund_name"></h-input>
                </h-form-item>
                <h-form-item label="差额" prop="init_diff_balance">
                    <h-typefield disabled type="money" suffixNum="2" divided
                                 v-model="allocInitInfo.init_diff_balance">
                    </h-typefield>
                </h-form-item>
                <h-form-item label="备注" prop="init_check_acc_remark">
                    <h-input type="text" v-model="allocInitInfo.init_check_acc_remark"></h-input>
                </h-form-item>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="allocInitCancel">取消</h-button>
                <h-button type="primary" @click="saveRemark">确认</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import HDatagrid from "../../../../components/HDatagrid";

    import {
        showText,
        formatMoney,
        getDictList,
        getFundInfo,
        parseDateString,
        parseDateStr
    } from "@/api/utils";

    import {
        initAccConfirm,
        updateInitAccRemark,
        queryAllocInit
    } from "@/api/cashcheck/cashCheck"

    import Cookies from 'js-cookie';

    export default {
        name: "balAccInitTemple",
        components: {HDatagrid},
        data(){
            return{
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                fundIdList:[],
                checkIsOrNo:[],
                initTemple:[],
                transferTypeList:[],
                checkAccResultList:[],
                checkAccStatusList:[{ code:'0', text:'未对账'},{ code:'1', text:'已对账'}],
                sysDate:Cookies.get('CurrSysDate'),
                showAllocInitDialog:false,
                allocInitColumns:[
                    {
                        title: "交收日期",
                        key: "curr_date",
                        align: "center",
                        ellipsis: true
                    },{
                        title: "产品编号",
                        key: "fund_id",
                        align: "center",
                        ellipsis: true,
                        hiddenCol:true
                    },{
                        title: "产品代码",
                        key: "fund_code",
                        align: "center",
                        ellipsis: true
                    },{
                        title: "产品名称",
                        key: "fund_name",
                        align: "center",
                        ellipsis: true
                    },{
                        title: "划款模式",
                        key: "transfer_type",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let transferType = showText(rowData.transfer_type,this.transferTypeList)
                            return h("span",{},transferType)
                        }
                    },{
                        title: "T-1日日终账套余额",
                        key: "account_last_balance",
                        align: 'center',
                        ellipsis: true,
                        width:120,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let accountLastBalance = formatMoney(rowData.account_last_balance)
                            return h("span",{style:{fontWeight:'bold'}},accountLastBalance)
                        }
                    },{
                        title: "日初账套余额",
                        key: "account_init_balance",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let accountInitBalance = formatMoney(rowData.account_init_balance)
                            return h("span",{style:{fontWeight:'bold'}},accountInitBalance)
                        }
                    },{
                        title: "日初证券清算款",
                        key: "stock_clear_balance",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let stockClearBalance = formatMoney(rowData.stock_clear_balance)
                            return h("span",{style:{fontWeight:'bold'}},stockClearBalance)
                        }
                    },{
                        title: "日初投资系统可用",
                        key: "o32_init_balance",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let o32InitBalance = formatMoney(rowData.o32_init_balance)
                            return h("span",{style:{fontWeight:'bold'}},o32InitBalance)
                        }
                    },{
                        title: "投资系统当前资金冻结金额",
                        key: "o32_init_frozen_balance",
                        align: 'center',
                        ellipsis: true,
                        width:130,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let o32InitFrozenBalance = formatMoney(rowData.o32_init_frozen_balance)
                            return h("span",{style:{fontWeight:'bold'}},o32InitFrozenBalance)
                        }
                    },{
                        title: "日初差额",
                        key: "init_diff_balance",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let initDiffBalance = formatMoney(rowData.init_diff_balance)
                            return h("span",{style:{fontWeight:'bold'}},initDiffBalance)
                        }
                    },{
                        title: "对账结果",
                        key: "init_check_acc_result",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) => {
                            const rowData = params.row;
                            let initChckAccResult = showText(rowData.init_check_acc_result,this.checkAccResultList)
                            return h("span",{},initChckAccResult)
                        }
                    },{
                        title: "对账状态",
                        key: "init_check_acc_status",
                        align: 'center',
                        ellipsis: true,
                        render: (h,params) => {
                            const rowData = params.row;
                            let initChckAccStatus = showText(rowData.init_check_acc_status,this.checkAccStatusList)
                            return h("span",{},initChckAccStatus)
                        }
                    },{
                        title: "日初备注",
                        key: "init_check_acc_remark",
                        align: 'center',
                        ellipsis: true
                    }
                ],
                queryForm:{
                    initQueryDate:Cookies.get('CurrSysDate'),
                    initCkaccStatus:'',
                    initFundIds:''
                },
                allocInitInfo:{
                    fund_id:'',
                    curr_date:'',
                    fund_code:'',
                    fund_name:'',
                    init_diff_balance:'',
                    init_check_acc_remark:''
                },
                allocProcess:{
                    fundCount:0,
                    unfinishedCount:0,
                    completedCount:0
                }
            }
        },
        watch:{
            showAllocInitDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
            initQueryDate:{
                get:function () {
                    if(this.queryForm.initQueryDate)
                        return parseDateString(this.queryForm.initQueryDate)
                    else
                        return parseDateString(this.sysDate)
                },
                set:function (newValue) {
                    this.queryForm.initQueryDate = parseDateStr(newValue)
                }
            },
            initFundIds:{
                get:function () {
                    if(this.queryForm.initFundIds)
                        return this.queryForm.initFundIds.split(",")
                    else
                        return []
                },
                set:function (newValue) {
                    this.queryForm.initFundIds = newValue.join(",")
                }
            }
        },
        activated(){
            getFundInfo('/common/fundNamelist.json?type='+'id').then(res => this.fundIdList = res.data)
        },
        mounted(){
            getDictList(90529).then(res => this.checkAccResultList = res.data)
            getDictList(610001).then(res => this.checkIsOrNo = res.data)
            getDictList(90524).then(res => this.transferTypeList = res.data)
            this.refresh();
        },
        methods:{
            refresh(){
                queryAllocInit(this.queryForm.initQueryDate,this.queryForm.initCkaccStatus,this.queryForm.initFundIds,this.$refs.datagrid.getPageNo(),this.$refs.datagrid.getPageSize()).then(res => {
                    this.initTemple = res.data.list
                    this.loadSuccess(this.initTemple);
                    this.$refs.datagrid.dataChange(1);
                })
            },
            loadSuccess(list){
                let data = list;
                if(data.length > 0){
                    let completeCount=0;
                    for (let i = 0; i < data.length; i++) {
                        if(data[i].init_check_acc_status=='1'){
                            completeCount++;
                        }
                    }
                    let unCompleteCount=data.length-completeCount;
                    this.allocProcess = {
                        fundCount:data.length,
                        unfinishedCount:unCompleteCount,
                        completedCount:completeCount
                    }
                }else{
                    this.allocProcess = {
                        fundCount:0,
                        unfinishedCount:0,
                        completedCount:0
                    }
                }
            },
            resizeTable(height){
                this.autoHeight = height
            },
            initPass(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('当前不存在日初对账数据，不允许日初余额核对','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                if(this.sysDate != this.queryForm.initQueryDate){
                    this.openMask();
                    this.$alert('非系统当日，不允许日初余额核对','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                initAccConfirm().then(res =>{
                    if(res && res.status == 200){
                        if(res.data.errorNo == '0'){
                            this.openMask();
                            this.$alert('操作成功','成功',{
                                confirmButtonText:'确定',
                                type:'success',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                            this.refresh();
                        }else{
                            this.openMask();
                            this.$alert(res.data.errorInfo,'错误',{
                                confirmButtonText:'确定',
                                type:'error',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                        }
                    }else{
                        this.openMask();
                        this.$alert('日初余额对账操作失败','错误',{
                            confirmButtonText:'确定',
                            type:'error',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    }
                })
            },
            initRemark(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                if(selects[0].curr_date < parseDateStr(this.sysDate)){
                    this.openMask();
                    this.$alert('只能修改当日数据的备注!','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                this.allocInitInfo = selects[0];
                this.$refs.allocInitForm.resetFields();
                this.showAllocInitDialog = true;
            },
            allocInitCancel(){
                this.showAllocInitDialog = false;
            },
            saveRemark(){
                let params = this.allocInitInfo.fund_id +','+this.allocInitInfo.curr_date+','+this.allocInitInfo.init_check_acc_remark;
                updateInitAccRemark(params).then(res => {
                    if(res.data.success){
                        this.$alert('操作成功','成功',{
                            confirmButtonText:'确定',
                            type:'success',
                            callback:action => {
                                this.showAllocInitDialog = false;
                            }
                        }).then(() => {
                            this.showAllocInitDialog = false;
                        })
                        this.refresh()
                    }else{
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText:'确定',
                            type:'error'
                        })
                    }
                })
            }
        }
    }
</script>

<style scoped>
</style>


