import fetch from "@/api/httpFetch";

export function sysParamQuery() {
    return fetch.post('/systemmgnt/systemparameter.json');
}

export function sysParamValue(sysParamNo) {
    return fetch.post('/systemmgnt/sysParamValue.json',{sysParamNo:sysParamNo});
}

export function sysParamPassword(sysParamNo) {
    return fetch.post('/systemmgnt/sysParamPassword.json',{sysParamNo:sysParamNo});
}

export function sysParamProcess(systemParam) {
    return fetch.post('/systemmgnt/parameProcess.json',systemParam, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function approveRuleChange() {
    return fetch.post('/systemmgnt/countUnFinishTransfer.json');
}


