<template>

    <!-- 指令新增/修改/详情弹出窗 -->
    <h-msg-box class="customMsgBox" v-model="showDialog" width="1040" top="24"  :footerHide="true" scrollable>
        <p slot="header">
            <span v-if="actionType=='add'">新增指令</span>
            <span v-if="actionType=='modify'">修改指令</span>
            <span v-if="actionType=='detail'">指令详情</span>
        </p>

        <div class="block-in-box border-dashed-bottom" style="border-color: #d3d3d3;">
            <header class="headline pl5">
                <i class="icon-l"></i>
                <span class="title">业务类型</span>
            </header>
            <h-form :model="transferInfo" :label-width="100" ref="transForm0" cols="3">
                <h-form-item required label="产品名称" prop="fund_id">
                    <h-input  disabled v-if="actionType!='add'" type="text"
                              v-model="transferInfo.fund_name">
                    </h-input>
                </h-form-item>

                <h-form-item required label="业务标志">
                    <el-cascader disabled style="width: 100%" size="mini" v-model="businClassFlag" :options="businClassTree"></el-cascader>
                </h-form-item>

                <h-form-item required v-if="showClearAgent" label="清算机构" prop="clear_agent_code_cn" style="height: 35.77px;">
                    <h-select disabled v-model="transferInfo.clear_agent_code_cn">
                        <h-option v-for="item in agentCodeList" :key="item.code" :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>

                <h-form-item label="投资类型" prop="invest_type" style="height: 35.77px;">
                    <h-select v-model="transferInfo.invest_type" :disabled="actionType=='detail'" filterable>
                        <h-option v-for="(item,index) in investTypeList" :key="index"
                                  :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>

                <h-form-item v-if="showRival" label="对手方"
                             prop="rival_code">
                    <h-input  disabled v-if="actionType!='add'" type="text"
                              v-model="transferInfo.traderival_name">
                    </h-input>
                </h-form-item>

                <h-form-item v-if="showAgency" label="销售机构"
                             prop="agency_code">
                    <h-select v-model="transferInfo.agency_code" :disabled="actionType=='detail'" filterable>
                        <h-option v-for="(item,index) in agencyList" :key="index"
                                  :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>

                <h-form-item v-if="transferInfo.cop_busin_class=='22'"  label="费用起始日期" prop="fee_begin_date">
                    <h-date-picker :disabled="actionType=='detail'" v-model="beginDate" type="date" format="yyyyMMdd" placeholder="选择日期">
                    </h-date-picker>
                </h-form-item>
                <h-form-item v-if="transferInfo.cop_busin_class=='22'"  label="费用结束日期" prop="fee_end_date">
                    <h-date-picker :disabled="actionType=='detail'" v-model="endDate" type="date" format="yyyyMMdd" placeholder="选择日期">
                    </h-date-picker>
                </h-form-item>

                <h-form-item label="特殊审批人" prop="approve_officer"
                             v-if="canSpecialApprove!=null&&canSpecialApprove==='1'" style="height: 35.77px;">
                    <h-select v-model="transferInfo.approve_officer" :disabled="actionType=='detail'" filterable>
                        <h-option v-for="(item,index) in specialApproveWorkerList" :key="index"
                                  :label="item.operator_name" :value="item.operator_no">
                        </h-option>
                    </h-select>
                </h-form-item>

                <h-form-item v-if="actionType=='detail' && showReportCode"
                             label="证券代码" prop="report_code" >
                    <h-input disabled type="text" v-model="transferInfo.report_code"></h-input>
                </h-form-item>

                <h-form-item v-if="actionType=='detail' && transferInfo.stock_name"
                             label="证券名称" prop="stock_name">
                    <h-input disabled type="text" v-model="transferInfo.stock_name"></h-input>
                </h-form-item>

                <h-form-item v-if="actionType=='detail' && transferInfo.market_no"
                             label="交易市场" prop="market_no" >
                    <h-select disabled v-model="transferInfo.market_no"  filterable>
                        <h-option v-for="item in marketList" :key="item.code"
                                  :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>

                <!-- <h-form-item label="附件" prop="transfer_remark">
                    <el-upload ref="uploader" :disabled="actionType=='detail'" class="upload-demo" :action="uploadUrl" :data="uploadData" with-credentials :on-success="fileUploaded" :on-remove="fileRemoved" :on-preview="downloadFile" :on-exceed="exceedLimit" :limit="5" :file-list="fileList">
                        <el-button size="mini" type="ghost">上传</el-button>
                    </el-upload>
                </h-form-item> -->
            </h-form>
            <h-form v-for="(item, index) in seatList" :key="index" :label-width="100" cols="3" :model="item" v-if="transferInfo.cop_busin_class=='22'&&transferInfo.cop_busin_flag=='20061'" ref="seatListForm">
                <h-row>
                    <h-col span="8">
                        <h-form-item :label='"席位号" + (index + 1)'  prop="seatId">
                            <h-input type="text" :maxlength="6" placeholder="请输入" v-model="item.seatId" :disabled="actionType=='detail'"></h-input>
                        </h-form-item>
                    </h-col>
                    <h-col span="8">
                        <h-form-item :label='"佣金费" + (index + 1)'  prop="fee">
                            <h-typefield type="money" placeholder="请输入" bigTips divided integerNum="12" v-model="item.fee" :disabled="actionType=='detail'" @on-blur="">
                                <span slot="append">元</span>
                            </h-typefield>
                        </h-form-item>
                    </h-col>
                    <h-col span="8" v-if="actionType!='detail'">
                        <h-icon v-if="index <= 0" name="addition" size="20" color="#11a0f8" class="pointer pl10" @on-click=""></h-icon>
                        <h-icon v-else name="offline" size="20" color="#11a0f8" class="pointer pl10" @on-click=""></h-icon>
                    </h-col>
                </h-row>
            </h-form>
        </div>

        <div class="block-in-box border-dashed-bottom pt10 pb10" style="border-color: #d3d3d3;">
            <h-row>
                <h-col span="8" class-name="border-dashed-right pl10 pr10" v-bind:style="accountStyle">
                    <header class="headline">
                        <i class="icon-l"></i>
                        <span class="title">出</span>
                    </header>
                    <h-form :model="outAcct" :label-width="100" ref="outAcctForm" cols="1">
                        <h-form-item  label="账户名称" prop="bank_account_name">
                            <h-input :disabled="actionType=='detail'" :title="outAcct.bank_account_name" v-model="outAcct.bank_account_name">
                            </h-input>
                        </h-form-item>
                        <h-form-item  label="银行账号" prop="bank_account_no">
                            <h-input :disabled="actionType=='detail'" :title="outAcct.bank_account_no" v-model="outAcct.bank_account_no">
                            </h-input>
                        </h-form-item>
                        <h-form-item  label="银行总行" prop="bic_code">
                            <el-select size="small" style="width: 100%" :disabled="actionType=='detail'" placement="top" showTitle v-model="outAcct.bic_code" :transfer="true" filterable>
                                <el-option v-for="item in bankList" :key="item.code" :label="item.text" :value="item.code">
                                </el-option>
                            </el-select>
                        </h-form-item>

                        <h-form-item label="开户行" prop="sub_bank_no">
                            <el-input type="textarea" resize="none" :disabled="actionType=='detail'" :autosize="{minRows: 1,maxRows: 2}" :title="outAcct.sub_bank_no" v-model="outAcct.sub_bank_no"></el-input>
                            <!--<h-input type="textarea" :disabled="actionType=='detail'" :autosize="{minRows: 1,maxRows: 3}" :title="outAcct.sub_bank_no" v-model="outAcct.sub_bank_no"></h-input>-->
                        </h-form-item>
                        <h-form-item label="大额支付号" prop="pay_no">
                            <h-input :disabled="actionType=='detail'" :title="outAcct.pay_no" v-model="outAcct.pay_no"></h-input>
                        </h-form-item>
                        <h-form-item label="开户城市" prop="city_no">
                            <el-cascader size="mini" style="width: 100%" :disabled="actionType=='detail'" v-model="outProvinceCity" :options="cityTree" filterable>
                            </el-cascader>
                        </h-form-item>
                    </h-form>
                </h-col>
                <h-col span="8" class-name="border-dashed-right pl10 pr10" v-bind:style="accountStyle">
                    <header class="headline">
                        <i class="icon-l"></i>
                        <span class="title">入</span>
                    </header>
                    <h-form :model="inAcct" :label-width="100" ref="inAcctForm" cols="1">
                        <h-form-item  label="账户名称" prop="bank_account_name">
                            <h-input :disabled="actionType=='detail'" :title="inAcct.bank_account_name" v-model="inAcct.bank_account_name">
                            </h-input>
                        </h-form-item>
                        <h-form-item  label="银行账号" prop="bank_account_no">
                            <h-input :disabled="actionType=='detail'" :title="inAcct.bank_account_no" v-model="inAcct.bank_account_no">
                            </h-input>
                        </h-form-item>
                        <h-form-item label="银行总行" prop="bic_code">
                            <el-select size="small" style="width: 100%" :disabled="actionType=='detail'" placement="top" showTitle v-model="inAcct.bic_code" :transfer="true" filterable>
                                <el-option v-for="item in bankList" :key="item.code" :label="item.text" :value="item.code">
                                </el-option>
                            </el-select>
                        </h-form-item>
                        <h-form-item label="开户行" prop="sub_bank_no">
                            <el-input type="textarea" resize="none" :disabled="actionType=='detail'" :autosize="{minRows: 1,maxRows: 2}" :title="inAcct.sub_bank_no" v-model="inAcct.sub_bank_no"></el-input>
                            <!--<h-input type="textarea" :disabled="actionType=='detail'" :autosize="{minRows: 1,maxRows: 3}" :title="inAcct.sub_bank_no" v-model="inAcct.sub_bank_no"></h-input>-->
                        </h-form-item>
                        <h-form-item label="大额支付号" prop="pay_no">
                            <h-input :disabled="actionType=='detail'" :title="inAcct.pay_no" v-model="inAcct.pay_no"></h-input>
                        </h-form-item>
                        <h-form-item label="开户城市" prop="city_no">
                            <el-cascader size="mini" style="width: 100%"  :disabled="actionType=='detail'" v-model="provinceCity" :options="cityTree" filterable>
                            </el-cascader>
                        </h-form-item>
                    </h-form>
                </h-col>
                <h-col span="8" v-bind:style="trusteeStyle">
                    <header class="headline pl10 pr10">
                        <i class="icon-l"></i>
                        <span class="title">联系信息</span>
                    </header>
                    <h-form  :label-width="100" ref="selfAcctForm" cols="1">
                        <h-form-item label="联系电话" prop="bank_phone">
                            <h-input :disabled="actionType=='detail'" :maxlength=32
                                     v-model="selfAcct.bank_phone">
                            </h-input>
                        </h-form-item>
                        <h-form-item label="邮箱" prop="bank_mail" >
                            <el-input placeholder="多个邮箱请用英文逗号隔开" size="small" type="textarea" maxlength="1024" resize="none" :disabled="actionType=='detail'" :autosize="{minRows: 1,maxRows:5}" :title="selfAcct.bank_mail" v-model="selfAcct.bank_mail"></el-input>
                        </h-form-item>
                        <h-form-item label="传真" prop="bank_fax">
                            <h-input :disabled="actionType=='detail'" :maxlength=32
                                     v-model="selfAcct.bank_fax">
                            </h-input>
                        </h-form-item>

                        <div v-if="showRivalContact">
                            <h-form-item label="基金公司电话" >
                                <h-input :disabled="actionType=='detail'" :maxlength=32
                                         v-model="rivalAcct.bank_phone">
                                </h-input>
                            </h-form-item>
                            <h-form-item label="基金公司邮箱" >
                                <el-input placeholder="多个邮箱请用英文逗号隔开" size="small" type="textarea" maxlength="1024" resize="none" :disabled="actionType=='detail'"
                                          :autosize="{minRows: 1,maxRows:5}" :title="rivalAcct.bank_mail" v-model="rivalAcct.bank_mail"></el-input>
                            </h-form-item>
                            <h-form-item label="基金公司传真" >
                                <h-input :disabled="actionType=='detail'" :maxlength=32
                                         v-model="rivalAcct.bank_fax">
                                </h-input>
                            </h-form-item>
                        </div>
                    </h-form>
                </h-col>
            </h-row>
        </div>

        <div class="block-in-box pt10 pb10">
            <h-row>
                <h-col span="16" class-name="border-dashed-right pl10 pr10" style="border-color: #d3d3d3;">
                    <header class="headline">
                        <i class="icon-l"></i>
                        <span class="title">划款信息</span>
                    </header>
                    <h-form :model="transferInfo" :label-width="100" ref="transForm">
                        <h-row>
                            <h-col span="10">
                                <h-form-item  label="划款金额" prop="settle_balance">
                                    <span slot="label" class="spcial-label-in-msg-box">划款金额</span>
                                    <h-typefield type="money" placeholder="" integerNum="15" suffixNum="2" divided
                                                 :disabled="actionType=='detail'"
                                                 v-model="transferInfo.settle_balance">
                                    </h-typefield>
                                </h-form-item>
                            </h-col>
                            <h-col span="2" >
                                <h-form-item :label-width="2">
                                <h-select style="height:28px "  v-model="transferInfo.currency_no" :disabled="actionType=='detail'" :setDefSelect="true" filterable>
                                    <h-option v-for="(item,index) in currencyList" :key="index"
                                              :label="item.text" :value="item.code">
                                    </h-option>
                                </h-select>
                                </h-form-item>
                            </h-col>
                            <h-col span="12">
                                <h-form-item  label="付款日期" prop="require_pay_date">
                                    <span slot="label" class="spcial-label-in-msg-box">付款日期</span>
                                    <h-date-picker disabled  v-model="payDate" type="datetime" format="yyyy-MM-dd HH:mm:ss" placeholder="选择日期">
                                    </h-date-picker>
                                </h-form-item>
                            </h-col>
                        </h-row>
                        <h-form-item label="大写金额">
                            <h-input disabled v-model="upBalance"></h-input>
                        </h-form-item>
                        <h-form-item label="划款事由" prop="transfer_purpose">
                            <h-input :disabled="actionType=='detail'" v-model="transferInfo.transfer_purpose"></h-input>
                        </h-form-item>
                        <h-form-item label="划款附言" prop="transfer_postscript">
                            <h-input :disabled="actionType=='detail'" v-model="transferInfo.transfer_postscript"></h-input>
                        </h-form-item>
                        <h-form-item label="划款备注" prop="transfer_remark">
                            <h-input :disabled="actionType=='detail'" v-model="transferInfo.transfer_remark"></h-input>
                        </h-form-item>
                    </h-form>
                </h-col>
                <h-col span="8" class-name="pl10">
                    <header class="headline">
                        <i class="icon-l"></i>
                        <span class="title">附件</span>
                    </header>
                    <el-upload ref="uploader" :disabled="actionType=='detail'"
                               class="drag-in-box" drag multiple
                               action="/"
                               :on-preview="downloadFile"
                               :limit="5" :file-list="fileList">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                    </el-upload>
                </h-col>
            </h-row>
        </div>

        <el-collapse v-model="activeName" v-show="actionType=='detail'">
            <el-collapse-item name="2">
                <template slot="title">
                    <i class="collapse-icon-l"></i>
                    <span class="collapse-title">指令操作明细</span>
                </template>
                <h-datagrid stripe :autoLoad="false" ref="flowData" highlight-row :columns="flowColumns" :bindForm="detailForm" url="/cashsettle/transferFlow.json">
                </h-datagrid>
            </el-collapse-item>
        </el-collapse>

    </h-msg-box>
</template>

<script>
    import {
        operatorList,
        getDictList,
        getFundInfo,
        getFundDetail,
        getBankList,
        getBrokerList,
        getCityTree,
        getRivalList,
        getIssuerList,
        getBusinFlagList,
        getBusinClassTree,
        parseDateString,
        parseDateStr,
        parseStrDate,
        smalltoBIG,
        getFileList,
        resolveMacro,
        getSysParamValue,
        formatMoney,
        showCity,
        showText,
        getAgencyList
    } from '@/api/utils'
    import {
        handleTransfer,
        getBusinClassGroup,
        getTransferMergeFileShowText,
        getTransferDetail,
        getSpecialApproveWorkerList
    } from '@/api/transfer/transfer'
    import HDatagrid from "./HDatagrid";

    export default {
        name: "TransferDetailDialog",
        components: {HDatagrid},
        watch: {
            'transferInfo.cop_busin_flag'(val) {
                if (this.transferInfo.cop_busin_class=='7'&&this.rivalContactFlags.indexOf(val.toString())>=0) {
                    this.showRivalContact = true;
                }else {
                    this.showRivalContact = false;
                }
                switch (this.transferInfo.cop_busin_class) {
                    case '1':
                        this.showRival = false;
                        this.showReportCode = val == 20020;
                        if(val == 20117 || val == 10071 || val == 30008){
                            this.showClearAgent = false;
                        }else if(val == 10070 || val == 20116 || val == 30110){
                            this.showClearAgent = false;
                        }else if(!this.showReportCode){
                            this.showClearAgent = true;
                        }else if(this.showReportCode){
                            this.showClearAgent = false;
                        }
                        break;
                    case '4,5,6':
                    case '4':
                    case '5':
                    case '6':
                        this.transferInfo.clear_agent_code_cn='';
                        this.showRival = val == 10078 || val == 11010 //解除质押
                        this.showReportCode = false;
                        this.showClearAgent = false;
                        break;
                    case '9':
                        this.showRival = val == 10023 || val == 20018 || val == 10074 || val == 10075 || val == 20130 || val == 20019;
                        this.showReportCode = val == 20017 || val == 20047 || val == 20049 || val == 20126 || val == 20128 || val == 20050 || val == 20051
                            || val == 20139 || val == 20140 || val == 10053 || val == 10054 || val == 10098 || val == 10099;
                        this.showClearAgent = false;
                        break;
                    case '7' :
                        this.showRival = false;
                        this.showReportCode = false;
                        this.showClearAgent = false;
                        break;
                    case '20':
                        this.showRival = true;
                        this.showReportCode = false;
                        this.showClearAgent = false;
                        break;
                    default:
                        this.showRival = false;
                        this.showReportCode = false;
                        this.showClearAgent = false;
                        break;

                }
            }
        },
        data(){
            return{

                marketList: [],
                canSpecialApprove: '',
                rivalContactFlags:'',
                activeName: ['2'],
                showRival:false,
                showAgency:false,
                showDialog: false,
                showClearAgent: false,
                showReportCode: false,
                businClassTree: [],
                specialApproveWorkerList: [],
                cityTree: [],
                transferInfo: {
                    fund_id: null,
                    sq_holder_account: '',
                    zz_holder_account: '',
                    transfer_purpose: '',
                    transfer_remark: '',
                    transfer_postscript: '',
                    clear_agent_code_cn: null,
                    cop_busin_class: null,
                    settle_balance: '',
                    rival_code:'',
                    issuer_code:'',
                    rival_name:'',
                    invest_type:'',
                    approve_officer:'',
                    currency_no:'',
                },
                inAcct: {
                    bank_account_no: "",
                    bic_code: null,
                },
                outAcct: {
                    bank_account_no: "",
                    bic_code: null,
                },
                selfAcct: {
                    bank_fax: '',
                    bank_mail: '',
                    bank_phone: '',
                    sh_fdb_account: '',
                    sz_fdb_account: '',
                },
                showRivalContact:false,
                rivalAcct: {
                    bank_fax: '',
                    bank_mail: '',
                    bank_phone: '',
                },
                actionType: "detail",
                detailForm: {
                    transfer_ins_id: null,
                    transfer_ins_modify: null,
                    includeHistory:true
                },
                accountStyle:{
                    borderColor: '#d3d3d3',
                    height: '230px'
                },
                trusteeStyle:{
                    height: '230px'
                },
                flowColumns: [{
                        width: 60,
                        type: 'index',
                        title: "序号",
                        align: "center"
                    },
                    {
                        width: 100,
                        key: "operator_name",
                        title: "操作员",
                        align: "left"
                    }, {
                        title: "审批节点",
                        key: "node_name",
                        width: 100,
                        align: "left",
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName;
                            switch (rowData.node_name) {
                                case "approve":
                                    typeName = "经办"
                                    break
                                case "settle":
                                    typeName = "交收"
                                    break
                                default:
                                    typeName = rowData.node_name
                                    break
                            }
                            return h("span", {}, typeName);
                        },
                    },
                    {
                        width: 100,
                        key: "transfer_flow_flag",
                        title: "操作说明",
                        align: "left",
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName
                            if (rowData.transfer_flow_flag==8&& rowData.node_name=='approve') {
                                typeName = '提交'
                            }else {
                                typeName = showText(rowData.transfer_flow_flag,
                                    this.transferFlagList);
                            }
                            return h("span", {}, typeName);
                        }
                    },
                    {
                        width: 150,
                        key: "exec_date",
                        title: "操作时间",
                        align: "left",
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName = parseDateString(rowData.exec_date, rowData.exec_time);
                            return h("span", {}, typeName);
                        }
                    },
                    {
                        key: "remark",
                        title: "备注",
                        align: "left"
                    }
                ],
                investTypeList: [],
                currencyList:[],
                fundList: [],
                rivalList: [],
                taTypeList: [{
                    code: '1',
                    text: '自建TA'
                },
                    {
                        code: '2',
                        text: '中登上海'
                    },
                    {
                        code: '3',
                        text: '中登深圳'
                    },
                ],
                operatorList: [],
                settleMentList: [
                    {
                    code: '1',
                    text: '见款付券'
                    }, {
                        code: '2',
                        text: '见券付款'
                    }, {
                        code: '3',
                        text: 'DVP'
                    },
                ],
                agentCodeList:[
                    {code:'3', text:'中债登'},
                    {code:'4', text:'上清所'}
                ],
                seatList: [{
                    seatId: '',
                    fee: '',
                }],
                fileList: [],
                bankList:[]
            }
        },
        mounted() {
            getDictList(90522).then(res => this.transferFlagList = res.data);
            getDictList(610400).then(res => this.marketList = res.data);
            getDictList(91064).then(res => this.investTypeList = res.data);
            getDictList(610406).then(res => {
                let tempList = res.data;
                tempList.forEach(item => {
                    if (item.code == "CNY"){
                        item.text = "元";
                    }
                })
                this.currencyList = tempList;
            });
            getBankList().then(res => this.bankList = res.data);
            getCityTree().then(res=> this.cityTree = res.data)
            getBankList("1").then(res => this.bankInfoList = res.data);
            getBusinClassTree(true).then(res => this.businClassTree = res.data);
            operatorList().then(res => this.operatorList = res.data);
            getAgencyList('').then(res => this.agencyList = res.data);

            getSpecialApproveWorkerList().then(res => {
                if (res.data.rows && res.data.rows.length > 0) {
                    this.specialApproveWorkerList = res.data.rows;
                    this.specialApproveWorkerList.unshift({
                        begin_date: "0",
                        end_date: "0",
                        operator_name: "全部",
                        operator_no: -1,
                        workgroup_id: 0
                    });
                } else {
                    this.specialApproveWorkerList = [];
                }
            });

        },
        activated(){
            getSysParamValue(10066).then(res => {
                if (res.data.success) {
                    this.rivalContactFlags = res.data.data;
                }
            });
            getSysParamValue(10038).then(res => {
                if (res.data.success) {
                    this.canSpecialApprove = res.data.data;
                }
            });
        },
        computed:{
            beginDate: {
                get: function () {
                    return parseDateString(this.transferInfo.fee_begin_date)
                },
                set: function (newValue) {
                    this.transferInfo.fee_begin_date = parseDateStr(newValue)
                }
            },
            endDate: {
                get: function () {
                    return parseDateString(this.transferInfo.fee_end_date)
                },
                set: function (newValue) {
                    this.transferInfo.fee_end_date = parseDateStr(newValue)
                }
            },
            upBalance() {
                return smalltoBIG(this.transferInfo.settle_balance);
            },
            businClassFlag: {
                get: function () {
                    let businClass = this.transferInfo.cop_busin_class
                    if (this.transferInfo.cop_busin_class == '4'
                        || this.transferInfo.cop_busin_class == '5'
                        || this.transferInfo.cop_busin_class == '6') {
                        businClass = '4,5,6'
                    }
                    return [businClass, this.transferInfo.cop_busin_flag?this.transferInfo.cop_busin_flag.toString():'']
                },
                set: function (newValue) {
                    this.transferInfo.cop_busin_class = newValue[0]
                    this.transferInfo.cop_busin_flag = newValue[1]

                }
            },
            payDate: {
                get: function () {
                    if(this.transferInfo.require_pay_date != null){
                        let arr = this.transferInfo.require_pay_date.toString().split(" ");
                        let date = arr[0].replace(new RegExp("-","gm"),"");
                        let time = arr[1];
                        if(typeof(time) == "undefined"){
                            time=this.transferInfo.require_pay_time;
                        }else{
                            time=time.replace(new RegExp(":","gm"),"");
                        }
                        return parseDateString(date,time);
                    }
                    return parseDateString(this.transferInfo.require_pay_date)
                },
                set: function (newValue) {
                    this.transferInfo.require_pay_date = parseDateStr(newValue,"yyyy-MM-dd hh:mm:ss")
                }
            },
            outProvinceCity: {
                get: function () {
                    // if (this.actionType!='add'){
                    //     return [this.transferInfo.self_province_code, this.transferInfo.self_city_no]
                    // }
                    return [this.outAcct.province_code, this.outAcct.city_no]
                },
                set: function (newValue) {
                    this.outAcct.province_code = newValue[0]
                    this.outAcct.city_no = newValue[1]
                }
            },
            provinceCity: {
                get: function () {
                    return [this.inAcct.province_code, this.inAcct.city_no]
                },
                set: function (newValue) {
                    this.inAcct.province_code = newValue[0]
                    this.inAcct.city_no = newValue[1]
                }
            }
        },
        methods:{
            resetDetail(){
                this.transferInfo ={}
                this.outAcct= {}
                this.inAcct ={}
                this.fileList =[]
                this.seatList = [];
            },
            showTransferDetail(value){
                this.resetDetail()
                let transferId = value.transfer_ins_id
                let insMod = value.transfer_ins_modify
                this.showDialog = true
                this.loadFiles(value.bill_name)
                this.detailForm.transfer_ins_id = transferId
                this.detailForm.transfer_ins_modify = insMod
                this.$refs.flowData.dataChange();

                getTransferDetail({
                    transferInsId:transferId,
                    transferInsModify:insMod
                }).then(res => {
                    if (res.data.success) {
                        this.transferInfo = res.data.data.transferInfo
                        this.transferInfo.traderival_name = value.traderival_name
                        this.transferInfo.fund_name = value.fund_name

                        if (res.data.data.outAcct) {
                            this.outAcct = res.data.data.outAcct
                            if(value.cash_direction =='1'){
                                this.rivalAcct.bank_fax = res.data.data.outAcct.bank_fax
                                this.rivalAcct.bank_mail = res.data.data.outAcct.bank_mail
                                this.rivalAcct.bank_phone = res.data.data.outAcct.bank_phone
                            }else {
                                this.selfAcct.bank_fax = res.data.data.outAcct.bank_fax
                                this.selfAcct.bank_mail = res.data.data.outAcct.bank_mail
                                this.selfAcct.bank_phone = res.data.data.outAcct.bank_phone
                            }
                        }
                        if (res.data.data.inAcct) {
                            this.inAcct = res.data.data.inAcct
                            if(value.cash_direction =='1'){
                                this.selfAcct.bank_fax = res.data.data.inAcct.bank_fax
                                this.selfAcct.bank_mail = res.data.data.inAcct.bank_mail
                                this.selfAcct.bank_phone = res.data.data.inAcct.bank_phone
                            }else{
                                this.rivalAcct.bank_fax = res.data.data.inAcct.bank_fax
                                this.rivalAcct.bank_mail = res.data.data.inAcct.bank_mail
                                this.rivalAcct.bank_phone = res.data.data.inAcct.bank_phone
                            }
                        }
                        if (this.transferInfo.cop_busin_class === '22' && this.transferInfo.cop_busin_flag == 20061 &&
                            this.transferInfo.seat_id_list && this.transferInfo.fee_list) {
                            let seatIdList = this.transferInfo.seat_id_list.split(',');
                            let feeList = this.transferInfo.fee_list.split(',');
                            seatIdList.forEach((item, index) => {
                                this.seatList.push({
                                    seatId: item,
                                    fee: feeList[index]
                                });
                            });
                        }
                    }
                })

                if(value.agency_code && value.cop_busin_class == '7'){
                    this.showAgency = true;
                }else{
                    this.showAgency = false;
                }


            },
            loadFiles(bill_name) {
                if (bill_name) {
                    getFileList(bill_name,'2').then(res => {
                        if (res.data.success) {
                            res.data.data.forEach(file => this.fileList.push({
                                name: file.file_name,
                                response: {
                                    data: file.file_id
                                }
                            }));
                        }
                        // this.transferInfo.bill_name = this.fileList.reduce((x, y) => x.response.data + ',' + y.response.data);
                    })
                }
            },
            downloadFile(file) {
                window.open(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=2&fileId=' + file.response.data)
            },
        }
    }
</script>
<style lang="scss" src="@/style/common.scss"></style>
<style lang="scss">
    .drag-in-box {
        .el-upload {
            display: block;
            height: 100%;

            .el-upload-dragger {
                width: 100%;
                height: 100%;
            }
        }
    }
    .h-typefield input {
        height: 28px;
    }

</style>


