<template>
  <div class="accept-container full-h">
    <iframe id="show-iframe" class="full-h" frameborder=0 name="showHere" scrolling=auto :src="url"></iframe>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
export default {
  name: 'hello',
  data () {
    return {
        url: ''
    }
  },
  created () {
    let param = ''
    param =  this.$route.meta.param.indexOf("?") > -1?'&oplusAaa=':'?oplusAaa='
      if (this.$route.meta.param.indexOf("/workflow/")>-1) {
          this.url = window.LOCAL_CONFIG.WORKFLOW_HOME + this.$route.meta.param +  param + Cookies.get('Admin-Token')
      }else {
          this.url = window.LOCAL_CONFIG.API_HOME + this.$route.meta.param +  param + Cookies.get('Admin-Token')
      }

  },
    watch:{      //监听路由变化

        $route( to , from ){
            let param = ''
            if (to.meta.param) {
                param =  to.meta.param.indexOf("?") > -1?'&oplusAaa=':'?oplusAaa='
                if (this.$route.meta.param.indexOf("/workflow/")>-1) {
                    this.url = window.LOCAL_CONFIG.WORKFLOW_HOME + this.$route.meta.param +  param + Cookies.get('Admin-Token')
                }else {
                    this.url = window.LOCAL_CONFIG.API_HOME + this.$route.meta.param +  param + Cookies.get('Admin-Token')
                }
            }


        }

    },
  // computed:{
  //     url(){
  //         let param = ''
  //         param =  this.$route.meta.param.indexOf("?") > -1?'&oplusAaa=':'?oplusAaa='
  //         return  window.LOCAL_CONFIG.API_HOME + this.$route.meta.param +  param + Cookies.get('Admin-Token')
  //     }
  // },
  mounted(){
    let oIframe = document.getElementById('show-iframe');
    let deviceWidth = document.getElementsByClassName('app-main')[0].clientWidth
    // const deviceHeight = document.getElementsByClassName('app-main')[0].clientHeight;
    // const deviceWidth = document.documentElement.clientWidth;
    // 15为app-main padding值
    let deviceHeight = document.documentElement.clientHeight -  document.getElementsByClassName('h-topbar-wrapper')[0].clientHeight - document.getElementsByClassName('h-navbar-wrapper')[0].clientHeight - 45
    this.$nextTick(function () {
      if(oIframe) {
          oIframe.style.width = deviceWidth + 'px';
          oIframe.style.height = deviceHeight + 'px';
        }
    })
    let that = this
    window.addEventListener('resize', function () {
        let oIframe = document.getElementById('show-iframe');
        let deviceWidth = document.getElementsByClassName('app-main')[0].clientWidth
        // const deviceHeight = document.getElementsByClassName('app-main')[0].clientHeight;
        // const deviceWidth = document.documentElement.clientWidth;
        // 15为app-main padding值
        let deviceHeight = document.documentElement.clientHeight -  document.getElementsByClassName('h-topbar-wrapper')[0].clientHeight - document.getElementsByClassName('h-navbar-wrapper')[0].clientHeight - 45
        that.$nextTick(function () {
          if(oIframe) {
            oIframe.style.width = deviceWidth + 'px';
            oIframe.style.height = deviceHeight + 'px';
          }
        })
    })
  }
}

</script>


<style lang="less" rel="stylesheet/less">
  #show-iframe {
    width: 100%;
    height: 100%;
    background-color: white;
  }
</style>


