<template>
    <div class="full-h" >
        <h-split v-model="split" min="130" max="600" @on-moving="onSplitMove">
            <div slot="left" span="4" class="full-h">
                <h-other-fund-tree :onFundClick="fundClick"></h-other-fund-tree>
            </div>
            <div slot="right" span="20" class="full-h" style="padding-left: 10px" id="full-h-wrapper">

                <!--<div>-->
                <!--<h-radio-group type="button" :btnIcon="true" :btnIconOnlyChecked="true" v-model="queryForm.manage_type" >-->
                <!--<h-radio v-for="item in manageList" :key="item.code" :label="item.code">{{item.text}}</h-radio>-->
                <!--</h-radio-group>-->
                <!--</div>-->
                <h-row>
                    <h-col>
                        <h-button-group>
                            <h-button v-if="queryForm.manage_type == '0'" icon="arrow-swap" @click="match">匹配</h-button>
                            <h-button v-if="queryForm.manage_type == '0'" icon="t-b-addto" @click="mainPush">主推</h-button>
                        </h-button-group>
                        <h-input v-if="queryForm.manage_type == '0'" v-model="queryFlowForm.keyWord" :maxlength="32" placeholder="请输入关键字"
                                 style="float:right;width: 200px">
                            <div slot="append">
                                <a href="javascript:void(0)">
                                    <h-icon name="search"
                                            @click.native.stop="refreshFlow"></h-icon>
                                </a>
                            </div>
                        </h-input>
                    </h-col>
                </h-row>

                <h-row >
                    <h-col span="7">
                        <h-datagrid
                                stripe hasPage highlight-row
                                :height="autoHeight" :auto-load="false"
                                :columns="liquidateInsColumns" :bindForm="queryForm"
                                :onRowClick="insRowClick" :onSelectChange="insSelectChange"
                                :pageSize="100" :pageSizeOpts="pageSizeOpts"
                                url="/liquidate/manage/record/page.json" ref="insDatagrid">
                        </h-datagrid>
                    </h-col>

                    <h-col span="17">
                        <h-datagrid hasPage stripe highlight-row :height="autoHeight" :row-class-name="rowClassName"
                                    :auto-load="false" :columns="liquidateFlowColumns" :bindForm="queryFlowForm"
                                    url="/liquidate/flow/page.json" ref="insFlowDatagrid">
                        </h-datagrid>
                    </h-col>
                </h-row>

                <el-tabs tab-position="top" style="margin-top: 5px" v-model="bottomTab">
                    <el-tab-pane label="已勾销列表" name ="1">
                        <h-button-group>
                            <h-button icon="t-b-modify" @click="cancelLiquidate">取消勾销</h-button>
                        </h-button-group>

                    </el-tab-pane>

                    <el-tab-pane label="已过滤列表" name ="2">
                        <h-button-group>
                            <h-button icon="t-b-modify" @click="cancelFilter">取消过滤</h-button>
                        </h-button-group>
                    </el-tab-pane>
                </el-tabs>
                <h-datagrid
                        has-page highlight-row stripe height="150"
                        :columns="bottomColumns" :bindForm="queryFilterForm"
                        :pageSize="100" :pageSizeOpts="pageSizeOpts"
                        :url="bottomUrl" :auto-load="false" style="overflow: hidden"
                        ref="hasLiquidateDatagrid">
                </h-datagrid>
            </div>
        </h-split>
        <h-msg-box class="customMsgBox" v-model="showPushDialog" width="300" :mask-closable="false">
            <p slot="header">
                <span>请选择业务标志</span>
            </p>
            <h-form :model="pushLiquidateInfo" :label-width="80" ref="pushLiquidateForm">

                <h-form-item required label="业务分类" prop="cop_busin_class">
                    <h-select v-model="pushLiquidateInfo.cop_busin_class" @on-change="businClassChange" >
                        <h-option v-for="item in copBusinClassList" :key="item.code" :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>

                <h-form-item required label="业务标志" prop="cop_busin_flag">
                    <h-select v-model="pushLiquidateInfo.cop_busin_flag" >
                        <h-option v-for="item in businFlagList" :key="item.code" :label="item.text" :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="cancelPushLiquidate">取消</h-button>
                <h-button type="primary" @click="savePushLiquidate">确认</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import HOtherFundTree from "../../../components/HOtherFundTree";
    import HDatagrid from "../../../components/HDatagrid";
    import {
        showText,
        getDictList,
        getBusinFlagList,
        formatMoney
    } from "@/api/utils"
    import {
        matchInsAndFlow,
        pushFlow,
        cancelLiquidate,
        cancelFilter,
        getRuleDetail
    } from "@/api/liquidate/manage"
    export default {
        name: "manage",
        components: {HDatagrid, HOtherFundTree},
        data(){
            return{
                split: 0.15,
                autoHeight:0,
                dealTypeList:[],
                copBusinClassList:[],
                copBusinFlagList:[],
                showPushDialog:false,
                pageSizeOpts:[25,50,75,100],
                bottomTab:'',
                bottomColumns:[],
                bottomUrl:'',
                liquidateInsColumns:[
                    {
                        title: "勾销指令序号",
                        key: "liquidate_ins_id",
                        align: "left",
                        hiddenCol:true,
                        ellipsis: true
                    },
                    {
                        title: "业务分类",
                        key: "cop_busin_class",
                        align: "center",
                        hiddenCol:true,
                        ellipsis: true
                    },
                    {
                        title: "业务标志",
                        key: "cop_busin_flag",
                        align: "center",
                        width:100,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row
                            let businFlag = showText(rowData.cop_busin_flag,this.copBusinFlagList)
                            return h("span",{},businFlag);
                        }
                    },
                    {
                        title: "金额",
                        key: "settle_balance",
                        align: "right",
                        width:110,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let settleBalance = formatMoney(rowData.settle_balance)
                            return h("span",{style:{fontWeight:'bold'}},settleBalance)
                        }
                    },
                    {
                        title: "资金方向",
                        key: "cash_direction",
                        align: "left",
                        width:70,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let cashDirection = showText(rowData.cash_direction,this.cashDirectionList)
                            return h("span",{},cashDirection)
                        }
                    },
                    {
                        title: "待交割序号",
                        key: "out_transfer_id",
                        align: "left",
                        ellipsis: true
                    },
                    {
                        title: "符合匹配规则的银行流水集合",
                        key: "serial_no_list",
                        align: "left",
                        hiddenCol:true,
                        ellipsis: true
                    }
                ],
                statusList:[
                    {
                        code:'0',
                        text:'未处理'
                    },
                    {
                        code:'1',
                        text:'待匹配'
                    },
                    {
                        code:'2',
                        text:'已匹配'
                    },
                    {
                        code:'3',
                        text:'待主推'
                    },
                    {
                        code:'4',
                        text:'已主推'
                    },
                    {
                        code:'5',
                        text:'已过滤'
                    },
                ],
                liquidateFlowColumns:[
                    {
                        title: "流水号",
                        key: "serial_no",
                        align: "left",
                        hiddenCol:true,
                        ellipsis: true
                    },
                    {
                        title: "勾销状态",
                        key: "liquidate_ins_status",
                        align: "left",
                        width:80,
                        sortable: true,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let dealType = showText(rowData.liquidate_ins_status,this.statusList);
                            return h("span",{},dealType)
                        }
                    },
                    {
                        title: "对方账户名",
                        key: "rival_account_name",
                        align: "left",
                        width:120,
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.rival_account_name
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                    {
                        title: "对方账号",
                        key: "rival_account_no",
                        width:100,
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.rival_account_no
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                    {
                        title: "交易金额",
                        key: "trade_balance",
                        width:120,
                        align: "right",
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let tradeBalance = formatMoney(rowData.trade_balance)
                            return h("span",{style:{fontWeight:'bold'}},tradeBalance)
                        }
                    },
                    {
                        title: "资金方向",
                        key: "cash_direction",
                        align: "left",
                        width:60,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let cashDirection = showText(rowData.cash_direction,this.cashDirectionList)
                            return h("span",{},cashDirection)
                        }
                    },
                    {
                        title: "余额",
                        key: "bank_remain_balance",
                        align: "right",
                        width:100,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let remainBalance = formatMoney(rowData.bank_remain_balance)
                            return h("span",{style:{fontWeight:'bold'}},remainBalance)
                        }
                    },
                    {
                        title: "划款摘要",
                        key: "transfer_remark",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.transfer_remark
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                    {
                        title: "划款用途",
                        key: "transfer_purpose",
                        align: "left",
                        width:140,
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.transfer_purpose
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                    {
                        title: "划款附言",
                        key: "transfer_postscript",
                        align: "left",
                        width:80,
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.transfer_postscript
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                ],
                hasLiquidateColumns:[
                    {
                        title: "产品代码",
                        key: "fund_code",
                        align: "left",
                        ellipsis: true
                    },
                    {
                        title: "产品名称",
                        key: "fund_name",
                        align: "left",
                        ellipsis: true
                    },
                    {
                        title: "业务分类",
                        key: "cop_busin_class",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let businClass = showText(rowData.cop_busin_class,this.copBusinClassList)
                            return h("span",{},businClass)
                        }
                    },
                    {
                        title: "业务标志",
                        key: "cop_busin_flag",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row
                            let businFlag = showText(rowData.cop_busin_flag,this.copBusinFlagList);
                            return h("span",{},businFlag)
                        }
                    },
                    {
                        title: "交易金额",
                        key: "trade_balance",
                        align: "right",
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let tradeBalance = formatMoney(rowData.trade_balance)
                            return h("span",{style:{fontWeight:'bold'}},tradeBalance)
                        }
                    },
                    {
                        title: "指令金额",
                        key: "settle_balance",
                        align: "right",
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let settle_balance = formatMoney(rowData.settle_balance)
                            return h("span",{style:{fontWeight:'bold'}},settle_balance)
                        }
                    },
                    {
                        title: "资金方向",
                        key: "cash_direction",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let cashDirection = showText(rowData.cash_direction,this.cashDirectionList)
                            return h("span",{},cashDirection)
                        }
                    },
                    {
                        title: "划款摘要",
                        key: "transfer_remark",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.transfer_remark
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                    {
                        title: "待交割序号",
                        key: "out_transfer_id",
                        align: "left",
                        width:80,
                        ellipsis: true
                    },
                    {
                        title: "对手方账号",
                        key: "rival_account_no",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.rival_account_no
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                    {
                        title: "对手方账户",
                        key: "rival_account_name",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.rival_account_name
                            return h("span", {attrs:{title:detail}}, detail);
                        }

                    },
                    {
                        title: "划款用途",
                        key: "transfer_purpose",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.transfer_purpose
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                    {
                        title: "划款附言",
                        key: "transfer_postscript",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.transfer_postscript
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },

                ],
                queryFilterForm:{
                    fund_id: ''
                },
                queryForm:{
                    manage_type:'',
                    fund_code:'',
                    fund_id:''
                },
                businFlagList:[],
                queryFlowForm:{
                    fund_id:'',
                    keyWord:'',
                    serial_no_list:''
                },
                pushLiquidateInfo:{
                    serial_no:'',
                    cop_busin_class:'',
                    cop_busin_flag:''
                },
                manageList:[
                    {
                        code:'0',
                        text:'勾销'
                    },
                    {
                        code:'1',
                        text:'过滤'
                    }
                ],
                cashDirectionList:[
                    {
                        code:'1',
                        text:'入款'
                    },
                    {
                        code:'2',
                        text:'出款'
                    }
                ]
            }
        },
        watch:{
            bottomTab(val){
                if (val == '2'){
                    this.bottomUrl = '/liquidate/flow/filter/page.json';
                    this.bottomColumns =  this.liquidateFlowColumns.filter(x=>x.key!='liquidate_ins_status');
                } else {
                    this.bottomUrl = '/liquidate/instruction/finish/page.json';
                    this.bottomColumns = this.hasLiquidateColumns;
                }
                this.$nextTick(() => {
                    this.$refs.hasLiquidateDatagrid.dataChange(1);
                })
            },
            'queryForm.manage_type'(val){
                if(val == '0'){
                    this.queryFlowForm.fund_id = this.queryForm.fund_id;
                    this.$nextTick(() =>{
                        this.$refs.insFlowDatagrid.dataChange(1);
                        this.$refs.insDatagrid.dataChange(1);
                    })
                }else{
                    this.$nextTick(() =>{
                        this.$refs.fliterDatagrid.dataChange(1);
                    })
                }
            },
            showPushDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        mounted(){
            getDictList(90523).then(res => this.copBusinClassList = res.data)
            getDictList('businFlag').then(res => this.copBusinFlagList = res.data)
            getDictList(91192).then(res => this.dealTypeList = res.data)
            this.bottomTab='1'
            this.re_layout();
        },
        methods:{

            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 310;
            },
            fundClick(data){
                if(data){
                    if(!this.queryForm.manage_type){
                        this.queryForm.manage_type = '0'
                    }
                    this.queryForm.fund_code = data.text.split(" ")[0];
                    this.queryFlowForm.fund_id = data.code;
                    this.queryForm.fund_id = data.code;
                    this.queryFilterForm.fund_id = data.code;
                    this.queryFlowForm.serial_no_list = '';
                    this.refresh();
                }
            },
            match(){
                let insSelects = this.$refs.insDatagrid.$refs.gridContent.getSelection();
                let flowSelects = this.$refs.insFlowDatagrid.$refs.gridContent.getSelection();
                if(insSelects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条勾销记录",'错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                if(flowSelects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条勾销流水记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                matchInsAndFlow(insSelects[0].liquidate_ins_id,flowSelects[0].serial_no).then( res =>{
                    if(res.data && res.data.code == '0'){
                        this.openMask();
                        this.$alert(res.data.msg,"成功",{
                            confirmButtonText:'确定',
                            type:'success',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() =>{
                            this.closeMask();
                        })
                        this.refresh();
                    }else{
                        this.openMask();
                        this.$alert(res.data.msg,"错误",{
                            confirmButtonText:'确定',
                            type:'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() =>{
                            this.closeMask();
                        })
                        this.refresh();
                    }
                })
            },
            mainPush(){

                let flowSelects = this.$refs.insFlowDatagrid.$refs.gridContent.getSelection();
                if(flowSelects.length == 0){
                    this.openMask();
                    this.$alert('请选择一条勾销流水记录','错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                if (flowSelects[0].rule_id) {
                    getRuleDetail(flowSelects[0].rule_id).then( res =>{
                        if(res.data && res.data.success){
                            this.pushLiquidateInfo.cop_busin_class = res.data.data.cop_busin_class
                            this.pushLiquidateInfo.cop_busin_flag = res.data.data.cop_busin_flag
                        }
                    })
                }
                this.pushLiquidateInfo.serial_no = flowSelects[0].serial_no;
                this.$refs.pushLiquidateForm.resetFields();
                this.showPushDialog = true;
            },
            cancelFilter(){
                let insSelects = this.$refs.hasLiquidateDatagrid.$refs.gridContent.getSelection();
                if(insSelects.length == 0){
                    this.openMask()
                    this.$alert("请选择一条勾销记录",'错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                cancelFilter(insSelects[0].serial_no).then( res =>{
                    if(res.data && res.data.code == '0'){
                        this.openMask()
                        this.$alert(res.data.msg,"成功",{
                            confirmButtonText:'确定',
                            type:'success',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() =>{
                            this.closeMask();
                        })
                        this.refresh();
                    }else{
                        this.openMask()
                        this.$alert(res.data.msg,"错误",{
                            confirmButtonText:'确定',
                            type:'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() =>{
                            this.closeMask();
                        })
                        this.refresh();
                    }
                })
            },
            businClassChange: function(currentValue){
                if(currentValue){
                    getBusinFlagList(currentValue).then(res => this.businFlagList = res.data);
                }
            },
            insRowClick(){
                let selects = this.$refs.insDatagrid.$refs.gridContent.getSelection();
                if(selects.length === 0){
                    this.queryFlowForm.serial_no_list = ''
                }else{
                    this.queryFlowForm.serial_no_list = selects[0].serial_no_list;
                }
                this.queryFlowForm.fund_id = this.queryForm.fund_id;
                this.$refs.insFlowDatagrid.dataChange(1);
            },
            insSelectChange(){
                let selects = this.$refs.insDatagrid.$refs.gridContent.getSelection();
                if(selects.length === 0){
                    this.queryFlowForm.serial_no_list = ''
                }else{
                    this.queryFlowForm.serial_no_list = selects[0].serial_no_list;
                }
                this.queryFlowForm.fund_id = this.queryForm.fund_id;
                this.$refs.insFlowDatagrid.dataChange(1);
            },
            cancelLiquidate(){
                let selects = this.$refs.hasLiquidateDatagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask()
                    this.$alert("请选择一条已勾销记录",'错误',{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                cancelLiquidate({id:selects[0].id,status:selects[0].liquidate_ins_status}).then( res =>{
                    if(res.data && res.data.code == '0'){
                        this.openMask();
                        this.$alert(res.data.msg,"成功",{
                            confirmButtonText:'确定',
                            type:'success',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() =>{
                            this.closeMask();
                        })
                        this.refresh();
                    }else{
                        this.openMask()
                        this.$alert(res.data.msg,"错误",{
                            confirmButtonText:'确定',
                            type:'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() =>{
                            this.closeMask();
                        })
                        this.refresh();
                    }
                })
            },
            cancelPushLiquidate(){
                this.showPushDialog = false;
            },
            savePushLiquidate(){
                this.showPushDialog = false
                pushFlow(this.pushLiquidateInfo).then(res =>{
                    if(res.data && res.data.code == '0'){
                        this.$alert(res.data.msg,"成功",{
                            confirmButtonText:'确定',
                            type:'success',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() =>{
                            this.closeMask();
                        })
                        this.refresh();
                    }else{
                        this.$alert(res.data.msg,"错误",{
                            confirmButtonText:'确定',
                            type:'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() =>{
                            this.closeMask();
                        })
                        this.refresh();
                    }
                })
            },
            refreshFlow(){
                this.$refs.insFlowDatagrid.dataChange(1);
            },
            refresh(){
                if(this.queryForm.manage_type == '0'){
                    this.$refs.insDatagrid.dataChange(1);
                    this.$refs.insFlowDatagrid.dataChange(1);
                }else{
                    this.$refs.fliterDatagrid.dataChange(1);
                }
                this.$refs.hasLiquidateDatagrid.dataChange(1);
            },
            rowClassName(row){
                if(this.queryFlowForm.serial_no_list){
                    if(this.queryFlowForm.serial_no_list.indexOf(row.serial_no.toString()) != -1){
                        return 'flow-table-info-row';
                    }
                }
            },
            onSplitMove() {
                this.$refs.insDatagrid.handleResize();
                this.$refs.insFlowDatagrid.handleResize();
                this.$refs.hasLiquidateDatagrid.handleResize();


            }
        }
    }
</script>

<style >

    .h-table-wrapper .h-table .h-table-tbody tr.flow-table-info-row td{
        background: #68F904 ;
    }
</style>


