import fetch from "@/api/httpFetch";

export function saveAgencyInfo(agencyInfo) {
    return fetch.post('/systemmgnt/agencyInfo/saveAgencyInfo.json',agencyInfo, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function deleteAgency(serialNoes) {
    return fetch.post('/systemmgnt/agencyInfo/agencyInfoDel.json',{serialNoes:serialNoes})
}


