<template>
    <div class="full-h">
        <h-split v-model="split" min="130" max="600" @on-moving="onSplitMove">
            <div slot="left" class="full-h" span="4">
                <h-other-fund-tree :showAll="true" :onFundClick="fundClick"></h-other-fund-tree>
            </div>

            <div slot="right" class="full-h" style="padding-left: 10px" span="20" id="full-h-wrapper-on-fundcashreport" ref="right">
                <div>
                    <h-row id="inner-on-fundcashreport">
                        <h-col>
                            <el-form style="font-size: 12px;":inline="true" size="mini" :model="dataQuery" class="demo-form-inline">
                                <el-form-item>
                                    <el-date-picker v-model="queryDate" type="date" placeholder="选择日期"></el-date-picker>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="cashQuery">查询</el-button>
                                    <el-button type="ghost" @click="cashModify">修改</el-button>
                                    <el-button type="ghost" @click="cashMsgOpen">余额查询</el-button>
                                    <el-button type="ghost" @click="flowMsgOpen">流水查询</el-button>
                                    <el-button type="ghost" @click="cashExport">导出</el-button>
                                </el-form-item>
                            </el-form>
                        </h-col>
                    </h-row>

                    <h-datagrid
                            hasPage
                            highlight-row
                            stripe
                            :height="autoHeight"
                            :pageSize="100" :pageSizeOpts="pageSizeOpts"
                            :bindForm="dataQuery"
                            :columns="columns"
                            :onRowClick="rowClick"
                            style="margin-top: 10px;"
                            url="/cashMgnt/fundCash/fundCashReport.json"
                            ref="datagrid">
                    </h-datagrid>

                    <el-tabs tab-position="top" style="margin-top: 5px">
                        <el-tab-pane label="托管户/自营账户流水">
                            <h-datagrid style="overflow: hidden"
                                    hasPage
                                    highlight-row
                                    stripe
                                    height="150"
                                    :pageSize="100"
                                    :bindForm="tradeFlowQuery"
                                    :columns="tradeFlowColumns"
                                    url="/cashMgnt/fundCash/tradeFlowQuery.json"
                                    ref="tradeFlowGrid">
                            </h-datagrid>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </div>
        </h-split>
        <h-msg-box class="customMsgBox"  v-model="accountCashModel" width="320" title="资金调整"  inline>
            <div >
                <h-form  :label-width="85">
                    <h-form-item label="产品名称" >
                        <h-input v-model="fundCashInfo.fund_name" readonly type="text"></h-input>
                    </h-form-item >
                    <h-form-item :label="item.bank_account_name" v-for="item in fundCashInfo.fund_cash_list" :key="item.bank_account_id" >
                        <h-typefield  v-model="item.account_cur_balance" integerNum="15" type="money" divided
                                      placeholder="请输入" :readonly="adjustable ? false : 'readonly'">
                        </h-typefield>
                    </h-form-item >
                    <h-form-item label="上清DVP余额" >
                        <h-typefield  v-model="fundCashInfo.sq_dvp_balance" integerNum="15" type="money" divided
                                      placeholder="请输入" :readonly="sqadjustable ? false : 'readonly'">
                        </h-typefield>
                    </h-form-item >
                    <h-form-item label="中债DVP金额" >
                        <h-typefield  v-model="fundCashInfo.zz_dvp_balance" integerNum="15" type="money" divided
                                      placeholder="请输入" :readonly="zzadjustable ? false : 'readonly'">
                        </h-typefield>
                    </h-form-item >
                </h-form>
            </div>

            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="ModifyCancle">取消</h-button>
                <h-button type="primary" @click="ModifyOK">确定</h-button>
            </div>
        </h-msg-box>

        <accCashFlowQueryMsgBox :msgBoxShow="cashFlowMsgBoxShow"
                                :queryType="cashFlowQueryType"
                                :includeSelfRun=true
                                @onCashFlowClose="onCashFlowClose"
                                @onOutRefresh="onCashFlowRefresh"
                                ref="accCashFlowQueryMsg">
        </accCashFlowQueryMsgBox>
    </div>
</template>

<script>
    import HOtherFundTree from "@/components/HOtherFundTree";
    import HDatagrid from '@/components/HDatagrid'
    import accCashFlowQueryMsgBox from '@/components/accCashFlowQueryMsgBox'
    import AccBalanceTooltipTable from '@/components/AccBalanceTooltipTable'
    import {
        parseDateString,
        parseDateStr,
        showText,
        formatMoney,
        dateFormate
    } from '@/api/utils'
    import {
        updateFundCash
    } from "@/api/cashmgnt/fundcash";
    import {deepCopy} from '@/utils/func'
    import Cookies from 'js-cookie'

    /* mixin dependency */
    import render from "@/mixins/render";

    import Vue from 'vue'
    Vue.component('AccBalanceTooltipTable', Vue.extend(AccBalanceTooltipTable))

    export default {
        mixins: [render],
        data(){
            let columns = [
                {
                    title: "产品代码",
                    key: "fund_code",
                    width: 100,
                    ellipsis:true,
                    align: 'center'
                },{
                    title: "产品名称",
                    key: "fund_name",
                    //width: 140,
                    ellipsis:true,
                    align: 'center'
                },{
                    title: "托管户/自营账户余额",
                    key: "account_cur_balance",
                    width: 145,
                    ellipsis:true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = formatMoney(rowData.account_cur_balance);
                        let accNameBalanceData = [];
                        if (rowData.accname_balance_sum) {
                            let accNameBalanceSumArray = rowData.accname_balance_sum.split(',');
                            if (accNameBalanceSumArray && accNameBalanceSumArray.length > 0) {
                                for (let accNameBalance of accNameBalanceSumArray) {
                                    let data = accNameBalance.split('|');
                                    if (data && data.length === 3) {
                                        accNameBalanceData.push({accName: data[1], balance: data[2]});
                                    }
                                }
                            }
                        }
                        let accNameBalanceTable = h('AccBalanceTooltipTable', {
                            props: {
                                datas: accNameBalanceData
                            }
                        });
                        return h('div', [
                            h('Tooltip', {
                                class: {
                                    'accbalance-tooltip-table': true
                                },
                                props: {
                                    always: false,
                                    delay: 400,
                                    trigger: 'hover',
                                    placement: 'bottom-start'
                                }
                            }, [
                                h('span', {style: {fontWeight: 'bold'}}, name),
                                h('span', {slot: 'content'}, [accNameBalanceTable])
                            ])
                        ]);
                    }
                },{
                    title: "更新时间",
                    key: "input_time",
                    width: 70,
                    ellipsis:true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = parseDateString(0,rowData.input_time);
                        return h("span", {}, name);
                    }
                },{
                    title: "上清DVP余额",
                    key: "sq_dvp_balance",
                    //width: 140,
                    ellipsis:true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = formatMoney(rowData.sq_dvp_balance);
                        return h("span", {style:{fontWeight:'bold'}}, name);
                    }
                },{
                    title: "更新时间",
                    key: "sq_input_time",
                    width: 70,
                    ellipsis:true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = parseDateString(0,rowData.sq_input_time);
                        return h("span", {}, name);
                    }
                },{
                    title: "中债DVP余额",
                    key: "zz_dvp_balance",
                    //width: 140,
                    ellipsis:true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = formatMoney(rowData.zz_dvp_balance);
                        return h("span", {style:{fontWeight:'bold'}}, name);
                    }
                },{
                    title: "更新时间",
                    key: "zz_input_time",
                    width: 70,
                    ellipsis:true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = parseDateString(0,rowData.zz_input_time);
                        return h("span", {}, name);
                    }
                }
            ]

            let tradeFlowColumns = [
                {
                    title: "交易日期",
                    key: "curr_date",
                    width: 100,
                    ellipsis:true,
                    align: 'center'
                },{
                    title: "银行账户",
                    key: "bank_account_no",
                    width: 140,
                    ellipsis:true,
                    align: 'center'
                },{
                    title: "账户名称",
                    key: "bank_account_name",
                    width: 140,
                    ellipsis:true,
                    align: 'center'
                },{
                    title: "资金方向",
                    key: "cash_direction",
                    width: 100,
                    ellipsis:true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = showText(rowData.cash_direction, this.cashDirectionList);
                        return h("span", {}, name);
                    }
                },{
                    title: "对方银行账户",
                    key: "rival_account_no",
                    width: 140,
                    ellipsis:true,
                    align: 'center'
                },{
                    title: "对方账户名称",
                    key: "rival_account_name",
                    width: 100,
                    ellipsis:true,
                    align: 'center'
                },{
                    title: "交易金额",
                    key: "trade_balance",
                    width: 140,
                    ellipsis:true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = formatMoney(rowData.trade_balance);
                        return h("span", {style:{fontWeight:'bold'}}, name);
                    }
                },{
                    title: "账户余额",
                    key: "bank_remain_balance",
                    width: 100,
                    ellipsis:true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = formatMoney(rowData.bank_remain_balance);
                        return h("span", {style:{fontWeight:'bold'}}, name);
                    }
                },{
                    title: "摘要",
                    key: "transfer_remark",
                    width: 100,
                    ellipsis:true,
                    align: 'center'
                },{
                    title: "用途",
                    key: "transfer_purpose",
                    width: 100,
                    ellipsis:true,
                    align: 'center'
                },{
                    title: "附言",
                    key: "transfer_postscript",
                    width: 100,
                    ellipsis:true,
                    align: 'center'
                },{
                    title: "备注",
                    key: "remark",
                    ellipsis:true,
                    align: 'center'

                }
            ]

            return{
                split:0.15,
                accountCashModel:false,
                accountCashList:[],
                dataQuery:{
                    fundId:'-1',
                    queryDate: Cookies.get('CurrSysDate')
                },
                tradeFlowQuery:{
                    fundId:0,
                    accountId:0,
                    queryDate:Cookies.get('CurrSysDate')
                },
                pageSizeOpts: [25, 50, 75, 100],
                columns: columns,
                tradeFlowColumns:tradeFlowColumns,
                fundCashInfo: {
                    fund_id: '',
                    fund_code:'',
                    fund_name: '',
                    curr_date:0,
                    sq_dvp_balance:0.0,
                    sq_input_time:0,
                    sq_account_id:0,
                    zz_dvp_balance:0.0,
                    zz_input_time:0,
                    zz_account_id:0,
                    account_cur_balance:0.0,
                    input_time:0,
                    bank_account_id:0,
                    transfer_type:null,
                    fund_cash_list: []
                },
                adjustable:false,
                sqadjustable:false,
                zzadjustable:false,
                cashDirectionList: [
                    { code:'0', text:'不变化'},
                    { code:'1',text:'增加'},
                    { code:'2', text:'减少'}
                ],
                prodGridWidth: 0,
                cashFlowQueryType: '',
                cashFlowMsgBoxShow: false,
            }
        },
        watch:{
            accountCashModel:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
            queryDate:{
                get:function () {
                    if(this.dataQuery.queryDate)
                        return parseDateString(this.dataQuery.queryDate)
                    else
                        return parseDateString(Cookies.get('CurrSysDate'))

                    if(this.tradeFlowQuery.queryDate)
                        return parseDateString(this.tradeFlowQuery.queryDate)
                    else
                        return parseDateString(Cookies.get('CurrSysDate'))
                },
                set:function (newValue) {
                    this.dataQuery.queryDate = parseDateStr(newValue);
                    this.tradeFlowQuery.queryDate = parseDateStr(newValue);
                }
            }
        },
        created(){

        },
        mounted() {
            this.reLayout()
        },
        methods: {
            reLayout() {
                this.re_layout("full-h-wrapper-on-fundcashreport", "inner-on-fundcashreport", 220);
            },
            fundClick(data) {
                if(data) {
                    if(data.code.split(",").length > 1){
                        this.dataQuery.fundId = null;
                    }else{
                        this.dataQuery.fundId = data.code;
                    }
                    this.cashQuery();
                }
            },
            cashQuery(){
                this.$refs.datagrid.dataChange(1);
                this.$refs.tradeFlowGrid.clearData();
            },
            cashExport(){
                this.$refs.datagrid.exportCsv({filename: '产品余额报表', original: false});
            },
            cashModify(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length==0){
                    this.openMask();
                    this.$alert('请先选中一条记录','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                let queryDate = this.dataQuery.queryDate;
                let nowDate = Cookies.get('CurrSysDate');

                if(queryDate < nowDate){
                    this.openMask();
                    this.$alert('不允许调整小于当日的账户余额','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return;
                }

                this.fundCashInfo = deepCopy(selects[0]);
                this.setFundCashList();

                if(selects[0].transfer_type != "1"){
                    this.adjustable = true;
                }else{
                    this.adjustable = false;
                }
                if(selects[0].sq_account_id != null){
                    this.sqadjustable = true
                }else{
                    this.sqadjustable = false;
                }
                if(selects[0].zz_account_id != null){
                    this.zzadjustable = true
                }else{
                    this.zzadjustable = false;
                }
                this.accountCashModel = true;
            },
            ModifyCancle(){
                this.accountCashModel = false;
            },
            ModifyOK(){
                updateFundCash(this.fundCashInfo).then(res => {
                    if (res) { //token过期判定
                        if (res.data.success == true) {
                            this.$alert('资金调整成功','成功',{
                                confirmButtonText: '确定',
                                type: 'success',
                                callback:action => {
                                    this.accountCashModel = false;
                                }
                            }).then(() => {
                                this.accountCashModel = false;
                            })
                            this.cashQuery();
                        } else {
                            this.$alert('资金调整失败','提示',{
                                confirmButtonText: '确定',
                                type: 'warning',
                                callback:action => {
                                    this.accountCashModel = false;
                                }
                            }).then(() => {
                                this.accountCashModel = false;
                            })
                        }
                    }
                })
            },
            rowClick(arr){
              //  this.tradeFlowQuery.accountId = arr[0].bank_account_id;
                this.fundCashInfo = deepCopy(arr[0]);
                this.setFundCashList();
                this.tradeFlowQuery.fundId = this.fundCashInfo.fund_id;
                this.tradeFlowQuery.accountId = this.fundCashInfo.bank_account_id;
                this.$refs.tradeFlowGrid.dataChange(1);
            },
            onSplitMove() {
                this.$refs.datagrid.handleResize();
                this.$refs.tradeFlowGrid.handleResize();
            },
            cashMsgOpen() {
                this.cashFlowMsgBoxShow = true;
                this.cashFlowQueryType = "1";
                this.$refs.accCashFlowQueryMsg.resetCashFlowQueryForm();
            },
            flowMsgOpen() {
                this.cashFlowMsgBoxShow = true;
                this.cashFlowQueryType = "2";
                this.$refs.accCashFlowQueryMsg.resetCashFlowQueryForm();
            },
            onCashFlowClose() {
                this.cashFlowMsgBoxShow = false;
            },
            onCashFlowRefresh() {
                this.cashQuery();
            },
            setFundCashList() {
                this.fundCashInfo.fund_cash_list = [];
                if (this.fundCashInfo.accname_balance_sum) {
                    let accNameBalanceSumArray = this.fundCashInfo.accname_balance_sum.split(',');
                    if (accNameBalanceSumArray && accNameBalanceSumArray.length > 0) {
                        for (let accNameBalance of accNameBalanceSumArray) {
                            let data = accNameBalance.split('|');
                            if (data && data.length === 3) {
                                let tempFundCash = deepCopy(this.fundCashInfo);
                                tempFundCash.bank_account_id = data[0];
                                tempFundCash.bank_account_name = data[1];
                                tempFundCash.account_cur_balance = data[2];
                                this.fundCashInfo.fund_cash_list.push(tempFundCash);
                            }
                        }
                    }
                }
            }
        },
        name: "Check",
        components: {HDatagrid, HOtherFundTree,accCashFlowQueryMsgBox}
    }
</script>

<style lang="less" rel="stylesheet/less">
    .accbalance-tooltip-table {
        .h-tooltip-inner {
            max-width: 400px;
            color: #333;
            background-color: rgb(250, 250, 250);
        }

        .h-tooltip-popper[x-placement^="top"] .h-tooltip-arrow,
        .h-tooltip-popper[x-placement^="bottom"] .h-tooltip-arrow {
            border-top-color: rgb(250, 250, 250);
            border-bottom-color: rgb(250, 250, 250);
        }
    }

    .s-theme-black .accbalance-tooltip-table {
        .h-tooltip-inner {
            color: #fff;
            background-color: #3C4655;
        }

        .h-tooltip-popper[x-placement^="top"] .h-tooltip-arrow,
        .h-tooltip-popper[x-placement^="bottom"] .h-tooltip-arrow {
            border-top-color: #3C4655;
            border-bottom-color: #3C4655;
        }
    }

</style>


