<template>
  <div class="h-datagrid">
    <div class="h-datagrid-title" v-if="title">
      <h4>{{ title }}</h4>
    </div>
    <div class="h-datagrid-toolbar" v-if="hasToolBar">
      <slot name="toolbar"></slot>
    </div>
    <h-table :data="tData" bodyAlgin='right' headAlgin='left'
             :columns="validColumns"
             :border="border"
             :size="size"
             :stripe="stripe"
             :show-header="showHeader"
             :width="width"
             :height="autoHeight"
             :canDrag="canDrag"
             :loading="loadingData"
             :disabled-hover="disabledHover"
             :highlight-row="highlightRow"
             :row-class-name="rowClassName"
             :no-data-text="onDataText"
             :no-filtered-data-text="noFilteredDataText"
             :row-select="rowSelect"
             @on-row-click="onRowClick"
             @on-row-dblclick="onRowDbClick"
             @on-current-change="onCurrentChange"
             @on-current-change-cancle="onCurrentChangeCancel"
             @on-select="onSelect"
             @on-select-cancel="onSelectCancel"
             @on-select-all="onSelectAll"
             @on-selection-change="onSelectChange"
             @on-sort-change="onSortChange"
             @on-expand="onExpand"
             ref="gridContent"></h-table>
    <h-page v-show="showPage"
            :current="current"
            :total="total"
            :page-size="pageSize"
            :placement="placement"
            :simple="simple"
            :show-total="showTotal"
            :show-elevator="showElevator"
            :show-sizer="showSizer"
            :pageSizeOpts="pageSizeOpts"
            :class-name="className"
            :styles="styles"
            @on-change="dataChange"
            @on-page-size-change="pageSizeChange"
            ref="gridPage" v-if="hasPage"></h-page>

      <h-msg-box class = "customMsgBox" v-if="gridName" v-model="showLayout" width = "490px">
          <p slot="header">
              <span>布局</span>
          </p>
          <h-transfer
                  :titles="['隐藏列','显示列']"
                  :data="columndata"
                  :target-keys="showColumns"
                  :render-format="renderMethod"
                  @on-right-drag="upDownMove"
                  @on-change="handleChange">
          </h-transfer>
          <div slot="footer">
              <h-button type="primary" @click="submitLayout">保存</h-button>
              <h-button type="ghost" @click="handleCancel">取消</h-button>
          </div>
      </h-msg-box>
  </div>
</template>
<script>
  const prefixCls = 'h-datagrid'

  import fetch from '../api/httpFetch.js'
  import Emitter from '@/utils/emitter';
  import {
      getShowColumn,
      setShowColumn
  } from '@/api/utils'

  export default {
    name: 'HDatagrid',
    data () {
      return {
          tData: [],
          total: 0,
          columndata: this.getMockData(),
          showColumns: this.getTargetKeys(),
          showColumnsTmp:'',
          dataInfo: {},
          pageInfo: {
            pageNo: 1,
            pageSize: 100
          },
          current: 1,
          transProps:{
            key:'key',
            label:'title'
          },
          showLayout:false,
          hasToolBar: true,
          loadingData: false //仅在url中生效，非URL在外部配置
      }
    },
    mixins: [Emitter],
    props: {
      gridName:String,
      title: String,//表格列表头部信息,
      gridData: Object, // 数据信息，包含表格信息rows及分页信息total[可以通过property自定义rows字段]
      columns: Array,
      border: {
        type: Boolean,
        default: true
      },
      size: {
        type: String,
        default: 'small'
      },
      stripe: {
        type: Boolean,
        default: false
      },
      showHeader: {
        type: Boolean,
        default: true
      },
      width: [Number, String],
      height: [Number, String],
      canDrag: {
        type: Boolean,
        default: true
      },
      disabledHover: {
        type: Boolean,
        default: false
      },
      highlightRow: {
        type: Boolean
      },
      rowClassName: Function,
      onDataText: String,
      rowSelect:{
        type: Boolean,
        defalut: false
      },
      noFilteredDataText: String,
      onRowClick: {
        type: Function,
        default () {
          return ''
        }
      },
      onRowDbClick: {
        type: Function,
        default () {
          return ''
        }
      },
      onCurrentChange: {
        type: Function,
        default () {
          return ''
        }
      },
      onCurrentChangeCancel: {
        type: Function,
        default () {
          return ''
        }
      },
      onSelect:{
        type: Function,
        default () {
          return ''
        }
      },
      onSelectCancel: {
        type: Function,
        default () {
          return ''
        }
      },
      onSelectAll: {
        type: Function,
        default () {
          return ''
        }
      },
      onSelectChange: {
        type: Function,
        default () {
          return ''
        }
      },
      onSortChange: {
        type: Function,
        default () {
          return ''
        }
      },
      onExpand: {
        type: Function,
        default () {
          return ''
        }
      },
      pageSize: Number,
      placement: {
        type: String,
        default: 'top'
      },
      pageSizeOpts: Array,
      simple: Boolean,
      showTotal: {
        type: Boolean,
        default: true
      },
      showElevator: {
        type: Boolean,
        default: true
      },
      showSizer: {
        type: Boolean,
        default: true
      },
      className: String,
      styles: Object,
      hasPage:{ //是否显示分页
        type: Boolean,
        default: false
      },
      getDataFunc:{ //分页获取data函数，该参数必须配gridData || 无分页情况,直接返回rows,同时配置get-data事件
        type: Boolean,
        default: false
      },
      // getData: Function, //分页获取data函数，该参数必须配gridData || 无分页情况,直接返回rows
      url: String, //参考2.0 通过url请求数据
      bindForm: Object,
      autoLoad: { //设置URL时是否自动加载数据，默认true
        type: Boolean,
        default: true
      },
      loading: { // 非url时使用
        type: Boolean,
        default: false
      },
      property: {
        type: Object,
        default() { // 自定义返回字段
          return {
          total: 'total',
          rows: 'rows',
          pageNo: 'pageNo',
          pageSize: 'pageSize',
          responseType: 'data'
          }
        }
      }
    },
    watch: {
      /*current (val) {
        this.pageInfo.pageNo = val
      },*/
      // pageSize :function(val) {
      //   console.log('======pageSize', val)
      //   this.pageInfo.pageSize = val
      // },
      pageSize: {
        handler: function(val) {
          this.pageInfo.pageSize = val
        },
        immediate: true
      },
      gridData: {
　　　　handler(newValue, oldValue) {
          if(newValue && newValue[this.property.rows] && newValue[this.property.rows].length > 0) {
            this.tData = newValue[this.property.rows]
          } else {
            this.tData = []
          }
          if (this.hasPage) {
            if(newValue && newValue[this.property.total]) {
              this.total = newValue[this.property.total]
            } else {
              this.tData = 0
            }
          }
　　　　},
　　　　deep: true
  　　},
      loading (val) {
        this.loadingData = val
      },
        showLayout:function (show) {
            if(show){
                this.openMask();
            }else{
                this.closeMask();
            }
        },
        isSidebarExpand: {
          handler: function (val) {
            this.$refs.gridContent.handleResize()
          }
        }
    },
    computed:{
        validColumns() {
            if (this.gridName) {
                /**********加载布局显示列********************/
                let solvedColumnsTmp = this.columns.filter(e => e.fixedLayout == '1');
                if (this.showColumns) {
                    for (let i=0; i < this.showColumns.length; i++){
                        solvedColumnsTmp.push((this.columns.filter(e => e.key == this.showColumns[i]))[0]);
                    }
                }
                return solvedColumnsTmp;
            } else {
                return this.columns;
            }
        },
        autoHeight(){
          if (this.total>this.pageInfo.pageSize){
              return this.height - 40
          } else {
              return this.height
          }
      },
        showPage(){
            return this.total>this.pageInfo.pageSize;
        },

        isSidebarExpand () {
          return this.$store.state.app.sidebar.opened
        }
      },
    methods :{
        getMockData () {
            let mockData = [];
            let tmpData = [];
            /***********列表存在勾选框等其他不需要展示列可以通过该方式剔除（x => x.fixedLayout.indexOf('1')）*************************/
            tmpData = this.columns.filter(x => !x.fixedLayout || x.fixedLayout.indexOf('1') < 0);
            for(let i = 0; i < tmpData.length; i++){
                mockData.push({
                    key: tmpData[i].key,
                    label: tmpData[i].title
                });
            }
            return mockData;
        },
        getTargetKeys () {
            return this.getMockData().filter(() => Math.random() * 2 > 1).map(item => item.key);
        },
        renderMethod (item) {
            return item.label;
        },
        handleChange (newTargetKeys) {
            this.showColumns = newTargetKeys;
        },
        upDownMove (newTargetKeys){
            let newTmpTargetKeys = [];
            for(let i=0;i<newTargetKeys.length;i++){
                newTmpTargetKeys.push( newTargetKeys[i].key);
            }
            this.showColumns = newTmpTargetKeys;
        },
        openLayout(){
          this.showLayout  = true
        },
        submitLayout(){
            this.showColumnsTmp = this.showColumns.join(",");
            setShowColumn({
                    datagridName:this.gridName,
                    showColumnStr: this.showColumnsTmp
                }).then(res => {
                if(res.data && res.data.code == "0"){
                    this.showLayout = false;
                }else{
                    this.$alert('调整布局失败','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                }
            })
        },
        handleCancel(){
            this.showLayout = false;
        },
        getPageNo(){
            return this.pageInfo.pageNo;
        },
        getPageSize(){
            return this.pageInfo.pageSize
        },
        getDataListByUrl () {
        let query ={}
        // 是否有分页存在
        this.loadingData = true
        if (this.hasPage) {
          query.pageNo =  this.pageInfo.pageNo
          query.pageSize = this.pageInfo.pageSize
          Object.assign(query, this.bindForm)
          let that = this
          fetch.post(this.url, query).then(res => {
            var data = res.data
            if (data) { //token过期
              this.loadingData = false
              this.tData = data.list?data.list:[]
              this.total = data.total
              that.$nextTick(function () {
                that.dispatch('curpage','successDatagridLoad',this.tData, this.total)
              })
              SSObservable.publish('re_layout')
            }
          })
        } else {
          query.page_index =  1
          query.page_size = 1000
          Object.assign(query, this.bindForm)
          let that = this
          fetch.post(this.url, query).then(res => {

            var data = res.data.rows?res.data.rows:res.data.data
            if (data) {
              this.loadingData = false
              this.tData = data
              this.$emit('getDataForDatagrid', this.tData);
              that.$nextTick(function () {
                that.dispatch('curpage','successDatagridLoad')
              })
              SSObservable.publish('re_layout')
            }
          })
        }
      },
        //清空数据
        clearData() {
            this.tData = [];
            this.total = 0;
            this.current = 1;
            this.pageInfo.pageNo = 1;
            this.pageInfo.pageSize = 100;
        },
      // 赋值
      setGridData () {
        if (this.gridData[this.property.rows]) {
          this.tData = this.gridData[this.property.rows]
        }
        if (this.hasPage) {
          if (this.gridData[this.property.total]) {
            this.total = this.gridData[this.property.total]
          }
        }
      },
      getDataList () {
        if (this.url) {
          this.getDataListByUrl()
        } else if (this.getDataFunc) {
          if (this.hasPage) {
            this.$emit('get-data', this.pageInfo)
          } else {
            this.$emit('get-data')
          }
        } else {
          this.setGridData()
        }
      },
      // 点击上一页
      dataChange (i) {
          if (i) {
              this.pageInfo.pageNo = i;
              this.current = i;
          }
          this.getDataList()
      },
      // 页码改变
      pageSizeChange (i) {
        this.pageInfo.pageSize = i
        this.current = 1;
        this.pageInfo.pageNo = 1
        this.getDataList()
      },
      // 反选某行的选中状态，仅多选
      toggleTableSelect (index) {
        if (this.$refs.gridContent.selectType) {
          this.$refs.gridContent.toggleSelect(index)
        } else (
          this.$alert('仅支持表格多选','提示',{
              confirmButtonText: '确定',
              type: 'warning'
          })
        )
      },
      // 表格导出功能
      exportCsv (params) {
        this.$refs.gridContent.exportCsv(params)
      },
      /*******同一个页面布局需要重新加载使用************************/
      changeGrid(gridName){
          let gridNameStr = gridName;
          if (gridNameStr){
              /**********加载布局显示列********************/
              getShowColumn(gridNameStr).then(res => {
                  if(res.data && res.data.code == "0"){
                      let data = res.data.data;
                      this.showColumns = data ? data.split(',') : [];
                  }
              })
          }
      },
      handleResize() {
        this.$refs.gridContent.handleResize()
      }
    },
    created () {
        if (this.gridName){
            getShowColumn(this.gridName).then(res => {
                if(res.data && res.data.code == "0"){
                    let data = res.data.data;
                    this.showColumns = data ? data.split(',') : [];
                }
            })
        }
        if(this.autoLoad) {
            this.getDataList()
          } else {
            if (this.gridData) {
              this.setGridData()
            }
          }
      if (!this.$slots.toolbar) {
        this.hasToolBar = false
      }
      // console.log("页面渲染完成？"+JSON.stringify(this.tData));
    },
    mounted() {
    }
   }
</script>
<style>
  .h-datagrid {

  }
    .h-datagrid-title {
    padding: 5px 10px 4px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    border-bottom: 1px solid transparent;
    color: #fff;
    background: #515A71;
    }
    .h-datagrid-title h4 {
    margin: 0;
    line-height: 20px;
    }
    .h-datagrid-toolbar {
    padding: 5px 10px;
    border: solid #d7dde4;
    border-width: 1px 1px 0;
    }
    .h-datagrid-toolbar .h-btn {
      border-color: #1388e6;
      padding: 0px;
    }
    .h-datagrid-toolbar .h-btn:hover {
      background: #1388e6;
    }
    .h-datagrid-toolbar .h-btn:focus {
      background: #0372cc;
    }
    .h-datagrid-toolbar .h-btn > .h-icon + span,
    .h-datagrid-toolbar .h-btn > span + .h-icon{
    font-size: 12px;
    color: #1388e6;
    margin-left: 0;
    }
    .h-datagrid-toolbar .h-btn:hover > .h-icon + span{
      color: #fff;
    }
    .h-datagrid-toolbar .h-btn:focus > .h-icon + span{
      color: #fff;
    }
    .h-datagrid-toolbar .h-btn .iconfont {
       height:13px;
       width:14px;
       color:#1388e6;
    }
    .h-datagrid-toolbar .h-btn:hover .iconfont {
      color:#fff;
    }
    .h-datagrid-toolbar .h-btn:focus .iconfont {
      color:#fff;
    }
    .h-datagrid-toolbar .h-btn.h-btn-disable > .h-icon + span,
    .h-datagrid-toolbar .h-btn.h-btn-disable > span + .h-icon,
    .h-datagrid-toolbar .h-btn.h-btn-disable .iconfont {
    color: inherit;
    }
    .h-datagrid .h-table-small td {
    height: 30px;
    }
    .h-datagrid .h-select-item {
    padding: 3px 16px;
    }
    .h-datagrid .h-page {
    margin: 5px;
    }
    .h-datagrid .h-page .h-page-prev,
    .h-datagrid .h-page .h-page-next,
    .h-datagrid .h-page .h-page-item-jump-prev,
    .h-datagrid .h-page .h-page-item-jump-next,
    .h-datagrid .h-page .h-page-item,
    .h-datagrid .h-page .h-page-options-elevator input {
    height: 26px;
    line-height: 24px;
    min-width: 24px;
    }
    .h-datagrid .h-page .h-select-single .h-select-selection,
    .h-datagrid .h-page .h-select-single .h-select-selection .h-select-placeholder,
    .h-datagrid .h-page .h-select-single .h-select-selection .h-select-selected-value {
    height: 28px;
    line-height: 26px;
    }
</style>



