<template>
    <div class="full-h" id="full-h-wrapper-on-fundreservereport">
        <h-row>
            <h-col>
                <el-form style="font-size: 12px;padding-left: 5px" :inline="true" size="mini" :model="dataQuery" class="demo-form-inline" >
                    <el-form-item>
                        <el-date-picker v-model="queryDate" type="date" placeholder="选择日期"></el-date-picker>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="Query">查询</el-button>
                        <el-button type="ghost" @click="Modify">修改</el-button>
                        <el-button type="ghost" @click="Export">导出</el-button>
                        <el-button type="ghost" @click="ParSet">参数</el-button>
                    </el-form-item>
                </el-form>
            </h-col>
        </h-row>

        <h-datagrid
                hasPage
                highlight-row
                stripe
                :height="autoHeight"
                :pageSize="100" :pageSizeOpts="pageSizeOpts"
                :bindForm="dataQuery"
                :columns="fundReservecolumns"
                style="margin-left: 5px;margin-top: 10px"
                :on-row-db-click="fundReserveReportDbClick"
                url="/cashcheck/fundReserve/fundReserveInfo.json"
                ref="datagrid">
        </h-datagrid>


        <h-msg-box class="customMsgBox"  v-model="parSetModel" width="350" title="参数调整"  :mask-closable=false inline>
            <div>
                <h-form  cols="1" :label-width="120">
                    <h-form-item label="债券备付金比例" :validRules="numberValidRule">
                        <h-typefield  v-model="sysparam10015" integerNum="3" suffixNum = "2" setNull type="money"
                                      placeholder="请输入" @on-blur="sys10015Changed" >
                        </h-typefield>
                    </h-form-item >
                    <h-form-item label="其他备付金比例" :validRules="numberValidRule">
                        <h-typefield  v-model="sysparam10014" integerNum="3" suffixNum = "2" setNull type="money"
                                      placeholder="请输入" @on-blur="sys10014Changed" >
                        </h-typefield>
                    </h-form-item >
                </h-form>
            </div>

            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="ParSetCancle">取消</h-button>
                <h-button type="primary" @click="ParSetOK">确定</h-button>
            </div>
        </h-msg-box>

        <h-msg-box class="customMsgBox"  v-model="adjustValueModel" width="350" title="交易量调整" :mask-closable=false inline>
            <div>
                <h-form  cols="1" :label-width="120">
                    <h-form-item label="产品名称">
                        <h-input  v-model="fundReserveInfo.fund_name" disabled >
                        </h-input>
                    </h-form-item >
                    <h-form-item label="债券交易量调整值" :validRules="numberValidRule">
                        <h-typefield  v-model="fundReserveInfo.bond_trade_adjust_value" integerNum="9" type="money"
                                       setNull divided>
                        </h-typefield>
                    </h-form-item >
                    <h-form-item label="其他交易量调整值" :validRules="numberValidRule">
                        <h-typefield  v-model="fundReserveInfo.other_trade_adjust_value" integerNum="9" type="money"
                                        setNull divided>
                        </h-typefield>
                    </h-form-item >
                </h-form>
            </div>

            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="ModifyCancle">取消</h-button>
                <h-button type="primary" @click="ModifyOK">确定</h-button>
            </div>
        </h-msg-box>

    </div>
</template>

<script>
    import FundTree from "@/components/FundTree";
    import HDatagrid from '@/components/HDatagrid'
    import {
        parseDateString,
        parseDateStr,
        formatMoney,
        getSysParamValue,
    } from '@/api/utils'
    import {
        updateSysParam,
        checkModifyDay,
        updateFundReserve
    } from "@/api/cashcheck/cashCheck";
    import Cookies from 'js-cookie'
    /* mixin dependency */
    import render from "@/mixins/render";

    export default {
        mixins: [render],
        data(){
            return {
                parSetModel: false,
                adjustValueModel: false,
                dataQuery: {
                    fundIdList: '',
                    queryDate: Cookies.get('CurrSysDate')
                },
                sysparam10014: 0.0,
                sysparam10015: 0.0,
                numberValidRule: [{test: /^[0-9]*$/, message: '只能输入数字'}],
                pageSizeOpts: [25, 50, 75, 100],
                fundReservecolumns: [
                   {
                        title: "产品代码",
                        key: "fund_code",
                        width: 100,
                        ellipsis:true,
                        align: 'center'
                    },{
                        title: "产品名称",
                        key: "fund_name",
                        //width: 120,
                        ellipsis:true,
                        align: 'center'
                    },{
                        title: "上月已缴备付金",
                        key: "last_reserve_payment",
                        width: 130,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            const rowData = params.row;
                            var name = formatMoney(rowData.last_reserve_payment);
                            return h("span", {}, name);
                        }

                    },{
                        title: "债券交易量调整值",
                        key: "bond_trade_adjust_value",
                        width: 130,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            const rowData = params.row;
                            var name = formatMoney(rowData.bond_trade_adjust_value);
                            return h("span", {}, name);
                        }
                    },{
                        title: "其他交易量调整值",
                        key: "other_trade_adjust_value",
                        width: 130,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            const rowData = params.row;
                            var name = formatMoney(rowData.other_trade_adjust_value);
                            return h("span", {}, name);
                        }
                    },{
                        title: "本月应缴备付金",
                        key: "curr_reserve_payment",
                        width: 130,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            const rowData = params.row;
                            var name = formatMoney(rowData.curr_reserve_payment);
                            return h("span", {}, name);
                        }
                    },{
                        title: "本月应备资金",
                        key: "curr_reserve_unpay",
                        width: 130,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            const rowData = params.row;
                            var name = formatMoney(rowData.curr_reserve_unpay);
                            return h("span", {}, name);
                        }
                    },{
                        title: "上日银行余额",
                        key: "account_last_balance",
                        width: 130,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            const rowData = params.row;
                            var name = formatMoney(rowData.account_last_balance);
                            return h("span", {}, name);
                        }
                    },{
                        title: "上日银行余额-本月应备资金",
                        key: "diff_balance",
                        width: 180,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            const rowData = params.row;
                            var name = formatMoney(rowData.diff_balance);
                            return h("span", {}, name);
                        }
                    }
                ],
                fundReserveInfo: {
                    fund_id: '',
                    fund_code:'',
                    fund_name: '',
                    curr_date:0,
                    bond_trade_adjust_value: 0.0,
                    other_trade_adjust_value: 0.0,
                    last_reserve_payment: 0.0,
                    curr_reserve_payment: 0.0,
                    curr_reserve_unpay: 0.0,
                    account_last_balance: 0.0,
                    estimate_curr_reserve_payment:0.0,
                    diff_balance: 0.0
                }
            }
        },
        watch:{
            parSetModel:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            },
            adjustValueModel:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
            queryDate:{
                get:function () {
                    if(this.dataQuery.queryDate)
                        return parseDateString(this.dataQuery.queryDate)
                    else
                        return parseDateString(Cookies.get('CurrSysDate'))
                },
                set:function (newValue) {
                    this.dataQuery.queryDate = parseDateStr(newValue)
                }
            }
        },
        created(){
            // dataQuery.relatedCashDate = new Date();

        },
        mounted() {
            this.reLayout()
        },
        methods: {
            reLayout() {
                this.re_layout("full-h-wrapper-on-fundreservereport", "inner-on-fundreservereport", 70);
            },
            Query(){
                this.$refs.datagrid.dataChange(1);
            },
            Export(){
                this.$refs.datagrid.exportCsv({filename: '备付金报表', original: false});
            },
            Modify(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length==0){
                    this.openMask();
                    this.$alert('请先选中一条记录','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                this.fundReserveReportDbClick(selects[0]);
            },
            fundReserveReportDbClick(data){
                let queryDate = data.curr_date;
                let nowDate = Cookies.get('CurrSysDate');

                if(queryDate < nowDate){
                    this.openMask();
                    this.$alert('不允许调整小于当日的交易量调整值','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return;
                }

                checkModifyDay(queryDate).then(res => {
                    if(res.data.success == false){
                        this.openMask();
                        this.$alert('不是本月前2个交易日或最后2个交易日，不允许调整交易量','提示',{
                            confirmButtonText: '确定',
                            type: 'warning',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                        return;
                    }
                    else{
                        this.fundReserveInfo = data;
                        this.adjustValueModel = true;
                    }
                }).catch(error => {
                    this.openMask();
                    this.$alert('判断是否是本月前2个交易日或最后2个交易日异常','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return;
                });
            },
            ModifyOK(){
                updateFundReserve(this.fundReserveInfo).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg,'成功',{
                                confirmButtonText: '确定',
                                type: 'success',
                                callback:action => {
                                    this.adjustValueModel = false;
                                }
                            }).then(() => {
                                this.adjustValueModel = false;
                            })
                            this.Query();
                        } else if (res.data && res.data.code == '1120010') {
                            //没有修改项时，直接关闭窗口
                            this.adjustValueModel = false;
                        }else {
                            this.$alert(res.data.msg,'错误',{
                                confirmButtonText: '确定',
                                type: 'warning',
                            })
                        }
                });
            },
            ModifyCancle(){
                this.adjustValueModel = false;
            },
            ParSet(){
                getSysParamValue(10014).then(res => {
                    this.sysparam10014 = res.data.data;
                }).catch(error => {
                    this.openMask();
                    this.$alert('债券备付金比例获取异常','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return;
                });

                getSysParamValue(10015).then(res => {
                    this.sysparam10015 = res.data.data;
                }).catch(error => {
                    this.openMask();
                    this.$alert('其他备付金比例获取异常','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return;
                });

                this.parSetModel = true;
            },
            ParSetCancle(){
               this.parSetModel = false;
            },
            ParSetOK(){

                updateSysParam(10014,this.sysparam10014).then(res => {
                    if (res.data.success == false) {
                        this.$alert('参数调整失败','错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                                this.parSetModel = false;
                            }
                        }).then(() => {
                            this.parSetModel = false;
                        })
                        return;
                    }
                }).catch(error => {
                    this.$alert('参数调整发生异常','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.parSetModel = false;
                        }
                    }).then(() => {
                        this.parSetModel = false;
                    })
                    return;
                });

                updateSysParam(10015,this.sysparam10015).then(res => {
                    if (res.data.success == false) {
                        this.$alert('参数调整失败','提示',{
                            confirmButtonText: '确定',
                            type: 'info',
                            callback:action => {
                                this.parSetModel = false;
                            }
                        }).then(() => {
                            this.parSetModel = false;
                        })
                        return;
                    }
                }).catch(error => {
                    this.$alert('参数调整发生异常','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.parSetModel = false;
                        }
                    }).then(() => {
                        this.parSetModel = false;
                    })
                    return;
                });

                this.$alert('参数调整成功','成功',{
                    confirmButtonText: '确定',
                    type: 'success',
                    callback:action => {
                        this.parSetModel = false;
                    }
                }).then(() => {
                    this.parSetModel = false;
                })

            },
            sys10014Changed(){
               if(this.sysparam10014 > 100 ){
                   this.$alert('其他备付金比例需要0-100之间','错误',{
                       confirmButtonText: '确定',
                       type: 'error'
                   })
                   this.sysparam10014 = 100;
                   return;
               }
                if(this.sysparam10014 < 0 ){
                    this.$alert('其他备付金比例需要0-100之间','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    })
                    this.sysparam10014 = 0 ;
                    return;
                }
            },
            sys10015Changed(){
                if(this.sysparam10015 > 100){
                    this.$alert('债券备付金比例需要0-100之间','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    })
                    this.sysparam10015 = 100;
                    return;
                }
                if(this.sysparam10015 < 0 ){
                    this.$alert('债券备付金比例需要0-100之间','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    })
                    this.sysparam10015 = 0;
                    return;
                }

            }
        },
        name: "Check",
        components: {HDatagrid, FundTree}
    }
</script>

<style scoped>

</style>


