<template>
    <div style="height:100%;">
        <label style="display:none;"><span></span><input type="text" name="hidden1" ></label>
        <label style="display:none;"><span></span><input type="password" name="hidden2" ></label>

        <h-row>
            <h-col span="24" style="padding-left: 6px;padding-right: 6px">
                <el-input  size="mini"  placeholder="关键字" v-model="filterText" class="input-with-select">
                </el-input>
            </h-col>
            <!--<h-col>
                <el-dropdown style="float:right;width: 100px" split-button type="ghost" @click="openQuery"  @command="handleConditionQuery">
                    <span style="font-size:10px">搜索</span>
                    <el-dropdown-menu slot="dropdown">
                    </el-dropdown-menu>
                </el-dropdown>
            </h-col>-->
        </h-row>

        <div class="auto-height" style="margin-top:10px;">
            <el-scrollbar style="height: 100%;" class="h-scrollbar">
                <el-tree :data="bankTree" :props="treeProps" @node-click="onBankClick" ref="bankTree" node-key="code" :default-expand-all="true" :expand-on-click-node="false" :filter-node-method="filterNode">
                    <span  class="custom-tree-node" slot-scope="{ node, data }" style="width: 100%">
                        <span >{{ node.label }}</span>
                    </span>
                </el-tree>
            </el-scrollbar>
        </div>
        <!--<h-msg-box class="customMsgBox"  v-model="setQueryMsg" width="400" :mask-closable=false>
            <p slot="header">
                <span>高级查询</span>
            </p>
            <h-form :model="bankInfoQuery" :label-width="60" ref="queryConditions" cols="2">
                <h-form-item  v-if="false" label="是否托管" prop="trust_flag" >
                    <el-select size="mini" v-model="bankInfoQuery.trust_flag"  filterable clearable>
                        <el-option v-for="item in trustFlagList" :value="item.code" :label="item.text" :key="item.code">
                        </el-option>
                    </el-select>
                </h-form-item>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="handleCancel">取消</h-button>
                <h-button type="primary" @click="handleConditionQuery">确定</h-button>
            </div>
        </h-msg-box>-->
    </div>
</template>

<script>
    import {getBankTreeList} from '@/api/utils' ;

    export default {
        name: "BankTree",
        data() {
            return {
                bankTree: [{text:'   加载中              ... ',code:''}],
                filterText:'',
                setQueryMsg: false,
                treeProps:{
                    label:"text",
                    children:'children'
                },
                bankInfoQuery: {
                    trust_flag:''
                },
                trustFlagList:[
                    {
                        code: '',
                        text: '全部'
                    }, {
                        code: '0',
                        text: '否'
                    }, {
                        code: '1',
                        text: '是'
                    }
                ]
            }
        },
        props: {
            queryUrl:{
                type: String,
                default: '/common/bankInfoList.json'
            },
            trustFlag: {
                type: Boolean,
                default: false
            },
            onBankClick: {
                type: Function,
                default(data) {

                }
            },
            showAll:{
                type: Boolean,
                defalut:false
            },
            onBankQuery:{
                type: Function,
                default(data) {

                }
            }
        },
        watch: {
            filterText(val) {
                this.$refs.bankTree.filter(val);
            },
        },
        mounted() {
            this.querydeal()
        },
        activated(){
            this.handleConditionQuery();
        },
        methods: {
            filterNode(value, data) {
                if (!value) return true;
                return data.text.indexOf(value) !== -1;
            },
            openQuery() {
                this.setQueryMsg = true;
            },
            handleCancel() {
                this.setQueryMsg = false;
            },
            querydeal(){
                if(this.trustFlag){
                    this.bankInfoQuery.trust_flag = '1';
                }
            },
            handleConditionQuery(){
                var params = "?includeSelfRun=true&trustFlag="+this.bankInfoQuery.trust_flag;
                getBankTreeList(this.queryUrl+params).then(res => {
                    if (res.data.length>0) {
                        if(this.showAll){
                            let bankIds = res.data.reduce(function(x, y){
                                return {code:x.code + ',' + y.code}
                            } ).code;
                            this.bankTree=[{text:'全部',code:bankIds,children:res.data}]
                            this.onBankClick({code:bankIds});
                        }else{
                            this.bankTree =res.data
                            this.onBankClick(this.bankTree[0]);
                        }
                        this.onBankQuery(this.bankTree)
                        this.filterText =
                        this.selectNodeByCode(this.bankTree[0].code)
                    }else{
                        this.bankTree= [{text:'暂无数据',code:''}]
                        this.onBankClick({code:''});
                    }
                }).catch(error => {
                    console.log(error);
                });
                this.setQueryMsg = false;
            }
            ,
            selectNodeByCode(code) {
                this.$nextTick(() => {
                    this.$refs.bankTree.setCurrentKey(code)
                })
            }
        }

    }
</script>

<style lang="scss" scoped>
    .auto-height {
        height: calc(100% - 38px);
    }
</style>
<style lang="less" rel="stylesheet/less">
    .el-scrollbar.h-scrollbar {
        .el-scrollbar__wrap{
            overflow-x: hidden;
        }
    }
    .el-tree>.el-tree-node {
        min-width: 100%;
        display: inline-block;
    }
</style>


