<template>
    <div class="c-tooltip-table">
        <div class="table" @click.stop="()=>{}">
            <div class="row head">
                <div class="cell">账户名称</div>
                <div class="cell">余额</div>
            </div>
            <div class="row" v-for="(data, i) in datas" :key="i">
                <div class="cell">{{data.accName}}</div>
                <div class="cell fontWeightBold">{{formatMoney(data.balance)}}</div>
            </div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import {formatMoney} from '@/api/utils'

    export default {
        name: 'AccBalanceTooltipTable',
        props: {
            datas: {
                type: Array,
                default: () => []
            }
        },
        data() {
            return {}
        },
        created() {

        },
        mounted() {

        },
        activated() {

        },
        methods: {
            formatMoney: formatMoney
        },
        components: {}
    }
</script>

<style lang="less" rel="stylesheet/less">
    @lineColor: #DCE1E7;
    .c-tooltip-table {
        background-color: #fff;

        .table {
            display: flex;
            flex-direction: column;
            border-top: 1px solid @lineColor;
            border-left: 1px solid @lineColor;

            .row {
                height: 30px;
                line-height: 30px;
                display: flex;
                flex-direction: row;
            }

            .head {
                background-color: #eeeeee;
            }

            .cell {
                text-align: center;
                width: 155px;
                border-bottom: 1px solid @lineColor;
                border-right: 1px solid @lineColor;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }

    .s-theme-black .c-tooltip-table {
        background-color: #3C4655;

        .table {
            border-top-color: #1F2834;
            border-left-color: #1F2834;

            .head {
                background-color: #1F2834;
            }

            .cell {
                border-bottom-color: #1F2834;
                border-right-color: #1F2834;
            }
        }
    }

</style>



