import fetch from "@/api/httpFetch";

export function insertRival(params) {
    return fetch.post("/systemmgnt/rival/insert.json", params, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    })
}

export function updateRival(params) {
    return fetch.post("/systemmgnt/rival/update.json", params, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    })
}

export function deleteRival(rivalId) {
    return fetch.post("/systemmgnt/rival/delete.json", {rivalId: rivalId})
}


