import fetch from "@/api/httpFetch";

export function update(params) {
    return fetch.post("systemmgnt/businFlag/update.json",params, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function otherInsert(param) {
    return fetch.post("/systemmgnt/businFlag/insert.json", param, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    })
}

export function otherUpdate(param) {
    return fetch.post("/systemmgnt/businFlag/updateBusin.json", param, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    })
}



