<template>
    <div class="systemmgnt-bankinfo" id="full-h-wrapper">
        <label style="display:none;"><span></span><input type="text" name="hidden1" ></label>
        <label style="display:none;"><span></span><input type="password" name="hidden2" ></label>
        <h-row>
            <h-col>
                <h-form :model="queryForm"  ref="queryForm">
                    <h-row>
                        <h-col span="5">
                            <h-form-item label="托管机构" :label-width="75">
                                <h-select v-model="queryForm.orgId" hideMult isString multiple filterable showBottom>
                                    <h-option v-for="item in custodialorgList" :key="item.code" :label="item.text" :value="item.code">
                                    </h-option>
                                </h-select>
                            </h-form-item>
                        </h-col>
                        <h-col span="5"  >
                            <h-form-item label="划款模式" :label-width="75">
                                <h-select v-model="queryForm.transferType" hideMult isString multiple filterable showBottom>
                                    <h-option v-for="item in transferTypeList" :key="item.code" :label="item.text" :value="item.code">
                                    </h-option>
                                </h-select>
                            </h-form-item>
                        </h-col>
                        <h-col span="3">
                            <h-form-item :label-width="20">
                                <h-button type="primary" @click="refresh">查询</h-button>
                            </h-form-item>
                        </h-col>
                    </h-row>
                </h-form>
            </h-col>
        </h-row>
        <h-button-group style="margin-bottom:4px;">
            <h-button icon="add" @click="addBankInfo">新增</h-button>
            <h-button icon="editor" @click="modifyBankInfo">修改</h-button>
            <h-button icon="delete" @click="deleteBankInfo">删除</h-button>
        </h-button-group>
        <h-datagrid stripe hasPage
                    :height="autoHeight"
                    highlight-row
                    :pageSize="100" :pageSizeOpts="pageSizeOpts"
                    :columns="bankInfoColumns"
                    :bindForm="queryForm" :on-row-db-click="bankInfoDbClick"
                    url="/systemmgnt/bankInfo/bankInfo.json"
                    ref="datagrid">
        </h-datagrid>
        <h-msg-box class="customMsgBox" v-model="showBankInfoDialog" width="700" :mask-closeable="false">
            <p slot="header">
                <span v-if="actionType == 'add'">新增托管人信息</span>
                <span v-else>修改托管人信息</span>
            </p>
            <h-form :model="bankInfo" :label-width="120" ref="bankInfoForm">
                <h-row>
                    <h-col span="12">
                        <h-form-item required label="托管机构" prop="org_id">
                            <h-select v-model="bankInfo.org_id" filterable @on-change="orgIdChange">
                                <h-option v-for="item in custodialorgList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required label="托管人名称" prop="custodian_name">
                            <h-input v-model="bankInfo.custodian_name"></h-input>
                        </h-form-item>
                        <h-form-item v-show="custodialorg.elec_transfer_flag == '1'" label="深证通公司代码" prop="szt_company_code">
                            <h-input v-model="bankInfo.szt_company_code"></h-input>
                        </h-form-item>
                        <h-form-item v-show="custodialorg.elec_transfer_flag == '1'" label="深证通应用标识" prop="szt_app_id">
                            <h-input v-model="bankInfo.szt_app_id"></h-input>
                        </h-form-item>
                        <h-form-item label-wrap v-show="custodialorg.elec_transfer_flag == '1'" label="深证通消息关联模式" prop="szt_msg_flag">
                            <h-select  v-model="bankInfo.szt_msg_flag" filterable>
                                <h-option v-for="item in msgFlagList" :value="item.code" :label="item.text" :key="item.code">
                                </h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item :required = "bankInfo.transfer_type == '1'" v-show="(custodialorg.elec_transfer_flag == '1') || bankInfo.transfer_type == '4'" label="查询频率" prop="query_interval">
                            <h-typefield v-model="bankInfo.query_interval" @on-blur="blurInterval" integerNum="3" suffixNum="0">
                                <span slot="append">分钟</span>
                            </h-typefield>
                        </h-form-item>
                        <h-form-item required label="是否日初对账" prop="init_check_acc_flag">
                            <h-select  v-model="bankInfo.init_check_acc_flag" >
                                <h-option v-for="item in checkIsOrNo" :value="item.code" :label="item.text" :key="item.code">
                                </h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required label="是否日终对账" prop="final_check_acc_flag">
                            <h-select  v-model="bankInfo.final_check_acc_flag">
                                <h-option v-for="item in checkIsOrNo" :value="item.code" :label="item.text" :key="item.code">
                                </h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item label="上海非担保结算账户" prop="sh_fdb_account">
                            <h-input :maxlength="128" v-model="bankInfo.sh_fdb_account"></h-input>
                        </h-form-item>
                        <h-form-item label="深圳非担保结算账户" prop="sz_fdb_account">
                            <h-input :maxlength="128" v-model="bankInfo.sz_fdb_account"></h-input>
                        </h-form-item>
                    </h-col>
                    <h-col span="12">
                        <h-form-item v-show="custodialorg.elec_transfer_flag == '1'"  label="深证通用户标识" prop="szt_user_id">
                            <h-input v-model="bankInfo.szt_user_id"></h-input>
                        </h-form-item>
                        <h-form-item v-show="custodialorg.elec_transfer_flag == '1'" label="深证通密钥" prop="szt_key">
                            <h-input v-model="bankInfo.szt_key" type="password" :maxlength="20" v-attrdel:title></h-input>
                        </h-form-item>
                        <h-form-item v-show="custodialorg.elec_transfer_flag == '1'" label="启用压缩" prop="szt_zip_flag">
                            <h-select  v-model="bankInfo.szt_zip_flag">
                                <h-option v-for="item in checkIsOrNo" :value="item.code" :label="item.text" :key="item.code">
                                </h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item v-show="custodialorg.elec_transfer_flag != '1'" required label="划款模式" prop="transfer_type">
                            <h-select v-model="bankInfo.transfer_type" filterable @on-change="transferTypeChange">
                                <h-option v-for="item in transferTypeFormList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item v-show="custodialorg.elec_transfer_flag == '1'" required label="划款模式" prop="transfer_type">
                            <h-select v-model="bankInfo.transfer_type" filterable @on-change="transferTypeChange">
                                <h-option v-for="item in transferTypeList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item v-show="custodialorg.elec_transfer_flag != '1'" required label="附件发送模式" prop="sendfile_type">
                            <h-select v-model="bankInfo.sendfile_type" filterable>
                                <h-option v-for="item in transferTypeFormList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item v-show="custodialorg.elec_transfer_flag == '1'" required label="附件发送模式" prop="sendfile_type">
                            <h-select v-model="bankInfo.sendfile_type" filterable>
                                <h-option v-for="item in transferTypeList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required label="附件发送时点" prop="sendfile_time">
                            <h-select  v-model="bankInfo.sendfile_time" filterable>
                                <h-option v-for="item in sendFileTimeList" :value="item.code" :label="item.text" :key="item.code">
                                </h-option>
                            </h-select>
                        </h-form-item>
                        <!--<h-form-item :required="bankInfo.init_check_acc_flag == '1'" label="日初对账日期" prop="check_acc_day">
                            <h-typefield v-model="bankInfo.check_acc_day" @on-blur="checkDay" integerNum="2" suffixNum="0"></h-typefield>
                        </h-form-item>-->
                        <h-form-item label="联系电话" prop="bank_phone">
                            <h-input :maxlength="32" v-model="bankInfo.bank_phone">
                            </h-input>
                        </h-form-item>
                        <h-form-item label="传真" prop="bank_fax">
                            <h-input :maxlength="32" v-model="bankInfo.bank_fax">
                            </h-input>
                        </h-form-item>
                        <h-form-item style="margin-top: 5px" label="邮箱" prop="bank_mail" :validRules="multiMailValidRule">
                            <h-input placeholder="多个邮箱请用英文逗号隔开" type="textarea" size="small" :maxlength="1024" :canResize="false"
                                     :rows="1" :autosize="{minRows: 1,maxRows:2}" v-model="bankInfo.bank_mail">
                            </h-input>
                        </h-form-item>
                    </h-col>
                </h-row>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="cancelBankInfo">取消</h-button>
                <h-button type="primary" @click="saveBankInfo">确认</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import HDatagrid from "../../../components/HDatagrid";

    import {
        showText,
        getKeys,
        encrypt,
        decrypt,
        getDictList,
        getCustodialorgList,
        getCustodialorg
    } from "@/api/utils";
    import {
        deleteBankInfo,
        addBankInfo,
        modifyBankInfo
    } from "@/api/systemmgnt/bankInfo";
    export default {
        name: "bankInfo",
        data(){
            return{
                autoHeight:0,
                pageSizeOpts: [25,50,75,100],
                transferTypeList: [],
                msgFlagList: [],
                keys: [],
                showBankInfoDialog:false,
                actionType:'',
                transferTypeFormList:[],
                custodialorgList:[],
                bankInfoColumns: [
                    {
                        title: "托管人编号",
                        key: "custodial_no",
                        width: 120,
                        sortable: true,
                        align: 'right',
                        ellipsis: true
                    }, {
                        title: "托管人名称",
                        key: "custodian_name",
                        align: 'left',
                        width: 120,
                        ellipsis: true
                    }, {
                        title: "托管机构",
                        key: "org_id",
                        align: 'left',
                        width: 120,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let orgName = showText(rowData.org_id,this.custodialorgList);
                            return h("span",{},orgName);
                        }
                    }, {
                        title: "托管人余额查询频率",
                        key: "query_interval",
                        align: 'right',
                        width: 120,
                        ellipsis: true
                    }, {
                        title: "划款模式",
                        key: "transfer_type",
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let sendFileType = showText(rowData.transfer_type,this.transferTypeList);
                            return h("span",{},sendFileType);
                        }
                    }, {
                        title: "附件发送模式",
                        key: "sendfile_type",
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let sendFileType = showText(rowData.sendfile_type,this.transferTypeList);
                            return h("span",{},sendFileType);
                        }
                    }, {
                        title: "附件发送时点",
                        key: "sendfile_time",
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let sendFileTime = showText(rowData.sendfile_time,this.sendFileTimeList);
                            return h("span",{},sendFileTime);
                        }
                    }, {
                        title: "日初对账",
                        key: "init_check_acc_flag",
                        align: 'left',
                        width: 80,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let initCheckFlag = showText(rowData.init_check_acc_flag,this.checkIsOrNo);
                            return h("span",{},initCheckFlag)
                        }
                    }, {
                        title: "日终对账",
                        key: "final_check_acc_flag",
                        align: 'left',
                        width: 80,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let finalCheckFlag = showText(rowData.final_check_acc_flag,this.checkIsOrNo);
                            return h("span",{},finalCheckFlag)
                        }
                    }, {
                        title: "深证通用户标识",
                        key: "szt_user_id",
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: "深证通公司代码",
                        key: "szt_company_code",
                        align: 'right',
                        ellipsis: true
                    }, {
                        title: "深证通应用标识",
                        key: "szt_app_id",
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: "启用压缩模式",
                        key: "szt_zip_flag",
                        align: 'left',
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let zipFlag = showText(rowData.szt_zip_flag,this.checkIsOrNo);
                            return h("span",{},zipFlag)
                        }
                    }, {
                        title: "深证通消息关联模式",
                        key: "szt_msg_flag",
                        align: 'left',
                        width: 120,
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let msgFlag = showText(rowData.szt_msg_flag,this.msgFlagList)
                            return h("span",{},msgFlag);
                        }
                    }, {
                        title: "上海非担保结算账户",
                        key: "sh_fdb_account",
                        align: 'left',
                        width: 120,
                        ellipsis: true
                    }, {
                        title: "深圳非担保结算账户",
                        key: "sz_fdb_account",
                        align: 'left',
                        width: 120,
                        ellipsis: true
                    }, {
                        title: '联系电话',
                        key: 'bank_phone',
                        align: 'left',
                        width: 120,
                        ellipsis: true,
                    },
                    {
                        title: '传真',
                        key: 'bank_fax',
                        align: 'left',
                        width: 120,
                        ellipsis: true,
                    },
                    {
                        title: '邮箱',
                        key: 'bank_mail',
                        align: 'left',
                        width: 120,
                        ellipsis: true,
                    }
                ],
                sendFileTimeList:[
                    {
                        code:'1',
                        text:'手工发送'
                    },{
                        code:'2',
                        text:'提交指令时'
                    },{
                        code:'3',
                        text:'发送指令时'
                    }
                ],
                checkIsOrNo:[
                    {
                        code:'0',
                        text:'否'
                    },{
                        code:'1',
                        text:'是'
                    }
                ],
                queryForm: {
                    orgId:'',
                    transferType:''
                },
                custodialorg:{
                    org_id:'',
                    bic_code:'',
                    org_name:'',
                    full_name:'',
                    org_type:'',
                    elec_transfer_flag:''
                },
                bankInfo:{
                    custodial_no:null,
                    custodian_name:'',
                    org_id:'',
                    szt_company_code:'',
                    szt_user_id:'',
                    szt_app_id:'',
                    szt_key:'',
                    szt_msg_flag:'',
                    szt_zip_flag:'',
                    query_interval:'',
                    transfer_type:'',
                    sendfile_type:'',
                    sendfile_time:'',
                    init_check_acc_flag:'',
                    final_check_acc_flag:'',
                    sh_fdb_account: '',
                    sz_fdb_account: '',
                    bank_phone: '',
                    bank_fax: '',
                    bank_mail: null,
                },
                multiMailValidRule: [{ test: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?([,][\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?)*/, message: '邮箱格式不正确' }],
            }
        },
        watch:{
            showBankInfoDialog:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
        },
        mounted(){
            getDictList(90563).then(res => this.msgFlagList = res.data)
            getCustodialorgList().then(res => this.custodialorgList = res.data)
            getDictList(90524).then(res => {
                if(res.data){
                    this.transferTypeList = res.data
                    this.transferTypeFormList = res.data.filter(result => result.code != 1)
                }
            })
            getKeys().then(res => this.keys=res.data)
            this.re_layout()
        },
        methods:{
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 110;
            },
            addBankInfo(){
                this.$refs.bankInfoForm.resetFields();
                this.bankInfo={
                    custodial_no:null,
                    custodian_name:'',
                    org_id:'',
                    szt_company_code:'',
                    szt_user_id:'',
                    szt_app_id:'',
                    szt_key:'',
                    szt_msg_flag:'',
                    szt_zip_flag:'',
                    query_interval:'30',
                    transfer_type:'2',
                    sendfile_type:'2',
                    sendfile_time:'1',
                    init_check_acc_flag:'0',
                    final_check_acc_flag:'0',
                    sh_fdb_account: '',
                    sz_fdb_account: '',
                    bank_phone: '',
                    bank_fax: '',
                    bank_mail: '',
                }
                this.custodialorg={
                    org_id:'',
                    bic_code:'',
                    org_name:'',
                    full_name:'',
                    org_type:'',
                    elec_transfer_flag:''
                }
                this.showBankInfoDialog = true;
                this.actionType='add'
            },
            modifyBankInfo(){
                this.$refs.bankInfoForm.resetFields();
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录","错误",{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }
                this.bankInfoDbClick(selects[0]);
            },
            bankInfoDbClick(data){
                this.bankInfo = data;
                if(this.bankInfo.szt_key){
                    this.bankInfo.szt_key = decrypt(this.bankInfo.szt_key,this.keys)
                }
                if(!this.bankInfo.transfer_type){
                    this.bankInfo.transfer_type = '';
                }
                if(!this.bankInfo.sendfile_type){
                    this.bankInfo.sendfile_type = '';
                }
                if (!this.bankInfo.bank_mail) {
                    this.bankInfo.bank_mail = '';
                }
                this.showBankInfoDialog = true
                this.actionType="modify"
            },
            deleteBankInfo(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if(selects.length == 0){
                    this.openMask();
                    this.$alert("请选择一条记录","错误",{
                        confirmButtonText:'确定',
                        type:'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    })
                    return
                }

                this.openMask();
                this.$confirm('确认删除该托管人信息吗？', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteBankInfo(selects[0].custodial_no).then(res =>{
                        if(res.data && res.data.code == '0'){
                            this.$alert(res.data.msg,"成功",{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            })
                            this.queryForm.org_id = '';
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg.split(","),"提示",{
                                confirmButtonText:'确定',
                                type:'info',
                                multiline:true,
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            })
                        }
                    })
                }).catch(() =>{
                    this.closeMask();
                })
            },
            orgIdChange(currentValue){
                getCustodialorg(currentValue).then(res =>{
                    if (res.data.success) {
                        this.custodialorg.full_name = res.data.data.full_name;
                        this.custodialorg.org_id = res.data.data.org_id;
                        this.custodialorg.bic_code = res.data.data.bic_code;
                        this.custodialorg.org_name = res.data.data.org_name;
                        this.custodialorg.org_type = res.data.data.org_type;
                        this.custodialorg.elec_transfer_flag = res.data.data.elec_transfer_flag;
                        this.bankInfo.custodian_name = res.data.data.org_name;
                        if(this.custodialorg.elec_transfer_flag == '0'){
                            this.bankInfo.szt_company_code = '';
                            this.bankInfo.szt_app_id = '';
                            this.bankInfo.szt_msg_flag = '';
                            if (this.bankInfo.transfer_type !='4'){
                                this.bankInfo.query_interval = '';
                            }
                            this.bankInfo.szt_user_id = '';
                            this.bankInfo.szt_key = '';
                            this.bankInfo.szt_zip_flag = '';
                        }
                    }
                })
            },
            blurInterval(){
                if(this.bankInfo.transfer_type == '4'){
                    if(this.bankInfo.query_interval<5 ){
                        this.$alert("查询频率不能少于5分钟","错误",{
                            confirmButtonText:'确定',
                            type:'error'
                        })
                        this.bankInfo.query_interval=5;
                    }
                }else{
                    if(this.bankInfo.query_interval<0 ){
                        this.$alert("查询频率的范围不能小于零","错误",{
                            confirmButtonText:'确定',
                            type:'error'
                        })
                        this.bankInfo.query_interval=30;
                    }
                }
            },
            transferTypeChange: function(currentValue){
                if(this.actionType == 'add'){
                    if(currentValue == '4'){
                        this.bankInfo.query_interval = 5;
                    }else{
                        this.bankInfo.query_interval = 30;
                    }
                }
            },
            cancelBankInfo(){
                this.showBankInfoDialog = false;
            },
            saveBankInfo(){
                let isValidate = true;
                this.$refs.bankInfoForm.validate((validate) =>isValidate = isValidate && validate)
                if(!isValidate){
                    this.$alert("输入项不满足要求，请重新检查!","错误",{
                        confirmButtonText:'确定',
                        type:'error'
                    })
                    return
                }
                if(this.bankInfo.transfer_type == '4'){
                    if(this.bankInfo.query_interval<5 ){
                        this.$alert("查询频率不能少于5分钟","错误",{
                            confirmButtonText:'确定',
                            type:'error'
                        })
                        return
                    }
                }else{
                    if(this.custodialorg.elec_transfer_flag == 0){
                        if(this.bankInfo.query_interval<30 ){
                            this.bankInfo.query_interval=30;
                        }else if(this.bankInfo.query_interval>120){
                            this.bankInfo.query_interval=120;
                        }
                    }else{
                        if(this.bankInfo.query_interval<0 ){
                            this.$alert("查询频率的范围不能小于零","错误",{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                            return
                        }
                    }
                }
                if(this.bankInfo.szt_key){
                    this.bankInfo.szt_key = encrypt(this.bankInfo.szt_key,this.keys)
                }

                if(this.actionType == 'add'){
                    addBankInfo(this.bankInfo).then(res =>{
                        if(res.data && res.data.success){
                            this.$alert(res.data.msg,"成功",{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.showBankInfoDialog = false
                                }
                            }).then(() =>{
                                this.showBankInfoDialog = false
                            })
                            this.refresh()
                        }else{
                            this.$alert(res.data.msg,"错误",{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }else if(this.actionType == 'modify'){
                    modifyBankInfo(this.bankInfo).then(res =>{
                        if(res.data && res.data.success){
                            this.$alert(res.data.msg,"成功",{
                                confirmButtonText:'确定',
                                type:'success',
                                callback: action => {
                                    this.showBankInfoDialog = false
                                }
                            }).then(() =>{
                                this.showBankInfoDialog = false
                            })
                            this.refresh();
                        }else{
                            this.$alert(res.data.msg,"错误",{
                                confirmButtonText:'确定',
                                type:'error'
                            })
                        }
                    })
                }
            }
        },
        components: {
            HDatagrid
        }
    }
</script>

<style lang="less" rel="stylesheet/less">
    .systemmgnt-bankinfo {
        .h-form-item {
            margin-bottom: 6px;
        }
    }
</style>


