<template>
    <div class="full-h">
        <h-split v-model="split" min="130" max="600" @on-moving="onSplitMoving">
            <div slot="left" span="4" class="full-h">
                <h-other-fund-tree :onFundClick="fundClick"></h-other-fund-tree>
            </div>
            <div slot="right" span="20" class="full-h" style="padding-left: 10px" id="full-h-wrapper-on-relatedcashreport">
                <h-row>
                    <h-col>
                        <el-form style="font-size: 12px;":inline="true" size="mini" :model="dataQuery" class="demo-form-inline">
                            <el-form-item>
                                <el-date-picker v-model="queryDate" type="date" placeholder="选择日期"></el-date-picker>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="cashQuery">查询</el-button>
                                <el-button type="ghost" @click="cashModify">调整</el-button>
                                <el-button type="ghost" @click="cashExport">导出</el-button>
                            </el-form-item>
                        </el-form>
                    </h-col>
                </h-row>
                <h-datagrid
                        hasPage
                        highlight-row
                        stripe
                        :height="autoHeight"
                        :pageSize="100" :pageSizeOpts="pageSizeOpts"
                        :bindForm="dataQuery"
                        :columns="accountCashcolumns"
                        style="margin-top: 10px;"
                        url="/cashcheck/relatedCash/relatedCashReport.json"
                        ref="datagridaccountCash">
                </h-datagrid>
            </div>
        </h-split>
        <h-msg-box class="customMsgBox" v-model="accountCashModel" width="800" height = "400" title="资金调整"
                   :mask-closable=false inline>
            <div v-for="data in accountCashList" >
                <h-form  ref="accountCashForm" cols="3" :label-width="80">
                    <h-form-item label="账户名称:" >
                        <h-input v-model="data.bank_account_name" disabled type="text"></h-input>
                    </h-form-item >
                    <h-form-item label="银行账号:" >
                        <h-input v-model="data.bank_account_no" disabled type="text"></h-input>
                    </h-form-item >
                    <h-form-item label="金额:" required :validRules="numberValidRule">
                        <h-typefield  v-model="data.account_cur_balance" integerNum="9"  type="money" divided
                                      placeholder="请输入" >
                        </h-typefield>
                    </h-form-item >
                </h-form>
            </div>

            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="ModifyCancle">取消</h-button>
                <h-button type="primary" @click="ModifyOK">确定</h-button>
            </div>
        </h-msg-box>

        <h-msg-box class="customMsgBox" v-model="accountCashSingleModel" width="300" height = "160" title="资金调整"
                   :mask-closable=false inline>
            <div>
                <h-form cols="1" :label-width="80">
                    <h-form-item label="账户名称:" >
                        <h-input v-model="accountCash.bank_account_name" disabled type="text"></h-input>
                    </h-form-item >
                    <h-form-item label="银行账号:" >
                        <h-input v-model="accountCash.bank_account_no" disabled type="text"></h-input>
                    </h-form-item >
                    <h-form-item label="金额:" required :validRules="numberValidRule">
                        <h-typefield  v-model="accountCash.account_cur_balance" integerNum="9"  type="money" divided
                                      placeholder="请输入" >
                        </h-typefield>
                    </h-form-item >
                </h-form>
            </div>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="ModifySingleCancle">取消</h-button>
                <h-button type="primary" @click="ModifySingleOK">确定</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import HOtherFundTree from "@/components/HOtherFundTree";
    import HDatagrid from '@/components/HDatagrid'
    import {
        parseDateString,
        parseDateStr,
        showText,
        formatMoney,
        dateFormate
    } from '@/api/utils'
    import {
          accountCashQuery,
          updateAccountCash
    } from "@/api/cashcheck/cashCheck";
    import Cookies from 'js-cookie'
    /* mixin dependency */
    import render from "@/mixins/render";

    export default {
        mixins: [render],
        data(){
            let accountCashcolumns = [
                    {
                        title: "产品代码",
                        key: "fund_code",
                        width: 100,
                        ellipsis:true,
                        align: 'center'
                    },{
                        title: "产品名称",
                        key: "fund_name",
                        //width: 140,
                        ellipsis:true,
                        align: 'center'
                    },{
                        title: "托管人名称",
                        key: "custodian_name",
                        //width: 120,
                        ellipsis:true,
                        align: 'center'
                    },{
                        title: "银行账户名称",
                        key: "bank_account_name",
                        //width: 150,
                        ellipsis:true,
                        align: 'center'
                    },{
                        title: "银行账号",
                        key: "bank_account_no",
                        width: 150,
                        ellipsis:true,
                        align: 'center'
                    },{
                        title: "账户用途",
                        key: "account_class",
                        //width: 100,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            const rowData = params.row;
                            var name = showText(rowData.account_class, this.accountClassList);
                            return h("span", {}, name);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.accountClassList);
                        }
                    },{
                        title: "余额",
                        key: "account_cur_balance",
                        //width: 120,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            const rowData = params.row;
                            var name = formatMoney(rowData.account_cur_balance);
                            return h("span", {style:{fontWeight:'bold'}}, name);
                        }
                    },{
                        title: "更新时间",
                        key: "input_time",
                        width: 100,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            const rowData = params.row;
                            var name = parseDateString(0,rowData.input_time);
                            return h("span", {}, name);
                        }
                    }
                ]

            return{
                split:0.15,
                accountCashModel:false,
                accountCashSingleModel:false,
                accountCashList:[],
                dataQuery:{
                    fundId:'-1',
                    queryDate: Cookies.get('CurrSysDate')
                },
                accountClassList: [
                    { code:'1', text:'普通账户'},
                    { code:'2',text:'托管账户'},
                    { code:'3', text:'三方存管账户'},
                    { code:'5', text:'上清DVP账户'},
                    { code:'6', text:'中债DVP账户'},
                    { code:'8', text:'期货资金账户'}
                ],
                pageSizeOpts: [25, 50, 75, 100],
                accountCashcolumns: accountCashcolumns,
                accountCash:{
                    fund_id:'',
                    fund_code:'',
                    fund_name:'',
                    custodial_no:'',
                    custodian_name:'',
                    bank_account_name:'',
                    bank_account_no:'',
                    account_class:'',
                    bank_account_id:0,
                    account_cur_balance:'',
                    input_time:'',
                    transfer_type:'',
                    curr_date:''
                },
                numberValidRule: [{test: /^[0-9]*$/, message: '只能输入数字'}]
            }
        },
        watch:{
            accountCashModel:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            },
            accountCashSingleModel:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                }
            }
        },
        computed:{
            queryDate:{
                get:function () {
                    if(this.dataQuery.queryDate)
                        return parseDateString(this.dataQuery.queryDate)
                    else
                        return parseDateString(Cookies.get('CurrSysDate'))
                },
                set:function (newValue) {
                    this.dataQuery.queryDate = parseDateStr(newValue)
                }
            }
        },
        created(){

        },
        mounted() {
            this.reLayout()
        },
        methods: {
            reLayout() {
                this.re_layout("full-h-wrapper-on-relatedcashreport", "inner-on-relatedcashreport", 60);
            },
            fundClick(data) {
                if(data) {
                    this.dataQuery.fundId = data.code;
                    this.$refs.datagridaccountCash.dataChange(1);
                }
            },
            cashModify(){
                let fundId = this.dataQuery.fundId;
                let queryDate = this.dataQuery.queryDate;
                let nowDate = Cookies.get('CurrSysDate');

                if(fundId == "-1"){
                    this.openMask();
                    this.$alert('请选择具体的产品信息','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return;
                }

                if(queryDate < nowDate){
                    this.openMask();
                    this.$alert('不允许调整小于当日的账户余额','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return;
                }

                let selects = this.$refs.datagridaccountCash.$refs.gridContent.getSelection();
                if (selects.length != 0){
                    this.cashModifySingle();
                }
                else {
                    this.cashModifyMul(fundId,queryDate);
                }
            },
            cashModifySingle(){


                this.accountCashList = [];
                this.accountCash = {};
                let selects = this.$refs.datagridaccountCash.$refs.gridContent.getSelection();
                let accountClass = selects[0].account_class;
                let transferType = selects[0].transfer_type;
                if(accountClass != "1" && accountClass != "2"){
                    this.openMask();
                    this.$alert('只允许修改托管户和普通银行账户的账户余额','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                        this.closeMask();
                    }
                    }).then(() => {
                            this.closeMask();
                    })
                    return;
                }

                if(accountClass == "2"){
                    if(transferType =="1" || transferType == "4"){
                        this.openMask();
                        this.$alert('不允许修改划款模式为电子直连的托管户账户余额','错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                            this.closeMask();
                        }
                        }).then(() => {
                                this.closeMask();
                        })
                        return;
                    }
                }
                // debugger;
                this.accountCash = selects[0];
                this.accountCashSingleModel = true;

            },
            cashModifyMul(fundId,queryDate){
                accountCashQuery(fundId,queryDate).then(res => {
                    if (res.data.success == true) {
                        this.accountCashList = res.data.data;

                        if(res.data.data.length == 0) {
                            this.openMask();
                            this.$alert('没有可以调整资金的银行账户','提示',{
                                confirmButtonText: '确定',
                                type: 'warning',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                        }
                        else {
                            this.accountCashModel = true;
                        }
                    } else {
                        this.openMask();
                        this.$alert('资金调整失败','提示',{
                            confirmButtonText: '确定',
                            type: 'warning',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                    }
                }).catch(error => {
                    this.openMask();
                    this.$alert('资金调整发生异常','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                });
            },
            cashQuery(){
                this.$refs.datagridaccountCash.dataChange(1);
            },
            cashExport(){
                this.$refs.datagridaccountCash.exportCsv({filename: '关联方余额报表', original: false});
            },
            ModifySingleCancle(){
                this.accountCashSingleModel = false;
            },
            ModifySingleOK(){
                this.accountCashList[0] = this.accountCash;
                updateAccountCash(this.accountCashList).then(res => {
                    if (res) {
                        if (res.data.success == true) {
                            this.$alert('资金调整成功','成功',{
                                confirmButtonText: '确定',
                                type: 'success',
                                callback:action => {
                                this.accountCashSingleModel = false;
                            }
                            }).then(() => {
                                    this.accountCashSingleModel = false;
                            })
                            this.cashQuery();
                        } else {
                            this.$alert('资金调整失败','错误',{
                                confirmButtonText: '确定',
                                type: 'error',
                                callback:action => {
                                this.accountCashSingleModel = false;
                        }
                        }).then(() => {
                                this.accountCashSingleModel = false;
                        })
                        }
                    }
                })
            },
            ModifyCancle(){
                this.accountCashModel = false;
            },
            ModifyOK(){
                updateAccountCash(this.accountCashList).then(res => {
                    if (res) {
                        if (res.data.success == true) {
                            this.$alert('资金调整成功','成功',{
                                confirmButtonText: '确定',
                                type: 'success',
                                callback:action => {
                                    this.accountCashModel = false;
                                }
                            }).then(() => {
                                this.accountCashModel = false;
                            })
                            this.cashQuery();
                        } else {
                            this.$alert('资金调整失败','错误',{
                                confirmButtonText: '确定',
                                type: 'error',
                                callback:action => {
                                    this.accountCashModel = false;
                                }
                            }).then(() => {
                                this.accountCashModel = false;
                            })
                        }
                    }
                })
            },
            onSplitMoving() {
                this.$refs.datagridaccountCash.handleResize();
            }
        },
        name: "Check",
        components: {HDatagrid, HOtherFundTree}
    }
</script>

<style scoped>

</style>


