<template>
    <div>
        <h-msg-box v-model="msgBoxShowNew" width="300" :mask-closable="false" class="customMsgBox">
            <p slot="header">
                <span v-if="queryType==='1'">余额查询</span>
                <span v-else-if="queryType==='2'">流水查询</span>
            </p>
            <h-form :model="cashFlowQueryInfo" :label-width="80" ref="cashFlowQueryForm">
                <h-form-item required label="产品名称" prop="fund_id">
                    <h-select v-model="cashFlowQueryInfo.fund_id" filterable @on-change="fundIdChange">
                        <h-option v-for="item in fundIdList" :key="item.code" :label="item.text"
                                  :value="item.code"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item required label="银行账号" prop="bank_account_id">
                    <h-select v-model="cashFlowQueryInfo.bank_account_id" @on-change="bankAccNoChange" filterable >
                        <h-option v-for="item in bankAccountList" :key="item.bank_account_id"
                                  :label="item.bank_account_no"
                                  :value="item.bank_account_id"></h-option>
                    </h-select>
                </h-form-item>
                <h-form-item label="账户名称" prop="bank_account_name">
                    <h-input v-model="cashFlowQueryInfo.bank_account_name" :disabled="true"></h-input>
                </h-form-item>
                <h-form-item label="托管人名称" prop="custodial_name">
                    <h-input v-model="cashFlowQueryInfo.custodial_name" :disabled="true"></h-input>
                </h-form-item>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="cancelCashFlowQuery">取消</h-button>
                <h-button type="primary" :loading="loading" @click="cashFlowQuery">确定</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import {getFundInfo, getBankList, showText} from '@/api/utils'
    import {accQueryGetAccInfo, submitAccQuery} from '@/api/reportmgnt/accQuery'
    import Cookies from 'js-cookie'

    export default {
        name: "accCashFlowQueryMsgBox",
        data() {
            return {
                fundIdList: [],
                bankAccountList: [],
                custodianList: [],
                loading: false,
                cashFlowQueryInfo: {
                    fund_id: '',
                    custodial_no: '',
                    custodial_name: '',
                    bank_account_no: '',
                    bank_account_name: '',
                    queryDate: Cookies.get('CurrSysDate'),
                    queryType: ''
                },
            }
        },
        props: {
            msgBoxShow: {
                type: Boolean,
                default: false
            },
            //查询类型（1-余额查询，2-流水查询）
            queryType: {
                type: String,
                default: ""
            },
            //是否包含自营账户
            includeSelfRun: {
                type: Boolean,
                default: false
            }
        },
        watch: {
            queryType: {
                handler: function (val) {
                    this.cashFlowQueryInfo.queryType = val;
                },
                immediate: true
            },
        },
        computed: {
            msgBoxShowNew: {
                get: function () {
                    return this.msgBoxShow;
                },
                set: function (setValue) {
                    //关闭按钮‘*’时，触发设置父组件的msgBoxShow的值
                    if (setValue === false) {
                        this.$emit('onCashFlowClose');
                    }
                }
            },
        },
        activated() {
            getFundInfo('/common/fundNamelist.json?type=' + 'id').then(res => this.fundIdList = res.data);
            getBankList("1").then(res => {
                if (res.data && res.data.length > 0) {
                    this.custodianList = res.data;
                }
            }).catch(error => {
                console.log(error);
            });
        },
        methods: {
            fundIdChange: function (current) {
                this.cashFlowQueryInfo.bank_account_no = '';
                this.cashFlowQueryInfo.bank_account_name = '';
                this.cashFlowQueryInfo.custodial_no = '';
                this.cashFlowQueryInfo.custodial_name = '';

                this.bankAccountList = [];
                accQueryGetAccInfo(current, this.includeSelfRun).then(res => this.bankAccountList = res.data);
            },
            bankAccNoChange(value) {
                this.cashFlowQueryInfo.bank_account_name = '';
                this.cashFlowQueryInfo.custodial_no = '';
                this.cashFlowQueryInfo.custodial_name = '';
                if (value) {
                    let currentAccount = this.bankAccountList.filter(a => a.bank_account_id === value);
                    if (currentAccount && currentAccount.length > 0) {
                        this.cashFlowQueryInfo.bank_account_name = currentAccount[0].bank_account_name;
                        this.cashFlowQueryInfo.custodial_no = currentAccount[0].custodial_no;
                        this.cashFlowQueryInfo.custodial_name = showText(currentAccount[0].custodial_no, this.custodianList);
                        this.cashFlowQueryInfo.bank_account_no = currentAccount[0].bank_account_no;
                    }
                }
            },
            resetCashFlowQueryForm() {
                this.$refs.cashFlowQueryForm.resetFields();
                this.cashFlowQueryInfo.queryDate = Cookies.get('CurrSysDate');
                this.loading = false;
            },
            cancelCashFlowQuery() {
                this.msgBoxShowNew = false;
            },
            cashFlowQuery() {
                let isValidate = true;
                this.$refs.cashFlowQueryForm.validate((validate) => isValidate = isValidate && validate)
                if (!isValidate) {
                    this.$alert('输入项不满足要求，请重新检查', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }
                this.loading = true;
                submitAccQuery(this.cashFlowQueryInfo.fund_id, this.cashFlowQueryInfo.custodial_no,
                    this.cashFlowQueryInfo.bank_account_no, this.cashFlowQueryInfo.queryDate,
                    this.cashFlowQueryInfo.queryType).then(
                    res => {
                        if (res && res.status == 200) {
                            if (res.data.success == true) {
                                this.$alert('操作成功', '成功', {
                                    confirmButtonText: '确定',
                                    type: 'success',
                                    callback: action => {
                                        this.msgBoxShowNew = false;
                                    }
                                }).then(() => {
                                    this.msgBoxShowNew = false;
                                });
                                this.$emit('onOutRefresh');
                            } else {
                                this.$alert(res.data.msg, '错误', {
                                    confirmButtonText: '确定',
                                    type: 'error'
                                });
                                this.msgBoxShowNew = false;
                            }
                        } else {
                            this.$alert('余额查询失败', '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                            this.msgBoxShowNew = false;
                        }
                    }
                );
            },
        },
    }
</script>

<style scoped>

</style>


