<template>
    <div class="full-h" >     <!-- class： CSS表示样式-->
        <h-split v-model="split" min="130" max="600" @on-moving="onSplitMove">
            <!-- 页面左边托管人列表-->
            <div slot="left" span="4" class="full-h">
                <!-- 列设置， 宽度设置成 span：4-->
                <!-- 加载fund-tree ，具体实现都在FundTree.vue中. 写法 <fund-tree></fund-tree> -->
                <!-- 并且在js脚本中需要引入该FundTree.vue 和 components 种需要添加 FundTree. -->
                <!-- :onFundClick="fundClick"  外部需要实现方法fundClick，支持传入参数.-->
                <!-- ref="treeFund"  外部需要实现方法fundClick，支持传入参数.-->
                <fund-tree  :showAll="true"  :onFundClick="fundClick" ref="treeFund"></fund-tree>
            </div>
            <!-- 页面左边托管人列表-->
            <div slot="right" span="20" class="full-h" style="padding-left: 10px" id="full-h-wrapper">
                <!-- 页面右边，查询条件-->
                <h-row>
                    <h-col>
                        <!-- form 表单 -->
                        <el-form style="font-size: 12px;":inline="true" size="mini" :model="queryForm"  class="demo-form-inline" >
                            <el-form-item label="费用标志" >
                                <h-select v-model="queryForm.copBusinFlag" filterable clearable>
                                    <h-option v-for="item in feeBusinList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                                </h-select>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="refreshFeeRule">查询</el-button>
                            </el-form-item>
                        </el-form>
                    </h-col>
                </h-row>
                <!-- 页面右边，查询条件-->
                <h-button-group>
                    <h-button icon="add" @click="feeRuleAdd">新增</h-button>
                    <h-button icon="editor" @click="feeRuleModify">修改</h-button>
                    <h-button icon="delete" @click="feeRuleDelete">删除</h-button>
                </h-button-group>
                <!-- 费用规则数据列表  :height 高度可以设置，:pageSize 每页记录数 ，hasPage ： 表示分页 :columns="feeCurrentColumns"  设置表头信息，-->
                <!-- feeCurrentColumns 存放在data(){} 中，url="/feeruleset/getFeeRule.json" 表示访问后台路径 -->
                <h-datagrid :height="autoHeight"
                            stripe hasPage highlight-row
                            :auto-load="false" :on-row-db-click="feeRuleSetDbClick"
                            :pageSize="20" :pageSizeOpts="pageSizeOpts"
                            :columns="feeCurrentColumns" :bindForm="queryForm"
                            url="/fee/rule/page.json" ref="datagrid">
                </h-datagrid>
            </div>
        </h-split>
        <!-- 费用规则数据列表, v-model="showFeeRuleDialog" 控制表单是否显示，showFeeRuleDialog 在js种设置成false表示不显示，true表示显示-->
        <h-msg-box class="customMsgBox" v-model="showFeeRuleDialog" width="500" :mask-closable=false>
            <p slot="header">
                <!-- vue if else 写法 -->
                <span v-if="actionType=='add'">新增费用规则</span>
                <span v-else-if="actionType=='modify'">修改费用规则</span>
            </p>
            <h-row class="full-h">
                <h-col span="22" style="margin-right: 5px">
                    <!-- form表单-->
                    <h-form :model="feeRuleInfo" :label-width="110" ref="feeRuleForm">
                        <!-- form下的每一个元素，required 表示必填，prop????-->
                        <h-form-item required label="产品" prop="fund_id_list">
                            <div>
                                <h-select style="width: 250px" v-model="feeRuleInfo.fund_id_list" hideMult isString multiple filterable showBottom>
                                    <h-option v-for="item in fundList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                                </h-select>
                                <h-button type="primary" style="float:right;height:30px;margin-top:3px;" @click="choseFundList">选择</h-button>
                            </div>
                        </h-form-item>
                        <h-form-item required label="费用标志" prop="cop_busin_flag">
                            <h-select v-model="feeRuleInfo.cop_busin_flag" filterable>
                                <h-option v-for="item in feeBusinList" :value="item.code" :label="item.text"
                                          :key="item.code"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required label="付费周期" prop="pay_period_type">
                            <h-select v-model="feeRuleInfo.pay_period_type" filterable>
                                <h-option v-for="item in payPeriodTypeList" :key="item.code" :label="item.text"
                                          :value="item.code.toString()"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required label="下一个结转日" prop="next_trade_date">
                            <h-row>
                            <h-col span="15" style="margin-right: 35px">
                                <!-- 日期组件，数据存放为date 类型并非字符串，需要注意 -->
                                <h-date-picker v-model="feeRuleInfo.next_trade_date" format="yyyy-MM-dd"
                                               type="date" placeholder="下一次取估值表科目的日期"
                                               @on-change="tradeDateChange"></h-date-picker>
                            </h-col>
                            <h-col span="5">
                                <h-checkbox v-model="trade_date_rule"  @on-change="tradeDateRuleChange">
                                    <span title="日期选择29，30，31 均表示月底">月底</span>
                                </h-checkbox>
                            </h-col>
                            </h-row>
                        </h-form-item>
                        <h-form-item required label="缴款日类型" prop="pay_date_type">
                            <h-select v-model="feeRuleInfo.pay_date_type" filterable>
                                <h-option v-for="item in payDateTypeList" :key="item.code" :label="item.text" :value="item.code.toString()"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required label="缴款日" prop="require_pay_date">
                            <h-input v-model="feeRuleInfo.require_pay_date" placeholder="每月第几个工作/自然日缴款"
                                     @on-keyup="payDateRule" ref="requirePayDate"></h-input>
                        </h-form-item>
                        <h-form-item required label="估值科目" prop="subject_no_list">
                            <h-input type="text" v-model="feeRuleInfo.subject_no_list" placeholder="支持多科目累加，英文短号分开"></h-input>
                        </h-form-item>
                        <h-form-item required label="提前冻结天数" prop="pre_frozen_days">
                            <h-select v-model="feeRuleInfo.pre_frozen_days" filterable>
                                <h-option v-for="item in preFrozenDaysList" :key="item.code" :label="item.text" :value="item.code.toString()"></h-option>
                            </h-select>
                        </h-form-item>
                    </h-form>
                </h-col>
            </h-row>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="feeRuleCancel">取消</h-button>
                <h-button type="primary" @click="feeRuleBusin">确认</h-button>
            </div>
        </h-msg-box>
        <fund-choose-msg-box
                @saveFundInfo="saveFundInfo"
                ref="feeRuleFundChooseMsgBox">
        </fund-choose-msg-box>
    </div>
</template>

<script>
    /**** import 加载其他页面或者js，加载的页面可以当对象使用，加载的js的方法需要维护到{} 中  ***/
    import FundTree from "@/components/FundTree";               //加载FundTree树，需要引入该FundTree文件
    import HDatagrid from "../../../components/HDatagrid";     //加载HDatagrid列表，需要引入该HDatagrid文件
    import Cookies from 'js-cookie'

    import {
        getBankList,
        getFundInfo,
        getBusinFlagList,
        parseDateStr,
        showText,
        getMonthLastDay,
        parseDateString,
        getDictList
    } from "@/api/utils";

    import {
        addFeeRule,
        modifyFeeRule,
        deleteFeeRule,
        discardFeeRule
    } from "@/api/feemanage/feerulessetjs";
    import FundChooseMsgBox from "../../../components/FundChooseMsgBox";


    export default {           // @unknow不理解作用
        name: "feeruleset",    // @unknow不理解作用
        // data(){return {}} 数据结构存放，对应页面上的数据。
        data() {
            return {
                split: 0.15,
                autoHeight: 0,
                pageSizeOpts: [25, 50, 75, 100],
                showFeeRuleDialog: false, /****false 表示变量：showFeeRuleDialog 为 boolean 类型 ***/
                actionType: '',   /****'' 表示变量：actionType 为  字符或者数字 ***/
                fundList: [],     /****[] 表示变量：fundList 为  数组  ***/
                feeBusinList: [],
                trade_date_rule:false,
                sysDate: Cookies.get('CurrSysDate'),    /****Cookies 加载Cookies中缓存的数据***/
                queryForm: {      /****queryForm: { }  类似Java对象可以通过对象.属性的方式使用***/
                    fundIds: '',
                    copBusinFlag: ''
                },
                bankInfo: {
                    trust_flag : '1'
                },
                feeCurrentColumns: [
                    {
                        type: 'selection',
                        width: 30,
                        align: "left"
                    }, {
                        title: "产品Id",
                        key: "fund_id_list",
                        hiddenCol: true,
                        align: "left",
                        width: 95,
                        ellipsis: true
                    }, {
                        title: "产品代码",
                        key: "fund_code_list",
                        hiddenCol: false,
                        align: "left",
                        width: 135,
                        ellipsis: true
                    },  {
                        title: "产品名称",
                        key: "fund_name_list",
                        align: "left",
                        ellipsis: true
                    }, {
                        title: "费用标志",
                        key: "cop_busin_flag",
                        align: "left",
                        width: 95,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var ccop_busin_flag_name = showText(rowData.cop_busin_flag, this.feeBusinList);
                            return h("span", {}, ccop_busin_flag_name);
                        }
                    }, {
                        title: "付费周期",
                        key: "pay_period_type",
                        hiddenCol: false,
                        align: "left",
                        width: 85,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var pay_period_type_name = showText(rowData.pay_period_type, this.payPeriodTypeList);
                            return h("span", {}, pay_period_type_name);
                        }
                    }, {
                        title: "缴款日类型",
                        key: "pay_date_type",
                        align: "left",
                        width: 95,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var pay_date_type_name = showText(rowData.pay_date_type, this.payDateTypeList);
                            return h("span", {}, pay_date_type_name);
                        }
                    }, {
                        title: "缴款日",
                        key: "require_pay_date",
                        align: "left",
                        width: 85,
                        ellipsis: true
                    }, {
                        title: "估值科目",
                        key: "subject_no_list",
                        align: "left",
                        ellipsis: true
                    }, {
                        title: "上一个结转日",
                        key: "pre_trade_date",
                        align: "left",
                        width: 95,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var date = parseDateString(rowData.pre_trade_date, null);
                            return h("span", {}, date);
                        }
                    }, {
                        title: "下一个结转日",
                        key: "next_trade_date",
                        align: "left",
                        width: 95,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var date = parseDateString(rowData.next_trade_date, null);
                            return h("span", {}, date);
                        }
                    }, {
                        title: "提前冻结天数",
                        key: "pre_frozen_days",
                        align: "left",
                        width: 95,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var preFrozenDaysName = showText(rowData.pre_frozen_days, this.preFrozenDaysList);
                            return h("span", {}, preFrozenDaysName);
                        }
                    }, {
                        title: "状态",
                        key: "fee_rule_flag",
                        hiddenCol: true,
                        align: "left",
                        width: 55,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var fee_rule_flag_name = showText(rowData.fee_rule_flag, this.feeRuleFlagList);
                            return h("span", {}, fee_rule_flag_name);
                        }
                    }, {
                        title: "序列",
                        key: "serial_no",
                        hiddenCol: true,
                        align: "left",
                        width: 95,
                        ellipsis: true
                    }, {
                        title: "下一个结转日规则",
                        key: "trade_date_rule_value",
                        hiddenCol: true,
                        align: "left",
                        width: 95,
                        ellipsis: true
                    }

                ],
                feeRuleInfo: {
                    fund_id_list: '',
                    pay_period_type: '',
                    pay_date_type: '',
                    require_pay_date: '',
                    subject_no_list: '',
                    next_trade_date: '',
                    pre_frozen_days: '',
                    serial_no: '',
                    trade_date_rule_value: ''
                },
                payPeriodTypeList: [],
                payDateTypeList: [],
                feeRuleFlagList :[
                    {
                        code: '0',
                        text: '正常'
                    }, {
                        code: '1',
                        text: '已作废'
                    }
                ],
                preFrozenDaysList :[
                    {
                        code: '0',
                        text: '不冻结'
                    }, {
                        code: '1',
                        text: 'T-1日'
                    }
                    , {
                        code: '2',
                        text: 'T-2日'
                    }
                ]
            }
        },
        watch:{
            showFeeRuleDialog:function (show) {
                if(show){
                    this.openMask()
                }else{
                    this.closeMask()
                }
            }
        },
        activated(){
            getBusinFlagList(22).then(res => this.feeBusinList = res.data);
            getFundInfo('/common/fundName.json', null).then(res=>this.fundList=res.data);
        },
        created() {
            getDictList(91182).then(res => this.payDateTypeList = res.data);
            getDictList(60074).then(res => this.payPeriodTypeList = res.data);
        },
        mounted() {  //页面渲染后需要执行的内容
            this.re_layout();
        },
        methods: {  //需要执行函数存放在该处
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 100;
            },
            fundClick(data) {
                if (data) {
                    this.queryForm.fundIds = data.code;
                    if (data.code.indexOf(",") < 0) {
                        this.refreshFeeRule();
                    } else {
                        this.queryForm.fundIds = "";
                        this.refreshFeeRule();
                    }
                }
            },
            feeRuleAdd() {
                //执行清空校验
                this.$refs.feeRuleForm.resetFields();
                trade_date_rule: false,
                this.feeRuleInfo = {
                    serial_no: '',
                    fund_id_list: '',
                    cop_busin_flag: '',
                    pay_period_type: '',
                    pay_date_type: '2',
                    require_pay_date: '',
                    subject_no_list: '',
                    next_trade_date: '',
                    pre_frozen_days: '1',
                    trade_date_rule_value: ''
                }
                this.showFeeRuleDialog = true;
                this.actionType = "add";
            },
            feeRuleModify() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();   //获取勾选的数据
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选中一条记录', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                this.feeRuleSetDbClick(selects[0]);
            },
            feeRuleSetDbClick(data){
                this.showFeeRuleDialog = true;
                this.actionType = "modify";
                this.feeRuleInfo = data;
                if (this.feeRuleInfo.trade_date_rule_value == '1') {
                    this.trade_date_rule = true;
                }
                this.feeRuleInfo.fund_id_list = this.feeRuleInfo.fund_id_list.toString();
                this.feeRuleInfo.cop_busin_flag = this.feeRuleInfo.cop_busin_flag.toString();  //@unknow不理解作用
                this.feeRuleInfo.next_trade_date = this.feeRuleInfo.next_trade_date.toString();  //@unknow不理解作用
            },
            feeRuleDelete() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选中一条记录', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                var serial_nos = '';
                for (var i = 0; i < selects.length; i++) {
                    serial_nos += selects[i].serial_no + ',';
                }
                serial_nos = serial_nos.substring(0, serial_nos.length - 1);
                this.openMask();
                this.$confirm('确认删除该记录吗？', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteFeeRule(serial_nos).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '成功', {
                                confirmButtonText: '确定',
                                type: "success",
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                            this.refreshFeeRule();
                        }else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                        }
                    });
                });
            },
            feeRuleDiscard(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.$alert('请先选中一条记录', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return
                }
                var serial_nos = '';
                for (var i = 0; i < selects.length; i++) {
                    serial_nos += selects[i].serial_no + ',';
                }
                serial_nos = serial_nos.substring(0, serial_nos.length - 1);
                this.$confirm('确认作废该记录吗？', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'

                }).then(() => {
                    discardFeeRule(serial_nos).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '成功', {
                                confirmButtonText: '确定',
                                type: "success"
                            });
                            this.refreshFeeRule();
                        }else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    });
                });
            },
            refreshFeeRule() {
                //执行查询
                this.$refs.datagrid.dataChange();
            },
            payDateRule() {
                var resultFlag = true;
                if (this.feeRuleInfo.pay_date_type == "2") {
                    if (!(this.feeRuleInfo.require_pay_date >= 1 && this.feeRuleInfo.require_pay_date <= 23)) {
                        this.$refs.requirePayDate.blur();
                        resultFlag = false;
                        this.$alert('输入缴款日有误，请输入1到23之间的数字',"错误", {
                            confirmButtonText: '确定',
                            type: 'error'
                        })
                    }
                } else if (this.feeRuleInfo.pay_date_type == "1") {
                    if (!(this.feeRuleInfo.require_pay_date >= 1 && this.feeRuleInfo.require_pay_date <= 28)) {
                        this.$refs.requirePayDate.blur();
                        resultFlag = false;
                        this.$alert('输入缴款日有误，请输入1到28之间的数字',"错误", {
                            confirmButtonText: '确定',
                            type: 'error'
                        })
                    }
                } else {
                    this.$refs.requirePayDate.blur();
                    resultFlag = false;
                    this.$alert('请先选择缴款日类型','错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    this.feeRuleInfo.require_pay_date = '';
                }
                return resultFlag;
            },
            feeRuleCancel() {
                this.showFeeRuleDialog = false;
            },
            feeRuleBusin() {
                let isValid = true;
                if(!this.payDateRule()){
                    return ;
                }
                this.$refs.feeRuleForm.validate((validate) => isValid = isValid && validate);
                if (!isValid) {
                    this.$alert('输入项不满足要求，请重新检查', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    })
                    return
                }
                this.feeRuleInfo.next_trade_date = parseDateStr(this.feeRuleInfo.next_trade_date, "yyyyMMdd");
                if (this.actionType == 'add') {
                    addFeeRule(this.feeRuleInfo).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '成功', {
                                confirmButtonText: '确定',
                                type: "success",
                                callback:action => {
                                    this.showFeeRuleDialog = false;
                                }
                            }).then(() => {
                                this.showFeeRuleDialog = false;
                            })
                            this.refreshFeeRule();
                        }else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            })
                        }
                    });
                }
                if (this.actionType == 'modify') {
                    modifyFeeRule(this.feeRuleInfo).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '成功', {
                                confirmButtonText: '确定',
                                type: "success",
                                callback:action => {
                                    this.showFeeRuleDialog = false;
                                }
                            }).then(() => {
                                this.showFeeRuleDialog = false;
                            })
                            this.refreshFeeRule();
                        }else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            })
                        }
                    });
                }

            },
            tradeDateRuleChange() {
                if (this.trade_date_rule) {
                    var date = this.sysDate;
                    if (this.feeRuleInfo.next_trade_date && this.feeRuleInfo.next_trade_date != '') {
                        date = parseDateStr(this.feeRuleInfo.next_trade_date);
                    }
                    this.feeRuleInfo.next_trade_date = getMonthLastDay(date);
                    this.feeRuleInfo.trade_date_rule_value = '1';
                } else {
                    this.feeRuleInfo.next_trade_date = '';
                    this.feeRuleInfo.trade_date_rule_value = '';
                }
            },
            tradeDateChange() {
                var date = parseDateStr(this.feeRuleInfo.next_trade_date);
                if (parseInt(date.substring(6, 8)) <= 28 || date == '' || !date) {
                    this.feeRuleInfo.trade_date_rule_value = '';
                    this.trade_date_rule = false;
                } else {
                    this.trade_date_rule = true;
                    this.feeRuleInfo.trade_date_rule_value = '1';
                    this.feeRuleInfo.next_trade_date = getMonthLastDay(date);
                }
            },
            choseFundList(){
                let fullWrapperHieght = document.getElementById("full-h-wrapper").offsetHeight;
                this.$refs.feeRuleFundChooseMsgBox.showMsgBox(this.feeRuleInfo.fund_id_list, fullWrapperHieght, '1');
            },
            saveFundInfo(data){
                this.feeRuleInfo.fund_id_list = data;
            },
            onSplitMove(){
                this.$refs.datagrid.handleResize();
            }
        },
        components: {
            FundChooseMsgBox, //引入自定义组件
            FundTree,
            HDatagrid
        }
    }
</script>





