          }, {
            code: '8',
            text: '期货资金账户'
          }
        ],
        uploadUrl: window.LOCAL_CONFIG.API_HOME + '/file/upload.json',
        uploadData: {
          fileType: '1',
          fundId: ''
        },
        fileList: [],
        newFundInfo: {
          fundId: '',
          fund_name: '',
          fund_code: '',
          actual_code: '',
          fund_mgr_operator: '',
          fund_status: '1',
          fund_trust_bank: '',
          fund_type: '',
          establish_date: '',
          expire_date: '',
          interbank_id: '',
          paper_no: '',
          stockholder_id: '',
          stockholder_id_sz: '',
          object_code: '',
          sh_ta_account: '',
          sz_ta_account: '',
          transfer_flag: '1',
          file_name: '',
          fund_mail: '',
          fund_mail_pwd: '',
          fund_accountant: '',
          ta_settle_type: '',
          fund_settle_type: '',
          trust_flag: '',
          accountList: [{
            account_class: '2',
            bank_account_status: '1'
          }],
          sub_fund_code_list: ''
        },
        keys: [],
        item: {

        },
        taMode:'1',
        stampList: []
      }
    },
    computed: {
      ...mapState({
        fundTypeList: state => state.dict.dict_610087,
        fundSettleTypes: state => state.dict.dict_90531,
        taSettleTypes: state => state.dict.dict_90530,
        dataSourceList: state => state.dict.dict_90528,
        accountTypeList: state => state.dict.dict_60058,
        accountStatusList: state => state.dict.dict_200008,
        currencyNoList: state => state.dict.dict_610406,
        bankFuturesTypeList: state => state.dict.dict_90532,
        taMatchTypeList: state => state.dict.dict_91386,
        operatorList: state => state.dict.operatorList,
        bankList: state => state.dict.bankList,
        trustBankList: state => state.dict.trustBankList,
        agencyList: state => state.dict.agencyList,
        brokerList: state => state.dict.brokerList,
        cityTree: state => state.dict.cityTree
      }),
      title() {
        let title = ''
        let actionType = this.actionType
        if (actionType === 'add') {
          title = '新增产品'
        }
        if (actionType === 'edit') {
          title = '修改产品'
        }
        if (actionType === 'detail') {
          title = '产品详情'
        }
        return title
      },
      isDisabled() {
        return this.actionType === 'detail'
      }
    },
    watch: {
      value: {
        handler: function (val) {
          this.visible = val
        },
        immediate: true
      },
      visible: {
        handler: function (val) {
          this.$emit('input', val)
          if (val) {
            this.showFundInfo()
          }else{
            this.showCustodialAccount = true;
            this.$refs.newFundForm.resetFields();
            if(this.actionType == 'add'){
              this.$refs.newFundAcctForm[0].resetFields();
            }
          }
        }
      },
      actionType: {
        handler: function (val) {
          if (val === 'edit') {

          }
        }
      },
      fundInfo: {
        handler: function (val) {
          if (val) {
            this.showFundInfo()
          }
        }
      }
    },
    created() {

    },
    mounted() {
      getKeys().then(res => this.keys = res.data);
    },
    activated() {
      getSysParamValue(10058).then(res => this.taMode = res.data.data);
      getStampList(3).then(res => {
        if(res.data && res.data.length >0){
          this.stampList = res.data;
        }
      })
    },
    methods: {
      showFundInfo() {
        if (this.actionType === 'edit') {
          this.newFundInfo = deepCopy(this.fundInfo)
          if (this.newFundInfo.fund_trust_bank === null) {
            this.newFundInfo.fund_trust_bank = ''
          }
          if(this.newFundInfo.fund_mgr_operator === null){
            this.newFundInfo.fund_mgr_operator = ''
          }
          if(this.newFundInfo.fund_accountant === null){
            this.newFundInfo.fund_accountant = ''
          }
          this.newFundInfo.accountList = [];

          this.newFundInfo.establish_date_str = parseDateString(this.newFundInfo.establish_date);
          this.newFundInfo.expire_date_str = parseDateString(this.newFundInfo.expire_date);
          if (this.newFundInfo.fund_mail_pwd) {
            this.newFundInfo.fund_mail_pwd = decrypt(this.newFundInfo.fund_mail_pwd, this.keys);
          }
          if (this.newFundInfo.fund_mail) {
            this.newFundInfo.fund_mail = this.newFundInfo.fund_mail.trim();
          }
          let item;
          for (item in this.newFundInfo) {
            if (item && (typeof this.newFundInfo[item] == 'string')) {
              this.newFundInfo[item] = this.newFundInfo[item].trim();
            }
          }
        }
        if (this.actionType === 'add') {
          this.resetInputFundInfo();
        }
      },
      newAccount() {
        this.newFundInfo.accountList.push({
          account_class: '2',
          bank_account_status: '1'
        })
      },
      custodialNoChanged(value){
        getBicCodeByCustodialNo(value).then(res => {
          if (res.data.data) {
            let bic_code = res.data.data.custodialorg.bic_code;
            this.newFundInfo.accountList.forEach(function (account , index, array){
              if (account.custodial_no != null && account.custodial_no == value) {
                let tempaccount = account;
                tempaccount.bic_code = bic_code;
                array.splice(index , 1 ,tempaccount)
              }
            })
          }
        })
      },
      codechange(value){
        this.newFundInfo.accountList.forEach(function (account , index, array){
          console.log(account)
          })
        },
      acctFundClassChanged(item){
        if(item.account_class == '5'){
          item.bic_code = 'SHCLEARH';
        }else if(item.account_class == '6'){
          item.bic_code = 'CHINABOND';
        }else{
          item.bic_code = '';
        }
      },
      removeAccount(index) {
        this.newFundInfo.accountList = this.newFundInfo.accountList.filter((x, i) => index !== i)
      },
      maintainDVP() {
        if (this.newFundInfo.interbank_id != '' && this.actionType === 'add') {
          var arr = this.newFundInfo.accountList;
          var result = false;
          for (var i = 0; i < arr.length; i++) {
            if (this.newFundInfo.accountList[i].account_class.indexOf("5") != -1 || this.newFundInfo.accountList[i].account_class.indexOf("6") != -1) {
              result = true;
            }
          }
          if (!result) {
            this.$alert('需要新增DVP账户', '提示', {
              confirmButtonText: '确定',
              type: 'info'
            });
          }
        }
      },
      handleCancel() {
        this.visible = false
      },
      handleOk() {
        let isValid = true
        this.$refs.newFundForm.validate((valid) => { isValid = isValid && valid })
        this.$refs.newFundAcctForm.forEach(acctForm => {
          acctForm.validate((valid) => { isValid = isValid && valid })
        })

        if (!isValid) {
          this.$alert('输入项不满足要求，请重新检查!', '错误', {
            confirmButtonText: '确定',
            type: 'error'
          })
        } else {
          this.newFundInfo.accountList.forEach(x => {
            if (x.provinceCity && x.provinceCity.length > 1) {
              x.province_code = x.provinceCity[0]
              x.city_no = x.provinceCity[1]
            }
          })
          this.newFundInfo.fund_mail_pwd = encrypt(this.newFundInfo.fund_mail_pwd, this.keys)
          this.newFundInfo.establish_date = parseDateStr(this.newFundInfo.establish_date_str)
          this.newFundInfo.expire_date = parseDateStr(this.newFundInfo.expire_date_str)
          if (parseInt(this.newFundInfo.expire_date) < parseInt(this.newFundInfo.establish_date)) {
            this.$alert('结束日期不能小于成立日期', '错误', {
              confirmButtonText: '确定',
              type: 'error'
            })
            return
          }

          //console.log('this.newFundInfo', this.newFundInfo)

          saveFundInfo(this.newFundInfo).then(res => {
            if (res.data && res.data.code === '0') {
              this.$alert(res.data.msg, '成功', {
                confirmButtonText: '确定',
                type: 'success'
              }).then(() => {
                this.visible = false;
                if (this.newFundInfo.fundId && this.actionType == 'edit') {
                  this.$emit('refreshFund', {
                    code: this.newFundInfo.fundId
                  })
                } else {
                  this.$emit('fundAdded')
                }
              })
            } else if (res.data && res.data.code === '1120010') {
              // 没有修改项时，直接关闭窗口
              // this.showFundDialog = false;
              this.visible = false
            } else {
              this.$alert(res.data.msg, '错误', {
                confirmButtonText: '确定',
                type: 'error'
              }).then(() => {
                this.newFundInfo.fund_mail_pwd = decrypt(this.newFundInfo.fund_mail_pwd, this.keys);
              });
            }
          })
        }
      },
      resetInputFundInfo() {
        this.newFundInfo = {
          fund_name: '',
          fund_code: '',
          actual_code: '',
          fund_mgr_operator: '',
          fund_status: '1',
          fund_trust_bank: '',
          fund_type: '',
          establish_date: '',
          expire_date: '',
          interbank_id: '',
          paper_no: '',
          stockholder_id: '',
          stockholder_id_sz: '',
          object_code: '',
          sh_ta_account: '',
          sz_ta_account: '',
          transfer_flag: '1',
          file_name: '',
          fund_mail: '',
          fund_mail_pwd: '',
          fund_accountant: '',
          ta_settle_type: '',
          fund_settle_type: '',
          trust_flag: '',
          accountList: [{
            account_class: '2',
            bank_account_status: '1'
          }],
          ta_match_type: '1'
        }
      },
      fileUploaded(response, file, fileList) {
        if(response.code == '0'){
          while (fileList.length > 1) {
            fileList.shift();
          }
          this.newFundInfo.file_id = response.data;
          this.newFundInfo.file_name = file.name;
        }else{
          this.$alert(response.msg, '错误',{
            confirmButtonText:'确定',
            type: 'error'
          });
          fileList.splice(fileList.length-1,1);
        }
        //if (this.showFundDialog) {

        //} else {
        // this.fundInfo.file_id = response.data;
        // this.fundInfo.file_name = file.name
        //}
      },
      fundSettleTypeChange: function (currentValue) {
        if (currentValue == '1' || currentValue == '4') {
          this.showCustodialAccount = false;
        } else {
          this.showCustodialAccount = true;
        }
      }
    },
    components: {

    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  .cmp-manager-fund-dialog {
  }
  .customMsgBox {
    .h-typefield, .h-typefield input {
      height: 28px;
      line-height: 28px;
      border-radius: 2px;
    }
  }
</style>



