import fetch from "@/api/httpFetch";

export function addApproveRule(params) {
    return fetch.post("/systemmgnt/approveRule/approveRuleAdd.json",params,{
        headers:{
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest(req){
            return JSON.stringify(req)
        }
    })
}

export function modifyApproveRule(params) {
    return fetch.post('/systemmgnt/approveRule/approveRuleModify.json',params,{
        headers:{
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest(req){
            return JSON.stringify(req)
        }
    })
}

export function delApproveRule(fundId,businClass,ruleType) {
    return fetch.post('/systemmgnt/approveRule/approveRuleDel.json',{fundId: fundId, businClass: businClass, ruleType: ruleType})
}


