<template>
  <div class="c-selection-tip">
    <div class="content" v-if="sumCNY !== 0 && sumUSD !== 0">
      <h-icon name="ios-information-outl"></h-icon>已选择: {{selections.length}} 项，已选金额：<span :style="color(sumCNY)">{{parseNumber(sumCNY)}}</span>元，<span :style="color(sumUSD)">{{parseNumber(sumUSD)}}</span>美元
    </div>
    <div class="content"  v-else-if="sumCNY === 0 && sumUSD !== 0">
      <h-icon name="ios-information-outl"></h-icon>已选择: {{selections.length}} 项，已选金额：<span :style="color(sumUSD)">{{parseNumber(sumUSD)}}</span>美元
    </div>
    <div class="content"  v-else>
      <h-icon name="ios-information-outl"></h-icon>已选择: {{selections.length}} 项，已选金额：<span :style="color(sumCNY)">{{parseNumber(sumCNY)}}</span>元
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import numberFormatter from '@utils/number'

export default {
  props: {
    selections: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      sum: 0,
      sumCNY: 0,
      sumUSD: 0
    }
  },
  watch: {
    selections: {
      handler: function (val) {
        let sumCNY = 0
        let sumUSD = 0
        for (let item of val) {
          // 入款 - 出款
          // cash_direction 1是入款，2是出款
          // settle_balance 金额

          let money = parseFloat(item.settle_balance || 0)
          if (item.cash_direction === '1') {
            if (item.currency_no === 'CNY') {
              sumCNY += money
            }
            if (item.currency_no === 'USD') {
              sumUSD += money
            }
          } else {
            if (item.currency_no === 'CNY') {
              sumCNY -= money
            }
            if (item.currency_no === 'USD') {
              sumUSD -= money
            }
          }
        }
        this.sumCNY = sumCNY
        this.sumUSD = sumUSD
      },
      immediate: true
    }
  },
  computed: {
    direction() {
      if (this.sum > 0) {
        return '入款'
      } else if (this.sum < 0) {
        return '出款'
      } else {
        return '无'
      }
    }
  },
  created() {

  },
  mounted() {

  },
  activated() {

  },
  methods: {
    color(val) {
      if (val < 0) {
        return { color: '#ff8a80'}
      }
      // if (val > 0) {
      //   return { color: 'green' }
      // }
      return {}
    },
    parseNumber(val) {
      return numberFormatter.formatNumber(val + '', 2, ',')
    }
  },
  components: {

  }
}
</script>

<style lang="less" rel="stylesheet/less">
.c-selection-tip {
  width: 100%;
  min-height: 30px;
  flex-shrink: 0;
  .content {
    display: flex;
    align-items: center;
    height: 30px;
    line-height: 30px;
    margin-top: 3px;
    padding-left: 10px;
    border-top: 1px solid #f3cf92;
    border-bottom: 1px solid #f3cf92;
    background-color: #fefaf4;
    .h-icon-ios-information-outl {
      margin-right: 5px;
      font-size: 20px;
      color: #f3cf92;
    }
  }
}
.s-theme-black .c-selection-tip .content {
  color: white;
  background-color: #826436;
  border-top-color: #F8B84E;
  border-bottom-color: #F8B84E;
}
</style>



