<!--转换机任务-->
<template>
    <div class="full-h" id="full-h-wrapper-on-newtrantask">
        <h-row id="inner-on-newtrantask">
            <h-col>
                <el-form :model="queryForm" :inline="true" size="mini" class="demo-form-inline">
                    <el-form-item label="操作员" prop="operatorNo">
                        <el-select v-model="queryForm.operatorNo" filterable clearable>
                            <el-option v-for="item in operatorNoList" :key="item.code" :label="item.text"
                                       :value="parseInt(item.code)">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="任务名称" prop="taskName">
                        <el-input maxlength=64 v-model="queryForm.taskName">
                        </el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="refresh">查询</el-button>
                    </el-form-item>
                </el-form>
            </h-col>
        </h-row>
        <h-button-group>
            <h-button icon="editor" @click="modifyTask">修改</h-button>
            <h-button icon="add" @click="copyTask">复制</h-button>
            <h-button icon="setup" @click="configTask">配置任务</h-button>
        </h-button-group>
        <h-datagrid stripe
                    highlight-row
                    hasPage
                    :height="autoHeight"
                    :pageSize="100"
                    :pageSizeOpts="pageSizeOpts"
                    :columns="columns"
                    :bindForm="queryForm"
                    :on-row-db-click="dbClickTask"
                    url="/systemmgnt/newtranTask/pageNewtranTaskList.json" ref="datagrid">
        </h-datagrid>

        <h-msg-box class="customMsgBox" v-model="showTaskDialog" width="580" :mask-closable="false">
            <p slot="header">
                <span v-if="actionType === 'modify'">修改任务</span>
                <span v-else-if="actionType === 'copy'">复制任务</span>
                <span v-else>操作任务</span>
            </p>
            <h-form :model="taskInfo" :label-width="90" cols="2" ref="taskForm">
                <h-form-item required label="任务名称" prop="trantask_name">
                    <h-input :maxlength=64 v-model="taskInfo.trantask_name"></h-input>
                </h-form-item>
                <h-form-item required label="操作员" prop="operator_no">
                    <h-select v-model="taskInfo.operator_no" filterable :clearable="false">
                        <h-option v-for="item in operatorNoList" :key="item.code" :label="item.text"
                                  :value="parseInt(item.code)">
                        </h-option>
                    </h-select>
                </h-form-item>
                <h-form-item required label="开始时间" prop="begin_time">
                    <h-time-picker v-model="taskInfo.begin_time" format="HH:mm:ss" :clearable="false"></h-time-picker>
                </h-form-item>
                <h-form-item required label="结束时间" prop="end_time">
                    <h-time-picker v-model="taskInfo.end_time" format="HH:mm:ss" :clearable="false"
                                   :minTime="taskInfo.begin_time"></h-time-picker>
                </h-form-item>
                <h-form-item required label="启用状态" prop="use_flag">
                    <h-select v-model="taskInfo.use_flag" filterable :clearable="false">
                        <h-option v-for="item in useFlagList" :key="item.code" :label="item.text"
                                  :value="item.code">
                        </h-option>
                    </h-select>
                </h-form-item>
            </h-form>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 10px" @click="cancelTask">取消</h-button>
                <h-button type="primary" @click="saveTask">确定</h-button>
            </div>
        </h-msg-box>

        <h-msg-box class="customMsgBox" v-model="configTaskDialog" width="240" :mask-closable="false">
            <p slot="header">
                <span>配置任务</span>
            </p>
            <div v-for="item in configDataList" style="height:28px;">
                <h-checkbox v-model="item.config_value" true-value="1" false-value="0">{{item.config_key1}}</h-checkbox>
            </div>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 10px" @click="cancelConfigTask">取消</h-button>
                <h-button type="primary" @click="saveConfigTask">确定</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import HDatagrid from '@/components/HDatagrid'
    import {
        operatorList,
        parseDateString,
        showText
    } from '@/api/utils'
    import {
        modifyNewtranTask,
        copyNewtranTask,
        findTaskConfigList,
        batchUpdateTaskConfig
    } from '@/api/systemmgnt/newtranTask'

    import render from "@/mixins/render"

    export default {
        name: "newTrantask",
        mixins: [render],
        components: {
            HDatagrid
        },
        data() {
            return {
                pageSizeOpts: [25, 50, 75, 100],
                actionType: '',
                operatorNoList: [],
                useFlagList: [
                    {code: "1", text: "启用"},
                    {code: "0", text: "不启用"},
                ],
                runOnceList: [
                    {code: "0", text: "是"},
                    {code: "1", text: "否"},
                ],
                columns: [
                    {
                        title: '序号',
                        type: 'index',
                        width: 120,
                        align: 'right'
                    }, {
                        title: '任务编号',
                        key: 'trantask_id',
                        align: 'right',
                        ellipsis: true,
                        width: 120,
                    }, {
                        title: '任务名称',
                        key: 'trantask_name',
                        align: 'left',
                        ellipsis: true
                    }, {
                        title: '操作员',
                        key: 'operator_no',
                        align: 'left',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let operatorName = showText(rowData.operator_no, this.operatorNoList);
                            return h("span", {}, operatorName);
                        }
                    }, {
                        title: '启用状态',
                        key: 'use_flag',
                        align: 'left',
                        ellipsis: true,
                        width: 90,
                        render: (h, params) => {
                            const rowData = params.row;
                            let userFlagName = showText(rowData.use_flag, this.useFlagList);
                            return h("span", {}, userFlagName);
                        }
                    }, {
                        title: '开始时间',
                        key: 'begin_time',
                        align: 'left',
                        ellipsis: true,
                        width: 90,
                        render: (h, params) => {
                            const rowData = params.row;
                            let beginTime = parseDateString(null, rowData.begin_time);
                            return h("span", {}, beginTime);
                        }
                    }, {
                        title: '结束时间',
                        key: 'end_time',
                        align: 'left',
                        ellipsis: true,
                        width: 90,
                        render: (h, params) => {
                            const rowData = params.row;
                            let endTime = parseDateString(null, rowData.end_time);
                            return h("span", {}, endTime);
                        }
                    }, {
                        title: '持续性任务',
                        key: 'run_once',
                        align: 'left',
                        ellipsis: true,
                        width: 100,
                        render: (h, params) => {
                            const rowData = params.row;
                            let runOnceName = showText(rowData.run_once, this.runOnceList);
                            return h("span", {}, runOnceName);
                        }
                    }
                ],
                queryForm: {
                    operatorNo: '',
                    taskName: ''
                },
                showTaskDialog: false,
                taskInfo: {
                    operator_no: '',
                    trantask_name: '',
                    trantask_id: '',
                    module_name: '',
                    class_name: '',
                    begin_time: '',
                    end_time: '',
                    sub_type: '',
                    trantask_time_scales: '',
                    lastexec_time: '',
                    lastexec_date: '',
                    priority_level: '',
                    use_flag: '',
                    init_function: '',
                    run_function: '',
                    thread_count: '',
                    run_once: ''
                },
                configTaskDialog: false,
                configDataList: [],
                /*configDataList: [{
                    title: "上清所数据文件导入",
                    key: "sqsImport",
                    value: "1"
                }, {
                    title: "中债登数据文件导入",
                    key: "zzdImport",
                    value: false
                }]*/
            }
        },
        activated() {
            this.refresh();
            operatorList().then(res => this.operatorNoList = res.data);
        },
        mounted() {
            this.reLayout();
        },
        watch: {
            showTaskDialog: function (show) {
                if (show) {
                    this.openMask();
                } else {
                    //重置表单
                    this.$refs.taskForm.resetFields();
                    this.closeMask();
                }
            },
        },
        methods: {
            refresh() {
                this.$refs.datagrid.dataChange(1);
            },
            reLayout() {
                this.re_layout("full-h-wrapper-on-newtrantask", "inner-on-newtrantask", 50);
            },
            modifyTask() {
                let select = this.getSelection();
                if (!select) {
                    return;
                }
                this.dbClickTask(select);
            },
            dbClickTask(data) {
                this.taskInfo = data;
                this.taskInfo.begin_time = parseDateString(null, data.begin_time);
                this.taskInfo.end_time = parseDateString(null, data.end_time);

                this.actionType = 'modify';
                this.showTaskDialog = true;
            },
            copyTask() {
                let select = this.getSelection();
                if (!select) {
                    return;
                }

                this.taskInfo = select;
                this.taskInfo.begin_time = parseDateString(null, this.taskInfo.begin_time);
                this.taskInfo.end_time = parseDateString(null, this.taskInfo.end_time);
                this.taskInfo.trantask_id = null;//设置任务id为空，后台新增任务

                this.actionType = 'copy';
                this.showTaskDialog = true;
            },
            configTask() {
                let select = this.getSelection();
                if (!select) {
                    return;
                }

                //1、判断是否可以配置任务
                if (!((select.module_name === "DataImportTask.dll" && select.class_name === "DataImport4InterBankSettleInstr") ||
                    (select.module_name === "DataImportTask.dll" && select.class_name === "DataImport4InterBankSettleCont") ||
                    (select.module_name === "DataImportTask.dll" && select.class_name === "DataImport4Apply"))) {
                    this.openMask();
                    this.$alert('当前任务没有提供配置参数', '提示', {
                        confirmButtonText: '确定',
                        type: 'warning',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return;
                }

                //2、获取已配置的任务
                findTaskConfigList(select.module_name, select.class_name).then(res => {
                    if (res.data && res.data.success) {
                        this.configDataList = res.data.data;
                        this.configTaskDialog = true;
                    } else {
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }
                }).catch(error => {
                    this.openMask();
                    this.$alert(error, '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                });
            },
            getSelection() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length === 0) {
                    this.openMask();
                    this.$alert('请选择一条记录', '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                    return null;
                }
                return selects[0];
            },
            saveTask() {
                let isValidate = true;
                this.$refs.taskForm.validate((validate) => isValidate = isValidate && validate)
                if (!isValidate) {
                    this.$alert('输入项不满足要求，请重新检查！', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }

                //结束时间必须大于开始时间
                if (this.taskInfo.end_time <= this.taskInfo.begin_time) {
                    this.$alert('结束时间必须大于开始时间', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }

                //设置请求对象
                let taskInfoDTO = {};
                Object.assign(taskInfoDTO, this.taskInfo);
                taskInfoDTO.begin_time = taskInfoDTO.begin_time.replace(/:/g, "");
                taskInfoDTO.end_time = taskInfoDTO.end_time.replace(/:/g, "");

                if (this.actionType === 'modify') {
                    modifyNewtranTask(taskInfoDTO).then(res => {
                        if (res.data && res.data.success) {
                            this.$alert(res.data.msg, '成功', {
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                }
                            }).then(() => {
                            });
                            this.showTaskDialog = false;
                            this.refresh();
                        } else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    }).catch(error => {
                        this.openMask();
                        this.$alert(error, '错误', {
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                    });
                } else if (this.actionType === 'copy') {
                    copyNewtranTask(taskInfoDTO).then(res => {
                        if (res.data && res.data.success) {
                            this.$alert(res.data.msg, '成功', {
                                confirmButtonText: '确定',
                                type: 'success',
                                callback: action => {
                                }
                            }).then(() => {
                            });
                            this.showTaskDialog = false;
                            this.refresh();
                        } else {
                            this.$alert(res.data.msg, '错误', {
                                confirmButtonText: '确定',
                                type: 'error'
                            });
                        }
                    }).catch(error => {
                        this.openMask();
                        this.$alert(error, '错误', {
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        });
                    });
                }
            },
            cancelTask() {
                this.showTaskDialog = false;
            },
            saveConfigTask() {
                if (!this.configDataList || this.configDataList.length <= 0) {
                    this.$alert('当前任务的配置参数不能为空', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }

                batchUpdateTaskConfig({
                    taskConfigList: this.configDataList
                }).then(res => {
                    if (res.data && res.data.success) {
                        this.$alert(res.data.msg, '成功', {
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                            }
                        }).then(() => {
                        });
                        this.configTaskDialog = false;
                        this.refresh();
                    } else {
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }
                }).catch(error => {
                    this.openMask();
                    this.$alert(error, '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    });
                });
            },
            cancelConfigTask() {
                this.configTaskDialog = false;
            },
        }
    }
</script>



