                this.bankInfoList = [{code:'-1',text:'全部'}];
                if (res.data && res.data.length > 0) {
                    this.bankInfoList = this.bankInfoList.concat(res.data);
                }
            });
            getFundInfo('/common/fundName.json', {transfer_flag:1,fund_status:[1]}).then(res => {
                this.fundList = [{code:'-1',text:'全部'}];
