                    this.custodialorgList = this.custodialorgList.concat(res.data);
                }
            });
            getDictList(610087).then(res => this.fundTypeList = res.data);
            getCityTree().then(res => this.cityTree = res.data);
            this.rivalScroll(0);
            this.issuerScroll(0);
            this.brokerScroll(0);
            getDictList(90525).then(res => {
                this.accountClassList = res.data.filter( r => (r.code != 1 && r.code != 2 && r.code != 3 && r.code != 5 && r.code != 6 && r.code != 8));
                this.accountTreeList[0].children = res.data.filter( r => (r.code != 1 && r.code != 2 && r.code != 3 && r.code != 5 && r.code != 6 && r.code != 8));
            });
        },
