        watch:{
            showAcctDialog: function (show) {
                if(show){
                    if(this.queryForm.accountClasses == 'e'){
                        getBusinFlagList("22", "e").then( res => {
                            if (res.data && res.data.length > 0) {
                                this.businFlagList = [{code:'-1',text:'全部'}];
                                this.businFlagList = this.businFlagList.concat(res.data);
                            }
                        })
                    }else if(this.queryForm.accountClasses == 'g'){
                        getBusinFlagList("23", "g").then( res => {
                            if (res.data && res.data.length > 0) {
                                this.businFlagList = [{code:'-1',text:'全部'}];
                                this.businFlagList = this.businFlagList.concat(res.data);
                            }
                        })
                    }
                    this.openMask();
                }else{
                    this.closeMask();
                }
            },
            fundIdList(newValue,oldValue){
                if((newValue.indexOf('-1') != -1 && oldValue.indexOf('-1') == -1) || newValue == '-1'){
                    this.accountInfo.fund_id_list = '-1';
                }else if((newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') != -1)
                    || (newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') == -1)){
                    this.accountInfo.fund_id_list = newValue;
                }else{
                    let newValueList = newValue.split(',');
                    this.accountInfo.fund_id_list = this.removeArr(newValueList,'-1');
                }
                this.fundIdList = this.accountInfo.fund_id_list;
            },
            bankNoList(newValue,oldValue){
                if((newValue.indexOf('-1') != -1 && oldValue.indexOf('-1') == -1) || newValue == '-1'){
                    this.accountInfo.bank_no_list = '-1'
                }else if((newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') != -1)
                    || (newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') == -1)){
                    this.accountInfo.bank_no_list = newValue;
                }else{
                    let newValueList = newValue.split(',');
                    this.accountInfo.bank_no_list = this.removeArr(newValueList,'-1');
                }
                this.bankNoList = this.accountInfo.bank_no_list;
            },
            copBusinFlagList(newValue,oldValue){
                if((newValue.indexOf('-1') != -1 && oldValue.indexOf('-1') == -1) || newValue == '-1'){
                    this.accountInfo.cop_busin_flag_list = '-1'
                }else if((newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') != -1)
                    || (newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') == -1)){
                    this.accountInfo.cop_busin_flag_list = newValue.join(",");
                }else{
                    let newValueList = newValue;
                    this.accountInfo.cop_busin_flag_list = this.removeArr(newValueList,'-1');
                }
                if (this.accountInfo.cop_busin_flag_list != this.copBusinFlagList.join(",")){
                    this.copBusinFlagList = this.accountInfo.cop_busin_flag_list.split(",");
                }
            },
            orgNoList(newValue,oldValue){
                if((newValue.indexOf('-1') != -1 && oldValue.indexOf('-1') == -1) || newValue == '-1'){
                    this.accountInfo.custodialorg_list = '-1'
                }else if((newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') != -1)
                    || (newValue.indexOf('-1') == -1 && oldValue.indexOf('-1') == -1)){
                    this.accountInfo.custodialorg_list = newValue;
                }else{
                    let newValueList = newValue.split(',');
                    this.accountInfo.custodialorg_list = this.removeArr(newValueList,'-1');
                }
                this.orgNoList = this.accountInfo.custodialorg_list;
            },
        },
        methods: {
            reLayout(){
                setTimeout(() => {
                    let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                    this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 35;
                }, 10);
            },
            classChange(val){
                if(val == 'e'){
                    getBusinFlagList("22", "e").then( res => {
                        if (res.data && res.data.length > 0) {
                            this.businFlagList = [{code:'-1',text:'全部'}];
                            this.businFlagList = this.businFlagList.concat(res.data);
                        }else{
                            this.businFlagList = [{code:'-1',text:'全部'}];
                        }
                        if (!showTextList(this.accountInfo.cop_busin_flag_list, this.businFlagList)) {
                            this.accountInfo.cop_busin_flag_list = '-1';
                        }
                        this.copBusinFlagList = this.accountInfo.cop_busin_flag_list.split(',');
                    })
                }else if(val == 'g'){
                    getBusinFlagList("23", "g").then( res => {
                        if (res.data && res.data.length > 0) {
                            this.businFlagList = [{code:'-1',text:'全部'}];
                            this.businFlagList = this.businFlagList.concat(res.data);
                        }else{
                            this.businFlagList = [{code:'-1',text:'全部'}];
                        }
                        if (!showTextList(this.accountInfo.cop_busin_flag_list, this.businFlagList)) {
                            this.accountInfo.cop_busin_flag_list = '-1';
                        }
                        this.copBusinFlagList = this.accountInfo.cop_busin_flag_list.split(',');
                    })
                } else if (val != 'a') {
                    this.accountInfo.cop_busin_flag_list = '';
                    this.copBusinFlagList = [];
                }
                if((val == '4' || val == '9' || val == 'e' || val == 'g') && !this.accountInfo.fund_id_list){
                    this.accountInfo.fund_id_list = '-1';
                }
                if((val == 'b' || val == 'c') && !this.accountInfo.bank_no_list){
                    this.accountInfo.bank_no_list = '-1';
                }
                if((val == 'a') && !this.accountInfo.custodialorg_list){
                    this.accountInfo.custodialorg_list = '-1';
                }
                this.$nextTick(()=>{
                    this.class_status = val
                });
                this.fundIdList = this.accountInfo.fund_id_list;
                this.bankNoList = this.accountInfo.bank_no_list;
                this.orgNoList = this.accountInfo.custodialorg_list;
                if (val != '4' && val != '9' && val != 'e' && val != 'g') {
                    this.accountInfo.fund_id_list = '';
                }
                if (val != '7' && val != 'i' && val != 'f') {
                    this.accountInfo.rival_code = '';
                }
                if (val != '9') {
                    this.accountInfo.ta_type = '';
                    this.accountInfo.sale_channel = '';
                }
                if (val != 'a') {
                    this.accountInfo.custodialorg_list = '';
                }
                if (val != 'b' && val != 'c') {
                    this.accountInfo.bank_no_list = '';
                }
                if (val != 'd' && val != 'i') {
                    this.accountInfo.issuer_code = '';
                }
                if (val != 'f') {
                    this.accountInfo.report_code = '';
                    this.accountInfo.stock_name = '';
                }
                if (val != 'h') {
                    this.accountInfo.agency_code = '';
                }
                if (val != 'i') {
                    this.accountInfo.stock_code_list = '';
                }
            },
            handleCancel() {
                this.accountInfo.agency_code = '';
                this.showAcctDialog = false;
            },
            handleNodeClick(data) {
                this.queryForm.accountClasses = data.code;
                if(this.queryForm.accountClasses.split(',').length > 1){
                    this.accountClasscolumns = this.columns.filter(x => !x.accountClass || x.accountClass.indexOf('*') >= 0);
                }else{
                    this.accountClasscolumns = this.columns.filter(x => !x.accountClass || x.accountClass.indexOf(this.queryForm.accountClasses) >= 0);
                }
                this.refreshTable();
            },
            refreshTable() {
                this.$refs.datagrid.dataChange(1);
            },
            handleAdd() {
                this.$refs.accountForm.resetFields();
                this.accountInfo ={
                    account_class : this.queryForm.accountClasses.split(",")[0],
                    bank_account_status :'1',
                };
                if(this.accountInfo.account_class == '4' || this.accountInfo.account_class == '9'
                    || this.accountInfo.account_class == 'e' || this.accountInfo.account_class == 'g'){
                    this.accountInfo.fund_id_list = '-1';
                }else{
                    this.accountInfo.fund_id_list = '';
                }
                if(this.accountInfo.account_class == 'b' || this.accountInfo.account_class == 'c'){
                    this.accountInfo.bank_no_list = '-1';
                }else{
                    this.accountInfo.bank_no_list = '';
                }
                if(this.accountInfo.account_class == 'e' || this.accountInfo.account_class == 'g'){
                    this.accountInfo.cop_busin_flag_list = '-1';
                    this.copBusinFlagList = this.accountInfo.cop_busin_flag_list.split(",");
                }else{
                    this.accountInfo.cop_busin_flag_list = '';
                    this.copBusinFlagList = [];
                }
                if(this.accountInfo.account_class == 'a'){
                    this.accountInfo.custodialorg_list = '-1';
                }else{
                    this.accountInfo.custodialorg_list = '';
                }
                this.fundIdList = this.accountInfo.fund_id_list;
                this.bankNoList = this.accountInfo.bank_no_list;
                this.orgNoList = this.accountInfo.custodialorg_list;
                this.provinceCity = []
                this.addAcct = true;
                this.showAcctDialog = true;
            },
            handleModify() {
                // this.$refs.accountForm.resetFields();
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选中一条记录','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    });
                    return
                }
                if (selects.length > 1){
                    this.openMask();
                    this.$alert('修改操作只能选择一条数据','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    }).then(() =>{
                        this.closeMask();
                    });
                    return
                }
                this.pubDbClick(selects[0]);
            },
            pubDbClick(data){
                this.addAcct = false;
                Object.assign(this.accountInfo,data);
                if(this.accountInfo.bic_code == null){
                    this.accountInfo.bic_code = '';
                }
                if(this.accountInfo.fund_id_list == null){
                    this.accountInfo.fund_id_list = '';
                }
                if(this.accountInfo.bank_no_list == null){
                    this.accountInfo.bank_no_list = '';
                }
                if(this.accountInfo.cop_busin_flag_list == null){
                    this.accountInfo.cop_busin_flag_list = '';
                    this.copBusinFlagList = [];
                }
                if(this.accountInfo.custodialorg_list == null){
                    this.accountInfo.custodialorg_list = '';
                }
                if (!this.accountInfo.rival_code) {
                    this.accountInfo.rival_code = '';
                }
                if (!this.accountInfo.issuer_code) {
                    this.accountInfo.issuer_code = '';
                }
                this.fundIdList = this.accountInfo.fund_id_list;
                this.bankNoList = this.accountInfo.bank_no_list;
                this.copBusinFlagList = this.accountInfo.cop_busin_flag_list.split(',');
                this.orgNoList = this.accountInfo.custodialorg_list;
                this.accountInfo.open_date = parseDateString(this.accountInfo.open_date);
                if (data.account_class=='7'){
                    this.rivalLoad(data.rival_code);
                } else if (data.account_class=='d' || data.account_class=='i'){
                    this.issuerLoad(data.issuer_code);
                } else if (data.account_class=='f'){
                    this.brokerLoad(data.rival_code);
                }
                this.showAcctDialog = true;
            },
            saveAccount() {
                let isValidate = true;
                this.$refs.accountForm.validate((validate) => isValidate = isValidate && validate);

                if ((this.accountInfo.account_class == 'e' || this.accountInfo.account_class == 'g') && !this.accountInfo.cop_busin_flag_list) {
                    this.$alert('业务标志不能为空，请重新检查!','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }

                if (!isValidate) {
                    this.$alert('输入项不满足要求，请重新检查!','错误',{
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }
                this.accountInfo.open_date = parseDateStr(this.accountInfo.open_date);
                saveAccount(this.accountInfo).then(res => {
                    if (res.data && res.data.code == '0') {
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                                this.showAcctDialog = false;
                            }
                        }).then(() => {
                            this.showAcctDialog = false;
                        });
                        this.refreshTable();
                    } else if (res.data && res.data.code == '1120010') {
                        //没有修改项时，直接关闭窗口
                        this.showAcctDialog = false;
                    } else {
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }
                })
            },
            handleDelete() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('请先选中至少一条记录','错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {
                            this.closeMask();
                        }
                    })
                    return
                }

              let bankAccountIdList = "";
              selects.forEach(account => {
                bankAccountIdList = bankAccountIdList + account.bank_account_id + ",";
              });
              bankAccountIdList = bankAccountIdList.substring(0, bankAccountIdList.length - 1);

                this.openMask();
                this.$confirm('确认要删除该账户吗？', '确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    deleteAccount(bankAccountIdList)
                        .then(res => {
                            if (res.data && res.data.code == '0') {
                                this.$alert(res.data.msg,'成功',{
                                    confirmButtonText: '确定',
                                    type: 'success',
                                    callback:action => {
                                        this.closeMask();
                                    }
                                }).then(() => {
                                    this.closeMask();
                                })
                                this.refreshTable();
                            } else {
                                this.$alert(res.data.msg,'错误',{
                                    confirmButtonText: '确定',
                                    type: 'error',
                                    callback:action => {
                                        this.closeMask();
                                    }
                                }).then(() => {
                                    this.closeMask();
                                })
                            }
                        })
                }).catch(() => {
                    this.closeMask();
                });
            },
            rivalScroll(num) {
                if (num <= 1) {
                    let length = this.rivalList.length;
                    let pageSize = 100;
                    let pagNo = length / 100;
                    getRivalList(pagNo, pageSize).then(res => {
                                                           this.rivalList = this.rivalList.concat(res.data.list.map(res => {
                                                               let item = {};
                                                               item.value = res.code;
                                                               item.label = res.text;
                                                               return item;
                                                           }))
                                                           if (!res.data.hasNextPage) {
                                                               this.rivalComplete = true
                                                           }
                                                       }
                    )
                }
            },
            rivalLoad(rivalCode) {
                if (!this.rivalList.find(x=>x.value==rivalCode)){
                    getRivalList(0, 50, '',rivalCode).then(res => {
                        this.rivalList = this.rivalList.concat(res.data.list.map(res => {
                            let item = {};
                            item.value = res.code;
                            item.label = res.text;
                            return item;
                        }))
                        // this.accountInfo.rival_code = rivalCode
                        // this.accountInfo.account_class = '7'
                    })
                }

            },
            rivalQuery(query) {
                this.rivalLoading = true;
                getRivalList(0, 50, query).then(res => {
                    this.rivalList = res.data.list.map(res => {
                        let item = {};
                        item.value = res.code;
                        item.label = res.text;
                        return item;
                    });
                    this.rivalLoading = false;
                    if (!res.data.hasNextPage) {
                        this.rivalComplete = true
                    }
                })
            },
            issuerScroll(num) {
                if (num <= 1) {
                    let length = this.issuerList.length;
                    let pageSize = 50;
                    let pagNo = length / 50;
                    getIssuerList(pagNo, pageSize).then(res => {
                                                            this.issuerList = this.issuerList.concat(res.data.list.map(res => {
                                                                let item = {};
                                                                item.value = res.code;
                                                                item.label = res.text;
                                                                return item;
                                                            }));
                                                            if (!res.data.hasNextPage) {
                                                                this.issuerComplete = true
                                                            }
                                                        }
                    )
                }
            },
            issuerLoad(issueCode) {
                if (!this.issuerList.find(x=>x.value==issueCode)){
                    getIssuerList(0, 50, '',issueCode).then(res => {
                        this.issuerList = this.issuerList.concat(res.data.list.map(res => {
                            let item = {};
                            item.value = res.code;
                            item.label = res.text;
                            return item;
                        }))
                    })
                }

            },
            issuerQuery(query) {
                this.issuerLoading = true;
                getIssuerList(0, 50, query).then(res => {
                    this.issuerList = res.data.list.map(res => {
                        let item = {};
                        item.value = res.code;
                        item.label = res.text;
                        return item;
                    });
                    this.issuerLoading = false;
                    if (!res.data.hasNextPage) {
                        this.issuerComplete = true
                    }
                })
            },
            brokerScroll(num) {
                if (num <= 1) {
                    let length = this.brokerList.length;
                    let pageSize = 100;
                    let pagNo = length / 100;
                    pageBrokerList(pagNo, pageSize).then(res => {
                            this.brokerList = this.brokerList.concat(res.data.list.map(res => {
                                let item = {};
                                item.value = res.code;
                                item.label = res.text;
                                return item;
                            }));
                            if (!res.data.hasNextPage) {
                                this.brokerComplete = true
                            }
                        }
                    )
                }
            },
            brokerLoad(brokerCode) {
                if (brokerCode && !this.brokerList.find(x=>x.value==brokerCode)){
                    pageBrokerList(0, 50, '', brokerCode).then(res => {
                        this.brokerList = this.brokerList.concat(res.data.list.map(res => {
                            let item = {};
                            item.value = res.code;
                            item.label = res.text;
                            return item;
                        }))
                    })
                }
            },
            brokerQuery(query){
                this.brokerLoading = true;
                pageBrokerList(0, 50, query).then(res =>{
                    this.brokerList = res.data.list.map(res => {
                        let item = {};
                        item.value = res.code;
                        item.label = res.text;
                        return item;
                    });
                    this.brokerLoading = false;
                    if(!res.data.hasNextPage){
                        this.brokerComplete = true;
                    }
                })
            },
            choseFundList(){
                let fullWrapperHieght = document.getElementById("full-h-wrapper").offsetHeight;
                this.$refs.maintainFundChooseMsgBox.showMsgBox(this.accountInfo.fund_id_list, fullWrapperHieght, '2');
            },
            /*taTypeChange(taType){
                getAgencyList(taType).then(res => this.agencyList = res.data);
            },*/
            uploadExcel(){
                this.$refs.importAccountTransfer.showDialog("2",null);
            },
            removeArr(source, dest){
                for (let i = 0; i < source.length; i++) {
                    if(source[i] == dest){
                        source.splice(i,1)
                    }
                }
                return source;
            },
            rowClassName(row){
                if(row.bank_account_status != '1'){
                    return 'alert-table-red-row';
                }else{
                    return '';
                }
            },
            saveFundInfo(data){
                this.fundIdList = data;
            },
            onSplitMove(){
                this.$refs.datagrid.handleResize();
            }
        },
        components: {
            FundChooseMsgBox,
            extendRow,
            HDatagrid,
            ImportAccountTransfer
        }
    }
</script>
<style lang="scss" src="@/style/common.scss"></style>
<style>
    .el-popover__title{
        padding: 0 4px;
        color: #333;
        font-size:12px;
    }
    .popover-font{
        color: #333;
        font-size: 12px;
        padding: 0 4px;
    }
</style>

<style lang="less" rel="stylesheet/less">
    .page-pubaccount {
        .ctrls {
            display: flex;
            justify-content: space-between;
            height: 34px;
            .h-input-type {
                height: 28px;
            }
        }
    }
    // .el-scrollbar.h-scrollbar {
    //     .el-scrollbar__wrap{
    //         overflow-x: hidden;
    //     }
    // }
    // .el-tree>.el-tree-node {
    //     min-width: 100%;
    //     display: inline-block;
    // }
    .customMsgBox.pubaccount-new {
        .h-select-multiple .h-select-selection .h-tag {
            max-width: 80%;
        }
    }
</style>


