export function getTransferMergeFileShowText(rowData) {
    //文件数量
    let fileCount = 0;
    if (rowData.bill_name) {
        fileCount = rowData.bill_name.split(',').length;
    }

    //原指令数量
    let transInfoCount = 0;
    if (rowData.deal_no && rowData.deal_no.indexOf('笔轧差') > 0) {
        transInfoCount = rowData.deal_no.substr(0, rowData.deal_no.indexOf('笔轧差'));
    }

    let fileShow = fileCount;
    if (transInfoCount !== 0) {
        fileShow = fileShow + '/' + transInfoCount;
    }
    return fileShow;
}
