<template>
    <div class="liquidate-recordquery" id="full-h-wrapper">
        <h-split v-model="split" min="130" max="600" @on-moving="onSplitMoving">
            <div slot="left" span="4" class="full-h">
                <h-other-fund-tree :show-all="true" :onFundClick="fundClick"></h-other-fund-tree>
            </div>
            <div slot="right" span="20" class="full-h" style="padding-left: 10px">
                <el-form style="font-size: 12px;":inline="true" size="mini" :model="queryForm" class="demo-form-inline">

                    <el-form-item label="勾销状态">
                        <el-select v-model="queryForm.status"   clearable collapse-tags>
                            <el-option v-for="item in statusList" :key="item.code" :label="item.text" :value="item.code">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="交收时间">
                        <el-date-picker
                                v-model="settleDate"
                                type="daterange"
                                range-separator="至"
                                :clearable="false"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>

                    <el-input v-model="queryForm.keyword" size="mini" :maxlength="32" placeholder="请输入关键字"
                              style="width: 200px">
                    </el-input>

                    <el-form-item>
                        <el-button type="primary" @click="refresh">查询</el-button>
                    </el-form-item>
                </el-form>
                <h-datagrid hasPage :height="autoHeight"
                            highlight-row :pageSize="100"
                            :pageSizeOpts="pageSizeOpts"
                            :columns="recordColumns"
                            :bindForm="queryForm"
                            :auto-load="false" style="overflow: hidden"
                            url="/liquidate/query/record/page.json"
                            ref="datagrid" >
                </h-datagrid>
            </div>
        </h-split>
    </div>
</template>

<script>
    import HDatagrid from "../../../components/HDatagrid";
    import HOtherFundTree from "../../../components/HOtherFundTree";
    import Cookies from 'js-cookie'
    import {
        showText,
        formatMoney,
        getDictList,
        parseDateStr,
        parseDateString,
    } from "@/api/utils"

    export default {
        name: "recordQuery",
        components: {HOtherFundTree,HDatagrid},
        data(){
            return{
                split: 0.15,
                autoHeight:0,
                pageSizeOpts:[25,50,75,100],
                dealTypeList:[],
                businClassList:[],
                businFlagList:[],
                sysDate:Cookies.get('CurrSysDate'),
                recordColumns:[
                    {
                        title: "产品代码",
                        key: "fund_code",
                        width:80,
                        align: "left",
                        ellipsis: true,
                        sortable: true
                    },
                    {
                        title: "产品名称",
                        key: "fund_name",
                        align: "left",
                        ellipsis: true
                    },
                    {
                        title: "勾销状态",
                        key: "liquidate_ins_status",
                        align: "left",
                        width:80,
                        ellipsis: true,
                        sortable: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let status = showText(rowData.liquidate_ins_status,this.statusList)
                            return h("span",{},status)
                        }
                    },
                    {
                        title: "业务分类",
                        key: "cop_busin_class",
                        align: "left",
                        width:80,
                        ellipsis: true,
                        sortable: true,
                        render: (h,params) =>{
                            const rowData = params.row
                            let businClass = showText(rowData.cop_busin_class,this.businClassList)
                            return h("span",{},businClass)
                        }
                    },
                    {
                        title: "业务标志",
                        key: "cop_busin_flag",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row
                            let businFlag = showText(rowData.cop_busin_flag,this.businFlagList)
                            return h("span",{},businFlag)
                        }
                    },
                    {
                        title: "资金方向",
                        key: "cash_direction",
                        align: "left",
                        ellipsis: true,
                        width:80,
                        sortable: true,
                        render: (h,params) =>{
                            const rowData = params.row
                            let settleDirection = showText(rowData.cash_direction,this.directionList)
                            return h("span",{},settleDirection)
                        }
                    },
                    {
                        title: "交易金额",
                        key: "trade_balance",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row
                            let trade_balance = formatMoney(rowData.trade_balance)
                            return h("span",{style:{fontWeight:'bold'}},trade_balance)
                        }
                    },
                    {
                        title: "指令金额",
                        key: "settle_balance",
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                            const rowData = params.row
                            let settleBalance = formatMoney(rowData.settle_balance)
                            return h("span",{style:{fontWeight:'bold'}},settleBalance)
                        }
                    },

                    {
                        title: "对手方账号",
                        key: "rival_account_no",
                        align: "center",
                        width:120,
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.rival_account_no
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    },
                    {
                        title: "对手方账户名",
                        key: "rival_account_name",
                        align: "center",
                        width:120,
                        ellipsis: true,
                        render: (h,params) =>{
                            let detail = params.row.rival_account_name
                            return h("span", {attrs:{title:detail}}, detail);
                        }
                    }, {
                        title: "交收日期",
                        key: "cash_settle_date",
                        align: "left",
                        ellipsis: true,
                        width:80,
                        sortable: true,
                        render: (h,params) =>{
                            const rowData = params.row
                            let cashSettleDate = parseDateString(rowData.cash_settle_date)
                            return h("span",{},cashSettleDate)
                        }
                    },{
                        title: "勾销方式",
                        key: "liquidate_ins_deal_type",
                        align: "left",
                        ellipsis: true,
                        sortable: true,
                        render: (h,params) =>{
                            const rowData = params.row;
                            let liquidateInsDealType = showText(rowData.liquidate_ins_deal_type,this.dealTypeList)
                            return h("span",{},liquidateInsDealType)
                        }
                    },


                ],
                directionList:[
                    {
                        code:'1',
                        text:'入款'
                    },
                    {
                        code:'2',
                        text:'出款'
                    }
                ],
                statusList:[
                    {
                        code:'0',
                        text:'未处理'
                    },
                    {
                        code:'1',
                        text:'待匹配'
                    },
                    {
                        code:'2',
                        text:'已匹配'
                    },
                    {
                        code:'4',
                        text:'已主推'
                    },
                ],
                queryForm:{
                    fund_code:'',
                    status:'',
                    keyword:'',
                    begin_date:Cookies.get('CurrSysDate'),
                    end_date:Cookies.get('CurrSysDate')
                }
            }
        },
        computed:{
            settleDate:{
                get:function () {
                    let begin, end
                    if(this.queryForm.begin_date){
                        begin = parseDateString(this.queryForm.begin_date)
                    }else{
                        begin = this.sysDate
                    }

                    if(this.queryForm.end_date){
                        end = parseDateString(this.queryForm.end_date)
                    }else{
                        end = this.sysDate
                    }

                    return [begin,end]
                },
                set:function (newValue) {
                    this.queryForm.begin_date = parseDateStr(newValue[0])
                    this.queryForm.end_date = parseDateStr(newValue[1])
                }
            }
        },
        mounted() {
            getDictList(90523).then(res => this.businClassList = res.data)
            getDictList('businFlag').then(res => this.businFlagList = res.data);
            getDictList(91192).then(res => this.dealTypeList = res.data);
            this.re_layout();
        },
        methods:{
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 60;
            },
            fundClick(data){
                if(data){
                    if (data.text.indexOf(" ")>=0)
                        this.queryForm.fund_code = data.text.split(" ")[0];
                    else
                        this.queryForm.fund_code = null
                    this.refresh();
                }
            },
            refresh(){
                this.$refs.datagrid.dataChange(1);
            },
            onSplitMoving() {
                this.$refs.datagrid.handleResize();
            }
        }
    }
</script>

<style scoped>
    .liquidate-recordquery {

    }
</style>


