import fetch from "@/api/httpFetch";

export function insertFixTemplate(params) {
    return fetch.post("/systemmgnt/template/insertFixTemplate.json",params,{
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function modifyFixTemplate(params) {
    return fetch.post("/systemmgnt/template/modifyFixTemplate.json",params,{
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}

export function deleteTemplate(serialNo,path) {
    return fetch.post("/systemmgnt/template/deleteTemplate.json",{serialNo:serialNo,path:path});
}


