<template>
    <div id="full-h-wrapper" class="full-h">
        <el-tabs :tab-position="tabPosition" @tab-click="handleClick">
            <el-tab-pane label="日初对账">
                <bal-acc-init-temple ref="initTemple"></bal-acc-init-temple>
            </el-tab-pane>
            <el-tab-pane label="日终对账">
                <bal-acc-final-temple ref="finalTemple"></bal-acc-final-temple>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
    import BalAccInitTemple from "./balAccInitTemple";
    import BalAccFinalTemple from "./balAccFinalTemple";
    export default {
        name: "balAccTemple",
        components: {BalAccFinalTemple, BalAccInitTemple},
        data(){
            return{
                tabPosition:'top',
            }
        },
        mounted(){
            this.re_layout()
        },
        methods:{
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                let initHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 190;
                let finalHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 190;
                this.$refs.initTemple.resizeTable(initHeight );
                this.$refs.finalTemple.resizeTable(finalHeight );
            },
            handleClick(tab, event){
                if(tab.label == '日初对账'){
                    let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                    let initHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 190;
                    this.$refs.initTemple.resizeTable(initHeight );
                    this.$refs.initTemple.refresh();
                }else if(tab.label == '日终对账'){
                    let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                    let finalHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 191;
                    this.$refs.finalTemple.resizeTable(finalHeight );
                    this.$refs.finalTemple.refresh();
                }
            }
        }
    }
</script>

<style scoped>

</style>


