<template>
    <div>
        <h-msg-box class="customMsgBox" width="820px" v-model="showUsedFundDialog" :mask-closable="false" top="50">
            <p slot="header">
                <span v-if="type == '1'">产品名称</span>
                <span v-if="type == '2'">可使用产品</span>
            </p>
            <el-form style="position:relative;font-size: 12px;white-space:nowrap;"size="mini" :inline="true" :model="queryInfo" class="demo-form-inline">
                <el-form-item label="产品类型">
                    <el-select v-model="fund_type" filterable clearable multiple collapse-tags>
                        <el-option v-for="item in fundTypeList" :key="item.code" :value="item.code" :label="item.text"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="托管人">
                    <el-select v-model="fund_trust_bank" filterable clearable multiple collapse-tags>
                        <el-option v-for="item in custodianInfoList" :key="item.code" :value="item.code" :label="item.text"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-input v-model="queryInfo.keyword" :maxlength="32" placeholder="请输入产品编码或名称" style="width: 195px"></el-input>
                </el-form-item>
                <el-form-item style="position:absolute;right:0;top:0;">
                    <el-button type="primary" @click="query" style="">查询</el-button>
                </el-form-item>
            </el-form>
            <h-table :data="gridData" bodyAlgin='right' headAlgin='left' :height="autoHeight"
                     :columns="fundColumns" highlight-row row-select ref="gridContent"
                     @on-select="selectedOne" @on-select-cancel="cancelSelected" @on-select-all="selectedAll">
            </h-table>
            <h-page v-show="showPage" :current="pageInfo.current" style="padding: 5px 0 0 0 ;" :total="pageInfo.total"
                    :pageSizeOpts="pageSizeOpts" show-total show-elevator show-sizer @on-change="dataChange"
                    :page-size="pageInfo.pageSize" @on-page-size-change="pageSizeChange" ref="gridPage"></h-page>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="cancelUsedFund">取消</h-button>
                <h-button type="primary" @click="saveUsedFund">确认</h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>

    import {pageFundList} from '@/api/fundAccount/fundAccount'
    import{
        getDictList,
        getBankList
    } from '@/api/utils'

    export default {
        name: "FundChooseMsgBox",
        data() {
            return {
                autoHeight: 0,
                showUsedFundDialog:false,
                showPage: false,
                type: '',
                selectedFundList: '',
                gridData:[],
                fundTypeList: [],
                custodianInfoList: [],
                pageSizeOpts: [25, 50, 75, 100],
                fundColumns: [
                    {
                        type:'selection',
                        width:50
                    }, {
                        title:'产品编号',
                        key:'fund_id',
                        ellipsis:true,
                        align:'left',
                        hiddenCol: true
                    },{
                        title:'产品代码',
                        key:'fund_code',
                        ellipsis:true,
                        align:'left'
                    }, {
                        title:'产品名称',
                        key:'fund_name',
                        ellipsis:true,
                        align:'left'
                    }
                ],
                queryInfo: {
                    fundType: '',
                    fundTrustBank: '',
                    keyword:''
                },
                pageInfo:{
                    pageNo: 1,
                    pageSize: 100,
                    total: 0,
                    current: 1
                },
            }
        },
        watch: {
            showUsedFundDialog: function (show) {
                if(!show){
                    this.selectedFundList = '';
                    this.gridData = [];
                    this.pageInfo.current = 1;
                }
            },
        },
        computed: {
            fund_type: {
                get: function () {
                    if(this.queryInfo.fundType)
                        return this.queryInfo.fundType.split(",")
                    else
                        return []
                },
                set: function (newValue) {
                    this.queryInfo.fundType = newValue.join(",")
                }
            },
            fund_trust_bank: {
                get: function () {
                    if(this.queryInfo.fundTrustBank)
                        return this.queryInfo.fundTrustBank.split(",")
                    else
                        return []
                },
                set: function (newValue) {
                    this.queryInfo.fundTrustBank = newValue.join(",")
                }
            }
        },
        activated() {
            getDictList(610087).then(res => this.fundTypeList = res.data);
            getBankList("1").then(res => {
                if (res.data && res.data.length > 0) {
                    this.custodianInfoList = res.data;
                }
            });
        },
        methods: {
            showMsgBox(list, height, type, trustType){
                this.selectedFundList = list;
                this.pageInfo.pageNo = 1;
                this.queryInfo.fundType = '';
                this.queryInfo.fundTrustBank = '';
                this.queryInfo.keyword = '';
                this.queryInfo.trustType = trustType;
                this.queryUsedFund(list);
                this.autoHeight = height - 150;
                this.type = type;
                this.showUsedFundDialog = true;
            },
            query(){
                this.queryUsedFund(this.selectedFundList);
            },
            queryUsedFund(list){
                pageFundList(this.pageInfo.pageNo,this.pageInfo.pageSize,this.queryInfo.fundType,this.queryInfo.fundTrustBank,this.queryInfo.keyword,this.queryInfo.trustType).then(res =>{
                    this.pageInfo.total = res.data.total;
                    if(this.pageInfo.total > this.pageInfo.pageSize){
                        this.showPage = true;
                    }else{
                        this.showPage = false;
                    }
                    this.gridData = res.data.list.map( x => {
                        return Object.assign( x, {_checked:false})
                    });
                    if(list && list.length > 0){
                        let fundList = list.split(",");
                        for( let x of this.gridData){
                            for( let y of fundList){
                                if(x.fund_id == y){
                                    x._checked = true;
                                    break;
                                }
                            }
                        }
                    }
                })
            },
            selectedOne(selection,row){
                if(this.selectedFundList){
                    this.selectedFundList += "," + row.fund_id;
                }else{
                    this.selectedFundList = row.fund_id.toString()
                }
            },
            cancelSelected(selection,row){
                if(this.selectedFundList){
                    let arr = this.selectedFundList.split(",");
                    for (let i = 0; i < arr.length; i++){
                        if(arr[i] == row.fund_id){
                            arr.splice(i,1);
                        }
                    }
                    this.selectedFundList = arr.join(",");
                }
            },
            selectedAll(selection){
                if(selection.length > 0){
                    for (let row of selection){
                        if(this.selectedFundList){
                            this.selectedFundList += "," + row.fund_id;
                        }else{
                            this.selectedFundList = row.fund_id
                        }
                    }
                }else{
                    for (let row of this.gridData){
                        if(this.selectedFundList){
                            let arr = this.selectedFundList.split(",");
                            for (let i = 0; i < arr.length; i++){
                                if(arr[i] == row.fund_id){
                                    arr.splice(i,1);
                                }
                            }
                            this.selectedFundList = arr.join(",");
                        }
                    }
                }
            },
            dataChange(i){
                if(i){
                    this.pageInfo.pageNo = i;
                    this.pageInfo.current = i;
                }
                this.queryUsedFund(this.selectedFundList);
            },
            pageSizeChange(i){
                this.pageInfo.pageSize = i;
                this.pageInfo.current = 1;
                this.pageInfo.pageNo = 1;
                this.queryUsedFund(this.selectedFundList);
            },
            cancelUsedFund(){
                this.showUsedFundDialog = false;
            },
            saveUsedFund(){
                let fundIdList = this.selectedFundList.replace(/\s+/g,",");
                this.showUsedFundDialog = false;
                this.$emit("saveFundInfo", fundIdList);
            }
        }
    }
</script>

<style scoped>

</style>


