import { deepCopy } from '../../../utils/func'
import data from './data'
import fetch from '../../../api/httpFetch.js'
import { dataSourceDesc, nodeNameDesc, clearAgentCodeDesc, marketNoDesc, formatMoney, cashDirectionDesc, settlementDesc, bankDesc, investTypeDesc } from '../../../filters'

import {
    getShowColumn,
    setShowColumn,
  parseDateString
} from '@/api/utils'

let service = {
  getShowColumn(gridName) {
    return getShowColumn(gridName)
  },
  getDatas(url, fundData, pageNo, pageSize) {
    let params = {
      pageNo,
      pageSize
    }
    Object.assign(params, fundData)
    return fetch.post(url, params)
  },
  /**
   * 将一维数组转换成 tree 数组，根据 parentId
   */
  makeTreeData(list) {
    let treeData = []
    for (let data of list) {
      if (treeData.length === 0) {
        treeData.push(data)
      } else {
        let find = this.findDataById(treeData, data.transfer_ins_batch_no)
        if (find) {
          if (!find.children) {
            find.children = []
          }
          find.children.push(data)
        } else {
          treeData.push(data)
        }
      }
    }
    return treeData
  },
  makeDataChecked(datas, checked = false) {
    let dosome = (datas) => {
      for (let data of datas) {
        data.checked = checked
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
    return datas
  },
  makeDataCheckedWithoutExcepts(datas, checked = false, exceptIds = []) {
    let dosome = (datas) => {
      for (let data of datas) {
        if (exceptIds.indexOf(data.transfer_ins_id) >= 0) {
          continue
        }
        data.checked = checked
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
    return datas
  },
  makeDataExpand(datas, expand = false) {
    let dosome = (datas) => {
      for (let data of datas) {
        data.expand = expand
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
    return datas
  },
  getCheckedDatas(datas) {
    let list = []
    let dosome = (datas) => {
      for (let data of datas) {
        if (data.checked) {
          list.push(data)
        }
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
    return list
  },
  findNode(nodes, id, find) {
    if (!nodes || nodes.length === 0) {
      return null
    }
    for (let i = 0; i < nodes.length; i++) {
      if (nodes[i].transfer_ins_id === id) {
        find.obj = nodes[i]
        break
      }
      if (nodes[i].children) {
        this.findNode(nodes[i].children, id, find)
      }
    }
  },
  findDataById(datas, id) {
    let find = {}
    this.findNode(datas, id, find)
    if (find.obj) {
      return find.obj
    }
    return null
  },
  findDataById2(datas, id) {
    for (let data of datas) {
      let chidren = data.children
      if (chidren && chidren.length > 0) {
        let subChildren = data.children
        for (let sub of subChildren) {
          if (sub.transfer_ins_id === id) {
            return sub
          }
        }
      } else {
        if (data.transfer_ins_id === id) {
          return data
        }
      }
    }
    return null
  },
  flatTreeDatas(datas) {
    let list = []
    let dosome = (datas) => {
      for (let data of datas) {
        list.push(data)
        if (data.children && data.children.length > 0) {
          dosome(data.children)
        }
      }
    }
    dosome(datas)
    return list
  },
  getFlatIndexById(datas, id) {
    let solved = this.flatTreeDatas(datas)
    return solved.findIndex(e => e.transfer_ins_id === id)
  },
  sort(datas, field, status = 'asc', isNum = false) {
    if (['asc', 'desc'].indexOf(status) < 0) {
      return datas
    }
    let compare = (property, asc = true) => {
      return function(obj1, obj2) {
        var value1 = obj1[property]
        var value2 = obj2[property]
        if (typeof (value1) === 'number' && typeof (value2) === 'number') {
          return asc ? value1 - value2 : value2 - value1
        } else {
          if (isNum) {
            value1 = parseFloat(value1)
            value2 = parseFloat(value2)
            return asc ? value1 - value2 : value2 - value1
          } else {
            value1 += ''
            value2 += ''
            return asc ? value1.localeCompare(value2) : value2.localeCompare(value1)
          }
        }
      }
    }
    return datas.sort(compare(field, status === 'asc'))
  },

  getColumnsConfig(that) {
    return [
      {
        title: '序号',
        key: 'transfer_ins_id',
        align: 'left',
        ellipsis: true,
        renderHeader: (h, params) => {
          return h('Sortheader', {
            props: {
              title: '序号',
              field: 'transfer_ins_id',
              sortingField: that.sortingField
            },
            on: {
              sort: (status) => {
                that.sortAtField(status, 'transfer_ins_id')
              }
            }
          })
        }
      },
      {
        title: '指令来源',
        key: 'data_source',
        width: 80,
        align: 'left',
        ellipsis: true,
        hiddenOther: true,
        render: (h, params) => {
          const rowData = params.row
          return h('span', {}, dataSourceDesc(rowData.data_source))
        }
      },
      {
        title: '产品名称',
        key: 'fund_name',
        width: 100,
        align: 'left',
        ellipsis: true
      },
      {
        title: '审批状态',
        key: 'node_name',
        width: 60,
        align: 'left',
        ellipsis: true,
        hiddenOther: true,
        render: (h, params) => {
          return h('span', {}, nodeNameDesc(params.row.node_name))
        }
      },
      {
        title: '合同状态',
        key: 'cash_settle_status',
        align: 'left',
        width: 160,
        sortable: true,
        ellipsis: true
      },
      {
        title: '清算机构',
        key: 'clear_agent_code_cn',
        align: 'left',
        width: 90,
        ellipsis: true,
        hiddenOther: true,
        render: (h, params) => {
          return h('span', {}, clearAgentCodeDesc(params.row.clear_agent_code_cn))
        },
        renderHeader: (h, params) => {
          return h('Sortheader', {
            props: {
              title: '清算机构',
              field: 'clear_agent_code_cn',
              sortingField: that.sortingField
            },
            on: {
              sort: (status) => {
                that.sortAtField(status, 'clear_agent_code_cn')
              }
            }
          })
        }
      },
      {
        key: 'market_no',
        width: 80,
        title: '交易市场',
        align: 'left',
        hiddenOther: true,
        render: (h, params) => {
          return h('span', {}, marketNoDesc(params.row.market_no))
        }
      },
      {
        title: '成交/合同编号',
        key: 'deal_no',
        width: 120,
        sortable: true,
        align: 'left',
        ellipsis: true
      },
      {
        title: '结算金额',
        key: 'settle_balance',
        ellipsis: true,
        width: 140,
        align: 'right',
        hiddenOther: true,
        render: (h, params) => {
          return h('span', { style: { fontWeight: 'bold' } }, formatMoney(params.row.settle_balance))
        }
      },
      {
        title: '起息日',
        key: 'fee_begin_date',
        width: 100,
        ellipsis: true,
        align: 'right',
        hiddenOther: true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = parseDateString(rowData.fee_begin_date)
          return h('span', {}, typeName)
        }
      },
      {
        title: '到期日',
        width: 100,
        key: 'fee_end_date',
        ellipsis: true,
        align: 'right',
        hiddenOther: true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = parseDateString(rowData.fee_end_date)
          return h('span', {}, typeName)
        }
      },
      {
        title: '证券代码',
        key: 'report_code',
        width: 90,
        ellipsis: true,
        align: 'left'
      },
      {
        title: '证券名称',
        width: 100,
        key: 'stock_name',
        ellipsis: true,
        align: 'left'
      },
      {
        title: '资金方向',
        key: 'cash_direction',
        width: 80,
        sortable: true,
        align: 'left',
        hiddenOther: true,
        render: (h, params) => {
          return h('span', {}, cashDirectionDesc(params.row.cash_direction))
        }
      },
      {
        title: '业务标志',
        key: 'cop_busin_caption',
        width: 130,
        sortable: true,
        ellipsis: true,
        align: 'left'
      },
      {
        title: '附件',
        key: 'bill_name',
        width: 50,
        align: 'left',
        ellipsis: true,
        hiddenOther: true,
        render: (h, params) => {
          const rowData = params.row
          if (rowData.bill_name) {
            return h('Icon', {
              props: {
                name: 'document'
              }
            })
          } else {
            var typeName = '-'
            return h('span', {}, typeName)
          }
        }
      },
      {
        title: '结算方式',
        key: 'settle_ment',
        align: 'left',
        width: 80,
        ellipsis: true,
        hiddenOther: true,
        render: (h, params) => {
          return h('span', {}, settlementDesc(params.row.settle_ment))
        }
      },
      {
        title: '托管人',
        key: 'fund_trust_bank',
        align: 'left',
        width: 100,
        ellipsis: true,
        hiddenOther: true,
        render: (h, params) => {
          return h('span', {}, bankDesc(params.row.fund_trust_bank))
        }
      },
      {
        title: '对手方',
        width: 100,
        key: 'rival_name',
        ellipsis: true,
        align: 'left'
      },
      {
        title: '交收日期',
        key: 'cash_settle_date',
        sortable: true,
        ellipsis: true,
        align: 'left',
        hiddenOther: true,
        render: (h, params) => {
          const rowData = params.row
          var typeName = parseDateString(rowData.cash_settle_date)
          return h('span', {}, typeName)
        }
      },
      {
        title: '投资类型',
        key: 'invest_type',
        ellipsis: true,
        align: 'left',
        width: 160,
        hiddenOther: true,
        render: (h, params) => {
          return h('span', {}, investTypeDesc(params.row.invest_type))
        }
      },
      {
        title: '原始划款事由',
        key: 'original_transfer_purpose',
        ellipsis: true,
        align: 'left'
      },
      {
        title: '外部划款序号',
        key: 'out_transfer_id',
        ellipsis: true,
        align: 'left'
      },
      {
        title: '交易员',
        key: 'trade_operator_name',
        ellipsis: true,
        align: 'left'
      },
      {
        title: '申购价格（元/股）',
        key: 'apply_price',
        ellipsis: true,
        align: 'left'
      },
      {
        title: '交易端备注',
        key: 'transfer_trade_remark',
        ellipsis: true,
        align: 'left'
      },
      {
        title: '指令端备注',
        key: 'transfer_instr_remark',
        ellipsis: true,
        align: 'left'
      }
    ]
  }
}

export default service



