<template>
    <div id="full-h-wrapper-on-reportmgnt-transferRecord" class="page-transfer-record full-h">
        <h-row style="margin-bottom: 0px" id="transferRecordForm">
            <!--<h-col>-->
                <el-form style="font-size: 12px;" :inline="true" size="mini" :model="queryForm" class="demo-form-inline">

                    <el-form-item label="产品名称">
                        <el-select v-model="fundIds" filterable multiple clearable collapse-tags>
                            <el-option v-for="item in fundList" :key="item.code" :label="item.text" :value="item.code">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="业务分类">
                        <el-select v-model="businClass" filterable multiple clearable collapse-tags>
                            <el-option v-for="item in businClassList" :key="item.code" :label="item.text" :value="item.code">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="指令状态" v-if="pageType==='2'">
                        <el-select v-model="transferInsStatus" filterable multiple clearable collapse-tags>
                            <el-option v-for="item in transferInsStatusList" :key="item.code" :label="item.text" :value="item.code">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="交收日期">
                        <el-date-picker
                                v-model="settleDate"
                                type="daterange"
                                :clearable="false"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="结算金额">
                        <div>
                            <h-typefield type="money" placeholder="" integerNum="15" suffixNum="2" :min=0 divided
                                         v-model="queryForm.settleBalanceMin" style="width:100px;margin-top:-5px;">
                                <span slot="append">元</span>
                            </h-typefield>
                            <span v-themed:text>-</span>
                            <h-typefield type="money" placeholder="" integerNum="15" suffixNum="2" :min=0 divided
                                         v-model="queryForm.settleBalanceMax" style="width:100px;margin-top:-5px;">
                                <span slot="append">元</span>
                            </h-typefield>
                        </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="refresh">查询</el-button>
                        <!--<h-button v-if="pageType==='2'" class="record-export" icon="undo" @click="exportData" style="margin-top:-3px;">导出</h-button>-->
                    </el-form-item>
                    <h-button-group>
                        <h-button v-if="pageType==='2'" class="record-export" icon="undo" @click="exportData">导出</h-button>
                    </h-button-group>
                    <el-form-item v-show="false">
                        <h-file-export sheetType="table"
                                       fileName='完结指令查询导出'
                                       :sheetNames="['完结指令查询']"
                                       :sheetHeaders="sheetHeadersColumns"
                                       :sheetTableData="sheetTableData">
                            <h-button icon="undo" ref="recordExportBtn">导出</h-button>
                        </h-file-export>
                    </el-form-item>
                    <h-button-group style = "float: right; border-right:solid 0 ;color:transparent;">
                        <h-button @click="openLayout"><h-icon class="iconfont icon-ziduanxuanze1"></h-icon> &nbsp;布局</h-button>
                    </h-button-group>
                    <!--<h-button-group style = "float:right;">
                        <h-button @click="exportData" style="min-width:30px;width:30px;"><h-icon class="icon-upload1"></h-icon></h-button>
                        <h-button @click="openLayout" style="min-width:30px;width:30px;"><h-icon class="iconfont icon-ziduanxuanze1"></h-icon></h-button>
                    </h-button-group>-->
                </el-form>
            <!--</h-col>-->
        </h-row>
        <!--<h-datagrid style="margin-top: 0px" class="mt10"
                    :height="autoHeight" stripe hasPage
                    highlight-row :pageSize="100"
                    :pageSizeOpts="pageSizeOpts"
                    :columns="transferColums"
                    :bindForm="queryForm"
                    :grid-name="gridName"
                    url="/reportmgnt/transRecord/transRecordList.json"
                    ref="datagrid">
        </h-datagrid>-->
        <!--<CommTreeGrid
                style="margin-top: 0px" class="mt10"
                :height="autoHeight"
                :pageSize="100"
                :onRowDbClick="showDetail"
                :pageSizeOpts="pageSizeOpts"
                :columns="transferColums"
                :fundData="queryForm"
                :grid-name="gridName"
                :headSelection="false"
                :isCheckbox="false"
                url="/reportmgnt/transRecord/transRecordList.json"
                ref="datagrid">
        </CommTreeGrid>-->
        <SimpleTreeGrid
                style="margin-top: 0px" class="mt10"
                :pageSize="100"
                :onRowDbClick="showDetail"
                :pageSizeOpts="pageSizeOpts"
                :columns="transferColums"
                :fundData="queryForm"
                :grid-name="gridName"
                :headSelection="false"
                :isCheckbox="false"
                url="/reportmgnt/transRecord/transRecordList.json"
                ref="datagrid">
        </SimpleTreeGrid>
        <div class="wrapper">
            <template v-if="this.statisticsData.totalUSD == '0'">
                <h-row class-name="border-dashed-top pt10 mt10">
                    <h-col span="12" class="inner">
                        <div class="item">
                            <div class="label">指令总数：</div>
                            <div class="value" :title="this.statisticsData.transferCount" >{{this.statisticsData.transferCount}}</div>
                        </div>
                    </h-col>
                    <h-col span="12" class="inner">
                        <div class="item">
                            <div class="label">人民币总额：</div>
                            <div class="value" :title="this.statisticsData.totalRMB">{{moneyFormat(this.statisticsData.totalRMB)}}</div>
                        </div>
                    </h-col>
                </h-row>
            </template>
            <template v-else>
                <h-row class-name="border-dashed-top pt10 mt10">
                    <h-col span="8" class="inner">
                        <div class="item">
                            <div class="label">指令总数：</div>
                            <div class="value" :title="this.statisticsData.transferCount">{{this.statisticsData.transferCount}}</div>
                        </div>
                    </h-col>
                    <h-col span="8" class="inner">
                        <div class="item">
                            <div class="label">人民币总额：</div>
                            <div class="value" :title="this.statisticsData.totalRMB">{{moneyFormat(this.statisticsData.totalRMB)}}</div>
                        </div>
                    </h-col>
                    <h-col span="8" class="inner">
                        <div class="item">
                            <div class="label">美元总额：</div>
                            <div class="value" :title="this.statisticsData.totalUSD">{{moneyFormat(this.statisticsData.totalUSD)}}</div>
                        </div>
                    </h-col>
                </h-row>
            </template>
        </div>
        <TransferDetailDialog ref="detailDialog"></TransferDetailDialog>
    </div>
</template>

<script>
    import {
        getDictList,
        getFundInfo,
        showText,
        parseDateStr,
        parseDateString,
        parseStrDate,
        formatMoney,
        getBankList,
        getAgencyList,
    } from '@/api/utils'
    import {getTransferMergeFileShowText, transRecordListExport, getStatisticsData} from '@/api/transfer/transfer'
    import {nodeNameDesc} from '@/filters/index'
    import {deepCopy} from '@/utils/func'
    import HDatagrid from "../../../components/HDatagrid";
    import CommTreeGrid from '@/components/CommTreeGrid'
    import SimpleTreeGrid from '@/components/SimpleTreeGrid'
    import Cookies from 'js-cookie'
    import TransferDetailDialog from "../../../components/TransferDetailDialog";

    export default {
        name: "transferRecord",
        data(){
            return {
                statisticsData:{
                    transferCount:'',
                    totalRMB:'',
                    totalUSD:''
                },
                pageSizeOpts: [25,50,75,100],
                autoHeight:0,
                getAgencyList:[],
                dataSourceList:[],
                transferColums: [
                    {
                        fixedLayout: "1",    //用于固定布局，表示不能通过布局修改
                        title: "序号",
                        key: "transfer_ins_id",
                        sortable: true,
                        align: "left",
                        ellipsis: true,
                    },{
                        title: "指令来源",
                        key: "data_source",
                        width: 80,
                        align: "left",
                        ellipsis: true,
                        render: (h,params) =>{
                             const rowData = params.row;
                             let dataSource = showText(rowData.data_source,this.dataSourceList);
                             return h("span",{},dataSource);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.dataSourceList);
                        },
                    },{
                        title: "产品代码",
                        key: "fund_code",
                        width: 100,
                        align: 'left',
                        ellipsis: true,
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '产品代码',
                                    field: 'fund_code',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'fund_code');
                                        this.sortingField = 'fund_code';
                                    }
                                }
                            })
                        }
                    }, {
                        title: "产品名称",
                        key: "fund_name",
                        width: 150,
                        align: "left",
                        ellipsis: true,
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '产品名称',
                                    field: 'fund_name',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'fund_name');
                                        this.sortingField = 'fund_name';
                                    }
                                }
                            })
                        }
                    },{
                        title: "外部产品名称",
                        key: "product_name",
                        width: 100,
                        align: "left",
                        ellipsis: true
                    }, {
                        title: "业务分类",
                        key: "cop_busin_class",
                        width: 120,
                        align: 'left',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let copBusinClass = showText(rowData.cop_busin_class, this.businClassList);
                            return h("span", {}, copBusinClass);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.businClassList);
                        },
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '业务分类',
                                    field: 'cop_busin_class',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'cop_busin_class');
                                        this.sortingField = 'cop_busin_class';
                                    }
                                }
                            })
                        }
                    }, {
                        title: "业务标志",
                        key: "cop_busin_flag",
                        width: 150,
                        ellipsis: true,
                        align: "left",
                        render: (h,params) =>{
                            const rowData = params.row;
                            let copBusinFlag = showText(rowData.cop_busin_flag, this.businFlagList);
                            return h("span",{},copBusinFlag);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.businFlagList);
                        },
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '业务标志',
                                    field: 'cop_busin_flag',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'cop_busin_flag');
                                        this.sortingField = 'cop_busin_flag';
                                    }
                                }
                            })
                        }
                    },{
                        title: "证券代码",
                        key: "report_code",
                        width: 100,
                        align: 'left',
                        ellipsis: true,
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '证券代码',
                                    field: 'report_code',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'report_code');
                                        this.sortingField = 'report_code';
                                    }
                                }
                            })
                        }
                    }, {
                        title: "证券名称",
                        key: "stock_name",
                        width: 150,
                        align: "left",
                        ellipsis: true
                    }, {
                        title: "交易市场",
                        key: "market_no",
                        width: 95,
                        align: "left",
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName = showText(rowData.market_no, this.marketList);
                            return h("span", {}, typeName);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.marketList);
                        },
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '交易市场',
                                    field: 'market_no',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'market_no');
                                        this.sortingField = 'market_no';
                                    }
                                }
                            })
                        }
                    }, {
                        title: "清算机构",
                        key: "clear_agent_code_cn",
                        align: "left",
                        width: 90,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName = showText(rowData.clear_agent_code_cn,
                                this.clearAgentList);
                            return h("span", {}, typeName);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.clearAgentList);
                        },
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '清算机构',
                                    field: 'clear_agent_code_cn',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'clear_agent_code_cn');
                                        this.sortingField = 'clear_agent_code_cn';
                                    }
                                }
                            })
                        }
                    },{
                        title: "投资类型",
                        key: "invest_type",
                        ellipsis: true,
                        align: "left",
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName = showText(rowData.invest_type, this.investTypeList);
                            return h("span", {}, typeName);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.investTypeList);
                        },
                    }, {
                        title: "托管人",
                        key: "fund_trust_bank",
                        align: "left",
                        width: 100,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName = showText(rowData.fund_trust_bank,
                                this.bankInfoList);
                            return h("span", {}, typeName);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.bankInfoList);
                        },
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '托管人',
                                    field: 'fund_trust_bank',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'fund_trust_bank');
                                        this.sortingField = 'fund_trust_bank';
                                    }
                                }
                            })
                        }
                    }, {
                        title: "出款账户名称",
                        key: "self_account_name",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "出款银行账号",
                        key: "self_account",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "出款银行",
                        key: "self_bank",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "出款开户行",
                        key: "self_open_bank",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "出款大额支付号",
                        key: "self_pay_no",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "入款账户名称",
                        key: "rival_account_name",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "入款银行账号",
                        key: "rival_account",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "入款银行",
                        key: "rival_bank",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "入款开户行",
                        key: "rival_open_bank",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "入款大额支付号",
                        key: "rival_pay_no",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "经办人",
                        key: "approve_operator",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "经办时间",
                        key: "approve_date",
                        ellipsis: true,
                        align: "left",
                        width: 135,
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName = parseDateString(rowData.approve_date, rowData.approve_time);
                            return h("span", {}, typeName);
                        },
                        exportRender: (value, rowData) => {
                            return parseDateString(rowData.approve_date, rowData.approve_time);
                        },
                    }, {
                        title: "交收人",
                        key: "settle_operator",
                        ellipsis: true,
                        align: "left",
                    }, {
                        title: "交收时间",
                        key: "settle_date",
                        ellipsis: true,
                        align: "left",
                        width: 135,
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName = parseDateString(rowData.settle_date, rowData.settle_time);
                            return h("span", {}, typeName);
                        },
                        exportRender: (value, rowData) => {
                            return parseDateString(rowData.settle_date, rowData.settle_time);
                        },
                    }, {
                        title: "结算金额",
                        key: "settle_balance",
                        ellipsis: true,
                        width: 140,
                        align: "right",
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName = formatMoney(rowData.settle_balance);
                            return h("span", {style:{fontWeight:'bold'}}, typeName);
                        },
                        /*exportRender: (value, rowData) => {
                            return formatMoney(value);
                        },*/
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '结算金额',
                                    field: 'settle_balance',
                                    sortingField: this.sortingField,
                                    align: "right",
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'settle_balance', true);
                                        this.sortingField = 'settle_balance';
                                    }
                                }
                            })
                        }
                    }, {
                        title: "审批状态",
                        key: "node_name",
                        width: 90,
                        align: "left",
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            return h("span", {}, nodeNameDesc(rowData.node_name));
                        },
                        exportRender: (value, rowData) => {
                            return nodeNameDesc(value);
                        },
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '审批状态',
                                    field: 'node_name',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'node_name');
                                        this.sortingField = 'node_name';
                                    }
                                }
                            })
                        }
                    }, {
                        title: "划款状态",
                        key: "transfer_fund_status",
                        width: 90,
                        align: "left",
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            if (rowData.transfer_ins_batch_no && parseInt(rowData.transfer_ins_batch_no) > 0) {
                                return h("span", {}, '-');
                            } else {
                                var typeName = showText(rowData.transfer_fund_status, this.transferFundStatusList);
                                return h("span", {}, typeName);
                            }
                        },
                        exportRender: (value, rowData) => {
                            if (rowData.transfer_ins_batch_no && parseInt(rowData.transfer_ins_batch_no) > 0) {
                                return '-';
                            } else {
                                return showText(value, this.transferFundStatusList);
                            }
                        },
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '划款状态',
                                    field: 'transfer_fund_status',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'transfer_fund_status');
                                        this.sortingField = 'transfer_fund_status';
                                    }
                                }
                            })
                        }
                    },{
                        title: "划款单",
                        key: "file_id",
                        width: 135,
                        ellipsis: true,
                        align: "left",
                        render: (h, params) => {
                            const rowData = params.row;
                            if (rowData.file_id) {
                                return h('div', [
                                    h('Button', {
                                        props: {
                                            type: 'primary',
                                            size: 'small'
                                        },
                                        style: {
                                            marginRight: '5px'
                                        },
                                        on: {
                                            click: () => {
                                                this.previewPDF(rowData.file_id)
                                            }
                                        }
                                    }, '预览'),
                                    h('Button', {
                                        props: {
                                            type: 'primary',
                                            size: 'small'
                                        },
                                        on: {
                                            click: () => {
                                                this.downloadByFileId(rowData.file_id)
                                            }
                                        }
                                    }, '下载')
                                ]);
                            } else {
                                var typeName = '-';
                                return h("span", {}, typeName);
                            }
                        },
                        exportRender: (value, rowData) => {
                            return '';
                        },
                    },{
                        title: "合同状态",
                        key: "cash_settle_status",
                        align: "left",
                        width: 130,
                        ellipsis: true,
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '合同状态',
                                    field: 'cash_settle_status',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'cash_settle_status');
                                        this.sortingField = 'cash_settle_status';
                                    }
                                }
                            })
                        }
                    }, {
                        title: "附件发送状态",
                        key: "file_send_status",
                        width: 100,
                        align: "left",
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var typeName = showText(rowData.file_send_status, this.sendStatusList);
                            return h("span", {}, typeName);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.sendStatusList);
                        },
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '附件发送状态',
                                    field: 'file_send_status',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'file_send_status');
                                        this.sortingField = 'file_send_status';
                                    }
                                }
                            })
                        }
                    },{
                        title: "附件",
                        key: "bill_name",
                        width: 50,
                        align: "left",
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            if (rowData.transfer_merge_flag === '1') {
                                let fileShow = getTransferMergeFileShowText(rowData);
                                return h("span", {}, fileShow);
                            } else {
                                if (rowData.bill_name) {
                                    return h('Icon', {
                                        props: {
                                            name: 'document'
                                        }
                                    })
                                } else {
                                    return h("span", {}, '-');
                                }
                            }
                        },
                        exportRender: (value, rowData) => {
                            if (rowData.transfer_merge_flag === '1') {
                                return getTransferMergeFileShowText(rowData);
                            } else {
                                return '';
                            }
                        },
                    },{
                        title: "指令状态",
                        key: "transfer_ins_status",
                        width: 100,
                        align: 'left',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let name = showText(rowData.transfer_ins_status, this.transferInsStatusList);
                            return h("span", {}, name);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.transferInsStatusList);
                        },
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '指令状态',
                                    field: 'transfer_ins_status',
                                    sortingField: this.sortingField
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'transfer_ins_status');
                                        this.sortingField = 'transfer_ins_status';
                                    }
                                }
                            })
                        }
                    },
                    {
                        title: "外部划款序号",
                        key: "out_transfer_id",
                        width: 100,
                        align: 'left',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let outTransferId;
                            if (rowData.out_transfer_id == -1) {
                                outTransferId = ''
                            } else {
                                outTransferId = rowData.out_transfer_id
                            }
                            return h("span", {}, outTransferId);
                        },
                        exportRender: (value, rowData) => {
                            if (value == -1) {
                                return '';
                            } else {
                                return value;
                            }
                        },
                    },{
                        title: "申请编号",
                        key: "zdsh_apply_no",
                        width: 120,
                        align: "left",
                        ellipsis: true
                    },
                    {
                        title: "交收日期",
                        key: "cash_settle_date",
                        width: 100,
                        align: 'right',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let cashSettleDate = parseDateString(rowData.cash_settle_date);
                            return h("span", {}, cashSettleDate);
                        },
                        exportRender: (value, rowData) => {
                            return parseDateString(value);
                        },
                        renderHeader: (h, params) => {
                            return h('Sortheader', {
                                props: {
                                    title: '交收日期',
                                    field: 'cash_settle_date',
                                    sortingField: this.sortingField,
                                    align: "right",
                                },
                                on: {
                                    sort: (status) => {
                                        this.$refs.datagrid.sortAtField(status, 'cash_settle_date');
                                        this.sortingField = 'cash_settle_date';
                                    }
                                }
                            })
                        }
                    },{
                        title: "指令下达时间",
                        key: "input_time",
                        align: 'left',
                        ellipsis: true,
                        width: 135,
                        render: (h, params) => {
                            const rowData = params.row;
                            let inputTime = parseDateString(rowData.input_date, rowData.input_time);
                            return h("span", {}, inputTime);
                        },
                        exportRender: (value, rowData) => {
                            return parseDateString(rowData.input_date, rowData.input_time);
                        },
                    },{
                        title: "划款发送时间",
                        key: "send_time",
                        align: 'left',
                        ellipsis: true,
                        width: 135,
                        render: (h, params) => {
                            const rowData = params.row;
                            let sendTime = parseDateString(rowData.transfer_date, rowData.transfer_time);
                            return h("span", {}, sendTime);
                        },
                        exportRender: (value, rowData) => {
                            return parseDateString(rowData.transfer_date, rowData.transfer_time);
                        },
                    },{
                        title: "划款成功时间",
                        key: "succ_time",
                        align: 'left',
                        ellipsis: true,
                        width: 135,
                        render: (h, params) => {
                            const rowData = params.row;
                            let succTime = parseDateString(rowData.transfer_succ_date, rowData.transfer_succ_time);
                            return h("span", {}, succTime);
                        },
                        exportRender: (value, rowData) => {
                            return parseDateString(rowData.transfer_succ_date, rowData.transfer_succ_time);
                        },
                    },{
                        title: "业务划款用时",
                        key: "business_cost",
                        width: 100,
                        align: 'left',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let businessCost = this.caculateTimeCost(rowData.transfer_date,rowData.transfer_time,rowData.input_date, rowData.input_time);
                            return h("span", {}, businessCost);
                        },
                        exportRender: (value, rowData) => {
                            return this.caculateTimeCost(rowData.transfer_date,rowData.transfer_time,rowData.input_date, rowData.input_time);
                        },
                    },{
                        title: "银行划款用时",
                        key: "transfer_cost",
                        width: 100,
                        align: 'left',
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            let transferCost = this.caculateTimeCost(rowData.transfer_succ_date, rowData.transfer_succ_time,rowData.transfer_date,rowData.transfer_time);
                            return h("span", {}, transferCost);
                        },
                        exportRender: (value, rowData) => {
                            return this.caculateTimeCost(rowData.transfer_succ_date, rowData.transfer_succ_time,rowData.transfer_date,rowData.transfer_time);
                        },
                    },{
                        title: "销售机构",
                        key: "agency_code",
                        ellipsis: true,
                        align: "left",
                        render: (h, params) => {
                            const rowData = params.row;
                            var agencyNmae = showText(rowData.agency_code,
                                this.agencyList);
                            return h("span", {}, agencyNmae);
                        },
                        exportRender: (value, rowData) => {
                            return showText(value, this.agencyList);
                        }
                    }
                ],
                businClassList: [],
                transferInsStatusList: [],
                marketList:[],
                clearAgentList: [
                    {code: '1', text: '中登上海'}, {code: '2', text: '中登深圳'},
                    {code: '3', text: '中债登'}, {code: '4', text: '上清所'}
                ],
                fundList: [],
                businFlagList:[],
                sysDate:'',
                transferFundStatusList: [],
                sendStatusList: [{code:'0',text:'无需发送'}],
                queryForm:{
                    beginDate:Cookies.get('CurrSysDate'),
                    endDate:Cookies.get('CurrSysDate'),
                    fundIds:'',
                    businClass:'',
                    transferInsStatus: this.$route.params.insStatus ? this.$route.params.insStatus : '',
                    settleBalanceMin: '',
                    settleBalanceMax: '',
                    pageType: this.pageType
                },
                bankList: [],
                bankInfoList:[],
                investTypeList: [],
                sheetHeadersColumns: [[]],//导出excel的列
                sheetTableData: [[]],//导出excel的数据
            }
        },
        props: {
            //页面类型（1-在途指令，2-完结指令）
            pageType: {
                type: String,
                default: ""
            },
        },
        computed:{
            settleDate:{
                get: function () {
                    let begin, end
                    if (this.queryForm.beginDate) {
                        begin = parseDateString(this.queryForm.beginDate)
                    } else {
                        begin = this.sysDate
                    }
                    if (this.queryForm.endDate) {
                        end = parseDateString(this.queryForm.endDate)
                    } else {
                        end = this.sysDate
                    }

                    return [begin, end]
                },
                set: function (newValue) {
                    this.queryForm.beginDate = parseDateStr(newValue[0])
                    this.queryForm.endDate = parseDateStr(newValue[1])
                }
            },
            fundIds:{
                get: function () {
                    if (this.queryForm.fundIds){
                        return this.queryForm.fundIds.split(",")
                    }
                    return []
                },
                set: function (newValue) {
                    this.queryForm.fundIds = newValue.join(",")
                }
            },
            businClass:{
                get: function () {
                    if (this.queryForm.businClass){
                        return this.queryForm.businClass.split(",")
                    }
                    return []
                },
                set: function (newValue) {
                    this.queryForm.businClass = newValue.join(",")
                }
            },
            transferInsStatus:{
                get: function () {
                    if (this.queryForm.transferInsStatus){
                        return this.queryForm.transferInsStatus.split(",")
                    }
                    return []
                },
                set: function (newValue) {
                    this.queryForm.transferInsStatus = newValue.join(",")
                }
            },
            gridName() {
                let gridName = "transRecordEndGrid";
                if (this.pageType === "1") {
                    gridName = "transRecordZtGrid";//在途指令
                } else if (this.pageType === "2") {
                    gridName = "transRecordEndGrid";//完结指令
                }
                return gridName;
            }
        },
        methods: {
            moneyFormat (money) {
                if (money && money != null) {
                    money = String(money);
                    var left = money.split('.')[0], right = money.split('.')[1];
                    right = right ? (right.length >= 2 ? '.' + right.substr(0, 2) : '.' + right + '0') : '.00';
                    var temp = left.split('').reverse().join('').match(/(\d{1,3})/g);
                    return (Number(money) < 0 ? '-' : '') + temp.join(',').split('').reverse().join('') + right;
                } else if (money === 0) { // 注意===在这里的使用，如果传入的money为0,if中会将其判定为boolean类型，故而要另外做===判断
                    return '0.00';
                } else {
                    return '';
                }
            },
            showDetail(value){
                this.$refs.detailDialog.showTransferDetail(value)
            },
            caculateTimeCost(endDate,endTime,startDate,startTime){
                if (!startTime||!endTime||endTime==0){
                    return ''
                }
                if (startDate!=endDate){
                    startTime=83000
                }
                if (startTime>endTime)
                    return ''
                let startStr  = endDate+''+ (Array(6).join(0) + startTime).slice(-6);
                let endStr = endDate+''+ (Array(6).join(0) + endTime).slice(-6);
                let interval = parseStrDate(endStr).getTime() - parseStrDate(startStr).getTime()
                let num = interval/1000
                let senconds =  (Array(2).join(0) + num%60).slice(-2) ;
                let minutes =  (Array(2).join(0) + Math.floor(num/60)%60).slice(-2) ;
                let hours = (Array(2).join(0) + Math.floor(num/3600)).slice(-2) ;
                return hours+':'+minutes+':'+senconds

            },
            re_layout() {
                /*let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                let transferRecordFormHeight = document.getElementById("transferRecordForm").offsetHeight;
                this.autoHeight = document.getElementById("full-h-wrapper-on-reportmgnt-transferRecord").offsetHeight
                    - wrapper0Height - transferRecordFormHeight;*/
            },
            refresh(){
                if (this.$route.params.insStatus) {
                    this.queryForm.transferInsStatus = this.$route.params.insStatus;
                }
                if (this.queryForm.beginDate && this.queryForm.endDate) {
                    let endTime = parseStrDate(this.queryForm.endDate);
                    let beginTime = parseStrDate(this.queryForm.beginDate);
                    let days = (endTime - beginTime) / (1000 * 60 * 60 * 24);
                    if (days > 30) {
                        this.$alert('查询日期间隔不能大于30天', '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                        return;
                    }
                }

                if (this.queryForm.settleBalanceMin && this.queryForm.settleBalanceMax) {
                    if (parseFloat(this.queryForm.settleBalanceMin) > parseFloat(this.queryForm.settleBalanceMax)) {
                        this.$alert('结算金额下限不能大于结算金额上限', '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                        return;
                    }
                }

                this.$refs.datagrid.changeGrid(this.gridName);
                this.$refs.datagrid.dataChange(1);
                this.getTotalInfo();
                this.re_layout();
            },
            getTotalInfo() {
                getStatisticsData(this.queryForm).then(res => {
                        if (res && res.status === 200 && res.data) {
                            this.statisticsData.transferCount = res.data.data.count;
                            this.statisticsData.totalRMB = res.data.data.totalRMB;
                            this.statisticsData.totalUSD = res.data.data.totalUSD;
                            console.log(this.statisticsData.totalRMB);
                        }
                    }
                )
            },
            exportData() {
                const loading = this.$loading({
                    lock: true,
                    text: '处理中',
                    spinner: 'el-icon-loading',
                    background: 'rgba(0, 0, 0, 0.7)'
                });
                transRecordListExport({
                    pageNo: 1,
                    pageSize: null,//pageSize传null，后台控制不分页
                    beginDate: this.queryForm.beginDate,
                    endDate: this.queryForm.endDate,
                    fundIds: this.queryForm.fundIds,
                    businClass: this.queryForm.businClass,
                    transferInsStatus: this.queryForm.transferInsStatus,
                    settleBalanceMin: this.queryForm.settleBalanceMin,
                    settleBalanceMax: this.queryForm.settleBalanceMax,
                    pageType: this.queryForm.pageType
                }).then(res => {
                    loading.close();
                    if (res && res.status === 200 && res.data) {
                        let solvedColumnsAll = deepCopy(this.$refs.datagrid.solvedColumns);
                        solvedColumnsAll.push({
                            title: "混合指令序号",
                            key: "transfer_ins_batch_no",
                            align: "left",
                            ellipsis: true
                        });
                        this.sheetHeadersColumns = [solvedColumnsAll];
                        this.sheetTableData = [res.data.list ? res.data.list : []];

                        //触发导出事件
                        this.$nextTick(() => {
                            this.$refs.recordExportBtn.$el.click();
                        });
                    } else {
                        this.$alert(res.data.msg, '错误', {
                            confirmButtonText: '确定',
                            type: 'error'
                        });
                    }
                }).catch(error => {
                    loading.close();
                    this.$alert(error, '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                });
            },
            openLayout(){
                this.$refs.datagrid.openLayout();
            },
            previewPDF(fileId) {
                let url = "/cop-web/static/plugins/pdfjs/generic/web/viewer.html?file="+encodeURIComponent(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=1&fileId=' +fileId);
                window.open(url);
            },
            downloadByFileId(fileId) {
                window.open(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=1&fileId=' +fileId)
            },
        },
        activated() {
            this.refresh();
            getFundInfo('/common/fundName.json', null).then(res=>this.fundList=res.data);
            getBankList().then(res => this.bankList = res.data);
            getBankList("1").then(res => this.bankInfoList = res.data);
            getAgencyList('').then(res => this.agencyList = res.data);
        },
        mounted(){
            getDictList(90523).then(res => this.businClassList = res.data);
            getDictList(90528).then(res => this.dataSourceList = res.data);
            getDictList(90537).then(res => {
                if (this.pageType === "1") {
                    this.transferInsStatusList = res.data.filter(x => "3,8,4,5,6".indexOf(x.code) < 0);
                } else if (this.pageType === "2") {
                    this.transferInsStatusList = res.data.filter(x => "3,8,2,7".indexOf(x.code) < 0);
                } else {
                    this.transferInsStatusList = res.data.filter(x => "3,8".indexOf(x.code) < 0);
                }
            });
            getDictList(610400).then(res => this.marketList = res.data);
            getDictList('businFlag').then(res => this.businFlagList = res.data);
            getDictList(90571).then(res => this.transferFundStatusList = res.data);
            getDictList(90426).then(res => this.sendStatusList = this.sendStatusList .concat(res.data));
            getDictList(91064).then(res => this.investTypeList = res.data);
            this.re_layout();
        },
        components: {
            TransferDetailDialog,
            HDatagrid,
            CommTreeGrid,
            SimpleTreeGrid
        }
    }
</script>

<style lang="less" rel="stylesheet/less">
    #full-h-wrapper-on-reportmgnt-transferRecord {
        position: absolute;
        display: flex;
        flex-direction: column;
    }
    .page-transfer-record .wrapper {
        background: #f6f7f9;

        .inner {
            height: 30px;
            padding: 0 12px;

            &:first-child {
                padding-left: 0;
            }

            &:last-child {
                padding-right: 0;
            }

            &:not(:last-child) {
                border-right: dashed 1px #666666;
            }

            .item {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                padding-right: 0;
                white-space: nowrap;

                &:last-child {
                    margin-bottom: 0;
                }

                .label {
                    white-space: nowrap;
                    font-size: 14px;
                    color: #999999;
                }

                .value {
                    padding-left: 10px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    font-size: 16px;
                    color: #333333;
                    float: right;
                }
            }
        }
    }


</style>


