    watch: {
        'transferInfo.cop_busin_flag'(val) {
            switch (this.transferInfo.cop_busin_class) {
                case '1':
                    this.showRival = false;
                    this.showReportCode = val == 20020;
                    if(val == 20117 || val == 10071 || val == 30008){
                        this.showClearAgent = false;
                    }else if(val == 10070 || val == 20116 || val == 30110){
                        this.showClearAgent = false;
                    }else if(!this.showReportCode){
                        this.showClearAgent = true;
                    }else if(this.showReportCode){
                        this.showClearAgent = false;
                    }
                    break;
                case '4,5,6':
                case '4':
                case '5':
                case '6':
                    this.transferInfo.clear_agent_code_cn='';
                    this.showRival = val == 10078 || val == 11010 //解除质押
                    this.showReportCode = false;
                    this.showClearAgent = false;
                    break;
                case '9':
                    this.showRival = val == 10023 || val == 20018 || val == 10074 || val == 10075 || val == 20130 || val == 20019;
                    this.showReportCode = val == 20017 || val == 20047 || val == 20049 || val == 20126 || val == 20128 || val == 20050 || val == 20051
                        || val == 20139 || val == 20140 || val == 10053 || val == 10054 || val == 10098 || val == 10099;
                    this.showClearAgent = false;
                    break;
                case '7' :
                    this.showRival = !this.showAgency;
                    this.showReportCode = false;
                    this.showClearAgent = false;
                    break;
                case '20':
                    this.showRival = true;
                    this.showReportCode = false;
                    this.showClearAgent = false;
                    break;
                default:
                    this.showRival = false;
                    this.showReportCode = false;
                    this.showClearAgent = false;
                    break;

            }
        },
        showDialog: function (show) {
            if (!show) {
                this.closeMask();
                this.showRival = false;
                this.outAcctList = []
                this.inAcctList = []
                this.fileList = [];
                this.transferInfo = {
                    require_pay_date: null,
                    fund_id: this.queryForm.fundIds,
                    sq_holder_account: '',
                    zz_holder_account: '',
                    transfer_purpose: '',
                    transfer_remark: '',
                    transfer_postscript: '',
                    clear_agent_code_cn: null,
                    cop_busin_class: null,
                    settle_balance: '',
                    rival_code:'',
                    invest_type:'',
                    currency_no:''
                };
                //重置表单
                this.$refs.transForm0.resetFields();
                this.$refs.transForm.resetFields();
                this.$refs.outAcctForm.resetFields();
                this.$refs.inAcctForm.resetFields();
                this.$refs.selfAcctForm.resetFields();
            } else {
                this.openMask();
                this.activeName = ['2', '3'];
            }
        },
        /*showDetailBox:function(show) {
            if(show){
                this.openMask();
            }else{
                this.closeMask();
            }
        }*/
