    components: {
        TransferDetailDialog,
        FundTree,
        HDatagrid,
        CommTreeGrid,
        SimpleTreeGrid,
        DetailPanel,
        SelectionTip
    },
    activated(){
        getSysParamValue(10038).then(res => {
            if (res.data.success) {
                this.canSpecialApprove = res.data.data;
            }
        });
    },
    mounted() {
        getDictList('businFlag').then(res => this.businFlagList = res.data);
        getDictList(90528).then(res => this.dataSourceList = res.data);
        getDictList(90522).then(res => this.transferFlagList = res.data);
        getDictList(90537).then(res => {
            this.transferStatusList = res.data.filter(r =>
                r.code === '2' || r.code === '4' || r.code === '5' || r.code === '6' || r.code === '7');
        });
        getCityTree().then(res=>this.cityTree = res.data)
        getDictList(90426).then(res => this.sendStatusList = this.sendStatusList .concat(res.data));
        getDictList(90523).then(res => this.businClassList = res.data);
        getDictList(90571).then(res => this.transferFundStatusList = res.data);
        getDictList(610400).then(res => this.marketList = res.data);
        getDictList(91064).then(res => this.investTypeList = res.data);
        getDictList(610406).then(res => {
            let tempList = res.data;
            tempList.forEach(item => {
                if (item.code == "CNY"){
                    item.text = "元";
                }
            })
            this.currencyList = tempList;
        });
        getBankList().then(res => this.bankList = res.data);
        getBankList("1").then(res => this.bankInfoList = res.data);
        getBusinClassTree(true).then(res => this.businClassTree = res.data);
        operatorList().then(res => this.operatorList = res.data);
        getAgencyList('').then(res => this.agencyList = res.data);

        getSpecialApproveWorkerList().then(res => {
            if (res.data.rows && res.data.rows.length > 0) {
                this.specialApproveWorkerList = res.data.rows;
                this.specialApproveWorkerList.unshift({
                    begin_date: "0",
                    end_date: "0",
                    operator_name: "全部",
                    operator_no: -1,
                    workgroup_id: 0
                });
            } else {
                this.specialApproveWorkerList = [];
            }
        });

        this.re_layout("full-h-wrapper-on-transfer-finished", "inner-on-transfer-finished", 110);
    },
    computed: {
        showAcctCity(){
            let isShow = this.inAcct.province_code;
            if(isShow){
                this.accountStyle.height = '235px';
                this.trusteeStyle.height = '235px';
            }else{
                this.accountStyle.height = '200px';
                this.trusteeStyle.height = '200px';
            }
            return isShow;
        },
        provinceCity: {
            get: function () {
                return [this.inAcct.province_code, this.inAcct.city_no]
            },
            set: function (newValue) {
                this.inAcct.province_code = newValue[0]
                this.inAcct.city_no = newValue[1]
            }
        },
        transferStatus: {
            get: function () {
                if (this.queryForm.transferStatus)
                    return this.queryForm.transferStatus.split(",")
                else
                    return []
            },
            set: function (newValue) {
                this.queryForm.transferStatus = newValue.join(",")
            }
        },
        businClass: {
            get: function () {
                if (this.queryForm.businClass)
                    return this.queryForm.businClass.split(",")
                else
                    return []
            },
            set: function (newValue) {
                this.queryForm.businClass = newValue.join(",")
            }
        },
        dateRange: {
            get: function () {
                let begin, end
                let now = parseStrDate(Cookies.get('CurrSysDate'));
                if (this.queryForm.beginDate) {
                    begin = parseDateString(this.queryForm.beginDate)
                } else {
                    begin = now
                    this.queryForm.beginDate = parseDateStr(now)
                }
                if (this.queryForm.endDate) {
                    end = parseDateString(this.queryForm.endDate)
                } else {
                    end = now
                    this.queryForm.endDate = parseDateStr(now)
                }

                return [begin, end]
            },
            set: function (newValue) {
                this.queryForm.beginDate = parseDateStr(newValue[0])
                this.queryForm.endDate = parseDateStr(newValue[1])
            }
        },
        upBalance() {
            return smalltoBIG(this.transferInfo.settle_balance);
        },
        businClassFlag: {
            get: function () {
                if (this.showTemplateList) {
                    return [this.queryTemp.cop_busin_class, this.transferInfo.cop_busin_flag?this.transferInfo.cop_busin_flag.toString():'']
                }
                let businClass = this.transferInfo.cop_busin_class
                if (this.transferInfo.cop_busin_class == '4'
                    || this.transferInfo.cop_busin_class == '5'
                    || this.transferInfo.cop_busin_class == '6') {
                    businClass = '4,5,6'
                }
                return [businClass, this.transferInfo.cop_busin_flag?this.transferInfo.cop_busin_flag.toString():'']
            },
            set: function (newValue) {
                if (this.showTemplateList) {
                    this.queryTemp.cop_busin_class = newValue[0]
                    this.queryTemp.cop_busin_flag = newValue[1]
                } else {
                    this.transferInfo.cop_busin_class = newValue[0]
                    this.transferInfo.cop_busin_flag = newValue[1]
                }

            }
        },
        payDate: {
            get: function () {
                if(this.transferInfo.require_pay_date != null){
                    let arr = this.transferInfo.require_pay_date.toString().split(" ");
                    let date = arr[0].replace(new RegExp("-","gm"),"");
                    let time = arr[1];
                    if(typeof(time) == "undefined"){
                        time=this.transferInfo.require_pay_time;
                    }else{
                        time=time.replace(new RegExp(":","gm"),"");
                    }
                    return parseDateString(date,time);
                }
                return parseDateString(this.transferInfo.require_pay_date)
            },
            set: function (newValue) {
                this.transferInfo.require_pay_date = parseDateStr(newValue,"yyyy-MM-dd hh:mm:ss")
            }
        },

        beginDate: {
            get: function () {
                return parseDateString(this.transferInfo.fee_begin_date)
            },
            set: function (newValue) {
                this.transferInfo.fee_begin_date = parseDateStr(newValue)
            }
        },
        endDate: {
            get: function () {
                return parseDateString(this.transferInfo.fee_end_date)
            },
            set: function (newValue) {
                this.transferInfo.fee_end_date = parseDateStr(newValue)
            }
        },
    },

    methods: {
        showCity:showCity,
        formatMoney: formatMoney,
        fundClick(data) {
            this.$refs.datagrid.changeGrid("finishedGrid");
            this.queryForm.fundIds = data.code;
            this.fundList = this.$refs.treeFund.getFundList();
            this.refreshTable();
        },
        rowClick(arr) {
            //let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
            let selects = this.$refs.datagrid.getSelection();
            if (selects.length === 0) {
                this.item = {};
            } else {
                this.item = deepCopy(arr[0]);
            }
            this.re_layout("full-h-wrapper-on-transfer-finished", "inner-on-transfer-finished", 110);
            //this.changeChildColumns();
        },
        handleSelectChange(arr) {
            //let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
            let selects = this.$refs.datagrid.getSelection();
            if (selects.length === 1) {
                this.item = deepCopy(selects[0]);
            } else {
                this.item = {};
            }
            this.re_layout("full-h-wrapper-on-transfer-finished", "inner-on-transfer-finished", 110);
            //this.changeChildColumns();
        },
        loadFiles(bill_name) {
            if (bill_name) {
                getFileList(bill_name,'2').then(res => {
                    if (res.data.success) {
                        res.data.data.forEach(file => this.fileList.push({
                            name: file.file_name,
                            response: {
                                data: file.file_id
                            }
                        }));
                    }
                    // this.transferInfo.bill_name = this.fileList.reduce((x, y) => x.response.data + ',' + y.response.data);
                })
            }
        },
        showText(value, list) {
            return showText(value, list)
        },
        showDetail(value) {
            this.$refs.detailDialog.showTransferDetail(value)
        },
        refreshTable(noInit) {
            this.$refs.datagrid.changeGrid("finishedGrid");

            if (this.queryForm.settleBalanceMin && this.queryForm.settleBalanceMax) {
                if (parseFloat(this.queryForm.settleBalanceMin) > parseFloat(this.queryForm.settleBalanceMax)) {
                    this.$alert('结算金额下限不能大于结算金额上限', '错误', {
                        confirmButtonText: '确定',
                        type: 'error'
                    });
                    return;
                }
            }

            if (noInit) {
                this.$refs.datagrid.dataChange();
            } else {
                this.$refs.datagrid.dataChange(1);
            }
            this.item = {};

            this.re_layout("full-h-wrapper-on-transfer-finished", "inner-on-transfer-finished", 110);
        },
        handleCancel() {
            this.showDialog = false
        },
        revokeSettle() {
            //let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
            let selects = this.$refs.datagrid.getSelection();
            if (selects.length == 0) {
                this.openMask();
                this.$alert('请先选中一条记录','错误',{
                    confirmButtonText: '确定',
                    type: 'error',
                    callback:action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                })
                return
            }

            //过滤掉子指令
            selects = filterTransferChildren(selects, this);
            if (selects.length <= 0) {
                this.openMask();
                this.$alert('子指令不能单独回溯','错误',{
                    confirmButtonText: '确定',
                    type: 'error',
                    callback:action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                })
                return;
            }

            if (selects[0].transfer_ins_status != '4') {
                this.openMask();
                this.$alert('只允许回溯交收成功的指令','错误',{
                    confirmButtonText: '确定',
                    type: 'error',
                    callback:action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                })
                return
            }
            let url = '/' +
                'cashsettle/finished/revokeSettle.json'
            let title = "确定回溯所选指令?";

            this.openMask();
            this.$prompt(title + '请填入原因并确认：', '交收回溯', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(({
                value
            }) => {
                var params = {
                    transferList: selects,
                    ignore: false,
                    param: value
                };
                handleTransfer(url, params).then(res => {
                    if (res.data.success) {
                        this.$alert(res.data.msg,'成功',{
                            confirmButtonText: '确定',
                            type: 'success',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                        this.refreshTable(true)
                    } else {
                        this.$alert(res.data.msg,'错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback:action => {
                                this.closeMask();
                            }
                        }).then(() => {
                            this.closeMask();
                        })
                        this.refreshTable(true)
                    }
                })
            }).catch(() =>{
                this.closeMask();
            });
        },
        fileUploaded(response, file, fileList) {
            this.transferInfo.bill_name =
                fileList.reduce((x, y) => x.response.data + ',' + y.response.data);
        },
        exceedLimit() {
            this.$alert('最多上传5个附件','错误',{
                confirmButtonText: '确定',
                type: 'error'
            });
        },
        previewPDF(fileId) {
            let pdfUrl = "/cop-web/static/plugins/pdfjs/generic/web/viewer.html?file="+encodeURIComponent(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=1&fileId=' +fileId);
            window.open(pdfUrl)

        },
        downloadByFileId(fileId) {
            window.open(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=1&fileId=' +fileId)
        },
        downloadFile(file) {
            window.open(window.LOCAL_CONFIG.API_HOME + '/file/download.json?fileType=2&fileId=' +
                file.response.data)
        },
        openLayout(){
            this.$refs.datagrid.openLayout();
        },
        onSelectionChange(selections) {
            this.selectionTipVisible = selections.length > 0
            this.selections = selections
            SSObservable.publish('onresize')
            SSObservable.publish('re_layout')
        }

    }
}
