            width: 3px;
            height: 12px;
            background-color: $base-icon-color;
            margin-right: 5px;
        }

        .title {
            padding: 0;
            margin-right: 10px;
            font-size: 12px;
            color: #666666;
        }

        .icon-operation {
            cursor: pointer;
            color: $base-icon-color;
        }
    }
}

.block-in-box {
    @include headline();
}

.collapse-icon-l {
  display: block;
  margin-left: 10px;
  width: 3px;
  height: 12px;
  background-color: $base-icon-color;
  margin-right: 5px;
}

.collapse-title {
  padding: 0;
  margin-right: 10px;
  font-size: 12px;
  color: #666666;
}

.wrapper {
    width: 100%;
    // height: 208px;
    padding: 10px;
    border: solid 1px #d3d3d3;
    background-color: #f6f7f9;

    @include headline();

    .inner-t {
        padding: 0 10px;
        border-right: dashed 1px #666666;

        &:nth-child(3n - 2) {
            padding-left: 0;
        }

        &:nth-child(3n) {
            padding-right: 0;
        }

        &:nth-child(3n) {
            border-right: none;
        }

        .item {
            line-height: 32px;
        }
    }

    .inner {
        height: 140px;
        padding: 0 12px;

        &:first-child {
            padding-left: 0;
        }

        &:last-child {
            padding-right: 0;
        }

        &:not(:last-child) {
            border-right: dashed 1px #666666;
        }
    }

    .inner-t,
    .inner {
        .item {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            padding-right: 0;
            white-space: nowrap;

            &:last-child {
                margin-bottom: 0;
            }

            .label {
                white-space: nowrap;
                font-size: 12px;
                color: #999999;
            }

            .value {
                padding-left: 10px;
                overflow: hidden;
                text-overflow: ellipsis;
                font-size: 14px;
                color: #333333;
            }
        }
    }
}
</style>
<style lang="less" rel="stylesheet/less">
    .transfer-finished {
        #full-h-wrapper-on-transfer-finished {
            display: flex;
            flex-direction: column;
        }
    }
</style>


