<script>
    import {
        sysParamQuery,
        sysParamProcess,
        sysParamValue,
        approveRuleChange,
        sysParamPassword
    } from '@/api/systemmgnt/systemParam';

    import {
        getKeys,
        encrypt,
        decrypt
    } from '@/api/utils'

    export default {
        name: "systemparameter",
        data(){
            return{
                /*isUseUFX:false,*/
                keys:[],
                szt_pwd:'',
                approveRuleModel: '',
                systemParamGroupListLeft : [],
                systemParamGroupListRight : [],
                systemParamGroup:{}
            }
        },
        mounted(){
            /*sysParamValue(10022).then(res => {
                if(res.data[0] == '1'){
                    this.isUseUFX = true;
                }else{
                    this.isUseUFX = false;
                }
            })*/
            getKeys().then(res => this.keys = res.data)
            sysParamQuery().then(res => {
                this.systemParamGroupListLeft=[];
                this.systemParamGroupListRight=[];
                let middle = Math.ceil(res.data.length/2);
                for( let i = 0; i <= middle; i++){
                    let returnData = res.data[i];
                    let paramValueList = res.data[i].parameterList;
                    for ( let j = 0; j<paramValueList.length ; j++){
                        if(paramValueList[j].showType == 1){
                            if(paramValueList[j].param_value == '1'){
                                returnData.parameterList[j].booleanParamValue = true;
                            }else{
                                returnData.parameterList[j].booleanParamValue = false;
                            }
                        }else if(paramValueList[j].showType == 10 && paramValueList[j].param_value){
                            let time = paramValueList[j].param_value.split("-");
                            returnData.parameterList[j].beginTime = time[0];
                            returnData.parameterList[j].endTime = time[1];
                        }else if(paramValueList[j].showType == 7){
                            sysParamPassword(paramValueList[j].sysparam_no).then(res => {
                                let keys = []
                                keys.push(res.data[1]);
                                keys.push(res.data[2]);
                                keys.push(res.data[3]);
                                returnData.parameterList[j].param_value = decrypt(res.data[0],keys)
                            })
                        }else if(paramValueList[j].showType == 3){
                            if(paramValueList[j].param_value){
                                returnData.parameterList[j].paramValueList = paramValueList[j].param_value.split(",");
                            }else{
                                returnData.parameterList[j].paramValueList = [];
                            }
                        }
                        if(paramValueList[j].sysparam_no == 10016){
                            this.approveRuleModel = paramValueList[j].param_value;
                        }
                    }
                    this.systemParamGroupListLeft.push(returnData);
                }
                for( let i = middle + 1;i<res.data.length;i++){
                    let returnData = res.data[i];
                    let paramValueList = res.data[i].parameterList;
                    for ( let j = 0; j<paramValueList.length ; j++){
                        if(paramValueList[j].showType == 1){
                            if(paramValueList[j].param_value == '1'){
                                returnData.parameterList[j].booleanParamValue = true;
                            }else{
                                returnData.parameterList[j].booleanParamValue = false;
                            }
                        }else if(paramValueList[j].showType == 10 && paramValueList[j].param_value){
                            let time = paramValueList[j].param_value.split("-");
                            returnData.parameterList[j].beginTime = time[0];
                            returnData.parameterList[j].endTime = time[1];
                        }else if(paramValueList[j].showType == 7){
                            sysParamPassword(paramValueList[j].sysparam_no).then(res => {
                                let keys = []
                                keys.push(res.data[1]);
                                keys.push(res.data[2]);
                                keys.push(res.data[3]);
                                returnData.parameterList[j].param_value = decrypt(res.data[0],keys)
                            })
                        }else if(paramValueList[j].showType == 3){
                            if(paramValueList[j].param_value){
                                returnData.parameterList[j].paramValueList = paramValueList[j].param_value.split(",");
                            }else{
                                returnData.parameterList[j].paramValueList = [];
                            }
                        }
                    }
                    this.systemParamGroupListRight.push(returnData);
                }
            })
        },
        methods:{
            systemSubmit(){
                this.systemParamGroup.parameterList = [];
                let isEnd = false;
                this.systemParamGroupListLeft.forEach(res => {
                    res.parameterList.forEach(x => {
                        if(x.showType == 3){
                            if (x.paramValueList&&x.paramValueList.length>0)
                                x.param_value = x.paramValueList.join(",");
                            else
                                x.param_value = ''
                        }else if(x.showType == 1){
                            if(x.booleanParamValue == true){
                                x.param_value = '1'
                            }else {
                                x.param_value = '0'
                            }
                        }else if(x.showType == 7){
                            x.param_value=encrypt(x.param_value,this.keys)
                        }else if(x.showType == 10){
                            x.param_value=x.beginTime+"-"+x.endTime;
                        }
                        if(x.sysparam_no == 10027 && !x.param_value){
                            this.openMask();
                            this.$alert('存档路径不能为空，请填写路径!','提示',{
                                confirmButtonText: '确定',
                                type: 'warning',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            });
                            isEnd = true;
                        }
                        this.systemParamGroup.parameterList.push(x);
                    })
                })
                this.systemParamGroupListRight.forEach(res => {
                    res.parameterList.forEach(x => {
                        if(x.showType == 3){
                            if (x.paramValueList&&x.paramValueList.length>0)
                                x.param_value = x.paramValueList.join(",");
                            else
                                x.param_value = ''
                        }else if(x.showType == 1){
                            if(x.booleanParamValue == true){
                                x.param_value = '1'
                            }else {
                                x.param_value = '0'
                            }
                        }else if(x.showType == 7){
                            x.param_value=encrypt(x.param_value,this.keys)
                        }else if(x.showType == 10){
                            x.param_value=x.beginTime+"-"+x.endTime;
                        }
                        if(x.sysparam_no == 10027 && !x.param_value){
                            this.openMask();
                            this.$alert('存档路径不能为空，请填写路径!','提示',{
                                confirmButtonText: '确定',
                                type: 'warning',
                                callback: action => {
                                    this.closeMask();
                                }
                            }).then(() =>{
                                this.closeMask();
                            });
                            isEnd = true;
                        }
                        this.systemParamGroup.parameterList.push(x);
                    })
                })
                if(isEnd){
                    this.systemReset();
                    return;
                }
                this.waitTime();
                sysParamProcess({
                    dic_no:0,
                    show_title:'',
                    title_name:'',
                    parameterList:this.systemParamGroup.parameterList
                }).then(res => {
                    if(res.data && res.data.success){
                        this.openMask()
                        this.$alert('系统参数修改成功','成功',{
                            confirmButtonText:'确定',
                            type:'success',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() =>{
                            this.closeMask();
                        });
                        this.systemReset();
                    }else {
                        this.openMask()
                        this.$alert("修改系统参数失败",'错误',{
                            confirmButtonText: '确定',
                            type: 'error',
                            callback: action => {
                                this.closeMask();
                            }
                        }).then(() =>{
                            this.closeMask();
                        });
                    }
                })
            },
            systemReset(){
                sysParamQuery().then(res => {
                    this.systemParamGroupListLeft=[];
                    this.systemParamGroupListRight=[];
                    let middle = Math.ceil(res.data.length/2);
                    for( let i = 0;i <= middle;i++){
                        let returnData = res.data[i];
                        let paramValueList = res.data[i].parameterList;
                        for ( let j = 0; j<paramValueList.length ; j++){
                            if(paramValueList[j].showType == 1){
                                if(paramValueList[j].param_value == '1'){
                                    returnData.parameterList[j].booleanParamValue = true;
                                }else{
                                    returnData.parameterList[j].booleanParamValue = false;
                                }
                            }else if(paramValueList[j].showType == 10 && paramValueList[j].param_value){
                                let time = paramValueList[j].param_value.split("-");
                                returnData.parameterList[j].beginTime = time[0];
                                returnData.parameterList[j].endTime = time[1];
                            }else if(paramValueList[j].showType == 7){
                                sysParamPassword(paramValueList[j].sysparam_no).then(res => {
                                    let keys = []
                                    keys.push(res.data[1]);
                                    keys.push(res.data[2]);
                                    keys.push(res.data[3]);
                                    returnData.parameterList[j].param_value = decrypt(res.data[0],keys)
                                })
                            }else if(paramValueList[j].showType == 3){
                                if(paramValueList[j].param_value){
                                    returnData.parameterList[j].paramValueList = paramValueList[j].param_value.split(",");
                                }else{
                                    returnData.parameterList[j].paramValueList = [];
                                }
                            }
                            if(paramValueList[j].sysparam_no == 10016){
                                this.approveRuleModel = paramValueList[j].param_value;
                            }
                        }
                        this.systemParamGroupListLeft.push(returnData);
                    }
                    for( let i = middle + 1;i<res.data.length;i++){
                        let returnData = res.data[i];
                        let paramValueList = res.data[i].parameterList;
                        for ( let j = 0; j<paramValueList.length ; j++){
                            if(paramValueList[j].showType == 1){
                                if(paramValueList[j].param_value == '1'){
                                    returnData.parameterList[j].booleanParamValue = true;
                                }else{
                                    returnData.parameterList[j].booleanParamValue = false;
                                }
                            }else if(paramValueList[j].showType == 10 && paramValueList[j].param_value){
                                let time = paramValueList[j].param_value.split("-");
                                returnData.parameterList[j].beginTime = time[0];
                                returnData.parameterList[j].endTime = time[1];
                            }else if(paramValueList[j].showType == 7){
                                sysParamPassword(paramValueList[j].sysparam_no).then(res => {
                                    let keys = []
                                    keys.push(res.data[1]);
                                    keys.push(res.data[2]);
                                    keys.push(res.data[3]);
                                    returnData.parameterList[j].param_value = decrypt(res.data[0],keys)
                                })
                            }else if(paramValueList[j].showType == 3){
                                if(paramValueList[j].param_value){
                                    returnData.parameterList[j].paramValueList = paramValueList[j].param_value.split(",");
                                }else{
                                    returnData.parameterList[j].paramValueList = [];
                                }
                            }
                        }
                        this.systemParamGroupListRight.push(returnData);
                    }
                })
            },
            /*useUFX(status){
                if(status){
                    this.isUseUFX = true;
                }else{
                    this.isUseUFX = false;
                }
            },*/
            coverFormat1(val){
                this.coverFormat(val,this.systemParamGroupListLeft);
                this.coverFormat(val,this.systemParamGroupListRight);
            },
            coverFormat2(val){
                this.coverFormat(val,this.systemParamGroupListLeft);
                this.coverFormat(val,this.systemParamGroupListRight);
            },
            coverFormat(val,systemParamGroupList){
                let cover = val;
                let coverArr = cover.split(".");
                if(coverArr[0] >100){
                    for (let i = 0;i<systemParamGroupList.length;i++){
                        if(systemParamGroupList[i].show_title==6){
                            for (let j = 0;j<systemParamGroupList[i].parameterList.length;j++) {
                                if(systemParamGroupList[i].parameterList[j].sysparam_no == 10014 && systemParamGroupList[i].parameterList[j].param_value>100){
                                    systemParamGroupList[i].parameterList[j].param_value=100.000000;
                                }else if(systemParamGroupList[i].parameterList[j].sysparam_no == 10015 && systemParamGroupList[i].parameterList[j].param_value>100){
                                    systemParamGroupList[i].parameterList[j].param_value=100.000000;
                                }
                            }
                        }
                    }
                }else if(coverArr[0].indexOf("-") != -1){
                    for (let i = 0;i<systemParamGroupList.length;i++){
                        if(systemParamGroupList[i].show_title==6){
                            for (let j = 0;j<systemParamGroupList[i].parameterList.length;j++) {
                                if(systemParamGroupList[i].parameterList[j].sysparam_no == 10014 && systemParamGroupList[i].parameterList[j].param_value.indexOf("-") != -1){
                                    this.openMask();
                                    this.$alert('其他备付金比例不能为负数','提示',{
                                        confirmButtonText: '确定',
                                        type: 'warning',
                                        callback: action => {
                                            this.closeMask();
                                        }
                                    }).then(() =>{
                                        this.closeMask();
                                    });
                                    systemParamGroupList[i].parameterList[j].param_value=0.000000;
                                }else if(systemParamGroupList[i].parameterList[j].sysparam_no == 10015 && systemParamGroupList[i].parameterList[j].param_value.indexOf("-") != -1){
                                    this.openMask();
                                    this.$alert('债券备付金比例不能为负数','提示',{
                                        confirmButtonText: '确定',
                                        type: 'warning',
                                        callback: action => {
                                            this.closeMask();
                                        }
                                    }).then(() =>{
                                        this.closeMask();
                                    });
                                    systemParamGroupList[i].parameterList[j].param_value=0.000000;
                                }
                            }
                        }
                    }
                }
            },
            waitTime(){
                this.waitTimeFunc(this.systemParamGroupListLeft);
                this.waitTimeFunc(this.systemParamGroupListRight);
            },
            waitTimeFunc(systemParamGroupList){
                for (let i = 0;i<systemParamGroupList.length;i++){
                    if(systemParamGroupList[i].show_title==6){
                        for (let j = 0;j<systemParamGroupList[i].parameterList.length;j++) {
                            if(systemParamGroupList[i].parameterList[j].sysparam_no == 10023){
                                let paramValue = systemParamGroupList[i].parameterList[j].param_value;
                                if(paramValue<5){
                                    systemParamGroupList[i].parameterList[j].param_value = 5;
                                    this.openMask();
                                    this.$alert("电子直连查询日终余额等待时间需在5-60分钟之间",'提示',{
                                        confirmButtonText: '确定',
                                        type: 'warning',
                                        callback: action => {
                                            this.closeMask();
                                        }
                                    }).then(() =>{
                                        this.closeMask();
                                    });
                                    return;
                                }else if(paramValue>60){
                                    systemParamGroupList[i].parameterList[j].param_value = 60;
                                    this.openMask();
                                    this.$alert("电子直连查询日终余额等待时间需在5-60分钟之间",'提示',{
                                        confirmButtonText: '确定',
                                        type: 'warning',
                                        callback: action => {
                                            this.closeMask();
                                        }
                                    }).then(() =>{
                                        this.closeMask();
                                    });
                                    return;
                                }
                            }
                        }
                    }
                }
            },
            approveRuleChange(data) {
                if(this.approveRuleModel !== data){
                    approveRuleChange().then( res => {
                        if(res && res.data.code != '0'){
                            this.$confirm(res.data.msg, '确认', {
                                confirmButtonText: '确定',
                                cancelButtonText: '取消',
                                type: 'warning'
                            }).then(() => {
                            }).catch(() =>{
                                for (let i = 0; i < this.systemParamGroupListLeft.length; i++){
                                    let param = this.systemParamGroupListLeft[i];
                                    if(param.show_title == '2'){
                                        let paramList = param.parameterList;
                                        for (let j = 0; j < paramList.length; j++){
                                            if(paramList[j].sysparam_no == 10016){
                                                paramList[j].param_value = this.approveRuleModel;
                                                break;
                                            }
                                        }
                                        break;
                                    }
                                }
                            });
                        }
                    })
                }
            }
        }
    }

</script>

<style lang="less" rel="stylesheet/less" scoped>
    /*.page-systemparameter {*/
        /*width: calc(100% - 20px);*/
        /*height: calc(100% - 75px);*/
        /*overflow: hidden;;*/
    /*}*/

    .page-systemparameter {
        .h-form-item-label {
            color: red;
        }
    }
    .content {
        position: absolute;
        display: flex;
        justify-content: space-between;
        left: 0;
        right: 0;
        top: 0;
        bottom: 40px;
        overflow-x: hidden;
        overflow-y: auto;
        & > div:first-child {
            // margin-right: 20px;
        }
    }

    .foot{
        background-color:#ffffff;
        position: fixed;
        bottom: 0;
        width: 96.5%;
        height: 40px;
    }

    .input_attr{
        width:70%;
    }

    .systemProBtn {
        position: absolute;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        bottom: 0px;
        left: 10px;
        right: 10px;
        height: 40px;
        background-color:#ffffff;
    }

    .border_item{
        margin-top:10px;
        margin-left:6px;
        -moz-border-radius: 15px;
        -webkit-border-radius: 15px;
        border-radius:4px;
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
        border: 1px solid #ebeef5;
    }

    .form_item{
        margin-left: 10px !important;
        margin-bottom: 6px !important;
    }

    .title{
        margin: 2px 0;
        padding:8px 0 5px 10px;
        font-size:12px;
        color:#ff6e1f;
        border-bottom: 1px solid #ebeef5;
        box-sizing: border-box;
    }


</style>

<style lang="scss">
    .cop-poptip.h-poptip {
        .h-poptip-rel{
            display: block;
        }
    }
</style>


