import fetch from "@/api/httpFetch";

export function saveWorkGroup(params) {
    return fetch.post("/systemmgnt/workGroup/saveWorkGroup.json",params,{
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    })
}

export function deleteWorkGroup(workGroupId) {
    return fetch.post("/systemmgnt/workGroup/deleteWorkGroup.json",{workGroupId:workGroupId})
}

export function saveOpWorkGroup(params) {
    return fetch.post("/systemmgnt/workGroup/saveOpWorkGroup.json",params,{
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    })
}

export function deleteOpWorkGroup(operatorNo,workGroupId) {
    return fetch.post("/systemmgnt/workGroup/deleteOpWorkGroup.json",{operatorNo:operatorNo,workGroupId:workGroupId})
}


